import type {ComponentProps} from 'react'
import React, {useCallback, useMemo} from 'react'

import {useAgentStateSelectedOption} from '@bpinc/ad-agent-state-context'
import {useGetAvatarUrl} from '@bpinc/ad-contacts-state-hooks'
import {useInteractionChatData} from '@bpinc/ad-interaction-chat-state-context'
import type {ChatDataObject} from '@bpinc/ad-interaction-chat-state-types'
import {useInternalChats} from '@bpinc/ad-interaction-internal-chat-state-context'
import type {InternalChatItems} from '@bpinc/ad-interaction-internal-chat-state-types'
import {useActiveInteractionProp, useInteractions} from '@bpinc/ad-interaction-state-context'
import {getInteractionStepId} from '@bpinc/ad-interaction-state-methods'
import type {Item} from '@bpinc/ad-interaction-state-types'
import {useIsOffline} from '@bpinc/ad-offline-state-context'
import {useSessionState} from '@bpinc/ad-session-context'
import {AgentStatusIcon} from '@bpinc/lib-agent-state-ui'
import {AvatarLegacy, getAvatarType} from '@bpinc/lib-avatar-ui'
import {AppRoutes} from '@bpinc/lib-communicator-navigation-state'
import {SmMenu} from '@bpinc/lib-external-ui-elements'
import {isHTMLButtonElement} from '@bpinc/lib-html-utils'
import {
    IconNavDial,
    IconNavDirectory,
    IconNavFavorites,
    IconNavRecents,
    IconNavSettings,
} from '@bpinc/lib-icons'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {useLocalizationState} from '@bpinc/state-localization-context'


interface Props {
    activePageName: string
    onMenuItemClick: (name: AppRoutes, params?: Record<string, unknown>) => void
    onSettingsClick: () => void
    onAgentStateClick: () => void
    onInteractionClick: (item: Item) => void
}

const getUnread = (item: Item, externalItems: ChatDataObject, internalItems: InternalChatItems) => {
    const userId = item.internalChatData && item.internalChatData.userId
    const interactionStepId = getInteractionStepId(item)
    const unreadExternal = interactionStepId && externalItems[interactionStepId] && externalItems[interactionStepId].unread
    const unreadInternal = userId && internalItems[userId] && internalItems[userId].unreadAmount
    return unreadExternal || unreadInternal || 0
}

const AppMinimized: React.FC<Props> = props => {
    const {activePageName, onMenuItemClick, onSettingsClick, onAgentStateClick, onInteractionClick} = props
    const {isLoggedIn} = useSessionState()
    const isOffline = useIsOffline()
    const {t} = useLocalizationState()
    const interactions = useInteractions()
    const getAvatarUrl = useGetAvatarUrl()
    const externalItems = useInteractionChatData()
    const internalItems = useInternalChats()
    const activeItemMediaType = useActiveInteractionProp(i => i?.mediaType)
    const selectedAgentStatusOption = useAgentStateSelectedOption()
    const handleItemKeyPress = useCallback((e: React.KeyboardEvent) => {
        // Enter, Space
        if (e.key === 'Enter' || e.key === ' ') {
            const button = e.target
            if (!isHTMLButtonElement(button)) {
                return
            }
            e.preventDefault()
            button.click()
        }
    }, [])

    const mainMenuItems = useMemo(() => {
        return [
            {route: AppRoutes.Recents, name: t.ADDRESS_BOOK_AREA.addressBook_recents(), Icon: IconNavRecents},
            {route: AppRoutes.Favorites, name: t.ADDRESS_BOOK_AREA.addressBook_favorites(), Icon: IconNavFavorites},
            {route: AppRoutes.Directory, name: t.ADDRESS_BOOK_AREA.addressBook_directory(), Icon: IconNavDirectory},
            {route: AppRoutes.Dial, name: t.ADDRESS_BOOK_AREA.addressBook_dial(), Icon: IconNavDial},
        ].map(item => {
            return {...item, onClick: () => onMenuItemClick(item.route)}
        })
    }, [onMenuItemClick, t.ADDRESS_BOOK_AREA])

    const interactionCallbacks = useMemo(() => interactions.map(item => ({
        onClick: () => onInteractionClick(item),
    })), [interactions, onInteractionClick])

    const itemStyles = tcn('h-9 w-9 justify-center p-0 [&:not(:last-child)]:mb-2')

    return (
        <div className="h-full w-11 p-1 outline outline-1 outline-main-border">
            {isLoggedIn ? (
                <SmMenu vertical secondary>
                    <SmMenu.Item
                        as="button"
                        className={tcn(itemStyles, '[&_img]:h-7 [&_img]:w-7')}
                        name="AgentState"
                        onClick={onAgentStateClick}
                    >
                        <AgentStatusIcon
                            busyMediaType={activeItemMediaType}
                            agentStatusOption={selectedAgentStatusOption}
                            isOffline={isOffline}
                        />
                    </SmMenu.Item>
                    {mainMenuItems.map(({Icon, name, onClick, route}) => (
                        <SmMenu.Item
                            as="button"
                            className={itemStyles}
                            key={name}
                            name={name}
                            onClick={onClick}
                            active={activePageName === route}
                            onKeyPress={handleItemKeyPress}
                        >
                            <Icon />
                        </SmMenu.Item>
                    ))}
                    <SmMenu.Item
                        as="button"
                        className={itemStyles}
                        name="Settings"
                        onClick={onSettingsClick}
                        onKeyPress={handleItemKeyPress}
                    >
                        <IconNavSettings />
                    </SmMenu.Item>
                    {interactions.map((item, i) => {
                        const {
                            displayData,
                            internalChatData,
                            externalChatData,
                            userId,
                            contactId,
                        } = item
                        const emiUserType = item.displayData?.emiUserType
                        const isChat = item.mediaType === 'chat' || !!internalChatData || !!externalChatData
                        const unread = getUnread(item, externalItems, internalItems)
                        let firstName, lastName

                        if (externalChatData || internalChatData) {
                            const data = externalChatData || internalChatData
                            firstName = data!.firstName
                            lastName = data!.lastName
                        } else {
                            firstName = displayData.firstName
                            lastName = displayData.lastName
                        }

                        const avatarProps: ComponentProps<typeof AvatarLegacy> = {
                            firstName,
                            lastName,
                            url: getAvatarUrl({
                                userId: userId,
                                contactId: contactId,
                                userType: emiUserType,
                                photoUrl: item.displayData?.emiPhotoUrl,
                            }),
                            headPhones: !!userId,
                            interactionIcon: isChat ? unread || 'chat' : 'phone',
                            size: 28,
                            type: getAvatarType(userId, contactId, emiUserType),
                        }

                        return (
                            <SmMenu.Item
                                as="button"
                                className={itemStyles}
                                key={item.id}
                                name={String(Math.random())}
                                onClick={interactionCallbacks[i].onClick}
                                onKeyPress={handleItemKeyPress}
                            >
                                <AvatarLegacy {...avatarProps} />
                            </SmMenu.Item>
                        )
                    })}
                </SmMenu>
            ) : null}
        </div>
    )
}

export {AppMinimized}
