import React, {useCallback} from 'react'

import {useInteraction} from '@bpinc/ad-interaction-state-context'
import {useCallButtonsHandlers} from '@bpinc/ad-interaction-state-hooks'
import {showMergeButton, showTransferButton} from '@bpinc/ad-interaction-state-methods'
import {useSessionState} from '@bpinc/ad-session-context'
import {HoldButton, MuteButton, RecordButton, TransferButton} from '@bpinc/lib-interaction-buttons'
import {useInteractionId} from '@bpinc/lib-interaction-context'
import {
    CircleButton,
    IconCompleteTransferFilled,
    IconDialpad,
    IconLeave,
    IconMerge,
} from '@bpinc/lib-jskit-ui'
import {useOpenInteractionSlidingModal, useShowSlidingModalChecker} from '@bpinc/lib-sliding-modal-context'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {CompleteButton} from './CompleteButton'
import {ShowMoreButton} from './ShowMoreButton'


type Props = {
    onClose: (e: React.MouseEvent) => void
}
export const VoiceCallButtons: React.FC<Props> = ({onClose}) => {
    const {t} = useLocalizationState()
    const interactionId = useInteractionId()
    const openInteractionSlidingModal = useOpenInteractionSlidingModal()
    const showTransferModal = useShowSlidingModalChecker(interactionId, 'transfer')
    const showKeypadModal = useShowSlidingModalChecker(interactionId, 'keypad')
    const {phoneDeviceCapabilities} = useSessionState()
    const activeItem = useInteraction(interactionId)
    const isHost = activeItem?.isConferenceHost

    const {
        showRecordButton,
        isCallEstablished,
        isMuted,
        isRecording,
        isOnHold,
        isRecordingMuted,
        isCallConference,
        isLoading,
        handleHoldClick,
        handleMuteClick,
        handleMergeClick,
        handleTransferClick,
        handleLeaveConference,
        handleRecordClick,
    } = useCallButtonsHandlers()

    const toggleTransfer = useCallback(
        function toggleTransferCallback() {
            openInteractionSlidingModal(interactionId, 'transfer')
        },
        [interactionId, openInteractionSlidingModal],
    )

    const toggleKeypad = useCallback(
        function toggleKeypadCallback() {
            openInteractionSlidingModal(interactionId, 'keypad')
        },
        [interactionId, openInteractionSlidingModal],
    )

    const showConferenceButtons =
        (isCallConference && isHost) || showMergeButton(activeItem) || showTransferButton(activeItem)

    const holdButtonDisabled = !isCallEstablished ||
        (isOnHold ? !phoneDeviceCapabilities.canRetrieve : !phoneDeviceCapabilities.canHold)

    const callInDeliveredState = activeItem?.state === 'delivered' || activeItem?.state === 'hold'

    const renderLeaveIcon = () => {
        return (<IconLeave className="size-5 [&_path]:stroke-neutral-0" />)
    }

    return (
        <div className="flex flex-auto items-center justify-between">
            <div className="flex items-center gap-2.5">
                {callInDeliveredState && (
                    <HoldButton
                        iconClassName="icon-fade"
                        isActive={isOnHold}
                        isLoading={isLoading.onHold}
                        onClick={handleHoldClick}
                        holdStartTime={activeItem?.displayData?.holdStartTime}
                        disabled={holdButtonDisabled}
                    />
                )}

                {callInDeliveredState && showRecordButton && (
                    <RecordButton
                        isLoading={isLoading.recording}
                        isActive={isRecording}
                        onClick={handleRecordClick}
                        disabled={!isCallEstablished || isOnHold || isRecordingMuted}
                        data-testid="call-recording-button"
                        data-recording-active={isRecording || undefined}
                    />
                )}

                {callInDeliveredState && (
                    <>
                        <MuteButton
                            isActive={isMuted}
                            isLoading={isLoading.muted}
                            onClick={handleMuteClick}
                            disabled={!isCallEstablished}
                        />
                        <TransferButton
                            isActive={showTransferModal}
                            onClick={toggleTransfer}
                            iconClassName="icon-fade"
                        />
                    </>
                )}
                {callInDeliveredState && showConferenceButtons && (
                    <>
                        {showMergeButton(activeItem) && (
                            <CircleButton
                                onClick={handleMergeClick}
                                tooltip={{content: t.CALL_DETAILS_FORM.interaction_merge()}}
                                data-testid="merge-button"
                            >
                                <IconMerge className="size-4.5 icon-fade" />
                            </CircleButton>
                        )}
                        {showTransferButton(activeItem) && (
                            <CircleButton
                                onClick={handleTransferClick}
                                tooltip={{content: t.CALL_DETAILS_FORM.interaction_complete()}}
                                data-testid="complete-transfer-button"
                            >
                                <IconCompleteTransferFilled className="size-4.5 icon-fade" />
                            </CircleButton>
                        )}
                    </>
                )}
                {callInDeliveredState && <ShowMoreButton />}
            </div>
            <div className="flex h-7 items-center gap-2">
                <CircleButton
                    tooltip={{content: t.ADDRESS_BOOK_AREA.addressBook_dial()}}
                    isActive={showKeypadModal}
                    onClick={toggleKeypad}
                    data-testid="show-dialpad-button"
                    className="h-7 w-12 rounded-xs"
                >
                    <IconDialpad className="icon-fade" />
                </CircleButton>
                {isCallConference && isHost && (
                    <CircleButton
                        onClick={handleLeaveConference}
                        tooltip={{content: t.CALL_DETAILS_FORM.interaction_leaveConference()}}
                        data-testid="leave-conference-button"
                        className="h-7 w-12 rounded-xs"
                    >
                        <IconLeave className="size-5 icon-error" />
                    </CircleButton>
                )}
                <CompleteButton
                    isDestructive
                    item={activeItem}
                    handleCloseClick={onClose}
                    renderIcon={!isHost && isCallConference ? renderLeaveIcon : undefined}
                />
            </div>
        </div>
    )
}
