import React, {useEffect} from 'react'
import {useSetAtom} from 'jotai'

import {useActiveInteractionId} from '@bpinc/ad-interaction-state-context'
import {useContactDetailsOpener} from '@bpinc/ad-interaction-state-hooks'
import {isRecentCallConference} from '@bpinc/ad-interaction-state-methods'
import type {InitiateCall} from '@bpinc/ad-interaction-state-types'
import {formatDate, useEvent} from '@bpinc/lib-helpers'
import {assertDefined} from '@bpinc/lib-types-helpers'
import {type ChatParams, SelectionContainer} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'
import type {RecentItem, RecentPartyType} from '@bpinc/state-recent-types'

import {ServiceSelect} from '../../../components/ServiceSelect'
import {RecentConferenceItem} from './RecentConferenceItem'
import {stateAtom} from './stateAtom'


type CallConferenceModalContentProps = {
    recentItem: RecentItem
    onCall: (callParams: InitiateCall) => void
    onChat: (chatParams: ChatParams) => void
    transferMode?: 'messages' | 'calls'
}

export const RecentConferenceModalContent: React.FC<CallConferenceModalContentProps> = (
    {recentItem, ...props},
) => {
    assertDefined(recentItem)

    const {RECENT: i18n} = useLocalizationState('t')
    const activeInteractionId = useActiveInteractionId()
    const openContactDetails = useContactDetailsOpener()
    const dispatch = useSetAtom(stateAtom)

    const displayDate = formatDate(recentItem.time, 'LLL')

    const description = isRecentCallConference(recentItem)
        ? i18n.conference_call_details_description_text(displayDate)
        : i18n.conference_chat_details_description_text(displayDate)

    useEffect(() => {
        return () => dispatch({type: 'clearState'})
    })

    const handleItemClick = useEvent((party: RecentPartyType) => {
        dispatch({type: 'selectParty', payload: {party}})
    })

    return (
        <div className="flex flex-col gap-4">
            <div>{description}</div>
            <ServiceSelect itemId={activeInteractionId} />
            <SelectionContainer>
                {recentItem.parties.map(party => (
                    <RecentConferenceItem
                        key={party.phone}
                        party={party}
                        onClick={handleItemClick}
                        onDetails={openContactDetails}
                        {...props}
                    />
                ))}
            </SelectionContainer>
        </div>
    )
}
