import {useEffect} from 'react'

import type {DirectoryItem} from '@bpinc/ad-directory-state-types'
import {useDirectoryUIState} from '@bpinc/lib-communicator-ui-context'
import {getDirectoryItemUniqueId} from '@bpinc/lib-helpers-ui'


export function useDirectoryFirstFoundItemAutoSelector(list: DirectoryItem[]) {
    const {setFirstFoundItemId} = useDirectoryUIState()

    useEffect(() => {
        setFirstFoundItemId(getDirectoryItemUniqueId(list[0]))
    }, [list, setFirstFoundItemId])
}
