import {useMemo} from 'react'

import type {AnyEmiUser, DirectoryStateData, SearchDirectoryCategory} from '@bpinc/ad-directory-state-types'

import {teamFilter} from '../../utils'
import type {DirectoryFolderType} from './helpers'
import {directoryFolderTypes} from './helpers'


export function useUsersList(type: DirectoryFolderType, item: SearchDirectoryCategory, {
    users = [],
    categoryEntries = {},
    emiUsers = {},
}: Pick<DirectoryStateData, 'users' | 'categoryEntries' | 'emiUsers'>) {
    return useMemo(() => {
        switch (type) {
            case directoryFolderTypes.TEAM:
            case directoryFolderTypes.SEARCH_TEAM:
                return [...users]
                    .filter(teamFilter(item.id))
            case directoryFolderTypes.CATEGORY:
            case directoryFolderTypes.SEARCH_CATEGORY:
                return [...(categoryEntries[item.id] || [])].sort((a, b) => {
                    const aTitle = `${a.title} ${a.number ?? a.email}`.toLowerCase()
                    const bTitle = `${b.title} ${b.number ?? b.email}`.toLowerCase()

                    return stringComparison(aTitle, bTitle)
                })
            case directoryFolderTypes.EMI_CATEGORY:
                return [...(emiUsers as Record<string, AnyEmiUser[]>)[item.id] || []].sort((a, b) => {
                    return stringComparison(a.title || '', b.title || '')
                })
            default:
                return []
        }
    }, [categoryEntries, emiUsers, item.id, type, users])
}

function stringComparison(a: string, b: string) {
    if (a > b) {
        return 1
    }
    if (a < b) {
        return -1
    }
    return 0
}
