import type {SyntheticEvent} from 'react'
import {
    useCallback,
    useEffect,
    useMemo,
    useRef,
    useState,
} from 'react'
import produce from 'immer'

import type {CalendarContact, ContactType} from '@bpinc/ad-contacts-state-types'
import {formatFullName} from '@bpinc/ad-formatting-helpers'
import {useInteraction} from '@bpinc/ad-interaction-state-context'
import {getGlobalInteractionId, getServiceId, getServiceNameFromItem} from '@bpinc/ad-interaction-state-methods'
import {useSessionState} from '@bpinc/ad-session-context'
import {useAllTimezones, useTimezonesStateMethods} from '@bpinc/ad-timezones-state-context'
import type {CalendarEventDetails} from '@bpinc/cal-request-urls'
import {useCalendarStateMethods} from '@bpinc/cal-state-context'
import type {CalendarEventScreenPop} from '@bpinc/cal-state-types'
import type {SmCheckboxProps, SmInputProps, SmTextAreaProps} from '@bpinc/lib-external-ui-elements'
import {convertToDayjs} from '@bpinc/lib-helpers'
import {useInteractionId} from '@bpinc/lib-interaction-context'
import {useOpenInteractionSlidingModal, useShowSlidingModalChecker} from '@bpinc/lib-sliding-modal-context'
import type {DropdownProps} from '@bpinc/lib-ui-elements/'
import type {ContactRecord, RemindInUnit, RescheduleForm as RescheduleFormType} from '@bpinc/state-interaction-ui'
import {getEmptyItemUIState, useInteractionUIState} from '@bpinc/state-interaction-ui'

import {useContactRecordInitializer} from './useContactRecordInitializer'


type SubmittingState = 'idle' | 'loading' | 'error' | 'success'

export const dateFormat = 'DD-MM-YYYY'
export const timeFormat = 'HH:mm'

export function useRescheduleFormState() {
    const sessionData = useSessionState()
    const itemId = useInteractionId()
    const {saveCalendarEventDetails} = useCalendarStateMethods()
    const [submittingState, setSubmittingState] = useState<SubmittingState>('idle')
    const closeModalAfterSuccessTimeoutRef = useRef(-1)
    const {userId, firstName, lastName, displayNameFormat} = sessionData
    const {getItemState, setItemState} = useInteractionUIState()
    const activeItem = useInteraction(itemId)
    const openInteractionSlidingModal = useOpenInteractionSlidingModal()
    const showRescheduleModal = useShowSlidingModalChecker(itemId, 'reschedule')
    const activeItemId = activeItem?.id ?? ''

    const rescheduleForm =
        getItemState(activeItemId)?.rescheduleForm ??
        getEmptyItemUIState().rescheduleForm

    const {
        dateFrom,
        dateTo,
        timeFrom,
        timeTo,
        selectedTimezoneId,
        remindIn,
        remindInUnit,
        completed,
        title,
        contacts,
        notes,
    } = rescheduleForm

    const setRescheduleFormState = useCallback(
        function setRescheduleFormStateCallback(
            newRescheduleForm: Partial<RescheduleFormType>,
        ) {
            setItemState(activeItemId, state => ({
                ...state,
                rescheduleForm: {...state.rescheduleForm, ...newRescheduleForm},
            }))
        },
        [activeItemId, setItemState],
    )

    useEffect(() => {
        if (!showRescheduleModal) {
            clearTimeout(closeModalAfterSuccessTimeoutRef.current)
            setSubmittingState('idle')
        }
    }, [showRescheduleModal])

    useContactRecordInitializer({contacts, setRescheduleFormState})
    const allTimezones = useAllTimezones()
    const timezonesMethods = useTimezonesStateMethods()
    const handleDateChange = useCallback(
        (_e: SyntheticEvent, {value, name}: SmInputProps) => {
            setRescheduleFormState({[name]: value})
        },
        [setRescheduleFormState],
    )

    const handleContactRecordChange = useCallback(
        (index: number, {name, value}: SmInputProps) => {
            setRescheduleFormState(
                produce(rescheduleForm, darft => {
                    darft.contacts.splice(index, 1, {
                        ...darft.contacts[index],
                        [name]: value,
                    })
                }),
            )
        },
        [rescheduleForm, setRescheduleFormState],
    )

    const handleContactNameChangeFactory = useCallback(
        function handleContactNameChangeFactoryCallback(index: number) {
            return function handleContactNameChange(
                _evt: SyntheticEvent,
                {value}: SmInputProps,
            ) {
                handleContactRecordChange(index, {name: 'name', value})
                setRescheduleFormState({name: value})
            }
        },
        [handleContactRecordChange, setRescheduleFormState],
    )

    const handleContactPhoneChangeFactory = useCallback(
        function handleContactPhoneChangeFactoryCallback(index: number) {
            return function handleContactPhoneChange(
                _evt: SyntheticEvent,
                {value}: SmInputProps,
            ) {
                handleContactRecordChange(index, {name: 'phone', value})
            }
        },
        [handleContactRecordChange],
    )

    const handleContactPhoneTypeChangeFactory = useCallback(
        function handleContactPhoneTypeChangeFactoryCallback(index: number) {
            return function handleContactPhoneTypeChange(
                _evt: SyntheticEvent,
                {value}: DropdownProps,
            ) {
                handleContactRecordChange(index, {name: 'phoneType', value})
            }
        },
        [handleContactRecordChange],
    )

    const handleContactCreateContactChangeFactory = useCallback(
        function handleContactCreateContactChangeFactoryCallback(index: number) {
            return function handleContactCreateContactChange(
                evt: SyntheticEvent,
                {value}: DropdownProps,
            ) {
                handleContactRecordChange(index, {name: 'createContact', value})
            }
        },
        [handleContactRecordChange],
    )

    const handleContactPhoneIdChangeFactory = useCallback(
        function handleContactCreateContactChangeFactoryCallback(index: number) {
            return function handleContactCreateContactChange(
                evt: SyntheticEvent,
                {value}: DropdownProps,
            ) {
                handleContactRecordChange(index, {name: 'phoneId', value})
            }
        },
        [handleContactRecordChange],
    )

    const handleRemindAmountChange = useCallback(
        function handleRemindAmountChangeCallback(
            event: SyntheticEvent,
            {value}: SmInputProps,
        ) {
            setRescheduleFormState({remindIn: value})
        },
        [setRescheduleFormState],
    )

    const handleRemindUnitsChange = useCallback(
        function handleRemindAmountChangeCallback(
            event: SyntheticEvent,
            {value}: DropdownProps,
        ) {
            setRescheduleFormState({remindInUnit: value as RemindInUnit})
        },
        [setRescheduleFormState],
    )

    const handleCompletedCheckboxChange = useCallback(
        function handleRemindAmountChangeCallback(
            event: SyntheticEvent,
            {checked}: SmCheckboxProps,
        ) {
            setRescheduleFormState({completed: checked})
        },
        [setRescheduleFormState],
    )
    const handleTitleChange = useCallback(
        function handleRemindAmountChangeCallback(
            event: SyntheticEvent,
            {value}: SmInputProps,
        ) {
            setRescheduleFormState({title: value})
        },
        [setRescheduleFormState],
    )

    const handleNotesChange = useCallback(
        function handleRemindAmountChangeCallback(
            event: SyntheticEvent,
            {value}: SmTextAreaProps,
        ) {
            setRescheduleFormState({notes: value as string})
        },
        [setRescheduleFormState],
    )

    const handleTimeZoneSelect = useCallback(
        (timezoneId: string) => {
            if (timezoneId) {
                const timezoneData = allTimezones.find(tz => tz.id === timezoneId)
                if (timezoneData) {
                    timezonesMethods.addToFavorites(timezoneData)

                    const selectedTimezoneData = allTimezones.find(
                        tz => tz.id === selectedTimezoneId,
                    )
                    if (selectedTimezoneData) {
                        const mOffset = Number(
                            timezoneData.standardOffset - selectedTimezoneData.standardOffset,
                        )
                        const dateFromFormatted = convertToDayjs(
                            `${dateFrom} ${timeFrom}`,
                            `${dateFormat} ${timeFormat}`,
                        )
                        const dateToFormatted = convertToDayjs(
                            `${dateTo} ${timeTo}`,
                            `${dateFormat} ${timeFormat}`,
                        )
                        if (
                            dateFromFormatted.isValid() &&
                            dateToFormatted.isValid() &&
                            !Number.isNaN(mOffset)
                        ) {
                            dateToFormatted.add(
                                dateFromFormatted.isAfter(dateToFormatted) ? 1 : 0,
                                'd',
                            )
                            dateFromFormatted.add(mOffset, 'm')
                            dateToFormatted.add(mOffset, 'm')
                            setRescheduleFormState({
                                dateFrom: dateFromFormatted.format(dateFormat),
                                dateTo: dateToFormatted.format(dateFormat),
                                timeFrom: dateFromFormatted.format(timeFormat),
                                timeTo: dateToFormatted.format(timeFormat),
                            })
                        }
                    }
                }
                setRescheduleFormState({selectedTimezoneId: timezoneId})
            }
        },
        [
            allTimezones,
            setRescheduleFormState,
            timezonesMethods,
            selectedTimezoneId,
            dateFrom,
            timeFrom,
            dateTo,
            timeTo,
        ],
    )

    const handleCancelClick = useCallback(
        function handleCancelClickCallback() {
            setRescheduleFormState(getEmptyItemUIState().rescheduleForm)
            openInteractionSlidingModal(itemId, 'reschedule')
        },
        [setRescheduleFormState, itemId, openInteractionSlidingModal],
    )

    const handleSubmitClick = useCallback(
        function handleSubmitClickCallback() {
            const params = rescheduleFormStateToPostCalendarEventParams(
                rescheduleForm,
            )

            if (params && params.title) {
                setSubmittingState('loading')
                saveCalendarEventDetails(params)
                    .then(() => {
                        setSubmittingState('success')
                        setRescheduleFormState(getEmptyItemUIState().rescheduleForm)
                        closeModalAfterSuccessTimeoutRef.current = setTimeout(() => {
                            openInteractionSlidingModal(itemId, 'reschedule')
                            setSubmittingState('idle')
                        }, 2500)
                    })
                    .catch(() => {
                        setSubmittingState('error')
                    // TODO: Add error handling UI.
                    })
            }

            function rescheduleFormStateToPostCalendarEventParams(
                currentRescheduleForm: RescheduleFormType,
            ): CalendarEventDetails | undefined {
                const dateFromInner = convertToDayjs(
                    `${currentRescheduleForm.dateFrom} ${currentRescheduleForm.timeFrom}`,
                    `${dateFormat} ${timeFormat}`,
                )

                const dateToInner = convertToDayjs(
                    `${currentRescheduleForm.dateTo} ${currentRescheduleForm.timeTo}`,
                    `${dateFormat} ${timeFormat}`,
                )

                const startTime = dateFromInner.isValid() ? dateFromInner.valueOf() : undefined

                const endTime = dateToInner.isValid() ? dateToInner.valueOf() : undefined

                const globalInteractionId = getGlobalInteractionId(activeItem)
                const serviceId = getServiceId(activeItem)

                const firstContact = currentRescheduleForm.contacts[0]

                const {listLinkId, listRecordId} = activeItem?.workitemData || {}

                const screenPopUrl = activeItem?.screenPopData?.web?.url
                const screenPop: CalendarEventScreenPop | undefined = screenPopUrl
                    ? {
                        web: {
                            type: 'web' as const,
                            action: 'OPEN_WEB_PAGE' as const,
                            URL: screenPopUrl,
                            popup: 'false',
                            popUponAnswer: 'false',
                            keepPopupOpenAfterFinish: 'false',
                            secondaryUrls: {},
                        },
                        salesforce: {
                            type: 'salesforce',
                            object_id: screenPopUrl,
                            action: 'SHOW_OBJECT',
                        },
                    }
                    : undefined

                if (
                    startTime &&
                    endTime &&
                    userId &&
                    globalInteractionId &&
                    serviceId &&
                    firstContact
                ) {
                    return {
                        completed: currentRescheduleForm.completed,
                        contacts: firstContact.contactData
                            ? [
                                contactDataToCalendarContact(
                                    firstContact.contactData,
                                    firstContact,
                                ),
                            ]
                            : [
                                {
                                    addPhone: false,
                                    alerts: 0,
                                    calendar_phone: {
                                        id: undefined,
                                        type: firstContact.phoneType,
                                        phone: firstContact.phone,
                                    },
                                    contact_type: 'STORED' as const,
                                    create: false,
                                    first_name: firstContact.name,
                                    phones: [
                                        {
                                            id: undefined,
                                            type: firstContact.phoneType,
                                            phone: firstContact.phone,
                                        },
                                    ],
                                    _id: {oid: null},
                                },
                            ],
                        email: activeItem?.displayData.email,
                        startTime,
                        endTime,
                        globalInteractionId,
                        listLinkId,
                        listRecordId,
                        name: activeItem?.displayData.displayName,
                        notes: currentRescheduleForm.notes,
                        number: activeItem?.displayData.number,
                        remindIn: currentRescheduleForm.remindIn,
                        remindInUnit: currentRescheduleForm.remindInUnit,
                        screenpop: JSON.stringify(screenPop),
                        screenpopSize: screenPop ? 1 : 0,
                        serviceId,
                        serviceName: getServiceNameFromItem(activeItem),
                        title: currentRescheduleForm.title,
                        userId,
                        userName: formatFullName(displayNameFormat, firstName, lastName),
                    }
                }
            }

            function contactDataToCalendarContact(
                contactData: ContactType,
                contactRecord: ContactRecord,
            ): CalendarContact {
                return {
                    addPhone: false, //TODO: implement functionality
                    addresses: contactData.addresses &&
                        contactData.addresses.map(({addressLine1, addressLine2, ...rest}) => ({
                            ...rest,
                            address_line1: addressLine1,
                            address_line2: addressLine2,
                        })),
                    alerts: contactData.alerts,
                    calendar_phone: {
                        id: undefined,
                        type: contactRecord.phoneType,
                        phone: contactRecord.phone,
                    },
                    company_id: contactData.companyId,
                    company_name: contactData.companyName,
                    contact_type: 'STORED' as const,
                    create: false,
                    dob: contactData.dob,
                    emails: contactData.emails &&
                        contactData.emails.map(e => ({
                            id: e.id,
                            type: e.type,
                            email_address: e.emailAddress,
                        })),
                    first_name: contactData.firstName,
                    last_name: contactData.lastName,
                    phones: contactData.phones,
                    segment: contactData.segment,
                    title: contactData.title,
                    web_url: contactData.webUrl,
                    _id: contactData.Id,
                }
            }
        },
        [
            activeItem,
            displayNameFormat,
            firstName,
            lastName,
            rescheduleForm,
            saveCalendarEventDetails,
            setRescheduleFormState,
            openInteractionSlidingModal,
            userId,
        ],
    )

    return useMemo(
        function memoizeRescheduleFormState() {
            return {
                dateFrom,
                dateTo,
                timeFrom,
                timeTo,
                selectedTimezoneId,
                remindIn,
                remindInUnit,
                completed,
                contacts,
                title,
                notes,
                submittingState,
                setSubmittingState,
                handlers: {
                    handleDateChange,
                    handleTimeZoneSelect,
                    handleRemindAmountChange,
                    handleRemindUnitsChange,
                    handleCompletedCheckboxChange,
                    handleContactRecordChange,
                    handleTitleChange,
                    handleCancelClick,
                    handleSubmitClick,
                    handleNotesChange,
                },
                contactHandlersFactories: {
                    handleContactNameChangeFactory,
                    handleContactPhoneChangeFactory,
                    handleContactPhoneTypeChangeFactory,
                    handleContactCreateContactChangeFactory,
                    handleContactPhoneIdChangeFactory,
                },
            }
        },
        [
            completed,
            contacts,
            dateFrom,
            dateTo,
            handleCancelClick,
            handleCompletedCheckboxChange,
            handleContactCreateContactChangeFactory,
            handleContactNameChangeFactory,
            handleContactPhoneChangeFactory,
            handleContactPhoneIdChangeFactory,
            handleContactPhoneTypeChangeFactory,
            handleContactRecordChange,
            handleDateChange,
            handleNotesChange,
            handleRemindAmountChange,
            handleRemindUnitsChange,
            handleSubmitClick,
            handleTimeZoneSelect,
            handleTitleChange,
            notes,
            remindIn,
            remindInUnit,
            selectedTimezoneId,
            submittingState,
            timeFrom,
            timeTo,
            title,
        ],
    )
}
