import React, {useMemo} from 'react'

import {PhoneTypes} from '@bpinc/ad-contacts-state-types'
import {useFormatting} from '@bpinc/ad-formatting-context'
import {SmForm, SmInput} from '@bpinc/lib-external-ui-elements'
import {Dropdown} from '@bpinc/lib-ui-elements'
import type {ContactRecord as ContactRecordType} from '@bpinc/state-interaction-ui'
import {useLocalizationState} from '@bpinc/state-localization-context'

import type {ExistingContactRecordProps} from './ExistingContactRecord'


interface OtherPhoneFormGroupProps {
    contact: ContactRecordType
    contactHandlers: ExistingContactRecordProps['contactHandlers']
}

export const OtherPhoneFormGroup: React.FC<OtherPhoneFormGroupProps> = ({contact, contactHandlers}) => {
    const {t} = useLocalizationState()
    const {formatPhoneType} = useFormatting()

    const options = useMemo(function memoizePhoneTypeOptions() {
        const contactPhoneTypes = Object.values(PhoneTypes)

        return contactPhoneTypes.map(phoneType => ({value: phoneType, text: formatPhoneType(phoneType)}))
    }, [formatPhoneType])

    return (
        <SmForm.Group className="flex items-center">
            <SmForm.Field
                value={contact.phone}
                onChange={contactHandlers.handleContactPhoneChange}
                placeholder={t.CALENDAR_EVENT_FORM.contact_on_label()}
                control={SmInput}
            />
            <SmForm.Field
                value={contact.phoneType}
                control={Dropdown}
                options={options}
                onChange={contactHandlers.handleContactPhoneTypeChange}
            />
        </SmForm.Group>
    )
}
