import React, {useMemo} from 'react'
import {useContext} from 'use-context-selector'

import type {FormDefinitionFieldKeyValueList, FormDefinitionPageItem} from '@bpinc/ad-request-urls'
import {getPageItemCSSProperties} from '@bpinc/lib-form-builder'

import {FormBuilderContext} from '../../FormBuilder'
import {SelectField as SelectFieldBase} from '../common/SelectField'
import {ViewItem} from '../common/ViewItem'


export const SelectField: React.FC<{
    pageItem: FormDefinitionPageItem
    field: FormDefinitionFieldKeyValueList
}> = ({
    pageItem,
    field,
}) => {
    const {
        form,
        view,
    } = useContext(FormBuilderContext)

    const options = useMemo(() => field.listNameDataItems.map(option => ({
        value: option.id,
        label: option.name,
    }) || []), [field])

    const editStyle = getPageItemCSSProperties(pageItem.editStyle)

    if (view) {
        const selectedOptionId = form.getValues()[field.name || '']
        const viewValue = options?.find(option => option.value === selectedOptionId)?.label
        const viewStyle = getPageItemCSSProperties(pageItem.style)

        if (field.hideEmpty && !selectedOptionId) {
            return null
        }

        return (
            <ViewItem
                viewStyle={viewStyle}
                label={field.label || ''}
                value={viewValue}
            />
        )
    }

    return (
        <SelectFieldBase
            name={field.name || ''}
            placeholder={field.emptyText || ''}
            label={field.label || ''}
            required={field.required}
            control={form.control}
            style={editStyle}
            options={options}
            className="flex items-center gap-x-[--form-cellPadding] gap-y-[--form-rowHeight]"
            labelClassName="w-[--form-fieldLabelWidth] min-w-[--form-fieldLabelWidth] max-w-[--form-fieldLabelWidth]"
        />
    )
}
