import React from 'react'

import {useInteractionProp} from '@bpinc/ad-interaction-state-context'
import {isCallConference, isChatConference, isNeedToShowWorkitemUI} from '@bpinc/ad-interaction-state-methods'
import {useInteractionId} from '@bpinc/lib-interaction-context'

import {ActiveItem} from './ActiveItem'
import {CallConferenceParties} from './CallConferenceParties'
import {ChatConferenceParties} from './ChatConferenceParties'
import {WorkitemDisplayContent} from './WorkitemDisplayContent'


export const ActiveInteractionTop: React.FC = React.memo(() => {
    const interactionId = useInteractionId()

    const activeItemIsCallConference = useInteractionProp(interactionId, isCallConference)
    const activeItemIsChatConference = useInteractionProp(interactionId, isChatConference)
    const parties = useInteractionProp(interactionId, activeItem => activeItem?.parties)
    const isHost = useInteractionProp(interactionId, activeItem => activeItem?.isConferenceHost)
    const needToShowWorkitemUI = useInteractionProp(interactionId, isNeedToShowWorkitemUI)

    if (needToShowWorkitemUI) {
        return <WorkitemDisplayContent />
    }

    return (
        <>
            {!activeItemIsChatConference && !activeItemIsCallConference && <ActiveItem />}
            {activeItemIsCallConference && (
                <CallConferenceParties isHost={isHost} callParties={parties} />
            )}
            {activeItemIsChatConference && <ChatConferenceParties />}
        </>
    )
})
