import type {FC} from 'react'
import React, {useCallback} from 'react'

import {useContactDisplayName, useGetAvatarUrl} from '@bpinc/ad-contacts-state-hooks'
import {formatPhone} from '@bpinc/ad-formatting-helpers'
import {useInteraction} from '@bpinc/ad-interaction-state-context'
import {isAfterCallWork} from '@bpinc/ad-interaction-state-methods'
import {Avatar} from '@bpinc/lib-avatar-ui'
import {DisconnectButton} from '@bpinc/lib-interaction-buttons/src/DisconnectButton'
import {useInteractionId} from '@bpinc/lib-interaction-context'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {assertDefined} from '@bpinc/lib-types-helpers'

import {useDisplayNameParticipant} from './helpers/useDisplayNameParticipant'
import type {ParticipantItem} from './helpers'


type Props = {
    isChat?: boolean
    item: ParticipantItem
    onRemoveItem: (id: string) => void
    canRemove?: boolean
}

export const InteractionParticipantsListItem: FC<Props> = ({item, onRemoveItem, isChat, canRemove}) => {
    const {phone, id, userId, contactId, email, is_qm} = item
    const contactDisplayName = useContactDisplayName(contactId)
    const getDisplayInfo = useDisplayNameParticipant({isChat})
    const {firstName, lastName, displayName, showPhone, showEmail} = getDisplayInfo(item, contactDisplayName)
    const getAvatarUrl = useGetAvatarUrl()
    const interactionId = useInteractionId()
    const activeItem = useInteraction(interactionId)
    assertDefined(activeItem)

    const avatarUrl = getAvatarUrl({userId, contactId})
    const isSupervisor = is_qm === '1'
    const showDisconnectButton = isChat ? !isAfterCallWork(activeItem) && userId && !isSupervisor : canRemove
    const avatarType = userId ? 'user' : contactId ? 'contact' : 'unknown'

    const handleDisconnect = useCallback(() => {
        onRemoveItem(id)
    }, [onRemoveItem, id])

    return (
        <div
            tabIndex={0}
            className={tcn('group flex cursor-pointer items-center justify-between gap-2 rounded-xs',
                'px-2 py-1 transition-colors hover:state-layer-bg-dark-on-white-1 focus:state-layer-bg-dark-on-white-1',
                'active:state-layer-bg-dark-on-white-1',
            )}
        >
            <div className="flex items-center">
                <Avatar
                    size="16"
                    headPhones={!!userId}
                    url={avatarUrl}
                    firstName={firstName}
                    lastName={lastName}
                    type={avatarType}
                />
                <div className="ml-1 flex gap-x-1 overflow-hidden">
                    <div className="flex items-center overflow-hidden">
                        <div
                            className="truncate font-semibold caption-s text-primary-on-primary"
                            data-testid="display-name"
                        >
                            {displayName}
                        </div>
                        {/*TO DO: add this button when designer will add in layout*/}
                        {/*    <InteractionFavoriteButton interaction={activeItem} party={item.party} />*/}
                    </div>

                    {showPhone && (
                        <div
                            className="truncate caption-s text-primary-on-primary"
                            data-testid="phone"
                        >
                            {userId
                                ? phone
                                : formatPhone(phone)
                            }
                        </div>
                    )}
                    {showEmail && (
                        <div
                            className="truncate caption-s text-primary-on-primary"
                            data-testid="email"
                        >
                            {email}
                        </div>
                    )}
                </div>
            </div>
            {showDisconnectButton && (
                <DisconnectButton
                    onClick={handleDisconnect}
                    className="opacity-0 hover:opacity-100 focus:opacity-100 active:opacity-100 group-hover:opacity-100 group-focus:opacity-100"
                />
            )}
        </div>
    )
}
