import type {ContactDetailsType} from '@bpinc/ad-contacts-state-types'


export const MAX_CONTACTS_FOR_MERGE = 3
export const DEFAULT_SEARCH_TAB: SearchPageTab = {id: 'search', type: 'search', title: '', closeable: false}

export type SearchPageTab = ContactTab | CaseTab | SearchTab | CreateContactTab | MergeContactsTab

export interface ContactTab {
    id: string
    title: string
    closeable: true
    type: 'contact'
    props: {
        editMode?: boolean
        onMerge: (() => void) | undefined
        onEditMode?: (edit: boolean) => void
    }
}

interface CaseTab {
    id: string
    title: string
    closeable: true
    type: 'case'
}

interface SearchTab {
    id: string
    title: string
    closeable: false
    type: 'search'
}

export interface CreateContactTab {
    id: string
    title: string
    closeable: true
    type: 'create_contact'
    props: {
        initialData?: ContactDetailsType
        onContactCreated?: (contactId: string) => void
    }
}

interface MergeContactsTab {
    id: string
    title: string
    closeable: false
    type: 'merge_contacts'
}

