import type {FC} from 'react'
import React from 'react'
import {Button} from '@servicepattern/ui'
import {useAtomValue, useSetAtom} from 'jotai'
import {flow} from 'lodash-es'

import {useInteractionProp} from '@bpinc/ad-interaction-state-context'
import {getInteractionStepId} from '@bpinc/ad-interaction-state-methods'
import {
    usePrerecordedMessagesState,
    usePrerecordedMessagesStateMethods,
} from '@bpinc/ad-prerecorded-messages-state-context'
import {useInteractionId} from '@bpinc/lib-interaction-context'
import {Modal} from '@bpinc/lib-jskit-ui'
import {assertDefined} from '@bpinc/lib-types-helpers'
import {useLocalizationState} from '@bpinc/state-localization-context'
import type {CommonModalProps} from '@bpinc/state-modals-types'

import {modalSelectedMessageAtom, selectedMessageStateAtom} from './atoms'
import {PrerecordedList} from './PrerecordedList'


export const PrerecordedMessagesModal: FC<CommonModalProps> = ({
    onClose,
    ...props
}) => {
    const selectedMessage = useAtomValue(modalSelectedMessageAtom)

    const t = useLocalizationState('t')
    const interactionId = useInteractionId()
    const interactionStepId = useInteractionProp(
        interactionId,
        getInteractionStepId,
    )
    const playingPrompts = usePrerecordedMessagesState('playingPrompts')

    const playingPromptId = playingPrompts[interactionStepId]?.promptId

    const {playPrompt, stopPlayingPrompt, stopAndPlayPrompt} =
    usePrerecordedMessagesStateMethods()

    const dispatch = useSetAtom(selectedMessageStateAtom)

    function selectedPromptIsPlaying() {
        return selectedMessage && selectedMessage.id === playingPromptId
    }

    function onPlayClick() {
        assertDefined(selectedMessage)

        if (selectedPromptIsPlaying()) {
            stopPlayingPrompt(interactionStepId)
        } else {
            dispatch({type: 'selectMessage', payload: {selectedMessage}})

            return playingPromptId
                ? stopAndPlayPrompt({itemId: interactionId, interactionStepId, promptId: selectedMessage.id})
                : playPrompt({
                    itemId: interactionId,
                    interactionStepId,
                    promptId: selectedMessage.id,
                })
        }
    }

    return (
        <Modal {...props} onClose={onClose}>
            <Modal.Header>{t.VOICE_CALL.prerecorded_messages()}</Modal.Header>
            <Modal.Content className="px-3 pt-0 [&_ul]:m-0 [&_ul]:p-0">
                <PrerecordedList />
            </Modal.Content>
            <Modal.Actions>
                <Button variant="ghost" onClick={() => onClose()}>
                    {t.GLOBAL.cancel()}
                </Button>
                {!playingPromptId && (
                    <Button
                        variant="secondary"
                        onClick={flow(onPlayClick, onClose)}
                    >
                        {t.VOICE_CALL.prerecorded_messages_play_and_leave()}
                    </Button>
                )}
                <Button onClick={onPlayClick} variant="primary">
                    {selectedPromptIsPlaying()
                        ? t.GLOBAL.stop()
                        : t.GLOBAL.play()}
                </Button>
            </Modal.Actions>
        </Modal>
    )
}
