import type {ComponentProps, HTMLProps, ReactNode} from 'react'
import React from 'react'

import type {TextAreaProps as BaseTextareaProps} from '@bpinc/lib-jskit-ui'
import {TextArea as TextAreaUI} from '@bpinc/lib-jskit-ui'
import {tcn} from '@bpinc/lib-tailwind-classnames'


interface DirectProps {
    label?: ReactNode
    labelClassName?: string
    wrapperClassName?: string
    error?: ReactNode
    errorClassName?: string
}

type TextAreaBaseProps = Omit<ComponentProps<'textarea'>, 'value'> &
Omit<BaseTextareaProps, keyof Omit<HTMLProps<HTMLTextAreaElement>, 'value'>>

export type TextAreaProps = DirectProps & Omit<TextAreaBaseProps, keyof DirectProps | 'inputRef' | 'ref'> & { inputClassName?: string }

export const TextArea = React.forwardRef<HTMLTextAreaElement, TextAreaProps>((props, ref) => {
    const {
        id,
        className,
        inputClassName,
        label,
        error,
        value,
        onChange,
        onKeyDown,
        onBlur,
        minRows = 1,
        maxRows = 8,
        placeholder = '',
        labelClassName,
        wrapperClassName,
        errorClassName,
        ...other
    } = props

    return (
        <div className={tcn(wrapperClassName, 'flex w-full flex-row')}>
            {label && <label className={tcn('my-1 block', labelClassName)} htmlFor={id}>{label}</label>}
            <div className="flex w-full flex-col">
                <div
                    className={tcn(
                        className,
                        'box-border w-full flex-1 rounded border border-[#abb6bb] bg-white p-2',
                        'focus:border-[#4b85a0] focus:shadow-inner',
                        'hover:border-[#96a7b0] hover:shadow-inner',
                        {
                            'self-end border-[#ff8997] bg-[#ffdee2] text-red-500': error,
                        },
                    )}
                >
                    <TextAreaUI
                        ref={ref}
                        className={tcn(
                            inputClassName,
                            'm-0 box-border block w-full resize-none overflow-auto border-none bg-transparent p-0 leading-tight focus:outline-none',
                        )}
                        value={value}
                        onChange={onChange}
                        onKeyDown={onKeyDown}
                        onBlur={onBlur}
                        minRows={minRows}
                        maxRows={maxRows}
                        placeholder={placeholder}
                        id={id}
                        {...other}
                    />
                </div>
                {error && <p className={errorClassName} role="alert aria-atomic">{error}</p>}
            </div>
        </div>
    )
})
