import {fromPairs} from 'lodash-es'

import {digitizePhone} from '@bpinc/ad-interaction-state-methods'


export * from './checkers'
export * from './getters'

const alphabet = 'abcdefghijklmnopqrstuvwxyz'
const digits = '0123456789'
const allowedSpecialKeys = '+*#:,'

const letterByCode: Record<string, string> = fromPairs([
    ...Array.from(alphabet).map(letter => [`Key${letter.toUpperCase()}`, letter]),
    ...Array.from(digits).map(digit => [`Digit${digit}`, digit]),
    ...Array.from(digits).map(digit => [`Numpad${digit}`, digit]),
])

export const getLetter = (key: string, code: string): string | undefined => {
    if (allowedSpecialKeys.indexOf(key) !== -1) {
        return key
    }
    return letterByCode[code]
}

export const sanitizePhone = (value: string): string => {
    const splitParts = value.split(':')
    const colonValue = splitParts.length > 1 ? splitParts[0] + ':' + splitParts.slice(1).join() : value
    return digitizePhone((colonValue[0] === '+' ? '+' : '') + colonValue.replace(/[^\d*#,:a-z|A-Z]/g, ''))
}
