import type {ReactElement} from 'react'
import React from 'react'
import type {FieldValues} from 'react-hook-form'

import type {
    FieldImplementationProps,
    FieldProps,
    FieldRawProps,
    FieldRenderImplementationProps,
} from '@bpinc/lib-forms-base'
import {FieldConstructor, FieldRawConstructor} from '@bpinc/lib-forms-base'
import {uuid} from '@bpinc/lib-uuid'

import type {DropdownProps} from './Dropdown'
import {Dropdown} from './Dropdown'


type SelectFieldProps = FieldImplementationProps<
    Omit<DropdownProps, 'onChange' | 'label'> & {
        label?: string
    }>

function renderElement<TFormValues extends FieldValues>({
    ref,
    options,
    onChange,
    label,
    ...props
}: FieldRenderImplementationProps<SelectFieldProps, TFormValues>): ReactElement {
    const ariaId1 = uuid()

    return (
        <Dropdown
            {...props}
            inputRef={ref}
            label={label}
            placeholder={label}
            errorClassName="self-end text-red-500"
            aria-labelledby={ariaId1}
            options={options}
            onChange={onChange}
        />
    )
}

export function SelectFieldRaw<TFormValues extends FieldValues>(props: FieldRawProps<SelectFieldProps, TFormValues>): ReactElement {
    return <FieldRawConstructor {...props} renderElement={renderElement} />
}

export function SelectField<TFormValues extends FieldValues>(props: FieldProps<SelectFieldProps, TFormValues>): ReactElement {
    return <FieldConstructor {...props} renderElement={renderElement} />
}

