import React from 'react'

import {useDIDNumberState} from '@bpinc/ad-did-number-context'
import {isPhone} from '@bpinc/ad-formatting-helpers'
import {useActivityHistoryOpener, useCallHandler} from '@bpinc/ad-interaction-state-hooks'
import type {InitiateCall} from '@bpinc/ad-interaction-state-types'
import {useServiceState} from '@bpinc/ad-service-context'
import {useSessionState} from '@bpinc/ad-session-context'
import {useRecentUIState} from '@bpinc/lib-communicator-ui-context'
import {useEvent} from '@bpinc/lib-helpers'
import {SearchField} from '@bpinc/lib-jskit-ui'
import {useNotificationsState} from '@bpinc/lib-notifications-context'
import {SearchForInteractionControls} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'
import {useRecentState} from '@bpinc/state-recent-context'


export const RecentSearchControls: React.FC = () => {

    const {RECENT: i18n} = useLocalizationState('t')
    const phoneDeviceCapabilities = useSessionState('phoneDeviceCapabilities')

    const {selected, searchValue, setSearchValue} = useRecentUIState()
    const {recentList} = useRecentState()
    const {selectedService} = useServiceState()
    const {selectedDIDNumber} = useDIDNumberState()
    const {hideNotificationsByTag} = useNotificationsState('methods')

    const handleCall = useCallHandler()
    const openActivityHistory = useActivityHistoryOpener()

    const handleChange = useEvent((e: React.ChangeEvent<HTMLInputElement>) => {
        setSearchValue(e.target.value)
    })

    const tryInitiateCall = useEvent((callParams: InitiateCall) => {
        handleCall({
            ...callParams,
            navigateToInteractionsPage: true,
        })
    })

    const onCall = useEvent(function dialRecent() {
        setSearchValue('')

        const selectedItem = selected && recentList.find(i => selected === i.itemId)

        if (!selectedItem) {
            if (!searchValue) {
                return
            }

            return tryInitiateCall({
                phone: searchValue,
                serviceId: selectedService,
                accessNumberId: selectedDIDNumber,
                origination: 'recent',
            })
        } else {
            tryInitiateCall({
                phone: selectedItem.number ?? '',
                firstName: selectedItem.firstName,
                lastName: selectedItem.lastName,
                serviceId: selectedService ?? selectedItem.serviceId,
                contactId: selectedItem.contactId,
                userId: selectedItem.userId,
                accessNumberId: selectedDIDNumber,
                origination: 'recent',
            })
        }
    })

    const onChat = useEvent(function chatRecent() {
        setSearchValue('')

        const selectedItem = recentList.find(i => selected === i.itemId)

        if (!selectedItem) {
            if (!searchValue) {
                return
            }

            hideNotificationsByTag('interaction-init')

            return openActivityHistory({
                phone: searchValue,
            })
        }

        // TODO: investigate do we need it or with which tag?
        hideNotificationsByTag('interaction-init')

        if (selectedItem.userId || selectedItem.contactId || selectedItem.number) {
            openActivityHistory({
                contactId: selectedItem.contactId,
                userId: selectedItem.userId,
                phone: selectedItem.number,
                firstName: selectedItem.firstName,
                lastName: selectedItem.lastName,
                origination: 'recent',
                emiPhotoUrl: selectedItem.photoUrl,
                emiUserType: selectedItem.userType,
                previousActivity: selectedItem.globalInteractionId
                    ? {
                        globalInteractionId: selectedItem.globalInteractionId,
                        mediaType: selectedItem.media,
                    }
                    : undefined,
            })
        }
    })

    return (
        <div className="flex items-start gap-2.5">
            <SearchField
                value={searchValue}
                onChange={handleChange}
                layoutClassName="grow"
                data-testid="search-input"
                aria-label={i18n.search_field_placeholder()}
                placeholder={i18n.search_field_placeholder()}
            />
            <SearchForInteractionControls
                searchQuery={searchValue}
                showCallButton={!!phoneDeviceCapabilities.canDial}
                callButtonDisabled={!selected && !isPhone(searchValue)}
                chatButtonDisabled={!selected && !isPhone(searchValue)}
                onCallClick={onCall}
                onChatClick={onChat}
            />
        </div>
    )
}
