import React from 'react'

import {SelectionContainer} from '@bpinc/lib-ui-elements'

import {PhoneNumberItem} from './PhoneNumberItem'
import {usePreviewRecordPhoneNumbers} from './usePreviewRecordPhoneNumbers'


export const WorkitemContacts: React.FC = () => {

    const phoneNumbers = usePreviewRecordPhoneNumbers()

    return (
        <SelectionContainer className="scrollbar-on-hover mx-4 mt-2 overflow-auto">
            {/* without this extra wrapper with filled background "scrollbar-on-hover" produces visual artifacts */}
            <div className="bg-primary-background">
                {phoneNumbers.map(numberData => (
                    <PhoneNumberItem key={numberData.number} {...numberData} />
                ))}
            </div>
        </SelectionContainer>
    )
}
