import type {PropsWithChildren, ReactElement, ReactFragment} from 'react'
import React from 'react'

import type {AuthorizedConnectors, BaseConnectors} from '@bpinc/ad-connected-state-types'
import type {PlatformSpecific} from '@bpinc/ad-platform-specific-types'
import type {RemoteName} from '@bpinc/lib-remotes-configs'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {AppStyleSelector} from '@bpinc/lib-ui-elements'
import type {AppType} from '@bpinc/state-lookup'

import {AuthorizationUIRouter} from './AuthorizationUIRouter'
import {TopLevelProviders} from './TopLevelProviders'


export type BaseConnectorsConsumerComponent = React.FC<PropsWithChildren<{baseConnectors: BaseConnectors}>>

type Props = {
    app: AppType
    platformSpecific: PlatformSpecific
    children: (
        baseConnectors: BaseConnectors,
        authorizedConnectors: AuthorizedConnectors,
    ) => ReactElement | ReactFragment
    BaseConnectorsConsumer?: BaseConnectorsConsumerComponent
    className?: string
    appRemoteName: RemoteName
}

export const AuthorizationModule: React.FC<Props> = ({
    app,
    platformSpecific,
    children,
    BaseConnectorsConsumer,
    className,
    appRemoteName,
}) => {
    return (
        <AppStyleSelector app={appRemoteName}>
            <div className={tcn('h-full', className)}>
                <TopLevelProviders
                    app={app}
                    platformSpecific={platformSpecific}
                    BaseConnectorsConsumer={BaseConnectorsConsumer}
                >
                    {baseConnectors => (
                        <AuthorizationUIRouter baseConnectors={baseConnectors}>
                            {children}
                        </AuthorizationUIRouter>
                    )}
                </TopLevelProviders>
            </div>
        </AppStyleSelector>
    )
}
