import React, {useMemo} from 'react'

import {useInteractions, useModalInteractionIds} from '@bpinc/ad-interaction-state-context'
import {isDeliveryPending, isExternalChat, isInternalChat, isWorkItem} from '@bpinc/ad-interaction-state-methods'
import type {Item} from '@bpinc/ad-interaction-state-types'
import {SmList} from '@bpinc/lib-external-ui-elements'

import {NotificationItem} from './NotificationItem'

import styles from '../styles/CompactNotificationModal.module.less'


export const CompactNotificationModal: React.FC = () => {
    const items = useInteractions()
    const modalItemIds = useModalInteractionIds()
    const subsequentModalItems = useMemo(
        () =>
            modalItemIds.reduce<Item[]>((arr, modalItemId) => {
                const foundItem = items.find(item => item.id === modalItemId)
                if (
                    items.length > 1 &&
                    foundItem &&
                    isDeliveryPending(foundItem) &&
                    (isExternalChat(foundItem) || isInternalChat(foundItem) || isWorkItem(foundItem))
                ) {
                    arr.push(foundItem)
                }
                return arr
            }, []),
        [modalItemIds, items],
    )

    const notificationItemsUI = useMemo(() => {
        return subsequentModalItems.map(modalItem => {
            return <NotificationItem key={modalItem.id} {...modalItem} />
        })
    }, [subsequentModalItems])

    return (
        <div className={styles.overlayContainer}>
            <SmList className={styles.notificationList}>{notificationItemsUI}</SmList>
        </div>
    )
}
