import React from 'react'

import {useCurrentTabRole} from '@bpinc/ad-current-tab-data-context'
import {useSessionState} from '@bpinc/ad-session-context'
import {ForceLogin, LoginPage} from '@bpinc/lib-login-ui'
import {ForceLogout} from '@bpinc/lib-login-ui/src/components/ForceLogout'
import {Loader} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'


export const UnauthorizedUI: React.FC = () => {
    const tabRole = useCurrentTabRole()
    const {
        authChecked,
        isLoggedIn,
        showLoginDialog,
        offerForceLogin,
        forceLogoutInfo,
    } = useSessionState(['authChecked', 'isLoggedIn', 'showLoginDialog', 'offerForceLogin', 'forceLogoutInfo'])
    const language = useLocalizationState('language')

    const showAppLoader = !language || (tabRole === 'undecided' && !authChecked)
    const showLoginPage = !showAppLoader && (showLoginDialog || !isLoggedIn)
    const showForceLogout = !!forceLogoutInfo && showLoginPage
    const showForceLogin = offerForceLogin && !forceLogoutInfo && showLoginPage

    return (
        <div className="mx-auto h-full w-[496px]">
            {showAppLoader && <Loader active size="huge" color="primary" />}
            {showLoginPage && <LoginPage />}
            {showForceLogout && <ForceLogout />}
            {showForceLogin && <ForceLogin />}
        </div>
    )
}
