import {useEffect} from 'react'

import {isSelectOrTextField} from '@bpinc/lib-helpers'

import {getLetter} from '../helpers'
import type {KeypadProps} from './KeyPad'


export function useLetterBasedDialing({allowLetterBasedDialing = true, handleButtonClick}: KeypadProps) {
    useEffect(() => {
        if (allowLetterBasedDialing) {
            document.addEventListener('keydown', onKeyPress)
        }

        return function unsubscribeOnKeyPress() {
            if (allowLetterBasedDialing) {
                document.removeEventListener('keydown', onKeyPress)
            }
        }

        function onKeyPress(evt: KeyboardEvent) {
            if (!isSelectOrTextField(document.activeElement) && !evt.altKey && !evt.ctrlKey) {
                const letter = getLetter(evt.key, evt.code) || ''
                if (/^[/d*#+:a-z|A-Z]$/.test(letter)) {
                    setTimeout(handleButtonClick(letter), 0)
                }
            }
        }
    }, [allowLetterBasedDialing, handleButtonClick])
}
