import type {KeyboardEvent, RefObject} from 'react'
import React, {
    useCallback,
    useEffect,
    useMemo,
    useRef,
    useState,
} from 'react'
import type {VirtuosoHandle} from 'react-virtuoso'
import {Virtuoso} from 'react-virtuoso'

import {useCaseStateMethods} from '@bpinc/ad-case-state-context'
import {useContactsState, useContactsStateMethods} from '@bpinc/ad-contacts-state-context'
import type {ContactShortType} from '@bpinc/ad-contacts-state-types'
import type {DirectoryItem as DirectoryItemType} from '@bpinc/ad-directory-state-types'
import {useDefaultTransferMode, useInteractionStateMethods} from '@bpinc/ad-interaction-state-context'
import {useCallHandler, useContactDetailsOpener} from '@bpinc/ad-interaction-state-hooks'
import type {InitiateCall} from '@bpinc/ad-interaction-state-types'
import {useVirtualScrollDataManager} from '@bpinc/ad-scroll-state-context-builder'
import {AppRoutes, useNavigationState} from '@bpinc/lib-communicator-navigation-state'
import {ContactListPlaceholder} from '@bpinc/lib-contact-list-item-ui'
import {useAsRef, useEvent} from '@bpinc/lib-helpers'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import type {SelectionOrigin} from '@bpinc/lib-ui-elements'
import {ErrorBoundary, Loader, SelectionContainer, SelectionItem} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {SCROLL_KEYS} from '../../../scroll-context'
import {UPLOAD_EXTERNAL_CONTACTS_LIMIT} from '../../constants'
import {useDirectoryFirstFoundItemAutoSelector} from '../../hooks/useDirectoryFirstFoundItemAutoSelector'
import {useDirectorySearch} from '../../hooks/useDirectorySearch'
import {DirectoryItem} from '../user/DirectoryItem'
import type {OnChatParams, OnEmailParams} from '../user/types'
import {ExternalUsersHeader} from './ExternalUsersHeader'
import {useExternalUserHighlight} from './useExternalUserHighlight'


export type ExternalContactProps = {
    query?: string
    noControls?: boolean
    transferMode?: 'messages' | 'calls'
    transferControlsStyle?: 'add' | 'replace'
    selectedId?: string
    onClick?: (evt: React.MouseEvent, item: DirectoryItemType) => void
    onDoubleClick?: (item: DirectoryItemType) => void
    withCreateContact?: boolean
    scrollParent?: HTMLElement | null
    searchInputRef?: RefObject<HTMLInputElement>
    filter?: (item: DirectoryItemType) => boolean
    isHighlighted?: (item: DirectoryItemType) => boolean
}

const defaultFilter = () => true

export const ExternalUsers: React.FC<ExternalContactProps> = ({
    query,
    transferMode: rawTransferMode,
    transferControlsStyle,
    noControls,
    onClick,
    selectedId,
    onDoubleClick,
    scrollParent,
    withCreateContact = true,
    searchInputRef,
    filter = defaultFilter,
    isHighlighted,
}) => {
    const t = useLocalizationState('t')
    const defaultTransferMode = useDefaultTransferMode()
    const transferMode = rawTransferMode || defaultTransferMode
    const {
        contactList,
        contactListLoaded,
        contactListByChunks,
        contactListLoading,
        contactListByChunksLoading,
    } = useContactsState([
        'contactList',
        'contactListLoaded',
        'contactListByChunks',
        'contactListLoading',
        'contactListByChunksLoading',
    ])
    const {getContactListByChunks, getContactList} = useContactsStateMethods()
    const setActivePage = useNavigationState('setActivePage')
    const {createCaseEmail} = useCaseStateMethods()
    const highlight = useExternalUserHighlight(query)
    const handleCall = useCallHandler()
    const openContactDetails = useContactDetailsOpener()

    const hasQuery = !!query
    const finalContactList = useMemo(
        () =>
            hasQuery
                ? contactList.filter(filter)
                : contactListByChunks.filter(filter),
        [hasQuery, contactList, filter, contactListByChunks],
    )

    const isDebouncingSearch = useDirectorySearch(query, (q: string) => getContactList({query: q}), 500)
    const {createPotentialItem} = useInteractionStateMethods()

    const [scrollNode, setScrollNode] = useState<HTMLDivElement | undefined>(undefined)
    const virtuosoRef = useRef<VirtuosoHandle>(null)

    const getContactKey = useEvent((_idx: number, contact: ContactShortType) => contact.id)
    const scrollSnapshotData = useVirtualScrollDataManager(SCROLL_KEYS.directory('external'), virtuosoRef)
    const contactsIds = useMemo(() => finalContactList.map((contact, i) => getContactKey(i, contact)), [finalContactList, getContactKey])
    const contactsIdsRef = useAsRef(contactsIds)
    const itemsProvider = useEvent(() => contactsIdsRef.current)
    const onItemSelect = useEvent((id: string, selectionOrigin: SelectionOrigin | undefined) => {
        if (selectionOrigin === 'keys') {
            const index = contactsIdsRef.current?.findIndex(contactId => id === contactId)
            virtuosoRef.current?.scrollIntoView({index})
        }
    })


    const fetchMoreContacts = useEvent((lastIndex: number) => {
        if (hasQuery) {
            return
        }

        const lastContact = finalContactList[lastIndex]
        getContactListByChunks({
            limit: UPLOAD_EXTERNAL_CONTACTS_LIMIT,
            start: {
                last_name: lastContact.lastName,
                id: lastContact.id,
            },
        })
    })

    //Initial contacts loading
    useEffect(() => {
        const shouldMakeInitialLoad = !hasQuery && !contactListByChunks.length && !contactListByChunksLoading && !contactListLoaded
        if (shouldMakeInitialLoad) {
            getContactListByChunks({
                limit: UPLOAD_EXTERNAL_CONTACTS_LIMIT,
            })
        }
    }, [contactListByChunks.length, contactListByChunksLoading, contactListLoaded, getContactListByChunks, hasQuery])

    const onCall = useEvent((callParams: InitiateCall) => {
        handleCall({
            ...callParams,
            navigateToInteractionsPage: true,
        })
    })

    const onChat = useEvent(({contactId, phone, firstName, lastName}: OnChatParams) => {
        if (contactId) {
            createPotentialItem({
                phone,
                contactId,
                firstName,
                lastName,
            })
            setActivePage({name: AppRoutes.Interactions})
        }
    })

    const onEmail = useEvent((emailParams: OnEmailParams) => {
        createCaseEmail(emailParams)
    })

    const renderContact = useCallback((index: number, contact: ContactShortType) => {
        const contactId = getContactKey(index, contact)
        const isSelected = selectedId === contactId

        return (
            <ErrorBoundary fallback={null}>
                <DirectoryItem
                    as={SelectionItem}
                    listNode={scrollNode}
                    item={contact}
                    onCall={onCall}
                    onChat={onChat}
                    onEmail={onEmail}
                    onClick={onClick}
                    onDoubleClick={onDoubleClick}
                    onDetails={openContactDetails}
                    isSelected={isHighlighted?.(contact) || isSelected}
                    transferMode={transferMode}
                    transferNumberInput={query}
                    transferControlsStyle={transferControlsStyle}
                    noControls={noControls}
                    highlight={highlight}
                    containerProps={{
                        itemId: contactId,
                        focusOnSelection: true,
                        tabIndex: 0,
                        touchedOnly: true,
                    }}
                />
            </ErrorBoundary>
        )
    }, [
        getContactKey,
        highlight,
        noControls,
        onCall,
        onChat,
        onClick,
        onDoubleClick,
        onEmail,
        openContactDetails,
        query,
        scrollNode,
        selectedId,
        transferControlsStyle,
        transferMode,
    ])

    const emptyMessage = useMemo(() => {
        const messageText = query ? t.DIRECTORY.no_results_external_for_query(query) : t.DIRECTORY.no_results_external()
        return <div className="w-full text-center">{messageText}</div>
    }, [t.DIRECTORY, query])

    useDirectoryFirstFoundItemAutoSelector(finalContactList)

    const handleKeyDown = useCallback((e: KeyboardEvent<HTMLDivElement>) => {
        switch (e.key) {
            case 'Escape': {
                searchInputRef?.current?.focus()
                break
            }
            case 'Home': {
                virtuosoRef.current?.scrollIntoView({
                    index: 0,
                    done: () => {
                        focusContactElement(scrollNode, contactsIdsRef.current[0])
                    }})
                break
            }
            case 'End': {
                const lastIndex = contactsIdsRef.current?.length - 1
                virtuosoRef.current?.scrollIntoView({
                    index: lastIndex,
                    done: () => {
                        focusContactElement(scrollNode, contactsIdsRef.current[lastIndex])
                    },
                })
                break
            }
        }
    }, [contactsIdsRef, scrollNode, searchInputRef])

    const showPlaceholder = contactListLoading || isDebouncingSearch

    return (
        <div className="relative flex flex-auto flex-col overflow-hidden">
            {withCreateContact && <ExternalUsersHeader />}
            <div
                ref={elem => setScrollNode(elem || undefined)}
                className={tcn('scrollbar-on-hover relative !m-0 flex min-h-0 flex-auto flex-col overflow-y-auto overflow-x-hidden', {
                    'overflow-y-hidden': showPlaceholder,
                })}
            >
                {showPlaceholder ? (
                    <div className="absolute inset-x-0">
                        <ContactListPlaceholder amount={15} />
                    </div>
                ) : (
                    <>
                        <SelectionContainer
                            itemsListProvider={itemsProvider}
                            onItemSelect={onItemSelect}
                            circular={!contactListByChunksLoading}
                            onKeyDown={handleKeyDown}
                        >
                            <Virtuoso
                                ref={virtuosoRef}
                                tabIndex={undefined}
                                data={finalContactList}
                                itemContent={renderContact}
                                computeItemKey={getContactKey}
                                customScrollParent={scrollParent || scrollNode}
                                endReached={fetchMoreContacts}
                                restoreStateFrom={scrollSnapshotData}
                            />
                        </SelectionContainer>
                        {!finalContactList.length && emptyMessage}
                    </>
                )}
            </div>
            <Loader
                active={!showPlaceholder && contactListByChunksLoading}
                size="medium"
                color="primary"
                className="absolute bottom-0 top-auto z-10"
            />
        </div>
    )
}

function focusContactElement(container: HTMLDivElement | undefined, id: string) {
    container?.querySelector<HTMLDivElement>(`[data-id="${id}"]`)?.focus()
}
