import React, {useCallback} from 'react'
import {Button} from '@servicepattern/ui'
import {useAtomValue} from 'jotai'

import {IconDialpad} from '@bpinc/lib-icons'
import {IconArrowDown24, IconArrowUp24} from '@bpinc/lib-jskit-ui'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {isPadOpenAtom, phoneInputAtom} from '../atoms'
import {useHandlers} from '../hooks/useHandlers'
import {KeyPad} from './KeyPad'


export const DialpadButtons: React.FC = () => {
    const {t} = useLocalizationState()
    const isPadOpen = useAtomValue(isPadOpenAtom)
    const phoneInput = useAtomValue(phoneInputAtom)

    const {
        onButtonClick,
        closeDialpad,
        openDialpad,
    } = useHandlers()

    const handleWheel = useCallback((e: React.WheelEvent) => {
        // scroll down
        e.deltaY < -10 && closeDialpad()
    }, [closeDialpad])
    const handleButtonClick = useCallback((value: string) => {
        return () => {
            onButtonClick(value)
            phoneInput?.focus()
        }
    }, [onButtonClick, phoneInput])

    return (
        <div
            className={tcn(
                'absolute inset-x-0 bottom-0 overflow-hidden',
                'flex flex-col',
                'border-t bg-white',
                'transition-all duration-300',
                {
                    'h-[calc(100%-105px)]': isPadOpen,
                    'h-14': !isPadOpen,
                },
            )}
        >
            <div className="flex w-full items-center justify-between px-6 py-3">
                <div className="flex items-center gap-1">
                    <IconDialpad />

                    <div className="uppercase tracking-[0.8px] caption-xs text-neutral-disabled">
                        {t.DIAL_PAD.dialpad()}
                    </div>
                </div>

                <Button
                    variant="ghost"
                    size="sm"
                    icon={isPadOpen ? <IconArrowDown24 /> : <IconArrowUp24 />}
                    onClick={isPadOpen ? closeDialpad : openDialpad}
                    aria-label={t.DIAL_PAD.hide_controls_label()}
                    data-testid="collapse-keypad"
                />
            </div>

            {isPadOpen && (
                <div
                    className="relative flex flex-1 items-center justify-center py-4"
                    onWheel={handleWheel}
                >
                    <KeyPad handleButtonClick={handleButtonClick} />
                </div>
            )}
        </div>
    )
}
