import {useEffect, useState} from 'react'

import {useInteractionProp} from '@bpinc/ad-interaction-state-context'
import {isExternalChat} from '@bpinc/ad-interaction-state-methods'
import {useInteractionId} from '@bpinc/lib-interaction-context'
import {useLocalizationState} from '@bpinc/state-localization-context'


export function useHeader() {
    const {t} = useLocalizationState()
    const [label, setLabel] = useState<string>('')
    const interactionId = useInteractionId()
    const activeItemIsExternalChat = useInteractionProp(interactionId, isExternalChat)

    useEffect(() => {
        setLabel(
            activeItemIsExternalChat
                ? t.EXTERNAL_CHAT.invite_dialog_label()
                : t.VOICE_CALL.consult_conference_blind_transfer(),
        )
    }, [t, activeItemIsExternalChat])

    return label
}
