import {useMemo} from 'react'

// More info in docs: https://help.brightpattern.com/5.8:Agent-guide/Tutorials/Search/AdvancedSearch#Contact_Keywords
const CONTACT_KEYWORDS_REGEX = /^(address|company_name|dob|email|first_name|last_name|name|phone|position|segment|title|url):/

export function useExternalUserHighlight(query: string | undefined) {
    return useMemo(() => {
        if (!query) {
            return undefined
        }
        return query.replace(CONTACT_KEYWORDS_REGEX, '').trim()
    }, [query])
}
