import type {HTMLAttributes} from 'react'
import React from 'react'
import * as Dialog from '@radix-ui/react-dialog'

import {formatChatDate} from '@bpinc/ad-formatting-helpers'
import {useBoolean, useEscapeManager} from '@bpinc/lib-helpers'
import {IconClose24} from '@bpinc/lib-jskit-ui'
import {tcn} from '@bpinc/lib-tailwind-classnames'


interface ImageFileMessageProps extends HTMLAttributes<HTMLImageElement> {
    fileName: string
    fileLink: string
    senderName: string | undefined
    timestamp: number
}

export const ImageFileMessage = React.forwardRef<
    HTMLImageElement,
    ImageFileMessageProps
>(
    (
        {
            fileLink,
            fileName,
            className,
            senderName,
            timestamp,
            ...htmlImageProps
        },
        ref,
    ) => {
        const open = useBoolean(false)

        useEscapeManager(open.setFalse, {enabled: open.value})

        return (
            <Dialog.Root open={open.value} onOpenChange={open.toggle}>
                <Dialog.Trigger asChild>
                    <img
                        ref={ref}
                        {...htmlImageProps}
                        className={tcn('cursor-zoom-in rounded-xs', className)}
                        alt={fileName}
                        src={fileLink}
                        title={fileName}
                        data-testid="attachment"
                        data-value={fileName}
                    />
                </Dialog.Trigger>
                <Dialog.Portal>
                    <Dialog.Overlay
                        className={tcn(
                            'fixed left-0 top-0 z-10 h-full w-full bg-inverse-overlay',
                            'data-[state=open]:animate-fade-in-100',
                            'data-state=[closed]:animate-fade-out-100',
                        )}
                    />
                    <Dialog.Content
                        className={tcn(
                            'fixed left-1/2 top-1/2 z-20 max-h-[90vw] max-w-[90vw] -translate-x-1/2 -translate-y-1/2',
                            'data-[state=open]:animate-fade-in-100',
                            'data-state=[closed]:animate-fade-out-100',
                        )}
                    >
                        <Dialog.Close className="absolute right-0 top-0 translate-y-[calc(-100%-10px)]">
                            <IconClose24 className="size-8 icon-reversed" />
                        </Dialog.Close>
                        <img
                            ref={ref}
                            {...htmlImageProps}
                            className={'h-auto w-auto'}
                            alt={fileName}
                            src={fileLink}
                            title={fileName}
                            data-testid="attachment"
                            data-value={fileName}
                        />
                        <p className="mt-2 caption-s text-inverse-disabled">
                            {fileName}
                        </p>
                        <p className="caption-s text-inverse-disabled">
                            <b className="text-inverse-disabled">
                                {senderName}
                            </b>
                            {', '}
                            <b className="text-inverse-disabled">
                                {formatChatDate(timestamp)}
                            </b>
                        </p>
                    </Dialog.Content>
                </Dialog.Portal>
            </Dialog.Root>
        )
    },
)
