import {useMemo} from 'react'

import {useSessionState} from '@bpinc/ad-session-context'


export function useModulesImportByPrivileges() {
    const privileges = useSessionState('privileges')

    useMemo(() => {
        if (privileges.CAN_START_INTERNAL_CHAT) {
            import('chatCenter')
        }

        if (privileges.CAN_USE_CALENDAR) {
            import('calendar')
        }

    }, [privileges.CAN_START_INTERNAL_CHAT, privileges.CAN_USE_CALENDAR])
}
