import React from 'react'

import type {Item} from '@bpinc/ad-interaction-state-types'

import {useInteractionTypeIcon} from './hooks/useInteractionTypeIcon'


type InteractionTypeImageProps = {
    item: Item
}

export const InteractionTypeImage: React.FC<InteractionTypeImageProps> = ({
    item,
}) => {
    const Icon = useInteractionTypeIcon(item)

    return (
        <>
            <Icon className="size-4 icon-neutral" />
            <div className="sr-only" data-testid="media-type">
                {item.mediaType}
            </div>
        </>
    )
}
