import type {ReactElement} from 'react'
import React from 'react'
import type {FieldValues} from 'react-hook-form'

import type {FieldImplementationProps, FieldProps, FieldRenderImplementationProps} from '@bpinc/lib-forms-base'
import {FieldConstructor} from '@bpinc/lib-forms-base'
import type {CheckboxProps} from '@bpinc/lib-ui-elements'
import {Checkbox} from '@bpinc/lib-ui-elements'


type CheckboxFieldProps = FieldImplementationProps<Omit<CheckboxProps, 'onChange' | 'checked' | 'type'>> & ({
    type?: 'checkbox'
    radioValue?: never
} | {
    type: 'radio'
    radioValue: string
})

function renderElement<TFormValues extends FieldValues>({
    value,
    type = 'checkbox',
    radioValue,
    ...props
}: FieldRenderImplementationProps<CheckboxFieldProps, TFormValues>): ReactElement {
    const checked = type === 'checkbox' ? value : value === radioValue

    if (type === 'checkbox') {
        return (
            <Checkbox
                {...props}
                type="checkbox"
                checked={checked}
            />
        )
    }

    return (
        <Checkbox
            {...props}
            type="radio"
            checked={checked}
            value={radioValue ?? ''}
        />
    )
}

export function CheckboxFieldBase<TFormValues extends FieldValues>(props: FieldProps<CheckboxFieldProps, TFormValues>): ReactElement {
    return <FieldConstructor {...props} renderElement={renderElement} />
}
