import React, {useMemo} from 'react'

import {useCommunicatorUIState} from '@bpinc/lib-communicator-ui-context'

import {MainMenu} from '../components/MainMenu'
import {SettingsMenu} from '../components/SettingsMenu'
import type {SidebarRightProps} from '../SidebarRight'
import {useHandlers} from './useHandlers'


export function useSidebarRightContent(props: SidebarRightProps) {
    const {settingsOpened, toggleSettings} = useCommunicatorUIState(['settingsOpened', 'toggleSettings'])
    const {activePageName, onMenuItemClick} = props

    const {closeMenu, openSettingsMenu, handleMenuKeyPress} = useHandlers()

    const menuProps = useMemo(
        function memoizeMenuProps() {
            return {
                vertical: true,
                secondary: true,
                onKeyDown: handleMenuKeyPress,
            }
        },
        [handleMenuKeyPress],
    )

    if (settingsOpened) {
        return (
            <SettingsMenu
                menuProps={menuProps}
                toggleSettings={toggleSettings}
                closeMenu={closeMenu}
                openSettingsMenu={openSettingsMenu}
                activePageName={activePageName}
            />
        )
    }
    return (
        <MainMenu
            menuProps={menuProps}
            toggleSettings={toggleSettings}
            onMenuItemClick={onMenuItemClick}
            activePageName={activePageName}
        />
    )
}
