import type {FC} from 'react'
import React from 'react'
import {Button} from '@servicepattern/ui'

import {useSessionState} from '@bpinc/ad-session-context'
import {useDirectoryUIState} from '@bpinc/lib-communicator-ui-context'
import {IconPlus16} from '@bpinc/lib-jskit-ui'
import {useLocalizationState} from '@bpinc/state-localization-context'


export const ExternalUsersHeader: FC = () => {
    const {t} = useLocalizationState()
    const toggleCreateContactMode = useDirectoryUIState(s => s.toggleCreateContactMode)
    const privileges = useSessionState('privileges')

    if (!privileges.EDIT_CONTACTS) {
        return null
    }

    return (
        <div className="mb-2 flex">
            <Button
                variant="ghost"
                size="sm"
                className="shrink-0"
                onClick={toggleCreateContactMode}
                iconLeft={<IconPlus16 className="icon-primary" />}
            >
                {t.CONTACT.create_new_contact_button_text()}
            </Button>
        </div>
    )
}
