import {atom} from 'jotai'
import {atomWithReducer, selectAtom} from 'jotai/utils'

import type {Message} from '@bpinc/ad-prerecorded-messages-state-types'


type SelectedMessagesState = {
    selectedMessage: Message | undefined
    messagesOrder: string[]
}

type Action = SelectedMessagesAction | RevalidateMessagesAction

type SelectedMessagesAction = {
    type: 'selectMessage'
    payload: {
        selectedMessage: Message
    }
}

type RevalidateMessagesAction = {
    type: 'revalidateMessages'
    payload: {
        existingMessages: Message[]
    }
}

function reducer(prev: SelectedMessagesState, action: Action) {
    switch (action.type) {
        case 'selectMessage':
            return action.payload.selectedMessage === prev.selectedMessage
                ? prev
                : {
                    selectedMessage: action.payload.selectedMessage,
                    messagesOrder: [
                        action.payload.selectedMessage.id,
                        ...prev.messagesOrder.filter(
                            message =>
                                message !== action.payload.selectedMessage.id,
                        ),
                    ],
                }
        case 'revalidateMessages': {
            const existingMessagesById = action.payload.existingMessages.reduce(
                (acc, message) => {
                    acc[message.id] = message
                    return acc
                },
                {} as Record<string, Message>,
            )

            const missingMessages = action.payload.existingMessages
                .filter(message => !prev.messagesOrder.includes(message.id))
                .map(message => message.id)

            const messagesOrder = [
                ...prev.messagesOrder.filter(m => existingMessagesById[m]),
                ...missingMessages,
            ]

            return {
                selectedMessage:
                    prev.selectedMessage &&
                    existingMessagesById[prev.selectedMessage.id]
                        ? prev.selectedMessage
                        : action.payload.existingMessages[0],
                messagesOrder,
            }
        }
    }
}

export const selectedMessageStateAtom = atomWithReducer<SelectedMessagesState, Action>(
    {
        selectedMessage: undefined,
        messagesOrder: [],
    },
    reducer,
)

export const selectedMessageAtom = selectAtom(
    selectedMessageStateAtom,
    state => state.selectedMessage,
)

export const messageOrderAtom = selectAtom(
    selectedMessageStateAtom,
    state => state.messagesOrder,
)

export const modalSelectedMessageAtom = atom<Message | undefined>(undefined)
