import React, {useEffect, useMemo} from 'react'

import {useActiveInteractionId} from '@bpinc/ad-interaction-state-context'
import {useActivityHistoryOpener, useCallHandler} from '@bpinc/ad-interaction-state-hooks'
import type {FavoritesChannelMedia} from '@bpinc/ad-transport-types'
import {useUserDetailsOnceGetter} from '@bpinc/ad-user-details-state-context'
import {useNavigationState} from '@bpinc/lib-communicator-navigation-state'
import {DirectoryContactChannelFavoriteButton} from '@bpinc/lib-favorites-button'
import type {FormElement} from '@bpinc/lib-form-builder'
import {useEvent} from '@bpinc/lib-helpers'
import {Loader} from '@bpinc/lib-ui-elements'
import type {FavoritesChannel, FavoritesChannelInternal, FavoritesChannelPhone} from '@bpinc/state-favorites-types'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {ServiceSelect} from '../../../components/ServiceSelect'
import {useInternalUserData} from '../../../hooks/useInternalUserData'
import {isDirectoryEntry, isMsTeamsChannel, isMsTeamsUser, isMsTeamsUserDetails} from '../../helpers'
import {Avatar} from '../Avatar'
import {ContactDetailsControls} from '../ContactDetailsControls'
import {FormElementView} from '../FormElementView'
import {Header} from '../Header'


interface Props {
    userId: string
}

interface Field {
    type?: FormElement['type']
    value: string
    label: string
    favoritesChannel?: FavoritesChannel
}

export const User: React.FC<Props> = ({userId}) => {
    const {CONTACT: i18n} = useLocalizationState('t')

    const activeInteractionId = useActiveInteractionId()
    const getUserDetailsOnce = useUserDetailsOnceGetter()
    const goBack = useNavigationState('goBack')
    const handleCall = useCallHandler()
    const openActivityHistory = useActivityHistoryOpener()

    const data = useInternalUserData(userId)

    const firstName = data && !isMsTeamsChannel(data) ? data.firstName : undefined
    const lastName = data && !isMsTeamsChannel(data) ? data.lastName : undefined
    const emiUserType =
        (data && (isMsTeamsUser(data) || isMsTeamsChannel(data)))
            ? data.userType
            : undefined
    const emiPhotoUrl =
        (data && (isMsTeamsUser(data) || isMsTeamsChannel(data)))
            ? data.photoUrl
            : undefined

    useEffect(() => {
        if (!data) {
            getUserDetailsOnce({userIds: [userId]})
        }
        // onComponentDidMount
        // eslint-disable-next-line
    }, [])

    const fields = useMemo<Field[]>(() => {
        if (!data) {
            return []
        }

        if (isMsTeamsChannel(data)) {
            return []
        }

        if (isMsTeamsUser(data) || isMsTeamsUserDetails(data)) {
            const msTeamsPhones = isMsTeamsUser(data) ? data.phones : data.msTeamsPhones ?? []
            return Object.values(msTeamsPhones).reduce<Field[]>((result, phone) => {
                result.push({
                    type: 'number' as const,
                    value: phone.phone,
                    label: i18n.phone_field_label(),
                    favoritesChannel: {
                        channelType: 'phone',
                        number: phone.phone,
                        numberType: phone.type,
                        media: ['voice'],
                    } satisfies FavoritesChannelPhone,
                })
                return result
            }, [])
        }

        return !data.extension
            ? []
            : [
                {
                    type: 'number' as const,
                    value: data.extension,
                    label: i18n.phone_field_label(),
                    favoritesChannel: {
                        channelType: 'internal',
                        number: data.extension,
                        media: ['voice', 'chat'],
                    } satisfies FavoritesChannelInternal,
                },
            ]
    }, [data, i18n])

    const handlePhoneChannelInteraction = useEvent((number: string, media: FavoritesChannelMedia) => {
        if (media === 'voice') {
            handleCall({
                userId,
                phone: number,
                firstName,
                lastName,
                navigateToInteractionsPage: true,
                origination: 'contact-profile',
            })
        } else {
            openActivityHistory({
                userId,
                phone: number,
                firstName,
                lastName,
                emiUserType,
                emiPhotoUrl,
                origination: 'contact-profile',
            })
        }
    })

    return (
        <div
            className="flex min-h-0 flex-auto flex-col overflow-y-auto overflow-x-hidden py-3"
            data-testid="contact-details"
        >
            {!data ? (
                <Loader
                    size="large"
                    color="primary"
                    active
                />
            ) : (
                <>
                    <Header user={data} onGoBack={goBack} />

                    <div className="flex items-center gap-2 px-6 pb-2 pt-4">
                        <ServiceSelect itemId={activeInteractionId} />
                        <ContactDetailsControls user={data} />
                    </div>

                    <div className="flex shrink-0 gap-8 px-6 py-4">
                        <Avatar user={data} />

                        {!!fields.length && (
                            <div role="list" className="flex grow flex-col overflow-y-auto overflow-x-hidden px-4">
                                {fields.map((field, index) => (
                                    <FormElementView
                                        key={index}
                                        label={field.label}
                                        type={field.type}
                                        value={field.value}
                                        userId={userId}
                                        isStaticEntry={isDirectoryEntry(data)}
                                        additionalControls={
                                            field.favoritesChannel && (
                                                <DirectoryContactChannelFavoriteButton
                                                    item={data}
                                                    channel={field.favoritesChannel}
                                                    onPhoneInteraction={handlePhoneChannelInteraction}
                                                />
                                            )
                                        }
                                    />
                                ))}
                            </div>
                        )}
                    </div>
                </>
            )}
        </div>
    )
}
