import {useCallback} from 'react'

import {useAssociatedChatInteractionProp, useInteractionProp} from '@bpinc/ad-interaction-state-context'
import {getChatParties} from '@bpinc/ad-interaction-state-methods'
import {useSessionState} from '@bpinc/ad-session-context'
import {useNonAssertedInteractionId} from '@bpinc/lib-interaction-context'


type UserAssociatedObject = { userId?: string; contactId?: string }

export const useChatPartyUsersFilter = () => {
    const interactionId = useNonAssertedInteractionId()
    const partiesList = useAssociatedChatInteractionProp(interactionId, getChatParties)
    const {userId: selfUserId} = useSessionState()

    return useCallback(
        (item: UserAssociatedObject) => {
            return (
                !partiesList?.find(
                    party =>
                        (party.user_id && party.user_id === item.userId) ||
                        (party.contact_id && party.contact_id === item.contactId),
                ) && item.userId != selfUserId
            )
        },
        [partiesList, selfUserId],
    )
}

export const useCallPartyUsersFilter = () => {
    const {userId: selfUserId} = useSessionState()
    const interactionId = useNonAssertedInteractionId()
    const partiesList = useInteractionProp(interactionId, activeItem => activeItem?.parties)

    return useCallback(
        (item: UserAssociatedObject) => {
            return (
                !partiesList?.find(
                    party =>
                        (party.userId && party.userId === item.userId) ||
                        (party.contactId && party.contactId === item.contactId),
                ) && item.userId != selfUserId
            )
        },
        [partiesList, selfUserId],
    )
}
