import {useCallback} from 'react'
import {get} from 'lodash-es'

import {isInternalChat, isPotentialInteraction} from '@bpinc/ad-interaction-state-methods'
import type {Item} from '@bpinc/ad-interaction-state-types'
import {useSessionState} from '@bpinc/ad-session-context'


type IsCallableItemChecker = (item?: Item) => boolean

export function useCallableItemChecker(): IsCallableItemChecker {
    const {userId: selfUserId} = useSessionState()

    return useCallback(
        (item?: Item) => {
            const itemNumber = get(item, 'displayData.number')
            const itemUserId =
                get(item, 'userId') || get(item, 'internalChatData.userId')
            const isSelf = itemUserId === selfUserId
            return (
                (isPotentialInteraction(item) || isInternalChat(item)) &&
                Boolean(itemNumber) &&
                !isSelf
            )
        },
        [selfUserId],
    )
}
