import React from 'react'

import {Avatar} from '@bpinc/lib-jskit-ui'
import {useConfPartiesToString} from '@bpinc/state-recent-helpers'
import type {RecentItem} from '@bpinc/state-recent-types'


type Props = {
    item: RecentItem
}

export const Header: React.FC<Props> = ({item: {parties}}) => {

    const confPartiesToString = useConfPartiesToString()

    return (
        <>
            <Avatar size="32" type="conference" />
            <span className="truncate">
                {`(${parties.length + 1}) ${confPartiesToString(parties)}`}
            </span>
        </>
    )
}
