import type {FC} from 'react'
import React from 'react'

import {AvatarLegacy} from '@bpinc/lib-avatar-ui'
import {RecentFavoriteButton} from '@bpinc/lib-favorites-button'
import {GoBackButton} from '@bpinc/lib-ui-elements'
import type {RecentItem} from '@bpinc/state-recent-types'

import type {EditingContactControlsProps} from './EditingContactControls'
import {EditingContactControls} from './EditingContactControls'


interface EditingContactDetailsHeaderProps extends EditingContactControlsProps {
    title?: string
    onGoBack: () => void
    email?: string
    recentItem?: RecentItem
}

export const EditingContactDetailsHeader: FC<EditingContactDetailsHeaderProps> = ({
    title,
    onGoBack,
    recentItem,
    ...props
}) => {
    return (
        <div className="relative flex pr-3.5">
            <div className="mr-4 shrink-0">
                <GoBackButton onClick={onGoBack} />
            </div>
            <AvatarLegacy size={60} type="unknown" />
            <div className="flex grow items-center overflow-hidden">
                {title && (
                    <div
                        className="ml-2 flex overflow-hidden text-2xl font-bold leading-[30px]"
                        role="alert"
                        data-testid="name"
                    >
                        <div className="truncate">{title}</div>
                        {!!recentItem && (
                            <RecentFavoriteButton item={recentItem} className="ml-2 text-lg" />
                        )}
                    </div>
                )}
                {(props.messengerId || props.phone) && <EditingContactControls {...props} />}
            </div>
        </div>
    )
}
