import {useMemo} from 'react'

import {useInteractions} from '@bpinc/ad-interaction-state-context'
import {isCall, isExternalChat, isInternalChat, isPotentialInteraction} from '@bpinc/ad-interaction-state-methods'
import type {Item} from '@bpinc/ad-interaction-state-types'


type InteractionCount = {
    call: number
    chat: number
    history: number
}

export function useInteractionsCount() {
    const interactions = useInteractions()

    return useMemo(function memoizeOutput() {
        return interactions.reduce((count: InteractionCount, interaction: Item) => {
            if (isExternalChat(interaction) || isInternalChat(interaction)) {
                count.chat++
                //reset history counter if there any other interactions
                count.history = 0
            } else if (isCall(interaction)) {
                count.call++
                //reset history counter if there any other interactions
                count.history = 0
            } else if (isPotentialInteraction(interaction)) {
                count.history++
            }
            return count
        }, {call: 0, chat: 0, history: 0})
    }, [interactions])

}
