import React from 'react'

import {IconAgentNotes} from '@bpinc/lib-jskit-ui'
import type {FCWithChildren} from '@bpinc/lib-types-helpers'
import {useLocalizationState} from '@bpinc/state-localization-context'


export const NotesMessage: FCWithChildren<string> = ({children}) => {
    const {t} = useLocalizationState()
    return (
        <div
            className="flex w-full flex-col gap-2 rounded-xs border py-1.5 pl-2 pr-3 bg-warning-variant border-on-warning"
            data-testid="notes"
        >
            <div className="flex items-center gap-1">
                <IconAgentNotes className="size-4" />
                <div className="font-semibold caption-xs text-primary-on-primary">
                    {t.ACTIVITY_HISTORY.agent_notes_title()}
                </div>
            </div>
            <div className="w-full gap-1 break-words caption-s text-neutral-on-secondary">
                {children}
            </div>
        </div>
    )
}
