import {useCallback} from 'react'

import {useDIDNumberState} from '@bpinc/ad-did-number-context'
import {useInteractionStateMethods} from '@bpinc/ad-interaction-state-context'
import {useServiceState} from '@bpinc/ad-service-context'


export const usePhoneClickHandler = () => {
    const interactionStateMethods = useInteractionStateMethods()
    const {selectedService} = useServiceState()
    const {selectedDIDNumber} = useDIDNumberState()

    return useCallback((e: React.MouseEvent) => {
        e.preventDefault()

        const el = e.currentTarget
        const phone = el.getAttribute('data-phone')

        if (!phone) {
            return
        }

        interactionStateMethods.initiateCall({
            phone,
            serviceId: selectedService,
            accessNumberId: selectedDIDNumber,
        })

        return
    }, [selectedService, selectedDIDNumber, interactionStateMethods])

}
