import {useCallback} from 'react'

import {useFormatting} from '@bpinc/ad-formatting-context'
import {formatPhone} from '@bpinc/ad-formatting-helpers'
import {useInteraction} from '@bpinc/ad-interaction-state-context'
import {isExternalChat} from '@bpinc/ad-interaction-state-methods'
import {useInteractionId} from '@bpinc/lib-interaction-context'
import {assertDefined} from '@bpinc/lib-types-helpers'
import {useLocalizationState} from '@bpinc/state-localization-context'

import type {ParticipantItem} from './types'


type DisplayNameParams = {
    isChat?: boolean
    contactDisplayName?: string
}

export const useDisplayNameParticipant = ({isChat}: DisplayNameParams) => {
    const {t} = useLocalizationState()
    const {formatFullName, getNamesFromDisplayName} = useFormatting()
    const interactionId = useInteractionId()
    const activeItem = useInteraction(interactionId)
    assertDefined(activeItem)

    return useCallback((item: ParticipantItem, contactDisplayName?: string) => {
        const {
            name: callPartyName,
            phone,
            email,
            userId,
        } = item
        const partyDisplayName = isChat ? formatFullName(item.firstName, item.lastName) : callPartyName
        const displayNameLocal = contactDisplayName || partyDisplayName
        const [firstName, lastName] = displayNameLocal
            ? getNamesFromDisplayName(displayNameLocal)
            : [item.firstName, item.lastName]

        if (!isChat) {
            const displayName = displayNameLocal || (phone
                ? (userId
                    ? phone
                    : formatPhone(phone)
                )
                : t.GLOBAL.anonymous()
            )
            return {displayName, showPhone: !!(displayNameLocal && phone), firstName, lastName}
        }

        const displayName = displayNameLocal || (
            (phone
                ? formatPhone(phone)
                : (
                    email || (isExternalChat(activeItem)
                        ? t.EXTERNAL_CHAT.display_name_placeholder()
                        : t.GLOBAL.anonymous()
                    )
                )
            )
        )
        return {displayName, showPhone: !!(displayNameLocal && phone), showEmail: !!(displayNameLocal && !phone && email), firstName, lastName}
    }, [getNamesFromDisplayName, formatFullName, activeItem, t, isChat])
}
