import React, {Fragment, useMemo} from 'react'

import type {AnyEmiUser, DirectoryEntry, DirectoryItem, SearchResult} from '@bpinc/ad-directory-state-types'
import {useFormatting} from '@bpinc/ad-formatting-context'
import {useDefaultTransferMode} from '@bpinc/ad-interaction-state-context'
import type {OpenContactDetailsParams} from '@bpinc/ad-interaction-state-hooks'
import type {InitiateCall} from '@bpinc/ad-interaction-state-types'
import {useSessionState} from '@bpinc/ad-session-context'
import {useDirectoryUIState} from '@bpinc/lib-communicator-ui-context'
import {isDirectoryEntry, isMsTeamsChannel} from '@bpinc/lib-helpers-ui'
import type {Dictionary} from '@bpinc/lib-types-helpers'
import {isDefined} from '@bpinc/lib-types-helpers'

import {useDirectoryFirstFoundItemAutoSelector} from '../hooks/useDirectoryFirstFoundItemAutoSelector'
import {omitSelfInUsers} from '../utils'
import type {OnChatParams, OnEmailParams} from './user/types'
import {DirectoryItems} from './DirectoryItems'


interface Props {
    transferMode?: 'messages' | 'calls'
    transferControlsStyle?: 'add' | 'replace'
    query?: string
    filteredSearchResults: SearchResult
    filteredDirectoryEntries: Dictionary<DirectoryEntry[]>
    emptyMessage?: React.ReactNode
    onCall: (callParams: InitiateCall) => void
    onChat: (chatParams: OnChatParams) => void
    onEmail: (emailParams: OnEmailParams) => void
    onDetails: (params: OpenContactDetailsParams) => void
    onClick?: (evt: React.MouseEvent, item: DirectoryItem) => void
    onDoubleClick?: (item: DirectoryItem) => void
    isHighlighted?: (item: DirectoryItem) => boolean
}

const userTypeOrder = [undefined, 'msteams', 'msteams_channels'] as const
function sortByUserType(a: AnyEmiUser, b: AnyEmiUser): number {
    const aIndex = userTypeOrder.indexOf(a.userType as 'msteams' | 'msteams_channels' | undefined)
    const bIndex = userTypeOrder.indexOf(b.userType as 'msteams' | 'msteams_channels' | undefined)
    if (aIndex > bIndex) return 1
    if (aIndex < bIndex) return -1
    return 0
}

function getName(formatFullName: (firstName: string, lastName: string) => string, item: DirectoryItem): string {
    if (isMsTeamsChannel(item) || isDirectoryEntry(item)) {
        return item.title
    }

    return formatFullName(item.firstName, item.lastName)
}

function sortByName(formatFullName: (firstName: string, lastName: string) => string, a: DirectoryItem, b: DirectoryItem): number {
    const getFormattedName = getName.bind(null, formatFullName)
    const aName = getFormattedName(a).toLowerCase()
    const bName = getFormattedName(b).toLowerCase()

    if (aName > bName) return 1
    if (aName < bName) return -1

    return 0
}

export const SearchResults: React.FC<Props> = props => {
    const defaultTransferMode = useDefaultTransferMode()
    const selected = useDirectoryUIState(s => s.selected)
    const {formatFullName} = useFormatting()
    const {
        query,
        transferMode = defaultTransferMode,
        filteredSearchResults,
        filteredDirectoryEntries,
        emptyMessage,
        ...rest
    } = props
    const {userId: selfUserId} = useSessionState()

    const items = useMemo(() => {
        const emi = filteredSearchResults.data.emiUsers as Record<string, AnyEmiUser[]>
        const sortByFormattedName = sortByName.bind(null, formatFullName)
        const {
            directoryEntries: directoryEntriesFromSearch,
            users: usersFromSearch,
        } = filteredSearchResults.data
        const planeFilteredDirectoryEntries = Object.values(filteredDirectoryEntries).flat(1)

        console.log('@@@ filteredSearchResults:', filteredSearchResults)

        /** It is a known issue that search returns not enough data to display directory entry correctly.
         * For this reason we need to prefetch all directory entries in directory
         * and use their data in search results instead
        */
        const directoryEntries = directoryEntriesFromSearch.map(({id}) => {
            return planeFilteredDirectoryEntries.find(entry => entry.id === id)
        }).filter(isDefined)

        // Display order:
        return [
            // 1) internal users
            ...omitSelfInUsers(usersFromSearch, selfUserId),
            // 2) directory entries
            ...directoryEntries.sort(sortByFormattedName),
            // 3) emi users (msteams users, then msteams channels)
            ...Object.keys(emi).reduce<AnyEmiUser[]>((arr, categoryKey) => {
                emi[categoryKey]?.forEach(user => arr.push(user))
                return arr
            }, [])
                .sort(sortByFormattedName).sort(sortByUserType),
        ]
    }, [filteredDirectoryEntries, filteredSearchResults.data, formatFullName, selfUserId])

    useDirectoryFirstFoundItemAutoSelector(items)

    if (items.length === 0) {
        return <Fragment>{emptyMessage}</Fragment>
    }

    return (
        <DirectoryItems
            transferMode={transferMode}
            transferNumberInput={query}
            items={items}
            highlight={query}
            selectedId={selected}
            {...rest}
        />
    )
}
