import {useCallback, useEffect} from 'react'

import {useInteractionStateConnector} from '@bpinc/ad-interaction-state-context'
import {AppRoutes, useNavigationStateRef} from '@bpinc/lib-communicator-navigation-state'


export function useInteractionStateSubscription() {
    const navigationStateRef = useNavigationStateRef()
    const interactionStateConnector = useInteractionStateConnector()
    const openInteractionsPage = useCallback(
        function openInteractionsPageCallback() {
            const {activePage, setActivePage} = navigationStateRef.current
            if (activePage.name !== AppRoutes.Interactions) {
                setActivePage({
                    name: AppRoutes.Interactions,
                })
            }
        },
        [navigationStateRef],
    )

    useEffect(() => {
        const subscription = interactionStateConnector?.dispenser.subscribe({
            onItemAccepted: openInteractionsPage,
            onActiveItemChanged: openInteractionsPage,
        })
        return () => subscription?.unsubscribe()
    }, [
        interactionStateConnector?.dispenser,
        navigationStateRef,
        openInteractionsPage,
    ])
}
