import React from 'react'

import type {ExternalChatMessageSentiment} from '@bpinc/ad-transport-types'
import {formatDate} from '@bpinc/lib-helpers'
import {useLocalizationState} from '@bpinc/state-localization-context'

import type {HistoryVoiceTranscriptMessage} from '../../../../../types'
import {CallMessageRecording} from './CallMessageRecording'
import {EmptyCallMessage} from './EmptyCallMessage'


export interface CallMessageProps {
    direction?: string
    duration?: string
    timestamp?: number
    sentiment?: ExternalChatMessageSentiment
    hasVoiceRecording?: boolean
    recordingLink?: string
    isMissedCall: boolean
    voiceTranscript?: HistoryVoiceTranscriptMessage[]
}

export const CallMessageContent: React.FC<CallMessageProps> = ({
    direction,
    duration,
    timestamp,
    hasVoiceRecording,
    recordingLink,
    isMissedCall,
    voiceTranscript,
    ...props
}) => {
    const {t} = useLocalizationState()
    const hasVoiceTranscript = !!voiceTranscript?.length
    const createdTime = formatDate(timestamp, 'LT')

    const ariaDirection =
        direction === 'incoming'
            ? t.ACTIVITY_HISTORY.call_message_incoming_aria_label()
            : t.ACTIVITY_HISTORY.call_message_outgoing_aria_label()

    const ariaLabel = [
        ariaDirection,
        t.ACTIVITY_HISTORY.call_message_created_time_aria() + createdTime,
        t.ACTIVITY_HISTORY.call_message_duration_aria() + duration,
    ].join('; ')

    const title = isMissedCall
        ? t.ACTIVITY_HISTORY.call_message_missed()
        : direction === 'incoming'
            ? t.ACTIVITY_HISTORY.call_message_incoming_text()
            : t.ACTIVITY_HISTORY.call_message_outgoing_text()

    if (!hasVoiceRecording || !recordingLink) {
        return (
            <EmptyCallMessage
                timestamp={timestamp}
                duration={duration}
                isMissedCall={isMissedCall}
                title={title}
                createdTime={createdTime}
                ariaLabel={ariaLabel}
                direction={direction}
                {...props}
            />
        )
    }

    return (
        <CallMessageRecording
            hasVoiceRecording={hasVoiceRecording}
            recordingLink={recordingLink}
            voiceTranscript={voiceTranscript}
            timestamp={timestamp}
            duration={duration}
            isMissedCall={isMissedCall}
            title={title}
            createdTime={createdTime}
            hasVoiceTranscript={hasVoiceTranscript}
            ariaLabel={ariaLabel}
            direction={direction}
            {...props}
        />
    )
}
