import React, {useCallback, useId, useState} from 'react'

import {useContactsState} from '@bpinc/ad-contacts-state-context'
import {useDirectorySearchResultsState} from '@bpinc/ad-directory-state-context'
import type {DirectoryItem} from '@bpinc/ad-directory-state-types'
import {IconChevronDown16, IconChevronRight16} from '@bpinc/lib-jskit-ui'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {useExternalContactFolderOpenerForSearchMode} from '../../hooks/useExternalContactFolderOpenerForSearchMode'
import {External} from '../External'
import {Internal} from '../Internal/'
import {combineDirectoryNavigationByArrows} from './utils'


interface Props {
    query?: string
    transferMode?: 'messages' | 'calls'
    onClick?: (evt: React.MouseEvent, item: DirectoryItem) => void
    isHighlighted?: (item: DirectoryItem) => boolean
    filter?: (item: DirectoryItem) => boolean
    transferControlsStyle?: 'add' | 'replace'
}

export const Combined: React.FC<Props> = ({
    query,
    transferMode,
    onClick,
    transferControlsStyle,
    filter,
    isHighlighted,
}) => {
    const {t} = useLocalizationState()
    const {
        contactList,
        contactListByChunks,
        contactListLoading: isExternalLoading,
    } = useContactsState([
        'contactList',
        'contactListByChunks',
        'contactListLoading',
    ])
    const {isLoading: isInternalLoading, count: internalSearchResultCount} =
        useDirectorySearchResultsState()

    const ariaId1 = useId()
    const [isExternalVisible, setIsExternalVisible] = useState<boolean>(false)

    const externalContactsList = query ? contactList : contactListByChunks
    const isLoading = isInternalLoading || isExternalLoading
    const isSearchMode = !!query?.length

    const toggleExternalVisibility = useCallback(
        () => setIsExternalVisible(value => !value),
        [],
    )

    useExternalContactFolderOpenerForSearchMode({
        isSearchMode,
        setIsExternalVisible,
    })

    const [scrollParent, setScrollParent] = useState<HTMLDivElement | null>(
        null,
    )

    function getNoResultsMessage() {
        if (!query || isLoading) {
            return null
        }

        if (!internalSearchResultCount && !externalContactsList.length) {
            return (
                <div className="text-center">
                    {t.DIRECTORY.no_results_for_query(query)}
                </div>
            )
        }
    }

    const IconChevron = isExternalVisible
        ? IconChevronDown16
        : IconChevronRight16

    return (
        <div
            onKeyDownCapture={combineDirectoryNavigationByArrows}
            className="scrollbar-on-hover overflow-auto"
            ref={setScrollParent}
        >
            {isLoading && (
                <div className="text-center">{t.GLOBAL.loading()}</div>
            )}
            {getNoResultsMessage()}
            <div
                className={tcn({
                    hidden:
                        isInternalLoading ||
                        (query && !internalSearchResultCount),
                })}
            >
                <div data-combined-directory="internal">
                    <Internal
                        filter={filter}
                        query={query}
                        onClick={onClick}
                        isHighlighted={isHighlighted}
                        transferMode={transferMode}
                        transferControlsStyle={transferControlsStyle}
                    />
                </div>
            </div>
            <div data-combined-directory="external">
                <button
                    className="flex w-full cursor-pointer items-center px-1 py-1.5 bg-primary-background"
                    data-testid="directory-folder"
                    data-element="directory-item"
                    aria-expanded={isExternalVisible ? 'true' : 'false'}
                    aria-controls={ariaId1}
                    onClick={toggleExternalVisibility}
                >
                    <IconChevron className="mr-2" />
                    <span
                        data-testid="category-title"
                        className="body text-primary-on-primary"
                    >
                        {t.DIRECTORY.external_contacts()}
                    </span>
                </button>
                <div
                    id={ariaId1}
                    className={tcn('min-h-2 bg-primary-background', {
                        hidden:
                            isExternalLoading ||
                            (query && externalContactsList.length === 0),
                    })}
                >
                    {isExternalVisible && (
                        <External
                            query={query}
                            onClick={onClick}
                            isHighlighted={isHighlighted}
                            scrollParent={scrollParent}
                            withCreateContact={false}
                            transferMode={transferMode}
                            transferControlsStyle={transferControlsStyle}
                        />
                    )}
                </div>
            </div>
        </div>
    )
}
