import type {CSSProperties} from 'react'
import React from 'react'
import type {Control, FieldValues, UseFieldArrayRemove} from 'react-hook-form'
import {useController} from 'react-hook-form'

import type {FieldSubPathsByValue, RulesTypes} from '@bpinc/lib-forms-base'
import {tcn} from '@bpinc/lib-tailwind-classnames'

import type {DropdownProps} from '../Dropdown'
import {SelectFieldRaw} from '../SelectField'
import {TextFieldRaw} from '../TextField'
import {HiddenFieldRaw} from './HiddenField'
import {RemoveRow} from './RemoveRow'


type SelectAndTextFieldRowProps<
    TFieldType,
    TValues extends FieldValues,
> = {
    control: Control<TValues>
    idFieldName: FieldSubPathsByValue<TValues, TFieldType>
    selectFieldName: FieldSubPathsByValue<TValues, TFieldType>
    textFieldName: FieldSubPathsByValue<TValues, TFieldType>
    options: DropdownProps['options']
    remove: UseFieldArrayRemove
    index: number
    textFiledRules?: RulesTypes
    inputType?: 'tel' | 'email'
    typeClassName?: string
    layoutClassName?: string
    style?: CSSProperties
}

export const SelectAndTextFieldRow = <
    TFieldType,
    TValues extends FieldValues,
>({
    idFieldName,
    selectFieldName,
    textFieldName,
    textFiledRules,
    style,
    control,
    index,
    options,
    remove,
    inputType,
    typeClassName,
    layoutClassName,
}: SelectAndTextFieldRowProps<TFieldType, TValues>) => {
    const {
        field: idField,
    } = useController({
        name: idFieldName,
        control,
    })

    const {
        field: selectField,
    } = useController({
        name: selectFieldName,
        control,
    })

    const {
        field: textField,
        fieldState,
    } = useController({
        name: textFieldName,
        control,
        rules: textFiledRules,
    })
    const error = fieldState.error?.message

    return (
        <div role="row" className={tcn(layoutClassName, 'flex flex-row')}>
            <HiddenFieldRaw field={idField} />
            <SelectFieldRaw className={typeClassName} field={selectField} style={style} options={options} />
            <TextFieldRaw field={textField} style={style} type={inputType} error={error} />
            <RemoveRow index={index} remove={remove} />
        </div>
    )
}
