import {EmailTypes, PhoneTypes} from '@bpinc/ad-contacts-state-types'
import type {RecentItem} from '@bpinc/state-recent-types'


export function recentToNewContact(item: RecentItem) {
    return {
        ['Contact.first_name']: item.firstName,
        ['Contact.last_name']: item.lastName,
        ['Contact.phones']: item.number
            ? [{phone: item.number, type: PhoneTypes.BUSINESS}]
            : [],
        ['Contact.emails']: item.email
            ? [{email_address: item.email, type: EmailTypes.PRIMARY}]
            : [],
    }
}
