import type {FC} from 'react'
import React, {useEffect} from 'react'

import {useContactsStateMethods} from '@bpinc/ad-contacts-state-context'
import {contactDetailsToContactShortType} from '@bpinc/ad-contacts-state-methods'
import type {ContactDetailsType, ContactDetailsUnflatten, WithId} from '@bpinc/ad-contacts-state-types'
import {Dropdown, Loader} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {ContactPreview} from './ContactPreview'
import {MergeContactsForm} from './MergeContactsForm'
import {useSearchContactState} from './useSearchContactState'


export interface MergeContactsProps {
    srcContactDetails: Partial<WithId<ContactDetailsType>>
    onMerge?: (mergedContactDetails: WithId<ContactDetailsUnflatten>, srcContactDetails: Partial<WithId<ContactDetailsType>>) => void
}

export const MergeContacts: FC<MergeContactsProps> = ({srcContactDetails, onMerge}) => {
    const {t} = useLocalizationState()
    const {getContactFormsDefinitions} = useContactsStateMethods()

    useEffect(() => {
        getContactFormsDefinitions({})
    }, [getContactFormsDefinitions])

    const {
        distContactDetails,
        searchQuery,
        contactsAreLoading,
        mergeFormPreparing,
        options,
        methods: {search, handleSearchChange, handleSelect},
    } = useSearchContactState()

    const contactId = srcContactDetails?.id || distContactDetails?.id

    return (
        <div className="flex-auto overflow-auto pt-[5px] ">
            <Dropdown
                fluid
                search={search}
                clearable
                selection
                searchQuery={searchQuery}
                loading={contactsAreLoading}
                placeholder={t.CONTACT.search_contact_to_merge()}
                onSearchChange={handleSearchChange}
                onChange={handleSelect}
                options={options}
                menuClassName="[&>:global(.visible.menu)]:!max-h-[calc(100vh-260px)] !max-h-[calc(100vh-260px)]"
            />

            {!distContactDetails && (
                <ContactPreview
                    {...contactDetailsToContactShortType(srcContactDetails)}
                    className="mt-4 text-left text-lg"
                />
            )}

            {contactId && srcContactDetails && distContactDetails && !mergeFormPreparing && (
                <MergeContactsForm
                    mergeFormPreparing={mergeFormPreparing}
                    contactId={contactId}
                    srcContactDetails={srcContactDetails}
                    distContactDetails={distContactDetails}
                    onMerge={onMerge}
                />
            )}

            <Loader active={mergeFormPreparing} color="primary" size="big" />
        </div>
    )
}
