import React, {useContext} from 'react'
import {Button} from '@servicepattern/ui'

import type {KBFeatures} from '@bpinc/kb-types'
import {IconClose24} from '@bpinc/lib-jskit-ui'
import {SlidingPanelContext} from '@bpinc/lib-jskit-ui/src/components/SlidingPanel/SlidingPanel'
import {createComponentLoader} from '@bpinc/lib-ui-elements'
import {assertKBBridgeInterface, useKbBridgeConnector} from '@bpinc/state-kb-bridge-provider'


const {KBPageLoader} = createComponentLoader('knowledgeBase', import('knowledgeBase'), 'KBPage')

const features: Partial<KBFeatures> = {
    setupInteractionKB: true,
    openInNewTab: true,
}

/** Interaction related KB */
export const InteractionKB: React.FC = () => {
    const kbBridge = useKbBridgeConnector()
    assertKBBridgeInterface(kbBridge)

    const {onClose} = useContext(SlidingPanelContext)

    const closeButton = (
        <Button
            className="ml-2"
            flat
            icon={<IconClose24 />}
            onPressed={onClose}
            tooltipPlacement="bottom-end"
            variant="ghost"
        />
    )
    return (
        <KBPageLoader
            closeButton={closeButton}
            features={features}
            kbBridge={kbBridge}
        />
    )
}
