import React from 'react'
import {Button} from '@servicepattern/ui'

import {useIsCommunicator} from '@bpinc/ad-current-tab-data-context'
import {isRecentCallConference, isRecentEmail, isRecentExtChatConference} from '@bpinc/ad-interaction-state-methods'
import type {InitiateCall} from '@bpinc/ad-interaction-state-types'
import {useSessionState} from '@bpinc/ad-session-context'
import {useEvent} from '@bpinc/lib-helpers'
import {
    ButtonIcon,
    IconEmail16,
    IconInformation,
    IconPhone16Filled,
    IconRoundChat16Filled,
} from '@bpinc/lib-jskit-ui'
import {assertDefined} from '@bpinc/lib-types-helpers'
import type {ChatParams} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'
import type {RecentItem} from '@bpinc/state-recent-types'

import type {OnEmailParams} from '../../directory/components/user/types'


type Props = {
    item: RecentItem
    transferMode?: 'messages' | 'calls'
    crmObjectsListOpened: boolean
    onCall: (callParams: InitiateCall) => void
    onChat: (chatParams: ChatParams) => void
    onEmail: (emailParams: OnEmailParams) => void
    onDetails: (item: RecentItem) => void
    toggleShowCrmObjectsList: () => void
}

export const RecentItemControls: React.FC<Props> = ({
    item,
    transferMode,
    crmObjectsListOpened,
    onCall,
    onChat,
    onEmail,
    onDetails,
    toggleShowCrmObjectsList,
}) => {

    const t = useLocalizationState('t')
    const {
        userId: selfUserId,
        phoneDeviceCapabilities,
    } = useSessionState(['userId', 'phoneDeviceCapabilities'])

    const isCommunicator = useIsCommunicator()

    const isSelf = item.userId === selfUserId
    const cleanNumber = item.number?.startsWith('+') ? item.number.substring(1) : item.number

    const showCallButton =
        transferMode !== 'calls' &&
        !!phoneDeviceCapabilities.canDial &&
        !isRecentCallConference(item) &&
        !isRecentExtChatConference(item) &&
        !!cleanNumber &&
        !isSelf

    const showChatButton =
        transferMode !== 'messages' &&
        !isRecentCallConference(item) &&
        !isRecentExtChatConference(item) &&
        (
            !!item.contactId ||
            (!!item.userId && !isRecentEmail(item)) ||
            !!cleanNumber ||
            !!item.groupChatData
        )

    const showEmailButton = !isCommunicator && isRecentEmail(item) && !!item.email
    const showDetailsButton = !!item.userId || !!item.contactId


    const handleCall = useEvent(() => {
        assertDefined(onCall)
        assertDefined(cleanNumber)
        onCall({
            phone: cleanNumber,
            contactId: item.contactId,
            userId: item.userId,
            firstName: item.firstName,
            lastName: item.lastName,
            serviceId: item.serviceId,
        })
    })

    const handleChat = useEvent(() => {
        assertDefined(onChat)
        onChat({
            phone: cleanNumber,
            contactId: item.contactId,
            userId: item.userId,
            firstName: item.firstName,
            lastName: item.lastName,
            serviceId: item.serviceId,
            groupChatId: item.groupChatData?.chatId,
        })
    })

    const handleEmail = useEvent(() => {
        assertDefined(onEmail)
        assertDefined(item.email)
        onEmail({
            contactId: item.contactId,
            emailAddress: item.email,
        })
    })

    const handleDetails = useEvent(() => onDetails(item))

    const handleCrmObjectsClick = useEvent((e: React.MouseEvent) => {
        e.stopPropagation()
        toggleShowCrmObjectsList()
    })

    return (
        <>
            {!!item.associatedCRMObjects?.length && (
                <Button
                    data-testid="crm-objects-button"
                    size="xs"
                    variant={crmObjectsListOpened ? 'secondary' : 'neutral'}
                    tooltip={t.RECENT.button_show_crm_objects_text()}
                    className="h-6 items-center pt-1"
                    onClick={handleCrmObjectsClick}
                >
                    {t.RECENT.button_show_crm_objects_text_short()}
                </Button>
            )}

            {showCallButton && (
                <ButtonIcon
                    data-testid="call-button"
                    tooltip={t.CONTACT.button_start_call_text()}
                    onClick={handleCall}
                >
                    <IconPhone16Filled />
                </ButtonIcon>
            )}
            {showChatButton && (
                <ButtonIcon
                    data-testid="chat-button"
                    tooltip={t.CONTACT.button_initiate_chat_text()}
                    onClick={handleChat}
                >
                    <IconRoundChat16Filled />
                </ButtonIcon>
            )}
            {showEmailButton && (
                <ButtonIcon
                    data-testid="email-button"
                    tooltip={t.CONTACT.button_send_email_text()}
                    onClick={handleEmail}
                >
                    <IconEmail16 />
                </ButtonIcon>
            )}
            {showDetailsButton && (
                <ButtonIcon
                    data-testid="user-info-button"
                    tooltip={t.CONTACT.button_show_contact_details_text()}
                    onPressed={handleDetails}
                >
                    <IconInformation />
                </ButtonIcon>
            )}
        </>
    )
}
