import type {ReactNode, SyntheticEvent} from 'react'
import React, {useMemo} from 'react'

import {useInteractionState} from '@bpinc/ad-interaction-state-context'
import {useSessionState} from '@bpinc/ad-session-context'
import type {ItemState} from '@bpinc/ad-transport-types'
import type {AppRoutes} from '@bpinc/lib-communicator-navigation-state'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {UserProfilePreview} from '../components/UserProfilePreview'
import {useSettingsHandlers} from './useSettingsHandlers'


interface UseSettingsMenuParams {
    toggleSettings: (forceOpenedOrEvent?: boolean | SyntheticEvent) => void
    closeMenu: () => void
    openSettingsMenu: () => void
}

const menuItemKeys = [
    'recent',
    'favorites',
    'directory',
    'dialpad',
    'knowledge-base',
    'settings',
    'help',
    'user-profile',
    'back',
    'phone-device',
    'audio-devices',
    'change-password',
    'predefined-chat-messages',
    'preferences',
    'emi-accounts',
    'prerecorded-messages',
    'keyboard_shortcuts',
    'ringtones',
    'about',
    'logout',
    'calendar',
] as const
export type MenuItemKey = typeof menuItemKeys[number]

export interface MenuItemParams {
    key: MenuItemKey
    onClick: (evt: SyntheticEvent) => void
    children: ReactNode
    enabledOffline?: boolean
    isLink?: boolean
    allowRender?: boolean
    page?: AppRoutes
    disabled?: boolean
    disabledTitle?: string
}

const activeCallStates: ItemState[] = ['IVR', 'delivery_pending', 'delivered', 'hold']

export function useSettingsMenu({
    toggleSettings,
    closeMenu,
    openSettingsMenu,
}: UseSettingsMenuParams): MenuItemParams[] {
    const {
        isSsoEnabled,
        phoneType,
        tenantFeatures,
    } = useSessionState()
    const {items} = useInteractionState()
    const {t} = useLocalizationState()

    const hasActiveCalls = items.some(item =>
        item.mediaType === 'voice' &&
        item.state &&
        activeCallStates.includes(item.state),
    )

    const isAudioDevicesLinkVisible = !!phoneType && [
        'phone_type_browser',
        'phone_type_soft_secure',
        'phone_type_soft_unsecure',
    ].includes(phoneType)

    const isEMIAvailable = !!tenantFeatures?.msTeamsIntegrationEnabled

    const {
        handlePrerecordedMessagesClick,
        handlePreferencesClick,
        handleUserProfileClick,
        handleAboutClick,
        handleKeyboardShortcutsClick,
        handleLogoutClick,
        handleChangePasswordClick,
        handlePhoneSelectionClick,
        handleAudioDevicesClick,
        handlePredefinedMessagesClick,
        handleRingtonesAndAlertsVolumeClick,
        handleEMIAccountsClick,
    } = useSettingsHandlers({closeMenu, openSettingsMenu, hasActiveCalls})

    return useMemo(
        function memoizeOutput() {
            return [
                {
                    key: 'user-profile',
                    onClick: handleUserProfileClick,
                    children: <UserProfilePreview />,
                    enabledOffline: true,
                },
                {
                    key: 'back',
                    onClick: toggleSettings,
                    children: (
                        <span data-testid="menu-item-text">
                            {t.SETTINGS.settings_menu_go_back()}
                        </span>
                    ),
                    isLink: true,
                    enabledOffline: true,
                },
                {
                    key: 'phone-device',
                    onClick: handlePhoneSelectionClick,
                    children: (
                        <span data-testid="menu-item-text">
                            {t.SETTINGS.settings_menu_phoneDevice()}
                        </span>
                    ),
                    disabled: hasActiveCalls,
                    disabledTitle: t.SETTINGS.menu_phone_devices_disabled_tooltip(),
                },
                {
                    key: 'audio-devices',
                    onClick: handleAudioDevicesClick,
                    allowRender: isAudioDevicesLinkVisible,
                    children: (
                        <span data-testid="menu-item-text">
                            {t.SETTINGS.settings_menu_audioDevices()}
                        </span>
                    ),
                },
                {
                    key: 'change-password',
                    onClick: handleChangePasswordClick,
                    children: (
                        <span data-testid="menu-item-text">
                            {t.CHANGE_PASSWORD_DIALOG_BOX.changePassword_heading()}
                        </span>
                    ),
                },
                {
                    key: 'predefined-chat-messages',
                    onClick: handlePredefinedMessagesClick,
                    children: (
                        <span data-testid="menu-item-text">
                            {t.SETTINGS.settings_menu_chatCannedResponses()}
                        </span>
                    ),
                },
                {
                    key: 'preferences',
                    onClick: handlePreferencesClick,
                    children: (
                        <span data-testid="menu-item-text">
                            {t.SETTINGS.settings_menu_preferences()}
                        </span>
                    ),
                    enabledOffline: true,
                },
                {
                    key: 'emi-accounts',
                    onClick: handleEMIAccountsClick,
                    allowRender: isEMIAvailable,
                    children: (
                        <span data-testid="menu-item-text">
                            {t.UNDEFINED.tab_enterprise_messaging_accounts_heading()}
                        </span>
                    ),
                },
                {
                    key: 'prerecorded-messages',
                    onClick: handlePrerecordedMessagesClick,
                    children: (
                        <span data-testid="menu-item-text">
                            {t.VOICE_PROMPTS_DIALOG_BOX.edit_voice_prompts_heading()}
                        </span>
                    ),
                },
                {
                    key: 'keyboard_shortcuts',
                    onClick: handleKeyboardShortcutsClick,
                    children: (
                        <span data-testid="menu-item-text">
                            {t.SETTINGS.settings_menu_shortcuts()}
                        </span>
                    ),
                    enabledOffline: true,
                },
                {
                    key: 'ringtones',
                    onClick: handleRingtonesAndAlertsVolumeClick,
                    children: (
                        <span data-testid="menu-item-text">
                            {t.SETTINGS.settings_menu_ringtone_volume()}
                        </span>
                    ),
                },
                {
                    key: 'about',
                    onClick: handleAboutClick,
                    children: (
                        <span data-testid="menu-item-text">
                            {t.SETTINGS.settings_menu_about()}
                        </span>
                    ),
                    enabledOffline: true,
                },
                {
                    key: 'logout',
                    onClick: handleLogoutClick,
                    allowRender: !isSsoEnabled,
                    children: (
                        <span data-testid="menu-item-text">
                            {t.GLOBAL.logout()}
                        </span>
                    ),
                },
            ]
        },
        [
            handleAboutClick,
            handleAudioDevicesClick,
            handleChangePasswordClick,
            handleEMIAccountsClick,
            handleKeyboardShortcutsClick,
            handleLogoutClick,
            handlePhoneSelectionClick,
            handlePredefinedMessagesClick,
            handlePreferencesClick,
            handlePrerecordedMessagesClick,
            handleRingtonesAndAlertsVolumeClick,
            handleUserProfileClick,
            hasActiveCalls,
            isAudioDevicesLinkVisible,
            isEMIAvailable,
            isSsoEnabled,
            toggleSettings,
            t,
        ],
    )
}
