import React, {useMemo} from 'react'

import type {VoiceTranscriptSuggestion} from '@bpinc/ad-interaction-state-types'

import {
    getExternalChatFileData,
    isErrorExternalChatMessage,
    isInviteExternalChatMessage,
    isLocationExternalChatMessage,
    isSystemExternalChatMessage,
} from '../../../helpers'
import type {ChatScrollNodeType, ExternalChatMessageWithDate} from '../../../types'
import {
    isActivityMessageGroup,
    isChatMessage,
    isChatMessageDate,
    isHistoryCallMessage,
    isHistoryEmailMessage,
    isHistoryOutboundMessagingCampaignMessage,
    isHistoryRemoteAssistMessage,
    isVoiceTranscriptSuggestion,
} from '../../../types'
import {ActivityMessageGroup} from './messages/ActivityMessageGroup'
import {BotSuggestion} from './messages/BotSuggestion'
import {DateMessage} from './messages/DateMessage'
import {FileMessage} from './messages/FileMessage'
import {InviteMessage} from './messages/InviteMessage'
import {LocationMessage} from './messages/LocationMessage'
import {OutboundMessagingCampaignMessage} from './messages/OutboundMessagingCampaignMessage'
import {PageLinkMessage} from './messages/PageLinkMessage'
import {PlainMessage} from './messages/PlainMessage'
import {SystemMessage} from './messages/SystemMessage'
import {WithOptionalNameAndAvatar} from './messages/WithOptionalNameAndAvatar'


export type MarkAsReadProps = {
    scrollNode: ChatScrollNodeType
    itemInteractionStepId: string
    itemContactId: string
    isDIDChat: boolean
    itemGlobalInteractionId: string
    interactionItemId: string
}

type ExternalChatMessageProps = MarkAsReadProps & {
    message: ExternalChatMessageWithDate | VoiceTranscriptSuggestion
}

export const ExternalChatMessage: React.FC<ExternalChatMessageProps> = React.memo(function ExternalChatMessage({
    message,
    scrollNode,
    itemInteractionStepId,
    itemContactId,
    isDIDChat,
    itemGlobalInteractionId,
    interactionItemId,
}) {
    const markAsReadData = useMemo(() => ({scrollNode,
        itemInteractionStepId,
        itemContactId,
        isDIDChat,
        itemGlobalInteractionId,
        interactionItemId}), [interactionItemId, isDIDChat, itemContactId, itemGlobalInteractionId, itemInteractionStepId, scrollNode])

    const fileData = useMemo(() => {
        if (isVoiceTranscriptSuggestion(message)) {
            return
        }
        return getExternalChatFileData(message)
    }, [message])

    if (isVoiceTranscriptSuggestion(message)) {
        return <BotSuggestion itemId={interactionItemId} direction="outgoing" message={message} />
    }

    if (isChatMessageDate(message)) {
        return <DateMessage message={message} />
    }

    if (isActivityMessageGroup(message)) {
        return <ActivityMessageGroup message={message} />
    }

    if (isHistoryOutboundMessagingCampaignMessage(message)) {
        return <OutboundMessagingCampaignMessage message={message} />
    }

    if (isHistoryCallMessage(message)) {
        return (
            <WithOptionalNameAndAvatar
                messageClassName="w-full"
                className="min-w-[300px]"
                message={message}
            >
                {message.msg}
            </WithOptionalNameAndAvatar>
        )
    }
    if (isHistoryEmailMessage(message)) {
        return <WithOptionalNameAndAvatar message={message}>{message.msg}</WithOptionalNameAndAvatar>
    }

    if (isChatMessage(message)) {
        if (isSystemExternalChatMessage(message)) {
            return <SystemMessage message={message} />
        }
        if (isInviteExternalChatMessage(message)) {
            return <InviteMessage message={message} />
        }
        if (isErrorExternalChatMessage(message)) {
            return <WithOptionalNameAndAvatar message={message}>{message.msg}</WithOptionalNameAndAvatar>
        }
        if (isLocationExternalChatMessage(message)) {
            const {page, longitude, latitude} = message
            return (
                <WithOptionalNameAndAvatar message={message}>
                    <LocationMessage link={page} longitude={longitude} latitude={latitude} />
                </WithOptionalNameAndAvatar>
            )
        }

        const {page, title, partyId} = message
        if (page) {
            return (
                <WithOptionalNameAndAvatar
                    message={message}
                    isFullWidth
                    withoutBorder
                    messageClassName="w-full !border-none !bg-transparent after:content-none before:content-none"
                >
                    <PageLinkMessage link={page} title={title} partyId={partyId} />
                </WithOptionalNameAndAvatar>
            )
        }
    }

    if (fileData) {
        return <FileMessage markAsReadData={markAsReadData} message={message} fileData={fileData} />
    }

    if (isHistoryRemoteAssistMessage(message)) {
        return <WithOptionalNameAndAvatar message={message}>{message.msg}</WithOptionalNameAndAvatar>
    }

    return (
        <WithOptionalNameAndAvatar message={message}>
            <PlainMessage message={message} markAsReadData={markAsReadData} />
        </WithOptionalNameAndAvatar>
    )
})
