import {useEffect} from 'react'

import {useInteractionChatStateConnector} from '@bpinc/ad-interaction-chat-state-context'
import type {WebRtcError} from '@bpinc/ad-interaction-chat-state-types'
import {useEvent} from '@bpinc/lib-helpers'
import type {NotificationType} from '@bpinc/lib-notifications-context'
import {Notifications, useNotificationsState} from '@bpinc/lib-notifications-context'
import {useLocalizationState} from '@bpinc/state-localization-context'


export const useWebRtcCallInChatErrorNotifications = () => {
    const interactionChatStateConnector = useInteractionChatStateConnector()
    const {showNotification} = useNotificationsState('methods')
    const {ERROR_MESSAGES: i18n} = useLocalizationState('t')

    const onWebRtcError = useEvent((error: WebRtcError) => {
        let type: NotificationType, message: string
        switch (error) {
            case 'invalid_server':
                type = 'error'
                message = i18n.chat_web_rtc_invalid_ice_server()
                break
            case 'answer_timeout':
                type = 'info'
                message = i18n.chat_web_rtc_answer_timeout()
                break
            case 'already_exists':
                type = 'warning'
                message = i18n.chat_web_rtc_only_1_call_simultaneously()
                break
        }
        showNotification({
            type,
            id: Notifications.ChatWebRtcError,
            message,
            closable: true,
            duration: 10_000,
        })
    })

    useEffect(() => {
        const subscription = interactionChatStateConnector.dispenser.subscribe({onWebRtcError})
        return subscription.unsubscribe
    }, [interactionChatStateConnector, onWebRtcError])
}
