import React from 'react'

import {SmImage} from '@bpinc/lib-external-ui-elements'
import {iconLocation} from '@bpinc/lib-icons'
import {useLocalizationState} from '@bpinc/state-localization-context'


type Props = {
    link?: string
    latitude?: number
    longitude?: number
}

const googleMapsUrl = (latitude: number, longitude: number) => {
    return `https://www.google.com/maps/search/?api=1&query=${latitude},${longitude}`
}

export const LocationMessage: React.FC<Props> = ({link, latitude, longitude}) => {

    const {EXTERNAL_CHAT: i18n} = useLocalizationState('t')
    const url = link || ((latitude && longitude) ? googleMapsUrl(latitude, longitude) : undefined)

    return (
        <SmImage
            src={iconLocation}
            title={i18n.location_message_icon_tooltip()}
            as="a"
            href={url}
            target="_blank"
        />
    )
}
