import type {FC} from 'react'
import React, {useCallback, useMemo, useState} from 'react'
import {useForm} from 'react-hook-form'
import {DevTool} from '@hookform/devtools'
import {merge} from 'lodash-es'

import {useContactsStateMethods} from '@bpinc/ad-contacts-state-context'
import {useFormatting} from '@bpinc/ad-formatting-context'
import {useInteractionChatState} from '@bpinc/ad-interaction-chat-state-context'
import {AppRoutes, useNavigationState} from '@bpinc/lib-communicator-navigation-state'
import {emptyDirectoryUIState, useDirectoryUIState} from '@bpinc/lib-communicator-ui-context'
import {SmForm} from '@bpinc/lib-external-ui-elements'
import {useEmailValidationRules, usePauseSymbolsValidation, usePhoneValidationRules} from '@bpinc/lib-forms-base'
import {MessengerFieldBase, TextField} from '@bpinc/lib-forms-ui'
import {usePersistedFormValues} from '@bpinc/lib-helpers'
import {Notifications, useNotificationsState} from '@bpinc/lib-notifications-context'
import {Loader, SemanticButton} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {EditingContactDetailsHeader} from '../../../contact-details'


export const CreateContactForm: FC = () => {
    const [isLoading, setIsLoading] = useState(false)
    const {t} = useLocalizationState()
    const {renderWithOptionalPostfix} = useFormatting()
    const {toggleCreateContactMode, setCreateContactFormValues, createContactFormValues} = useDirectoryUIState()
    const setActivePage = useNavigationState('setActivePage')
    const {showNotification} = useNotificationsState('methods')
    const {createContact} = useContactsStateMethods()
    const form = useForm({
        defaultValues: createContactFormValues,
        reValidateMode: 'onBlur',
    })
    const {control, handleSubmit, getValues} = form
    const emailValidationRules = useEmailValidationRules()
    const phoneValidationRules = usePhoneValidationRules()
    const pauseSymbolsValidationRules = usePauseSymbolsValidation()
    const phoneRules = useMemo(() => merge(phoneValidationRules, pauseSymbolsValidationRules), [phoneValidationRules, pauseSymbolsValidationRules])


    const {controlledUnmountRef} = usePersistedFormValues({
        clearPersistedValues: () => setCreateContactFormValues(emptyDirectoryUIState.createContactFormValues),
        setPersistedValues: () => setCreateContactFormValues(getValues()),
    })

    const onSubmit = useMemo(
        function onSubmitCallback() {
            return handleSubmit(function onSuccess(data) {
                setIsLoading(true)
                createContact(
                    {
                        contact: {
                            first_name: data.firstName,
                            last_name: data.lastName,
                            ...(data.email ? {emails: [{type: 'PRIMARY', email_address: data.email}]} : {}),
                            ...(data.phone ? {phones: [{type: 'BUSINESS', phone: data.phone}]} : {}),
                            ...(data.messenger?.type && data.messenger?.user_id ? {messengers: [
                                {
                                    type: data.messenger.type,
                                    user_id: data.messenger.user_id,
                                    user_name: data.messenger.user_name,
                                    account_id: data.messenger.account_id,
                                },
                            ]} : {}),
                        },
                    },
                    {
                        onSuccess(contactId: string) {
                            controlledUnmountRef.current = true
                            setIsLoading(false)
                            toggleCreateContactMode()
                            setActivePage({name: AppRoutes.ContactDetails, params: {contactId}})
                        },
                        onError() {
                            setIsLoading(false)
                            showNotification({
                                type: 'error',
                                id: Notifications.ContactCreationError,
                                tags: ['current-view'],
                                message: t.VALIDATION.CONTACT_CREATION_ERROR(),
                                duration: 10_000,
                            })
                        },
                    },
                )
            })
        },
        [
            controlledUnmountRef,
            createContact,
            handleSubmit,
            setActivePage,
            showNotification,
            t.VALIDATION,
            toggleCreateContactMode,
        ],
    )

    const goBack = useCallback(
        function goBackCallback() {
            controlledUnmountRef.current = true
            toggleCreateContactMode()
        },
        [controlledUnmountRef, toggleCreateContactMode],
    )

    const availableMessengers = useInteractionChatState('messengers')
    return (
        <>
            <EditingContactDetailsHeader onGoBack={goBack} />
            <SmForm className="overflow-y-auto overflow-x-hidden rounded pb-2 pr-3.5 pt-[21px]">
                <TextField
                    name="firstName"
                    autoFocus
                    control={control}
                    label={renderWithOptionalPostfix(t.UNDEFINED.contact_first_name())}
                    placeholder={t.UNDEFINED.contact_first_name()}
                />
                <TextField
                    name="lastName"
                    control={control}
                    required
                    label={t.UNDEFINED.contact_last_name()}
                    placeholder={t.UNDEFINED.contact_last_name()}
                />
                <TextField
                    name="phone"
                    className="w-1/2"
                    control={control}
                    label={renderWithOptionalPostfix(t.UNDEFINED.contact_phone())}
                    placeholder={t.UNDEFINED.contact_phone()}
                    rules={phoneRules}
                />
                <TextField
                    name="email"
                    control={control}
                    className="w-1/2"
                    label={renderWithOptionalPostfix(t.UNDEFINED.contact_email())}
                    placeholder={t.UNDEFINED.contact_email()}
                    rules={emailValidationRules}
                />
                <MessengerFieldBase
                    withoutPadding
                    control={control}
                    label={t.UNDEFINED.contact_messenger()}
                    availableMessengers={availableMessengers}
                    className="first:!px-0"
                    accountIdFieldName="messenger.account_id"
                    typeFiledName="messenger.type"
                    userIdFieldName="messenger.user_id"
                    userNameFieldName="messenger.user_name"
                />
                <SemanticButton basic size="mini" onClick={onSubmit}>
                    {t.CONTACT.create_contact()}
                </SemanticButton>
                {/* TODO: Add open in CRM functionality when design is ready */}
                <SemanticButton basic size="mini" onClick={onSubmit}>
                    {t.CONTACT.create_and_open_in_crm()}
                </SemanticButton>
            </SmForm>
            {process.env.NODE_ENV === 'development' && <DevTool control={form.control} />}
            {isLoading && <Loader active color="primary" size="big" className="pr-3.5" />}
        </>
    )
}
