import type {ReactElement, ReactFragment} from 'react'

import {type ScrollData, useScrollStateProviderBuilder} from '@bpinc/ad-scroll-state-context-builder'
import type {FCWithChildren} from '@bpinc/lib-types-helpers'

import type {ScrollStateData} from './types'


const scrollAtTheTopNative: ScrollData = {type: 'NATIVE'}
const scrollAtTheTopVirtual: ScrollData = {type: 'VIRTUAL'}

const defaultScrollStateData: ScrollStateData = {
    recent: {
        all: scrollAtTheTopNative,
        messages: scrollAtTheTopNative,
        calls: scrollAtTheTopNative,
    },
    directory: {
        internal: scrollAtTheTopNative,
        external: scrollAtTheTopVirtual,
    },
    favorites: scrollAtTheTopNative,
}

export const ScrollStateProvider: FCWithChildren<ReactElement | ReactFragment> = ({children}) => {
    const {Provider} = useScrollStateProviderBuilder<ScrollStateData>({
        children,
        defaultScrollStateData,
    })

    return Provider
}

