import type {ReactElement, ReactFragment} from 'react'
import React from 'react'

import type {AuthorizedConnectors, BaseConnectors} from '@bpinc/ad-connected-state-types'
import {AuthorizedProviders} from '@bpinc/ad-local-providers'
import {useSessionState} from '@bpinc/ad-session-context'

import {AuthorizedConnectorsConsumers} from './AuthorizedConnectorsConsumers'
import {UnauthorizedUI} from './UnauthorizedUI'


type Props = {
    baseConnectors: BaseConnectors
    children: (
        baseConnectors: BaseConnectors,
        authorizedConnectors: AuthorizedConnectors,
    ) => ReactElement | ReactFragment
}

export const AuthorizationUIRouter: React.FC<Props> = React.memo(({
    baseConnectors,
    children,
}) => {
    const isLoggedIn = useSessionState('isLoggedIn')

    if (isLoggedIn) {
        return (
            <AuthorizedProviders baseConnectors={baseConnectors}>
                {authorizedConnectors => (
                    <AuthorizedConnectorsConsumers
                        baseConnectors={baseConnectors}
                        authorizedConnectors={authorizedConnectors}
                    >
                        {children}
                    </AuthorizedConnectorsConsumers>
                )}
            </AuthorizedProviders>
        )
    }
    return <UnauthorizedUI />
})
