import React, {useEffect, useMemo, useState} from 'react'

import {useResizeObserver} from '@bpinc/lib-helpers'
import {useInteractionId} from '@bpinc/lib-interaction-context'
import {useSlidingModalState} from '@bpinc/lib-sliding-modal-context'


interface AnimatedInteractionsListProps {
    children: React.ReactNode
}

export const AnimatedInteractionsList: React.FC<AnimatedInteractionsListProps> = ({
    children,
}) => {
    const interactionId = useInteractionId()
    const isInteractionsListShown = useSlidingModalState(
        s => Object.values(s.getInteractionModalData(interactionId)).every(data => !data.show),
    )

    const [contentRef, setContentRef] = useState<HTMLDivElement | null>(null)
    const resizeObserver = useResizeObserver(contentRef)
    const [contentHeight, setContentHeight] = useState<number | undefined>(undefined)

    useEffect(() => setContentHeight(resizeObserver?.contentRect.height), [resizeObserver])

    const style = useMemo(() => {
        const height = isInteractionsListShown ? contentHeight : 0

        return {
            height,
            minHeight: height,
            maxHeight: height,
        }
    }, [contentHeight, isInteractionsListShown])

    return (
        <div
            className="overflow-hidden transition-all"
            style={style}
        >
            <div ref={setContentRef}>
                {children}
            </div>
        </div>
    )
}
