import React from 'react'

import {useAssociatedChatInteractionId, useInteractionProp} from '@bpinc/ad-interaction-state-context'
import {isInternalMessage} from '@bpinc/ad-interaction-state-methods'
import {useInteractionId} from '@bpinc/lib-interaction-context'
import {assertDefined} from '@bpinc/lib-types-helpers'
import {useInteractionUIStateItemSelector} from '@bpinc/state-interaction-ui'

import {ChatInWindowPlaceholder} from './ChatInWindowPlaceholder'
import {InteractionTabs} from './InteractionTabs'
import {InternalScenarioMessages} from './InternalScenarioMessages'


export const ActiveInteractionContentArea: React.FC = () => {
    const interactionId = useInteractionId()
    const activeChatId = useAssociatedChatInteractionId(interactionId)
    assertDefined(activeChatId) // is it safe assertion?
    const activeItemIsInternalMessage = useInteractionProp(interactionId, isInternalMessage)
    const chatView = useInteractionUIStateItemSelector(activeChatId, s => s?.chatView)

    if (activeItemIsInternalMessage) {
        return <InternalScenarioMessages />
    }

    if (chatView === 'embedded') {
        return <InteractionTabs />
    }

    return <ChatInWindowPlaceholder />
}
