import type {FC} from 'react'
import React from 'react'
import {useForm} from 'react-hook-form'
import {DevTool} from '@hookform/devtools'

import type {ContactDetailsType, WithId} from '@bpinc/ad-contacts-state-types'
import {SmForm, SmList} from '@bpinc/lib-external-ui-elements'
import {Loader, SemanticButton} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {useFormElements} from '../../hooks/useFormElements'
import {useFormElementsToViews} from '../../hooks/useFormElementsToViews'
import {FormElementView} from '../FormElementView'
import type {MergeContactsProps} from './'
import {useOnSubmit} from './useOnSubmit'


export interface MergeContactsFormProps extends MergeContactsProps {
    mergeFormPreparing: boolean
    distContactDetails: Partial<WithId<ContactDetailsType>> | undefined
    contactId: string
}

export const MergeContactsForm: FC<MergeContactsFormProps> = ({
    mergeFormPreparing,
    srcContactDetails,
    distContactDetails,
    contactId,
    onMerge,
}) => {
    const {t} = useLocalizationState()

    const form = useForm()
    const {control, handleSubmit} = form
    const formElements = useFormElements()
    const fields = useFormElementsToViews({elements: formElements, values: distContactDetails, srcValues: srcContactDetails})
    const {submitting, onSubmit} = useOnSubmit({
        srcContactDetails,
        onMerge,
        contactId,
        handleSubmit,
        distContactDetails,
    })

    return (
        <>
            <SmForm className="overflow-hidden pt-5 text-left">
                <h3 className="m-0 text-xl">
                    {t.CONTACT.merge_form_title()}
                </h3>
                <section className="pt-4">
                    {!mergeFormPreparing && (
                        <SmList divided relaxed>
                            {fields.map((field, i) => (
                                <FormElementView
                                    values={distContactDetails}
                                    key={i}
                                    contactId={contactId}
                                    {...field}
                                    control={control}
                                />
                            ))}
                        </SmList>
                    )}
                </section>
                <SemanticButton
                    className="mt-3.5 h-8 px-6"
                    onClick={onSubmit}
                    disabled={submitting}
                    primary
                >
                    {t.CONTACT.merge_contacts()}
                </SemanticButton>
                <Loader
                    active={submitting}
                    color="primary"
                    size="big"
                />
            </SmForm>
            {process.env.NODE_ENV === 'development' && <DevTool control={form.control} />}
        </>
    )
}
