import {useMemo} from 'react'

import {addTime, formatDate} from '@bpinc/lib-helpers'
import {useLocalizationState} from '@bpinc/state-localization-context'


type RescheduleOption = {
    id: number
    name: string
    getDateAndTime: () => {newTime: string; newDate: string}
}

const rescheduleDateFormat = 'L'
const rescheduleTimeFormat = 'LT'

export function useRescheduleFormOptions() {
    const {t} = useLocalizationState()

    return useMemo<RescheduleOption[]>(() => [
        {
            id: 1,
            name: t.RESCHEDULE.rsc_afternoon(),
            getDateAndTime: () => {
                const newTime = '12:00'
                const newDate = formatDate(
                    addTime(new Date(), 'days', new Date().getHours() >= 12 ? 1 : 0),
                    rescheduleDateFormat,
                )
                return {newTime, newDate}
            },
        },
        {
            id: 2,
            name: t.RESCHEDULE.rsc_evening(),
            getDateAndTime: () => {
                const newTime = '18:00'
                const newDate = formatDate(
                    addTime(new Date(), 'days', new Date().getHours() >= 18 ? 1 : 0),
                    rescheduleDateFormat,
                )
                return {newTime, newDate}
            },
        },
        {
            id: 3,
            name: t.RESCHEDULE.rsc_tomorrowMorning(),
            getDateAndTime: () => {
                const newTime = '09:00'
                const newDate = formatDate(addTime(new Date(), 'days', 1), rescheduleDateFormat)
                return {newTime, newDate}
            },
        },
        {
            id: 4,
            name: t.RESCHEDULE.rsc_tomorrowAfternoon(),
            getDateAndTime: () => {
                const newTime = '12:00'
                const newDate = formatDate(addTime(new Date(), 'days', 1), rescheduleDateFormat)
                return {newTime, newDate}
            },
        },
        {
            id: 5,
            name: t.RESCHEDULE.rsc_tomorrowEvening(),
            getDateAndTime: () => {
                const newTime = '18:00'
                const newDate = formatDate(addTime(new Date(), 'days', 1), rescheduleDateFormat)
                return {newTime, newDate}
            },
        },
        {
            id: 6,
            name: t.RESCHEDULE.rsc_in1hour(),
            getDateAndTime: () => {
                const date = addTime(new Date(), 'hours', 1)
                const newTime = formatDate(date, rescheduleTimeFormat)
                const newDate = formatDate(date, rescheduleDateFormat)
                return {newTime, newDate}
            },
        },
        {
            id: 7,
            name: t.RESCHEDULE.rsc_in2hours(),
            getDateAndTime: () => {
                const date = addTime(new Date(), 'hours', 2)
                const newTime = formatDate(date, rescheduleTimeFormat)
                const newDate = formatDate(date, rescheduleDateFormat)
                return {newTime, newDate}
            },
        },
    ], [t])
}
