import type {FC} from 'react'
import React, {useCallback, useEffect} from 'react'
import {useForm} from 'react-hook-form'

import {useCalendarState, useCalendarStateMethods} from '@bpinc/cal-state-context'
import type {CalendarEventFields} from '@bpinc/cal-state-types'
import type {CommonSlidingModalItemProps} from '@bpinc/lib-sliding-modal-context'
import {createComponentLoader, SlidingModal} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'


const {CalendarEventFormLoader} = createComponentLoader('calendar', () => import('calendar'), 'CalendarEventForm')

export const CalendarEventSlidingModal: FC<CommonSlidingModalItemProps> = ({closeModal, closing, style, show}) => {
    const {t} = useLocalizationState()
    const selectedEvent = useCalendarState('selectedEvent')
    const {closeEventForm} = useCalendarStateMethods()

    const form = useForm<CalendarEventFields>()

    const handleClose = useCallback(() => {
        closeEventForm()
    }, [closeEventForm])

    useEffect(() => {
        if (!selectedEvent) {
            closeModal()
        }
    }, [closeModal, selectedEvent])

    return (
        <SlidingModal
            name="calendarEvent"
            header={t.CALENDAR_EVENT_MODAL.title()}
            closeModal={handleClose}
            show={show}
            closing={closing}
            style={style}
        >
            {selectedEvent && (
                <CalendarEventFormLoader
                    event={selectedEvent}
                    form={form}
                    onClose={handleClose}
                />
            )}
        </SlidingModal>
    )
}
