import {useCallback} from 'react'

import {PHONE_NUMBER_VALID_SYMBOLS_REGEX} from '@bpinc/ad-regexp-constants'
import {useSessionState} from '@bpinc/ad-session-context'

import type {CategoryItem, ComposedContact, InternalContact} from '../hooks/useQueryContacts/useQueryContacts'


export function isInternalContact(
    contact: ComposedContact,
): contact is InternalContact {
    return 'userId' in contact
}

export function isCategoryItem(
    contact: ComposedContact,
): contact is CategoryItem {
    return 'mediaType' in contact && 'title' in contact
}
export function isInternalContactExtension(extension: string) {
    return new RegExp(PHONE_NUMBER_VALID_SYMBOLS_REGEX).test(extension)
}
export function useCheckSelfUser() {
    const {userId: selfUserId} = useSessionState()
    return useCallback((userId: string) => {
        return selfUserId && userId === selfUserId
    }, [selfUserId])
}
