import type {FC} from 'react'
import React from 'react'

import {personalPaginationAtom, personalSortingAtom} from '../atoms'
import {Pagination} from '../Pagination'
import {SortingControls} from '../SortingControls'
import {useSortingOptions} from './useSortingOptions'


interface TableHeaderContentProps {
    noData: boolean
    actualQueueCount: number
    totalQueueCount: number | undefined
}

export const TableHeaderContent: FC<TableHeaderContentProps> = ({
    actualQueueCount,
    totalQueueCount,
    noData,
}) => {
    const sortingOptions = useSortingOptions()

    if (noData) {
        return null
    }

    return (
        <div className="flex items-center justify-between">
            <SortingControls
                sortingAtom={personalSortingAtom}
                options={sortingOptions}
            />
            <Pagination
                paginationAtom={personalPaginationAtom}
                actualQueueCount={actualQueueCount}
                totalQueueCount={totalQueueCount}
            />
        </div>
    )
}
