import type {FC} from 'react'
import React from 'react'
import {useAtom, useAtomValue} from 'jotai'

import {useTransferHandlers} from '@bpinc/ad-interaction-state-hooks'
import {useBoolean} from '@bpinc/lib-helpers'
import {useInteractionId} from '@bpinc/lib-interaction-context'
import {
    IconCompleteTransfer,
    IconCompleteTransferFilled,
    IconConference,
    IconConferenceCall24,
    IconPhone,
    IconPhone24Filled,
    SplitButtonDropdown,
} from '@bpinc/lib-jskit-ui'
import {useCloseInteractionSlidingModal} from '@bpinc/lib-sliding-modal-context'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {dialNumberAtom, selectedActionAtom} from '../../atoms'


export const ActionButton: FC = () => {
    const open = useBoolean()
    const t = useLocalizationState('t')
    const [action, setAction] = useAtom(selectedActionAtom)
    const dialNumber = useAtomValue(dialNumberAtom)
    const itemId = useInteractionId()
    const closeModal = useCloseInteractionSlidingModal()
    // eslint-disable-next-line react-perf/jsx-no-new-array-as-prop
    const options = [
        {
            value: 'consult-call',
            text: t.VOICE_CALL.dial_consult_button_tooltip(),
            selectedIcon: <IconPhone24Filled className="svg-color-current" />,
            optionIcon: <IconPhone className="size-4" />,
        },
        {
            value: 'transfer',
            text: t.VOICE_CALL.blind_transfer_button_tooltip(),
            selectedIcon: (
                <IconCompleteTransferFilled className="svg-color-current" />
            ),
            optionIcon: <IconCompleteTransfer className="size-4" />,
        },
        {
            value: 'conference',
            text: t.VOICE_CALL.dial_conference_button_tooltip(),
            selectedIcon: (
                <IconConferenceCall24 className="svg-color-current" />
            ),
            optionIcon: <IconConference className="size-4" />,
        },
    ] as const

    const selectedOption = options.find(o => o.value === action)
    const transferHandlers = useTransferHandlers()

    return (
        <SplitButtonDropdown
            actionButton={{
                onClick: () => {
                    switch (action) {
                        case 'consult-call':
                            transferHandlers.consultCallHandler(dialNumber)
                            break
                        case 'transfer':
                            transferHandlers.blindTransferHandler(dialNumber)
                            break
                        case 'conference':
                            transferHandlers.singleStepConferenceHandler(
                                dialNumber,
                            )
                            break
                    }
                    closeModal(itemId, 'transfer')
                },
            }}
            open={open.value}
            onOpenChange={open.toggle}
            className="h-10"
            options={options.map(o => (
                <button
                    className="flex gap-2"
                    key={o.value}
                    onClick={() => setAction(o.value)}
                >
                    {o.optionIcon}
                    <span>{o.text}</span>
                </button>
            ))}
        >
            <span className="flex items-center gap-2">
                {selectedOption?.selectedIcon}
                <span>{selectedOption?.text}</span>
            </span>
        </SplitButtonDropdown>
    )
}
