import type {FC} from 'react'
import React from 'react'
import {useAtom} from 'jotai'

import {InputField} from '@bpinc/lib-jskit-ui'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {dialNumberAtom} from '../../atoms'
import {ActionButton} from './ActionButton'


export const TransferModalFooter: FC = () => {
    const [dialNumber, setDialNumber] = useAtom(dialNumberAtom)
    const t = useLocalizationState('t')

    return (
        <>
            <InputField
                layoutClassName="flex-auto"
                value={dialNumber}
                onChange={e => {
                    setDialNumber(e.target.value)
                }}
                fieldClassName="h-10"
                label={t.CONTACT.channel_favorites_phone_icon_tooltip()}
            />
            <ActionButton />
        </>
    )
}
