import type {FC} from 'react'
import React, {useCallback} from 'react'

import {CHAT_VOICE_RECORDING_PLAYER_GROUP} from '@bpinc/ad-constants'
import {IconInteractionCall, IconMissedCall16} from '@bpinc/lib-jskit-ui'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {AudioPlayer, Sentiment} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {useContentVoiceTranscript} from './hooks/useContentVoiceTranscript'
import type {CallMessageProps} from './CallMessageContent'
import {TranscriptDropdown} from './TranscriptDropdown'


type Props = CallMessageProps & {
    isOpenModal?: boolean
    hasVoiceTranscript?: boolean
    title: string
    createdTime?: string
    ariaLabel?: string
    recordingLink: string
    containerClassName?: string
    audioPlayerClassName?: string
}

export const CallMessageRecording: FC<Props> = React.memo(props => {
    const {
        isOpenModal,
        hasVoiceTranscript,
        isMissedCall,
        duration,
        title,
        recordingLink,
        sentiment,
        createdTime,
        ariaLabel,
        containerClassName,
        audioPlayerClassName,
    } = props
    const {t} = useLocalizationState()
    const contentVoiceTranscript = useContentVoiceTranscript(props)

    const renderTranscriptDropdown = useCallback(() => {
        return (
            <>
                {hasVoiceTranscript && <TranscriptDropdown isOpenModal={isOpenModal} contentVoiceTranscript={contentVoiceTranscript} /> }
            </>
        )
    }, [contentVoiceTranscript, hasVoiceTranscript, isOpenModal])

    return (
        <div className={tcn('flex flex-col gap-1', containerClassName)} data-testid="call-content" data-missed={isMissedCall}>
            <div className="flex items-center justify-between gap-2" aria-label={ariaLabel}>
                <div className="flex items-center gap-2">
                    <div className="flex items-center gap-1">
                        {isMissedCall
                            ? <IconMissedCall16 className="size-4" aria-hidden />
                            : <IconInteractionCall className="size-4 icon-default" aria-hidden />
                        }
                        <span
                            className="caption-s text-primary-on-primary"
                            aria-hidden
                            data-testid="call-title"
                        >
                            {title}
                        </span>
                    </div>
                    {!!duration && (
                        <span>
                            <span className="aria-description">{t.ACTIVITY_HISTORY.call_message_duration_aria()}</span>
                            <div className="flex items-center gap-2 caption-s text-neutral-fade">
                                <span aria-hidden className="flex size-0.5 items-center rounded-full bg-neutral-500" />
                                <span data-testid="call-duration">{duration}</span>
                            </div>
                        </span>
                    )}
                </div>
                <div className="flex items-center gap-1">
                    {sentiment && <Sentiment sentiment={sentiment} className="mr-1 w-5" />}
                    <span>
                        <span className="aria-description">
                            {t.ACTIVITY_HISTORY.call_message_created_time_aria()}
                        </span>
                        <span data-testid="call-start-time" className="caption-s text-neutral-fade">{createdTime}</span>
                    </span>
                </div>
            </div>
            <div className="flex select-none flex-nowrap">
                <AudioPlayer
                    isNewAudioPlayerUI
                    src={recordingLink}
                    skipUnmount
                    playerGroupTag={CHAT_VOICE_RECORDING_PLAYER_GROUP}
                    pauseGroupWhenPlaying
                    renderOptionalNode={renderTranscriptDropdown}
                    className={audioPlayerClassName}
                />
            </div>
        </div>
    )
})
