import {useEffect, useRef} from 'react'

import {useDirectoryStateMethods} from '@bpinc/ad-directory-state-context'

import type {OpenFolders} from './helpers'
import {directoryFolderTypes} from './helpers'
import type {DirectoryListItem} from './useDirectoryFoldersList'


type UseTeamMembersLoaderParams = {
    openFolders: OpenFolders
    foldersList: DirectoryListItem[]
}

export function useTeamMembersLoader({openFolders, foldersList}: UseTeamMembersLoaderParams) {
    const directoryStateMethods = useDirectoryStateMethods()

    const isInitialLoadRef = useRef(true)

    useEffect(() => {
        const addedItems = foldersList.filter(({item}) => openFolders.diff.added.indexOf(item.id) !== -1)
        const addedTeamsToLoad = addedItems.filter(({type}) => {
            return type === directoryFolderTypes.TEAM || type === directoryFolderTypes.SEARCH_TEAM
        })

        // load users of new opened emi teams/categories
        const addedEmiCategoryIds = addedItems.reduce((ids, {item, type}) => {
            if (type === directoryFolderTypes.EMI_CATEGORY) {
                ids.push(item.id)
            }
            return ids
        }, [] as string[])
        addedEmiCategoryIds.forEach(emiCategoryId => {
            directoryStateMethods.getCategoryUsers(emiCategoryId)
        })

        // load users of all opened teams
        if (isInitialLoadRef.current || addedTeamsToLoad.length) {
            const openedItems = foldersList.filter(({item}) => openFolders.list.indexOf(item.id) !== -1)
            const teamsToLoad = openedItems
                .filter(({type}) => type === directoryFolderTypes.TEAM || type === directoryFolderTypes.SEARCH_TEAM)
                .map(item => item.item.id)

            if (teamsToLoad.length) {
                directoryStateMethods.getLoggedUsers(teamsToLoad)
                isInitialLoadRef.current = false
            }
        }
        // eslint-disable-next-line
    }, [isInitialLoadRef, directoryStateMethods, openFolders, foldersList.length])
}
