import React from 'react'

import {useInteractionStateMethods} from '@bpinc/ad-interaction-state-context'
import {getMediaIcon, isPotentialInteraction} from '@bpinc/ad-interaction-state-methods'
import {InteractionMediaIcon, InteractionTimer} from '@bpinc/lib-ui-elements'

import {useActiveOrLastInteraction} from '../hooks/useActiveOrLastInteraction'
import {useInteractionBarDisplayName} from '../hooks/useInteractionBarText'
import {InteractionBarUnread} from './InteractionBarUnread'
import {InteractionsCount} from './InteractionsCount'

import styles from '../styles/InteractionBar.module.less'


type InteractionBarProps = {
    action: () => void
}

export const InteractionBar: React.FC<InteractionBarProps> = ({action}) => {
    const activeItem = useActiveOrLastInteraction()
    const {activateItem} = useInteractionStateMethods()
    const displayName = useInteractionBarDisplayName(activeItem)

    if (!activeItem) {
        return null
    }

    const {
        displayData: {deliveredStartTime},
    } = activeItem

    const iconType = getMediaIcon(activeItem)

    const handleClick = () => {
        activateItem(activeItem.id)
        action()
    }

    return (
        <>
            <button onClick={handleClick} className={styles.bar} data-testid="interaction-bar">
                <span key={activeItem.id} className={styles.desc}>
                    <InteractionMediaIcon showPotentialInteractionIcon icon={iconType} className="mr-1.5" />
                    <span data-testid="display-name">{displayName}</span>
                    {!isPotentialInteraction(activeItem) && deliveredStartTime && (
                        <InteractionTimer startTime={deliveredStartTime} data-testid="duration-timer" />
                    )}
                </span>
                <InteractionsCount />
                <InteractionBarUnread />
            </button>
        </>
    )
}
