import type {ReactNode} from 'react'
import React from 'react'

import {tcn} from '@bpinc/lib-tailwind-classnames'


interface Props {
    title: React.ReactNode
    size?: 'small'
    children: ReactNode
}

export const Info: React.FC<Props> = ({title, size, children}) => {
    return (
        <div className={tcn('flex py-1 align-top text-[15px]', {'text-[13px] text-main': size === 'small'})}>
            <div className="inline-block whitespace-nowrap pr-[0.4em] text-main-text-inverted">
                {`${title}:`}
            </div>
            <div className="inline-block break-words align-top">
                {children}
            </div>
        </div>
    )
}
