import React from 'react'


interface AriaDescriptionProps {
    displayName?: string
    contact?: string
    serviceName?: string
    type: 'chat' | 'workitem'
}

export const AriaDescription: React.FC<AriaDescriptionProps> = ({displayName, serviceName, type}) => {
    return (
        <>
            {displayName && (
                <div aria-live="assertive" role="alert" className="aria-description">
                    {type === 'chat' ? 'Chat from ' : serviceName}
                    {' '}
                    {displayName}
                </div>
            )}
        </>
    )
}
