import React, {useMemo} from 'react'

import {useContactsState} from '@bpinc/ad-contacts-state-context'
import type {EmiItemStatus} from '@bpinc/ad-emi-helpers'
import {formatDate} from '@bpinc/ad-formatting-helpers'
import type {HandleCallParams} from '@bpinc/ad-interaction-state-hooks'
import {
    isRecentCall,
    isRecentCallConference,
    isRecentChat,
    isRecentEmail,
    isRecentExtChatConference,
    isRecentInternalChat,
} from '@bpinc/ad-interaction-state-methods'
import {useAuthorizedSession} from '@bpinc/ad-session-context'
import type {EmiUserType} from '@bpinc/ad-transport-types'
import {useSubscribeForUserDetails} from '@bpinc/ad-user-details-state-context'
import {ContactListItem} from '@bpinc/lib-contact-list-item-ui'
import {useBoolean, useEvent} from '@bpinc/lib-helpers'
import {type ChatParams, SelectionItem} from '@bpinc/lib-ui-elements'
import type {RecentItem} from '@bpinc/state-recent-types'

import type {OnEmailParams} from '../../directory/components/user/types'
import {isClickActionAvailable} from '../hooks/isClickActionAvailable'
import {CRMObjectsList} from './CRMObjectsList'
import {RecentItemAvatar} from './RecentItemAvatar'
import {RecentItemBody} from './RecentItemBody'
import {RecentItemControls} from './RecentItemControls'
import {RecentItemTitle} from './RecentItemTitle'
import {RecentItemUnreadIndicator} from './RecentItemUnreadIndicator'


export type RecentListItemProps = {
    transferMode?: 'messages' | 'calls'
    transferControlsStyle?: 'add' | 'replace'
    repeatCount?: number
    isSelected: boolean
    isHighlighted?: boolean
    recentItem: RecentItem

    onClick(item: RecentItem, e: React.MouseEvent): void
    onDoubleClick(item: RecentItem): void
    onCallClick(callParams: HandleCallParams): void
    onChatClick(chatParams: ChatParams): void
    onEmailClick(emailParams: OnEmailParams): void
    onDetailsClick(item: RecentItem): void
    useEmiItemStatus(emiUserType?: EmiUserType, ownerId?: string): EmiItemStatus
}


export const RecentListItem: React.FC<RecentListItemProps> = React.memo(function RecentListItem(props) {
    const {
        isHighlighted,
        transferMode,
        transferControlsStyle,
        onClick,
        onDoubleClick,
        onCallClick,
        onChatClick,
        onEmailClick,
        onDetailsClick,
        useEmiItemStatus,
        repeatCount,
        isSelected,
        recentItem,
    } = props

    const contacts = useContactsState('contacts')
    const {userId: selfUserId} = useAuthorizedSession()

    const item = useMemo(() => {
        if (isRecentEmail(recentItem)) {
            const {contactId} = recentItem
            const number = contactId && contacts[contactId]?.data?.primaryPhone?.phone
            if (number) {
                return {...recentItem, number}
            }
        }
        if (isRecentInternalChat(recentItem)) {
            const chatMembers = recentItem.groupChatData?.members
            if (chatMembers?.length === 2) {
                const secondParty = chatMembers.find(member => member.id !== selfUserId)
                if (secondParty) {
                    return {
                        ...recentItem,
                        firstName: secondParty.firstName,
                        lastName: secondParty.lastName,
                        userId: secondParty.id,
                    }
                }
            }
        }
        return recentItem
    }, [contacts, recentItem, selfUserId])


    const isMissed = item.direction === 'INBOUND' && item.duration === '0'
    const isIncoming = item.direction === 'INBOUND'
    const isOutgoing = item.direction === 'OUTBOUND'

    const clickActionAvailable = isClickActionAvailable(item)

    useSubscribeForUserDetails(item.userId)

    const {value: crmObjectsListOpened, toggle: toggleShowCrmObjectsList} = useBoolean()

    const avatar = (
        <RecentItemAvatar
            item={item}
            onCallClick={onCallClick}
            onChatClick={onChatClick}
            onEmailClick={onEmailClick}
        />
    )

    const title = <RecentItemTitle item={item} repeatCount={repeatCount} />
    const body = <RecentItemBody item={item} />

    const datetime = (
        <span data-testid="date" className="caption-s text-neutral-on-secondary">
            {formatDate(item.time)}
        </span>
    )

    const unreadIndicator = <RecentItemUnreadIndicator item={item} />

    const controls = (
        <RecentItemControls
            item={item}
            transferMode={transferMode}
            crmObjectsListOpened={crmObjectsListOpened}
            onCall={onCallClick}
            onChat={onChatClick}
            onEmail={onEmailClick}
            onDetails={onDetailsClick}
            toggleShowCrmObjectsList={toggleShowCrmObjectsList}
        />
    )

    const bottomContent = crmObjectsListOpened && item.associatedCRMObjects?.length
        ? <CRMObjectsList crmObjects={item.associatedCRMObjects} />
        : null

    const {active, disableReasonMessage} = useEmiItemStatus(item.userType, item.ownerId)

    const handleClick = useEvent((e: React.MouseEvent) => {
        onClick(item, e)
    })

    const handleDoubleClick = useEvent(() => {
        onDoubleClick(item)
    })

    return (
        <>
            <ContactListItem
                as={SelectionItem}
                containerProps={{
                    focusOnSelection: true,
                    touchedOnly: true,
                    itemId: item.itemId,
                    title: disableReasonMessage,
                    'data-testid': 'recent-item',
                    'data-conference': isRecentCallConference(item) || isRecentExtChatConference(item) || undefined,
                    'data-chat': isRecentChat(item) || undefined,
                    'data-call': isRecentCall(item) || undefined,
                    'data-missed': isMissed || undefined,
                    'data-incoming': isIncoming || undefined,
                    'data-outgoing': isOutgoing || undefined,
                    'data-email': isRecentEmail(item) || undefined,
                    'data-disabled': !active || undefined,
                    'data-selected': isSelected,
                }}
                isSelected={isHighlighted || isSelected}
                showControls={isSelected}
                isConference={isRecentCallConference(item) || isRecentExtChatConference(item)}
                transferMode={transferMode}
                transferControlsStyle={transferControlsStyle}
                userId={item.userId}
                contactId={item.contactId}
                number={item.number}
                avatar={avatar}
                title={title}
                titleRight={datetime}
                body={body}
                bodyRight={unreadIndicator}
                controls={controls}
                bottomContent={bottomContent}
                onClick={clickActionAvailable ? handleClick : undefined}
                onDoubleClick={handleDoubleClick}
                userType={item.userType}
                disabled={!active}
            />
        </>
    )
})
