import type {ReactElement} from 'react'
import React, {useMemo} from 'react'

import type {DispositionsAccordionProps} from '../components/Dispositions/DispositionsAccordion'
import {DispositionsListItem} from '../components/Dispositions/DispositionsListItem'


type Panel = {
    title: string
    value: string
    content: Array<{
        element: ReactElement
        text: string
    }>
}

export function useAccordionPanels({
    list,
    searchValue,
    ...listItemProps
}: DispositionsAccordionProps) {
    return useMemo(
        function memoizeListForAccordion() {
            let panel: Panel | undefined

            return list.reduce<Panel[]>((acc, disposition, index, array) => {
                if (disposition.isGroupLabel) {
                    if (panel?.content.length) {
                        addPanelToPanels()
                    }
                    panel = createPanel()
                } else {
                    extendPanelContent()
                    if (isLastElement()) {
                        addPanelToPanels()
                    }
                }

                return acc

                function isLastElement() {
                    return index === array.length - 1
                }
                function extendPanelContent() {
                    panel?.content.push({
                        text: disposition.label,
                        element: (
                            <DispositionsListItem
                                {...disposition}
                                {...listItemProps}
                                highlight={searchValue}
                                key={disposition.value}
                            />
                        ),
                    })
                }
                function addPanelToPanels() {
                    return panel && acc.push(panel)
                }
                function createPanel() {
                    return {
                        value: disposition.value,
                        title: disposition.label,
                        content: [],
                    }
                }
            }, [])
        },
        [searchValue, list, listItemProps],
    )
}
