import React from 'react'
import {useAtomValue} from 'jotai'

import {useActiveInteractionId} from '@bpinc/ad-interaction-state-context'
import {tcn} from '@bpinc/lib-tailwind-classnames'

import {ServiceSelect} from '../../components/ServiceSelect'
import {isPadOpenAtom} from '../atoms'
import {useSearchQueryFromPageParams} from '../hooks/useSearchQueryFromPageParams'
import {DialpadButtons} from './DialpadButtons'
import {DialpadContacts} from './DialpadContacts'
import {DialpadForm} from './DialpadForm'
import {useAccessibleShortcuts} from './useAccessibleShortcuts'


export const Dialpad: React.FC = () => {
    const activeInteractionId = useActiveInteractionId()

    const isPadOpen = useAtomValue(isPadOpenAtom)

    useSearchQueryFromPageParams()
    useAccessibleShortcuts()

    return (
        <div
            className="relative flex flex-1 flex-col overflow-hidden"
            data-testid="dialpad"
        >

            <div className="px-4 pt-2">
                <ServiceSelect itemId={activeInteractionId} />
            </div>

            <div
                className={tcn('flex flex-1 flex-col overflow-y-hidden pb-[50px] pl-4 pr-0.5 pt-[7px]', {
                    'pb-[350px]': isPadOpen,
                })}
            >
                <DialpadForm />
                <DialpadContacts />
            </div>

            <DialpadButtons />
        </div>
    )
}
