import {useMemo} from 'react'

import type {ContactType} from '@bpinc/ad-contacts-state-types'
import {useFormatting} from '@bpinc/ad-formatting-context'
import {formatPhone} from '@bpinc/ad-formatting-helpers'
import type {DropdownItemProps} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'


interface UseContactPhoneOptionsParams {
    contactData?: ContactType
}

export const OTHER_PHONE_OPTION_VALUE = 'OTHER'


export function useContactPhoneOptions({
    contactData,
}: UseContactPhoneOptionsParams): DropdownItemProps[] {
    const {t} = useLocalizationState()

    const {formatPhoneType} = useFormatting()
    const contactPhones = contactData?.phones?.map(number => {
        return {
            value: number.id,
            text: `${formatPhone(number.phone)} (${formatPhoneType(number.type)})`,
        }
    })

    return useMemo(
        function memoizePhoneOptions() {
            return [
                ...(contactPhones ?? []),
                {
                    value: OTHER_PHONE_OPTION_VALUE,
                    text: t.CALL_DETAILS_FORM.rcp_option_other(),
                },
            ]
        },
        [contactPhones, t.CALL_DETAILS_FORM],
    )
}
