import React from 'react'

import {useContact} from '@bpinc/ad-contacts-state-context'
import {useFormatting} from '@bpinc/ad-formatting-context'
import {useActiveInteraction} from '@bpinc/ad-interaction-state-context'
import {IconSendInvitation, Link} from '@bpinc/lib-jskit-ui'
import {useLocalizationState} from '@bpinc/state-localization-context'


interface PageLinkMessageProps {
    link: string
    partyId?: string
    title?: string
}

export const PageLinkMessage: React.FC<PageLinkMessageProps> = ({
    link,
    title,
    partyId,
}) => {
    const {t} = useLocalizationState()
    const item = useActiveInteraction()
    const partyData = item?.chatParties?.find(party => party.party_id === partyId)
    const contactId = partyData?.contact_id
    const contactData = useContact(contactId)?.data
    const {formatFullName} = useFormatting()
    const displayNameContact = formatFullName(contactData?.firstName, contactData?.lastName)
    const partyName = formatFullName(partyData?.first_name, partyData?.last_name)
    const externalChatName = formatFullName(item?.externalChatData?.firstName, item?.externalChatData?.lastName)
    const displayNameText = displayNameContact || partyName || externalChatName || !!contactData?.phones?.length && contactData?.phones?.[0]?.phone
        || !!contactData?.emails?.length && contactData?.emails[0]?.emailAddress || t.EXTERNAL_CHAT.display_name_placeholder()

    return (
        <div className="flex items-center justify-center bg-transparent py-4">
            <div className="flex flex-wrap items-center justify-center">
                <IconSendInvitation className="mr-2 size-4 icon-default" />
                {displayNameText && (<span className="mr-1 font-semibold caption-s text-neutral-on-secondary">{displayNameText}</span>)}
                <span className="caption-s text-neutral-fade">
                    {t.ACTIVITY_HISTORY.navigate_to()}
                </span>
                <Link
                    href={link}
                    title={link}
                    data-testid="link"
                    className="!caption-s"
                >
                    {title || t.EXTERNAL_CHAT.external_chat_customer_navigates_to()}
                </Link>
            </div>
        </div>
    )
}
