import React, {useRef} from 'react'

import {InputField} from '@bpinc/lib-jskit-ui'

import {KeyPad} from '../../../dialpad'
import {useDtmfInputStateAndHandlers} from './useDtmfInputStateAndHandlers'


export const KeypadModalContent = () => {
    const inputRef = useRef<HTMLInputElement>(null)
    const {
        inputValue,
        handleButtonClick,
        moveCaretToTheEnd,
        preventToMoveLeft,
        handleInputChange,
    } = useDtmfInputStateAndHandlers(inputRef)

    return (
        <div className="flex flex-auto flex-col">
            <InputField
                ref={inputRef}
                autoFocus
                onMouseDown={moveCaretToTheEnd}
                onFocus={moveCaretToTheEnd}
                onKeyDown={preventToMoveLeft}
                onSelect={moveCaretToTheEnd}
                value={inputValue}
                onChange={handleInputChange}
            />
            <div className="mt-12 flex flex-auto items-center justify-center">
                <KeyPad
                    handleButtonClick={handleButtonClick}
                    allowLetterBasedDialing={false}
                />
            </div>
        </div>
    )
}
