import type {Dispatch, SetStateAction} from 'react'
import {useEffect, useState} from 'react'

import {useLocalSettings} from '@bpinc/ad-local-settings-context'

import type {OpenFolders} from './helpers'
import {getActiveGroupStorageName} from './helpers'


type UseOpenedTeamsRetrieverFromLocalStorageParams = {
    isSearchMode: boolean
    setOpenFolders: Dispatch<SetStateAction<OpenFolders>>
}

export function useOpenedTeamsRetrieverFromLocalStorage(
    {isSearchMode, setOpenFolders}: UseOpenedTeamsRetrieverFromLocalStorageParams,
) {
    const localSettings = useLocalSettings()
    const [retrievedFromLocalStorage, setRetrievedFromLocalStorage] = useState(false)

    useEffect(() => {

        setRetrievedFromLocalStorage(false)

        localSettings.get(getActiveGroupStorageName({isSearchMode})).then(list => {
            if (typeof list !== 'undefined') {
                setOpenFolders({list, diff: {added: [], removed: []}})
            }
            setRetrievedFromLocalStorage(true)
        })
    }, [isSearchMode, localSettings, setOpenFolders])

    return retrievedFromLocalStorage
}
