import React, {useCallback, useMemo} from 'react'
import {TabsList, TabsRoot, TabsTrigger} from '@servicepattern/ui'

import {useBrowserTabMetaInfo} from '@bpinc/ad-current-tab-data-context'
import {useInteractionStateSelector} from '@bpinc/ad-interaction-state-context'
import {useSessionState} from '@bpinc/ad-session-context'
import {AppRoutes, useNavigationState} from '@bpinc/lib-communicator-navigation-state'
import {
    IconCalendar,
    IconDialpad,
    IconDirectory,
    IconInteractionDetails,
    IconRecent,
    IconStar24,
} from '@bpinc/lib-jskit-ui'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {MissedCallsBadge, UnreadBadge} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {useUnreadAmount} from '../hooks/useUnreadAmount'


const recentBadgeStyles = tcn('absolute right-0 top-0 !h-4 !w-4 translate-x-1/2')

export const MainPageNav: React.FC = React.memo(function MainPageNav() {
    const {t} = useLocalizationState()
    const privileges = useSessionState('privileges')
    const tabMetaInfo = useBrowserTabMetaInfo()

    const {setActivePage, noVisibleInteractionsOnMainPannel} = useNavigationState()

    const activePage = useNavigationState('activePage')

    const unreadAmount = useUnreadAmount()
    const missedCallsAmount = useInteractionStateSelector(s => s.missedCallsAmount)

    const navTabs = useMemo(() => {
        const listInner = [
            {
                id: AppRoutes.Interactions,
                testId: 'nav-active-interaction',
                title: t.ADDRESS_BOOK_AREA.active_interaction() as string,
                className: tcn(`flex w-12 justify-center`),
                disabled: noVisibleInteractionsOnMainPannel,
                content: (
                    <IconInteractionDetails
                        className={getInteractionTabSVGStyles()}
                    />
                ),
            },
            {
                id: AppRoutes.Directory,
                testId: 'nav-directory',
                title: t.ADDRESS_BOOK_AREA.addressBook_directory() as string,
                className: tcn(`flex w-12 justify-center`),
                content: <IconDirectory className={getSVGStylesIfActive(AppRoutes.Directory)} />,
            },
        ]

        listInner.push({
            id: AppRoutes.Dial,
            testId: 'nav-dial',
            title: t.ADDRESS_BOOK_AREA.addressBook_dial(),
            className: tcn(`flex w-12 justify-center`),
            content: <IconDialpad className={getSVGStylesIfActive(AppRoutes.Dial)} />,
        })

        if (privileges.SYS_USE_FAVORITES_TAB) {
            listInner.push({
                id: AppRoutes.Favorites,
                testId: 'nav-favorites',
                title: t.ADDRESS_BOOK_AREA.addressBook_favorites(),
                className: tcn(`flex w-12 justify-center`),
                content: <IconStar24 className={getSVGStylesIfActive(AppRoutes.Favorites)} />,
            })
        }

        if (privileges.SYS_USE_RECENT_TAB) {
            listInner.push({
                id: AppRoutes.Recents,
                testId: 'nav-recents',
                title: t.ADDRESS_BOOK_AREA.addressBook_recents(),
                className: tcn(`flex w-12 justify-center`),
                content: (
                    <>
                        <IconRecent className={getSVGStylesIfActive(AppRoutes.Recents)} />
                        <UnreadBadge className={recentBadgeStyles} unreadAmount={unreadAmount} />
                        {!unreadAmount && <MissedCallsBadge className={recentBadgeStyles} amount={missedCallsAmount} />}
                    </>
                ),
            })
        }


        if (tabMetaInfo?.app === 'adc' && privileges.CAN_USE_CALENDAR) {
            listInner.push({
                id: AppRoutes.Calendar,
                testId: 'nav-calendar',
                title: t.ADDRESS_BOOK_AREA.addressBook_calendar(),
                className: tcn(`flex w-12 justify-center`),
                content: <IconCalendar className={getSVGStylesIfActive(AppRoutes.Calendar)} />,
            })
        }

        return listInner

        function getSVGStylesIfActive(page: AppRoutes) {
            return page === activePage.name ? 'svg-color-primary' : 'svg-color-neutral-600'
        }

        function getInteractionTabSVGStyles() {
            const isInteractionTabActive = AppRoutes.Interactions === activePage.name
            return tcn({
                'svg-color-primary': isInteractionTabActive,
                'svg-color-neutral-600': !isInteractionTabActive && !noVisibleInteractionsOnMainPannel,
                'svg-color-neutral-400': noVisibleInteractionsOnMainPannel,
            })
        }
    }, [
        t.ADDRESS_BOOK_AREA,
        privileges.SYS_USE_FAVORITES_TAB,
        privileges.SYS_USE_RECENT_TAB,
        privileges.CAN_USE_CALENDAR,
        tabMetaInfo?.app,
        missedCallsAmount,
        unreadAmount,
        activePage.name,
        noVisibleInteractionsOnMainPannel,
    ])

    const onTabChange = useCallback((newTabID: string) => {
        const newPage = newTabID as AppRoutes
        setActivePage({name: newPage})
    }, [setActivePage])

    return (
        <TabsRoot>
            <TabsList
                className="justify-center gap-6"
                data-testid="bottom-nav"
                topBorder
            >
                {navTabs.map(tab => (
                    <TabsTrigger
                        key={tab.id}
                        value={tab.id}
                        onClick={() => onTabChange(tab.id)}
                        isSelected={tab.id === activePage.name}
                        className={tab.className}
                        data-testid={tab.testId}
                        topBorder
                        disabled={tab.disabled}
                        triggerClassName="flex items-center justify-center p-3"
                    >
                        {tab.content}
                    </TabsTrigger>
                ))}
            </TabsList>
        </TabsRoot>
    )
})
