import React, {useMemo} from 'react'
import cn from 'classnames'
import {pickBy} from 'lodash-es'

import {useCaseState} from '@bpinc/ad-case-state-context'
import type {ActivityChat, ChatTranscriptMessage as Message, Party} from '@bpinc/ad-case-state-types'
import {useGetAvatarUrl} from '@bpinc/ad-contacts-state-hooks'
import {useFormatting} from '@bpinc/ad-formatting-context'
import {formatDate} from '@bpinc/ad-formatting-helpers'
import {useAssociatedChatInteractionProp} from '@bpinc/ad-interaction-state-context'
import {getInteractionStepId} from '@bpinc/ad-interaction-state-methods'
import {ContactListItem} from '@bpinc/lib-contact-list-item-ui'
import {SmIcon} from '@bpinc/lib-external-ui-elements'
import {useInteractionId} from '@bpinc/lib-interaction-context'

import {getNameFromParty} from '../utils'
import {ChatTranscriptMessage} from './ChatTranscriptMessage'
import {Disposition} from './Disposition'
import {Info} from './Info'


interface Props {
    activity: ActivityChat
}

const fulfillMessages = (messages: Message[] = [], parties: Party[]): Message[] => {
    const messagesByPartyId: {[id: string]: Message} = {}
    const isDefined = (v: unknown) => typeof v !== 'undefined'

    messages = messages
        .filter(
            ({event}) => event && !['party_changed', 'party_navigation', 'party_message_attributes'].includes(event),
        )
        .map(m => {
            const id = m.partyId
            const message = pickBy(m, isDefined) as Message
            const party = parties.find(currParty => currParty.partyId === message.partyId)

            message.firstName = party?.firstName || message.firstName
            message.lastName = party?.lastName || message.lastName

            messagesByPartyId[id] = {
                ...messagesByPartyId[id],
                ...message,
            }

            return message
        })

    return messages.map(m => ({...messagesByPartyId[m.partyId], ...m}))
}

export const ChatPreview: React.FC<Props> = ({activity}) => {
    const {formatFullName} = useFormatting()
    const getAvatarUrl = useGetAvatarUrl()
    const chats = useCaseState('chats')
    const {direction, createdTime, parties, services, globalInteractionId} = activity
    const isOutbound = direction === 'OUTBOUND'

    const partyExternal = parties.find(p => p.partyType !== 'USER')
    const partyInternal = parties.find(p => p.partyType === 'USER')
    const [fromFirstName, fromLastName] = getNameFromParty(partyExternal)
    const [handledByFirstName, handledByLastName] = getNameFromParty(partyInternal)
    const contactId = isOutbound ? undefined : partyExternal?.contactId
    const userId = isOutbound ? partyInternal?.userId : undefined

    const {data = null} = chats[globalInteractionId] || {}

    const avatar = useMemo(() => {
        return {
            firstName: fromFirstName,
            lastName: fromLastName,
            url: isOutbound && partyInternal ? getAvatarUrl({userId: partyInternal.userId}) : undefined,
            isAgent: isOutbound,
        }
    }, [fromFirstName, fromLastName, getAvatarUrl, isOutbound, partyInternal])

    const title = formatFullName(fromFirstName, fromLastName)
    const interactionId = useInteractionId()
    const interactionStepId = useAssociatedChatInteractionProp(interactionId, getInteractionStepId)
    const noteContentClassName = cn('relative inline-block bg-[--c-note-content-bg] py-2 pl-2 pr-6',
        'after:absolute after:bottom-0 after:right-0 after:h-0 after:w-0 after:border-[8px]',
        'after:border-b-white after:border-l-[--c-note-border] after:border-r-white after:border-t-[--c-note-border]')
    const body = (
        <span className="relative -top-1 text-[0.8em]">
            <SmIcon name="chat" />
            {formatDate(createdTime)}
        </span>
    )

    const messages = useMemo(() => {
        return data ? fulfillMessages(data.messages, parties) : []
    }, [data, parties])

    if (!data) return null

    return (
        <div>
            <ContactListItem
                as="div"
                contactId={contactId}
                userId={userId}
                avatarProps={avatar}
                title={title}
                body={body}
                isAlignedToRight={isOutbound}
                isFitted
            />
            <div className="my-2 rounded bg-note-bg p-2">
                <Info title="Handled by" size="small">
                    {formatFullName(handledByFirstName, handledByLastName)}
                </Info>
                {messages.map((message, i) => (
                    <ChatTranscriptMessage key={i} {...message} />
                ))}
            </div>
            {partyInternal && partyInternal.notes && (
                <div className={cn(noteContentClassName, 'mb-2')}>{partyInternal.notes}</div>
            )}
            {partyInternal && partyInternal.disposition && (
                <Disposition data={partyInternal} type="chat" services={services} />
            )}
        </div>
    )
}
