import type {MouseEvent} from 'react'
import React, {useCallback} from 'react'

import {AppRoutes, useNavigationState} from '@bpinc/lib-communicator-navigation-state'
import {SmIcon} from '@bpinc/lib-external-ui-elements'


type EmailMessageProps = {
    caseId: string
    caseNumber: string
    caseTitle: string
}

export const EmailMessage: React.FC<EmailMessageProps> = ({caseId, caseNumber, caseTitle}) => {
    const setActivePage = useNavigationState('setActivePage')
    const handleClick = useCallback(function handleClickCallback(evt: MouseEvent) {
        evt.preventDefault()

        setActivePage({
            name: AppRoutes.Cases,
            params: {
                id: caseId,
            },
        })
    }, [setActivePage, caseId])

    return (
        <div>
            <div>
                <SmIcon name="envelope" color="grey" />
            </div>
            <div>
                <a href="#" onClick={handleClick}>
                    {`#${caseNumber}`}
                </a>
                {' '}
                <span>
                    {caseTitle}
                </span>
            </div>
        </div>
    )
}
