import {useEffect} from 'react'

import {useSessionState} from '@bpinc/ad-session-context'
import {useEvent, usePreviousValueRef} from '@bpinc/lib-helpers'
import {Notifications, useNotificationsState} from '@bpinc/lib-notifications-context'
import {useLocalizationState} from '@bpinc/state-localization-context'


export function useSipServerDisconnectedNotification() {
    const {showNotification, hideNotificationById} = useNotificationsState('methods')
    const {WINDOW_POPUP_TITLES_AND_MESSAGES: i18n} = useLocalizationState('t')
    const sipServer = useSessionState('sipServer')
    const prevSipServerRef = usePreviousValueRef(sipServer)

    const updateNotification = useEvent(() => {
        if (sipServer) {
            hideNotificationById(Notifications.SipServerConnectionError)
        } else if (prevSipServerRef.current) {
            // we had SIP server, but not anymore
            showNotification({
                type: 'error',
                id: Notifications.SipServerConnectionError,
                message: i18n.window_messages_3pcc_server_connectionLost(),
            })
        }
    })

    useEffect(() => {
        updateNotification()
        // track sipServer change only
    }, [sipServer, updateNotification])
}
