import React, {useMemo} from 'react'

import type {DirectoryEntry as DirectoryEntryType} from '@bpinc/ad-directory-state-types'
import {isHuntGroupEntry, isStaticEmailEntry, isStaticVoiceEntry} from '@bpinc/ad-directory-state-types'
import type {LegacyAvatarType} from '@bpinc/lib-avatar-ui'
import type {PropsAvatar} from '@bpinc/lib-contact-list-item-ui'
import {ContactListItem} from '@bpinc/lib-contact-list-item-ui'
import {DirectoryFavoriteButton} from '@bpinc/lib-favorites-button'
import {useEvent} from '@bpinc/lib-helpers'
import {assertDefined} from '@bpinc/lib-types-helpers'
import {highlighted, SelectionItem} from '@bpinc/lib-ui-elements'

import {useDirectoryEntryOnlineStatus} from '../../../hooks/useDirectoryEntryOnlineStatus'
import type {InternalCategoryUserProps} from '../types'
import {DirectoryEntryBody} from './DirectoryEntryBody'
import {DirectoryEntryControls} from './DirectoryEntryControls'
import {DirectoryEntryMetrics} from './DirectoryEntryMetrics'


function getAvatarType(item: DirectoryEntryType): LegacyAvatarType {
    if (isHuntGroupEntry(item)) {
        return 'hunt-group'
    } else if (isStaticVoiceEntry(item) || isStaticEmailEntry(item)) {
        return 'static-entry'
    } else {
        return 'scenario-entry'
    }
}

export const DirectoryEntry: React.FC<InternalCategoryUserProps> = ({
    transferMode,
    transferNumberInput,
    transferControlsStyle,
    item,
    onCall,
    onChat,
    onEmail,
    onDetails,
    highlight,
    onClick,
    onDoubleClick,
    showFavoriteButton = true,
    ...other
}) => {

    const onlineStatus = useDirectoryEntryOnlineStatus(item.serviceId, item.huntGroupId)
    const avatarType = getAvatarType(item)

    const avatar = useMemo<PropsAvatar>(() => ({
        fullName: item.title,
        avatarType,
        isAgent: true,
        onlineStatusIcon: onlineStatus,
    }), [avatarType, item.title, onlineStatus])

    const title = useMemo(() => {
        return (
            <span className="flex gap-1">
                <b data-testid="name" className="inline-block truncate font-semibold body text-primary-on-primary">
                    {highlighted({highlight, text: item.title})}
                </b>
                {showFavoriteButton && (
                    <DirectoryFavoriteButton forContactListItem item={item} />
                )}
            </span>
        )
    }, [highlight, showFavoriteButton, item])

    const body = <DirectoryEntryBody item={item} highlight={highlight} />

    const stats = <DirectoryEntryMetrics item={item} />

    const controls = (
        <DirectoryEntryControls
            item={item}
            transferMode={transferMode}
            onCall={onCall}
            onChat={onChat}
            onEmail={onEmail}
            onDetails={onDetails}
        />
    )

    const handleCall = useEvent((e: React.MouseEvent) => {
        e.preventDefault()
        e.stopPropagation()

        assertDefined(item.number)

        onCall?.({
            phone: item.number,
            userId: item.id,
            firstName: item.title,
            origination: 'directory',
        })
    })

    const handleClick = useEvent((evt: React.MouseEvent) => {
        onClick?.(evt, item)
    })
    const handleDoubleClick = useEvent(() => {
        onDoubleClick?.(item)
    })

    return (
        <ContactListItem
            as={SelectionItem}
            keyHandlerElement="directory-item"
            transferMode={transferMode}
            transferNumberInput={transferNumberInput}
            transferControlsStyle={transferControlsStyle}
            userId={item.id}
            number={item.number}
            avatarProps={avatar}
            title={title}
            body={body}
            stats={stats}
            controls={controls}
            onClick={onClick ? handleClick : handleCall}
            onDoubleClick={onDoubleClick && handleDoubleClick}
            isStaticEntry
            containerProps={{
                focusOnSelection: true,
                itemId: item.id,
                tabIndex: 0,
                'data-testid': 'directory-item',
                'data-entry-point': 'true',
            }}
            {...other}
        />
    )
}
