import {atom} from 'jotai'

import type {CaseServiceFilter, CasesSortBy, CaseStatus} from '@bpinc/ad-request-urls'

import type {SearchPageTab} from './utils'
import {DEFAULT_SEARCH_TAB} from './utils'


export const searchQueryAtom = atom<string>('')
export const searchPageTabsAtom = atom<SearchPageTab[]>([DEFAULT_SEARCH_TAB])
export const searchPageActiveTabIdAtom = atom<string>(DEFAULT_SEARCH_TAB.id)

export const selectedContactIdAtom = atom<string | undefined>(undefined)
export const selectedCaseIdAtom = atom<string | undefined>(undefined)
export const selectedCaseTabWidthAtom = atom<number>(600)

export const casesStatusesAtom = atom<CaseStatus[]>([])
export const casesSortingAtom = atom<{
    sortBy: CasesSortBy
    asc: boolean
}>({
    sortBy: 'score',
    asc: false,
})
export const casesServiceAtom = atom<CaseServiceFilter | undefined>(undefined)
export const casesTeamIdAtom = atom<string | undefined>(undefined)

export const mergingContactsAtom = atom<string[]>([])
