import React, {useMemo} from 'react'

import {useFormatting} from '@bpinc/ad-formatting-context'
import {useSessionState} from '@bpinc/ad-session-context'
import {tcn} from '@bpinc/lib-tailwind-classnames'

import {getExternalChatMessageDisplayName} from '../../../../../helpers'
import type {ExternalChatMessage, ExternalChatMessageFileData} from '../../../../../types'
import {isHistoryMessage} from '../../../../../types'
import type {MarkAsReadProps} from '../../ExternalChatMessage'
import {WithOptionalNameAndAvatar} from '../WithOptionalNameAndAvatar'
import {FileMessageContent} from './FileMessageContent'

import styles from './FileMessage.module.less'


type FileMessageProps = {
    message: ExternalChatMessage
    fileData: ExternalChatMessageFileData
    markAsReadData: MarkAsReadProps
}

export const FileMessage: React.FC<FileMessageProps> = ({message, fileData, markAsReadData}) => {
    const {fileError, fileType} = fileData
    const {sessionId, domain, loginId, privileges} = useSessionState()
    const session = useMemo(() => ({id: sessionId, domain, loginId}), [sessionId, domain, loginId])
    const canMask = !!privileges.CAN_MASK_INTERACTION_CONTENT
    const globalInteractionId = isHistoryMessage(message) ? message.globalInteractionId : undefined
    const isAudioFile = fileType === 'audio'
    const {formatFullName} = useFormatting()

    return (
        <WithOptionalNameAndAvatar
            message={message}
            className={tcn(styles.file, {
                [styles.isError]: fileError,
                [styles.voice]: isAudioFile,
            })}
        >
            <FileMessageContent
                timestamp={message.timestamp}
                senderName={getExternalChatMessageDisplayName(message, formatFullName)}
                fileData={fileData}
                markAsReadData={markAsReadData}
                canMask={canMask}
                session={session}
                globalInteractionId={globalInteractionId}
            />
        </WithOptionalNameAndAvatar>
    )
}
