import {useMemo} from 'react'

import {useFormatting} from '@bpinc/ad-formatting-context'
import {formatPhone} from '@bpinc/ad-formatting-helpers'
import type {Item} from '@bpinc/ad-interaction-state-types'


export function useDisplayName(modalItem: Item) {
    const {formatFullName, getNamesFromDisplayName} = useFormatting()

    return useMemo(function memoizeName() {
        const {firstName: callFirstName, lastName: callLastName, number = '', displayName = '', email} = modalItem.displayData || {}
        const contact = email || formatPhone(number)
        const chatFirstName = modalItem?.externalChatData?.firstName
        const chatLastName = modalItem?.externalChatData?.lastName

        const [firstNameFromDisplayName, lastNameFromDisplayName] = getNamesFromDisplayName(displayName)
        const firstName = firstNameFromDisplayName || callFirstName || chatFirstName
        const lastName = lastNameFromDisplayName || callLastName || chatLastName
        const fullName = displayName || formatFullName(firstName, lastName)

        return fullName || contact
    }, [formatFullName, getNamesFromDisplayName, modalItem.displayData, modalItem?.externalChatData?.firstName, modalItem?.externalChatData?.lastName])
}
