import React from 'react'

import type {
    DirectoryEntry,
    DirectoryObject,
    DirectoryStateUserObject,
    EmiUsers,
    SearchDirectoryCategory,
} from '@bpinc/ad-directory-state-types'
import {assertEmiCategoryId} from '@bpinc/ad-directory-state-types'
import type {Dictionary} from '@bpinc/lib-types-helpers'
import {Highlight} from '@bpinc/lib-ui-elements'

import {teamFilter} from '../../utils'
import type {DirectoryFolderType} from './helpers'
import {directoryFolderTypes} from './helpers'


type DirectoryFolderItem = DirectoryObject | SearchDirectoryCategory

type Props = {
    type: DirectoryFolderType
    folderItem: DirectoryFolderItem
    users: DirectoryStateUserObject[]
    directoryEntries: Dictionary<DirectoryEntry[]>
    emiUsers: EmiUsers
    searchQuery?: string
}

export const DirectoryFolderTitle: React.FC<Props> = ({
    type,
    folderItem,
    users,
    directoryEntries,
    emiUsers,
    searchQuery,
}) => {

    const childrenCount = getFolderChildrenCount()
    const showCount = childrenCount !== undefined

    function getFolderChildrenCount(): number | undefined {
        switch (type) {
            case directoryFolderTypes.TEAM:
                return 'usersCount' in folderItem
                    ? folderItem.usersCount
                    : users.filter(teamFilter(folderItem.id)).length

            case directoryFolderTypes.CATEGORY:
                return directoryEntries[folderItem.id]?.length

            case directoryFolderTypes.EMI_CATEGORY:
                assertEmiCategoryId(folderItem.id)
                return emiUsers[folderItem.id]?.length
        }
    }

    return (
        <>
            <span
                data-testid="category-title"
                className="body text-primary-on-primary"
            >
                {searchQuery
                    ? <Highlight text={searchQuery}>{folderItem.name}</Highlight>
                    : folderItem.name
                }
            </span>
            {showCount && (
                <span className="ml-2 font-semibold caption-s text-neutral-disabled ">
                    (
                    {childrenCount}
)
                </span>
            )}
        </>
    )
}
