import React, {useMemo} from 'react'

import {AppRoutes, useNavigationState} from '@bpinc/lib-communicator-navigation-state'
import {useDirectoryUIState} from '@bpinc/lib-communicator-ui-context'
import {DIRECTORY_TABS_INDEXES} from '@bpinc/lib-communicator-ui-types'
import {createComponentLoader} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'


const {CalendarUserSelectLoader} = createComponentLoader('calendar', () => import('calendar'), 'CalendarUserSelect')

export const useNavigationTitle = (): string | JSX.Element => {
    const {t} = useLocalizationState()
    const directoryCreateContactMode = useDirectoryUIState(s =>
        s.createContactMode && s.tabIndex === DIRECTORY_TABS_INDEXES.external,
    )
    const {activePage} = useNavigationState()

    const titles: {[route: string]: string | JSX.Element} = useMemo(() => ({
        [AppRoutes.Recents]: t.ADDRESS_BOOK_AREA.addressBook_recents(),
        [AppRoutes.Dial]: t.ADDRESS_BOOK_AREA.addressBook_dial(),
        [AppRoutes.Favorites]: t.ADDRESS_BOOK_AREA.addressBook_favorites(),
        [AppRoutes.Directory]: directoryCreateContactMode
            ? t.CONTACT.create_contact()
            : t.ADDRESS_BOOK_AREA.addressBook_directory(),
        [AppRoutes.Cases]: t.CRM.crm_case_tab(),
        //[AppRoutes.KnowledgeBase]: t.KNOWLEDGE_BASE.kb_heading(),
        [AppRoutes.Interactions]: '',
        [AppRoutes.Calendar]: <CalendarUserSelectLoader className="left-[-140px]" />,
        [AppRoutes.ContactDetails]: t.ADDRESS_BOOK_AREA.contact(),
    }),
    [directoryCreateContactMode, t])

    return titles[activePage.name]
}
