import type {ChangeEvent, KeyboardEvent, RefObject, SyntheticEvent} from 'react'
import {useCallback, useMemo, useState} from 'react'

import {useInteractionProp, useInteractionStateMethods} from '@bpinc/ad-interaction-state-context'
import {digitizePhone, isCall} from '@bpinc/ad-interaction-state-methods'
import type {Item, ItemState} from '@bpinc/ad-interaction-state-types'
import {isHTMLInputElement} from '@bpinc/lib-html-utils'
import {useInteractionId} from '@bpinc/lib-interaction-context'


function checkAllowToSendDtmf(item?: Item) {
    const itemState = item?.state

    return isCall(item) && inDeliveryOrDeliveryPendingState(itemState)

    function inDeliveryOrDeliveryPendingState(state?: ItemState) {
        return state === 'delivered' || state === 'delivery_pending'
    }
}

function isInvalidDtmfInput(inputValue: string) {
    const onlyDTMFsRegexp = /^[0-9*#,]*$/g
    return !onlyDTMFsRegexp.test(inputValue)
}

export function useDtmfInputStateAndHandlers(inputRef: RefObject<HTMLInputElement>) {
    const [inputValue, setInputValue] = useState<string>('')
    const [delay, setDelay] = useState(0)
    const interactionId = useInteractionId()

    const {sendDtmf} = useInteractionStateMethods()

    const activeItemCallId = useInteractionProp(interactionId, activeItem => activeItem?.callData?.callId)

    const allowToSendDtmf = useInteractionProp(interactionId, checkAllowToSendDtmf)

    const sendDTMFs = useCallback(
        function sendDTMFsCallback(DTMFs: string[], delayMs: number) {
            if (!activeItemCallId) return
            setTimeout(() => {
                DTMFs.forEach(pendingDtmf => {
                    sendDtmf({dtmf: pendingDtmf, callId: activeItemCallId})
                })
            }, delayMs)
        },
        [activeItemCallId, sendDtmf],
    )

    const handleInputChange = useCallback(
        function handleInputChangeCallback(evt: ChangeEvent<HTMLInputElement>) {
            if (!activeItemCallId) return

            const newInputValue = digitizePhone(evt.target.value.toLowerCase())

            if (isInvalidDtmfInput(newInputValue)) {
                return
            }

            if (allowToSendDtmf) {
                setInputValue(newInputValue)
                const {DTMFsToSend, DTMFSToQueue} = getDTMFs(inputValue, newInputValue)

                decreaseDelayWhenComaIsRemoved(inputValue, newInputValue)

                sendDTMFs(DTMFsToSend, delay)
                queueDTMFs(DTMFSToQueue)
            }

            function decreaseDelayWhenComaIsRemoved(prevValue: string, newValue: string) {
                const prevComasAmount = getComasAmount(prevValue)
                const newComasAmount = getComasAmount(newValue)

                if (newComasAmount < prevComasAmount) {
                    decreaseDelay()
                }

                function getComasAmount(string: string) {
                    return string.split(',').length - 1
                }

                function decreaseDelay() {
                    const secondsToSubtract = getComasAmountDifference()
                    const millisecondsToSubtract = secondsToSubtract * 1000
                    setDelay(currentdelay => currentdelay - millisecondsToSubtract)
                }

                function getComasAmountDifference() {
                    return prevComasAmount - newComasAmount
                }
            }

            function queueDTMFs(DTMFsToQueue: string[][]) {
                if (DTMFsToQueue.length) {
                    let localDelay = delay
                    DTMFsToQueue.forEach(DTMFs => {
                        localDelay += 1000
                        sendDTMFs(DTMFs, localDelay)
                    })
                    setDelay(localDelay)
                }
            }

            function getDTMFs(
                prevInputValue: string,
                newInputValueInner: string,
            ): {DTMFsToSend: string[]; DTMFSToQueue: string[][]} {
                const sameBeginning = getSameBeginning(prevInputValue, newInputValueInner)

                const DTMFsAsString = newInputValueInner.replace(sameBeginning, '')

                const [DTMFsToSend, ...DTMFsToQueue] = DTMFsAsString.split(',')

                return {
                    DTMFsToSend: DTMFsToSend.split(''),
                    DTMFSToQueue: DTMFsToQueue.map(DTMFs => DTMFs.split('')),
                }

                function getSameBeginning(a: string, b: string) {
                    let sameBeginningCurrent = ''
                    for (let i = 0; i < b.length; i++) {
                        if (a[i] !== b[i]) {
                            return sameBeginningCurrent
                        }
                        sameBeginningCurrent += a[i]
                    }

                    return sameBeginningCurrent
                }
            }
        },
        [activeItemCallId, allowToSendDtmf, delay, inputValue, sendDTMFs],
    )

    const moveCaretToTheEnd = useCallback(function moveCaretToTheEndCallback(e: SyntheticEvent<HTMLInputElement>) {
        e.preventDefault()
        const input = e.target
        if (!isHTMLInputElement(input)) {
            return
        }
        input.focus()
        input.selectionStart = input.selectionEnd = input.value.length + 1
    }, [])

    const handleButtonClick = useCallback(
        function handleButtonClickCallback(value: string) {
            return function handleButtonClickInner() {
                value = value.trim()
                if (allowToSendDtmf && activeItemCallId) {
                    sendDtmf({dtmf: value, callId: activeItemCallId})
                    setInputValue(inputValue + value)
                }
                if (inputRef.current) {
                    inputRef.current.focus()
                }
            }
        },
        [activeItemCallId, allowToSendDtmf, inputRef, inputValue, sendDtmf],
    )

    const preventToMoveLeft = useCallback(function preventToMoveLeftCallback(evt: KeyboardEvent<HTMLInputElement>) {
        if (evt.key === 'ArrowLeft') {
            evt.preventDefault()
        }
    }, [])

    const preventDefault = useCallback(function preventDefaultCallback(evt: SyntheticEvent) {
        evt.preventDefault()
    }, [])

    return useMemo(
        function memoize() {
            return {
                inputValue,
                preventDefault,
                handleInputChange,
                moveCaretToTheEnd,
                handleButtonClick,
                preventToMoveLeft,
            }
        },
        [inputValue, preventDefault, handleInputChange, moveCaretToTheEnd, handleButtonClick, preventToMoveLeft],
    )
}
