import {useMemo} from 'react'

import {useInteractionId} from '@bpinc/lib-interaction-context'
import type {DispositionOption} from '@bpinc/lib-interaction-ui-hooks'
import {useEvent} from '@bpinc/lib-jskit-ui/src/utils/useEvent'
import {useInteractionUIState} from '@bpinc/state-interaction-ui'


export function useSearchDisposition(options: DispositionOption[]) {
    const itemId = useInteractionId()

    const {items, setItemState} = useInteractionUIState()

    const searchValue = items[itemId]?.dispositionSearch ?? ''

    const setSearch = useEvent(
        function handleChangeCallback(value: string) {
            setItemState(itemId, oldState => ({
                ...oldState,
                dispositionSearch: value,
            }))
        })


    const optionsFilteredBySearch = useMemo(
        function memoizeOptionsFilteredBySearch() {
            if (!searchValue) return options

            const searchValueMatchesDispositionFilter = searchValueMatchesDisposition(
                searchValue,
            )

            return options
                .filter(searchValueMatchesDispositionFilter)
                .reduce(removeEmptyGroupLabelsReducer, [])
        },
        [options, searchValue],
    )

    return useMemo(
        function memoizeOutput() {
            return {
                searchValue,
                setSearch,
                optionsFilteredBySearch,
            }
        },
        [optionsFilteredBySearch, searchValue, setSearch],
    )
}

//------------------Functions block------------------------------//
function searchValueMatchesDisposition(searchValue: string) {
    return function searchValueMatchesDispositionFilter(
        disposition: DispositionOption,
    ) {
        if (disposition.isGroupLabel) return true

        return disposition.label.toLowerCase().includes(searchValue.toLowerCase())
    }
}

function removeEmptyGroupLabelsReducer(
    dispositions: DispositionOption[],
    disposition: DispositionOption,
    index: number,
    dispositionsList: DispositionOption[],
) {
    if (isEmptyGroupLabel(disposition, dispositionsList[index + 1])) {
        return dispositions
    }

    dispositions.push(disposition)
    return dispositions
}

function isEmptyGroupLabel(
    disposition: DispositionOption,
    nextDisposition?: DispositionOption,
) {
    return (
        disposition.isGroupLabel &&
        (!nextDisposition || nextDisposition.isGroupLabel)
    )
}
