import React, {useRef} from 'react'

import {useInteractionModalsAutoOpener} from '@bpinc/lib-interaction-ui-hooks'


export const InteractionModalsAutoOpener: React.FC = () => {
    const ref = useRef<HTMLDivElement>(null)
    useInteractionModalsAutoOpener(ref)

    return (
        <div ref={ref} />
    )
}
