import {useEffect} from 'react'

import {useAgentState} from '@bpinc/ad-agent-state-context'
import {formatDate} from '@bpinc/ad-formatting-helpers'
import {useEvent} from '@bpinc/lib-helpers'
import {Notifications, useNotificationsState} from '@bpinc/lib-notifications-context'
import {useLocalizationState} from '@bpinc/state-localization-context'


export function useInteractionNotAcceptedNotification() {
    const {notReadyReason, state, stateSetDate, systemNotReadyReasons} = useAgentState()
    const {showNotification, hideNotificationById} = useNotificationsState('methods')
    const {GLOBAL: i18n} = useLocalizationState('t')

    const updateNotification = useEvent(() => {
        if (!systemNotReadyReasons) {
            return
        }
        if (state === 'not_ready' && notReadyReason === systemNotReadyReasons.noAnswer) {
            const time = stateSetDate && formatDate(stateSetDate)
            const currentState = '"No answer"'
            const message = i18n.notification_agentInNotReadyNoAnswer(currentState, time || '[TIME]')

            showNotification({
                type: 'warning',
                id: Notifications.IncomingInteractionNotAcceptedError,
                message,
            })
        } else {
            hideNotificationById(Notifications.IncomingInteractionNotAcceptedError)
        }
    })

    useEffect(() => {
        updateNotification()
        // update only on notReadyReason change
    }, [notReadyReason, updateNotification])
}
