import type {RefObject} from 'react'
import {useCallback} from 'react'

import {isHTMLElement} from '@bpinc/lib-html-utils'


const MOVE_KEYS = ['ArrowDown', 'ArrowUp', 'Home', 'End']

/**
 * In this hook uses this accordion pattern for handler keys - https://www.w3.org/WAI/ARIA/apg/patterns/accordion/
 * It contains regulars rules from link and all optional rules
 * @returns callback function that returns the focus of the correct HTML element
 */
export const useDirectoryHandlerKeys = (ref: RefObject<HTMLDivElement>, selector: string): (e: KeyboardEvent) => void => {
    return useCallback((e: KeyboardEvent) => {
        if (!e.target || !isHTMLElement(e.target)) {
            return
        }
        const type = e.target.dataset?.element
        if (type && type === selector && MOVE_KEYS.includes(e.key) && ref.current) {
            const directoryItems = Array.from(ref.current.querySelectorAll<HTMLElement>(`[data-element="directory-item"]`))
            if (!directoryItems.length) {
                return
            }
            let currentFocusIndex = directoryItems.indexOf(e.target) ?? 0
            switch (e.key) {
                case 'ArrowDown':
                    currentFocusIndex = currentFocusIndex < directoryItems.length - 1 ? currentFocusIndex + 1 : 0
                    return directoryItems[currentFocusIndex].focus()
                case 'ArrowUp':
                    currentFocusIndex = currentFocusIndex > 0 ? currentFocusIndex - 1 : directoryItems.length - 1
                    return directoryItems[currentFocusIndex].focus()
                case 'Home':
                    currentFocusIndex = 0
                    return directoryItems[currentFocusIndex].focus()
                case 'End':
                    currentFocusIndex = directoryItems.length - 1
                    return directoryItems[currentFocusIndex].focus()
            }
        }
    }, [ref, selector])
}
