import React from 'react'

import {useIsCommunicator} from '@bpinc/ad-current-tab-data-context'
import {AppRoutes, useNavigationState} from '@bpinc/lib-communicator-navigation-state'
import {useLocalizationState} from '@bpinc/state-localization-context'


export const MainPageTitle: React.FC = () => {
    const {activePage} = useNavigationState()
    const isCommunicator = useIsCommunicator()
    const {ADDRESS_BOOK_AREA: i18n} = useLocalizationState('t')

    const pageTitles: Partial<Record<AppRoutes, string>> = {
        [AppRoutes.Calendar]: i18n.addressBook_calendar(),
        [AppRoutes.ContactDetails]: i18n.addressBook_contact_details(),
        [AppRoutes.Cases]: i18n.addressBook_cases(),
        [AppRoutes.Dial]: i18n.addressBook_dial_pad(),
        [AppRoutes.Directory]: i18n.addressBook_directory(),
        [AppRoutes.Favorites]: i18n.addressBook_favorites(),
        [AppRoutes.KnowledgeBase]: i18n.addressBook_knowledge_base(),
        [AppRoutes.Recents]: i18n.addressBook_recents(),
        [AppRoutes.Help]: i18n.addressBook_help(),
    }

    // TODO: I think this component should be moved to the specific page components
    if (
        isCommunicator ||
        activePage.name === AppRoutes.Interactions ||
        activePage.name === AppRoutes.ContactDetails
    ) {
        return null
    }

    return (
        <div className="flex shrink-0 items-center p-4">
            <span data-testid="page-title" className="uppercase tracking-[0.8px] caption-xs text-neutral-disabled">
                {pageTitles[activePage.name]?.toUpperCase()}
            </span>
        </div>
    )
}
