import {useEffect, useMemo, useState} from 'react'

import type {Item} from '@bpinc/ad-interaction-state-types'
import {cutMilliseconds, useTimer} from '@bpinc/lib-helpers'
import {useUnread as useUnreadHelper} from '@bpinc/lib-helpers-ui'


const MAX_TIME = 90

// @TODO - we need to make that 90 seconds timer synchronized between multiple browser tabs.
//  In order to achieve this we will have to rewrite this timer using timers-state
export const useUnread = (item: Item, withTimer: boolean) => {
    const [startTime, setStartTime] = useState<number | undefined>(undefined)
    const customerWaitTimer = useTimer(startTime, {format: cutMilliseconds})
    const unreadAmount = useUnreadHelper(item)

    useEffect(
        function setTimer() {
            withTimer && setStartTime(
                unreadAmount > 0
                    ? new Date().getTime()
                    : undefined,
            )
        },
        [unreadAmount, withTimer],
    )

    useEffect(
        function resetTimer() {
            if (customerWaitTimer && customerWaitTimer >= MAX_TIME) {
                setStartTime(undefined)
            }
        },
        [customerWaitTimer],
    )

    return useMemo(() => ({unreadAmount, customerWaitTimer}), [unreadAmount, customerWaitTimer])
}
