import {useCallback} from 'react'
import {useAtom} from 'jotai'

import {useSessionState} from '@bpinc/ad-session-context'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {mergingContactsAtom} from './atoms'
import {useSearchPageTabsActions} from './useSearchPageTabsActions'
import {MAX_CONTACTS_FOR_MERGE} from './utils'


export function useMergeContactsActions() {
    const {t} = useLocalizationState()
    const privileges = useSessionState('privileges')
    const [mergingContacts, setMergingContacts] = useAtom(mergingContactsAtom)
    const {handleTabOpen, handleTabClose} = useSearchPageTabsActions()
    const canAddContactForMerge = MAX_CONTACTS_FOR_MERGE > mergingContacts.length && privileges.EDIT_CONTACTS && privileges.DELETE_CONTACTS

    const addContactForMerge = useCallback((contactId: string) => {
        setMergingContacts(contacts => {
            return Array.from(new Set([...contacts, contactId]))
        })
        handleTabOpen({
            id: 'merge',
            type: 'merge_contacts',
            title: t.GLOBAL.merge(),
            closeable: false,
        })
    }, [handleTabOpen, setMergingContacts, t.GLOBAL])

    const removeContactFromMerge = useCallback((contactId: string) => {
        const contacts = mergingContacts.filter(id => id !== contactId)
        setMergingContacts(contacts)
        if (!contacts.length) {
            handleTabClose('merge')
        }
    }, [handleTabClose, mergingContacts, setMergingContacts])

    const closeContactsMerge = useCallback(() => {
        setMergingContacts([])
        handleTabClose('merge')
    }, [handleTabClose, mergingContacts, setMergingContacts])

    return {
        addContactForMerge: canAddContactForMerge ? addContactForMerge : undefined,
        removeContactFromMerge,
        canAddContactForMerge,
        closeContactsMerge,
    }
}
