import React from 'react'

import type {Party, Service} from '@bpinc/ad-case-state-types'
import {useFormatting} from '@bpinc/ad-formatting-context'
import {formatDate} from '@bpinc/ad-formatting-helpers'

import {Info} from './Info'


interface Props {
    data: Party
    type?: string
    services?: Service[]
}

export const Disposition: React.FC<Props> = ({data, type, services}) => {
    const {formatFullName} = useFormatting()
    const {firstName, lastName, serviceId, disposition, startTime = ''} = data

    const name = formatFullName(firstName, lastName)
    const date = formatDate(startTime, {prefixed: true, full: true})
    const heading = [name, 'dispositioned', type, 'interaction', date].filter(Boolean).join(' ')
    const service = (services || []).find(s => s.serviceId === serviceId)

    return (
        <div className="rounded border border-main p-2 text-lg">
            <div className="text-main">
                {heading}
            </div>
            <div className="mb-0.5 mt-1.5">
                {disposition}
            </div>
            {service && (
                <Info title="Service">
                    {service.serviceName}
                </Info>
            )}
        </div>
    )
}
