import type {ReactElement} from 'react'
import React from 'react'
import type {FieldValues} from 'react-hook-form'

import {SmRef} from '@bpinc/lib-external-ui-elements'
import type {FieldRawProps, FieldRenderImplementationProps} from '@bpinc/lib-forms-base'
import {FieldRawConstructor} from '@bpinc/lib-forms-base'


function renderElement<TFormValues extends FieldValues>({
    ref,
    ...props
}: FieldRenderImplementationProps<unknown, TFormValues>): ReactElement {
    return (
        <SmRef innerRef={ref}>
            <input
                hidden
                data-form-field="true"
                data-field-type="hidden-field"
                {...props}
            />
        </SmRef>
    )
}


export function HiddenFieldRaw<TFormValues extends FieldValues>(props: FieldRawProps<unknown, TFormValues>): ReactElement {
    return <FieldRawConstructor {...props} renderElement={renderElement} />
}
