import {useMemo} from 'react'

import {useDirectoryState} from '@bpinc/ad-directory-state-context'
import {MS_TEAMS_CHANNELS_CATEGORY_ID, MS_TEAMS_CHATS_CATEGORY_ID} from '@bpinc/ad-directory-state-types'
import {useUserDetailsById} from '@bpinc/ad-user-details-state-context'


export function useInternalUserData(userId?: string) {
    const {
        users,
        searchResults,
        emiUsers,
    } = useDirectoryState([
        'users',
        'searchResults',
        'emiUsers',
    ])
    const userDetails = useUserDetailsById(userId)

    return useMemo(() => {
        const dialpadSearchInternalUsers = searchResults.dialpad?.data?.users ?? []
        const msTeamsUsers = emiUsers[MS_TEAMS_CHATS_CATEGORY_ID] ?? []
        const msTeamsChannels = emiUsers[MS_TEAMS_CHANNELS_CATEGORY_ID] ?? []

        return (
            [
                ...users,
                ...dialpadSearchInternalUsers,
                ...msTeamsUsers,
            ].find(user => user.userId === userId)
            || msTeamsChannels.find(({channelId}) => channelId === userId)
            || userDetails?.data
        )
    }, [
        emiUsers,
        searchResults.dialpad?.data?.users,
        userDetails?.data,
        userId,
        users,
    ])
}
