import type {FC, RefObject} from 'react'
import React, {useRef} from 'react'

import type {DirectoryItem as DirectoryItemType} from '@bpinc/ad-directory-state-types'
import type {OpenContactDetailsParams} from '@bpinc/ad-interaction-state-hooks'
import type {InitiateCall} from '@bpinc/ad-interaction-state-types'
import {getDirectoryItemUniqueId} from '@bpinc/lib-helpers-ui'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {ErrorBoundary} from '@bpinc/lib-ui-elements'

import {DirectoryItem} from './user/DirectoryItem'
import type {OnChatParams, OnEmailParams} from './user/types'


interface Props {
    items: DirectoryItemType[]
    listRef?: RefObject<HTMLDivElement>
    transferMode?: 'messages' | 'calls'
    transferNumberInput?: string
    transferControlsStyle?: 'add' | 'replace'
    highlight?: string
    noControls?: boolean
    selectedId?: string
    contentClassName?: string
    onCall?: (callParams: InitiateCall) => void
    onChat?: (chatParams: OnChatParams) => void
    onEmail?: (emailParams: OnEmailParams) => void
    onDetails?: (params: OpenContactDetailsParams) => void
    onClick?: (evt: React.MouseEvent, item: DirectoryItemType) => void
    onDoubleClick?: (item: DirectoryItemType) => void
    isHighlighted?: (item: DirectoryItemType) => boolean
}

export const DirectoryItems: FC<Props> = ({
    transferMode,
    transferNumberInput,
    transferControlsStyle,
    items,
    highlight,
    listRef,
    selectedId,
    noControls,
    contentClassName,
    onCall,
    onChat,
    onEmail,
    onDetails,
    onClick,
    onDoubleClick,
    isHighlighted,
}) => {
    const innerListRef = useRef<HTMLDivElement>(null)
    const finalListRef = listRef || innerListRef

    return (
        <div ref={innerListRef} className={tcn('[&>div]:border-b [&>div]:border-main-border [&>div]:last:border-b-0', 'ui divided relaxed list')}>
            {items.map((item, index) => {
                const isSelected = selectedId === getDirectoryItemUniqueId(item)
                return (
                    <ErrorBoundary key={index} fallback={null}>
                        <DirectoryItem
                            onDoubleClick={onDoubleClick}
                            listNode={finalListRef.current}
                            noControls={noControls}
                            item={item}
                            onCall={onCall}
                            onChat={onChat}
                            onEmail={onEmail}
                            isSelected={isHighlighted?.(item) || isSelected}
                            showControls={isSelected}
                            onDetails={onDetails}
                            transferMode={transferMode}
                            transferNumberInput={transferNumberInput}
                            transferControlsStyle={transferControlsStyle}
                            highlight={highlight}
                            contentClassName={contentClassName}
                            onClick={onClick}
                        />
                    </ErrorBoundary>
                )
            })}
        </div>
    )
}
