import {useCallback, useMemo} from 'react'

import {useSessionStateMethods} from '@bpinc/ad-session-context'
import {
    useOpenAboutModal,
    useOpenAudioDevicesModal,
    useOpenChangePasswordModal,
    useOpenEMIAccountsModal,
    useOpenKeyboardShortcutsModal,
    useOpenLogoutModal,
    useOpenPredefinedMessagesModal,
    useOpenPreferencesModal,
    useOpenPrerecordedMessagesModal,
    useOpenUserProfileModal,
    useOpenVolumeModal,
} from '@bpinc/lib-modals-ui'


interface UseHandlersParams {
    closeMenu(): void
    openSettingsMenu(): void
    hasActiveCalls: boolean
}

export function useSettingsHandlers(params: UseHandlersParams) {
    const {hasActiveCalls, closeMenu, openSettingsMenu} = params
    const {startPhoneTypeSelection} = useSessionStateMethods()
    const openChangePasswordModal = useOpenChangePasswordModal()
    const openLogoutModal = useOpenLogoutModal()
    const openEMIAccountsModal = useOpenEMIAccountsModal()
    const openUserProfileModal = useOpenUserProfileModal()
    const openKeyboardShortcutsModal = useOpenKeyboardShortcutsModal()
    const openPredefinedMessagesModal = useOpenPredefinedMessagesModal()
    const openVolumeModal = useOpenVolumeModal()
    const openPreferencesModal = useOpenPreferencesModal()
    const openAboutModal = useOpenAboutModal()
    const openAudioDevicesModal = useOpenAudioDevicesModal()
    const openPrerecordedMessagesModal = useOpenPrerecordedMessagesModal()

    const handlePredefinedMessagesClick = useCallback(
        function handlePredefinedMessagesClickCb() {
            closeMenu()
            openPredefinedMessagesModal(undefined, {
                afterClose: openSettingsMenu,
            })
        },
        [closeMenu, openPredefinedMessagesModal, openSettingsMenu],
    )

    const handlePhoneSelectionClick = useCallback(() => {
        if (hasActiveCalls) {
            return
        }
        closeMenu()
        startPhoneTypeSelection()
    }, [hasActiveCalls, closeMenu, startPhoneTypeSelection])

    const handleAudioDevicesClick = useCallback(() => {
        closeMenu()
        openAudioDevicesModal(undefined, {
            afterClose: openSettingsMenu,
        })
    }, [closeMenu, openAudioDevicesModal, openSettingsMenu])

    const handleChangePasswordClick = useCallback(() => {
        closeMenu()
        openChangePasswordModal({}, {afterClose: openSettingsMenu})
    }, [closeMenu, openChangePasswordModal, openSettingsMenu])

    const handleLogoutClick = useCallback(() => {
        closeMenu()
        openLogoutModal(undefined, {afterClose: openSettingsMenu})
    }, [closeMenu, openLogoutModal, openSettingsMenu])

    const handleUserProfileClick = useCallback(() => {
        closeMenu()
        openUserProfileModal(undefined, {
            afterClose: openSettingsMenu,
        })
    }, [closeMenu, openSettingsMenu, openUserProfileModal])

    const handleAboutClick = useCallback(() => {
        closeMenu()
        openAboutModal(undefined, {
            afterClose: openSettingsMenu,
        })
    }, [closeMenu, openAboutModal, openSettingsMenu])

    const handleKeyboardShortcutsClick = useCallback(() => {
        closeMenu()
        openKeyboardShortcutsModal(undefined, {
            afterClose: openSettingsMenu,
        })
    }, [closeMenu, openKeyboardShortcutsModal, openSettingsMenu])

    const handlePrerecordedMessagesClick = useCallback(() => {
        closeMenu()
        openPrerecordedMessagesModal(undefined, {
            afterClose: openSettingsMenu,
        })
    }, [closeMenu, openPrerecordedMessagesModal, openSettingsMenu])

    const handlePreferencesClick = useCallback(() => {
        closeMenu()
        openPreferencesModal(undefined, {
            afterClose: openSettingsMenu,
        })
    }, [closeMenu, openPreferencesModal, openSettingsMenu])

    const handleEMIAccountsClick = useCallback(() => {
        closeMenu()
        openEMIAccountsModal(undefined, {
            afterClose: openSettingsMenu,
        })
    }, [closeMenu, openEMIAccountsModal, openSettingsMenu])

    const handleRingtonesAndAlertsVolumeClick = useCallback(() => {
        closeMenu()
        openVolumeModal(undefined, {
            afterClose: openSettingsMenu,
        })
    }, [closeMenu, openSettingsMenu, openVolumeModal])

    return useMemo(
        function memoizeOutput() {
            return {
                closeMenu,
                handlePrerecordedMessagesClick,
                handlePreferencesClick,
                handleUserProfileClick,
                handleAboutClick,
                handleKeyboardShortcutsClick,
                handleLogoutClick,
                handleChangePasswordClick,
                handlePhoneSelectionClick,
                handleAudioDevicesClick,
                handlePredefinedMessagesClick,
                handleRingtonesAndAlertsVolumeClick,
                handleEMIAccountsClick,
            }
        },
        [
            closeMenu,
            handleChangePasswordClick,
            handleLogoutClick,
            handlePhoneSelectionClick,
            handleAudioDevicesClick,
            handlePredefinedMessagesClick,
            handlePrerecordedMessagesClick,
            handlePreferencesClick,
            handleUserProfileClick,
            handleAboutClick,
            handleKeyboardShortcutsClick,
            handleRingtonesAndAlertsVolumeClick,
            handleEMIAccountsClick,
        ],
    )
}
