import React from 'react'

import type {ContactShortType} from '@bpinc/ad-contacts-state-types'
import type {DirectoryStateUserObject} from '@bpinc/ad-directory-state-types'
import type {OpenContactDetailsParams} from '@bpinc/ad-interaction-state-hooks'
import type {InitiateCall} from '@bpinc/ad-interaction-state-types'
import {useUserDetailsById} from '@bpinc/ad-user-details-state-context'
import {useEvent} from '@bpinc/lib-helpers'
import type {ChatParams} from '@bpinc/lib-ui-elements'
import type {RecentPartyType} from '@bpinc/state-recent-types'

import {ExternalContact} from '../../../directory/components/user/external-contact/ExternalContact'
import {InternalUser} from '../../../directory/components/user/internal-user/InternalUser'


type Props = {
    party: RecentPartyType
    transferMode?: 'messages' | 'calls'
    onClick: (party: RecentPartyType) => void
    onCall: (callParams: InitiateCall) => void
    onChat: (chatParams: ChatParams) => void
    onDetails: (params: OpenContactDetailsParams) => void
}

export const RecentConferenceItem: React.FC<Props> = ({
    party,
    transferMode,
    onClick,
    onCall,
    onChat,
    onDetails,
}) => {

    const userDetails = useUserDetailsById(party.userId)

    const handleClick = useEvent(() => {
        onClick({
            phone: userDetails?.data.extension,
            ...party,
        })
    })

    if (party.userId) {
        // ext chat party data doesn't include extension for internal users
        const userData: DirectoryStateUserObject = {
            userId: party.userId,
            firstName: party.firstName ?? '',
            lastName: party.lastName ?? '',
            extension: userDetails?.data.extension ?? '',
            presenceState: userDetails?.data.presenceState ?? 'offline',
            isAgent: userDetails?.data.isAgent,
            photoId: userDetails?.data.photoId,
            teamId: userDetails?.data.teamId,
        }
        return (
            <InternalUser
                user={userData}
                transferMode={transferMode}
                onClick={handleClick}
                onCall={onCall}
                onChat={onChat}
                onDetails={onDetails}
            />
        )
    }
    if (party.contactId) {
        const contactData: ContactShortType = {
            id: party.contactId,
            firstName: party.firstName ?? '',
            lastName: party.lastName ?? '',
            phone: party.phone ?? '',
            email: '',
        }
        return (
            <ExternalContact
                contact={contactData}
                transferMode={transferMode}
                onClick={handleClick}
                onCall={onCall}
                onChat={onChat}
                onDetails={onDetails}
            />
        )
    }

    return null
}
