import React from 'react'

import {useContact} from '@bpinc/ad-contacts-state-context'
import {useActivityHistoryOpener, useCallHandler} from '@bpinc/ad-interaction-state-hooks'
import type {FavoritesChannelMedia} from '@bpinc/ad-transport-types'
import {ExternalContactFieldFavoriteButton} from '@bpinc/lib-favorites-button'
import type {Toggle} from '@bpinc/lib-helpers'
import {useEvent} from '@bpinc/lib-helpers'

import {useFormElements} from '../../../hooks/useFormElements'
import {useFormElementsToViews} from '../../../hooks/useFormElementsToViews'
import {Avatar} from '../../Avatar'
import {FormElementView} from '../../FormElementView'
import {ExternalViewControls} from './ExternalViewControls'


export const ExternalView: React.FC<{
    toggleEditMode: Toggle
    contactId: string
}> = ({toggleEditMode, contactId}) => {

    const handleCall = useCallHandler()
    const openActivityHistory = useActivityHistoryOpener()

    const formElements = useFormElements()
    const contactData = useContact(contactId)
    const formValues = contactData?.details
    const fields = useFormElementsToViews({elements: formElements, values: formValues})

    const handlePhoneChannelInteraction = useEvent((number: string, media: FavoritesChannelMedia) => {
        if (media === 'voice') {
            handleCall({
                contactId,
                phone: number,
                firstName: contactData?.data?.firstName,
                lastName: contactData?.data?.lastName,
                navigateToInteractionsPage: true,
                origination: 'contact-profile',
            })
        } else {
            openActivityHistory({
                contactId,
                phone: number,
                firstName: contactData?.data?.firstName,
                lastName: contactData?.data?.lastName,
                origination: 'contact-profile',
            })
        }
    })

    return (
        <>
            <ExternalViewControls
                contactId={contactId}
                toggleEditMode={toggleEditMode}
            />

            <div className="flex shrink-0 gap-8 px-6 py-4">
                {contactData?.data && <Avatar user={contactData?.data} />}

                <div role="list" className="grow">
                    {fields.map((item, i) => {
                        if (!item.value && !item.messenger) {
                            return null
                        }
                        return (
                            <FormElementView
                                values={formValues}
                                key={i}
                                contactId={contactId}
                                additionalControls={
                                    <ExternalContactFieldFavoriteButton
                                        contactId={contactId}
                                        field={item}
                                        onPhoneInteraction={handlePhoneChannelInteraction}
                                    />
                                }
                                {...item}
                            />
                        )
                    })}
                </div>
            </div>
        </>
    )
}
