import React from 'react'

import type {AppRoutes} from '@bpinc/lib-communicator-navigation-state'
import {useCommunicatorUIState} from '@bpinc/lib-communicator-ui-context'
import {SmSidebar} from '@bpinc/lib-external-ui-elements'
import {FocusTrap} from '@bpinc/lib-ui-elements'

import {useHandlers} from './hooks/useHandlers'
import {useSidebarRightContent} from './hooks/useSidebarRightContent'

import styles from '../../styles/MainPage.module.less'


export interface SidebarRightProps {
    activePageName: AppRoutes
    onMenuItemClick(name: AppRoutes, params?: Record<string, unknown>): void
}

export const SidebarRight: React.FC<SidebarRightProps> = React.memo(props => {
    const rightPanelOpened = useCommunicatorUIState('rightPanelOpened')
    const {closeMenu} = useHandlers()
    const content = useSidebarRightContent(props)

    return (
        <SmSidebar
            className={styles.sidebar}
            animation="overlay"
            direction="right"
            visible={rightPanelOpened}
            onHide={closeMenu}
            data-page-section="sidebar sidebar_right"
            data-testid="right-sidebar"
        >
            <FocusTrap
                enabled={rightPanelOpened}
                delayFocus={500} // sidebar opening animation duration
            >
                {content}
            </FocusTrap>
        </SmSidebar>
    )
})
