import React, {useCallback} from 'react'

import {useInteractionStateMethods} from '@bpinc/ad-interaction-state-context'
import {getServiceNameFromItem, isWorkItem} from '@bpinc/ad-interaction-state-methods'
import type {Item} from '@bpinc/ad-interaction-state-types'
import {AppRoutes, useNavigationState} from '@bpinc/lib-communicator-navigation-state'
import {SmButton, SmList, SmListItem} from '@bpinc/lib-external-ui-elements'
import {IconCampaignNotification, IconChatNotification} from '@bpinc/lib-icons'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {useDisplayName} from '../hooks/useDisplayName'
import {AriaDescription} from './AriaDescription'

import styles from '../styles/CompactNotificationModal.module.less'


export const NotificationItem: React.FC<Item> = item => {
    const {id: itemId} = item
    const displayName = useDisplayName(item)
    const serviceName = getServiceNameFromItem(item)

    const {
        rejectItem,
        acceptItem,
        activateItem,
        dialWorkitem,
    } = useInteractionStateMethods()
    const {t} = useLocalizationState()
    const {activePage, setActivePage} = useNavigationState()

    const onReject = useCallback(() => {
        rejectItem(itemId)
    }, [rejectItem, itemId])

    const onAccept = useCallback(() => {
        acceptItem(itemId)
    }, [acceptItem, itemId])

    const onAcceptAndOpen = useCallback(() => {
        acceptItem(itemId)
        activateItem(itemId)
        if (activePage.name !== AppRoutes.Interactions) {
            setActivePage({name: AppRoutes.Interactions})
        }
    }, [acceptItem, activateItem, itemId, activePage, setActivePage])

    const onDial = useCallback(() => {
        onAcceptAndOpen()
        dialWorkitem({
            workitemItemId: itemId,
        })
    }, [dialWorkitem, itemId, onAcceptAndOpen])

    return (
        <SmListItem className={styles.notificationItem}>
            <SmList.Content className={styles.notificationItemContent}>
                <div className={styles.background} />
                <div className={styles.content}>
                    {isWorkItem(item) && serviceName && <div className={styles.serviceRow}>{serviceName}</div>}
                    <div className={styles.displayRow}>
                        <SmList.Icon className={styles.chatIcon}>
                            {isWorkItem(item) ? <IconCampaignNotification /> : <IconChatNotification />}
                        </SmList.Icon>
                        <div className={styles.displayName}>{displayName}</div>
                        {isWorkItem(item) && <div className={styles.phoneNumber}>{item.displayData.number}</div>}
                    </div>
                    <div className={styles.controlsRow}>
                        <div>
                            <SmButton data-testid="reject-button" negative size="small" className={styles.control} onClick={onReject}>
                                {t.INCOMING_CALL_DIALOG_BOX.incomingInteraction_reject()}
                            </SmButton>
                        </div>
                        <div>
                            <SmButton positive size="small" data-testid="accept-button" className={styles.control} onClick={onAccept}>
                                {t.INCOMING_CALL_DIALOG_BOX.incomingInteraction_accept()}
                            </SmButton>
                            {isWorkItem(item) ? (
                                <SmButton size="small" className={tcn(styles.control, styles.invertedButton)} onClick={onDial}>
                                    {t.INCOMING_CALL_DIALOG_BOX.dial_number()}
                                </SmButton>
                            ) : (
                                <SmButton positive size="small" className={styles.control} onClick={onAcceptAndOpen}>
                                    {t.INCOMING_CALL_DIALOG_BOX.button_accept_and_open_label()}
                                </SmButton>
                            )}
                        </div>
                    </div>
                </div>
                <AriaDescription type={isWorkItem(item) ? 'workitem' : 'chat'} serviceName={serviceName} displayName={displayName} />
            </SmList.Content>
        </SmListItem>
    )
}
