import {createSelector} from 'reselect'

import type {ActivityCase} from '@bpinc/ad-case-state-types'


type FormatNameFunction = (
    firstName: string | undefined,
    lastName: string | undefined,
) => string

const selectDispositions = createSelector(
    (activity: ActivityCase) => activity.parties,
    (parties: ActivityCase['parties']) => {
        const result = new Set<string>()

        parties?.forEach(party => {
            if (party.partyType !== 'CONTACT' && party.disposition) {
                result.add(party.disposition)
            }
        })

        return Array.from(result)
    },
)

const selectServices = (activity: ActivityCase) => activity.services

//TODO: Remove it if it won't be used after the case page is done.
const selectNotes = createSelector(
    (activity: ActivityCase) => activity.parties,
    (parties: ActivityCase['parties']) => {
        const result = new Set<string>()

        parties?.forEach(party => {
            if (party.partyType !== 'CONTACT' && party.notes) {
                result.add(party.notes)
            }
        })

        return Array.from(result)
    },
)

const selectAgentsNames = (
    formatNameFunction: FormatNameFunction,
) =>
    createSelector(
        (a: ActivityCase) => a.parties,
        (parties: ActivityCase['parties']) => {
            const result = new Set<string>()

            parties?.forEach(party => {
                if (party.partyType !== 'CONTACT') {
                    const name = formatNameFunction(
                        party.firstName,
                        party.lastName,
                    )
                    if (name) {
                        result.add(name)
                    }
                }
            })

            return Array.from(result)
        },
    )

export const selectAgentsString = (formatNameFunction: FormatNameFunction) => createSelector(
    selectAgentsNames(formatNameFunction),
    agents => agents.join(', '),
)

const selectServicesString = createSelector(selectServices, services =>
    services?.map(service => service.serviceName)?.join(', '),
)

export const selectDispositionsString = createSelector(
    selectDispositions,
    dispositions => dispositions?.join(', '),
)

export const selectServicesAndDispositionsString = createSelector(
    selectServicesString,
    selectDispositionsString,
    (servicesString, dispositionsString) => {
        return [servicesString, dispositionsString].filter(Boolean).join(': ')
    },
)
