import {useCallback} from 'react'

import {useInteraction, useInteractions} from '@bpinc/ad-interaction-state-context'
import {getInteractionStepId, isCall, isPotentialInteraction} from '@bpinc/ad-interaction-state-methods'
import {useChatAttachmentsUIState} from '@bpinc/lib-attachments-state'
import {useAsRef} from '@bpinc/lib-helpers'
import {assertDefined} from '@bpinc/lib-types-helpers'

import {useInitiateChatByAttachment} from '../../../../hooks/useInitiateChatByAttachment'


export function useOnDrop(itemId: string) {
    const addAttachment = useChatAttachmentsUIState('addAttachment')
    const chatItem = useInteraction(itemId)
    const items = useInteractions()
    const itemsRef = useAsRef(items)
    itemsRef.current = items

    assertDefined(chatItem)
    const initiateChat = useInitiateChatByAttachment({chatItem})
    const shouldInitiateChat =
        isPotentialInteraction(chatItem) || isCall(chatItem)

    return useCallback(
        function onDropCallback(acceptedFiles: File[]) {
            if (!acceptedFiles.length) {
                return
            }

            if (chatItem) {
                if (shouldInitiateChat) {
                    initiateChat()
                }
                const interactionStepId = getInteractionStepId(chatItem)
                addAttachments(interactionStepId)
            }

            function addAttachments(interactionStepId: string) {
                acceptedFiles.forEach(file => addAttachment(interactionStepId, file))
            }
        },
        [chatItem, shouldInitiateChat, initiateChat, addAttachment],
    )
}
