import React from 'react'

import {SmList} from '@bpinc/lib-external-ui-elements'
import type {DispositionOption} from '@bpinc/lib-interaction-ui-hooks'
import {tcn} from '@bpinc/lib-tailwind-classnames'

import {DispositionsListItem} from './DispositionsListItem'


interface DispositionsListProps {
    list: DispositionOption[]
    highlight?: string
    onSelect: (option: DispositionOption) => void
    selectedDisposition: DispositionOption
    className?: string
}

export const DispositionsList: React.FC<DispositionsListProps> = React.memo(function DispositionsList({
    list,
    className,
    ...props
}) {
    return (
        <SmList
            className={tcn(className, 'mt-[8.5px] max-h-[200px] flex-auto overflow-auto rounded border border-main-border')}
            divided
            verticalAlign="middle"
            data-testid="available-dispositions"
        >
            {list.map(dispositionOption => (
                <DispositionsListItem key={dispositionOption.value} {...props} {...dispositionOption} />
            ))}
        </SmList>
    )
})
