import React from 'react'
import {useContext} from 'use-context-selector'

import {useFormatting} from '@bpinc/ad-formatting-context'
import type {ContactPhone, FormDefinitionFieldPhoneList, FormDefinitionPageItem} from '@bpinc/ad-request-urls'
import {getPageItemCSSProperties} from '@bpinc/lib-form-builder'

import {FormBuilderContext} from '../../FormBuilder'
import {PhonesFieldArray} from '../common/FieldArray/PhonesFieldArray'
import {PhoneLink} from '../common/PhoneLink'
import {ViewItem} from '../common/ViewItem'


export const PhoneListField: React.FC<{
    pageItem: FormDefinitionPageItem
    field: FormDefinitionFieldPhoneList
}> = ({
    pageItem,
    field,
}) => {
    const {
        form,
        view,
    } = useContext(FormBuilderContext)

    const {formatPhoneType} = useFormatting()

    if (view) {
        const listValue = form.getValues()[field.name || ''] as ContactPhone[]
        const viewStyle = getPageItemCSSProperties(pageItem.style)

        return (
            <>
                {listValue?.map((phone, index: number) => (
                    <ViewItem
                        key={index}
                        viewStyle={viewStyle}
                        label={formatPhoneType(phone.type)}
                        value={<PhoneLink phone={phone.phone} />}
                    />
                ))}
            </>
        )
    }

    const editStyle = getPageItemCSSProperties(pageItem.editStyle)

    return (
        <PhonesFieldArray
            name={field.name || ''}
            control={form.control}
            style={editStyle}
            layoutClassName={'gap-x-[--form-cellPadding] gap-y-[--form-rowHeight]'}
            typeClassName={'w-[--form-fieldLabelWidth] min-w-[--form-fieldLabelWidth] max-w-[--form-fieldLabelWidth]'}
        />
    )
}
