import {useMemo} from 'react'

import {
    isAfterCallWork,
    isCall,
    isCallConference,
    isChatConference,
    isContact,
    isEmail,
    isExternalChat,
    isInternalChat,
    isInternalMessage,
    isOnHold,
    isOutboundInteraction,
    isPotentialInteraction,
    isWorkItem,
} from '@bpinc/ad-interaction-state-methods'
import type {Item} from '@bpinc/ad-interaction-state-types'
import {
    IconAfterCallWorkAD,
    IconChat,
    IconConferenceCall16,
    IconConferenceChat16,
    IconEmail16,
    IconInternalChat16,
    IconOutboundCall16,
    IconOutboundPreview16,
    IconPause16,
    IconPhone16Filled,
    IconRoundChat16Filled,
} from '@bpinc/lib-jskit-ui'


export function useInteractionTypeIcon(item: Item) {
    return useMemo(
        function getInteractionTypeIcon() {
            if (isWorkItem(item)) {
                return IconOutboundPreview16
            }

            if (isPotentialInteraction(item)) {
                return isContact(item) ? IconRoundChat16Filled : IconInternalChat16
            }

            if (isOnHold(item) && isCall(item)) {
                return IconPause16
            }

            if (isChatConference(item)) {
                return IconConferenceChat16
            }

            if (isAfterCallWork(item)) {
                return IconAfterCallWorkAD
            }

            if (isInternalMessage(item)) {
                return IconChat
            }

            if (isExternalChat(item)) {
                return IconRoundChat16Filled
            }

            if (isInternalChat(item)) {
                return IconInternalChat16
            }

            if (isEmail(item)) {
                return IconEmail16
            }

            if (isCallConference(item)) {
                return IconConferenceCall16
            }

            if (isOutboundInteraction(item) && isCall(item)) {
                return IconOutboundCall16
            }

            return IconPhone16Filled
        },
        [item],
    )
}
