import React from 'react'
import type {Control} from 'react-hook-form'

import type {FormDefinitionPageItem} from '@bpinc/ad-request-urls'
import {getFormElementStyle, getPageItemCSSProperties} from '@bpinc/lib-form-builder'

import {TextField} from '../TextField'
import {TextAreaField} from './TextAreaField'


const labelClassName = `w-[--form-fieldLabelWidth] min-w-[--form-fieldLabelWidth] max-w-[--form-fieldLabelWidth]`
const editItemClassName = `flex items-center gap-x-[--form-cellPadding] gap-y-[--form-rowHeight]`

export const GeneralTextFormField: React.FC<{
    pageItem: FormDefinitionPageItem
    control: Control

    multiline: boolean
    placeholder: string
    label: string | null
    required: boolean
    name: string
    type?: string
    minRows: number
}> = ({
    pageItem,
    control,

    multiline,
    placeholder,
    label,
    required,
    name,
    type = 'text',
    minRows,
}) => {
    const style = getFormElementStyle(pageItem)
    const editStyle = getPageItemCSSProperties(pageItem.editStyle)

    return multiline ? (
        <TextAreaField
            placeholder={placeholder}
            label={label}
            required={required}
            name={name}
            control={control}
            minRows={minRows}
            wrapperClassName={editItemClassName}
            style={style}
            labelClassName={labelClassName}
        />
    ) : (
        <TextField
            placeholder={placeholder}
            label={label}
            required={required}
            name={name}
            control={control}
            style={editStyle}
            type={type as any}
        />
    )
}
