import {useEffect} from 'react'

import {useContactsState, useContactsStateMethods} from '@bpinc/ad-contacts-state-context'
import type {RecentItem} from '@bpinc/state-recent-types'


export function useContactData(recentsList: RecentItem[]) {

    const contacts = useContactsState('contacts')
    const {getContacts} = useContactsStateMethods()

    useEffect(() => {
        const ids: string[] = []
        let timeout: number | undefined = undefined

        recentsList.forEach(item => {
            const {contactId} = item
            if (contactId && !contacts[contactId] && !ids.includes(contactId)) {
                ids.push(contactId)
            }
        })

        if (ids.length) {
            timeout = setTimeout(() => getContacts(ids), 500)
            return () => clearTimeout(timeout)
        }

        // trigger on list change only
        // eslint-disable-next-line
    }, [recentsList]);

}
