import {useMemo} from 'react'

import {digitizePhone} from '@bpinc/ad-interaction-state-methods'
import {useCommunicatorUIState} from '@bpinc/lib-communicator-ui-context'
import {PhoneNumberUtilCountryCodeSource} from '@bpinc/lib-phonenumber-formatter'

import {usePhoneNumberFormatter} from './usePhoneNumberFormatter'


export function useFormatedPhone() {
    const {searchValue} = useCommunicatorUIState('dialpad')
    const formatPhoneNumber = usePhoneNumberFormatter()

    return useMemo(() => {
        const digitizedNumber = digitizePhone(searchValue)
        const {
            formattedNumber,
            meta = {success: false, countryCodeSource: 0},
        } = formatPhoneNumber(digitizedNumber)

        return {
            phoneNumber: formattedNumber,
            isValidPhoneNumber: meta.success,
            countryCodeSourceFromPrefix: meta.success ? meta.countryCodeSource === PhoneNumberUtilCountryCodeSource.FROM_DEFAULT_COUNTRY : true,
        }
    }, [searchValue, formatPhoneNumber])
}
