import {useLayoutEffect, useState} from 'react'


export function useCommunicatorContentHeight(isActive: boolean) {
    const [communicatorContentHeight, setCommunicatorContentHeight] = useState(0)

    useLayoutEffect(() => {
        if (isActive) {
            const communicatorContent = document.querySelector('[data-testid="communicator_content"]')
            const communicatorContentObserver = new ResizeObserver(([entry]) => {
                setCommunicatorContentHeight(entry.contentRect.height)
            })
            communicatorContent && communicatorContentObserver.observe(communicatorContent)

            return () => {
                communicatorContentObserver.disconnect()
            }
        }

    }, [isActive])

    return communicatorContentHeight
}
