import React from 'react'

import {useContact} from '@bpinc/ad-contacts-state-context'
import {useFormatting} from '@bpinc/ad-formatting-context'
import {formatPhone} from '@bpinc/ad-formatting-helpers'
import {
    isRecentCall as isCall,
    isRecentCallConference as isCallConference,
    isRecentChat as isChat,
    isRecentEmail as isEmail,
    isRecentExtChatConference as isChatConference,
    isRecentGroupChat as isGroupChat,
    isRecentWorkitem,
} from '@bpinc/ad-interaction-state-methods'
import {RecentFavoriteButton} from '@bpinc/lib-favorites-button'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {useLocalizationState} from '@bpinc/state-localization-context'
import type {RecentItem} from '@bpinc/state-recent-types'


type Props = {
    item: RecentItem
    repeatCount?: number
}

export const RecentItemTitle: React.FC<Props> = ({item, repeatCount}) => {

    const t = useLocalizationState('t')
    const {formatFullName} = useFormatting()

    const subTitleClassName = tcn('caption-s text-neutral-on-secondary')
    const titleClassName = tcn('font-semibold body text-primary-on-primary')

    const {data: contactData} = useContact(item.contactId) ?? {}

    const formattedPhone = item.number && formatPhone(item.number)
    const contactName = formatFullName(contactData?.firstName, contactData?.lastName)
    const displayName =
        contactName ||
        formatFullName(item.firstName, item.lastName) ||
        item.name ||
        formattedPhone ||
        t.GLOBAL.anonymous()

    let innerTitle

    const addRepeatCount = (text: string | undefined, count?: number): string | undefined => {
        return text && repeatCount ? `${text} (${count})` : text
    }

    if (isCallConference(item) || isChatConference(item)) {
        innerTitle = (
            <span className={titleClassName}>
                {`${t.RECENT.conference_title_text()} (${item.parties.length})`}
            </span>
        )

    } else if (isChat(item)) {
        const chatDisplayName = item.groupChatData ? item.groupChatData.title : displayName

        innerTitle = (
            <span className={titleClassName} data-testid="name">
                {chatDisplayName}
            </span>
        )

    } else if (isCall(item)) {
        innerTitle = (
            <span className={titleClassName} data-testid="name">
                {addRepeatCount(displayName, repeatCount)}
            </span>
        )

    } else if (isEmail(item)) {
        const emailDisplayName =
            formatFullName(item.firstName, item.lastName) ||
            item.email ||
            t.GLOBAL.unidentified()

        // do not show repeatCount for emails, because it can be just the number of times the agent opened the same email
        innerTitle = (
            <>
                <span className={subTitleClassName} data-testid="case-number">
                    {`#${item.caseNumber}`}
                </span>
                <span> </span>
                <span className={titleClassName} data-testid="name">
                    {emailDisplayName}
                </span>
            </>
        )

    } else if (isRecentWorkitem(item)) {
        innerTitle = (
            <span className={titleClassName} data-testid="name">
                {displayName}
            </span>
        )

    } else {
        /** This should not be reached in normal. Keep it to catch bugs in saved data */
        innerTitle = (
            <span className={titleClassName} data-testid="unknown-media">
                {`[Unknown media: ${item.media}]`}
            </span>
        )
    }

    return (
        <span className="flex gap-1">
            <span className="truncate">
                {innerTitle}
                {item.serviceName && (
                    <>
                        <span className="mx-1 inline-block h-[9px] border-l border-inverse" />
                        <span data-testid="service-name" className={subTitleClassName}>
                            {item.serviceName}
                        </span>
                    </>
                )}
            </span>
            {!isCallConference(item) && !isChatConference(item) && !isGroupChat(item) && (
                <RecentFavoriteButton forContactListItem item={item} />
            )}
        </span>
    )
}
