import type {SyntheticEvent} from 'react'
import React, {useCallback, useRef, useState} from 'react'
import {debounce} from 'lodash-es'

import {useContactsStateMethods} from '@bpinc/ad-contacts-state-context'
import type {ContactDetailsType, ContactShortType, WithId} from '@bpinc/ad-contacts-state-types'
import {bpLogger} from '@bpinc/ad-persistent-logger'
import type {SmDropdownItemProps} from '@bpinc/lib-external-ui-elements'
import {SmDropdown} from '@bpinc/lib-external-ui-elements'
import {getErrorMessage, useAsRef} from '@bpinc/lib-helpers'
import type {DropdownProps} from '@bpinc/lib-ui-elements'

import {ContactPreview} from './ContactPreview'


function getOption(contact: ContactShortType) {
    return {
        value: contact.id,
        as: (props: SmDropdownItemProps) => (
            <SmDropdown.Item {...props} className="!px-4 !py-1">
                <ContactPreview {...contact} />
            </SmDropdown.Item>
        ),
    }
}

export function useSearchContactState() {
    const [distContactDetails, setDistContactDetails] = useState<Partial<WithId<ContactDetailsType>>>()
    const [mergeFormPreparing, setMergeFormPreparing] = useState(false)
    const [searchQuery, setSearchQuery] = useState<string | undefined>('')
    const contactsStateMethods = useContactsStateMethods()
    const [contactsAreLoading, setContactsAreLoading] = useState(false)
    const [options, setOptions] = useState<SmDropdownItemProps[]>([])

    const searchQueryRef = useAsRef(searchQuery)

    const getContactsList = useCallback(
        function getContactsListCallback(searchQueryInner: string | undefined) {
            const isOutdated = (checkingSearchQuery: string | undefined) => searchQueryRef.current !== checkingSearchQuery

            if (isOutdated(searchQueryInner)) {
                setOptions([])
                setContactsAreLoading(false)
                return
            }

            contactsStateMethods
                .getContactList({query: searchQueryInner})
                .then(contacts => {
                    if (!contacts || isOutdated(searchQueryInner)) {
                        return
                    }

                    setOptions(contacts?.map(getOption))
                })
                .finally(() => {
                    setContactsAreLoading(false)
                })
        },
        [contactsStateMethods, searchQueryRef],
    )

    const debouncedGetContactsList = useRef(debounce(getContactsList, 300)).current

    const handleSearchChange = useCallback(
        function handleSearchChangeCallback(_evt: SyntheticEvent, {searchQuery: searchQueryInner}: DropdownProps) {
            if (searchQueryInner) {
                setContactsAreLoading(true)
                debouncedGetContactsList(searchQueryInner)
            } else {
                setOptions([])
            }

            setSearchQuery(searchQueryInner)
        },
        [debouncedGetContactsList],
    )

    const handleSelect = useCallback(
        function handleSelectCallback(_evt: SyntheticEvent, {value: contactId}: DropdownProps) {
            if (typeof contactId !== 'string') {
                return
            }

            setMergeFormPreparing(true)

            contactsStateMethods
                .getContactDetails({id: contactId, force: true})
                .then(selectedContact => {
                    if (selectedContact) {
                        setDistContactDetails({...selectedContact, id: contactId})
                    }
                })
                .catch(err => {
                    bpLogger.error(getErrorMessage(err))
                })
                .finally(() => {
                    setMergeFormPreparing(false)
                })
        },
        [contactsStateMethods],
    )

    const search = useCallback(() => {
        return contactsAreLoading || mergeFormPreparing ? [] : options
    }, [contactsAreLoading, mergeFormPreparing, options])

    return {
        mergeFormPreparing,
        distContactDetails,
        searchQuery,
        contactsAreLoading,
        options,
        methods: {
            search,
            handleSearchChange,
            handleSelect,
        },
    }
}
