// Communicator's root component rendered as a standalone application
import React from 'react'

import {createPlatformSpecific} from '@bpinc/ad-platform-specific-web-impl'
import {AuthorizationModule} from '@bpinc/lib-authorization-module'
import {createProviderWrapper} from '@bpinc/lib-helpers'

import {standaloneProviders} from '../constants'
import {App} from './App/App'
import {IntegrationApiAuthorizedInitializer} from './IntegrationApiAuthorizedInitializer'
import {IntegrationApiBaseInitializer} from './IntegrationApiBaseInitializer'


const CommunicatorStandaloneProviders = createProviderWrapper(standaloneProviders)

export const CommunicatorApp: React.FC = () => {
    return (
        <AuthorizationModule
            platformSpecific={createPlatformSpecific('adc')}
            app="adc"
            BaseConnectorsConsumer={IntegrationApiBaseInitializer}
            appRemoteName="communicator"
        >
            {(baseConnectors, authorizedConnectors) => (
                <CommunicatorStandaloneProviders>
                    <IntegrationApiAuthorizedInitializer
                        baseConnectors={baseConnectors}
                        authorizedConnectors={authorizedConnectors}
                    >
                        <App />
                    </IntegrationApiAuthorizedInitializer>
                </CommunicatorStandaloneProviders>
            )}
        </AuthorizationModule>
    )
}
