import {useMemo} from 'react'

import {
    isAfterCallWork,
    isCall,
    isCallConference,
    isChatConference,
    isPotentialInteraction,
    isWorkItem,
} from '@bpinc/ad-interaction-state-methods'
import type {Item} from '@bpinc/ad-interaction-state-types'
import {useLocalizationState} from '@bpinc/state-localization-context'


interface Output {
    text: string
    tooltip?: string
}

export function useCompleteButtonTexts(item?: Item): Output {
    const {t} = useLocalizationState()

    return useMemo(function memoizeOutput() {

        if (!item) {
            return {
                text: '',
            }
        }

        if (isPotentialInteraction(item)) {
            return {
                text: t.ACTIVITY_HISTORY.button_close_tooltip(),
            }
        }
        if (isAfterCallWork(item) || isWorkItem(item)) {
            return {
                text: t.CALL_DETAILS_FORM.interaction_completeACW(),
                tooltip: t.GLOBAL.complete_interaction(),
            }
        }

        if (isChatConference(item)) {
            return {
                text: t.CALL_DETAILS_FORM.interaction_leaveConference(),
            }
        }

        if (isCallConference(item)) {
            return {
                text: item.isConferenceHost
                    ? t.CALL_DETAILS_FORM.interaction_endConference()
                    : t.CALL_DETAILS_FORM.interaction_leaveConference(),
            }
        }

        if (isCall(item)) {
            return {text: t.CALL_DETAILS_FORM.interaction_endCall()}
        }

        return {text: t.CALL_DETAILS_FORM.interaction_endChat()}

    }, [item, t])
}
