import {useMemo} from 'react'

import {useDirectoryState} from '@bpinc/ad-directory-state-context'
import type {DirectoryEntry} from '@bpinc/ad-directory-state-types'


export function useCategoryItems(searchQuery?: string) {
    const categoryEntries = useDirectoryState('categoryEntries')

    return useMemo(() => {
        const categoryItemsInner: DirectoryEntry[] = []

        Object.values(categoryEntries).map((list: DirectoryEntry[]) => {
            list.forEach(item => {
                if (!searchQuery || item.number?.includes(searchQuery)) {
                    categoryItemsInner.push(item)
                }
            })
        })

        return categoryItemsInner
    }, [categoryEntries, searchQuery])
}
