import type {IntegrationApiStateTrackerEvents} from '@bpinc/ad-integration-api-state-types'
import type {EventDispatch} from '@bpinc/lib-typed-events'
import {assertDefined} from '@bpinc/lib-types-helpers'
import {uuid} from '@bpinc/lib-uuid'


type PendingRequest<Params = any, Ret = any> = {
    resolve: (data: Ret) => void
    reject: (error: string) => void
    payload: Params
    id: string
}

type ApiDispatcher = Omit<EventDispatch<IntegrationApiStateTrackerEvents>, 'onInitialState' | 'onChange'>

type PendingRequests = {
    [Key in keyof ApiDispatcher]?: PendingRequest[]
}

class ApiRequestTracker {

    private readonly pendingRequests: PendingRequests = {}

    constructor(private timeout: number) {}

    public newPendingRequest<Key extends keyof ApiDispatcher>(
        requestType: Key,
        payload: Parameters<ApiDispatcher[Key]>,
    ): Promise<ReturnType<ApiDispatcher[Key]> extends (...args: any[]) => Promise<infer R> ? R : void> {
        return new Promise((resolve, reject) => {
            if (!this.pendingRequests[requestType]) {
                this.pendingRequests[requestType] = []
            }
            const pendingRequest = {
                id: uuid(),
                resolve,
                reject,
                payload,
            }
            const requestsOfType = this.pendingRequests[requestType]
            assertDefined(requestsOfType)
            requestsOfType.push(pendingRequest)

            setTimeout(() => {
                const delIndex = requestsOfType.indexOf(pendingRequest)
                if (delIndex !== -1) {
                    reject(
                        'Agent Desktop is unable to complete your ' +
                        `request in the specified timeout ${this.timeout} ms`,
                    )
                }
            }, this.timeout)
        })
    }

    public resolvePendingRequest<Key extends keyof ApiDispatcher>(
        requestType: Key,
        data: ReturnType<ApiDispatcher[Key]> extends Promise<infer T> ? T : never,
        requestId: string,
    ): void {
        const requestData = (this.pendingRequests[requestType]! || []).find(req => req.id === requestId)
        if (!requestData) {
            return
        }
        const delIndex = this.pendingRequests[requestType]!.indexOf(requestData)
        if (delIndex !== -1) {
            requestData.resolve(data)
            this.pendingRequests[requestType]!.splice(delIndex, 1)
        }
    }

    public rejectPendingRequest<Key extends keyof ApiDispatcher>(
        requestType: Key,
        error: string,
        requestId: string,
    ): void {
        const requestsOfType = this.pendingRequests[requestType]
        const requestData = requestsOfType?.find(req => req.id === requestId)
        if (!requestsOfType || !requestData) {
            return
        }
        const delIndex = requestsOfType.indexOf(requestData)
        if (delIndex !== -1) {
            requestData.reject(error)
            requestsOfType.splice(delIndex, 1)
        }
    }

    public resolveAllPendingRequests<Key extends keyof ApiDispatcher>(
        requestType: Key,
        data: ReturnType<ApiDispatcher[Key]> extends Promise<infer T> ? T : never,
    ): void {
        this.pendingRequests[requestType]?.forEach(requestData => {
            this.resolvePendingRequest(requestType, data, requestData.id)
        })
    }

    public rejectAllPendingRequests<Key extends keyof ApiDispatcher>(
        requestType: Key,
        error: string,
    ): void {
        this.pendingRequests[requestType]?.forEach(requestData => {
            this.rejectPendingRequest(requestType, error, requestData.id)
        })
    }
}

function createApiRequestTracker(timeout: number) {
    return new ApiRequestTracker(timeout)
}

export {createApiRequestTracker}
