import React, {useCallback, useEffect} from 'react'
import {noop} from 'lodash-es'

import {useRescueState, useRescueStateMethods} from '@bpinc/ad-rescue-state-context'
import {SmButton, SmMessage} from '@bpinc/lib-external-ui-elements'
import {IconInsert} from '@bpinc/lib-icons'
import {Modal} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'
import type {CommonModalProps} from '@bpinc/state-modals-types'

import styles from '../styles/Rescue.module.less'


const getLink = (type: 'desktop' | 'mobile', code?: string): string => {
    const base = type === 'desktop'
        ? 'https://oty.com/?code='
        : 'http://RescueMobile.com/'

    return base + code
}


type RescueModalProps = CommonModalProps & {
    type: 'voice' | 'chat'
    onLinkPaste?: (link: string) => void
}

export const RescueModal: React.FC<RescueModalProps> = ({type, onLinkPaste = noop, onClose, ...other}) => {
    const {t} = useLocalizationState()

    const getErrorText = useCallback((errorCode: string) => {
        switch (errorCode) {
            case 'NOTECHCONSOLERUNNING': return t.ERROR_MESSAGES.rescue_modal_no_tech_console_running()
            default: return t.ERROR_MESSAGES.rescue_modal_unknown_error()
        }
    }, [t.ERROR_MESSAGES])

    const {pinCode, errorCode} = useRescueState()
    const {requestPinCode} = useRescueStateMethods()
    const links = {
        desktop: getLink('desktop', pinCode),
        mobile: getLink('mobile', pinCode),
    }

    const handleGenerateNewPinClick = requestPinCode
    const handleDesktopPaste = useCallback(() => {
        onLinkPaste(links.desktop)
    }, [links.desktop, onLinkPaste])
    const handleMobilePaste = useCallback(() => {
        onLinkPaste(links.mobile)
    }, [links.mobile, onLinkPaste])

    useEffect(() => {
        requestPinCode()
        // componentDidMount
        // eslint-disable-next-line
    }, [])

    return (
        <Modal
            onClose={onClose}
            aria-labelledby="remoteAssistDialogHeader"
            {...other}
        >
            <Modal.Header
                role="heading"
                id="remoteAssistDialogHeader"
            >
                {t.REMOTE_ASSIST_DIALOG_BOX.remote_assist_heading()}
            </Modal.Header>

            <Modal.Content className={styles.content}>
                <Modal.Description>
                    {errorCode && <SmMessage error>{getErrorText(errorCode)}</SmMessage>}

                    <div className={styles.pin}>
                        {pinCode || '------'}
                    </div>

                    {type === 'chat' && (
                        <div className={styles.links}>
                            <table>
                                <tbody>
                                    <tr>
                                        <td>
                                            {t.REMOTE_ASSIST_DIALOG_BOX.remote_assist_paste_into_chat_text_desktop()}
                                        </td>
                                        <td>
                                            <a href={links.desktop} target="_blank" rel="noopener noreferrer">
                                                {links.desktop}
                                            </a>
                                        </td>
                                        <td>
                                            <IconInsert onClick={handleDesktopPaste} />
                                        </td>
                                    </tr>
                                    <tr>
                                        <td>
                                            {t.REMOTE_ASSIST_DIALOG_BOX.remote_assist_paste_into_chat_text_mobile()}
                                        </td>
                                        <td>
                                            <a href={links.mobile} target="_blank" rel="noopener noreferrer">
                                                {links.mobile}
                                            </a>
                                        </td>
                                        <td>
                                            <IconInsert onClick={handleMobilePaste} />
                                        </td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                    )}

                    <div className={styles.manualLink}>
                        {t.REMOTE_ASSIST_DIALOG_BOX.remote_assist_manual_url_text()}
                        {t.REMOTE_ASSIST_DIALOG_BOX.remote_assist_manual_url_address()}
                    </div>

                    <SmButton onClick={handleGenerateNewPinClick} basic>
                        {t.REMOTE_ASSIST_DIALOG_BOX.remote_assist_create_another_pin_button()}
                    </SmButton>
                </Modal.Description>
            </Modal.Content>
            <Modal.Actions>
                <SmButton onClick={() => onClose()}>
                    {t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_buttonClose()}
                </SmButton>
            </Modal.Actions>
        </Modal>
    )
}
