import React from 'react'

import {tcn} from '@bpinc/lib-tailwind-classnames'


type UnreadProps = {
    unreadAmount: number
    customerWaitTimer: number | undefined
    showTimerProgressBar: boolean
}

export const Unread: React.FC<UnreadProps> = ({unreadAmount, customerWaitTimer, showTimerProgressBar}) => {
    const isActive = {'bg-primary-700': Boolean(customerWaitTimer)}

    const textAmount = unreadAmount > 99 ? '99+' : unreadAmount

    const width = `${customerWaitTimer ? (customerWaitTimer + 10) : 100}%`

    if (unreadAmount < 1) return null

    return (
        <div className="relative ml-1.5 size-4">
            <div
                data-testid="unread-counter"
                className={tcn(
                    'flex size-full items-center justify-center rounded-full text-icon bg-warning',
                    isActive,
                )}
            >
                <span className="select-none caption-xs text-inverse-primary">
                    {textAmount}
                </span>
            </div>
            {showTimerProgressBar && (
                <div className="absolute -bottom-1.5 -left-0.5 m-auto h-[3px] w-5 rounded-full bg-primary-container-variant2">
                    <div
                        className={tcn('h-[3px] rounded-full bg-warning', isActive)}
                        style={{width}}
                    />
                </div>
            )}
        </div>
    )
}
