import React from 'react'

import {Attachment} from '@bpinc/lib-jskit-ui'


interface AttachmentFileMessageProps {
    fileName: string
    fileLink: string
}

export const AttachmentFileMessage = React.forwardRef<HTMLDivElement, AttachmentFileMessageProps>(({
    fileLink,
    fileName,
}, attachmentRef) => {

    return (
        <div ref={attachmentRef}>
            <Attachment.File height="44" name={fileName} link={fileLink} withDownloadButton />
        </div>
    )
})
