import React from 'react'

import type {Item} from '@bpinc/ad-interaction-state-types'
import {tcn} from '@bpinc/lib-tailwind-classnames'

import {useDisplayContent} from './hooks/useDisplayContent'


type DisplayTextProps = {
    item: Item
}

export const DisplayText: React.FC<DisplayTextProps> = ({item}) => {
    const {name, service} = useDisplayContent(item)

    return (
        <>
            {name && (
                <div
                    className={tcn(
                        'shrink-0 truncate font-semibold caption-s text-primary-on-primary',
                        {'max-w-[62%]': Boolean(service)},
                    )}
                    data-testid="display-name"
                >
                    {name}
                </div>
            )}
            {service && (
                <div
                    className="ml-1 flex-auto truncate caption-s text-neutral-fade"
                    data-testid="service-name"
                >
                    {service}
                </div>
            )}
        </>
    )
}
