import {useCallback} from 'react'

import {useInteractionChatStateMethods} from '@bpinc/ad-interaction-chat-state-context'
import type {ChatMessage} from '@bpinc/ad-interaction-message-types'


export function useHandleCancelInviteClick(message: ChatMessage) {
    const {itemId, partyId, inviteId} = message
    const {removeInviteFromChat} = useInteractionChatStateMethods()

    return useCallback(
        function handleCancelClickCallback() {
            removeInviteFromChat({
                itemId: itemId,
                partyId: partyId,
                messageId: inviteId || '',
            })
        },
        [inviteId, itemId, partyId, removeInviteFromChat],
    )

}
