import React from 'react'

import {getMessageId} from '../../../../../helpers'
import type {ActivityMessageGroup as ActivityMessageGroupType} from '../../../../../types'
import {WithHoverOverActions} from '../../../ChatMessagesActions'
import {WithOptionalNameAndAvatar} from '../WithOptionalNameAndAvatar'
import {DispositionMessage} from './DispositionMessage'
import {NotesMessage} from './NotesMessage'
import {useDispositionTitle} from './useDispositionTitle'


type ActivityMessageGroupProps = {
    message: ActivityMessageGroupType
}

export const ActivityMessageGroup: React.FC<ActivityMessageGroupProps> = ({message}) => {
    const {notes, disposition, serviceName} = message
    const messageId = getMessageId(message)

    const dispositionTitle = useDispositionTitle(message)

    return (
        <div>
            {notes && (
                <WithOptionalNameAndAvatar isFullWidth message={message}>
                    <WithHoverOverActions messageId={messageId} className="w-full">
                        <NotesMessage>{notes}</NotesMessage>
                    </WithHoverOverActions>
                </WithOptionalNameAndAvatar>
            )}
            {disposition && (
                <WithOptionalNameAndAvatar isFullWidth message={message}>
                    <DispositionMessage
                        messageId={messageId}
                        title={dispositionTitle}
                        disposition={disposition}
                        serviceName={serviceName}
                    />
                </WithOptionalNameAndAvatar>
            )}
        </div>
    )
}
