import type {FC} from 'react'
import React from 'react'

import {CircleButton, IconForward16} from '@bpinc/lib-jskit-ui'
import {useLocalizationState} from '@bpinc/state-localization-context'

import type {ControlButton} from './types'


export const TransferButton: FC<ControlButton> = ({isActive, onClick, disabled, iconClassName, tooltip}) => {
    const {t} = useLocalizationState()
    return (
        <CircleButton
            isActive={isActive}
            onClick={onClick}
            disabled={disabled}
            tooltip={{content: tooltip || t.CALL_DETAILS_FORM.interaction_initiate_transfer()}}
            data-testid="transfer-button"
            data-on-hold={isActive || undefined}
        >
            <IconForward16 className={iconClassName} />
        </CircleButton>
    )
}
