import React, {useMemo} from 'react'
import {Button} from '@servicepattern/ui'

import type {DirectoryEntry} from '@bpinc/ad-directory-state-types'
import {isEmailServiceEntry, isScenarioChatEntry} from '@bpinc/ad-directory-state-types'
import {useActivityHistoryOpener, useCallHandler} from '@bpinc/ad-interaction-state-hooks'
import {useSessionState} from '@bpinc/ad-session-context'
import type {FavoritesChannelMedia} from '@bpinc/ad-transport-types'
import {DirectoryContactChannelFavoriteButton} from '@bpinc/lib-favorites-button'
import {useEvent} from '@bpinc/lib-helpers'
import {IconPhone24} from '@bpinc/lib-jskit-ui'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import type {FavoritesChannel} from '@bpinc/state-favorites-types'
import {useLocalizationState} from '@bpinc/state-localization-context'


type Props = {
    item: DirectoryEntry
}

export const DirectoryEntryControls: React.FC<Props> = ({item}) => {
    const t = useLocalizationState('t')
    const {phoneDeviceCapabilities} = useSessionState()
    const onCall = useCallHandler()
    const openActivityHistory = useActivityHistoryOpener()

    const handleCall = useEvent(() => {
        onCall({
            phone: item.number,
            userId: item.id,
            lastName: item.title,
            navigateToInteractionsPage: true,
            origination: 'contact-profile',
        })
    })

    const handlePhoneChannelInteraction = useEvent((number: string, media: FavoritesChannelMedia) => {
        if (media === 'voice') {
            handleCall()
        } else {
            openActivityHistory({
                phone: number,
                userId: item.id,
                lastName: item.title,
                origination: 'contact-profile',
            })
        }
    })

    const favoritesButton = useMemo(() => {
        let channel: FavoritesChannel | undefined
        if (isScenarioChatEntry(item)) {
            channel = {channelType: 'internal', media: ['chat']}
        }
        if (isEmailServiceEntry(item)) {
            channel = {channelType: 'email', email: ''}
        }
        if (channel) {
            return (
                <DirectoryContactChannelFavoriteButton
                    item={item}
                    channel={channel}
                    onPhoneInteraction={handlePhoneChannelInteraction}
                />
            )
        }
        return null
    }, [handlePhoneChannelInteraction, item])

    return (
        <div className={tcn('-mt-1 ml-auto flex flex-none items-center', {'ml-3 mt-1': !!favoritesButton})}>
            {item.mediaType === 'voice' && phoneDeviceCapabilities.canDial && (
                <Button
                    onClick={handleCall}
                    tooltip={t.CONTACT.button_start_call_text()}
                    data-testid="call-button"
                    variant="ghost"
                    icon={<IconPhone24 />}
                />
            )}
            {favoritesButton}
        </div>
    )
}
