import React from 'react'
import {useContext} from 'use-context-selector'

import type {FormDefinitionFieldLabel} from '@bpinc/ad-request-urls'
import {parseTemplateToString} from '@bpinc/lib-form-builder'

import {FormBuilderContext} from '../../FormBuilder'


export const LabelField: React.FC<{
    field: FormDefinitionFieldLabel
}> = ({
    field,
}) => {
    const {runtimeVariables} = useContext(FormBuilderContext)
    const label = parseTemplateToString(field.value || '', runtimeVariables)
    return <h4>{label}</h4>
}
