import {useCallback} from 'react'

import {useFormatting} from '@bpinc/ad-formatting-context'
import type {ChatUser} from '@bpinc/state-group-chat-types'


export function useGroupChatMembersToString() {
    const {formatFullName} = useFormatting()

    return useCallback((members: ChatUser[]) => {
        return members
            .map(member => formatFullName(member.firstName, member.lastName))
            .join(', ')
    }, [formatFullName])
}
