import React from 'react'

import {useLocalizationState} from '@bpinc/state-localization-context'

import {useInteractionsCount} from '../hooks/useInteractionsCount'

import styles from '../styles/InteractionBar.module.less'


export const InteractionsCount: React.FC = () => {
    const {t} = useLocalizationState()
    const interactionsCount = useInteractionsCount()

    return (
        <>
            {!!interactionsCount.call && (
                <span className={styles.desc}>
                    <span>{t.GLOBAL.interaction_bar_calls_label()}</span>
                    <span>: </span>
                    <span data-testid="calls-number">{interactionsCount.call}</span>
                </span>
            )}
            {!!interactionsCount.chat && (
                <span className={styles.desc}>
                    <span>{t.GLOBAL.interaction_bar_chats_label()}</span>
                    <span>: </span>
                    <span data-testid="chats-number">{interactionsCount.chat}</span>
                </span>
            )}
            {!!interactionsCount.history && (
                <span className={styles.desc}>
                    <span>{t.GLOBAL.interaction_bar_contact_activities_label()}</span>
                    <span>: </span>
                    <span data-testid="contact-histories-number">{interactionsCount.history}</span>
                </span>
            )}
        </>
    )
}
