import type {CSSProperties} from 'react'
import React from 'react'


export const ViewItem: React.FC<{
    label?: JSX.Element | string
    value?: JSX.Element | string
    viewStyle?: CSSProperties
}> = ({
    label,
    value,
    viewStyle,
}) => {

    return (
        <div className="flex w-full items-center gap-x-[--form-cellPadding] gap-y-[--form-rowHeight] overflow-hidden">
            <span className="w-[--form-fieldLabelWidth] min-w-[--form-fieldLabelWidth] max-w-[--form-fieldLabelWidth]">
                {label}
            </span>

            <div
                className="flex-auto"
                style={viewStyle}
            >
                {value}
            </div>
        </div>
    )
}
