import type {CSSProperties} from 'react'
import React, {useCallback} from 'react'
import type {FieldValues, UseFieldArrayRemove} from 'react-hook-form'

import type {ServerAddressType} from '@bpinc/ad-contacts-state-types'
import type {FieldArrayControlProps, FieldArrayPathByValue} from '@bpinc/lib-forms-base'
import {IconAddAddress} from '@bpinc/lib-icons'

import {AddressFieldArrayContent} from './AddressFieldArrayContent'
import {FieldArray} from './FieldArray'


type AddressFieldType = Partial<ServerAddressType>

const INITIAL_VALUES: AddressFieldType = {
    address_line1: '',
    address_line2: '',
    city: '',
    postcode: '',
}

type AddressFieldArrayProps = {
    className?: string
    typeClassName?: string
    layoutClassName?: string
    style?: CSSProperties
}

export const AddressFieldArray = <
    TValues extends FieldValues,
    TFieldName extends FieldArrayPathByValue<TValues, AddressFieldType>,
>({
    name,
    style,
    control,
    className,
    typeClassName,
    layoutClassName,
}: FieldArrayControlProps<AddressFieldType, TValues, TFieldName> & AddressFieldArrayProps) => {
    const renderComponents = useCallback(
        function renderComponentsCallback(
            index: number,
            remove: UseFieldArrayRemove,
        ) {
            return (
                <AddressFieldArrayContent
                    style={style}
                    layoutClassName={layoutClassName}
                    typeClassName={typeClassName}
                    remove={remove}
                    name={name}
                    control={control}
                    index={index}
                />
            )
        },
        [style, layoutClassName, control, name, typeClassName],
    )

    return (
        <FieldArray
            className={className}
            layoutClassName={layoutClassName}
            control={control}
            name={name}
            initialValues={INITIAL_VALUES}
            renderComponents={renderComponents}
            AddButtonIcon={IconAddAddress}
        />
    )
}
