import React from 'react'

import type {CommonSlidingModalItemProps} from '@bpinc/lib-sliding-modal-context'
import {FocusTrap, SlidingModal} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

//TODO: Move RescheduleForm closer to components that use it?
import {RescheduleForm} from '../../reschedule'


const rescheduleSlidingModalContent = (
    <FocusTrap className="flex flex-col items-start text-left">
        <RescheduleForm />
    </FocusTrap>
)

export const RescheduleSlidingModal: React.FC<CommonSlidingModalItemProps> = props => {
    const {t} = useLocalizationState()

    return (
        <SlidingModal
            name="reschedule"
            focusTrap={false}
            header={t.CALL_DETAILS_FORM.button_schedule_followup_label()}
            {...props}
        >
            {rescheduleSlidingModalContent}
        </SlidingModal>
    )
}
