import React, {useCallback, useEffect, useState} from 'react'
import {throttle} from 'lodash-es'

import {useInteractionChatStateConnector} from '@bpinc/ad-interaction-chat-state-context'
import {useInteractionProp} from '@bpinc/ad-interaction-state-context'
import {getInteractionStepId} from '@bpinc/ad-interaction-state-methods'
import {SmIcon} from '@bpinc/lib-external-ui-elements'
import {Button} from '@bpinc/lib-ui-elements'
import {useInteractionUIChatScrollerApi} from '@bpinc/state-interaction-ui'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {isScrollAtBottom} from '../../../helpers'
import {useFirstUnreadMessage} from '../../../hooks/useFirstUnreadMessage'
import type {ChatScrollNodeType} from '../../../types'


type ScrollToFirstUnreadBadgeProps = {
    itemId: string
    scrollNode: ChatScrollNodeType
}

export const ScrollToFirstUnreadBadge: React.FC<ScrollToFirstUnreadBadgeProps> = ({itemId, scrollNode}) => {
    const {t} = useLocalizationState()
    const [unreadMessagesClicked, setUnreadMessagesClicked] = useState(true)
    const [scrollAtBottom, setScrollAtBottom] = useState(false)
    const scrollerApi = useInteractionUIChatScrollerApi(itemId)
    const firstUnreadMessageId = useFirstUnreadMessage(itemId)
    const interactionStepId = useInteractionProp(itemId, getInteractionStepId)
    const interactionChatStateConnector = useInteractionChatStateConnector()

    useEffect(() => {
        const subscription = interactionChatStateConnector?.dispenser.subscribe({
            onNewMessage: newMessage => {
                if (newMessage.item_id === interactionStepId) {
                    setUnreadMessagesClicked(false)
                }
            },
        })
        return () => {
            subscription?.unsubscribe()
        }
    }, [interactionChatStateConnector?.dispenser, interactionStepId])

    useEffect(() => {
        const scrollAtBottomThrottled = throttle(() => {
            setScrollAtBottom(isScrollAtBottom(scrollNode))
        }, 500)
        scrollNode?.addEventListener('scroll', scrollAtBottomThrottled)
        return () => {
            scrollNode?.removeEventListener('scroll', scrollAtBottomThrottled)
        }
    }, [scrollNode])

    const handleClick = useCallback(
        function handleClickCallback() {
            if (!firstUnreadMessageId) {
                return
            }

            setUnreadMessagesClicked(true)
            scrollerApi?.scrollTo(firstUnreadMessageId, 'end')
        }, [firstUnreadMessageId, scrollerApi],
    )


    if (!firstUnreadMessageId || unreadMessagesClicked || scrollAtBottom) {
        return null
    }

    return (
        <Button
            enabledInOffline
            transparent
            onClick={handleClick}
            className="absolute bottom-4 left-4 z-10 h-8 items-center
            rounded border border-solid border-primary-legacy bg-white px-6 text-primary-legacy
            hover:border-primary-hover hover:text-primary-hover
            active:border-primary-active active:text-primary-active"
        >
            {t.CHAT_CENTER.unread_messages_button_text()}
            <SmIcon name="caret down" className="leading-[16px]" />
        </Button>
    )
}
