import {useCallback, useMemo} from 'react'
import {useAtom, useAtomValue, useSetAtom} from 'jotai'

import {useDIDNumberState} from '@bpinc/ad-did-number-context'
import {useErrorValidation} from '@bpinc/ad-error-hooks'
import {useIntegrationApiStateRef} from '@bpinc/ad-integration-api-state-context'
import {useActiveInteractionProp, useInteractionStateMethods} from '@bpinc/ad-interaction-state-context'
import {useActivityHistoryOpener} from '@bpinc/ad-interaction-state-hooks'
import {digitizePhone} from '@bpinc/ad-interaction-state-methods'
import type {InitiateCall} from '@bpinc/ad-interaction-state-types'
import {useLocalSettings} from '@bpinc/ad-local-settings-context'
import {useServiceState} from '@bpinc/ad-service-context'
import {AppRoutes, useNavigationState} from '@bpinc/lib-communicator-navigation-state'
import {useCommunicatorUIState} from '@bpinc/lib-communicator-ui-context'
import {getInputCaretPos, setInputCaretPos} from '@bpinc/lib-input-utils'
import {useNotificationsState} from '@bpinc/lib-notifications-context'
import {assertDefined} from '@bpinc/lib-types-helpers'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {isPadOpenAtom, phoneInputAtom, selectedItemIdAtom} from '../atoms'
import {isCategoryItem, isInternalContact, sanitizePhone} from '../helpers'
import {useQueryContacts} from './useQueryContacts/useQueryContacts'
import {usePhoneNumberFormatter} from './usePhoneNumberFormatter'


let scheduledSetInputCaretPos: {
    input: HTMLInputElement
    pos: number[]
} | null = null

export type InitiateChatAndCallOptionalParams = {
    userId?: string
    contactId?: string
    firstName?: string
    lastName?: string
}

export type DialpadHandlersType = ReturnType<typeof useHandlers>

export const useHandlers = () => {
    const [isPadOpen, setIsPadOpen] = useAtom(isPadOpenAtom)
    const setSelectedItemId = useSetAtom(selectedItemIdAtom)
    const {contacts} = useQueryContacts()

    const {searchValue, setSearchValue} = useCommunicatorUIState('dialpad')

    const phoneInput = useAtomValue(phoneInputAtom)

    const {selectedService} = useServiceState()
    const {selectedDIDNumber} = useDIDNumberState()
    const interactionStateMethods = useInteractionStateMethods()
    const openActivityHistory = useActivityHistoryOpener()
    const integrationApiStateRef = useIntegrationApiStateRef()
    const setActivePage = useNavigationState('setActivePage')
    const {showNotification, hideNotificationsByTag} = useNotificationsState('methods')
    const errorValidation = useErrorValidation()
    const {t} = useLocalizationState()
    const formatPhoneNumber = usePhoneNumberFormatter()
    const localSettings = useLocalSettings()

    const toggleDialpad = useCallback((open: boolean) => {
        setIsPadOpen(open)
    }, [setIsPadOpen])

    const closeDialpad = useCallback(() => toggleDialpad(false), [toggleDialpad])
    const openDialpad = useCallback(() => toggleDialpad(true), [toggleDialpad])

    const errorMessages = useMemo(
        () => ({
            noNumber: t.DIAL_PAD.error_empty_number,
        }),
        [t],
    )

    const onInputValueChange = useCallback(
        (value: string, id?: string) => {
            setSelectedItemId(id)
            hideNotificationsByTag('interaction-init')
            if (!isPadOpen) {
                openDialpad()
            }
            setSearchValue(value)
        },
        [setSelectedItemId, hideNotificationsByTag, isPadOpen, openDialpad, setSearchValue],
    )

    const firstContact = contacts[0]
    const contactsLength = contacts.length

    const initiateInteractionParams: InitiateChatAndCallOptionalParams = useMemo(
        function memoizeFirstContactId() {
            if (contactsLength !== 1) {
                return {}
            }
            if (isCategoryItem(firstContact)) {
                if (firstContact.number === searchValue) {
                    return {userId: firstContact.id, firstName: firstContact.title}
                } else {
                    return {}
                }
            }
            if (isInternalContact(firstContact)) {
                if (firstContact.extension === searchValue) {
                    return {
                        userId: firstContact.userId,
                        firstName: firstContact.firstName,
                        lastName: firstContact.lastName,
                    }
                } else {
                    return {}
                }
            }
            if (firstContact.phone === searchValue) {
                return {
                    contactId: firstContact.id,
                    firstName: firstContact.firstName,
                    lastName: firstContact.lastName,
                }
            } else {
                return {}
            }
        },
        [contactsLength, firstContact, searchValue],
    )

    const updateCaretPosition = useCallback((value?: string) => {
        if (value) {
            if (phoneInput) {
                const [start, end] = getInputCaretPos(phoneInput)
                const newCaretPos = start + value.length

                scheduledSetInputCaretPos = {
                    input: phoneInput,
                    pos: [newCaretPos, newCaretPos],
                }

                return {start, end}
            }
        } else {
            return {start: 0, end: 0}
        }
    }, [phoneInput])

    const setValueIntoCaretPosition = useCallback(
        (value?: string) => {
            if (!value) {
                return false
            }
            if (!phoneInput) {
                onInputValueChange(searchValue + value)
                return true
            }
            const position = updateCaretPosition(value)

            if (!position) {
                return
            }

            const newValue = sanitizePhone(
                searchValue.slice(0, position.start) + value + searchValue.slice(position.end),
            )

            onInputValueChange(newValue)

            requestAnimationFrame(() => {
                if (scheduledSetInputCaretPos) {
                    setInputCaretPos(scheduledSetInputCaretPos.input, scheduledSetInputCaretPos.pos)
                }
            })

            return searchValue !== newValue
        },
        [onInputValueChange, searchValue, updateCaretPosition, phoneInput],
    )

    const callId = useActiveInteractionProp(activeItem => activeItem?.callData?.callId)

    const isDeliveredCall = useActiveInteractionProp(
        activeItem => activeItem?.state === 'delivered' && activeItem?.mediaType === 'voice',
    )

    const onButtonClick = useCallback((value: string) => {
        hideNotificationsByTag('interaction-init')
        setValueIntoCaretPosition(value)

        if (isDeliveredCall && callId) {
            interactionStateMethods.sendDtmf({
                dtmf: value.trim(),
                callId,
            })
        }
    }, [
        callId,
        hideNotificationsByTag,
        interactionStateMethods,
        isDeliveredCall,
        setValueIntoCaretPosition,
    ])

    const initiateChatWith = useCallback(
        function chatContactCallback(
            phone: string,
            origination: 'dialpad' | 'dialpad-search' | 'integration-api',
            {contactId, userId, firstName, lastName}: InitiateChatAndCallOptionalParams = {},
        ) {
            hideNotificationsByTag('interaction-init')

            const digitizedNumber = digitizePhone(phone)
            const {formattedNumber} = formatPhoneNumber(digitizedNumber)

            if (userId || digitizedNumber) {
                openActivityHistory({
                    contactId,
                    userId,
                    phone: formattedNumber,
                    firstName,
                    lastName,
                    origination,
                })
                setSearchValue('')
            }
        },
        [setSearchValue, hideNotificationsByTag, formatPhoneNumber, openActivityHistory],
    )

    const restoreLastDialedNumber = useCallback(
        function restoreLastDialedNumberCallback() {
            localSettings.get('adLastCalledNumber').then(phone => {
                if (phone) {
                    setSearchValue(phone)
                }
            })
        },
        [localSettings, setSearchValue],
    )

    const onChatClick = useCallback(() => {
        if (searchValue) {
            initiateChatWith(searchValue, 'dialpad', initiateInteractionParams)
        } else {
            restoreLastDialedNumber()
        }
    },
    [searchValue, initiateChatWith, initiateInteractionParams, restoreLastDialedNumber])

    const callNumber = useCallback(
        function callNumberCallback(
            number: string,
            origination: 'dialpad' | 'dialpad-search' | 'integration-api',
            initiateCallParams?: Omit<InitiateCall, 'phone' | 'origination'>,
        ) {
            const errors = [
                ...errorValidation.getNumberErrors({
                    number: searchValue || number,
                    errorMessages,
                }),
                ...errorValidation.getServiceAndDIDNumbersErrors({
                    skip: ['noChatInService'],
                    interactionType: 'call',
                }),
            ]
            if (errors.length) {
                showNotification(errors[0])
                return
            }
            hideNotificationsByTag('interaction-init')

            if (number) {
                const digitizedNumber = digitizePhone(number)
                const {formattedNumber} = formatPhoneNumber(digitizedNumber)
                assertDefined(formattedNumber)

                const deferredDialData = integrationApiStateRef.current.deferredDialData
                const associatedRecords =
                    deferredDialData?.number === number && deferredDialData?.objects.length
                        ? deferredDialData.objects
                        : initiateCallParams?.associatedRecords

                interactionStateMethods.initiateCall({
                    ...initiateCallParams,
                    phone: formattedNumber,
                    serviceId: selectedService,
                    accessNumberId: selectedDIDNumber,
                    associatedRecords,
                    origination,
                })
                setActivePage({name: AppRoutes.Interactions})
                setSearchValue('')
            }
        },
        [
            errorValidation,
            searchValue,
            errorMessages,
            formatPhoneNumber,
            hideNotificationsByTag,
            showNotification,
            integrationApiStateRef,
            interactionStateMethods,
            selectedService,
            selectedDIDNumber,
            setActivePage,
            setSearchValue,
        ],
    )


    const onCallClick = useMemo(() => {
        return () =>
            searchValue
                ? callNumber(searchValue, 'dialpad', initiateInteractionParams)
                : restoreLastDialedNumber()
    }, [callNumber, initiateInteractionParams, searchValue, restoreLastDialedNumber])

    const onContactSelect = useCallback(
        (params: {id: string; phone: string}) => {
            const {id, phone} = params

            openDialpad()
            onInputValueChange(phone, id)

            const newPos = phone.length
            if (phoneInput) {
                phoneInput.focus()
                scheduledSetInputCaretPos = {input: phoneInput, pos: [newPos, newPos]}
            }
        },
        [openDialpad, onInputValueChange, phoneInput],
    )

    return useMemo(
        function memoizeOutput() {
            return {
                callNumber,
                closeDialpad,
                initiateChatWith,
                onButtonClick,
                onCallClick,
                onChatClick,
                onContactSelect,
                onInputValueChange,
                openDialpad,
                updateCaretPosition,
            }
        },
        [
            callNumber,
            closeDialpad,
            initiateChatWith,
            onButtonClick,
            onCallClick,
            onChatClick,
            onContactSelect,
            onInputValueChange,
            openDialpad,
            updateCaretPosition,
        ],
    )
}
