import React from 'react'

import {formatPhone} from '@bpinc/ad-formatting-helpers'

import {usePhoneClickHandler} from './usePhoneClickHandler'


export const PhoneLink: React.FC<{
    phone: string
}> = ({
    phone,
}) => {
    const phoneClickHandler = usePhoneClickHandler()

    return (
        <a
            onClick={phoneClickHandler}
            data-phone={phone}
            tabIndex={0}
        >
            {formatPhone(phone)}
        </a>
    )
}
