import React from 'react'
import type {FieldArray as FieldArrayType, FieldValues} from 'react-hook-form'
import {useFieldArray} from 'react-hook-form'

import type {FieldArrayInnerControlProps, FieldArrayPathByValue} from '@bpinc/lib-forms-base'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import type {SVGRComponent} from '@bpinc/lib-types-helpers'

import {AddRow} from './AddRow'
import {FieldArrayItem} from './FieldArrayItem'


type FieldArrayProps = {
    label?: string
    layoutClassName?: string
    className?: string
    AddButtonIcon: SVGRComponent
    addButtonText?: string
}

export const FieldArray = <
    TFieldType extends FieldArrayType,
    TValues extends FieldValues,
    TFieldName extends FieldArrayPathByValue<TValues, TFieldType>,
>({
    control,
    name,
    AddButtonIcon,
    addButtonText,
    renderComponents,
    layoutClassName,
    initialValues,
    label,
    className,
}: FieldArrayInnerControlProps<TFieldType, TValues, TFieldName> & FieldArrayProps) => {
    const {fields, append, remove} = useFieldArray({
        control,
        name,
    })

    return (
        <div className={tcn('flex flex-col', layoutClassName, className)}>
            {fields.map((_, index) => (
                <FieldArrayItem
                    key={index}
                    layoutClassName={layoutClassName}
                    label={label}
                >
                    {renderComponents(index, remove, fields.length)}
                </FieldArrayItem>
            ))}
            <AddRow
                append={append}
                initialValues={initialValues}
                AddButtonIcon={AddButtonIcon}
                addButtonText={addButtonText}
            />
        </div>
    )
}

