import type {Dispatch, SetStateAction} from 'react'

import {useLocalSettings} from '@bpinc/ad-local-settings-context'
import {useEvent} from '@bpinc/lib-helpers'

import type {OpenFolders} from './helpers'
import {getActiveGroupStorageName} from './helpers'


type DirectoryFolderToggleParams = {
    isSearchMode: boolean
    setOpenFolders: Dispatch<SetStateAction<OpenFolders>>
}

export function useToggleDirectoryFolder({isSearchMode, setOpenFolders}: DirectoryFolderToggleParams) {
    const localSettings = useLocalSettings()

    return useEvent((folderId: string) => {
        setOpenFolders(state => {

            let newList, newDiff

            if (state.list.includes(folderId)) {
                newList = state.list.filter(id => id !== folderId)
                newDiff = {added: [], removed: [folderId]}
            } else {
                newList = [...state.list, folderId]
                newDiff = {added: [folderId], removed: []}
            }

            localSettings.set(getActiveGroupStorageName({isSearchMode}), newList)

            return {list: newList, diff: newDiff}
        })
    })
}
