import React, {useMemo} from 'react'

import {useGetAvatarUrl} from '@bpinc/ad-contacts-state-hooks'
import {getMsTeamsUserPrimaryPhoneNumber} from '@bpinc/ad-emi-helpers'
import {useFormatting} from '@bpinc/ad-formatting-context'
import {prepareHighlightFormattedPhone} from '@bpinc/ad-formatting-helpers'
import {getUserStatusIcon, useUserDetailsById} from '@bpinc/ad-user-details-state-context'
import type {PropsAvatar} from '@bpinc/lib-contact-list-item-ui'
import {ContactListItem} from '@bpinc/lib-contact-list-item-ui'
import {DirectoryFavoriteButton} from '@bpinc/lib-favorites-button'
import {useEvent} from '@bpinc/lib-helpers'
import {IconPhone16Filled} from '@bpinc/lib-jskit-ui'
import {highlighted, SelectionItem} from '@bpinc/lib-ui-elements'

import {UserOnlineStatus} from '../internal-user/UserOnlineStatus'
import type {MsTeamsUserProps} from '../types'
import {MsTeamsUserControls} from './MsTeamsUserControls'


export const MsTeamsUser: React.FC<MsTeamsUserProps> = props => {
    const {
        transferMode,
        transferControlsStyle,
        user,
        onChat,
        onCall,
        onDetails,
        highlight,
        onClick,
        onDoubleClick,
        showFavoriteButton = true,
        ...other
    } = props

    const {formatFullName} = useFormatting()
    const getAvatarUrl = useGetAvatarUrl()
    const details = useUserDetailsById(user.userId)

    //TODO: When MS Teams work again, check if we can omit user.presenceState as the second argument
    const onlineStatusIcon = getUserStatusIcon(details?.data, user.presenceState)
    const primaryPhoneNumber = getMsTeamsUserPrimaryPhoneNumber(user.phones)

    const avatar: PropsAvatar = useMemo(
        () => ({
            firstName: user.firstName,
            lastName: user.lastName,
            isAgent: true,
            url: getAvatarUrl({
                userId: user.userId,
                userType: user.userType,
                photoUrl: user.photoUrl,
            }),
            onlineStatusIcon,
            isOnline: onlineStatusIcon === 'ready',
        }),
        [getAvatarUrl, onlineStatusIcon, user],
    )

    const title = useMemo(() => (
        <span className="flex gap-1">
            <b data-testid="name" className="inline-block truncate font-semibold body text-primary-on-primary">
                {highlighted({
                    highlight,
                    text: formatFullName(user.firstName, user.lastName),
                })}
            </b>
            {showFavoriteButton && (
                <DirectoryFavoriteButton forContactListItem item={user} />
            )}
        </span>
    ), [formatFullName, highlight, showFavoriteButton, user])

    const body = useMemo(() => {
        if (!primaryPhoneNumber) {
            return null
        }
        return (
            <span className="flex items-center gap-1">
                <IconPhone16Filled className="my-px mr-1 inline-block shrink-0 icon-neutral" />
                <span data-testid="phone" className="truncate caption-s text-neutral-on-secondary">
                    {highlighted({
                        highlight,
                        text: primaryPhoneNumber,
                        sanitizeText: prepareHighlightFormattedPhone,
                    })}
                </span>
            </span>
        )
    }, [primaryPhoneNumber, highlight])

    const stats = <UserOnlineStatus details={details} />

    const controls = (
        <MsTeamsUserControls
            user={user}
            transferMode={transferMode}
            onChat={onChat}
            onCall={onCall}
            onDetails={onDetails}
        />
    )

    const handleChat = useEvent((e: React.MouseEvent) => {
        e.preventDefault()
        e.stopPropagation()
        onChat?.({
            userId: user.userId,
            firstName: user.firstName,
            lastName: user.lastName,
            userType: user.userType,
            phone: primaryPhoneNumber ?? '',
        })
    })

    const handleClick = useEvent((e: React.MouseEvent) => {
        onClick?.(e, user)
    })
    const handleDoubleClick = useEvent(() => {
        onDoubleClick?.(user)
    })

    return (
        <ContactListItem
            as={SelectionItem}
            keyHandlerElement="directory-item"
            transferMode={transferMode}
            transferControlsStyle={transferControlsStyle}
            userId={user.userId}
            userType={user.userType}
            hasChat={user.hasChat}
            number={primaryPhoneNumber}
            avatarProps={avatar}
            title={title}
            body={body}
            stats={stats}
            controls={controls}
            onClick={onClick ? handleClick : handleChat}
            onDoubleClick={onDoubleClick && handleDoubleClick}
            containerProps={{
                itemId: user.userId,
                focusOnSelection: true,
                tabIndex: 0,
                'data-testid': 'directory-item',
                'data-ms-teams-user': 'true',
            }}
            {...other}
        />
    )
}
