import type {ChatType} from '@bpinc/ad-interaction-chat-state-types'


export const SCROLL_KEYS = {
    recent: (tab: ChatType) => `recent.${tab}`,
    directory: (tab: 'internal' | 'external') => `directory.${tab}`,
    favorites: () => 'favorites',
    INTERACTIONS: {
        transfer: {
            recent: (activeItemId: string, tab: ChatType) => `interactions.${activeItemId}.recent.${tab}`,
            directory: (activeItemId: string, tab: 'internal' | 'external') => `interactions.${activeItemId}.directory.${tab}`,
            favorites: (activeItemId: string) => `interactions.${activeItemId}.favorites`,
        },
    },
}
