import type {FormDefinitionPageItem} from '@bpinc/ad-request-urls'


/**
 * Mapping string is a string that is used to map form element to a specific scenario variable
 * It is specified in different ways for different form elements
 */
export function getFormElementMappingString(field: FormDefinitionPageItem | undefined) {
    if (
        field?.field.type === 'CUSTOM' &&
        field?.field.leadMappingString
    ) {
        return field.field.leadMappingString
    }

    if (
        field?.field.type === 'STATIC' &&
        field?.field.staticType === 'MESSENGER' &&
        field?.field.mapping
    ) {
        return field.field.mapping
    }

    return ''
}
