import React, {useMemo, useRef} from 'react'

import type {DirectoryItem as DirectoryItemType} from '@bpinc/ad-directory-state-types'
import {useIntersectionObserver} from '@bpinc/lib-helpers'
import {isDirectoryEntry, isExternalUser, isMsTeamsChannel, isMsTeamsUser} from '@bpinc/lib-helpers-ui'

import {DirectoryEntry} from './directory-entry/DirectoryEntry'
import {ExternalContact} from './external-contact/ExternalContact'
import {InternalUser} from './internal-user/InternalUser'
import {MsTeamsChannel} from './msteams-channel/MsTeamsChannel'
import {MsTeamsUser} from './msteams-user/MsTeamsUser'
import type {DirectoryItemProps} from './types'
import {useSubscribeForUserOrServiceDetails} from './useSubscribeForUserOrServiceDetails'


interface Props extends DirectoryItemProps {
    item: DirectoryItemType
    listNode: HTMLDivElement | null | undefined
    className?: string
}

export const DirectoryItem: React.FC<Props> = React.memo(function DirectoryItem({item, listNode, className, ...props}) {
    const ref = useRef(null)

    const observer = useIntersectionObserver(ref, {root: listNode})
    useSubscribeForUserOrServiceDetails(item, observer?.isIntersecting)

    const userComponent = useMemo(function memoizeUserComponent() {
        if (isMsTeamsChannel(item)) {
            return <MsTeamsChannel {...props} item={item} />
        }
        if (isMsTeamsUser(item)) {
            return <MsTeamsUser {...props} user={item} />
        }

        if (isExternalUser(item)) {
            return <ExternalContact {...props} contact={item} />
        }

        if (isDirectoryEntry(item)) {
            return <DirectoryEntry {...props} item={item} />
        }

        return <InternalUser {...props} user={item} />
    }, [props, item])

    return <div ref={ref} className={className}>{userComponent}</div>
})
