import type {MutableRefObject} from 'react'
import React, {useEffect, useRef} from 'react'

import {useInteractionChatDataSelector} from '@bpinc/ad-interaction-chat-state-context'
import {useAssociatedChatInteractionProp} from '@bpinc/ad-interaction-state-context'
import {getInteractionStepId} from '@bpinc/ad-interaction-state-methods'
import {useWebRtcState} from '@bpinc/ad-webrtc-state-context'
import {useInteractionId} from '@bpinc/lib-interaction-context'
import {tcn} from '@bpinc/lib-tailwind-classnames'

import {InteractionVideoWarning} from './InteractionVideoWarning'

import styles from '../../styles/InteractionsPage.module.less'


const connectStream = (
    ref: MutableRefObject<HTMLVideoElement | null>,
    stream?: MediaStream,
): () => void => {
    const el = ref.current
    if (el && stream) el.srcObject = stream
    return () => el && el.removeAttribute('srcObject')
}

export const InteractionVideo: React.FC = React.memo(() => {
    const {streams} = useWebRtcState()
    const interactionId = useInteractionId()
    const activeChatId = useAssociatedChatInteractionProp(interactionId, getInteractionStepId)
    const chatCall = useInteractionChatDataSelector(activeChatId, s => s?.call)
    const {localStreamId, remoteStreamId} = chatCall || {}
    const externalVideoRef = useRef<HTMLVideoElement | null>(null)
    const internalVideoRef = useRef<HTMLVideoElement | null>(null)

    const localStream = localStreamId ? streams[localStreamId] : undefined
    const remoteStream = remoteStreamId ? streams[remoteStreamId] : undefined

    useEffect(
        () => connectStream(internalVideoRef, localStream),
        [localStream],
    )
    useEffect(
        () => connectStream(externalVideoRef, remoteStream),
        [remoteStream],
    )

    return (
        <div className={tcn(styles.interactionVideo, 'px-4')}>
            <div className={styles.interactionVideoExternal}>
                <video
                    ref={externalVideoRef}
                    controls={false}
                    playsInline
                    autoPlay
                />
            </div>
            <div className={styles.interactionVideoInternal}>
                <video
                    ref={internalVideoRef}
                    controls={false}
                    playsInline
                    autoPlay
                    muted
                />
            </div>

            <InteractionVideoWarning localStream={localStream} />
        </div>
    )
})
