import React, {useCallback, useState} from 'react'

import {SmLabel} from '@bpinc/lib-external-ui-elements'
import {clipboard} from '@bpinc/lib-helpers'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {useLocalizationState} from '@bpinc/state-localization-context'

import styles from '../styles/MainPage.module.less'


type Props = {
    url: string
}

export const DirectChatLinkSafariNotification: React.FC<Props> = ({url}) => {
    const {t} = useLocalizationState()
    const [copyLinkBadgeTrigger, setCopyLinkBadgeTrigger] = useState(0)

    const onNotificationClick = useCallback(() => {
        clipboard.copy(url)
        setCopyLinkBadgeTrigger(c => ++c)
    }, [url])

    return (
        <div className={styles.directChatLinkCopyNotification} onClick={onNotificationClick}>
            <span>
                {t.HOME_SCREEN.direct_chat_copy_link_safari_notification()}
            </span>
            {!!copyLinkBadgeTrigger && (
                <SmLabel
                    key={copyLinkBadgeTrigger}
                    pointing="left"
                    className={tcn(styles.copiedLabel, styles.inNotification)}
                >
                    {t.HOME_SCREEN.direct_chat_invitation_link_copied_label()}
                </SmLabel>
            )}
        </div>
    )
}
