import React, {useCallback} from 'react'
import cn from 'classnames'

import {AgentStatuses} from '@bpinc/lib-agent-state-ui'
import {useCommunicatorUIState} from '@bpinc/lib-communicator-ui-context'
import {SmSidebar} from '@bpinc/lib-external-ui-elements'
import {useEscapeManager} from '@bpinc/lib-helpers'
import {FocusTrap} from '@bpinc/lib-ui-elements'

import styles from '../styles/MainPage.module.less'


export const SidebarLeft: React.FC = React.memo(function SidebarLeft() {
    const {
        leftPanelOpened,
        toggleLeftPanel,
    } = useCommunicatorUIState([
        'leftPanelOpened',
        'toggleLeftPanel',
    ])

    const closeSideBar = useCallback(() => toggleLeftPanel(false), [toggleLeftPanel])

    useEscapeManager(closeSideBar, {enabled: leftPanelOpened})

    return (
        <SmSidebar
            animation="overlay"
            direction="left"
            onHide={closeSideBar}
            visible={leftPanelOpened}
            className={cn('overflow-hidden', styles.sidebar)}
            data-page-section="sidebar sidebar_left"
            data-testid="left-sidebar"
        >
            <FocusTrap
                enabled={leftPanelOpened}
                delayFocus={500} // sidebar opening animation duration
                className="flex h-full flex-col"
            >
                <AgentStatuses closePanel={closeSideBar} />
            </FocusTrap>
        </SmSidebar>
    )
})
