import React, {useMemo, useState} from 'react'

import {useInteractionProp, useInteractionStateMethods} from '@bpinc/ad-interaction-state-context'
import {getInteractionStepId, isAfterCallWork} from '@bpinc/ad-interaction-state-methods'
import {SmForm} from '@bpinc/lib-external-ui-elements'
import {isHTMLInputElement} from '@bpinc/lib-html-utils'
import {useInteractionId} from '@bpinc/lib-interaction-context'
import {Modal, Radio, SemanticButton} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'
import type {CommonModalProps} from '@bpinc/state-modals-types'


interface Option {
    label: string
    value: string
    disabled?: boolean
}

export const CallProblemModal: React.FC<CommonModalProps> = ({onClose, ...other}) => {
    const {t} = useLocalizationState()
    const {reportProblemItem} = useInteractionStateMethods()
    const interactionId = useInteractionId()
    const itemIsAfterCallWork = useInteractionProp(interactionId, isAfterCallWork)

    const options = useMemo(
        function getOptions() {
            return [
                {
                    value: 'POOR_VOICE_QUALITY',
                    label: t.CALL_DETAILS_FORM.rcp_option_poor_voice(),
                },
                {
                    value: 'CALL_WENT_SILENT',
                    label: t.CALL_DETAILS_FORM.rcp_option_silent(),
                },
                {
                    value: 'CALL_DROPPED',
                    label: t.CALL_DETAILS_FORM.rcp_option_dropped(),
                    disabled: !itemIsAfterCallWork,
                },
                {
                    value: 'OTHER',
                    label: t.CALL_DETAILS_FORM.rcp_option_other(),
                },
            ]
        },
        [itemIsAfterCallWork, t.CALL_DETAILS_FORM],
    )
    const interactionStepId = useInteractionProp(interactionId, getInteractionStepId)
    const [problem, setProblem] = useState<Option>(options[0])

    function handleReport() {
        reportProblemItem(problem.value, interactionStepId)
        onClose()
    }

    function handleChange(e: React.FormEvent<HTMLInputElement>) {
        const input = e.target
        if (!isHTMLInputElement(input)) {
            return
        }
        const selectedOption = options.find(option => option.value === input.value)
        if (selectedOption) {
            setProblem(selectedOption)
        }
    }

    return (
        <Modal
            onClose={onClose}
            size="tiny"
            className="max-w-[354px]"
            aria-labelledby="callProblemDialogHeader"
            {...other}
        >
            <Modal.Header role="heading" id="callProblemDialogHeader">
                {t.CALL_DETAILS_FORM.rcp_heading()}
            </Modal.Header>
            <Modal.Content>
                <SmForm>
                    {options.map(function renderOptions(option) {
                        return (
                            <SmForm.Field key={option.value}>
                                <Radio
                                    label={option.label}
                                    name="problem"
                                    value={option.value}
                                    checked={problem.value === option.value}
                                    onChange={handleChange}
                                    disabled={option.disabled}
                                />
                            </SmForm.Field>
                        )
                    })}
                </SmForm>
            </Modal.Content>
            <Modal.Actions>
                <SemanticButton
                    onClick={() => onClose()}
                    tooltip={t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_buttonCancel()}
                    enabledInOffline
                >
                    {t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_buttonCancel()}
                </SemanticButton>
                <SemanticButton onClick={handleReport} primary tooltip={t.CALL_DETAILS_FORM.button_call_problem_report_label()}>
                    {t.CALL_DETAILS_FORM.button_call_problem_report_label()}
                </SemanticButton>
            </Modal.Actions>
        </Modal>
    )
}
