import React, {useId} from 'react'
import {useContext} from 'use-context-selector'

import {useFormatting} from '@bpinc/ad-formatting-context'
import {STATES} from '@bpinc/ad-location-data'
import type {ContactAddress, FormDefinitionFieldAddressList, FormDefinitionPageItem} from '@bpinc/ad-request-urls'
import {getPageItemCSSProperties} from '@bpinc/lib-form-builder'

import {FormBuilderContext} from '../../FormBuilder'
import {AddressFieldArray} from '../common/FieldArray/AddressFieldArray'
import {ViewItem} from '../common/ViewItem'


export const AddressListField: React.FC<{
    pageItem: FormDefinitionPageItem
    field: FormDefinitionFieldAddressList
}> = ({
    pageItem,
    field,
}) => {
    const {
        form,
        view,
    } = useContext(FormBuilderContext)

    const {formatAddressType} = useFormatting()
    const ariaId1 = useId()
    const ariaId2 = useId()

    if (view) {
        const addresses = form.getValues()[field.name || ''] as ContactAddress[]
        const viewStyle = getPageItemCSSProperties(pageItem.style)

        return (
            <>
                {addresses?.map((address, index) => {
                    const value = [
                        address.address_line1,
                        address.address_line2,
                        address.city,
                        STATES[address.state as keyof typeof STATES],
                        address.postcode,
                    ]
                        .filter(Boolean)
                        .join(', ')

                    return (
                        <ViewItem
                            key={index}
                            viewStyle={viewStyle}
                            label={formatAddressType(address.type)}
                            value={(
                                <a
                                    tabIndex={0}
                                    aria-labelledby={`${ariaId1} ${ariaId2}`}
                                    id={ariaId2}
                                >
                                    {value}
                                </a>
                            )}
                        />
                    )
                })}
            </>
        )
    }

    const editStyle = getPageItemCSSProperties(pageItem.editStyle)

    return (
        <AddressFieldArray
            name={field.name || ''}
            control={form.control}
            style={editStyle}
            layoutClassName={'gap-x-[--form-cellPadding] gap-y-[--form-rowHeight]'}
            typeClassName={'w-[--form-fieldLabelWidth] min-w-[--form-fieldLabelWidth] max-w-[--form-fieldLabelWidth]'}
        />
    )
}
