import type {Assignments} from '@bpinc/ad-case-state-types'


export const getAssignee = (
    assignments: Assignments | undefined,
    formatFullName: (first: string, last: string) => string,
): string => {
    const users = assignments && assignments.personal

    if (users && users.length) {
        return users
            .map(user => formatFullName(user.firstName || '', user.lastName || ''))
            .join(', ')
    }

    return ''
}
