import React, {useCallback} from 'react'

import {useInteractionProp, useInteractionStateMethods} from '@bpinc/ad-interaction-state-context'
import {useInteractionId} from '@bpinc/lib-interaction-context'
import {assertDefined} from '@bpinc/lib-types-helpers'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {CrmRecordItem} from './CrmRecordItem'


export const CrmRecords: React.FC = () => {
    const {t} = useLocalizationState()
    const interactionId = useInteractionId()

    const {toggleAssociatedActivityRecord} = useInteractionStateMethods()

    const {list, selected} = useInteractionProp(interactionId, activeItem => {
        return activeItem?.associatedObjects ?? {list: [], selected: [], touched: false, primary: []}
    })

    const handleChange = useCallback((recordId: string) => {
        assertDefined(interactionId)
        toggleAssociatedActivityRecord(interactionId, recordId)
    }, [toggleAssociatedActivityRecord, interactionId])

    return (
        <>
            <h4 className="first:mt-4">
                {t.DISPOSITION.resolution_crm_objects_new_label()}
            </h4>

            <ul className="mb-3 mt-1.5 flex-[1_3_auto] overflow-auto rounded border border-main-border pl-4">
                {list.map(record => (
                    <CrmRecordItem
                        key={record.id}
                        record={record}
                        checked={selected.includes(record.id)}
                        handleChange={handleChange}
                    />
                ))}
                {!list.length && (
                    <div className="mx-1.5 my-2.5">
                        {t.DISPOSITION.resolution_crm_objects_empty_label()}
                    </div>
                )}
            </ul>
        </>
    )
}
