import {useEffect} from 'react'

import {MAX_INTERACTIONS_PER_PAGE} from './usePagination'


type GetItemCoordsByIndex = (index: number) => {page: number; index: number}
type GoToPage = (page: number) => void

interface UseAccessibleShortcutsParams {
    currentPage: number
    getItemCoordsByIndex: GetItemCoordsByIndex
    goToPage: GoToPage
}

export function useAccessibleShortcuts(
    {currentPage, getItemCoordsByIndex, goToPage}: UseAccessibleShortcutsParams,
): void {
    useEffect(() => {

        function onKeyPress(evt: KeyboardEvent) {
            if (evt.altKey && /^Digit[0-9]$/.test(evt.code)) {
                evt.preventDefault()
                const index = +evt.code.replace('Digit', '') - 1
                return navigateOverInteractionsByNumbers(index, getItemCoordsByIndex, goToPage)
            }
            if (evt.altKey && /^ArrowLeft|ArrowRight$/.test(evt.code)) {
                evt.preventDefault()
                setTimeout(() => navigateOverInteractionsByArrows(evt, currentPage, getItemCoordsByIndex, goToPage), 0)
            }
        }

        document.addEventListener('keydown', onKeyPress)

        return function unsubscribeOnKeyPress() {
            document.removeEventListener('keydown', onKeyPress)
        }
    }, [currentPage, getItemCoordsByIndex, goToPage])
}

function navigateOverInteractionsByArrows(
    evt: KeyboardEvent,
    currentPage: number,
    getItemCoordsByIndex: GetItemCoordsByIndex,
    goToPage: GoToPage,
) {
    const interactions: HTMLElement[] = Array.from(document.querySelectorAll('[data-testid="interaction-item"]'))

    if (interactions?.length) {
        let currentPagedIndex = interactions.findIndex(elt => !!elt.dataset.active)

        if (evt.code === 'ArrowLeft') {
            currentPagedIndex--
        } else {
            currentPagedIndex++
        }

        const currentIndex = currentPagedIndex + currentPage * MAX_INTERACTIONS_PER_PAGE

        return navigateOverInteractionsByNumbers(currentIndex, getItemCoordsByIndex, goToPage)
    }
}

function navigateOverInteractionsByNumbers(
    index: number,
    getItemCoordsByIndex: GetItemCoordsByIndex,
    goToPage: GoToPage,
) {
    const interactions: HTMLElement[] = Array.from(document.querySelectorAll('[data-testid="interaction-item"]'))

    if (interactions && interactions.length) {
        const {page, index: pagedIndex} = getItemCoordsByIndex(index)

        if (page === -1 || pagedIndex === -1) {
            return
        }

        goToPage(page)

        if (interactions[pagedIndex]) {
            return interactions[pagedIndex].click()
        }
    }
}
