import React, {useCallback} from 'react'
import {Button} from '@servicepattern/ui'

import type {DirectoryStateUserObject} from '@bpinc/ad-directory-state-types'
import {useActivityHistoryOpener, useCallHandler} from '@bpinc/ad-interaction-state-hooks'
import {useSessionState} from '@bpinc/ad-session-context'
import type {UserDetails} from '@bpinc/ad-user-details-state-types'
import {AppRoutes, useNavigationState} from '@bpinc/lib-communicator-navigation-state'
import {IconChat, IconPhone24} from '@bpinc/lib-jskit-ui'
import {assertDefined} from '@bpinc/lib-types-helpers'
import {useLocalizationState} from '@bpinc/state-localization-context'


interface Props {
    user: DirectoryStateUserObject | UserDetails
}

export const AgentControls: React.FC<Props> = ({user}) => {
    const {t} = useLocalizationState()
    const setActivePage = useNavigationState('setActivePage')
    const onCall = useCallHandler()
    const {userId: selfUserId, phoneDeviceCapabilities} = useSessionState()
    const openActivityHistory = useActivityHistoryOpener()
    const handleChat = useCallback(() => {
        assertDefined(selfUserId)
        const {userId, firstName = '', lastName = ''} = user
        openActivityHistory({
            userId,
            firstName,
            lastName,
            origination: 'contact-profile',
        })
        setActivePage({name: AppRoutes.Interactions})
    }, [selfUserId, user, openActivityHistory, setActivePage])

    const handleCall = useCallback(() => {
        onCall({
            phone: user.extension,
            firstName: user.firstName,
            lastName: user.lastName,
            userId: user.userId,
            navigateToInteractionsPage: true,
            origination: 'contact-profile',
        })
    }, [onCall, user.extension, user.firstName, user.lastName, user.userId])

    return (
        <div className="-mt-1 ml-auto flex flex-none items-center">
            <Button
                onClick={handleChat}
                tooltip={t.CONTACT.button_initiate_chat_text()}
                data-testid="chat-button"
                variant="ghost"
                icon={<IconChat />}
            />
            {phoneDeviceCapabilities.canDial && (
                <Button
                    onClick={handleCall}
                    tooltip={t.CONTACT.button_start_call_text()}
                    data-testid="call-button"
                    variant="ghost"
                    icon={<IconPhone24 />}
                />
            )}
        </div>
    )
}
