import type {SyntheticEvent} from 'react'
import React from 'react'

import type {SmInputProps} from '@bpinc/lib-external-ui-elements'
import type {DropdownProps} from '@bpinc/lib-ui-elements'
import type {ContactRecord as ContactRecordType} from '@bpinc/state-interaction-ui'

import {ContactRecord} from './ContactRecord'


export interface ContactRecordsProps {
    contacts: ContactRecordType[]
    contactHandlersFactories: {
        handleContactNameChangeFactory: (
            index: number
        ) => (evt: SyntheticEvent<Element, Event>, {value}: SmInputProps) => void
        handleContactPhoneChangeFactory: (
            index: number
        ) => (evt: SyntheticEvent<Element, Event>, {value}: SmInputProps) => void
        handleContactPhoneIdChangeFactory: (
            index: number
        ) => (evt: SyntheticEvent<Element, Event>, {value}: DropdownProps) => void
        handleContactPhoneTypeChangeFactory: (
            index: number
        ) => (
            evt: SyntheticEvent<Element, Event>,
            {value}: DropdownProps
        ) => void
        handleContactCreateContactChangeFactory: (
            index: number
        ) => (
            evt: SyntheticEvent<Element, Event>,
            {value}: DropdownProps
        ) => void
    }
}

export const ContactRecords: React.FC<ContactRecordsProps> = ({
    contactHandlersFactories,
    contacts,
}) => {
    return (
        <>
            {contacts.map((contact, index) => (
                <ContactRecord
                    key={index}
                    index={index}
                    contact={contact}
                    contactHandlersFactories={contactHandlersFactories}
                />
            ))}
        </>
    )
}
