import type {SyntheticEvent} from 'react'
import React, {useMemo} from 'react'

import {PhoneTypes} from '@bpinc/ad-contacts-state-types'
import {useFormatting} from '@bpinc/ad-formatting-context'
import type {SmInputProps} from '@bpinc/lib-external-ui-elements'
import {SmCheckbox, SmForm, SmInput, SmSegment} from '@bpinc/lib-external-ui-elements'
import type {DropdownProps} from '@bpinc/lib-ui-elements'
import {Dropdown} from '@bpinc/lib-ui-elements'
import type {ContactRecord as ContactRecordType} from '@bpinc/state-interaction-ui'
import {useLocalizationState} from '@bpinc/state-localization-context'


interface ContactRecordProps {
    contact: ContactRecordType
    contactHandlers: {
        handleContactNameChange: (evt: SyntheticEvent<Element, Event>, {value}: SmInputProps) => void
        handleContactPhoneChange: (evt: SyntheticEvent<Element, Event>, {value}: SmInputProps) => void
        handleContactPhoneTypeChange: (
            evt: SyntheticEvent<Element, Event>,
            {value}: DropdownProps
        ) => void
        handleContactCreateContactChange: (
            evt: SyntheticEvent<Element, Event>,
            {value}: DropdownProps
        ) => void
    }
}

export const NonExistingContactRecord: React.FC<ContactRecordProps> = ({
    contactHandlers,
    contact,
}) => {
    const {t} = useLocalizationState()

    const {formatPhoneType} = useFormatting()
    const options = useMemo(function memoizePhoneTypeOptions() {
        const contactPhoneTypes = Object.values(PhoneTypes)

        return contactPhoneTypes.map(phoneType => ({value: phoneType, text: formatPhoneType(phoneType)}))
    }, [formatPhoneType])

    return (
        <SmSegment>
            <SmForm.Field
                value={contact.name}
                onChange={contactHandlers.handleContactNameChange}
                label={t.CALENDAR_EVENT_FORM.contact_name_placeholder()}
                placeholder={t.CALENDAR_EVENT_FORM.contact_name_placeholder()}
                control={SmInput}
            />
            <SmForm.Field
                value={contact.phone}
                onChange={contactHandlers.handleContactPhoneChange}
                label={t.CALENDAR_EVENT_FORM.contact_on_label()}
                placeholder={t.CALENDAR_EVENT_FORM.contact_on_label()}
                control={SmInput}
            />
            <SmForm.Group>
                <SmForm.Field
                    checked={contact.createContact}
                    label={t.CALENDAR_EVENT_FORM.contact_create_box_label()}
                    control={SmCheckbox}
                    onChange={contactHandlers.handleContactCreateContactChange}
                />
                <SmForm.Field
                    value={contact.phoneType}
                    control={Dropdown}
                    options={options}
                    onChange={contactHandlers.handleContactPhoneTypeChange}
                />
            </SmForm.Group>
        </SmSegment>
    )
}
