import React from 'react'
import {Button} from '@servicepattern/ui'

import {useInteraction, useInteractionStateMethods} from '@bpinc/ad-interaction-state-context'
import {useSystemDisposition} from '@bpinc/ad-interaction-state-hooks'
import type {Disposition} from '@bpinc/ad-services-dispositions-types'
import {useEvent} from '@bpinc/lib-helpers'
import {useInteractionId} from '@bpinc/lib-interaction-context'
import {CircleButton} from '@bpinc/lib-jskit-ui'
import {assertDefined} from '@bpinc/lib-types-helpers'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {useScheduleReminderOption} from './useScheduleReminderOption'


interface WorkItemButtonsProps {
    onClose: (e: React.MouseEvent, disposition?: Disposition) => void
}

function isDisposition(disposition: Disposition | null): disposition is Disposition {
    return !!disposition
}

export const WorkItemButtons: React.FC<WorkItemButtonsProps> = ({onClose}) => {
    const t = useLocalizationState('t')
    const interactionId = useInteractionId()
    const activeItem = useInteraction(interactionId)
    const {setDisposition} = useInteractionStateMethods()
    const dispositionForSkip = useSystemDisposition('skipped')

    const scheduleReminderButton = useScheduleReminderOption()

    const onSkipClick = useEvent((e: React.MouseEvent) => {
        if (isDisposition(dispositionForSkip) && interactionId) {
            setDisposition({
                itemId: interactionId,
                disposition: dispositionForSkip,
            })
        }
        onClose(e, dispositionForSkip || undefined)
    })

    assertDefined(activeItem)

    return (
        <div className="flex flex-auto items-center justify-between">
            <div className="flex items-center gap-2.5">
                <CircleButton
                    data-testid={scheduleReminderButton['data-testid']}
                    tooltip={{content: scheduleReminderButton.tooltip}}
                    onClick={scheduleReminderButton.onClick}
                >
                    <scheduleReminderButton.Icon className="size-4.5 icon-fade" />
                </CircleButton>
            </div>
            <div className="flex items-center gap-2">
                <Button
                    variant="secondary"
                    size="sm"
                    onClick={onClose}
                    className="h-7"
                    data-testid="complete-button"
                >
                    {t.CALL_DETAILS_FORM.interaction_completeACW()}
                </Button>
                {!!dispositionForSkip && (
                    <Button
                        variant="secondary"
                        size="sm"
                        onClick={onSkipClick}
                        className="h-7 text-on-error"
                        data-testid="skip-workitem-button"
                    >
                        {t.CALL_DETAILS_FORM.interaction_endWorkItem()}
                    </Button>
                )}
            </div>
        </div>
    )
}
