import {useSubscribeForHuntGroupDetails, useSubscribeForServiceDetails} from '@bpinc/ad-service-details-state-context'
import {useSubscribeForUserDetails} from '@bpinc/ad-user-details-state-context'
import type {FavoritesItem} from '@bpinc/state-favorites-types'


export function useSubscribeForFavoriteItemDetails(item: FavoritesItem, isVisible?: boolean) {
    useSubscribeForUserDetails(item.userId, isVisible)
    useSubscribeForUserDetails(item.emiUserId, isVisible)
    useSubscribeForServiceDetails(item.serviceId, isVisible)
    useSubscribeForHuntGroupDetails(item.huntGroupId, isVisible)
}
