import React, {useCallback, useEffect, useRef, useState} from 'react'
import {debounce} from 'lodash-es'

import {useLocalizationState} from '@bpinc/state-localization-context'

import {MINIMAL_PROFILING_VALUE, useCPUProfiling} from './useCPUProfiling'

import styles from './InteractionVideoWarning.module.less'


export const InteractionVideoWarning: React.FC<{
    localStream?: MediaStream
}> = ({
    localStream,
}) => {
    const {t} = useLocalizationState()

    const [hasMicError, setHasMicError] = useState(false)
    const [hasPerformanceError, setHasPerformanceError] = useState(false)

    const debouncedClearMicError = useRef(debounce(() => {
        setHasMicError(false)
    }, 2000)).current

    const debouncedClearPerformanceError = useRef(debounce(() => {
        setHasPerformanceError(false)
    }, 2000)).current

    const showError = useCallback((type: 'mic' | 'performance') => {
        if (type === 'mic') {
            setHasMicError(true)
            debouncedClearMicError()
        }
        if (type === 'performance') {
            setHasPerformanceError(true)
            debouncedClearPerformanceError()
        }
    }, [debouncedClearMicError, debouncedClearPerformanceError])

    const getAndResetProfilingValue = useCPUProfiling()

    useEffect(() => {
        const audioTrack = localStream?.getAudioTracks()[0]

        const intervalId = setInterval(() => {
            if (
                audioTrack?.muted
                || !audioTrack?.enabled
                || audioTrack.readyState === 'ended'
            ) {
                showError('mic')
            }

            const profilingValue = getAndResetProfilingValue()
            if (profilingValue < MINIMAL_PROFILING_VALUE) {
                showError('performance')
            }
        }, 1000)

        return () => {
            clearInterval(intervalId)
        }
    }, [getAndResetProfilingValue, localStream, showError])

    return (
        <div className={styles.interactionVideoWarning}>
            {hasMicError && (
                <div className={styles.interactionVideoWarningItem}>
                    {t.ERROR_MESSAGES.microphone_muted()}
                </div>
            )}

            {hasPerformanceError && (
                <div className={styles.interactionVideoWarningItem}>
                    {t.ERROR_MESSAGES.low_cpu()}
                </div>
            )}
        </div>
    )
}
