import type {FC} from 'react'
import React from 'react'

import {Chat} from '@bpinc/lib-chat-ui'
import type {InteractionTab} from '@bpinc/state-interaction-ui'

import {CrmScreen, Resolution} from '../../../resolution'
import {WorkitemContacts} from './workitem/WorkitemContacts'
import {WorkitemDetails} from './workitem/WorkitemDetails'
import {WorkitemHistory} from './workitem/WorkitemHistory'


type Props = {
    activeChatId: string
    activeTab: InteractionTab
}

export const InteractionTabContent: FC<Props> = React.memo(React.forwardRef<HTMLDivElement, Props>((
    {activeTab, activeChatId},
    ref,
) => {

    const tabContent = routeTabContent()

    function routeTabContent() {
        switch (activeTab) {
            case 'transcript':
            case 'all':
            case 'calls':
            case 'messages':
                return <Chat itemId={activeChatId} scope="communicator" />
            case 'crm':
                return <CrmScreen />
            case 'resolution':
                return <Resolution className="px-4" />
            case 'workitemContacts':
                return <WorkitemContacts />
            case 'workitemDetails':
                return <WorkitemDetails />
            case 'workitemHistory':
                return <WorkitemHistory />
        }
    }

    return (
        <div
            ref={ref}
            className="flex h-0 flex-auto flex-col overflow-auto p-0"
            data-testid="interaction-body"
        >
            {tabContent}
        </div>
    )
}))
