export const directoryFolderTypes = {
    TEAM: 'TEAM',
    CATEGORY: 'CATEGORY',
    SEARCH_TEAM: 'SEARCH_TEAM',
    SEARCH_CATEGORY: 'SEARCH_CATEGORY',
    EMI_CATEGORY: 'EMI_CATEGORY',
} as const

export type DirectoryFolderType = typeof directoryFolderTypes[keyof typeof directoryFolderTypes]


export type OpenFolders = {
    list: string[]
    diff: {
        added: string[]
        removed: string[]
    }
}

const ACTIVE_GROUP_STORAGE_NAME = 'adcDirectoryInternalActiveGroup'
const ACTIVE_GROUP_FOR_SEARCH_STORAGE_NAME = 'adcDirectoryInternalActiveGroupForSearch'


export function getActiveGroupStorageName({isSearchMode}: {isSearchMode: boolean }) {
    return isSearchMode ? ACTIVE_GROUP_FOR_SEARCH_STORAGE_NAME : ACTIVE_GROUP_STORAGE_NAME
}
