import type {ReactNode} from 'react'
import React from 'react'

import type {ChatTranscriptMessage as Message} from '@bpinc/ad-case-state-types'
import {AttachmentFileMessage, ImageFileMessage} from '@bpinc/lib-chat-ui'
import {useLocalizationState} from '@bpinc/state-localization-context'

// -------------------------
// Chat
// -------------------------
export const useMessage = (
    message: Message,
    formatName: (f?: string, l?: string) => string,
    getFileLink: (fileId: string) => string,
): ReactNode => {
    const {event, firstName, lastName, msg, fileId, fileName, fileType} = message
    const name = formatName(firstName, lastName) || 'Chat BOT'
    const {t} = useLocalizationState()

    switch (event) {
        case 'party_joined':
            return t.CASE_MESSAGE.party_joined(name)
        case 'party_message':
            return msg?.includes('<br>') ? `${name}:<br> ${msg}` : `${name}: ${msg}`
        case 'party_file': {
            if (!fileId || !fileName) {
                return
            }

            const fileLink = getFileLink(fileId)
            return fileType === 'image' ? (
                <>
                    <p>
                        {name}
                        :
                    </p>
                    <ImageFileMessage
                        fileLink={fileLink}
                        fileName={fileName}
                        timestamp={message.timestamp}
                        senderName={name}
                    />
                </>
            ) : (
                <>
                    <p>
                        {name}
                        :
                    </p>
                    <AttachmentFileMessage
                        fileName={fileName}
                        fileLink={fileLink}
                    />
                </>
            )
        }
        case 'party_message_attributes':
            return ''
        case 'party_changed':
            return ''
        case 'party_navigation':
            return ''
        case 'party_left':
            return t.CASE_MESSAGE.party_left(name)
    }
}
