import {useEffect} from 'react'

import {useAppConfigStateMethods} from '@bpinc/ad-app-config-state-context'
import {useSessionState} from '@bpinc/ad-session-context'


export function useAppConfigCheckers() {
    const {checkWhetherToRenderReportProblemButton} = useAppConfigStateMethods()
    const {isLoggedIn, tenantId} = useSessionState()

    useEffect(() => {
        if (isLoggedIn && tenantId) {

            checkWhetherToRenderReportProblemButton(tenantId)
        }
    }, [checkWhetherToRenderReportProblemButton, isLoggedIn, tenantId])

}
