import React, {useCallback} from 'react'

import {useInteractionId} from '@bpinc/lib-interaction-context'
import type {InteractionModalName} from '@bpinc/lib-sliding-modal-context'
import {useCloseInteractionSlidingModal, useSlidingModalState} from '@bpinc/lib-sliding-modal-context'

import {KeypadModal} from './modals/KeypadModal/KeypadModal'
import {KnowledgeBaseSlidingModal} from './modals/KnowledgeBaseSlidingModal'
import {RecentConferenceSlidingModal} from './modals/RecentConference'
import {RescheduleSlidingModal} from './modals/RescheduleSlidingModal'
import {TransferModal} from './modals/TransferModal/TransferModal'


type InteractionsSlidingModalsListItemProps = {
    name: InteractionModalName
}

export const InteractionsSlidingModalsListItem: React.FC<InteractionsSlidingModalsListItemProps> = ({name}) => {
    const closeInteractionSlidingModal = useCloseInteractionSlidingModal()
    const itemId = useInteractionId()
    const data = useSlidingModalState(s => s.getInteractionModalData(itemId))

    const closeModal = useCallback(
        function closeModalCallback() {
            closeInteractionSlidingModal(itemId, name)
        },
        [closeInteractionSlidingModal, itemId, name],
    )

    switch (name) {
        case 'knowledgeBase': {
            return <KnowledgeBaseSlidingModal closeModal={closeModal} {...data[name]} />
        }
        case 'keypad': {
            return <KeypadModal closeModal={closeModal} {...data[name]} />
        }
        case 'transfer': {
            return <TransferModal closeModal={closeModal} {...data[name]} />
        }
        case 'reschedule': {
            return <RescheduleSlidingModal closeModal={closeModal} {...data[name]} />
        }
        case 'recentConference': {
            return <RecentConferenceSlidingModal closeModal={closeModal} {...data[name]} />
        }
        default:
            return null
    }
}

