import React from 'react'

import {useContactDisplayName} from '@bpinc/ad-contacts-state-hooks'
import {useInteraction} from '@bpinc/ad-interaction-state-context'
import {isCallConference, isChatConference, isExternalChat} from '@bpinc/ad-interaction-state-methods'
import {InteractionFavoriteButton} from '@bpinc/lib-favorites-button'
import {useInteractionId} from '@bpinc/lib-interaction-context'
import {useIsMultiuserGroupChat} from '@bpinc/state-group-chat-context'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {ActiveInteractionTopButtons} from './ActiveInteractionTopButtons'
import {ActiveItemAvatar} from './ActiveItemAvatar'
import {ActiveItemDisplayName} from './ActiveItemDisplayName'
import {ActiveItemPhone} from './ActiveItemPhone'


export const ActiveItem: React.FC = () => {
    const interactionId = useInteractionId()
    const activeItem = useInteraction(interactionId)
    const {t} = useLocalizationState()
    const contactId = activeItem?.contactId
    const contactDisplayName = useContactDisplayName(contactId)
    const isMultiuserGroupChat = useIsMultiuserGroupChat(activeItem?.groupChatId)

    if (!activeItem) {
        return null
    }

    const {
        displayData: {
            displayName: interactionDisplayName,
            number,
            numberType,
        },
        externalChatData,
    } = activeItem

    const displayName = contactDisplayName || interactionDisplayName
    const notEmptyDisplayName = displayName || (
        isExternalChat(activeItem)
            ? t.EXTERNAL_CHAT.display_name_placeholder()
            : t.GLOBAL.anonymous()
    )

    const showFavoritesButton =
        activeItem &&
        !isMultiuserGroupChat &&
        !isCallConference(activeItem) &&
        !isChatConference(activeItem)

    return (
        <div className="flex flex-auto items-center gap-2 px-4 py-2" data-testid="party">
            <ActiveItemAvatar />
            <div className="flex flex-auto flex-col">
                <div className="relative flex flex-auto items-center justify-between">
                    <div className="flex items-center">
                        <ActiveItemDisplayName displayName={notEmptyDisplayName} />
                        {showFavoritesButton && (
                            <InteractionFavoriteButton interaction={activeItem} />
                        )}
                    </div>
                    <ActiveInteractionTopButtons />
                </div>
                {!externalChatData && number && (
                    <ActiveItemPhone number={number} numberType={numberType} isContact={!!contactId} />
                )}
            </div>
        </div>
    )
}
