import type {SyntheticEvent} from 'react'
import React, {useCallback} from 'react'

import {useInteraction} from '@bpinc/ad-interaction-state-context'
import {
    isCall,
    isExternalChat,
    isNeedToShowWorkitemUI,
    isQualityManagementItem,
} from '@bpinc/ad-interaction-state-methods'
import {useDisconnectButtonClickHandler} from '@bpinc/lib-helpers-ui'
import {useInteractionId} from '@bpinc/lib-interaction-context'

import {ChatButtons} from '../buttons/ChatButtons'
import {SupervisorButtons} from '../buttons/SupervisorButtons'
import {VoiceCallButtons} from '../buttons/VoiceCallButtons'
import {WorkItemButtons} from '../buttons/WorkItemButtons'
import {PrerecordedMessages} from './prerecorded-messages'


export const InteractionButtons: React.FC = React.memo(() => {
    const interactionId = useInteractionId()
    const activeItem = useInteraction(interactionId)
    const handleDisconnectButtonClick = useDisconnectButtonClickHandler()

    const onCloseInteraction = useCallback(
        function onCloseCallback(event: SyntheticEvent) {
            return handleDisconnectButtonClick(activeItem, {event})
        },
        [handleDisconnectButtonClick, activeItem],
    )

    if (isNeedToShowWorkitemUI(activeItem)) {
        return (
            <div className="flex items-center px-4 py-2">
                <WorkItemButtons onClose={onCloseInteraction} />
            </div>
        )
    }

    return (
        <div className="flex flex-col px-4">
            {isCall(activeItem) && <PrerecordedMessages />}
            <div className="flex flex-auto items-center py-2">
                {isExternalChat(activeItem) && (
                    <ChatButtons onClose={onCloseInteraction} />
                )}
                {isCall(activeItem) && (
                    <VoiceCallButtons onClose={onCloseInteraction} />
                )}
            </div>
            {isQualityManagementItem(activeItem) && (
                <SupervisorButtons item={activeItem} />
            )}
        </div>
    )
})
