import {useEffect} from 'react'
import type {PrimitiveAtom} from 'jotai'
import {useAtom} from 'jotai'

import {useBoolean} from '@bpinc/lib-helpers'


const optimisticTimeout = 1_000

export function useOptimisticActivation(
    isActive: boolean,
) {
    const optimisticActive = useBoolean(isActive)

    useEffect(() => {
        let timeout: number
        if (isActive !== optimisticActive.value) {
            timeout = setTimeout(() => {
                optimisticActive.toggle(isActive)
            }, optimisticTimeout)
            return () => clearTimeout(timeout)
        }
    }, [isActive, optimisticActive])

    return [optimisticActive.value, optimisticActive.toggle] as const
}

export function useOptimisticAtomActivation(
    isActive: boolean,
    atom: PrimitiveAtom<boolean>,
) {
    const [value, setValue] = useAtom(atom)

    useEffect(() => {
        let timeout: number
        if (isActive !== value) {
            timeout = setTimeout(() => {
                setValue(isActive)
            }, optimisticTimeout)
            return () => clearTimeout(timeout)
        }
    }, [isActive, setValue, value])

    return [value, setValue] as const
}
