import React from 'react'

import {useServiceDetailsState} from '@bpinc/ad-service-details-state-context'
import {useUserDetailsById} from '@bpinc/ad-user-details-state-context'
import type {FavoritesItem} from '@bpinc/state-favorites-types'
import {isFavoritesDirectoryEntryItem} from '@bpinc/state-favorites-types'

import {HuntGroupMetrics} from '../../../directory/components/user/directory-entry/HuntGroupMetrics'
import {ServiceMetrics} from '../../../directory/components/user/directory-entry/ServiceMetrics'
import {UserOnlineStatus} from '../../../directory/components/user/internal-user/UserOnlineStatus'


type Props = {
    item: FavoritesItem
}

export const FavoriteItemStats: React.FC<Props> = ({item}) => {

    const {huntGroupsDetails, serviceDetails} = useServiceDetailsState()
    const userDetails = useUserDetailsById(item.userId ?? item.emiUserId)

    if (userDetails) {
        return <UserOnlineStatus details={userDetails} />
    }

    if (isFavoritesDirectoryEntryItem(item)) {

        const huntGroupMetrics = item.huntGroupId ? huntGroupsDetails[item.huntGroupId] : undefined
        const serviceMetrics = item.serviceId ? serviceDetails[item.serviceId] : undefined

        if (huntGroupMetrics) {
            return <HuntGroupMetrics metrics={huntGroupMetrics} />
        }
        if (serviceMetrics) {
            return <ServiceMetrics metrics={serviceMetrics} />
        }
    }

    return null
}
