import React from 'react'

import type {HuntGroupDetails} from '@bpinc/ad-service-details-state-types'
import {useLocalizationState} from '@bpinc/state-localization-context'


type Props = {
    metrics: HuntGroupDetails
}

export const HuntGroupMetrics: React.FC<Props> = ({metrics}) => {

    const {DIRECTORY: i18n} = useLocalizationState('t')

    function getDisplayMetrics(): string[] {
        if (metrics.agentsLogged !== undefined && metrics.phonesRegistered !== undefined) {
            return [
                i18n.logged_in(metrics.agentsLogged),
                i18n.phones_registered(metrics.phonesRegistered),
            ]
        }
        if (metrics.agentsLogged !== undefined) {
            return [i18n.logged_in(metrics.agentsLogged)]
        }
        if (metrics.phonesRegistered !== undefined) {
            return [i18n.phones_registered(metrics.phonesRegistered)]
        }
        return []
    }

    const displayMetrics = getDisplayMetrics()

    return (
        <div
            data-testid="rt-metrics"
            className="flex flex-col overflow-hidden caption-s text-neutral-fade"
        >
            {displayMetrics.map((metric => (
                <span key={metric}>{metric}</span>
            )))}
        </div>
    )
}
