import type {SyntheticEvent} from 'react'
import React, {useCallback, useMemo} from 'react'

import {useInteractionStateSelector} from '@bpinc/ad-interaction-state-context'
import {useSessionState} from '@bpinc/ad-session-context'
import {AppRoutes} from '@bpinc/lib-communicator-navigation-state'
import {
    IconHelp,
    IconKnowledgeBase,
    IconNavCalendar,
    IconNavDial,
    IconNavDirectory,
    IconNavFavorites,
    IconNavRecents,
    IconNavSettings,
} from '@bpinc/lib-icons'
import type {SVGRComponent} from '@bpinc/lib-types-helpers'
import {MissedCallsBadge, UnreadBadge} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {useUnreadAmount} from '../../../hooks/useUnreadAmount'
import type {MenuItemKey, MenuItemParams} from './useSettingsMenu'

import styles from '../../../styles/MainPage.module.less'


interface UseMainMenuParams {
    onMenuItemClick: (name: AppRoutes, params?: Record<string, unknown>) => void
    toggleSettings: (forceOpenedOrEvent?: boolean | SyntheticEvent) => void
}

export function useMainMenu({onMenuItemClick, toggleSettings}: UseMainMenuParams): MenuItemParams[] {
    const {t} = useLocalizationState()
    const {privileges} = useSessionState()
    const missedCallsAmount = useInteractionStateSelector(s => s.missedCallsAmount)

    const unreadAmount = useUnreadAmount()

    const getMenuItemContentWithIconAndName = useCallback(function getMenuItemContentCallback(
        Icon: SVGRComponent,
        name: string,
        misseCallsAmount?: number,
        unread?: number,
    ) {
        return (
            <>
                <div className={styles.mainMenuIconWrapper}>
                    <Icon className={styles.icon} />
                    <UnreadBadge className={styles.unread} unreadAmount={unread} />
                </div>
                <span data-testid="menu-item-text">{name}</span>
                <MissedCallsBadge className="ml-4" amount={misseCallsAmount} />
            </>
        )
    },
    [])

    const navigateTo = useCallback(
        function navigateToCallback(route: AppRoutes) {
            return function clickHandler() {
                onMenuItemClick(route)
            }
        },
        [onMenuItemClick],
    )

    return useMemo(
        function memoizeOutput() {
            const items: MenuItemParams[] = []

            if (privileges.SYS_USE_RECENT_TAB) {
                items.push({
                    key: 'recent' as MenuItemKey,
                    page: AppRoutes.Recents,
                    onClick: navigateTo(AppRoutes.Recents),
                    children: getMenuItemContentWithIconAndName(
                        IconNavRecents,
                        t.ADDRESS_BOOK_AREA.addressBook_recents(),
                        missedCallsAmount,
                        unreadAmount,
                    ),
                    enabledOffline: true,
                })
            }

            if (privileges.SYS_USE_FAVORITES_TAB) {
                items.push({
                    key: 'favorites' as MenuItemKey,
                    page: AppRoutes.Favorites,
                    onClick: navigateTo(AppRoutes.Favorites),
                    children: getMenuItemContentWithIconAndName(
                        IconNavFavorites,
                        t.ADDRESS_BOOK_AREA.addressBook_favorites(),
                    ),
                    enabledOffline: true,
                })
            }

            items.push(
                {
                    key: 'directory' as MenuItemKey,
                    page: AppRoutes.Directory,
                    onClick: navigateTo(AppRoutes.Directory),
                    children: getMenuItemContentWithIconAndName(
                        IconNavDirectory,
                        t.ADDRESS_BOOK_AREA.addressBook_directory(),
                    ),
                    enabledOffline: true,
                },
                {
                    key: 'dialpad' as MenuItemKey,
                    page: AppRoutes.Dial,
                    onClick: navigateTo(AppRoutes.Dial),
                    children: getMenuItemContentWithIconAndName(IconNavDial, t.ADDRESS_BOOK_AREA.addressBook_dial()),
                    enabledOffline: true,
                })

            if (privileges.CAN_USE_CALENDAR) {
                items.push({
                    key: 'calendar' as MenuItemKey,
                    page: AppRoutes.Calendar,
                    onClick: navigateTo(AppRoutes.Calendar),
                    children: getMenuItemContentWithIconAndName(
                        IconNavCalendar,
                        t.ADDRESS_BOOK_AREA.addressBook_calendar(),
                    ),
                    enabledOffline: true,
                })
            }

            items.push({
                key: 'knowledge-base' as MenuItemKey,
                page: AppRoutes.KnowledgeBase,
                onClick: navigateTo(AppRoutes.KnowledgeBase),
                children: getMenuItemContentWithIconAndName(IconKnowledgeBase, t.KNOWLEDGE_BASE.kb_heading()),
            },
            {
                key: 'settings' as MenuItemKey,
                onClick: toggleSettings,
                children: getMenuItemContentWithIconAndName(IconNavSettings, t.GLOBAL.settings()),
                enabledOffline: true,
            },
            {
                key: 'help' as MenuItemKey,
                page: AppRoutes.Help,
                onClick: navigateTo(AppRoutes.Help),
                children: getMenuItemContentWithIconAndName(IconHelp, t.GLOBAL.help()),
                enabledOffline: true,
            },
            )

            return items
        },
        [
            privileges.SYS_USE_RECENT_TAB,
            privileges.SYS_USE_FAVORITES_TAB,
            privileges.CAN_USE_CALENDAR,
            navigateTo,
            getMenuItemContentWithIconAndName,
            t.ADDRESS_BOOK_AREA,
            t.KNOWLEDGE_BASE,
            t.GLOBAL,
            toggleSettings,
            missedCallsAmount,
            unreadAmount,
        ],
    )
}
