import {useEffect} from 'react'

import {useIntegrationApiStateMethods} from '@bpinc/ad-integration-api-state-context'
import type {Toggle} from '@bpinc/lib-helpers'
import type {StateSetter} from '@bpinc/lib-types-helpers'

import {useBPClientStateSubscription} from './useBPClientStateSubscription'
import {useIntegrationApiSubscription} from './useIntegrationApiSubscription'
import {useInteractionChatStateSubscription} from './useInteractionChatStateSubscription'
import {useInteractionStateSubscription} from './useInteractionStateSubscription'
import {useRemoteAssistStateSubscription} from './useRemoteAssistStateSubscription'


interface Params {
    toggleSettings: Toggle
    toggleRightPanel: Toggle
    isMinimized: boolean
    setIsMinimized: StateSetter<boolean>
}
export function useStateSubscriptions({toggleRightPanel, toggleSettings, isMinimized, setIsMinimized}: Params) {
    /*
        Subscriptions
    */
    useInteractionStateSubscription()
    useInteractionChatStateSubscription()
    useBPClientStateSubscription()
    useRemoteAssistStateSubscription()
    useIntegrationApiSubscription({toggleRightPanel, toggleSettings, setIsMinimized})

    /*
        Dispatchers
    */
    const {widgetMinimizedChange} = useIntegrationApiStateMethods()

    useEffect(() => {
        widgetMinimizedChange(isMinimized)
    }, [widgetMinimizedChange, isMinimized])
}
