import React, {useCallback, useEffect, useRef} from 'react'
import cn from 'classnames'

import {useCaseState, useCaseStateMethods} from '@bpinc/ad-case-state-context'
import type {Activity} from '@bpinc/ad-case-state-types'
import {useFormatting} from '@bpinc/ad-formatting-context'
import {AppRoutes, useNavigationState} from '@bpinc/lib-communicator-navigation-state'
import {SmButton} from '@bpinc/lib-external-ui-elements'
import {Loader} from '@bpinc/lib-ui-elements'

import {getAssignee} from '../utils'
import {ChatPreview} from './ChatPreview'
import {EmailPreview} from './EmailPreview'
import {Info} from './Info'
import {NotesPreview} from './NotesPreview'
import {Reporter} from './Reporter'
import {VoicePreview} from './VoicePreview'


interface Props {
    id: string
}


export const Case: React.FC<Props> = ({id}) => {
    const {setActivePage, goBack} = useNavigationState(['setActivePage', 'goBack'])
    const {formatFullName} = useFormatting()
    const cases = useCaseState('cases')
    const {getCase} = useCaseStateMethods()
    const {data = null, recordings = undefined, assignments = undefined, isLoading = true} = cases[id] || {}
    const assignedTo = getAssignee(assignments, formatFullName)

    const handleEmailOpen = useCallback((emailId: string) => {
        setActivePage({
            name: AppRoutes.Cases,
            params: {id, emailId},
        })
    }, [setActivePage, id])

    useEffect(() => {
        !cases[id] && getCase(id)
        // do not track methods change
        // eslint-disable-next-line
    }, [id])

    const scrollRef = useRef<HTMLDivElement>(null)
    const caseListItemClassName = cn('[&:not(:first-child)]:mt-2')

    return (
        <div className="relative h-full overflow-auto p-4" ref={scrollRef}>
            <div className="absolute left-0 top-0 cursor-pointer px-4 py-3">
                <SmButton icon="angle left" onClick={goBack} basic compact />
            </div>

            {isLoading ? (
                <Loader
                    size="large"
                    color="primary"
                    active
                />
            ) : !data ? (
                <div className="text-center">
                    Error occurred while loading data
                </div>
            ) : (
                <div>
                    <div className="mb-[1em] truncate px-[3em] text-center text-[1.2em]">
                        {`#${data.number} ${data.title}`}
                    </div>

                    <Reporter data={data} />

                    <Info title="State">{data.status}</Info>
                    {assignedTo && <Info title="Assigned to">{assignedTo}</Info>}
                    {data.categoryName && <Info title="Category">{data.categoryName}</Info>}

                    {data.activities.length > 0 ? (
                        <div>
                            {data.activities.map((activity: Activity) => {
                                if (activity.mediaType === 'NOTES') {
                                    return (
                                        <div key={activity.id} className={caseListItemClassName}>
                                            <NotesPreview activity={activity} />
                                        </div>
                                    )
                                } else if (activity.mediaType === 'EMAIL') {
                                    return (
                                        <div key={activity.id} className={caseListItemClassName}>
                                            <EmailPreview
                                                activity={activity}
                                                onOpenClick={handleEmailOpen}
                                            />
                                        </div>
                                    )
                                } else if (activity.mediaType === 'VOICE') {
                                    return (
                                        <div key={activity.id} className={caseListItemClassName}>
                                            <VoicePreview activity={activity} scrollRef={scrollRef} recordings={recordings} />
                                        </div>
                                    )
                                } else if (activity.mediaType === 'CHAT') {
                                    return (
                                        <div key={activity.id} className={caseListItemClassName}>
                                            <ChatPreview activity={activity} />
                                        </div>
                                    )
                                }
                            })}
                        </div>
                    ) : (
                        <div>No activities</div>
                    )}
                </div>
            )}
        </div>
    )
}
