import {useCaseStateMethods} from '@bpinc/ad-case-state-context'
import {useActivityHistoryOpener, useCallHandler} from '@bpinc/ad-interaction-state-hooks'
import type {InitiateCall} from '@bpinc/ad-interaction-state-types'
import {useEvent} from '@bpinc/lib-helpers'

import type {OnEmailParams} from '../user/types'


export function useContentHandlers() {

    const {createCaseEmail} = useCaseStateMethods()

    const handleCall = useCallHandler()
    const openActivityHistory = useActivityHistoryOpener()

    const onCall = useEvent((callParams: InitiateCall) => {
        handleCall({...callParams, navigateToInteractionsPage: true})
    })

    const onEmail = useEvent((emailParams: OnEmailParams) => {
        createCaseEmail(emailParams)
    })

    return {
        onChat: openActivityHistory,
        onCall,
        onEmail,
    }
}
