import React, {useEffect} from 'react'

import type {BaseConnectorsConsumerComponent} from '@bpinc/lib-authorization-module'
import {connectIntegrationApiBase} from '@bpinc/lib-communicator-integration-api-connection'


export const IntegrationApiBaseInitializer: BaseConnectorsConsumerComponent = ({baseConnectors, children}) => {
    useEffect(() => {
        const subscriptions = connectIntegrationApiBase(baseConnectors)
        return () => {
            subscriptions.forEach(s => s.unsubscribe())
        }
    }, [baseConnectors])

    return <>{children}</>
}
