import type React from 'react'
import {useCallback, useMemo} from 'react'

import {useCommunicatorUIState} from '@bpinc/lib-communicator-ui-context'


export function useHandlers() {
    const {toggleRightPanel, toggleSettings} = useCommunicatorUIState(['toggleRightPanel', 'toggleSettings'])

    const closeMenu = useCallback(
        function closeMenuCallback() {
            toggleSettings(false)
            toggleRightPanel(false)
        },
        [toggleRightPanel, toggleSettings],
    )

    const openSettingsMenu = useCallback(
        function openSettingsMenuCallback() {
            toggleSettings(true)
            toggleRightPanel(true)
        },
        [toggleSettings, toggleRightPanel],
    )

    const handleMenuKeyPress = useCallback(
        (e: React.KeyboardEvent) => {
            if (['Escape'].includes(e.key)) {
                e.preventDefault()
                closeMenu()
            }
        },
        [closeMenu],
    )

    return useMemo(
        function memoizeOutput() {
            return {
                toggleSettings,
                closeMenu,
                openSettingsMenu,
                handleMenuKeyPress,
            }
        },
        [
            toggleSettings,
            closeMenu,
            openSettingsMenu,
            handleMenuKeyPress,
        ],
    )
}
