import React, {useCallback} from 'react'
import {Button} from '@servicepattern/ui'
import {partial} from 'lodash-es'

import {contactToShortContact} from '@bpinc/ad-contacts-state-methods'
import type {ContactType} from '@bpinc/ad-contacts-state-types'
import {useInteractionStateMethods} from '@bpinc/ad-interaction-state-context'
import {useCallHandler} from '@bpinc/ad-interaction-state-hooks'
import type {InitiateCall} from '@bpinc/ad-interaction-state-types'
import {AppRoutes, useNavigationState} from '@bpinc/lib-communicator-navigation-state'
import {useHandleUseContact, useIsUseContactButtonVisible} from '@bpinc/lib-contact-details'
import {IconChat, IconPhone24} from '@bpinc/lib-jskit-ui'
import {InteractionDropdown, Svg} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import iconUseContact from '../../images/use-contact.svg'


interface Props {
    user: ContactType
}

const getId = (user: ContactType) => user.Id.oid

const getPhone = (user: ContactType) => {
    if (user.primaryPhone) {
        return user.primaryPhone.phone
    }
    return user.phones?.length ? user.phones[0].phone : ''
}

const getFirstName = (user: ContactType) => {
    return user.firstName
}

const getLastName = (user: ContactType) => {
    return user.lastName
}

export const ExternalUserControls: React.FC<Props> = ({user}) => {
    const {t} = useLocalizationState()

    const [contactId, phone, firstName, lastName] = [
        getId(user),
        getPhone(user),
        getFirstName(user),
        getLastName(user),
    ]

    const interactionStateMethods = useInteractionStateMethods()

    const setActivePage = useNavigationState('setActivePage')

    const isUseButtonVisible = useIsUseContactButtonVisible()
    const handleUse = useHandleUseContact()

    const handleChat = useCallback(() => {
        if (contactId) {
            interactionStateMethods.createPotentialItem({
                phone,
                contactId,
                firstName: user.firstName,
                lastName: user.lastName,
            })
            setActivePage({name: AppRoutes.Interactions})
        }
    }, [
        contactId,
        interactionStateMethods,
        phone,
        setActivePage,
        user.firstName,
        user.lastName,
    ])

    const onCall = useCallHandler()

    const handleCall = useCallback(
        (initiateCallParams: InitiateCall) => {
            onCall({...initiateCallParams, navigateToInteractionsPage: true})
        },
        [onCall],
    )


    return (
        <div className="flex flex-none items-center">
            {isUseButtonVisible && (
                <Button
                    onClick={partial(handleUse, contactToShortContact(user))}
                    tooltip={t.UNDEFINED.button_use_contact_title()}
                    data-testid="use-contact-button"
                    variant="ghost"
                    icon={<Svg src={iconUseContact} className="!h-3.5 !w-3.5" />} // TODO: change icon when it's created by designers
                />
            )}
            <Button
                onClick={handleChat}
                tooltip={t.CONTACT.button_initiate_chat_text()}
                data-testid="chat-button"
                variant="ghost"
                icon={<IconChat />}
            />
            <InteractionDropdown
                searchMode="none"
                callParams={{
                    phone,
                    firstName,
                    lastName,
                    contactId,
                    origination: 'contact-profile',
                }}
                onCall={handleCall}
                trigger={({handleCallClick, isLoading, openerRef}) => (
                    <Button
                        onClick={handleCallClick}
                        tooltip={t.CONTACT.button_start_call_text()}
                        state={isLoading ? 'loading' : undefined}
                        ref={openerRef}
                        data-testid="call-button"
                        variant="ghost"
                        icon={<IconPhone24 />}
                    />
                )
                }
            />
        </div>
    )
}
