import {useCallback, useState} from 'react'

import {useInteractionStateMethods} from '@bpinc/ad-interaction-state-context'
import type {Item} from '@bpinc/ad-interaction-state-types'
import type {ActivePage} from '@bpinc/lib-communicator-navigation-state'
import {AppRoutes, useNavigationState} from '@bpinc/lib-communicator-navigation-state'
import type {Toggle} from '@bpinc/lib-helpers'


interface Params {
    toggleSettings: Toggle
    toggleRightPanel: Toggle
    toggleLeftPanel: Toggle
    onMenuItemClick: (name: AppRoutes, params?: Record<string, unknown>) => void
}

export function useMinimized({toggleSettings, toggleLeftPanel, onMenuItemClick, toggleRightPanel}: Params) {
    const [isMinimized, setIsMinimized] = useState<boolean>(false)

    const {activateItem: activateInteractionItem} = useInteractionStateMethods()

    const setActivePage = useNavigationState('setActivePage')

    const onMinimizedMenuItemClick = useCallback(
        (name: AppRoutes, params?: Record<string, unknown>) => {
            setIsMinimized(false)
            onMenuItemClick(name, params)
        },
        [onMenuItemClick],
    )

    const onMinimizedSettingsClick = useCallback(() => {
        setIsMinimized(false)
        toggleRightPanel(true)
        toggleSettings(true)
    }, [toggleRightPanel, toggleSettings])

    const onMinimizedAgentStateClick = useCallback(() => {
        setIsMinimized(false)
        toggleLeftPanel(true)
    }, [toggleLeftPanel])

    const onMinimizedInteractionClick = useCallback(
        (item: Item) => {
            setIsMinimized(false)
            activateInteractionItem(item.id)

            const page: ActivePage = {
                name: AppRoutes.Interactions,
            }

            if (item.userId) page.params = {userId: item.userId}
            if (item.contactId) page.params = {contactId: item.contactId}

            setActivePage(page)
        },
        [activateInteractionItem, setActivePage],
    )

    return {
        isMinimized,
        setIsMinimized,
        onMinimizedMenuItemClick,
        onMinimizedSettingsClick,
        onMinimizedAgentStateClick,
        onMinimizedInteractionClick,
        onMenuItemClick,
    }
}
