import React, {useEffect} from 'react'

import {useCaseState, useCaseStateMethods} from '@bpinc/ad-case-state-context'
import {formatDate, formatEmail} from '@bpinc/ad-formatting-helpers'
import {useNavigationState} from '@bpinc/lib-communicator-navigation-state'
import {SmButton} from '@bpinc/lib-external-ui-elements'
import {Loader} from '@bpinc/lib-ui-elements'

import {EmailContent} from './EmailContent'
import {Info} from './Info'


interface Props {
    id: string
}

export const Email: React.FC<Props> = ({id}) => {
    const goBack = useNavigationState('goBack')
    const emails = useCaseState('emails')
    const {getEmail} = useCaseStateMethods()
    const {data = null, isLoading = true} = emails[id] || {}

    useEffect(() => {
        !emails[id] && getEmail(id)
        // do not track methods change
        // eslint-disable-next-line
    }, [id, emails])

    return (
        <div className="relative p-4">
            <div className="absolute left-0 top-0 cursor-pointer px-4 py-3">
                <SmButton icon="angle left" onClick={goBack} basic compact />
            </div>

            {isLoading ? (
                <Loader
                    size="large"
                    color="primary"
                    active
                />
            ) : !data ? (
                <div className="text-center">
                    Error occurred while loading data
                </div>
            ) : (
                <>
                    <div className="mb-[1em] truncate px-[3em] text-center text-[1.2em]">
                        {data.subject}
                    </div>
                    {data.fromAddresses && data.fromAddresses.length > 0 && (
                        <Info title="From">
                            {data.fromAddresses.map(formatEmail).join(', ')}
                        </Info>
                    )}
                    {data.toAddresses && data.toAddresses.length > 0 && (
                        <Info title="To">
                            {data.toAddresses.map(formatEmail).join(', ')}
                        </Info>
                    )}
                    <Info title="Date">
                        {formatDate(data.date)}
                    </Info>

                    <EmailContent html={data.content} />
                </>
            )}
        </div>
    )
}
