import React from 'react'

import {useInteractionStateSelector} from '@bpinc/ad-interaction-state-context'
import {getAssociatedCallItem} from '@bpinc/ad-interaction-state-methods'

import {useMessagesList} from '../../../hooks/useMessagesList'
import type {ChatProps} from '../../../types'
import {VoiceTranscript, VoiceTranscriptStateProvider} from './index'
import {useBotSuggestions} from './selectors'


export const VoiceTranscriptMessages: React.FC<ChatProps> = React.memo(function ExternalMessages({
    itemId,
}) {
    const callItem = useInteractionStateSelector(state => getAssociatedCallItem({item: state.items.find(i => i.id === itemId), items: state.items}))
    const botSuggestions = useBotSuggestions(itemId)
    const {messagesList} = useMessagesList(itemId)

    if (!callItem) {
        return null
    }

    return (
        <VoiceTranscriptStateProvider>
            <VoiceTranscript
                botSuggestions={botSuggestions}
                voiceTranscript={callItem?.voiceTranscript}
                chatMessages={messagesList}
                itemId={callItem?.id}
            />
        </VoiceTranscriptStateProvider>
    )
})
