import React, {useMemo} from 'react'
import {partial} from 'lodash-es'

import {useEmiItemStatus} from '@bpinc/ad-emi-helpers'
import {useDefaultTransferMode} from '@bpinc/ad-interaction-state-context'
import {useNativeScrollDataManager} from '@bpinc/ad-scroll-state-context-builder'
import {useSessionState} from '@bpinc/ad-session-context'
import {ContactListPlaceholder} from '@bpinc/lib-contact-list-item-ui'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {ErrorBoundary, SelectionContainer} from '@bpinc/lib-ui-elements'
import {useFavoritesState} from '@bpinc/state-favorites-context'

import {SCROLL_KEYS} from '../../../scroll-context'
import {useFilteredAndSortedFavoritesList} from '../../hooks/useFilteredAndSortedFavoritesList'
import type {FavoritesListProps} from '../../types'
import {FavoritesListItem} from './FavoritesListItem'
import {getUniqueChannelId} from './getUniqueChannelId'
import {useDetailsButtonClickHandler} from './useDetailsButtonClickHandler'
import {useItemClickHandler} from './useItemClickHandler'


export const FavoritesList: React.FC<FavoritesListProps> = props => {
    const defaultTransferMode = useDefaultTransferMode()

    const {isHighlighted, transferMode = defaultTransferMode, transferControlsStyle, onClick} = props
    const {favoritesList, isLoading} = useFavoritesState()
    const emi = useSessionState('emi')

    const handleDetailsClick = useDetailsButtonClickHandler()
    const handleClick = useItemClickHandler(onClick)
    const sortedAndFilterFavoritesList = useFilteredAndSortedFavoritesList({
        ...props,
        favoritesList,
    })

    const useEmiItemStatusCurried = useMemo(() => partial(useEmiItemStatus, emi), [emi])
    const listRef = useNativeScrollDataManager(SCROLL_KEYS.favorites())

    return (
        <SelectionContainer
            ref={listRef}
            className={tcn(
                'flex h-full min-h-0 flex-auto flex-col',
                'scrollbar-on-hover overflow-auto',
                {'overflow-hidden': isLoading},
            )}
        >
            {/* without this extra wrapper with filled background "scrollbar-on-hover" produces visual artifacts */}
            <div className="bg-primary-background">
                {
                    isLoading ? (
                        <div className="mt-2">
                            <ContactListPlaceholder amount={15} />
                        </div>
                    ) : (sortedAndFilterFavoritesList.map(listItem => (
                        <ErrorBoundary key={getUniqueChannelId(listItem)} fallback={null}>
                            <FavoritesListItem
                                {...listItem}
                                isHighlighted={isHighlighted?.(listItem)}
                                transferMode={transferMode}
                                transferControlsStyle={transferControlsStyle}
                                onClick={handleClick}
                                onDetailsClick={handleDetailsClick}
                                useEmiItemStatus={useEmiItemStatusCurried}
                            />
                        </ErrorBoundary>
                    )))
                }
            </div>
        </SelectionContainer>
    )
}
