import type {RefObject} from 'react'
import {useLayoutEffect, useRef} from 'react'

import {useNonAssertedInteractionId} from '@bpinc/lib-interaction-context'
import {useShowSlidingModalChecker} from '@bpinc/lib-sliding-modal-context'


export function useTransferRecentConferenceScrollManager(scrollRef: RefObject<HTMLDivElement>) {
    const interactionId = useNonAssertedInteractionId()
    const showRecentConferenceModal = useShowSlidingModalChecker(interactionId, 'recentConference')


    const scrollPositionRef = useRef(0)
    useLayoutEffect(() => {
        if (showRecentConferenceModal) {
            scrollPositionRef.current = scrollRef.current?.scrollTop ?? 0
            scrollRef.current?.scrollTo({top: 0})
        } else {
            requestAnimationFrame(() => {
                scrollRef.current?.scrollTo({top: scrollPositionRef.current})
                scrollPositionRef.current = 0
            })
        }
    }, [scrollRef, showRecentConferenceModal])
}
