import type React from 'react'

import type {CreateCaseEmail} from '@bpinc/ad-case-state-types'
import type {ContactShortType} from '@bpinc/ad-contacts-state-types'
import type {
    DirectoryEntry,
    DirectoryItem,
    DirectoryStateUserObject,
    MsTeamsChannel,
    MsTeamsUser,
    SearchUserItem,
} from '@bpinc/ad-directory-state-types'
import type {OpenContactDetailsParams} from '@bpinc/ad-interaction-state-hooks'
import type {InitiateCall} from '@bpinc/ad-interaction-state-types'
import type {EmiUserType} from '@bpinc/ad-transport-types'
import type {RequiredBy} from '@bpinc/lib-types-helpers'


export interface ExternalUserProps extends DirectoryItemProps {
    contact: ContactShortType
}

export interface InternalCategoryUserProps extends DirectoryItemProps {
    item: DirectoryEntry
}

export interface MsTeamsChannelProps extends DirectoryItemProps {
    item: MsTeamsChannel
}

export interface MsTeamsUserProps extends DirectoryItemProps {
    user: MsTeamsUser
}

export interface InternalUserProps extends DirectoryItemProps {
    user: InternalUser
}

export type InternalUser = DirectoryStateUserObject | SearchUserItem

export function isSearchUser(user: InternalUser | MsTeamsUser): user is SearchUserItem {
    return (
        'userType' in user && user.userType === 'internal' &&
        'acdState' in user && 'isLoggedIn' in user
    )
}

export interface DirectoryItemProps {
    isSelected?: boolean
    onClick?: (evt: React.MouseEvent, item: DirectoryItem) => void
    onDoubleClick?: (item: DirectoryItem, callbacks?: OnClickCallbacks) => void
    onCall?: (callParams: InitiateCall) => void
    onChat?: (chatParams: OnChatParams) => void
    onEmail?: (emailParams: OnEmailParams) => void
    onDetails?: (params: OpenContactDetailsParams) => void
    transferMode?: 'messages' | 'calls'
    transferNumberInput?: string
    transferControlsStyle?: 'add' | 'replace'
    highlight?: string
    noControls?: boolean
    showControls?: boolean
    contentClassName?: string
    as?: React.ElementType
    containerProps?: Record<string, unknown>
    showFavoriteButton?: boolean
}

export interface OnClickCallbacks {
    onCall: () => void
}
export interface OnChatParams {
    userId?: string
    contactId?: string
    firstName: string
    lastName: string
    phone: string
    userType?: EmiUserType
    photoUrl?: string
    members?: string[]
    //chatId?: string
}

export type OnEmailParams = RequiredBy<CreateCaseEmail, 'emailAddress'>
