import {useGetAvatarUrl} from '@bpinc/ad-contacts-state-hooks'
import {useSessionState} from '@bpinc/ad-session-context'

import type {MultiAvatarItem, ParticipantItem} from './types'


export const useAvatarParticipantsList = (parties: ParticipantItem[]) => {
    const {userId: selfUserId, firstName, lastName} = useSessionState()
    const getAvatarUrl = useGetAvatarUrl()
    const avatarItemsProps: MultiAvatarItem[] = parties.map(item => ({
        firstName: item.firstName,
        lastName: item.lastName,
        type: item.userId ? 'user' as const : item.contactId ? 'contact' as const : 'unknown' as const,
        url: getAvatarUrl({userId: item.userId, contactId: item.contactId}),
    }))

    if (selfUserId) {
        avatarItemsProps.push({
            firstName,
            lastName,
            url: getAvatarUrl({userId: selfUserId}),
            type: 'user',
        })
    }
    return avatarItemsProps.slice(0, 3)
}

