import React from 'react'

import {useGetAvatarUrl} from '@bpinc/ad-contacts-state-hooks'
import {useFormatting} from '@bpinc/ad-formatting-context'
import {useSessionState} from '@bpinc/ad-session-context'
import {Avatar} from '@bpinc/lib-avatar-ui'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {useLocalizationState} from '@bpinc/state-localization-context'


export const SelfItem = () => {
    const getAvatarUrl = useGetAvatarUrl()
    const {t} = useLocalizationState()
    const {formatFullName} = useFormatting()
    const {userId, isAgent, firstName, lastName} = useSessionState()
    const avatarUrl = getAvatarUrl({userId})
    return (
        <div
            tabIndex={0}
            className={tcn('group flex shrink-0 cursor-pointer items-center justify-between gap-2 rounded-xs',
                'px-2 py-1 transition-colors hover:state-layer-bg-dark-on-white-1',
            )}
        >
            <div className="flex items-center">
                <Avatar
                    size="16"
                    headPhones={isAgent}
                    url={avatarUrl}
                    firstName={firstName}
                    lastName={lastName}
                    type={isAgent ? 'user' : 'contact'}
                />
                <div className="ml-1 flex gap-x-1 overflow-hidden">
                    <div className="flex items-center overflow-hidden">
                        <div className="truncate font-semibold caption-s text-primary-on-primary" data-testid="display-name">
                            {formatFullName(firstName, lastName)}
                        </div>
                    </div>
                    <div className="truncate caption-s text-primary-on-primary">
                        {t.INTERACTIONS.self_item_name()}
                    </div>
                </div>
            </div>
        </div>
    )

}
