import type {DirectoryObject, DirectoryStateUserObject, SearchUserItem} from '@bpinc/ad-directory-state-types'


export const teamFilter = (teamId: string) => (user: DirectoryStateUserObject) => {
    return user.teamId === teamId
}

export const omitSelfInTeams = (teams: DirectoryObject[], selfTeamId?: string) => {
    return teams.map(team => {
        if (team.id === selfTeamId) {
            if (typeof team.usersCount !== 'undefined') {
                return {
                    ...team,
                    usersCount: team.usersCount - 1,
                }
            }
        }
        return team
    })
}

export const omitSelfInUsers = (
    users: Array<DirectoryStateUserObject | SearchUserItem>,
    selfUserId?: string,
) => {
    return users.filter(user => user.userId !== selfUserId)
}
