import React, {useEffect} from 'react'

import {useBpClientState, useBpClientStateConnector} from '@bpinc/ad-bpclient-state-context'
import {platformInfo} from '@bpinc/ad-build-and-platform-info'
import {useEvent} from '@bpinc/lib-helpers'
import {useInstallationMethods} from '@bpinc/lib-helpers-ui'
import {useLocalizationState} from '@bpinc/state-localization-context'
import {useModalsStateMethods} from '@bpinc/state-modals-context'


const linkClass = 'cursor-pointer text-primary-legacy'

const EXTENSION_ID = 'ext-link'
const EXTENSION_ID_FF = 'ext-link-firefox'
const PLUGIN_ID = 'plugin-link'

type ModalContentProps = {
    isExtAvailable: boolean
    install: boolean
}

const ModalContent: React.FC<ModalContentProps> = ({isExtAvailable, install}) => {
    const {t: {INSTALLATION_PROCESS: i18n}} = useLocalizationState()
    const {
        installChromeExtension,
        installFirefoxExtension,
        installBPClientPlugin,
    } = useInstallationMethods()

    /* Set click handler after the modal is rendered or translations are changed */
    useEffect(() => {
        const chromeLink = document.getElementById(EXTENSION_ID)
        const firefoxLink = document.getElementById(EXTENSION_ID_FF)
        const pluginLink = document.getElementById(PLUGIN_ID)

        chromeLink?.addEventListener('click', installChromeExtension)
        firefoxLink?.addEventListener('click', installFirefoxExtension)
        pluginLink?.addEventListener('click', installBPClientPlugin)

        return () => {
            chromeLink?.removeEventListener('click', installChromeExtension)
            firefoxLink?.removeEventListener('click', installFirefoxExtension)
            pluginLink?.removeEventListener('click', installBPClientPlugin)
        }
    }, [installChromeExtension, installFirefoxExtension, installBPClientPlugin])

    let content: string = i18n.install_updateHeader(PLUGIN_ID, linkClass)

    if (install) {
        if (platformInfo.browser === 'chrome') {
            content = isExtAvailable
                ? i18n.install_installHeader(PLUGIN_ID, linkClass)
                : i18n.install_installHeaderChrome(EXTENSION_ID, PLUGIN_ID, linkClass)

        } else if (platformInfo.browser === 'firefox') {
            content = isExtAvailable
                ? i18n.install_installHeader(PLUGIN_ID, linkClass)
                : i18n.install_installHeaderFirefox(EXTENSION_ID_FF, PLUGIN_ID, linkClass)

        } else {
            content = i18n.install_installHeader(PLUGIN_ID, linkClass)
        }
    }

    return <div dangerouslySetInnerHTML={{__html: content}} />
}

export const useInstallationRequiredPopup = () => {
    const {WINDOW_POPUP_TITLES_AND_MESSAGES: i18n} = useLocalizationState('t')
    const isExtensionAvailable = useBpClientState('isExtensionAvailable')
    const bpClientConnector = useBpClientStateConnector()
    const {openModal} = useModalsStateMethods()

    const onInstallationRequired = useEvent((install: boolean) => {
        openModal({
            title: i18n.window_title_warning(),
            body: (
                <ModalContent
                    install={install}
                    isExtAvailable={isExtensionAvailable}
                />
            ),
            actions: {
                ok: {
                    title: i18n.window_buttonOk(),
                },
            },
        })
    })

    useEffect(() => {
        const subscription = bpClientConnector.dispenser.subscribe({onInstallationRequired})
        return subscription.unsubscribe
    }, [bpClientConnector, onInstallationRequired])
}
