import type {FC} from 'react'
import React from 'react'

import {stopPropagation, useBoolean} from '@bpinc/lib-helpers'
import {ButtonIcon, IconTranscriptOptions, TriggerDropdown} from '@bpinc/lib-jskit-ui'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {useTranscriptOptions} from './hooks/useTranscriptOptions'


type Props = {
    isOpenModal?: boolean
    contentVoiceTranscript: JSX.Element | null
}

export const TranscriptDropdown: FC<Props> = React.memo(({contentVoiceTranscript, isOpenModal = false}) => {
    const {t} = useLocalizationState()
    const {value: open, toggle, setFalse} = useBoolean()
    const transcriptOptions = useTranscriptOptions({
        isOpenModal,
        contentVoiceTranscript,
        handleCloseView: setFalse,
    })

    return (
        <TriggerDropdown
            modal={false}
            open={open}
            onClick={stopPropagation}
            onFocus={stopPropagation}
            onOpenChange={toggle}
            className="rounded-xs"
            align="end"
            side="bottom"
            options={transcriptOptions.map(({renderIcon, optionName, onClick}, i) => (
                <button key={i} className="flex items-center gap-2" onClick={onClick}>
                    {renderIcon()}
                    <div className="min-w-[140px] caption-s text-primary-on-primary">{optionName}</div>
                </button>
            ))}
        >
            <ButtonIcon
                tooltip={
                    open
                        ? t.ACTIVITY_HISTORY.call_message_collapse_voice_transcript()
                        : t.ACTIVITY_HISTORY.call_message_expand_voice_transcript()
                }
                data-testid="voice-transcript-button"
                className="rounded-xs"

            >
                <IconTranscriptOptions className="size-4" />
            </ButtonIcon>
        </TriggerDropdown>
    )
})
