import type {FC} from 'react'
import React from 'react'
import type {ButtonProps} from '@servicepattern/ui'
import {Button} from '@servicepattern/ui'

import {IconEndCall} from '@bpinc/lib-jskit-ui'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {useLocalizationState} from '@bpinc/state-localization-context'


type Props = ButtonProps & {
    isDestructive?: boolean
    renderIcon?: () => JSX.Element
}
export const EndCallButton: FC<Props> = React.memo(({isDestructive, renderIcon, ...props}) => {
    const {t} = useLocalizationState()
    return (
        <Button
            //TODO: Add the click throttle
            variant={isDestructive ? 'destructive' : 'neutral'}
            size="sm"
            color="danger"
            className={tcn({'h-7 w-12 px-3 py-0.5': isDestructive, 'ml-2 h-6 px-2 py-1 text-on-error': !isDestructive})}
            tooltip={t.CALL_DETAILS_FORM.interaction_endCall()}
            data-testid="complete-button"
            {...props}
        >
            {renderIcon?.() || <IconEndCall className="size-5 svg-color-current" />}
        </Button>
    )
})
