import {useEffect} from 'react'

import {useAgentState} from '@bpinc/ad-agent-state-context'
import {useSessionState} from '@bpinc/ad-session-context'
import {useEvent} from '@bpinc/lib-helpers'
import {Notifications, useNotificationsState} from '@bpinc/lib-notifications-context'
import {useLocalizationState} from '@bpinc/state-localization-context'


export function useReservedForIncomingInteractionNotification() {
    const {showNotification, hideNotificationById} = useNotificationsState('methods')
    const {reservedForIncomingInteraction} = useAgentState()
    const isLoggedIn = useSessionState('isLoggedIn')
    const {GLOBAL: i18n} = useLocalizationState('t')

    const updateNotification = useEvent(() => {
        if (!isLoggedIn) {
            return
        }
        if (reservedForIncomingInteraction) {
            showNotification({
                type: 'info',
                id: Notifications.ReservedForIncomingInteractionMessage,
                message: i18n.callback_reservation_header(),
            })
        } else {
            hideNotificationById(Notifications.ReservedForIncomingInteractionMessage)
        }
    })

    useEffect(() => {
        updateNotification()
        // track only reservedForIncomingInteraction
    }, [reservedForIncomingInteraction, updateNotification])
}
