import {useEffect, useRef} from 'react'

import {useSessionState} from '@bpinc/ad-session-context'
import {useCommunicatorUIState} from '@bpinc/lib-communicator-ui-context'


export function useOpenSettingsOnPhoneSelectionDialogClosed() {
    const {offerPhoneSelection} = useSessionState()
    const prevOfferPhoneSelection = useRef<unknown>()
    const {toggleRightPanel, toggleSettings} = useCommunicatorUIState(['toggleRightPanel', 'toggleSettings'])

    useEffect(() => {
        // closed phone selection dialog
        if (prevOfferPhoneSelection.current && !offerPhoneSelection) {
            toggleRightPanel(true)
            toggleSettings(true)
        }
        prevOfferPhoneSelection.current = offerPhoneSelection

        // do not track methods change
        // eslint-disable-next-line
    }, [offerPhoneSelection])
}
