import type {FC} from 'react'
import React, {isValidElement, useCallback} from 'react'

import type {ChatTranscriptMessage as ChatTranscriptMessageType} from '@bpinc/ad-case-state-types'
import {useFormatting} from '@bpinc/ad-formatting-context'
import {useChatMessageSanitizer} from '@bpinc/ad-interaction-state-hooks'
import {paramsToQueryString, requestUrls} from '@bpinc/ad-request-urls'
import {useSessionState} from '@bpinc/ad-session-context'
import {formatTime, useInnerHTML} from '@bpinc/lib-helpers'

import {useMessage} from '../utils'
import {Info} from './Info'


export const ChatTranscriptMessage: FC<ChatTranscriptMessageType> = message => {
    const {formatFullName} = useFormatting()

    const {sessionId, domain, loginId} = useSessionState()

    const getFileLink = useCallback(
        function getFileLinkCallback(fileId: string) {
            const queryString = paramsToQueryString({
                token: sessionId,
                user: loginId,
                tenant: domain,
                id: fileId,
                no_disposition: 0,
            })
            return `${requestUrls.getChatAttachmentDownload}?${queryString}`
        },
        [domain, loginId, sessionId],
    )

    const messageNode = useMessage(message, formatFullName, getFileLink)
    const fileMessage = isValidElement(messageNode) ? messageNode : null

    const textMessageNormalizedByURLParser = useChatMessageSanitizer({message: messageNode})

    const textMessageHTML = useInnerHTML(textMessageNormalizedByURLParser)

    return (
        <Info title={formatTime(message.timestamp)}>
            {fileMessage || <span dangerouslySetInnerHTML={textMessageHTML} />}
        </Info>
    )
}
