import React from 'react'

import {useInteraction, useInteractionProp} from '@bpinc/ad-interaction-state-context'
import {
    isCall,
    isGroupChat,
    isInternalChat,
    isInternalInteraction,
    isPotentialGroupChat,
    isPotentialInteraction,
    isWorkItem,
} from '@bpinc/ad-interaction-state-methods'
import {useSessionState} from '@bpinc/ad-session-context'
import {useInteractionId} from '@bpinc/lib-interaction-context'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {createComponentLoader} from '@bpinc/lib-ui-elements'

import {useCallableItemChecker} from '../../hooks/useCallableItemChecker'
import {CallButton} from '../buttons/CallButton'
import {FlagButton} from '../buttons/FlagButton'
import {RemoteAssistButton} from '../buttons/RemoteAssistButton'
import {ReportProblemButton} from '../buttons/ReportProblemButton'


const {ChatMembersButtonProviderLoader} = createComponentLoader(
    'chatCenter',
    import('chatCenter'),
    'ChatMembersButtonProvider',
)

interface ActiveInteractionTopButtonsProps {
    className?: string
}

export const ActiveInteractionTopButtons: React.FC<
    ActiveInteractionTopButtonsProps
> = ({className}) => {
    const {tenantFeatures, privileges} = useSessionState()

    const interactionId = useInteractionId()
    const interaction = useInteraction(interactionId)

    const showFlagButton = useInteractionProp(
        interactionId,
        activeItem =>
            !isPotentialInteraction(activeItem) && !isInternalChat(activeItem),
    )
    const showReportProblemButton = isWorkItem(interaction) || isCall(interaction)

    const isCallableItem = useCallableItemChecker()
    const showCallButton = isCallableItem(interaction)

    const remoteAssistEnabled =
        !!tenantFeatures?.nativeRemoteAssistEnabled &&
        !!privileges.SYS_USE_REMOTE_ASSIST

    const showRemoteAssistButton =
        isInternalChat(interaction) && remoteAssistEnabled

    const showChatMembersButton =
        isGroupChat(interaction) ||
        isPotentialGroupChat(interaction) ||
        isInternalInteraction(interaction)


    return (
        <div className={tcn('flex items-center gap-2', className)}>
            {showFlagButton && <FlagButton />}
            {showReportProblemButton && <ReportProblemButton />}
            {showRemoteAssistButton && <RemoteAssistButton />}
            {showCallButton && <CallButton />}
            {showChatMembersButton && (
                <ChatMembersButtonProviderLoader
                    scope="communicator"
                    size="16"
                />
            )}
        </div>
    )
}
