import React, {useMemo} from 'react'

import type {Item} from '@bpinc/ad-interaction-state-types'
import {useSessionState} from '@bpinc/ad-session-context'
import {useSubscribeForUserDetails, useUserDetailsState} from '@bpinc/ad-user-details-state-context'
import {assertDefined} from '@bpinc/lib-types-helpers'
import {createComponentLoader} from '@bpinc/lib-ui-elements'


const {AgentSupervisorControlsLoader} = createComponentLoader('supervision', () => import('supervision'), 'AgentSupervisorControls')

type SupervisorButtonsProps = {
    item: Item
}

export const SupervisorButtons: React.FC<SupervisorButtonsProps> = ({item}) => {
    const selfUserId = useSessionState('userId')

    const userIds = useMemo(() => {
        const chatUserIds = (item.allChatParties || [])
            .filter(p => p.user_id && p.is_qm !== '1' && p.user_id !== selfUserId)
            .map(p => p.user_id || '')

        const callUserIds = (item.allParties || [])
            .filter(p => p.userId && p.role !== 'supervisor' && p.userId !== selfUserId)
            .map(p => p.userId || '')

        return [...chatUserIds, ...callUserIds]
    }, [item.allChatParties, item.allParties, selfUserId])

    useSubscribeForUserDetails(userIds)

    const qmMonitoredItemId = item.scenarioData?.qmMonitoredItemId
    assertDefined(qmMonitoredItemId)

    const userId = useUserDetailsState(s => {
        const itemUserMap = userIds.reduce<Record<string, string>>((result, id) => {
            const {userId: userDataId, items} = s.userDetails[id]?.data || {}
            if (!userDataId || !items?.length) {
                return result
            }
            items.forEach(i => {
                result[i.id] = userDataId
            })
            return result
        }, {})

        return itemUserMap[qmMonitoredItemId]
    })

    if (!userId) {
        return null
    }

    return <AgentSupervisorControlsLoader userId={userId} itemId={qmMonitoredItemId} />
}
