import {useCallback, useEffect, useRef} from 'react'


export const MINIMAL_PROFILING_VALUE = 100

export function useCPUProfiling() {
    const profilingValueRef = useRef(0)

    useEffect(() => {
        let wasUnmounted = false

        async function runProfilling() {
            const tick = () => new Promise(resolve => {
                setTimeout(() => {
                    profilingValueRef.current++
                    resolve(profilingValueRef.current)

                    if (!wasUnmounted) {
                        tick()
                    }
                })
            })

            profilingValueRef.current = 0
            tick()
        }

        runProfilling()

        return () => {
            wasUnmounted = true
        }
    }, [])

    const getAndResetProfilingValue = useCallback(() => {
        const savedProfilingValue = profilingValueRef.current
        profilingValueRef.current = 0

        return savedProfilingValue
    }, [])

    return getAndResetProfilingValue
}
