import React from 'react'
import {useAtomValue, useSetAtom} from 'jotai'

import {isPhone} from '@bpinc/ad-formatting-helpers'
import type {InitiateCall} from '@bpinc/ad-interaction-state-types'
import {useServiceState} from '@bpinc/ad-service-context'
import {useSessionState} from '@bpinc/ad-session-context'
import {useEvent} from '@bpinc/lib-helpers'
import {SearchField} from '@bpinc/lib-jskit-ui'
import {type ChatParams, SearchForInteractionControls} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'
import type {RecentItem} from '@bpinc/state-recent-types'

import {stateAtom} from './stateAtom'


type Props = {
    recentItem: RecentItem
    onCall: (callParams: InitiateCall) => void
    onChat: (chatParams: ChatParams) => void
}

export const Footer: React.FC<Props> = ({recentItem, onCall, onChat}) => {

    const {RECENT: i18n} = useLocalizationState('t')
    const phoneDeviceCapabilities = useSessionState('phoneDeviceCapabilities')
    const {selectedService} = useServiceState()

    const {selected, searchValue} = useAtomValue(stateAtom)
    const dispatch = useSetAtom(stateAtom)

    const handleChange = useEvent((e: React.ChangeEvent<HTMLInputElement>) => {
        // digits only
        const value = e.target.value.replace(/\D/g, '')
        dispatch({type: 'setSearchValue', payload: {value}})
    })

    const handleCallClick = useEvent(() => {
        onCall({
            phone: searchValue,
            firstName: selected?.firstName,
            lastName: selected?.lastName,
            serviceId: selectedService ?? recentItem.serviceId,
            contactId: selected?.contactId,
            userId: selected?.userId,
            origination: 'recent',
        })
    })

    const handleChatClick = useEvent(() => {
        onChat({
            phone: searchValue,
            firstName: selected?.firstName,
            lastName: selected?.lastName,
            serviceId: selectedService ?? recentItem.serviceId,
            contactId: selected?.contactId,
            userId: selected?.userId,
            origination: 'recent',
        })
    })

    return (
        <div className="flex grow items-start gap-2.5">
            <SearchField
                value={searchValue}
                onChange={handleChange}
                layoutClassName="grow"
                data-testid="search-input"
                aria-label={i18n.conference_details_input_placeholder()}
                placeholder={i18n.conference_details_input_placeholder()}
            />
            <SearchForInteractionControls
                searchQuery={searchValue}
                showCallButton={!!phoneDeviceCapabilities.canDial}
                callButtonDisabled={!selected && !isPhone(searchValue)}
                chatButtonDisabled={!selected && !isPhone(searchValue)}
                onCallClick={handleCallClick}
                onChatClick={handleChatClick}
            />
        </div>
    )
}
