import React from 'react'

import {
    isQualityManagementItemInMonitorMode,
    isQualityManagementItemInWhisperMode,
} from '@bpinc/ad-interaction-state-methods'
import type {Item} from '@bpinc/ad-interaction-state-types'
import {IconCoaching, IconMonitor} from '@bpinc/lib-jskit-ui'


type MonitorIcon = {
    item: Item
}

const className = 'ml-1 size-4 icon-neutral'

export const MonitorIcon: React.FC<MonitorIcon> = ({item}) => {
    if (isQualityManagementItemInMonitorMode(item)) {
        return <IconMonitor className={className} />
    }

    if (isQualityManagementItemInWhisperMode(item)) {
        return <IconCoaching className={className} />
    }

    return null
}
