import {useCallback} from 'react'

import {useFormatting} from '@bpinc/ad-formatting-context'
import {formatPhone} from '@bpinc/ad-formatting-helpers'
import {useLocalizationState} from '@bpinc/state-localization-context'
import type {RecentPartyType} from '@bpinc/state-recent-types'


export function useConfPartiesToString() {
    const t = useLocalizationState('t')
    const {formatFullName} = useFormatting()

    return useCallback((parties: RecentPartyType[]) => {
        return parties.map(party => {
            if (party.lastName) {
                return formatFullName(party.firstName, party.lastName)
            }
            if (party.phone) {
                return formatPhone(party.phone)
            }
            return t.GLOBAL.anonymous()
        }).join(', ')
    }, [formatFullName, t.GLOBAL])
}

