import React from 'react'

import {createComponentLoader} from '@bpinc/lib-ui-elements'


const {
    CalendarNavigationLoader,
    CalendarViewSelectLoader,
    CalendarViewTitleLoader,
} = createComponentLoader(
    'calendar',
    () => import('calendar'),
    'CalendarNavigation',
    'CalendarViewSelect',
    'CalendarViewTitle',
)

export const CalendarHeader = React.memo(() => {
    return (
        <div className="my-2 grid grid-cols-[150px_1fr_150px] px-4 py-0">
            <CalendarNavigationLoader />

            <div className="align-center flex justify-center text-base">
                <CalendarViewTitleLoader />
            </div>

            <div className="flex justify-end">
                <CalendarViewSelectLoader />
            </div>
        </div>
    )
})
