import {useCallback, useRef} from 'react'

// eslint-disable-next-line
type AnyFunction = (...args: any[]) => any

/**
 * A custom hook that ensures a stable function reference across component re-renders.
 * This can be useful when passing callbacks to deeply nested child components that rely on
 * reference equality to prevent unnecessary renders.
 *
 * @param fn - The function for which to maintain a stable reference.
 * @returns A callback function with a stable reference, which calls the provided function `fn`.
 *
 * @template F - The type of the function provided to the hook. It must extend AnyFunction.
 *
 * @example
 * const handleClick = useEvent(() => {
 *   console.log('Button clicked');
 * });
 *
 * // `handleClick` can now be passed as a prop without causing unnecessary re-renders
 * // due to reference changes.
 */
export function useEvent<F extends AnyFunction>(fn: F): F {
    const fnRef = useRef(fn)
    // Update the current ref to the latest version of `fn` on each render
    fnRef.current = fn

    // `useCallback` is used here to return a memoized version of the function
    // that only changes if one of the dependencies has changed. In this case,
    // the dependency array is empty, so it never changes.
    return useCallback((...args: Parameters<F>): ReturnType<F> => {
        // Call the latest version of `fn` using the current parameters
        return fnRef.current(...args)
    }, []) as F
}
