import {useEffect} from 'react'

import {useBpClientState} from '@bpinc/ad-bpclient-state-context'
import {bpLogger} from '@bpinc/ad-persistent-logger'
import {useSessionState, useSessionStateConnector} from '@bpinc/ad-session-context'
import type {PhoneCapabilities, ServerErrorSource} from '@bpinc/ad-session-types'
import {useEvent} from '@bpinc/lib-helpers'
import {Notifications, useNotificationsState} from '@bpinc/lib-notifications-context'
import {useLocalizationState} from '@bpinc/state-localization-context'


const PHONE_OUT_OF_SERVICE_ERROR_DELAY = 5000

export const usePhoneOutOfServiceNotification = () => {
    const sessionStateConnector = useSessionStateConnector()
    const isHelperAppInstanceCreated = useBpClientState('isHelperAppInstanceCreated')
    const {showNotification, hideNotificationById} = useNotificationsState('methods')
    const phoneType = useSessionState('phoneType')
    const t = useLocalizationState('t')

    const onUpdateCapabilities = useEvent((caps: PhoneCapabilities) => {
        if (!caps.canDial && phoneType !== 'phone_type_none') {
            bpLogger.warn('SIP phone or external line is out of service')

            let message: string | undefined
            switch (phoneType) {
                case 'phone_type_nailed':
                    message = t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_messages_virtual_phone_not_available()
                    break
                case 'phone_type_soft_secure':
                case 'phone_type_soft_unsecure':
                    if (isHelperAppInstanceCreated) {
                        message = t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_messages_phone_not_available()
                    } else {
                        message = t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_messages_helper_app_not_available()
                    }
                    break
                default:
                    message = t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_messages_phone_not_available()
            }

            showNotification({
                type: 'error',
                id: Notifications.PhoneOutOfServiceError,
                message,
                closable: false,
                showDelay: PHONE_OUT_OF_SERVICE_ERROR_DELAY,
            })
        } else {
            bpLogger.info('SIP phone or external line is in service')
            hideNotificationById(Notifications.PhoneOutOfServiceError)
        }
    })

    const onServerError = useEvent((source: ServerErrorSource, errorCode: number) => {
        if (source === 'agentServer' && errorCode === 4) {
            showNotification({
                type: 'warning',
                id: Notifications.PhoneOutOfServiceError,
                message: t.ERROR_MESSAGES.phoneIsNotRegistered(),
                closable: false,
                showDelay: PHONE_OUT_OF_SERVICE_ERROR_DELAY,
            })
        }
    })

    const onThreePCCServerGone = useEvent(() => {
        showNotification({
            type: 'warning',
            id: Notifications.PhoneOutOfServiceError,
            message: t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_messages_3pcc_server_connectionLost(),
            closable: false,
            showDelay: PHONE_OUT_OF_SERVICE_ERROR_DELAY,
        })
    })

    const onThreePCCServerReady = useEvent(() => {
        hideNotificationById(Notifications.PhoneOutOfServiceError)
    })

    useEffect(() => {
        const subscription = sessionStateConnector.dispenser.subscribe({
            onUpdateCapabilities,
            onServerError,
            onThreePCCServerGone,
            onThreePCCServerReady,
        })
        return subscription.unsubscribe
    }, [
        onUpdateCapabilities,
        onServerError,
        onThreePCCServerGone,
        onThreePCCServerReady,
        sessionStateConnector,
    ])
}
