import {useRef, useState} from 'react'


export const MAX_INTERACTIONS_PER_PAGE = 4

type Props = {
    itemIds: string[]
}

export function usePagination({itemIds}: Props) {
    const [currentPage, setCurrentPage] = useState<number>(0)

    const totalAmountRef = useRef(itemIds.length)
    totalAmountRef.current = itemIds.length

    const paginationIsEnabledRef = useRef(false)
    paginationIsEnabledRef.current = totalAmountRef.current > MAX_INTERACTIONS_PER_PAGE + 1

    const numberOfPages = Math.ceil(totalAmountRef.current / MAX_INTERACTIONS_PER_PAGE)
    const isLastPage = (page: number) => page + 1 >= numberOfPages
    const isFirstPage = (page: number) => page + 1 <= 1

    function goNextPage() {
        if (!isLastPage(currentPage)) {
            setCurrentPage(currentPage + 1)
        }
    }

    function goPreviousPage() {
        if (!isFirstPage(currentPage)) {
            setCurrentPage(isFirstPage(currentPage) ? 1 : currentPage - 1)
        }
    }

    const pagedItems = itemIds.filter((_, index) => {
        if (!paginationIsEnabledRef.current) {
            return true
        }
        const startIndex = currentPage * MAX_INTERACTIONS_PER_PAGE - 1
        const lastIndex = startIndex + MAX_INTERACTIONS_PER_PAGE
        return index > startIndex && index <= lastIndex
    })

    function getItemCoordsByIndex(index: number) {

        if (index + 1 > totalAmountRef.current) {
            return {page: -1, index: -1}
        }

        if (!paginationIsEnabledRef.current) {
            return {page: 0, index}
        } else {
            const page = Math.floor(index / MAX_INTERACTIONS_PER_PAGE)
            const pagedIndex = index % MAX_INTERACTIONS_PER_PAGE
            return {page, index: pagedIndex}
        }

    }

    function goToPage(page: number) {
        setCurrentPage(page)
    }
    return {
        currentPage,
        goNextPage,
        goPreviousPage,
        showPrev: paginationIsEnabledRef.current && !isFirstPage(currentPage),
        showNext: paginationIsEnabledRef.current && !isLastPage(currentPage),
        prevAmount: MAX_INTERACTIONS_PER_PAGE,
        nextAmount: isLastPage(currentPage + 1) ? (totalAmountRef.current % MAX_INTERACTIONS_PER_PAGE || MAX_INTERACTIONS_PER_PAGE) : MAX_INTERACTIONS_PER_PAGE,
        pagedItems,
        paginationIsEnabled: paginationIsEnabledRef.current,
        getItemCoordsByIndex,
        goToPage,
    }
}
