import type {CSSProperties} from 'react'
import React, {useMemo} from 'react'
import type {
    Control,
    FieldArrayPath,
    FieldValues,
    Path,
    UseFieldArrayRemove,
} from 'react-hook-form'
import {useController} from 'react-hook-form'

import {AddressTypes} from '@bpinc/ad-contacts-state-types'
import {useFormatting} from '@bpinc/ad-formatting-context'
import {STATES} from '@bpinc/ad-location-data'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {SelectFieldRaw} from '../SelectField'
import {TextFieldRaw} from '../TextField'
import {RemoveRow} from './RemoveRow'


type AddressFieldArrayContentProps<TValues extends FieldValues> = {
    index: number
    control: Control<TValues>
    name: FieldArrayPath<TValues>
    remove: UseFieldArrayRemove
    typeClassName?: string
    layoutClassName?: string
    style?: CSSProperties
}

type STATES_CODE = keyof typeof STATES

export const AddressFieldArrayContent = <TValues extends FieldValues>({
    index,
    control,
    name,
    remove,
    typeClassName,
    layoutClassName,
    style,
}: AddressFieldArrayContentProps<TValues>) => {
    const {t} = useLocalizationState()
    const {formatAddressType} = useFormatting()

    const optionsAddressStates = useMemo(() => {
        return Object.keys(STATES).map(code => ({
            value: code,
            label: STATES[code as STATES_CODE],
        }))
    }, [])

    const optionsAddressTypes = useMemo(() => {
        return Object.keys(AddressTypes).map(type => ({
            value: type,
            label: formatAddressType(type),
        }))
    }, [formatAddressType])

    const {field: typeField} = useController({
        name: `${name}.${index}.type` as Path<TValues>,
        control,
    })

    const {field: addressLine1Field} = useController({
        name: `${name}.${index}.address_line1` as Path<TValues>,
        control,
    })

    const {field: addressLine2Field} = useController({
        name: `${name}.${index}.address_line2` as Path<TValues>,
        control,
    })

    const {field: cityField} = useController({
        name: `${name}.${index}.city` as Path<TValues>,
        control,
    })

    const {field: stateField} = useController({
        name: `${name}.${index}.state` as Path<TValues>,
        control,
    })

    const {field: zipField} = useController({
        name: `${name}.${index}.postcode` as Path<TValues>,
        control,
    })

    return (
        <div role="row" className={tcn('flex items-start', layoutClassName)}>
            <SelectFieldRaw
                style={style}
                className={typeClassName}
                field={typeField}
                options={optionsAddressTypes}
                placeholder={t.FORMS.address_type()}
                aria-label={t.FORMS.address_type()}
            />
            <div className={tcn('flex min-w-0 flex-col', layoutClassName)}>
                <TextFieldRaw
                    style={style}
                    field={addressLine1Field}
                    placeholder={t.FORMS.address_line_1()}
                    aria-label={t.FORMS.address_line_1()}
                />
                <TextFieldRaw
                    style={style}
                    field={addressLine2Field}
                    placeholder={t.FORMS.address_line_2()}
                    aria-label={t.FORMS.address_line_2()}
                />
                <TextFieldRaw
                    style={style}
                    field={cityField}
                    aria-label={t.FORMS.city()}
                    placeholder={t.FORMS.city()}
                />
                <div role="row" className={tcn('flex items-start', layoutClassName)}>
                    <SelectFieldRaw
                        style={style}
                        field={stateField}
                        options={optionsAddressStates}
                        placeholder={t.FORMS.state()}
                    />
                    <TextFieldRaw
                        style={style}
                        field={zipField}
                        aria-label={t.FORMS.zip()}
                        placeholder={t.FORMS.zip()}
                    />
                </div>
            </div>
            <RemoveRow index={index} remove={remove} />
        </div>
    )
}
