import type {FC, HTMLAttributes} from 'react'
import React from 'react'

import {IconCogwheel} from '@bpinc/lib-icons'
import {tcn} from '@bpinc/lib-tailwind-classnames'


export const SystemMessageUI: FC<HTMLAttributes<HTMLDivElement>> = ({children, className, ...rest}) => {
    return (
        <div className={tcn('flex items-center px-3 py-2 text-sm/[18px] text-chat-title', className)} {...rest}>
            <IconCogwheel className="mr-4.5 h-6 shrink-0" />
            <div data-testid="content">{children}</div>
        </div>
    )
}
