import type {FC} from 'react'

import {Avatar} from '@bpinc/lib-avatar-ui'
import {Avatar as NewAvatarUI} from '@bpinc/lib-jskit-ui'
import {tcn} from '@bpinc/lib-tailwind-classnames'

import type {ParticipantItem} from './helpers'
import {MAX_PARTICIPANTS_COUNT, useAvatarParticipantsList} from './helpers'


export const MultiAvatar: FC<{avatarsItems: ParticipantItem[]}> = ({avatarsItems}) => {
    const leftItemsCount = `+${avatarsItems?.length - 1}`
    const avatarParticipantsList = useAvatarParticipantsList(avatarsItems)
    if (!avatarParticipantsList?.length) {
        return null
    }
    return (
        <div className="flex items-center -space-x-1">
            {avatarParticipantsList.map((item, i) => {
                const index = avatarParticipantsList.length - i
                return (
                    <Avatar
                        key={i}
                        {...item}
                        className={tcn(`z-[${index}] border border-white`)}
                    />
                )
            })}
            {avatarsItems?.length > MAX_PARTICIPANTS_COUNT && <NewAvatarUI fallback={leftItemsCount} className="z-0" />}
        </div>
    )
}
