import React from 'react'
import {useContext} from 'use-context-selector'

import {useInteractionChatState} from '@bpinc/ad-interaction-chat-state-context'
import type {ContactMessenger, FormDefinitionFieldMessenger, FormDefinitionPageItem} from '@bpinc/ad-request-urls'
import {getPageItemCSSProperties} from '@bpinc/lib-form-builder'
import {MessengerView} from '@bpinc/lib-forms-ui'
import {useGetMessengerName} from '@bpinc/lib-ui-elements'

import {FormBuilderContext} from '../../FormBuilder'
import {MessengersFieldArray} from '../common/FieldArray/MessengersFieldArray'
import {ViewItem} from '../common/ViewItem'


export const MessengersField: React.FC<{
    pageItem: FormDefinitionPageItem
    field: FormDefinitionFieldMessenger
}> = ({
    pageItem,
    field,
}) => {
    const {
        form,
        view,
        contactData,
    } = useContext(FormBuilderContext)

    const availableMessengers = useInteractionChatState('messengers')

    const getMessengerName = useGetMessengerName()

    if (view) {
        const messengers = form.getValues()[field.name || ''] as ContactMessenger[]
        const viewStyle = getPageItemCSSProperties(pageItem.style)

        if (!messengers) {
            return null
        }

        return (
            <>
                {messengers?.map((messenger, index) => (
                    <ViewItem
                        key={index}
                        viewStyle={viewStyle}
                        label={getMessengerName(messenger.type)}
                        value={(
                            <MessengerView
                                messenger={messenger}
                                {...contactData}
                            />
                        )}
                    />
                ))}
            </>
        )
    }

    const editStyle = getPageItemCSSProperties(pageItem.editStyle)

    return (
        <MessengersFieldArray
            name={field.name || ''}
            control={form.control}
            style={editStyle}
            availableMessengers={availableMessengers}
            layoutClassName={'gap-x-[--form-cellPadding] gap-y-[--form-rowHeight]'}
            typeClassName={'w-[--form-fieldLabelWidth] min-w-[--form-fieldLabelWidth] max-w-[--form-fieldLabelWidth]'}
        />
    )
}
