import {useEffect} from 'react'

import {useRecentUIState} from '@bpinc/lib-communicator-ui-context'
import {usePrevious} from '@bpinc/lib-helpers'

import type {UniqueRecentItem} from './useFilteredList'


export function useItemAutoSelector(filteredList: UniqueRecentItem[]) {
    const {tabIndex, searchValue, setSelected} = useRecentUIState()
    const prevTabIndex = usePrevious(tabIndex)

    useEffect(() => {
        if (searchValue && filteredList.length) {
            setSelected(s => {
                const tabIndexChanged = tabIndex !== prevTabIndex
                const firstItem = filteredList[0]

                if (tabIndexChanged) {
                    return firstItem.itemId
                }

                const alreadySelected =
                    s && filteredList.find(item => item.itemId === s)
                if (!alreadySelected) {
                    return firstItem.itemId
                }
                return alreadySelected.itemId
            })
        }

        if (!searchValue || !filteredList.length) {
            setSelected(undefined)
        }
    }, [searchValue, filteredList, setSelected, tabIndex, prevTabIndex])
}
