import React from 'react'

import {useInteractionProp} from '@bpinc/ad-interaction-state-context'
import {isExternalChat} from '@bpinc/ad-interaction-state-methods'
import {useInteractionId} from '@bpinc/lib-interaction-context'
import type {CommonSlidingModalItemProps} from '@bpinc/lib-sliding-modal-context'
import {SlidingModal} from '@bpinc/lib-ui-elements'

import {TransferModalFooter} from './InteractionsTransfer/components/TransferModalFooter'
import {InteractionsTransfer} from './InteractionsTransfer'
import {useAutoClose} from './useAutoClose'
import {useHeader} from './useHeader'


export const TransferModal: React.FC<CommonSlidingModalItemProps> = React.memo(function TransferModal(props) {
    const header = useHeader()
    const itemId = useInteractionId()
    const activeItemIsExternalChat = useInteractionProp(itemId, isExternalChat)

    useAutoClose()

    return (
        <SlidingModal
            name="transfer"
            header={header}
            footer={!activeItemIsExternalChat && <TransferModalFooter />}
            contentClassName="bg-secondary-background p-0"
            footerClassName="items-end"
            {...props}
        >
            <InteractionsTransfer />
        </SlidingModal>
    )
})
