import React, {useCallback} from 'react'

import type {ChatMessage} from '@bpinc/ad-interaction-message-types'
import {IconJoin, IconLeaveParty} from '@bpinc/lib-jskit-ui'
import {useLocalizationState} from '@bpinc/state-localization-context'


export const useGetSystemMessageIcon = () => {
    return useCallback((message: ChatMessage) => {
        switch (message.eventType) {
            case 'external_chat_party_joined_new':
                if (message.qmMode !== 'whisper') {
                    return <IconJoin className="size-4 icon-default" />
                }
                return null
            case 'external_chat_party_left_new':
                return <IconLeaveParty className="size-4 icon-default" />
            default:
                return null
        }
    }, [])
}

const useGetTypeReceivedData = () => {
    const {t} = useLocalizationState()
    return useCallback((message: string) => {
        switch (message) {
            case 'external_chat_file_received':
                return t.EXTERNAL_CHAT.file()
            case 'external_chat_contact_received':
                return t.EXTERNAL_CHAT.contact()
            case 'external_chat_url_received':
                return t.EXTERNAL_CHAT.url()
            default:
                return null
        }
    }, [t.EXTERNAL_CHAT])
}

export const useGetTextContentSystemMessage = () => {
    const {t} = useLocalizationState()
    const getTypeReceivedData = useGetTypeReceivedData()
    return useCallback((msg: ChatMessage) => {
        const name = `"${msg.partyName}"`
        if (msg.needLocalization && msg.eventType && msg.partyName && msg.msg) {
            switch (msg.eventType) {
                case 'external_chat_party_joined_new':
                case 'external_chat_party_left_new':
                    return (
                        <>
                            <span className="font-semibold caption-s text-neutral-on-secondary">{msg.partyName}</span>
                            <span className="caption-s text-neutral-fade">{msg.msg}</span>
                        </>
                    )
                case 'external_chat_party_coaching':
                    return (
                        <>
                            <span className="font-semibold caption-s text-neutral-on-secondary">{msg.partyName}</span>
                            <span className="caption-s text-neutral-fade">{t.EXTERNAL_CHAT.external_chat_party_coaching_new()}</span>
                        </>
                    )
                case 'external_chat_file_received':
                case 'external_chat_contact_received':
                case 'external_chat_url_received':
                    return (
                        <>
                            <span className="caption-s text-neutral-fade">{getTypeReceivedData(msg.eventType)}</span>
                            <span className="font-semibold caption-s text-neutral-on-secondary">{name}</span>
                            <span>{t.EXTERNAL_CHAT.received()}</span>
                        </>
                    )
                case 'external_chat_file_sent':
                    return (
                        <>
                            <span className="caption-s text-neutral-fade">{t.EXTERNAL_CHAT.external_chat_file_sent_new()}</span>
                            <span className="font-semibold caption-s text-neutral-on-secondary">{name}</span>
                        </>
                    )
                default:
                    return <span className="caption-s text-neutral-fade">{msg.msg}</span>
            }

        }
    }, [getTypeReceivedData, t.EXTERNAL_CHAT])
}
