import {useMemo} from 'react'

import {useTimezonesState} from '@bpinc/ad-timezones-state-context'


export function useFavoriteTimeZones() {
    const {
        allTimezones,
        favoriteTimezones,
    } = useTimezonesState()

    return useMemo(function memoizeOutput() {
        return [
            ...favoriteTimezones,
            ...allTimezones
                .slice(0, 10)
                .filter(tz => !favoriteTimezones.find(ftz => ftz.id === tz.id))
                .slice(0, Math.abs(5 - favoriteTimezones.length)),
        ]
    }, [allTimezones, favoriteTimezones])
}
