import React from 'react'

import type {Case} from '@bpinc/ad-case-state-types'
import {useGetContactPictureUrl} from '@bpinc/ad-contacts-state-hooks'
import {useFormatting} from '@bpinc/ad-formatting-context'
import {formatDate} from '@bpinc/ad-formatting-helpers'
import {ContactListItem} from '@bpinc/lib-contact-list-item-ui'


interface Props {
    data: Case
}

export const Reporter: React.FC<Props> = props => {
    const {data: {reporterId, reporterFirstName, reporterLastName, createdTime}} = props
    const {formatFullName} = useFormatting()
    const getContactPictureUrl = useGetContactPictureUrl()

    const name = formatFullName(reporterFirstName, reporterLastName)
    const avatar = name
        ? {firstName: reporterFirstName, lastName: reporterLastName, url: reporterId && getContactPictureUrl(reporterId)}
        : {firstName: 'Unidentified'}
    const title = `${name || 'Unidentified'}, reporter`
    const body = createdTime
        ? <span className="relative -top-1 text-[0.8em]">{formatDate(createdTime)}</span>
        : undefined

    return (
        <div>
            <ContactListItem
                as="div"
                avatarProps={avatar}
                title={title}
                body={body}
                contactId={reporterId}
                isFitted
            />
        </div>
    )
}
