import {useCallback, useEffect} from 'react'

import {useActivityHistoryByContactId} from '@bpinc/ad-contacts-state-context'
import {useInteractionProp, useInteractionStateMethods} from '@bpinc/ad-interaction-state-context'
import {useContactActivityHistory} from '@bpinc/ad-interaction-state-hooks'
import {getContactId} from '@bpinc/ad-interaction-state-methods'
import {bpLogger} from '@bpinc/ad-persistent-logger'
import {useAsRef} from '@bpinc/lib-helpers'
import {useInteractionId} from '@bpinc/lib-interaction-context'
import {useInteractionUIChatType} from '@bpinc/state-interaction-ui'


const UPDATE_ACTIVITY_HISTORY_TIMEOUT = 3000

export function useRecentActivityHistoryUploader() {
    const {getMessages} = useContactActivityHistory()
    const {clearRemovedAssociatedItem} = useInteractionStateMethods()
    const interactionId = useInteractionId()
    const type = useInteractionUIChatType(interactionId)
    const contactId = useInteractionProp(interactionId, getContactId)
    const contactActivityHistoryRef = useAsRef(useActivityHistoryByContactId(contactId))
    const associatedItemWasJustRemoved = useInteractionProp(interactionId, activeItem => !!activeItem?.removedAssociatedItemId)

    const typeRef = useAsRef(type)
    const contactIdRef = useAsRef(contactId)
    const associatedItemWasJustRemovedRef = useAsRef(associatedItemWasJustRemoved)


    const loadMessages = useCallback(
        function loadMessagesCallback() {
            getMessages({
                contactId: contactIdRef.current,
                type: typeRef.current,
                checkForLatestMessages: true,
            }).catch(err => {
                bpLogger.error(err || 'Error while trying to load new messages')
            })
        },
        [contactIdRef, getMessages, typeRef],
    )

    useEffect(() => {
        const {isLoading, messages} = contactActivityHistoryRef.current || {}
        if (
            !isLoading &&
            contactId &&
            !!messages?.length &&
            interactionId
        ) {
            if (associatedItemWasJustRemovedRef.current) {
                setTimeout(() => {
                    clearRemovedAssociatedItem(interactionId)
                    loadMessages()
                }, UPDATE_ACTIVITY_HISTORY_TIMEOUT)
            } else {
                loadMessages()
            }
        }
    }, [contactId, interactionId, loadMessages, clearRemovedAssociatedItem, associatedItemWasJustRemovedRef, contactActivityHistoryRef])
}
