import {useEmiErrorsNotifications} from './interaction/useEmiErrorsNotifications'
import {useFailedCallNotification} from './interaction/useFailedCallNotification'
import {useFavoritesLimitReachedError} from './interaction/useFavoritesLimitReachedError'
import {useInteractionNotAcceptedNotification} from './interaction/useInteractionNotAcceptedNotification'
import {usePhoneDeviceNotDetectedNotification} from './interaction/usePhoneDeviceNotDetectedNotification'
import {usePhoneTypeErrorNotification} from './interaction/usePhoneTypeErrorNotification'
import {
    useReservedForIncomingInteractionNotification,
} from './interaction/useReservedForIncomingInteractionNotification'
import {useWebRtcCallInChatErrorNotifications} from './interaction/useWebRtcCallInChatErrorNotifications'
import {useDomainAuthorizationPopup} from './system/useDomainAuthorizationPopup'
import {useInstallationRequiredPopup} from './system/useInstallationRequiredPopup'
import {useOfflineNotification} from './system/useOfflineNotification'
import {useOutdatedBrowserExtensionPopup} from './system/useOutdatedBrowserExtensionPopup'
import {useOutdatedClientNotification} from './system/useOutdatedClientNotification'
import {usePhoneOutOfServiceNotification} from './system/usePhoneOutOfServiceNotification'
import {useSipServerDisconnectedNotification} from './system/useSipServerDisconnectedNotification'
import {useSlowNetworkNotification} from './system/useSlowNetworkNotification'
import {useUnauthorizedDomainNotification} from './system/useUnauthorizedDomainNotification'


/**
 * Top-level notifications which should be shown for application WITH COMM.PANEL ONLY
 */
export function useInteractionNotifications() {
    useEmiErrorsNotifications()
    useFailedCallNotification()
    useInteractionNotAcceptedNotification()
    usePhoneDeviceNotDetectedNotification()
    usePhoneTypeErrorNotification()
    useReservedForIncomingInteractionNotification()
    useWebRtcCallInChatErrorNotifications()
    useFavoritesLimitReachedError()
}

/**
 * Top-level notifications which should be shown in ANY application of AD framework (standalone KB, for instance)
 */
export function useSystemNotifications() {
    useDomainAuthorizationPopup()
    useInstallationRequiredPopup()
    useOfflineNotification()
    useOutdatedBrowserExtensionPopup()
    useOutdatedClientNotification()
    usePhoneOutOfServiceNotification()
    useSipServerDisconnectedNotification()
    useSlowNetworkNotification()
    useUnauthorizedDomainNotification()
}
