import React, {useCallback} from 'react'

import {useInteractionStateMethods} from '@bpinc/ad-interaction-state-context'
import type {CallParty} from '@bpinc/ad-interaction-state-types'

import {getParticipantsListData} from '../InteractionParticipantsList/helpers'
import {InteractionParticipantsList} from '../InteractionParticipantsList/InteractionParticipantsList'


interface CallConferenceParties {
    isHost: boolean | undefined
    callParties: CallParty[] | undefined
}

export const CallConferenceParties: React.FC<CallConferenceParties> = ({isHost, callParties = []}) => {
    const participantsListData = getParticipantsListData({callParties})
    const {removeCallParty} = useInteractionStateMethods()

    const onRemoveItem = useCallback((id: string) => {
        const party = callParties.find(i => i.id === id)
        if (party) {
            removeCallParty(party)
        }
    }, [removeCallParty, callParties])

    return (
        <InteractionParticipantsList
            onRemoveItem={onRemoveItem}
            canRemove={!!isHost}
            participantsItems={participantsListData}
        />
    )
}
