import React, {useEffect, useMemo, useRef} from 'react'
import type {UseFormReturn} from 'react-hook-form'
import {type FieldValues, useForm} from 'react-hook-form'
import {DevTool} from '@hookform/devtools'
import type {Context} from 'use-context-selector'
import {createContext} from 'use-context-selector'

import type {FormDefinition} from '@bpinc/ad-request-urls'
import {createLeadMappingStringToFormValueMap, getFormCSSVariables} from '@bpinc/lib-form-builder'
import type {ChangeFormPageId} from '@bpinc/lib-form-builder-ui'

import {CompactForm} from './CompactForm'
import {WideForm} from './WideForm'


type FormBuilderCommons = {
    formDefinition: FormDefinition
    changeFormPageId?: ChangeFormPageId
    view?: boolean
    contactData?: {
        contactId: string
        firstName: string
        lastName: string
    }
    /** List of intent ID detected for the associated interaction */
    intents?: string[]
    /** Compact mode is used in Communicator. Form fields are displayed in a single column */
    compact?: boolean
}
type FormBuilderProps = FormBuilderCommons & {
    pageIndex?: number
    onFormAvailable: (form: UseFormReturn) => void
    variables?: Record<string, unknown>
    defaultValues?: Record<string, unknown>
    shouldResetOnChange?: boolean
}

type FormBuilderContextValue = FormBuilderCommons & {
    runtimeVariables: Record<string, unknown>
    form: UseFormReturn
    pageIndex: number
}

export let FormBuilderContext: Context<FormBuilderContextValue>

export const FormBuilder: React.FC<FormBuilderProps> = ({
    variables = {},
    formDefinition,
    intents = [],
    pageIndex = 0,
    changeFormPageId,
    view = false,
    onFormAvailable,
    contactData,
    defaultValues,
    shouldResetOnChange = false,
    compact = false,
}) => {
    const form = useForm<FieldValues>({
        defaultValues,
        mode: 'onChange',
        reValidateMode: 'onChange',
    })

    const isMountedRef = useRef(false)

    useEffect(() => {
        if (!isMountedRef.current) {
            isMountedRef.current = true
            return
        }

        if (shouldResetOnChange) {
            form.reset(defaultValues)
            /**
             * Form could be changed outside the component, so we need to trigger the validation.
             * If we reset the form with default values, we need to trigger the form validation
             * manually, because the form is not dirty and the validation is not triggered.
             * setTimeout is a workaround to trigger the validation after the form is rendered.
             */
            setTimeout(form.trigger)
        }
    }, [shouldResetOnChange, defaultValues, form])

    useEffect(() => {
        onFormAvailable?.(form)
    }, [form, onFormAvailable])

    const formValues = form.watch()
    const runtimeVariables = useMemo(() => ({
        ...variables,
        ...createLeadMappingStringToFormValueMap(formValues, formDefinition),
    }), [formDefinition, variables, formValues])

    const formCSSVariables = useMemo(() => getFormCSSVariables(formDefinition), [formDefinition])

    const contextValue = useMemo<FormBuilderContextValue>(() => ({
        formDefinition,
        pageIndex,
        changeFormPageId,
        view,
        contactData,
        form,
        runtimeVariables,
        intents,
        compact,
    }), [
        formDefinition,
        pageIndex,
        changeFormPageId,
        view,
        contactData,
        form,
        runtimeVariables,
        intents,
        compact,
    ])

    if (!FormBuilderContext) {
        FormBuilderContext = createContext<FormBuilderContextValue>(contextValue)
    }

    return (
        <FormBuilderContext.Provider value={contextValue}>
            <div
                style={formCSSVariables}
                className="
                    flex
                    flex-auto
                    flex-col
                    gap-6
                    bg-[--form-backgroundColor]
                    text-[--form-textColor]
                "
            >
                {compact ? <CompactForm /> : <WideForm />}
            </div>

            {process.env.NODE_ENV === 'development' && <DevTool control={form.control} />}
        </FormBuilderContext.Provider>
    )

}
