import React, {useCallback, useEffect} from 'react'

import {
    useInteractionChatData,
    useInteractionChatStateConnector,
    useInteractionChatStateMethods,
    useInteractionChatStateSelector,
    useWebRtcConfig,
} from '@bpinc/ad-interaction-chat-state-context'
import {useInteraction} from '@bpinc/ad-interaction-state-context'
import {useCallHandler} from '@bpinc/ad-interaction-state-hooks'
import {getInteractionStepId} from '@bpinc/ad-interaction-state-methods'
import {useRescueState} from '@bpinc/ad-rescue-state-context'
import {useSessionState} from '@bpinc/ad-session-context'
import {IconCobrowsing} from '@bpinc/lib-icons'
import {MuteButton, TransferButton} from '@bpinc/lib-interaction-buttons'
import {useInteractionId} from '@bpinc/lib-interaction-context'
import {
    CircleButton,
    IconCamera,
    IconCameraOff,
    IconEndWebCall,
    IconRemoteAssist,
    IconStartWebCall,
    IconVoiceInbound,
} from '@bpinc/lib-jskit-ui'
import {useOpenInteractionSlidingModal, useShowSlidingModalChecker} from '@bpinc/lib-sliding-modal-context'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {CircleButton as LegacyCircleButton} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {useOpenRescueModal} from '../../../../modals'
import {CompleteButton} from './CompleteButton'
import {RemoteAssistButton} from './RemoteAssistButton'


interface ChatButtonsProps {
    onClose: (arg0: React.MouseEvent) => void
}

export const ChatButtons: React.FC<ChatButtonsProps> = ({onClose}) => {
    const openInteractionSlidingModal = useOpenInteractionSlidingModal()
    const interactionId = useInteractionId()
    const showTransferModal = useShowSlidingModalChecker(interactionId, 'transfer')
    const {t} = useLocalizationState()
    const activeItem = useInteraction(interactionId)
    const handleCall = useCallHandler()
    const {
        createRtcCall: createChatCall,
        stopRtcCall: stopChatCall,
        toggleRtcCallDevice: toggleChatCallDevice,
        requestCobrowsing,
    } = useInteractionChatStateMethods()
    const webRtcConfig = useWebRtcConfig()
    const activeChatId = getInteractionStepId(activeItem)
    const chatData = useInteractionChatData(activeChatId)
    const {tenantFeatures, privileges} = useSessionState()
    const interactionChatStateConnector = useInteractionChatStateConnector()

    const phone = activeItem?.displayData.number
    const cobrowsingWidgetKey = chatData?.cobrowsingWidgetKey
    const isCobrowsingActive = chatData?.cobrowsingActive
    const chatCall = chatData?.call
    const doesAnotherChatWithCallExist = useInteractionChatStateSelector(s =>
        Object.keys(s.chatData).some(id => id !== activeChatId && s.chatData[id].call?.isConfirmed),
    )

    const isChatCallActive = !!chatCall?.isConfirmed
    const remoteAssistEnabled = !!tenantFeatures?.nativeRemoteAssistEnabled && !!privileges.SYS_USE_REMOTE_ASSIST
    const chatInDeliveredState = activeItem?.state === 'delivered' || activeItem?.state === 'hold'

    useEffect(() => {
        const subscription = interactionChatStateConnector.dispenser.subscribe({
            onCobrowsingStarted: cobrowsingPayload => {
                if (cobrowsingPayload.cobrowsingLink) {
                    window.open(cobrowsingPayload.cobrowsingLink, '_blank', '')
                }
            },
        })
        return () => subscription.unsubscribe()
    }, [interactionChatStateConnector])

    const handleCallClick = useCallback(
        function handleCallClickCallback() {
            handleCall({
                phone,
                contactId: activeItem?.contactId,
                userId: activeItem?.userId,
                firstName: activeItem?.displayData?.firstName,
                lastName: activeItem?.displayData?.lastName,
                origination: 'interaction',
            })
        },
        [
            activeItem?.contactId,
            activeItem?.displayData?.firstName,
            activeItem?.displayData?.lastName,
            activeItem?.userId,
            handleCall,
            phone,
        ],
    )

    const handleChatCallStart = useCallback(
        function handleChatCallStartCallback() {
            createChatCall({chatId: activeChatId, isConfirmed: true})
        },
        [activeChatId, createChatCall],
    )

    const handleChatCallClose = useCallback(
        function handleChatCallCloseCallback() {
            stopChatCall({chatId: activeChatId})
        },
        [activeChatId, stopChatCall],
    )

    const handleChatCallToggleAudio = useCallback(
        function handleChatCallToggleAudioCallback() {
            if (chatCall) {
                toggleChatCallDevice({
                    chatId: activeChatId,
                    device: 'audio',
                    enabled: !chatCall.withAudio,
                })
            }
        },
        [activeChatId, toggleChatCallDevice, chatCall],
    )

    const handleChatCallToggleVideo = useCallback(
        function handleChatCallToggleVideoCallback() {
            if (chatCall) {
                toggleChatCallDevice({
                    chatId: activeChatId,
                    device: 'video',
                    enabled: !chatCall.withVideo,
                })
            }
        },
        [activeChatId, toggleChatCallDevice, chatCall],
    )

    const handleCobrowsingClick = useCallback(
        function handleCobrowsingClickCallback() {
            if (activeChatId && !isCobrowsingActive && activeItem?.state === 'delivered') {
                requestCobrowsing(activeChatId)
            }
        },
        [activeChatId, isCobrowsingActive, activeItem, requestCobrowsing],
    )

    const toggleTransfer = useCallback(
        function toggleTransferCallback() {
            openInteractionSlidingModal(interactionId, 'transfer')
        },
        [openInteractionSlidingModal, interactionId],
    )

    const openRescueModal = useOpenRescueModal()
    const {isEnabled: isRescueEnabled} = useRescueState()

    const handleRescueClick = useCallback(() => {
        openRescueModal({type: 'chat'})
    }, [openRescueModal])

    return (
        <div className="flex size-full items-center justify-between">
            <div className="flex h-7 items-center gap-2.5">
                {chatInDeliveredState && (
                    <TransferButton
                        iconClassName="icon-fade"
                        isActive={showTransferModal}
                        onClick={toggleTransfer}
                        tooltip={t.EXTERNAL_CHAT.invite_dialog_label()}
                    />
                )}
                {chatInDeliveredState && isChatCallActive && (
                    <>
                        <MuteButton
                            isActive={!chatCall?.withAudio}
                            onClick={handleChatCallToggleAudio}
                            data-testid="webrtc-mute-button"
                            data-muted={!chatCall?.withAudio || undefined}
                        />
                        <CircleButton
                            isActive={!chatCall?.withVideo}
                            onClick={handleChatCallToggleVideo}
                            tooltip={{content:
                                chatCall?.withVideo
                                    ? t.CALL_DETAILS_FORM.voip_video_on_tooltip()
                                    : t.CALL_DETAILS_FORM.voip_video_off_tooltip(),
                            }}
                            data-testid="webrtc-video-button"
                            data-video-active={chatCall?.withVideo || undefined}
                        >
                            {chatCall?.withVideo
                                ? <IconCamera className="size-4.5 icon-fade" />
                                : <IconCameraOff className="size-4.5 icon-fade" />
                            }
                        </CircleButton>
                    </>
                )}
                {chatInDeliveredState && (
                    <>
                        {cobrowsingWidgetKey && (
                            <LegacyCircleButton
                                active={isCobrowsingActive && activeItem?.state === 'delivered'}
                                onClick={handleCobrowsingClick}
                                tooltip={t.INTERNAL_CHAT.chat_cobrowsing_button_tooltip()}
                                data-testid="request-cobrowsing-button"
                                clickThrottle={500}
                            >
                                <IconCobrowsing />
                            </LegacyCircleButton>
                        )}

                        {isRescueEnabled && !remoteAssistEnabled && (
                            <CircleButton
                                onClick={handleRescueClick}
                                tooltip={{content: t.CALL_DETAILS_FORM.button_remote_access_tooltip()}}
                                data-testid="remote-assist-button"
                            >
                                <IconRemoteAssist />
                            </CircleButton>
                        )}

                        {remoteAssistEnabled && <RemoteAssistButton />}
                    </>
                )}
            </div>
            <div className="flex h-7 items-center">
                {/*TO DO: add clickThrottle={500} when we will refactor button in jskit-ui*/}
                {chatInDeliveredState && (webRtcConfig || isChatCallActive) && (
                    <>
                        <CircleButton
                            className="mr-2"
                            onClick={isChatCallActive ? handleChatCallClose : handleChatCallStart}
                            tooltip={{content: isChatCallActive
                                ? t.CALL_DETAILS_FORM.interaction_endCall()
                                : t.EXTERNAL_CHAT.button_start_web_call_tooltip(),
                            }}
                            disabled={!isChatCallActive ? doesAnotherChatWithCallExist : false}
                            data-testid={isChatCallActive ? 'webrtc-end-call-button' : 'webrtc-call-button'}
                        >
                            {isChatCallActive
                                ? <IconEndWebCall className="size-5" />
                                : <IconStartWebCall className="size-5" />
                            }
                        </CircleButton>
                        <CircleButton
                            disabled={!phone}
                            className="mr-2 h-7 w-12 rounded-xs"
                            onClick={handleCallClick}
                            tooltip={{content: t.CALL_DETAILS_FORM.voip_start_tooltip()}}
                            data-testid="call-button"
                        >
                            <IconVoiceInbound className={tcn('size-5', {'icon-disabled': !phone})} />
                        </CircleButton>
                    </>
                )}
                <CompleteButton isChat handleCloseClick={onClose} item={activeItem} />
            </div>
        </div>
    )
}
