import React from 'react'
import {useContext} from 'use-context-selector'

import type {FormDefinitionFieldButton} from '@bpinc/ad-request-urls'
import {FormBuilderButton} from '@bpinc/lib-form-builder-ui'

import {FormBuilderContext} from '../../FormBuilder'


export const ButtonField: React.FC<{
    field: FormDefinitionFieldButton
}> = ({
    field,
}) => {
    const {
        form,
        formDefinition,
        changeFormPageId,
    } = useContext(FormBuilderContext)

    return (
        <FormBuilderButton
            actionCases={field.properties.actionCase}
            action={field.properties.action}
            values={form.getValues()}
            formDefinition={formDefinition}
            changeFormPageId={changeFormPageId}
        >
            {field.value}
        </FormBuilderButton>
    )
}
