import React from 'react'

import {useSlidingModalState} from '@bpinc/lib-sliding-modal-context'
import {typedKeys} from '@bpinc/lib-types-helpers'

import {GlobalSlidingModalsListItem} from './GlobalSldigingModalsListItem'


export const GlobalSlidingModalsList: React.FC = React.memo(
    function GlobalSlidingModalsList() {
        const visibleGlobalModals = useSlidingModalState(s => {
            const allModals = typedKeys(s.globalModalsData)
            return allModals.filter(m => s.globalModalsData[m].show || s.globalModalsData[m].closing)
        })

        return (
            <div className="pointer-events-none absolute inset-y-0 z-[15] h-full w-full overflow-hidden">
                {visibleGlobalModals.map(name => (
                    <GlobalSlidingModalsListItem key={name} name={name} />
                ))}
            </div>
        )
    },
)
