import React from 'react'

import {useInteractionProp} from '@bpinc/ad-interaction-state-context'
import {useDispositionsSettings} from '@bpinc/ad-interaction-state-hooks'
import {isWorkItem} from '@bpinc/ad-interaction-state-methods'
import {useInteractionId} from '@bpinc/lib-interaction-context'
import {tcn} from '@bpinc/lib-tailwind-classnames'

import {DispositionRescheduleForm} from '../../reschedule'
import {useShowDispositionRescheduleForm} from '../hooks/useShowDispositionRescheduleForm'
import {DispositionSelect} from './Dispositions/DispositionSelect'
import {ResolutionNote} from './Notes/ResolutionNote'


type ResolutionProps = {
    className?: string
}

export const Resolution: React.FC<ResolutionProps> = React.memo(function Resolution({className}) {
    const interactionId = useInteractionId()
    const {useDispositions} = useDispositionsSettings(interactionId)
    const activeItemIsWorkItem = useInteractionProp(interactionId, isWorkItem)
    const showDispositionRescheduleForm = useShowDispositionRescheduleForm()

    return (
        <div className={tcn('overflow-auto p-4 pb-8', className)}>
            {(activeItemIsWorkItem || useDispositions) && <DispositionSelect accordion />}
            <ResolutionNote layoutClassName="mt-6" />
            {showDispositionRescheduleForm && <DispositionRescheduleForm />}
        </div>
    )
})
