import {useContactsStateMethods} from '@bpinc/ad-contacts-state-context'
import {useOpenAddEmailModal, useOpenAddPhoneModal} from '@bpinc/lib-contact-details/src/components/modals'
import {useEvent} from '@bpinc/lib-helpers'
import type {RecentItem} from '@bpinc/state-recent-types'


export function useUpdateContactWithRecentChannel() {
    const openAddEmailModal = useOpenAddEmailModal()
    const openAddPhoneModal = useOpenAddPhoneModal()

    const {getContact} = useContactsStateMethods()

    return useEvent(async (contactId: string, recentItem: RecentItem): Promise<void> => {
        const contact = await getContact({id: contactId})

        if (!contact) {
            return Promise.reject()
        }

        const {number, email} = recentItem

        if (!contact.emails.find(item => item.emailAddress === email)) {
            return new Promise(resolve => {
                openAddEmailModal({
                    email,
                    contactId,
                    firstName: contact.firstName,
                    lastName: contact.lastName,
                    onConfirm: resolve,
                })
            })
        }
        if (!contact.phones?.find(item => item.phone === number)) {
            return new Promise(resolve => {
                openAddPhoneModal({
                    phone: number,
                    contactId,
                    firstName: contact.firstName,
                    lastName: contact.lastName,
                    onConfirm: resolve,
                })
            })
        }
        // TODO: handle messengers
    })
}
