import React from 'react'

import {useChatMessageSanitizer} from '@bpinc/ad-interaction-state-hooks'
import {formatDate, useInnerHTML} from '@bpinc/lib-helpers'

import type {HistoryVoiceTranscriptMessage} from '../../../../../types'
import {useCallMessageVoiceTranscriptName} from './useCallMessageVoiceTranscriptName'


interface CallMessageVoiceTranscriptProps {
    message: HistoryVoiceTranscriptMessage
}

export const CallMessageVoiceTranscript: React.FC<CallMessageVoiceTranscriptProps> = ({
    message,
}) => {
    const name = useCallMessageVoiceTranscriptName(message)
    const sanitizedText = useChatMessageSanitizer({message: message.msg})

    const messageHTML = useInnerHTML(sanitizedText)

    return (
        <section className="flex w-full gap-2.5" data-testid="transcript-message">
            <div className="shrink-0 caption-s text-neutral-fade" data-testid="transcript-message-time">
                {formatDate(message.endTimestamp, 'h:mm A')}
            </div>
            <div className="flex flex-col">
                {name && (
                    <div
                        className="break-all caption-s text-neutral-fade"
                        data-testid="transcript-message-name"
                    >
                        {name}
                    </div>
                )}
                <div
                    className="break-words body"
                    data-testid="transcript-message-text"
                    dangerouslySetInnerHTML={messageHTML}
                />
            </div>
        </section>
    )
}
