import {DIDNumberStateProvider} from '@bpinc/ad-did-number-context'
import {ServiceStateProvider} from '@bpinc/ad-service-context'
import {UIContextProvider} from '@bpinc/ad-ui-context'
import {ADCShortcutsProvider as ShortcutsProvider} from '@bpinc/lib-accessible-shortcuts-context'
import {ChatAttachmentsUIStateProvider} from '@bpinc/lib-attachments-state'
import {ChannelStateProvider} from '@bpinc/lib-chat-ui'
import {NavigationStateProvider} from '@bpinc/lib-communicator-navigation-state'
import {CommunicatorUIProvider} from '@bpinc/lib-communicator-ui-context'
import {ActiveInteractionContextProvider} from '@bpinc/lib-interaction-context'
import {SlidingModalContextProvider} from '@bpinc/lib-sliding-modal-context'
import {CountrySelectorStateProvider} from '@bpinc/lib-ui-elements'
import {InteractionUIStateProvider} from '@bpinc/state-interaction-ui'

import {ScrollStateProvider} from '../scroll-context'
import {WebUserActivityTracker} from './web-user-activity-tracker'


export const parallelChatProviders = [
    SlidingModalContextProvider,
    ChannelStateProvider,
    ChatAttachmentsUIStateProvider,
    DIDNumberStateProvider,
    WebUserActivityTracker,
]


export const providers = [
    ActiveInteractionContextProvider,
    SlidingModalContextProvider,
    ScrollStateProvider,
    ChannelStateProvider,
    ChatAttachmentsUIStateProvider,
    CountrySelectorStateProvider,
    DIDNumberStateProvider,
    WebUserActivityTracker,
]

// These providers are only required for the standalone Communicator application
// and not for the built-in application, as they are already defined in the AgentDesktop.
export const standaloneProviders = [
    ActiveInteractionContextProvider,
    ServiceStateProvider,
    ...providers,
    InteractionUIStateProvider,
    NavigationStateProvider,
    ShortcutsProvider,
    UIContextProvider,
    CommunicatorUIProvider,
]
