import {useMemo} from 'react'

import {useGetAvatarUrl} from '@bpinc/ad-contacts-state-hooks'
import {useDirectoryUser} from '@bpinc/ad-directory-state-context'
import type {MsTeamsUser} from '@bpinc/ad-directory-state-types'
import {
    isRecentCallConference,
    isRecentEmail,
    isRecentExtChatConference,
    isRecentGroupChat,
} from '@bpinc/ad-interaction-state-methods'
import {useSessionStateRef} from '@bpinc/ad-session-context'
import {getUserStatusIcon, useUserDetailsById} from '@bpinc/ad-user-details-state-context'
import {getAvatarType} from '@bpinc/lib-avatar-ui'
import {getGroupChatAvatar} from '@bpinc/lib-chat-ui/src/helpers'
import type {PropsAvatar} from '@bpinc/lib-contact-list-item-ui'
import type {RecentItem} from '@bpinc/state-recent-types'


export function useAvatar(item: RecentItem, relatedEMIUser?: MsTeamsUser) {
    const sessionRef = useSessionStateRef()
    const getAvatarUrl = useGetAvatarUrl()

    const userId = getUserId() || ''

    useDirectoryUser(userId)
    const userDetails = useUserDetailsById(userId)?.data

    return useMemo(() => {
        let avatar: PropsAvatar
        if (isRecentCallConference(item) || isRecentExtChatConference(item)) {
            avatar = {
                avatarType: 'conference',
                isAgent: isRecentGroupChat(item),
            }
        } else if (isRecentEmail(item)) {
            avatar = {
                avatarType: 'case',
            }
        } else if (item.groupChatData) {
            avatar = getGroupChatAvatar({
                chatData: item.groupChatData,
                p2pUser: userDetails,
                session: sessionRef.current,
            })
        } else {
            avatar = {
                avatarType: getAvatarType(item.userId, item.contactId, item.userType),
                firstName: item.firstName || item.name,
                lastName: item.lastName,
                isAgent: !!item.userType || !!item.userId,
                url: getAvatarUrl({
                    userId: item.userId,
                    contactId: item.contactId,
                    userType: item.userType,
                    photoUrl: item.photoUrl,
                }),
            }
        }
        if (userDetails) {
            avatar.isOnline = userDetails.presenceState === 'available'
            avatar.onlineStatusIcon = getUserStatusIcon(userDetails)
        }
        return avatar
    }, [getAvatarUrl, item, userDetails, sessionRef])

    function getUserId(): string | undefined {
        if (item.userType === 'msteams' || item.userType === 'msteams_channel') {
            return relatedEMIUser?.userId
        }
        if (item.groupChatData && item.groupChatData.members.length <= 2) {
            const selfUserId = sessionRef.current.userId
            return item.groupChatData.members.find(m => m.id !== selfUserId)?.id
        }
        return item.userId
    }
}
