import React from 'react'

import type {ChatMessage} from '@bpinc/ad-interaction-message-types'
import {SemanticButton} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {useHandleCancelInviteClick} from './useHandleCancelInviteClick'


type InviteMessageProps = {
    message: ChatMessage
}

export const InviteMessage: React.FC<InviteMessageProps> = React.memo(({message}) => {
    const {t} = useLocalizationState()

    const handleCancelClick = useHandleCancelInviteClick(message)

    return (
        <div className="select-none pb-2.5" data-testid="message-item" data-outgoing="true" data-invitation="true">
            <span className="mr-[5px]">{t.EXTERNAL_CHAT.external_chat_party_inviting()}</span>
            <SemanticButton
                size="mini"
                negative
                onClick={handleCancelClick}
                tooltip={t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_buttonCancel()}
                data-testid="cancel-invitation-button"
            >
                {t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_buttonCancel()}
            </SemanticButton>
        </div>
    )
})
