import {atom} from 'jotai'

import type {QueueItem, QueueType} from '@bpinc/ad-request-urls'


export type Pagination = {
    skip: number
    size: number
}

export type Sorting = {
    field: string
    direction: 'asc' | 'desc'
}

export type User = {
    id: string
    name: string
}


export type CaseTab = QueueTab | ViewCaseTab

interface QueueTab extends BaseTab {
    id: 'TEAM' | 'PERSONAL' | 'FOLLOWUP'
    type: 'queue'
    closeable: false
}

export interface ViewCaseTab extends BaseTab {
    id: string
    case: {
        id: string
        serviceId: string | undefined
        number: number
    }
    type: 'view_case'
    closeable: true
}

interface BaseTab {
    queueType: QueueType
    id: string
    closeable: boolean
}

const DEFAULT_SORTING_FIELD = 'updateTime'
const DEFAULT_SERVICE = 'all'
const INITIAL_QUEUE_TABLE_WIDTH = 400

export const PAGINATION_CHUNK_SIZE = 100

const DEFAULT_CASE_TABS: CaseTab[] = [
    {
        id: 'TEAM',
        type: 'queue',
        queueType: 'TEAM',
        closeable: false,

    },
    {
        id: 'PERSONAL',
        type: 'queue',
        queueType: 'PERSONAL',
        closeable: false,
    },
    {
        id: 'FOLLOWUP',
        queueType: 'FOLLOWUP',
        type: 'queue',
        closeable: false,
    },
]

//-------All tabs-------//
export const tabsAtom = atom(DEFAULT_CASE_TABS)
export const selectedTabIdAtom = atom('TEAM')
export const selectedTabAtom = atom(get => get(tabsAtom).find(tab => tab.id === get(selectedTabIdAtom)))
export const queueTableWidthAtom = atom<number>(INITIAL_QUEUE_TABLE_WIDTH)

//-------Personal tab-------//
//@ts-expect-error - User is always defined in JotaiProvider (self user by default).
export const personalTabUserAtom = atom<User>()

export const personalPaginationAtom = atom<Pagination>({
    skip: 0,
    size: 100,
})

export const personalActiveQueueItemAtom = atom<QueueItem | undefined>(undefined)

export const personalSortingAtom = atom<Sorting>({
    field: DEFAULT_SORTING_FIELD,
    direction: 'desc',
})
//-------Team tab-------//
export const teamIdAtom = atom<string | undefined>(undefined)
export const serviceFilterAtom = atom(DEFAULT_SERVICE)
export const teamActiveQueueItemAtom = atom<QueueItem | undefined>(undefined)
export const teamSortingAtom = atom<Sorting>({
    field: DEFAULT_SORTING_FIELD,
    direction: 'desc',
})
export const teamPaginationAtom = atom<Pagination>({
    skip: 0,
    size: 100,
})


//-------Pinned tab-------//
export const pinnedActiveQueueItemAtom = atom<QueueItem | undefined>(undefined)
export const pinnedSortingAtom = atom<Sorting>({
    field: DEFAULT_SORTING_FIELD,
    direction: 'desc',
})
