import React, {useCallback} from 'react'

import type {ChatType} from '@bpinc/ad-interaction-chat-state-types'
import {useInteractionProp} from '@bpinc/ad-interaction-state-context'
import {getGlobalInteractionId} from '@bpinc/ad-interaction-state-methods'
import {ErrorBoundary} from '@bpinc/lib-ui-elements'

import type {ChatCommonMessagesType, ChatScrollNodeType} from '../../../types'
import {isVoiceTranscriptMessage, isVoiceTranscriptSuggestion} from '../../../types'
import {VirtualizedMessages} from '../VirtualizedMessages/VirtualizedMessages'
import {ErrorMessage} from './messages/ErrorMessage'
import {ExternalChatMessage} from './ExternalChatMessage'


const ErrorBoundaryFallback = <ErrorMessage>Broken message</ErrorMessage>

type VirtualizedExternalMessagesProps = {
    messages: ChatCommonMessagesType[]
    scrollNode: ChatScrollNodeType
    itemId: string
    contactId: string
    type: ChatType
    interactionStepId: string
}

export const VirtualizedExternalMessages = React.memo<VirtualizedExternalMessagesProps>(
    function VirtualizedExternalMessages({
        itemId,
        contactId,
        scrollNode,
        interactionStepId,
        messages,
        type,
    }) {
        const isDIDChat = useInteractionProp(itemId, item => item?.scenarioData?.isDID ?? false)
        const globalInteractionId = useInteractionProp(itemId, getGlobalInteractionId) ?? ''
        const renderMessage = useCallback((message: ChatCommonMessagesType) => {
            if (isVoiceTranscriptMessage(message) && !isVoiceTranscriptSuggestion(message)) {
                return null
            }

            return (
                <div data-message="true">
                    <ErrorBoundary fallback={ErrorBoundaryFallback}>
                        <ExternalChatMessage
                            message={message}
                            scrollNode={scrollNode}
                            interactionItemId={itemId}
                            itemContactId={contactId}
                            itemInteractionStepId={interactionStepId}
                            itemGlobalInteractionId={globalInteractionId}
                            isDIDChat={isDIDChat}
                        />
                    </ErrorBoundary>
                </div>
            )
        }, [contactId, globalInteractionId, interactionStepId, isDIDChat, itemId, scrollNode])

        return (
            <VirtualizedMessages
                key={`${itemId}_${type}`}
                itemId={itemId}
                contactId={contactId}
                type={type}
                messages={messages}
                renderMessage={renderMessage}
                scrollNode={scrollNode}
            />
        )
    })

