import React from 'react'
import {useContext} from 'use-context-selector'

import type {FormDefinitionFieldNotes, FormDefinitionPageItem} from '@bpinc/ad-request-urls'

import {FormBuilderContext} from '../../FormBuilder'
import {GeneralTextFormField} from '../common/GeneralTextFormField'


export const NotesField: React.FC<{
    pageItem: FormDefinitionPageItem
    field: FormDefinitionFieldNotes
}> = ({
    pageItem,
    field,
}) => {
    const {form} = useContext(FormBuilderContext)

    return (
        <GeneralTextFormField
            placeholder={field.emptyText || ''}
            label={field.label}
            required={field.required}
            key={field.key}
            name={field.name || ''}
            control={form.control}
            pageItem={pageItem}
            minRows={field.initialLinesNumber}
            multiline={field.multiline}
        />
    )
}
