import React, {useMemo} from 'react'

import {useFormatting} from '@bpinc/ad-formatting-context'
import {useSessionState} from '@bpinc/ad-session-context'
import {useUserProfileState} from '@bpinc/ad-user-profile-state-context'
import {ContactListItem} from '@bpinc/lib-contact-list-item-ui'
import {useLocalizationState} from '@bpinc/state-localization-context'

import styles from './UserProfilePreview.module.less'


export const UserProfilePreview: React.FC = () => {
    const {formatFullName} = useFormatting()
    const {persistentAvatar} = useUserProfileState()
    const {firstName, lastName, userId, phoneNumber = '_'} = useSessionState()

    const {t} = useLocalizationState()

    const avatar = useMemo(() => {
        return {
            firstName,
            lastName,
            url: persistentAvatar?.src,
        }
    }, [firstName, lastName, persistentAvatar?.src])

    const title = (
        <>
            <span className={styles.name} data-testid="name">
                {formatFullName(firstName, lastName)}
            </span>
            <span className={styles.phone} data-testid="extension">
                {t.USER_PROFILE.sidebar_ext(phoneNumber)}
            </span>
        </>
    )
    const body = (
        <span className={styles.link} data-testid="menu-item-text">
            {t.USER_PROFILE.sidebar_profile_link()}
        </span>
    )

    return <ContactListItem as="div" avatarProps={avatar} title={title} body={body} userId={userId} isFitted isLowRise />
}
