import {useMemo} from 'react'
import {useContext} from 'use-context-selector'

import type {FormDefinitionField} from '@bpinc/ad-request-urls'
import {checkConditions, isFieldWithConditions} from '@bpinc/lib-form-builder'

import {FormBuilderContext} from '../FormBuilder'


export function useConditionalFieldCheck(field: FormDefinitionField) {
    const {intents, runtimeVariables} = useContext(FormBuilderContext)

    return useMemo(() => {
        if (!isFieldWithConditions(field)) {
            return true
        }
        return !!checkConditions({
            conditions: field.conditions,
            values: runtimeVariables,
            intents,
        })
    }, [field, runtimeVariables, intents])
}
