import {useCallback} from 'react'
import {useAtom} from 'jotai'

import {searchPageActiveTabIdAtom, searchPageTabsAtom} from './atoms'
import type {SearchPageTab} from './utils'


export function useSearchPageTabsActions() {
    const [tabs, setTabs] = useAtom(searchPageTabsAtom)
    const [activeTabId, setActiveTabId] = useAtom(searchPageActiveTabIdAtom)

    const handleTabOpen = useCallback((tab: SearchPageTab) => {
        const targetTab = tabs.find(it => it.id === tab.id)
        if (targetTab) {
            // tab is open. focus it
            setActiveTabId(tab.id)
        } else {
            // open new tab
            setTabs([...tabs, tab])
            setActiveTabId(tab.id)
        }
    }, [setActiveTabId, setTabs, tabs])

    const handleTabClose = useCallback((id: string) => {
        if (id === activeTabId) setActiveTabId(tabs[0].id)
        setTabs(tabs.filter(tab => tab.id !== id))
    }, [activeTabId, setActiveTabId, setTabs, tabs])


    const handleTabUpdate = useCallback((tab: SearchPageTab) => {
        setTabs(currentTabs => currentTabs.map(t => {
            if (t.id === tab.id) {
                return tab
            }
            return t
        }))
    }, [setTabs])

    return {handleTabOpen, handleTabClose, handleTabUpdate}
}
