import type React from 'react'
import {useCallback, useMemo} from 'react'

import type {PopupMenuOption} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'


interface ExternalMoreButtonOptionsProps {
    setLinkClicks: React.Dispatch<React.SetStateAction<number>>
    toggleShow: () => void
    handleMerge: () => void
    handleCopyLink: () => void
}
export const useExternalMoreButtonOptions = ({
    setLinkClicks,
    toggleShow,
    handleMerge,
    handleCopyLink,
}: ExternalMoreButtonOptionsProps): PopupMenuOption[] => {
    const {t} = useLocalizationState()

    const openInCRM = useCallback(() => {
        'TODO: HANDLER WHEN DESIGN IS READY'
        toggleShow()
    }, [toggleShow])

    const copySharableLink = useCallback(() => {
        handleCopyLink()
        setLinkClicks(n => ++n)
        toggleShow()
    }, [toggleShow, setLinkClicks, handleCopyLink])

    const mergeToExisting = useCallback(() => {
        handleMerge()
        toggleShow()
    }, [toggleShow, handleMerge])
    return useMemo(
        function memoizeItems() {
            return [
                {
                    children: t.CONTACT.open_in_crm(),
                    onClick: openInCRM,
                },
                {
                    children: t.GLOBAL.copy_shareable_link(),
                    onClick: copySharableLink,
                },
                {
                    children: t.CONTACT.merge_to_existing(),
                    onClick: mergeToExisting,
                },
            ]
        },
        [
            t,
            openInCRM,
            copySharableLink,
            mergeToExisting,
        ],
    )
}
