import {useMemo} from 'react'
import type {Column} from '@tanstack/react-table'
import {createColumnHelper} from '@tanstack/react-table'

import type {ContactShortType} from '@bpinc/ad-contacts-state-types'
import {useLocalizationState} from '@bpinc/state-localization-context'


const columnHelper = createColumnHelper<ContactShortType>()

export const useContactTableColumns = () => {
    const {t} = useLocalizationState()

    return useMemo(() => [
        columnHelper.accessor('firstName', {
            id: 'firstName',
            header: () => t.UNDEFINED.contact_first_name(),
            meta: {
                width: '20%',
            },
        }),
        columnHelper.accessor('lastName', {
            id: 'lastName',
            header: () => t.UNDEFINED.contact_last_name(),
            meta: {
                width: '20%',
            },
        }),
        columnHelper.accessor('phone', {
            id: 'phone',
            header: () => t.UNDEFINED.contact_phone(),
            meta: {
                width: '10%',
            },
        }),
        columnHelper.accessor('email', {
            id: 'email',
            header: () => t.UNDEFINED.contact_email(),
            meta: {
                width: '50%',
            },
        }),
        columnHelper.accessor('source', {
            id: 'source',
            header: () => t.UNDEFINED.column_type(),
            meta: {
                width: '100px',
            },
        }),
    ], [t.UNDEFINED])
}

export function getContactTableColumnWidth(column: Column<ContactShortType>) {
    return (column.columnDef.meta && 'width' in column.columnDef.meta) ? column.columnDef.meta.width as string : 'auto'
}

