import {useMemo} from 'react'

import type {FormButtonAction} from '@bpinc/ad-request-urls'
import {useServiceState} from '@bpinc/ad-service-context'
import {useServicesDispositionsState} from '@bpinc/ad-services-dispositions-context'
import {useSessionState} from '@bpinc/ad-session-context'


export function useButtonPrivilegesChecker(action?: FormButtonAction) {
    const {privileges, phoneDeviceCapabilities} = useSessionState()
    const {selectedService} = useServiceState()
    const {outboundChatChannels} = useServicesDispositionsState()

    return useMemo(function memoizeHasPrivileges() {
        const privilegesLoaded = !!Object.keys(privileges).length
        const canStartExternalChatPrivilege =
            privilegesLoaded && !!privileges.CAN_START_EXTERNAL_CHAT

        if (action === 'Start SMS') {
            return canStartExternalChatPrivilege && !!withOutboundChatChannel()
        }

        if (action === 'Call number') {
            return !!phoneDeviceCapabilities.canDial
        }

        return true

        function withOutboundChatChannel() {
            return selectedService && outboundChatChannels[selectedService]?.includes('SMS')
        }
    }, [outboundChatChannels, phoneDeviceCapabilities.canDial, privileges, selectedService, action])

}
