import type {CSSProperties, ReactElement, ReactNode} from 'react'
import React, {useCallback, useId} from 'react'
import type {Control} from 'react-hook-form'
import {useController} from 'react-hook-form'

import type {ContactDetailsType} from '@bpinc/ad-contacts-state-types'
import {formatPhone} from '@bpinc/ad-formatting-helpers'
import {useActivityHistoryOpener, useCallHandler} from '@bpinc/ad-interaction-state-hooks'
import type {ContactMessenger} from '@bpinc/ad-request-urls'
import {SmForm} from '@bpinc/lib-external-ui-elements'
import type {ButtonElement, FormElement, ImageElement} from '@bpinc/lib-form-builder'
import {FormBuilderButton, Image, Spacer} from '@bpinc/lib-form-builder-ui'
import {MessengerView, RadioFieldRaw} from '@bpinc/lib-forms-ui'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {useLocalizationState} from '@bpinc/state-localization-context'


export interface FormElementViewProps {
    label?: string
    value?: string
    contactId?: string
    userId?: string
    values?: ContactDetailsType
    type?: FormElement['type']
    image?: ImageElement
    button?: ButtonElement
    messenger?: ContactMessenger
    height?: CSSProperties['height']
    name?: string
    options?: FormElement['options']
    control?: Control<ContactDetailsType>
    additionalControls?: ReactElement
    isStaticEntry?: boolean
}

export const FormElementView: React.FC<FormElementViewProps> = ({
    label,
    value,
    type,
    image,
    button,
    height,
    contactId,
    userId,
    messenger,
    values,
    name,
    options,
    isStaticEntry,
    control,
    additionalControls,
}) => {
    const {t} = useLocalizationState()
    const handleCall = useCallHandler()
    const openActivityHistory = useActivityHistoryOpener()

    const ariaId1 = useId()
    const ariaId2 = useId()

    const firstName = values?.['Contact.first_name']
    const lastName = values?.['Contact.last_name']

    const viewListItemClassName = tcn('flex flex-col border-b border-main-border px-4 py-2')

    const handleClick = useCallback(
        (e: React.MouseEvent) => {
            e.preventDefault()

            const el = e.currentTarget
            const elementType = el.getAttribute('data-type') as FormElementViewProps['type']
            const elementValue = el.getAttribute('data-value')

            if (elementType) {
                switch (elementType) {
                    case 'number':
                        return (
                            elementValue &&
                            handleCall({
                                phone: elementValue,
                                contactId,
                                firstName,
                                lastName,
                                navigateToInteractionsPage: true,
                                origination: 'contact-profile',
                            })
                        )
                    case 'email':
                        return alert('TODO: handle email click for: ' + elementValue)
                    case 'message':
                        return openActivityHistory({
                            contactId,
                            userId,
                            origination: 'contact-profile',
                            firstName,
                            lastName,
                            phone: userId ? undefined : value,
                        })
                }
            }
        },
        [handleCall, contactId, firstName, lastName, openActivityHistory, userId, value],
    )

    const renderTextFormElement = useCallback(
        function renderTextFormElementCallback(content: ReactElement) {
            return (
                <div
                    role="listitem"
                    className={viewListItemClassName}
                    id={ariaId1}
                    data-testid={name}
                    data-form-field="true"
                    data-field-type="text-view"
                >
                    <div>{label}</div>
                    <div className="!flex leading-[20px]">
                        {content}
                        {additionalControls && <div className="ml-2 inline-flex">{additionalControls}</div>}
                    </div>
                </div>
            )
        },
        [additionalControls, viewListItemClassName, ariaId1, label, name],
    )

    const renderLink = useCallback(
        function renderLinkCallback(content: ReactNode) {
            return (
                <a
                    onClick={handleClick}
                    data-type={type}
                    data-value={value}
                    tabIndex={0}
                    role="button"
                    aria-labelledby={`${ariaId1} ${ariaId2}`}
                    id={ariaId2}
                    data-testid="content"
                    data-clickable="true"
                    className="truncate font-bold text-primary-action"
                >
                    {content}
                </a>
            )
        },
        [ariaId1, ariaId2, handleClick, type, value],
    )


    if (button) {
        if (button.action === 'Augmentation' || button.action === 'Create email') {
            return null
        }

        return (
            <div role="listitem" className={viewListItemClassName}>
                <FormBuilderButton
                    {...button}
                    primary
                    contactId={contactId}
                    values={values}
                    dataKey={name}
                    action={button.action || 'Do Nothing'}
                >
                    {button.value}
                </FormBuilderButton>
            </div>
        )
    }
    if (image) {
        return (
            <div role="listitem" className={viewListItemClassName}>
                <Image {...image} value={value} />
            </div>
        )
    }

    if (type === 'spacer') {
        return (
            <div role="listitem" className={viewListItemClassName}>
                <Spacer height={height} />
            </div>
        )
    }

    if (type === 'link') {
        return (
            <div role="listitem" className={viewListItemClassName} data-testid={name} data-form-field="true" data-field-type="link">
                <a href={`//${value}`} target="_blank" rel="noopener noreferrer">
                    {label}
                </a>
            </div>
        )
    }

    if (type === 'radio' && name && control) {
        return (
            <div role="listitem" className={viewListItemClassName} data-testid={name} data-form-field="true" data-field-type="link">
                <div>{label}</div>
                <div>
                    <SmForm.Group className="mx-[-9px] mb-0 mt-2">
                        {options?.map(option => (
                            <MergeContactRadioField
                                key={option.id}
                                control={control}
                                name={name}
                                label={option.name}
                                radioValue={option.id}
                                defaultValue={options[0]?.id}
                            />
                        ))}
                    </SmForm.Group>
                </div>
            </div>
        )
    }

    if (type === 'message') {
        return renderTextFormElement(
            isStaticEntry ? (
                <span data-testid="content" className="truncate">{value}</span>
            ) : (
                renderLink(userId === value ? t.CONTACT.internal_messages_field_value() : formatPhone(value))
            ),
        )
    }

    if (type === 'messenger' && messenger) {
        return renderTextFormElement(
            <MessengerView
                contactId={contactId}
                messenger={messenger}
                firstName={firstName}
                lastName={lastName}
            />,
        )
    }

    return renderTextFormElement(
        type ? renderLink(type === 'number' ? formatPhone(value) : value) : <span data-testid="content" className="truncate">{value}</span>,
    )
}

type MergeContactRadioFieldProps = {
    control: Control<ContactDetailsType>
    name: string
    label: string
    defaultValue?: string
    radioValue: string
}

const MergeContactRadioField: React.FC<MergeContactRadioFieldProps> = ({
    control,
    radioValue,
    name,
    defaultValue,
    label,
}) => {
    const {field} = useController({
        name,
        control,
        defaultValue,
    })

    return (
        <RadioFieldRaw
            field={field}
            className="!w-1/2"
            label={label}
            radioValue={radioValue}
        />
    )
}
