import React from 'react'

import {useAppConfigState} from '@bpinc/ad-app-config-state-context'
import type {ServiceDetails} from '@bpinc/ad-service-details-state-types'
import {toHHMMSS, useEvent} from '@bpinc/lib-helpers'
import {isDefined} from '@bpinc/lib-types-helpers'
import {useLocalizationState} from '@bpinc/state-localization-context'


type Props = {
    metrics: ServiceDetails
}

export const ServiceMetrics: React.FC<Props> = ({metrics}) => {

    const {DIRECTORY: i18n} = useLocalizationState('t')
    const {primaryMetric, secondaryMetric} = useAppConfigState(['primaryMetric', 'secondaryMetric'])

    const getDisplayMetrics = useEvent((): string[] => {
        return [primaryMetric, secondaryMetric]
            .map(serverMetricName => {
                switch (serverMetricName) {
                    case 'in_calls_waiting': {
                        const value = Number(metrics.inCallsWaiting)
                        return i18n.waiting(value)
                    }
                    case 'in_max_wait_time': {
                        const seconds = Number(metrics.inMaxWaitTime)
                        const value = toHHMMSS(seconds)
                        return i18n.max_wait(value)
                    }
                    case 'in_ewt': {
                        const seconds = Number(metrics.inEwt)
                        const value = toHHMMSS(seconds)
                        return i18n.ewt(value)
                    }
                    case 'service_level': {
                        const value = Number(metrics.serviceLevel)
                        return i18n.svc_level(value)
                    }
                    case 'agents_ready': {
                        const value = Number(metrics.agentsReady)
                        return i18n.ready(value)
                    }
                    case 'agents_logged': {
                        const value = Number(metrics.agentsLogged)
                        return i18n.logged_in(value)
                    }
                }
            })
            .filter(isDefined)
    })

    const displayMetrics = getDisplayMetrics()

    return (
        <div
            data-testid="rt-metrics"
            className="flex flex-col overflow-hidden caption-s text-neutral-fade"
        >
            {displayMetrics.map((metric => (
                <span key={metric} className="truncate">{metric}</span>
            )))}
        </div>
    )

}
