import {useMemo} from 'react'

import {useServiceDetailsState} from '@bpinc/ad-service-details-state-context'


export function useDirectoryEntryOnlineStatus(serviceId: string | undefined, huntGroupId: string | undefined) {
    const {serviceDetails, huntGroupsDetails} = useServiceDetailsState()
    const details = serviceId && serviceDetails[serviceId]
    const huntGroupDetails = huntGroupId ? huntGroupsDetails[huntGroupId] : undefined

    return useMemo(
        function memoizeStaticEntryOnlineStatus() {
            if (details) {
                if (Number(details.agentsReady)) {
                    return 'readyService'
                } else if (Number(details.agentsLogged)) {
                    return 'notReadyService'
                } else {
                    return 'loggedOut'
                }
            }
            if (huntGroupDetails) {
                if (huntGroupDetails.agentsLogged) {
                    return 'readyService'
                } else if (huntGroupDetails.phonesRegistered) {
                    return 'notReadyService'
                } else {
                    return 'loggedOut'
                }
            }
        },
        [details, huntGroupDetails],
    )
}
