import type {FC} from 'react'
import React, {useEffect, useRef} from 'react'

import {MicrophoneVolume} from '@bpinc/lib-helpers'
import {IconMute16, IconVoiceVolume} from '@bpinc/lib-jskit-ui'
import {tcn} from '@bpinc/lib-tailwind-classnames'


interface MicrophoneIconProps {
    muted: boolean
    className?: string
}

const micVolumeManager = new MicrophoneVolume()

export const MicrophoneIcon: FC<MicrophoneIconProps> = ({muted, className}) => {
    const microphoneRef = useRef<SVGSVGElement>(null)

    useEffect(() => {
        if (!muted) {
            micVolumeManager.startAnalysis(volume => {
                if (!microphoneRef.current) {
                    return
                }

                updateGradient(microphoneRef.current)

                function updateGradient(svg: SVGElement) {
                    const breakpoint = svg.querySelector<SVGStopElement>('linearGradient stop')
                    if (!breakpoint) {
                        return
                    }

                    breakpoint.setAttribute('offset', `${volume}%`)
                }
            })
        }

        return () => {
            micVolumeManager.stopAnalysis()
        }
    }, [muted])
    return muted ? (
        <IconMute16 className={tcn('size-4.5', className)} />
    ) : (
        <IconVoiceVolume className={tcn('size-4.5 icon-default', className)} ref={microphoneRef} />
    )
}
