import React from 'react'

import {useFormatting} from '@bpinc/ad-formatting-context'
import {formatPhone} from '@bpinc/ad-formatting-helpers'


interface ActiveItemPhoneProps {
    number: string
    numberType?: string
    isContact: boolean
}

export const ActiveItemPhone: React.FC<ActiveItemPhoneProps> = ({number, numberType, isContact}) => {
    const {
        formatPhoneType,
    } = useFormatting()

    const phoneName = formatPhoneType(numberType)

    return (
        <div className="truncate caption-s text-neutral-fade">
            {isContact && (
                <>
                    <span data-testid="phone-type">
                        {phoneName}
                    </span>
                    <span>: </span>
                </>
            )}
            <span data-testid="phone">
                {formatPhone(number)}
            </span>
        </div>
    )
}
