import React from 'react'

import type {InteractionAssociatedObject} from '@bpinc/ad-interaction-state-types'

import {CrmObjectListItem} from './CRMObjectListItem'


type Props = {
    crmObjects: InteractionAssociatedObject[]
}

export const CRMObjectsList: React.FC<Props> = ({
    crmObjects,
}) => {
    return (
        <div data-testid="crm-objects" className="ml-11 flex flex-col">
            {crmObjects.map(crmObject => (
                <CrmObjectListItem key={crmObject.id} crmObject={crmObject} />
            ))}
        </div>
    )
}
