import type {FC} from 'react'
import React from 'react'

import {useActiveInteraction} from '@bpinc/ad-interaction-state-context'

import {ActiveInteractionCaseFooterContent} from './ActiveInteractionCaseFooterContent'
import type {CaseProps} from './Case'
import {Case} from './Case'


type ActiveCaseProps = CaseProps

export const ActiveCase: FC<ActiveCaseProps> = props => {
    const item = useActiveInteraction()


    if (!item?.emailData?.caseId) {
        return null
    }


    return (
        <Case
            {...props}
            interactionId={item.id}
            activeDrafts={item?.emailData?.activeDrafts}
            FooterContent={
                <ActiveInteractionCaseFooterContent
                    caseId={item.emailData.caseId}
                />
            }
        />
    )
}
