import {useMemo} from 'react'
import {partialRight} from 'lodash-es'

import {useDirectoryState, useDirectoryStateMethods} from '@bpinc/ad-directory-state-context'
import type {SearchUserItem} from '@bpinc/ad-directory-state-types'
import {useSessionState} from '@bpinc/ad-session-context'

import {isInternalContactExtension, useCheckSelfUser} from '../../helpers'
import type {InternalContact} from './useQueryContacts'


export function useInternalContacts(searchQuery?: string) {
    const {privileges} = useSessionState()
    const checkSelfUser = useCheckSelfUser()
    const searchResults = useDirectoryState('searchResults')
    const {findContacts} = useDirectoryStateMethods()

    const privilegesLoaded = !!Object.keys(privileges).length
    const internalContacts: InternalContact[] = useMemo(
        function memoizeInternalContacts() {
            const internalContactsInner =
                privilegesLoaded && privileges.CAN_VIEW_DIRECTORY
                    ? searchResults['dialpad']?.data.users ?? []
                    : []
            if (searchQuery?.length && internalContactsInner.length) {
                return internalContactsInner.filter(({extension, userId}: SearchUserItem) =>
                    extension.includes(searchQuery) && !checkSelfUser(userId) && isInternalContactExtension(extension),
                )
            }
            return internalContactsInner
        },
        [privileges.CAN_VIEW_DIRECTORY, privilegesLoaded, searchQuery, searchResults],
    )


    const getInternalContactsByQuery = useMemo(
        function memoizeGetInternalContactsByQuery() {
            return partialRight(findContacts, 'dialpad', 0)
        },
        [findContacts],
    )

    const internalContactsLoading = useMemo(function memoizeInternalContactsLoading() {
        return searchResults['dialpad']?.isLoading ?? false
    }, [searchResults])

    return useMemo(
        function memoizeOutput() {
            return {internalContacts, getInternalContactsByQuery, internalContactsLoading}
        },
        [getInternalContactsByQuery, internalContacts, internalContactsLoading],
    )
}
