import type {ChangeEvent} from 'react'
import React, {useCallback, useEffect, useRef, useState} from 'react'
import {debounce} from 'lodash-es'

import {
    useInteractionProp,
    useInteractionStateConnector,
    useInteractionStateMethods,
} from '@bpinc/ad-interaction-state-context'
import {getItemLastNote} from '@bpinc/ad-interaction-state-methods'
import {useInteractionId} from '@bpinc/lib-interaction-context'
import {TextAreaField} from '@bpinc/lib-jskit-ui'
import {Notifications, useNotificationsState} from '@bpinc/lib-notifications-context'
import {useLocalizationState} from '@bpinc/state-localization-context'

import type {ResolutionNoteProps} from '../../types'


const NOTES_MAX_LENGTH = 3000
const HIDE_ERROR_BLUR_TIMEOUT = 2000

export const ResolutionNote: React.FC<ResolutionNoteProps> = React.memo(function ResolutionNote({
    layoutClassName,
}) {
    const interactionId = useInteractionId()
    const synchronizedNotes = useInteractionProp(interactionId, getItemLastNote)
    const [note, setNote] = useState(synchronizedNotes)

    const {updateNote: setNoteSynched} = useInteractionStateMethods()
    const debouncedSetNoteSynched = useRef(debounce(setNoteSynched, 300)).current
    const {t} = useLocalizationState()
    const {showNotification, hideNotificationById} = useNotificationsState('methods')

    const interactionStateConnector = useInteractionStateConnector()

    useEffect(function subscribeToNoteUpdates() {
        const subscription = interactionStateConnector?.dispenser.subscribe({
            onNoteUpdated: (itemId, newNote) => {
                if (itemId === interactionId) {
                    setNote(newNote)
                }
            },
        })

        return () => {
            subscription?.unsubscribe()
        }
    }, [interactionId, interactionStateConnector?.dispenser])

    const handleChange = useCallback(
        (event: ChangeEvent<HTMLTextAreaElement>) => {
            const notes = event.target.value

            const validatedNotes = validateNotes()

            debouncedSetNoteSynched({itemId: interactionId, note: validatedNotes})
            setNote(validatedNotes)

            //===
            function validateNotes() {
                if (notes.length > NOTES_MAX_LENGTH) {
                    showNotification({
                        type: 'error',
                        id: Notifications.NotesLengthExceededError,
                        tags: ['current-view'],
                        message: t.DISPOSITION.notes_length_error_text(String(NOTES_MAX_LENGTH)),
                    })
                    return notes.substring(0, NOTES_MAX_LENGTH)
                } else {
                    hideNotificationById(Notifications.NotesLengthExceededError)
                    return notes
                }
            }
        },
        [interactionId, debouncedSetNoteSynched, hideNotificationById, showNotification, t.DISPOSITION],
    )

    const handleBlur = useCallback(
        function handleBlurCallback(evt: React.FocusEvent<HTMLTextAreaElement>) {
            setNoteSynched({itemId: interactionId, note: evt.target.value})
            setTimeout(() => {
                hideNotificationById(Notifications.NotesLengthExceededError)
            }, HIDE_ERROR_BLUR_TIMEOUT)
        },
        [interactionId, hideNotificationById, setNoteSynched],
    )

    return (
        <TextAreaField
            value={note}
            textAreaClassName="min-h-[85px]"
            label={t.DISPOSITION.note()}
            layoutClassName={layoutClassName}
            placeholder={t.DISPOSITION.write_a_note()}
            onBlur={handleBlur}
            onChange={handleChange}
            data-testid="notes-textarea"
        />
    )
})
