import React, {useEffect} from 'react'

import {useBpClientStateConnector} from '@bpinc/ad-bpclient-state-context'
import {SmButton} from '@bpinc/lib-external-ui-elements'
import {useEvent} from '@bpinc/lib-helpers'
import {useLocalizationState} from '@bpinc/state-localization-context'
import {useModalsStateMethods} from '@bpinc/state-modals-context'

import {CURRENT_DOMAIN_AUTH_URL} from '../constants'


const DOMAIN_AUTHORIZATION_MODAL_ID = 'bpClient/onShowDomainAuthorizationDialog'


export const useDomainAuthorizationPopup = () => {
    const bpClientConnector = useBpClientStateConnector()
    const {openModal, closeModal} = useModalsStateMethods()
    const t = useLocalizationState('t')

    const onShowDomainAuthorizationDialog = useEvent(() => {
        openModal({
            id: DOMAIN_AUTHORIZATION_MODAL_ID,
            title: t.INSTALLATION_PROCESS.bp_client_not_authorized_header(),
            body: (
                <>
                    <span>
                        {t.INSTALLATION_PROCESS.bp_client_not_authorized_message_part1()}
                    </span>
                    <a href={CURRENT_DOMAIN_AUTH_URL} rel="noopener noreferrer" target="_blank">
                        {t.ERROR_MESSAGES.domainAuthentificationErrorAnchor()}
                    </a>
                    <span>
                        {t.INSTALLATION_PROCESS.bp_client_not_authorized_message_part2()}
                    </span>
                </>
            ),
            actions: ({closeModal: closeThisModal}) => (
                <SmButton onClick={closeThisModal}>
                    {t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_buttonOk()}
                </SmButton>
            ),
        })
    })

    const onHideDomainAuthorizationDialog = useEvent(() => {
        closeModal(DOMAIN_AUTHORIZATION_MODAL_ID)
    })

    useEffect(() => {
        const subscription = bpClientConnector.dispenser.subscribe({
            onShowDomainAuthorizationDialog,
            onHideDomainAuthorizationDialog,
        })
        return subscription.unsubscribe
    }, [bpClientConnector, onShowDomainAuthorizationDialog, onHideDomainAuthorizationDialog])
}
