import {useEffect} from 'react'

import {useCommunicatorUIState} from '@bpinc/ad-main-page-ui-context'
import {useInteractionId} from '@bpinc/lib-interaction-context'
import {useOpenInteractionSlidingModal} from '@bpinc/lib-sliding-modal-context'


export const useActiveTransferModalOpener = () => {
    const itemId = useInteractionId()
    const {isOpenTransferModal, handleToggleTransferModal} = useCommunicatorUIState()
    const openInteractionSlidingModal = useOpenInteractionSlidingModal()

    useEffect(() => {
        if (isOpenTransferModal) {
            openInteractionSlidingModal(itemId, 'transfer')
            handleToggleTransferModal?.(false)
        }
    }, [itemId, openInteractionSlidingModal, isOpenTransferModal, handleToggleTransferModal])
}
