import {useMemo} from 'react'

import {useInteractionProp} from '@bpinc/ad-interaction-state-context'
import {getItemDisposition, getServiceId} from '@bpinc/ad-interaction-state-methods'
import {useServicesDispositionsState} from '@bpinc/ad-services-dispositions-context'
import type {DispositionType} from '@bpinc/ad-transport-types'
import {useInteractionId} from '@bpinc/lib-interaction-context'


export function useSelectedDispositionType(): DispositionType | undefined {
    const interactionId = useInteractionId()
    const serviceId = useInteractionProp(interactionId, getServiceId)
    const selectedDisposition = useInteractionProp(interactionId, getItemDisposition)
    const {dispositions} = useServicesDispositionsState()

    return useMemo(function memoizeOutput() {
        const dispositionsData =
            dispositions && serviceId ? dispositions[serviceId] : {}

        const selectedDispositionData =
            selectedDisposition &&
            dispositionsData?.[selectedDisposition.id]


        return selectedDispositionData?.type
    }, [dispositions, selectedDisposition, serviceId])
}
