import type {CSSProperties} from 'react'
import React, {useCallback} from 'react'
import type {
    FieldArray,
    FieldArrayPath,
    FieldValues,
    Path,
    UseFieldArrayRemove,
} from 'react-hook-form'
import {useFieldArray} from 'react-hook-form'

import type {MessengerData} from '@bpinc/ad-interaction-chat-state-types'
import type {ContactMessenger} from '@bpinc/ad-request-urls'
import type {FieldControlProps} from '@bpinc/lib-forms-base'
import {IconAddMessenger} from '@bpinc/lib-icons'
import {tcn} from '@bpinc/lib-tailwind-classnames'

import {AddRow} from './AddRow'
import {FieldArrayItem} from './FieldArrayItem'
import {MessengerFieldItem} from './MessengerFieldItem'
import {RemoveRow} from './RemoveRow'


type MessengersFieldArrayFormType = {type: ContactMessenger['type'] | null; user_name: string; user_id: string; account_id: string}

const INITIAL_VALUES: MessengersFieldArrayFormType = {type: null, user_name: '', user_id: '', account_id: ''}

type MessengersFieldArrayProps<TValues extends FieldValues> = {
    name: FieldArrayPath<TValues>
    className?: string
    typeClassName?: string
    layoutClassName?: string
    availableMessengers: MessengerData[]
    style?: CSSProperties
}

export const MessengersFieldArray = <TValues extends FieldValues>({
    availableMessengers,
    name,
    control,
    className,
    layoutClassName,
    typeClassName,
    style,
}: FieldControlProps<TValues, FieldValues> & MessengersFieldArrayProps<TValues>) => {
    const {fields, append, remove: removeItem} = useFieldArray({
        control,
        name,
    })

    const renderComponents = useCallback(
        function renderComponentsCallback(
            field: Partial<MessengersFieldArrayFormType>,
            index: number,
            remove: UseFieldArrayRemove,
        ) {
            return (
                <div role="row" className={tcn(layoutClassName, 'flex flex-row')}>
                    <MessengerFieldItem
                        control={control}
                        typeFiledName={`${name}.${index}.type` as Path<TValues>}
                        userIdFieldName={`${name}.${index}.user_id` as Path<TValues>}
                        userNameFieldName={`${name}.${index}.user_name` as Path<TValues>}
                        accountIdFieldName={`${name}.${index}.account_id` as Path<TValues>}
                        availableMessengers={availableMessengers}
                        accountIdDefault={field.account_id}
                        userIdDefault={field.user_id}
                        userNameDefault={field.user_name}
                        typeClassName={typeClassName}
                        style={style}
                    />
                    <RemoveRow index={index} remove={remove} />
                </div>
            )
        },
        [availableMessengers, control, layoutClassName, name, style, typeClassName],
    )

    return (
        <div className={tcn('flex flex-col', layoutClassName, className)}>
            {fields.map((field, index) => (
                <FieldArrayItem key={index}>
                    {renderComponents(field as TValues, index, removeItem)}
                </FieldArrayItem>
            ))}
            {Boolean(availableMessengers.length) && (
                <AddRow
                    append={append}
                    initialValues={INITIAL_VALUES as FieldArray<TValues>}
                    AddButtonIcon={IconAddMessenger}
                />
            )}
        </div>
    )
}
