import React, {useMemo} from 'react'

import {useDirectoryState, useDirectoryStateMethods} from '@bpinc/ad-directory-state-context'
import type {DirectoryItem} from '@bpinc/ad-directory-state-types'
import {useDefaultTransferMode} from '@bpinc/ad-interaction-state-context'
import {useContactDetailsOpener} from '@bpinc/ad-interaction-state-hooks'
import {useNativeScrollDataManager} from '@bpinc/ad-scroll-state-context-builder'
import {ContactListPlaceholder} from '@bpinc/lib-contact-list-item-ui'
import {useEventListener} from '@bpinc/lib-helpers'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {SelectionContainer} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {SCROLL_KEYS} from '../../../scroll-context'
import {useDirectoryHandlerKeys} from '../../hooks/useDirectoryHandlerKeys'
import {useDirectorySearch} from '../../hooks/useDirectorySearch'
import {SearchResults} from '../SearchResults'
import {DirectoryFolderItem} from './DirectoryFolderItem'
import {useAgentTeamsInitializer} from './useAgentTeamsInitializer'
import {useContentHandlers} from './useContentHandlers'
import {useDirectoryFoldersList} from './useDirectoryFoldersList'
import {useFilteredDirectoryState} from './useFilteredDirectoryState'
import {useFilteredUsers} from './useFilteredUsers'
import {useOpenedTeamsRetrieverFromLocalStorage} from './useOpenedTeamsRetrieverFromLocalStorage'
import {useOpenFoldersState} from './useOpenFoldersState'
import {useTeamMembersLoader} from './useTeamMembersLoader'
import {useToggleDirectoryFolder} from './useToggleDirectoryFolder'


type Props = {
    transferMode?: 'messages' | 'calls'
    transferControlsStyle?: 'add' | 'replace'
    query?: string
    onClick?: (evt: React.MouseEvent, item: DirectoryItem) => void
    onDoubleClick?: (item: DirectoryItem) => void
    isHighlighted?: (item: DirectoryItem) => boolean
    filter?: (item: DirectoryItem) => boolean
}

export const Internal: React.FC<Props> = props => {
    const defaultTransferMode = useDefaultTransferMode()
    const isTeamsLoading = useDirectoryState('isTeamsLoading')
    const {
        query,
        transferMode = defaultTransferMode,
        transferControlsStyle,
        onClick,
        onDoubleClick,
        filter,
        isHighlighted,
    } = props
    const [openFolders, setOpenFolders] = useOpenFoldersState(query)
    const {DIRECTORY: i18n} = useLocalizationState('t')
    const isSearchMode = !!query

    const {
        filteredSearchResults,
        filteredDirectoryEntries,
        filteredCategories,
        filteredEmiUsers,
    } = useFilteredDirectoryState({transferMode, filter})

    const filteredUsers = useFilteredUsers({transferMode, filter})

    const foldersList = useDirectoryFoldersList({
        isSearchMode,
        filteredSearchResults,
        filteredCategories,
    })

    const {findContacts} = useDirectoryStateMethods()

    const {onCall, onChat, onEmail} = useContentHandlers()

    const openContactDetails = useContactDetailsOpener()

    useDirectorySearch(query, (q: string) => findContacts(q, 'directory'))

    useAgentTeamsInitializer()
    useTeamMembersLoader({openFolders, foldersList})
    const retrievedOpenedTeamsFromLocalStorage = useOpenedTeamsRetrieverFromLocalStorage({
        isSearchMode,
        setOpenFolders,
    })

    const retrieveScrollCondition = useMemo(() => {
        return isSearchMode
            ? !filteredSearchResults.isLoading
            : !!foldersList.length && retrievedOpenedTeamsFromLocalStorage

    }, [filteredSearchResults.isLoading, isSearchMode, foldersList.length, retrievedOpenedTeamsFromLocalStorage])

    const listRef = useNativeScrollDataManager(SCROLL_KEYS.directory('internal'), {retrieveScrollCondition})

    const toggleDirectoryFolder = useToggleDirectoryFolder({isSearchMode, setOpenFolders})

    const emptyMessage = useMemo(() => {

        if (filteredSearchResults.count) {
            return (
                <div className="text-center" data-testid="too-many-search-results">
                    {i18n.too_many_results_found()}
                </div>
            )
        }

        const messageText = query
            ? i18n.no_results_internal_for_query(query)
            : i18n.no_results_internal()

        return <div className="text-center">{messageText}</div>

    }, [filteredSearchResults.count, query, i18n])

    const directoryHandlerKeys = useDirectoryHandlerKeys(listRef, 'directory-item')
    useEventListener('keydown', directoryHandlerKeys)

    const showPlaceholder = (isSearchMode && filteredSearchResults.isLoading) || (isTeamsLoading && !foldersList.length)

    return (
        <SelectionContainer
            className={tcn(
                'scrollbar-on-hover flex-auto overflow-auto',
                '[&:not(:first-child)]:mt-0 [&:not(:last-child)]:mb-0',
                '[&:not(:last-child)]:border-b [&:not(:last-child)]:border-main-border',
                {'overflow-y-hidden': showPlaceholder},
            )}
        >
            {/* without this extra wrapper with filled background "scrollbar-on-hover" produces visual artifacts */}
            <div className="bg-primary-background">
                {showPlaceholder ? (
                    <div className="mt-2">
                        <ContactListPlaceholder amount={15} />
                    </div>
                ) : (
                    <>
                        {foldersList.map(({type, item}) => (
                            <DirectoryFolderItem
                                key={item.id}
                                type={type}
                                item={item}
                                opened={openFolders.list.includes(item.id)}
                                query={query}
                                transferMode={transferMode}
                                transferControlsStyle={transferControlsStyle}
                                listRef={listRef}
                                filteredUsers={filteredUsers}
                                filteredDirectoryEntries={filteredDirectoryEntries}
                                filteredEmiUsers={filteredEmiUsers}
                                toggleDirectoryFolder={toggleDirectoryFolder}
                                onCall={onCall}
                                onChat={onChat}
                                onEmail={onEmail}
                                onDetails={openContactDetails}
                                onClick={onClick}
                                onDoubleClick={onDoubleClick}
                                isHighlighted={isHighlighted}
                            />
                        ))}
                        {isSearchMode && !filteredSearchResults.isLoading
                            ? (
                                <SearchResults
                                    transferMode={transferMode}
                                    transferControlsStyle={transferControlsStyle}
                                    query={query}
                                    filteredSearchResults={filteredSearchResults}
                                    filteredDirectoryEntries={filteredDirectoryEntries}
                                    emptyMessage={emptyMessage}
                                    onCall={onCall}
                                    onChat={onChat}
                                    onEmail={onEmail}
                                    onDetails={openContactDetails}
                                    onClick={onClick}
                                    onDoubleClick={onDoubleClick}
                                    isHighlighted={isHighlighted}
                                />
                            )
                            : null
                        }
                        {!isSearchMode && !foldersList.length
                            ? emptyMessage
                            : null
                        }
                    </>
                )}
            </div>
        </SelectionContainer>
    )
}

