import {useEffect, useState} from 'react'

import {useEvent} from '@bpinc/lib-helpers'


export function useDirectorySearch(
    query: string | undefined,
    searchFunction: (query: string) => void,
    debounceTimeout?: number,
) {
    const [isDebounceProcessing, setIsDebounceProcessing] = useState(false)

    const searchEvent = useEvent(searchFunction)

    useEffect(() => {
        let timeout: number

        if (query) {
            if (debounceTimeout) {
                setIsDebounceProcessing(true)
                timeout = setTimeout(() => {
                    searchEvent(query)
                    setIsDebounceProcessing(false)
                }, debounceTimeout)
            } else {
                searchEvent(query)
            }
        }

        return () => {
            if (timeout) {
                setIsDebounceProcessing(false)
                clearTimeout(timeout)
            }
        }
    }, [query, debounceTimeout, searchEvent])

    return isDebounceProcessing
}
