import React, {useEffect} from 'react'

import {useBpClientState} from '@bpinc/ad-bpclient-state-context'
import {useEvent} from '@bpinc/lib-helpers'
import {Notifications, useNotificationsState} from '@bpinc/lib-notifications-context'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {CURRENT_DOMAIN_AUTH_URL} from '../constants'


export function useUnauthorizedDomainNotification() {
    const {showNotification, hideNotificationById} = useNotificationsState('methods')
    const isDomainAuthorized = useBpClientState('isDomainAuthorized')
    const {ERROR_MESSAGES: i18n} = useLocalizationState('t')

    const updateNotification = useEvent(() => {
        if (!isDomainAuthorized) {
            showNotification({
                type: 'error',
                id: Notifications.DomainNotAuthorizedForBPClientError,
                closable: false,
                message: (
                    <>
                        <span>
                            {i18n.domain_authentication_error_part1()}
                        </span>
                        <a href={CURRENT_DOMAIN_AUTH_URL} rel="noopener noreferrer" target="_blank">
                            {i18n.domainAuthentificationErrorAnchor()}
                        </a>
                        <span>
                            {i18n.domain_authentication_error_part2()}
                        </span>
                    </>
                ),
            })
        } else {
            hideNotificationById(Notifications.DomainNotAuthorizedForBPClientError)
        }
    })

    useEffect(() => {
        updateNotification()
        // track only isDomainAuthorized
    }, [isDomainAuthorized, updateNotification])
}
