import {useEffect} from 'react'

import {useInteractionProp} from '@bpinc/ad-interaction-state-context'
import {isAfterCallWork} from '@bpinc/ad-interaction-state-methods'
import {useInteractionId} from '@bpinc/lib-interaction-context'
import {useCloseInteractionSlidingModal, useShowSlidingModalChecker} from '@bpinc/lib-sliding-modal-context'


export function useAutoClose() {
    const interactionId = useInteractionId()
    const isACW = useInteractionProp(interactionId, isAfterCallWork)

    const closeInteractionSlidingModal = useCloseInteractionSlidingModal()
    const showTransferModal = useShowSlidingModalChecker(interactionId, 'transfer')

    useEffect(() => {
        if (showTransferModal && isACW) {
            closeInteractionSlidingModal(interactionId, 'transfer')
        }
    }, [isACW, showTransferModal, closeInteractionSlidingModal, interactionId])

}
