import type {CSSProperties} from 'react'
import React, {useCallback, useMemo} from 'react'
import type {FieldValues, UseFieldArrayRemove} from 'react-hook-form'

import type {ServerEmailType} from '@bpinc/ad-contacts-state-types'
import {EmailTypes} from '@bpinc/ad-contacts-state-types'
import {useFormatting} from '@bpinc/ad-formatting-context'
import type {FieldArrayControlProps, FieldArrayPathByValue, FieldSubPathsByValue} from '@bpinc/lib-forms-base'
import {useEmailValidationRules} from '@bpinc/lib-forms-base'
import {IconAddEmail} from '@bpinc/lib-icons'
import type {OptionalBy} from '@bpinc/lib-types-helpers'

import {FieldArray} from './FieldArray'
import {SelectAndTextFieldRow} from './SelectAndTextFieldRow'


type EmailFieldType = OptionalBy<ServerEmailType, 'id'>

const INITIAL_VALUES: EmailFieldType = {type: EmailTypes.BUSINESS, email_address: ''}

type EmailFieldArrayProps = {
    typeClassName?: string
    className?: string
    layoutClassName?: string
    style?: CSSProperties
}

export const EmailFieldArray = <
    TValues extends FieldValues,
    TFieldName extends FieldArrayPathByValue<TValues, EmailFieldType>
>({
    name,
    control,
    className,
    typeClassName,
    layoutClassName,
    style,
}: FieldArrayControlProps<EmailFieldType, TValues, TFieldName> & EmailFieldArrayProps) => {
    const {formatEmailType} = useFormatting()
    const emailValidationRules = useEmailValidationRules()

    const optionsEmailTypes = useMemo(() => {
        return Object.keys(EmailTypes).map(type => ({
            value: type,
            label: formatEmailType(type),
        }))
    }, [formatEmailType])

    const renderComponents = useCallback(
        function renderComponentsCallback(
            index: number,
            remove: UseFieldArrayRemove,
        ) {
            return (
                <SelectAndTextFieldRow
                    idFieldName={`${name}.${index}.id` as FieldSubPathsByValue<TValues, EmailFieldType>}
                    selectFieldName={`${name}.${index}.type` as FieldSubPathsByValue<TValues, EmailFieldType>}
                    textFieldName={`${name}.${index}.email_address` as FieldSubPathsByValue<TValues, EmailFieldType>}
                    control={control}
                    layoutClassName={layoutClassName}
                    typeClassName={typeClassName}
                    index={index}
                    options={optionsEmailTypes}
                    textFiledRules={emailValidationRules}
                    inputType="email"
                    remove={remove}
                    style={style}
                />
            )
        },
        [layoutClassName, typeClassName, name, control, optionsEmailTypes, emailValidationRules, style],
    )

    return (
        <FieldArray
            className={className}
            layoutClassName={layoutClassName}
            control={control}
            name={name}
            initialValues={INITIAL_VALUES}
            renderComponents={renderComponents}
            AddButtonIcon={IconAddEmail}
        />
    )
}
