import {useMemo} from 'react'

import type {FavoritesItem} from '@bpinc/state-favorites-types'
import {
    isFavoritesDirectoryEntryItem,
    isFavoritesMultiMediaChannel,
    isFavoritesUserItem,
} from '@bpinc/state-favorites-types'

import {useCallPartyUsersFilter, useChatPartyUsersFilter} from '../../directory'
import type {FavoritesListChannelItem, FavoritesListProps} from '../types'


interface UseFilteredFavoritesListParams extends FavoritesListProps {
    favoritesList: FavoritesItem[]
}

function isScenarioEntity(item: FavoritesItem) {
    return isFavoritesUserItem(item) || isFavoritesDirectoryEntryItem(item)
}

export function useFilteredAndSortedFavoritesList({
    transferMode,
    favoritesList,
    filter = Boolean,
}: UseFilteredFavoritesListParams) {
    const chatUsersFilter = useChatPartyUsersFilter()
    const callUsersFilter = useCallPartyUsersFilter()

    return useMemo(() => {
        return favoritesList.reduce<FavoritesListChannelItem[]>((result, item) => {
            item.channels.forEach(channel => {
                if (isFavoritesMultiMediaChannel(channel)) {
                    channel.media.forEach(media => {
                        if (!channel.hiddenMedia?.includes(media)) {
                            result.push({item, channel, media})
                        }
                    })

                } else if (!channel.hidden) {
                    result.push({item, channel})
                }
            })

            return result
        }, [])
            .filter(filterFavorites)
            .sort((a, b) => sortFavoritesByName(a.item, b.item))

        function filterFavorites(data: FavoritesListChannelItem) {
            return (
                filter(data) && isAgentInChatTransfer() && isNotAPartyOfActiveInteractionInTransferMode()
            )

            // TODO: revisit this logic, it looks wrong
            function isAgentInChatTransfer() {
                return transferMode === 'messages' ? isScenarioEntity(data.item) : true
            }

            function isNotAPartyOfActiveInteractionInTransferMode() {
                return transferMode === 'calls'
                    ? callUsersFilter(data.item)
                    : transferMode === 'messages'
                        ? chatUsersFilter(data.item)
                        : true
            }
        }

        function sortFavoritesByName(item1: FavoritesItem, item2: FavoritesItem) {
            return item1.displayName.localeCompare(item2.displayName, undefined, {sensitivity: 'base'})
        }

    }, [
        favoritesList,
        filter,
        transferMode,
        callUsersFilter,
        chatUsersFilter,
    ])
}
