import React, {useCallback} from 'react'

import {IconDownloadTranscript, IconViewTranscript} from '@bpinc/lib-jskit-ui'
import {useCloseGlobalSlidingModal, useOpenGlobalSlidingModal} from '@bpinc/lib-sliding-modal-context'
import {useLocalizationState} from '@bpinc/state-localization-context'


type TranscriptOptions = {
    renderIcon: () => JSX.Element
    onClick: () => void
    optionName: string
}

type UseTranscriptionOptionsType = {
    isOpenModal: boolean
    handleCloseView: () => void
    contentVoiceTranscript: JSX.Element | null
}

export const useTranscriptOptions = ({isOpenModal, contentVoiceTranscript, handleCloseView}: UseTranscriptionOptionsType): TranscriptOptions[] => {
    const {t} = useLocalizationState()
    const openGlobalSlidingModal = useOpenGlobalSlidingModal()
    const closeGlobalSlidingModal = useCloseGlobalSlidingModal()
    const iconClassName = 'icon-default size-4'

    const handleOpenViewTranscript = useCallback(() => {
        openGlobalSlidingModal('voiceTranscript', {contentVoiceTranscript})
    }, [openGlobalSlidingModal, contentVoiceTranscript])

    const handleCloseViewTranscript = useCallback(() => {
        closeGlobalSlidingModal('voiceTranscript')
    }, [closeGlobalSlidingModal])

    const viewOption = {
        optionName: isOpenModal ? t.ACTIVITY_HISTORY.close_transcript_option() : t.ACTIVITY_HISTORY.view_transcript_option(),
        onClick: isOpenModal ? handleCloseViewTranscript : handleOpenViewTranscript,
        renderIcon: () => <IconViewTranscript className={iconClassName} />,
    }

    //TODO: add func for download call transcript file
    const downloadOption = {
        optionName: t.ACTIVITY_HISTORY.download_transcript_option(),
        onClick: handleCloseView,
        renderIcon: () => <IconDownloadTranscript className={iconClassName} />,
    }

    return [viewOption, downloadOption]
}
