import React from 'react'

import {formatPhone} from '@bpinc/ad-formatting-helpers'
import {useChatMessageSanitizer} from '@bpinc/ad-interaction-state-hooks'
import {useInnerHTML} from '@bpinc/lib-helpers'
import {IconOutboundCampaign} from '@bpinc/lib-icons'
import {tcn} from '@bpinc/lib-tailwind-classnames'

import {getMessageId} from '../../../../helpers'
import type {HistoryOutboundMessagingCampaignMessage} from '../../../../types'
import {MessageContent} from './MessageContent'
import {MessageTopInfo} from './MessageTopInfo'

import styles from '../../../../styles/Chat.module.less'


type OutboundMessagingCampaignMessageProps = {
    message: HistoryOutboundMessagingCampaignMessage
}

export const OutboundMessagingCampaignMessage: React.FC<OutboundMessagingCampaignMessageProps> = ({message}) => {
    const {serviceName, serviceAddress, msg, timestamp} = message
    const topInfo = `${serviceName} ${formatPhone(serviceAddress)}`
    const id = getMessageId(message)

    const sanitizedMessage = useChatMessageSanitizer({message: msg})

    const messageInnerHTML = useInnerHTML(sanitizedMessage)

    return (
        <div
            className={tcn(styles.messageBlock, styles.outgoing)}
            data-testid="message-item"
            data-outgoing-campaign="true"
            data-outgoing="true"
        >
            <div className={styles.messageWrapper}>
                <MessageTopInfo
                    direction="outgoing"
                    timestamp={timestamp}
                    showName
                    gapBeforeTime
                    displayName={topInfo}
                />
                <section className={tcn(styles.message, styles.outgoing)}>
                    <MessageContent messageId={id}>
                        <span dangerouslySetInnerHTML={messageInnerHTML} />
                    </MessageContent>
                </section>
            </div>
            <IconOutboundCampaign className={styles.avatar} />
        </div>
    )
}
