import {useMemo} from 'react'

import {useInteractionProp} from '@bpinc/ad-interaction-state-context'
import {useInteractionId} from '@bpinc/lib-interaction-context'
import {assertDefined} from '@bpinc/lib-types-helpers'

import type {PhoneNumberItemProps} from './PhoneNumberItem'


export function usePreviewRecordPhoneNumbers() {
    const activeItemId = useInteractionId()
    const workitemData = useInteractionProp(activeItemId, item => item?.workitemData)
    assertDefined(workitemData)

    return useMemo(() => {
        return workitemData.contactInfo.addresses
            .filter(item => item.type === 'phone')
            .map<PhoneNumberItemProps>(item => ({
                number: item.value,
                label: item.label,
                suggested: item.value === workitemData.suggestedPhone,
                disabled: item.allowed !== 'yes',
            }))
    }, [workitemData.contactInfo.addresses, workitemData.suggestedPhone])
}
