import React from 'react'
import {get} from 'lodash-es'

import {bpLogger} from '@bpinc/ad-persistent-logger'
import {useNavigationState} from '@bpinc/lib-communicator-navigation-state'

import {Case} from './Case'
import {Email} from './Email'


export const Cases = () => {
    const {activePage} = useNavigationState()
    const id = get(activePage.params, 'id')
    const emailId = get(activePage.params, 'emailId')

    if (emailId) return <Email id={emailId} />

    if (id) return <Case id={id} />

    bpLogger.error('You are trying to open a case page without providing the case ID.')
    return null
}
