import {useEffect} from 'react'

import {useIntegrationApiStateConnector} from '@bpinc/ad-integration-api-state-context'
import type {Toggle} from '@bpinc/lib-helpers'
import type {StateSetter} from '@bpinc/lib-types-helpers'


interface Params {
    toggleSettings: Toggle
    toggleRightPanel: Toggle
    setIsMinimized: StateSetter<boolean>
}

export function useIntegrationApiSubscription({toggleRightPanel, toggleSettings, setIsMinimized}: Params) {
    const integrationApiConnector = useIntegrationApiStateConnector()

    useEffect(() => {
        const subscription = integrationApiConnector.dispenser.subscribe({
            onSetWidgetMinimized,
        })

        return () => {
            subscription.unsubscribe()
        }


        function onSetWidgetMinimized(widgetMinimized: boolean) {
            if (widgetMinimized) {
                toggleSettings(false)
                toggleRightPanel(false)
            }
            setIsMinimized(widgetMinimized)
            return Promise.resolve()
        }
    }, [integrationApiConnector.dispenser, setIsMinimized, toggleRightPanel, toggleSettings])
}
