import React from 'react'

import {SmForm} from '@bpinc/lib-external-ui-elements'
import {Dropdown} from '@bpinc/lib-ui-elements'
import type {ContactRecord} from '@bpinc/state-interaction-ui'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {useContactPhoneOptions} from '../../hooks/useContactPhoneOptions'
import type {ExistingContactRecordProps} from '../ContactRecords/ExistingContactRecord'


interface ContactPhonesDropdownProps {
    contact: ContactRecord
    contactHandlers: ExistingContactRecordProps['contactHandlers']
}

export const ContactPhonesDropdown: React.FC<ContactPhonesDropdownProps> = ({contact, contactHandlers}) => {
    const {t} = useLocalizationState()
    const options = useContactPhoneOptions({contactData: contact.contactData})

    return (
        <SmForm.Group label={t.CALENDAR_EVENT_FORM.contact_on_label()}>
            <label className="text-main-secondary">{t.CALENDAR_EVENT_FORM.contact_on_label()}</label>
            <SmForm.Field
                aria-label={t.CALENDAR_EVENT_FORM.contact_on_label()}
                control={Dropdown}
                onChange={contactHandlers.handleContactPhoneIdChange}
                value={contact.phoneId}
                options={options}
            />
        </SmForm.Group>
    )
}

