import React from 'react'

import type {FormDefinitionPageItem} from '@bpinc/ad-request-urls'

import {AddressListField} from './AddressListField'
import {AmountField} from './AmountField'
import {ButtonField} from './ButtonField'
import {CheckboxField} from './CheckboxField'
import {DateTimeField} from './DateTimeField'
import {EmailField} from './EmailField'
import {EmailListField} from './EmailListField'
import {ImageField} from './ImageField'
import {LabelField} from './LabelField'
import {LinkField} from './LinkField'
import {MessengersField} from './MessengersField'
import {NotesField} from './NotesField'
import {PhoneField} from './PhoneField'
import {PhoneListField} from './PhoneListField'
import {SelectField} from './SelectField'
import {SpacerField} from './SpacerField'
import {TextFormField} from './TextFormField'
import {useConditionalFieldCheck} from './useConditionalFieldCheck'


export const FormElementResolver: React.FC<{
    pageItem: FormDefinitionPageItem
}> = ({
    pageItem,
}) => {
    const {field} = pageItem

    const isConditionMet = useConditionalFieldCheck(field)
    if (!isConditionMet) {
        return null
    }

    if (field?.type === 'CUSTOM') {
        switch (field?.customType) {
            case 'TEXT':
                return <TextFormField pageItem={pageItem} field={field} />
            case 'EMAIL':
                return <EmailField pageItem={pageItem} field={field} />
            case 'PHONE':
                return <PhoneField pageItem={pageItem} field={field} />
            case 'PHONE_LIST':
                return <PhoneListField pageItem={pageItem} field={field} />
            case 'EMAIL_LIST':
                return <EmailListField pageItem={pageItem} field={field} />
            case 'ADDRESS_LIST':
                return <AddressListField pageItem={pageItem} field={field} />
            case 'DATETIME':
                return <DateTimeField pageItem={pageItem} field={field} />
            case 'CHECKBOX':
                return <CheckboxField pageItem={pageItem} field={field} />
            case 'KEY_VALUE_LIST':
                return <SelectField pageItem={pageItem} field={field} />
            case 'NOTES':
                return <NotesField pageItem={pageItem} field={field} />
            case 'AMOUNT':
                return <AmountField pageItem={pageItem} field={field} />
            case 'IMAGE':
                return <ImageField pageItem={pageItem} field={field} />
        }
    }

    if (field?.type === 'STATIC') {
        switch (field?.staticType) {
            case 'LINK':
                return <LinkField field={field} />
            case 'TEXT':
                return <LabelField field={field} />
            case 'SPACER':
                return <SpacerField pageItem={pageItem} />
            case 'BUTTON':
                return <ButtonField field={field} />
            case 'MESSENGER':
                return <MessengersField pageItem={pageItem} field={field} />
        }
    }

    if (
        !(field?.type === 'STATIC' && field?.staticType === 'CONTAINER') &&
        !(field?.type === 'STATIC' && field?.staticType === 'SECTION')
    ) {
        console.warn('Not supported field type', field)
    }

    return null
}
