import React from 'react'
import {tw} from '@servicepattern/ui'

import {AddToExistingModal, QuickAddModal} from '@bpinc/ad-contact-modals'
import {useContactsStateMethods} from '@bpinc/ad-contacts-state-context'
import {type ContactShortType, EmailTypes, PhoneTypes} from '@bpinc/ad-contacts-state-types'
import {useFormatting} from '@bpinc/ad-formatting-context'
import {AppRoutes, useNavigationState} from '@bpinc/ad-navigation-state-context'
import {useContactsActions} from '@bpinc/ad-searchpage-ui'
import {useEvent} from '@bpinc/lib-helpers'
import type {MoreOption} from '@bpinc/lib-jskit-ui'
import {MoreOptions} from '@bpinc/lib-jskit-ui'
import {useLocalizationState} from '@bpinc/state-localization-context'
import {useModalComponentOpener} from '@bpinc/state-modals-context'
import {useRecentStateMethods} from '@bpinc/state-recent-context'
import type {RecentItem} from '@bpinc/state-recent-types'

import {recentToNewContact} from './recentToNewContact'
import {useUpdateContactWithRecentChannel} from './useUpdateContactWithRecentChannel'


type Props = {
    item: RecentItem
    closeHovercard: () => void
}

export const CreateContactOptionsButton: React.FC<Props> = ({item, closeHovercard}) => {

    const t = useLocalizationState('t')
    const {formatFullName} = useFormatting()
    const goTo = useNavigationState('goTo')
    const {linkWithContact} = useRecentStateMethods()
    const {createContact} = useContactsStateMethods()

    const {handleContactCreate, handleContactOpen} = useContactsActions()
    const updateContactWithRecentChannel = useUpdateContactWithRecentChannel()
    // TODO: Standalone Comm.Widget must handle these action differently. Not using fullscreen AD modals
    const openQuickAddModal = useModalComponentOpener(QuickAddModal)
    const openAddToExistingModal = useModalComponentOpener(AddToExistingModal)


    const addNewContact = useEvent(() => {
        handleContactCreate(
            formatFullName(item.firstName, item.lastName),
            recentToNewContact(item),
            contactId => {
                linkWithContact(item.itemId, contactId)
            },
        )
    })

    async function handleAddToExisting(selectedContact: ContactShortType) {
        await updateContactWithRecentChannel(selectedContact.id, item)
        return closeHovercard()
    }

    function handleAddNewContact() {
        addNewContact()
        closeHovercard()
        return Promise.resolve()
    }

    function handleQuickAdd() {
        openQuickAddModal({
            defaultValues: {
                firstName: item.firstName,
                lastName: item.lastName,
                email: item.email,
                phone: item.number,
            },
            onSubmit: contact => {
                createContact(
                    {
                        contact: {
                            first_name: contact.firstName,
                            last_name: contact.lastName,
                            emails: contact.email
                                ? [{email_address: contact.email, type: EmailTypes.PRIMARY}]
                                : [],
                            phones: contact.phone
                                ? [{phone: contact.phone, type: PhoneTypes.BUSINESS}]
                                : [],
                        },
                    },
                    {
                        onSuccess(contactId: string) {
                            linkWithContact(item.itemId, contactId)
                            goTo(AppRoutes.search)
                            handleContactOpen({
                                id: contactId,
                                firstName: contact.firstName,
                                lastName: contact.lastName,
                            })
                        },
                    },
                )
            },
        })
        closeHovercard()
    }

    function onAddToExistingContactClick() {
        openAddToExistingModal({
            handleAddNewContact,
            handleAddToExisting,
        })
        closeHovercard()
    }


    const contactOptions = [
        {
            key: 'quick-add',
            content: t.INTERACTIONS.quick_add(),
            onSelect: handleQuickAdd,
            className: tw`min-w-40`,
        },
        {
            key: 'new-contact',
            content: t.INTERACTIONS.new_contact(),
            onSelect: handleAddNewContact,
            className: tw`min-w-40`,
        },
        {
            key: 'add-to-existing',
            content: t.INTERACTIONS.add_to_existing(),
            onSelect: onAddToExistingContactClick,
            className: tw`min-w-40`,
        },
    ] satisfies MoreOption[]

    return (
        <MoreOptions
            size="xs"
            className="size-7"
            buttonProps={{variant: 'neutral'}}
            options={contactOptions}
        />
    )
}
