import React, {useCallback, useEffect, useState} from 'react'

import {useActiveInteractionId} from '@bpinc/ad-interaction-state-context'
import {useEvent, useResizeObserver} from '@bpinc/lib-helpers'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {useAccessibleShortcuts} from '../hooks/useAccessibleShortcuts'
import {usePagination} from '../hooks/usePagination'
import {InteractionsListItem} from './InteractionsListItem/'
import {AnimatedInteractionsList} from './AnimatedInteractionsList'
import {InteractionsListResizer} from './InteractionsListResizer'
import {Pagination} from './Pagination'


type Props = {
    itemIds: string[]
    selectedItemId: string | undefined
    setSelectedItemId: (id: string) => void
}


export const InteractionsList: React.FC<Props> = React.memo(
    function InteractionsList({
        itemIds,
        selectedItemId,
        setSelectedItemId,
    }) {
        const {t} = useLocalizationState()

        const [height, setHeight] = useState(100)
        const [isHeightCustomized, setIsHeightCustomized] = useState(false)
        const [menuRef, setMenuRef] = useState<HTMLDivElement | null>(null)
        const menuResizeObserver = useResizeObserver(menuRef)
        const detectedHeight = menuResizeObserver?.contentRect.height

        const menuStyle = isHeightCustomized ? {height} : undefined

        const ITEM_HEIGHT = 37
        const minHeight = itemIds.length * ITEM_HEIGHT

        useEffect(
            function saveAutoHeight() {
                if (detectedHeight && !isHeightCustomized) {
                    setHeight(detectedHeight)
                }
            },
            [detectedHeight, isHeightCustomized, setHeight],
        )

        const resetHeightOnItemsChange = useEvent(() => {
            if (minHeight > height) {
                setHeight(minHeight)
            }
        })

        useEffect(
            resetHeightOnItemsChange,
            [minHeight, resetHeightOnItemsChange],
        )

        const onManualResize = useCallback(
            function onManualResize(newHeight: number) {
                if (newHeight <= minHeight) {
                    setIsHeightCustomized(false)
                    return
                }

                setIsHeightCustomized(true)
                setHeight(newHeight)
            },
            [setHeight, setIsHeightCustomized, minHeight],
        )

        const onResetCustomHeight = useCallback(
            function onResetCustomHeight() {
                setIsHeightCustomized(false)
            },
            [setIsHeightCustomized],
        )

        const {
            currentPage,
            goToPage,
            getItemCoordsByIndex,
            pagedItems,
            paginationIsEnabled,
            ...paginationProps
        } = usePagination({itemIds})

        useAccessibleShortcuts({getItemCoordsByIndex, goToPage, currentPage})

        const activeItemId = useActiveInteractionId()

        const handleItemClick = useCallback(
            function handleItemClickOuter(itemId: string) {
                return function handleItemClickInner() {
                    setSelectedItemId(itemId)
                }
            },
            [setSelectedItemId],
        )

        if (pagedItems.length === 0) {
            return null
        }

        return (
            <AnimatedInteractionsList>
                <div
                    className="max-w-full shrink-0 overflow-hidden px-1 pb-2"
                    data-testid="interactions-list"
                >
                    <div className="z-10 flex shrink-0 items-center p-4 uppercase tracking-[0.8px] caption-xs text-neutral-disabled">
                        {t.COMMUNICATION_PANEL.active_interactions()}
                        {' '}
                    (
                        {itemIds.length}
                    )
                    </div>

                    <div
                        className="min-h-9.5"
                        style={menuStyle}
                        ref={setMenuRef}
                    >
                        {pagedItems.map((itemId: string, index) => {
                            return (
                                <InteractionsListItem
                                    key={index}
                                    itemId={itemId}
                                    onClick={handleItemClick(itemId)}
                                    isSelected={selectedItemId === itemId}
                                    isActive={activeItemId === itemId}
                                />
                            )
                        })}
                        {paginationIsEnabled && <Pagination {...paginationProps} />}
                    </div>

                    <InteractionsListResizer
                        height={height}
                        onResize={onManualResize}
                        onReset={onResetCustomHeight}
                        min={minHeight}
                    />
                </div>
            </AnimatedInteractionsList>
        )
    },
)
