import React, {useId} from 'react'

import type {
    DirectoryEntry,
    DirectoryItem,
    DirectoryStateUserObject,
    EmiUsers,
    SearchDirectoryCategory,
} from '@bpinc/ad-directory-state-types'
import type {OpenContactDetailsParams} from '@bpinc/ad-interaction-state-hooks'
import type {InitiateCall} from '@bpinc/ad-interaction-state-types'
import {useEvent} from '@bpinc/lib-helpers'
import {IconChevronDown16, IconChevronRight16} from '@bpinc/lib-jskit-ui'
import type {Dictionary} from '@bpinc/lib-types-helpers'
import {SelectionItem} from '@bpinc/lib-ui-elements'

import {DirectoryItems} from '../DirectoryItems'
import type {OnChatParams, OnEmailParams} from '../user/types'
import {DirectoryFolderTitle} from './DirectoryFolderTitle'
import type {DirectoryFolderType} from './helpers'
import {useUsersList} from './useUsersList'


type DirectoryFolderItem = {
    transferMode?: 'calls' | 'messages'
    transferControlsStyle?: 'add' | 'replace'
    query?: string
    type: DirectoryFolderType
    opened: boolean
    item: SearchDirectoryCategory
    listRef?: React.RefObject<HTMLDivElement>
    filteredUsers: DirectoryStateUserObject[]
    filteredDirectoryEntries: Dictionary<DirectoryEntry[]>
    filteredEmiUsers: EmiUsers
    toggleDirectoryFolder: (folderId: string) => void
    onCall: (callParams: InitiateCall) => void
    onChat: (chatParams: OnChatParams) => void
    onEmail: (emailParams: OnEmailParams) => void
    onDetails: (params: OpenContactDetailsParams) => void
    onClick?: (evt: React.MouseEvent, item: DirectoryItem) => void
    onDoubleClick?: (item: DirectoryItem) => void
    isHighlighted?: (item: DirectoryItem) => boolean
}

export const DirectoryFolderItem: React.FC<DirectoryFolderItem> = React.memo(({
    filteredUsers,
    filteredDirectoryEntries,
    filteredEmiUsers,
    type,
    item,
    opened,
    query,
    transferMode,
    transferControlsStyle,
    listRef,
    toggleDirectoryFolder,
    onCall,
    onChat,
    onEmail,
    onDetails,
    onClick,
    isHighlighted,
    onDoubleClick,
}) => {

    const ariaId1 = useId()

    const itemsList = useUsersList(type, item, {
        users: filteredUsers,
        categoryEntries: filteredDirectoryEntries,
        emiUsers: filteredEmiUsers,
    })

    const folderClickHandler = useEvent(() => {
        toggleDirectoryFolder(item.id)
    })

    const IconChevron = opened ? IconChevronDown16 : IconChevronRight16

    return (
        <>
            <SelectionItem
                focusOnSelection
                className="flex w-full cursor-pointer items-center px-1 py-1.5 bg-primary-background"
                tabIndex={0}
                data-testid="directory-folder"
                data-element="directory-item"
                aria-expanded={opened ? 'true' : 'false'}
                aria-controls={ariaId1}
                onClick={folderClickHandler}
            >
                <IconChevron className="mr-2" />
                <DirectoryFolderTitle
                    type={type}
                    folderItem={item}
                    users={filteredUsers}
                    directoryEntries={filteredDirectoryEntries}
                    emiUsers={filteredEmiUsers}
                    searchQuery={query}
                />
            </SelectionItem>

            <div id={ariaId1}>
                {opened && (
                    <DirectoryItems
                        listRef={listRef}
                        transferMode={transferMode}
                        transferNumberInput={query}
                        transferControlsStyle={transferControlsStyle}
                        items={itemsList}
                        onCall={onCall}
                        onChat={onChat}
                        onEmail={onEmail}
                        onDetails={onDetails}
                        highlight={query}
                        onClick={onClick}
                        onDoubleClick={onDoubleClick}
                        isHighlighted={isHighlighted}
                    />
                )}
            </div>
        </>
    )
})
