import React from 'react'

import type {FormDefinitionFieldLink} from '@bpinc/ad-request-urls'


export const LinkField: React.FC<{
    field: FormDefinitionFieldLink
}> = ({
    field,
}) => {
    return (
        <a
            href={field.linkUrl}
            target="_blank"
            rel="noopener noreferrer"
            className="underline"
        >
            {field.initialValue || field.linkUrl}
        </a>
    )
}
