import React, {useEffect, useMemo, useState} from 'react'

import type {Party, Service} from '@bpinc/ad-case-state-types'
import type {Attachment as AttachmentType} from '@bpinc/ad-request-urls'
import {sanitizeHtml} from '@bpinc/lib-html-utils'

import {EmailAttachment} from './EmailAttachment'
import {Info} from './Info'


interface Props {
    html: string
    title?: string
    info?: React.ReactNode
    actions?: React.ReactNode
    attachments?: AttachmentType[]
    services?: Service[]
    partyInternal?: Party
}

export const EmailContent: React.FC<Props> = ({html, title, info, actions, partyInternal, services, attachments}) => {
    const [content, setContent] = useState<string>('')
    const innerHtmlObject = useMemo(() => ({__html: content}), [content])
    const serviceId = partyInternal?.serviceId
    const service = (services || []).find(s => s.serviceId === serviceId)

    useEffect(() => {
        setContent(sanitizeHtml(html))
    }, [html])

    return (
        <div className="my-2 rounded bg-note-bg  p-2">
            {info && <div>{info}</div>}
            {title && <div className="mb-3 font-bold [&:not(:first-child)]:mt-2">{title}</div>}
            <div dangerouslySetInnerHTML={innerHtmlObject} />
            {attachments?.map(attachment => <EmailAttachment {...attachment} key={attachment.id} />)}
            {actions}
            {service && (
                <Info title="Service">
                    {service.serviceName}
                </Info>
            )}
        </div>
    )
}
