import type {CSSProperties} from 'react'
import React, {useMemo} from 'react'


interface SpacerProps {
    height?: CSSProperties['height']
}

export const Spacer: React.FC<SpacerProps> = ({height}) => {
    const style = useMemo(() => {
        if (typeof height !== 'number') {
            return {}
        }

        return {
            flexShrink: 0,
            height: height * 2,
        }
    }, [height])

    return (
        <div style={style} />
    )
}
