import React from 'react'

import {useInteraction} from '@bpinc/ad-interaction-state-context'
import {getServiceNameFromItem} from '@bpinc/ad-interaction-state-methods'
import {useInteractionId} from '@bpinc/lib-interaction-context'
import {ButtonIcon, IconChatPanelClose, IconDivider} from '@bpinc/lib-jskit-ui'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {useParallelChats} from '../../hooks/useParallelChats'


export const ActiveInteractionTitle: React.FC = () => {
    const {t} = useLocalizationState()
    const interactionId = useInteractionId()
    const item = useInteraction(interactionId)
    const serviceName = getServiceNameFromItem(item)

    const {closeParallelChat, showCloseParallelChatButton} = useParallelChats(item)

    return (
        <div className="flex items-center justify-between px-4 py-2">
            <div className="flex items-center gap-2">
                <div className="uppercase caption-xs text-neutral-disabled">
                    {t.INTERACTIONS.interaction_details()}
                </div>
                {serviceName && (
                    <>
                        <IconDivider />
                        <div className="capitalize caption-s text-neutral-disabled">
                            {serviceName}
                        </div>
                    </>
                )}
            </div>
            {showCloseParallelChatButton && (
                <ButtonIcon
                    tooltip={t.KNOWLEDGE_BASE.close_tab_text()}
                    onClick={closeParallelChat}
                    className="p-0"
                >
                    <IconChatPanelClose className="!size-6" />
                </ButtonIcon>
            )}
        </div>
    )
}
