import {useMemo} from 'react'

import {useContactDisplayName} from '@bpinc/ad-contacts-state-hooks'
import {useFormatting} from '@bpinc/ad-formatting-context'
import {formatPhone} from '@bpinc/ad-formatting-helpers'
import {isCallConference, isChatConference, isExternalChat, isWorkItem} from '@bpinc/ad-interaction-state-methods'
import type {Item} from '@bpinc/ad-interaction-state-types'
import {useLocalizationState} from '@bpinc/state-localization-context'


type DisplayContent = {
    name?: string
    service?: string
}

export function useDisplayContent(item: Item): DisplayContent {
    const {formatFullName} = useFormatting()
    const {displayData, contactId} = item ?? {}
    const {
        displayName: interactionDisplayName,
        firstName,
        lastName,
    } = displayData ?? {}
    const {t} = useLocalizationState()
    const contactDisplayName = useContactDisplayName(contactId)

    const callConfDisplayText = t.HOME_SCREEN.interaction_call_conference_label()
    const chatConfDisplayText = t.HOME_SCREEN.interaction_chat_conference_label()

    return useMemo((): DisplayContent => {
        const {
            firstName: chatFirstName,
            lastName: chatLastName,
        } = item?.externalChatData ?? {}
        const fullName =
            contactDisplayName ||
            interactionDisplayName ||
            formatFullName(chatFirstName, chatLastName) ||
            formatFullName(firstName, lastName)
        const number = formatPhone(displayData?.number)

        const serviceName: string = isWorkItem(item)
            ? item?.workitemData?.serviceName || ''
            : item?.scenarioData?.serviceName || ''

        if (isCallConference(item)) {
            return serviceName
                ? {name: callConfDisplayText, service: serviceName}
                : {name: callConfDisplayText}
        }

        if (isChatConference(item)) {
            return serviceName
                ? {name: chatConfDisplayText, service: serviceName}
                : {name: chatConfDisplayText}
        }

        const name = isExternalChat()
            ? fullName || number || t.EXTERNAL_CHAT.display_name_placeholder()
            : fullName || number || t.GLOBAL.anonymous()

        return serviceName
            ? name
                ? {name, service: serviceName}
                : {service: serviceName}
            : {name}
    }, [
        callConfDisplayText,
        chatConfDisplayText,
        contactDisplayName,
        displayData?.number,
        interactionDisplayName,
        firstName,
        formatFullName,
        item,
        lastName,
        t,
    ])
}
