import type {ContactType} from '@bpinc/ad-contacts-state-types'
import type {
    DirectoryEntry,
    DirectoryStateUserObject,
    MsTeamsChannel,
    MsTeamsUser,
    SearchUserItem,
} from '@bpinc/ad-directory-state-types'
import type {UserDetails} from '@bpinc/ad-user-details-state-types'


export type User = ContactType | InternalUser | DirectoryEntry | EmiUser

type InternalUser = DirectoryStateUserObject | UserDetails | SearchUserItem

type EmiUser = MsTeamsUser | MsTeamsChannel

export const isExternalUser = (user: User): user is ContactType => 'Id' in user

export const isDirectoryEntry = (user: User): user is DirectoryEntry =>
    'title' in user && 'mediaType' in user

export const isMsTeamsChannel = (user: User): user is MsTeamsChannel =>
    'userType' in user && user.userType === 'msteams_channel'

export const isMsTeamsUser = (user: User): user is MsTeamsUser =>
    'userType' in user && user.userType === 'msteams'

export const isMsTeamsUserDetails = (user: User): user is UserDetails & {userType: 'msteams'} =>
    'userType' in user && user.userType === 'msteams' &&
    'msTeamsPhones' in user

export const isInternalUser = (user: User): user is InternalUser =>
    !isExternalUser(user) &&
    !isDirectoryEntry(user) &&
    !isMsTeamsUser(user) &&
    !isMsTeamsChannel(user)
