import React from 'react'

import {useActiveInteractionId} from '@bpinc/ad-interaction-state-context'

import {ServiceSelect} from '../../components/ServiceSelect'
import type {FavoritesListProps} from '../types'
import {FavoritesList} from './FavoritesList'


export const Favorites: React.FC<FavoritesListProps> = props => {

    const activeInteractionId = useActiveInteractionId()

    return (
        <div
            className="flex h-full min-h-0 flex-auto flex-col gap-2.5 px-4 pt-2"
            data-testid="favorites"
        >
            <ServiceSelect itemId={activeInteractionId} />
            <FavoritesList {...props} />
        </div>
    )
}
