import {useCallback} from 'react'

import {SHOW_MORE_TIMEZONES} from './TimeZoneDropdown'
import {useTimeZonesModalOpener} from './useTimeZonesModalOpener'


interface UseHandleChangeParams {
    handleSelect: (timeZoneId: string) => void
}
export function useHandleChange({handleSelect}: UseHandleChangeParams) {

    const openFullTimezonesList = useTimeZonesModalOpener({handleSelect})

    return useCallback(
        function handleChangeCallback(value: string | undefined) {
            if (!value) {
                return
            }

            return value === SHOW_MORE_TIMEZONES
                ? openFullTimezonesList()
                : handleSelect(value)
        },
        [handleSelect, openFullTimezonesList],
    )

}
