import React, {useEffect} from 'react'
import {TabsContent, TabsList, TabsRoot, TabsTrigger} from '@servicepattern/ui'

import {
    useActiveInteractionId,
    useInteractionStateMethods,
    useInteractionStateSelector,
} from '@bpinc/ad-interaction-state-context'
import {useRecentUIState} from '@bpinc/lib-communicator-ui-context'
import {useEvent, usePageVisibility} from '@bpinc/lib-helpers'
import {tcn} from '@bpinc/lib-tailwind-classnames'

import {ServiceSelect} from '../../components/ServiceSelect'
import type {ItemType} from './RecentList'
import {RecentList} from './RecentList'
import {RecentSearchControls} from './RecentSearchControls'


const tabs = [
    {
        id: 'all',
        testId: 'tab-all',
        content: 'All',
    },
    {
        id: 'calls',
        testId: 'tab-calls',
        content: 'Calls',
    },
    {
        id: 'messages',
        testId: 'tab-messages',
        content: 'Messages',
    },
]

export const Recent: React.FC = () => {

    const {resetMissedCalls} = useInteractionStateMethods()
    const missedCallsAmount = useInteractionStateSelector(s => s.missedCallsAmount)
    const {handleTabChange, tabIndex} = useRecentUIState()
    const pageIsVisible = usePageVisibility()
    const activeInteractionId = useActiveInteractionId()

    const selectedTabId = tabs[Number(tabIndex)].id as ItemType

    const onTabChange = useEvent((newTabId: string) => {
        const newTabIndex = tabs.findIndex(tab => tab.id === newTabId)
        handleTabChange(newTabIndex)
    })

    useEffect(() => {
        if (pageIsVisible) {
            resetMissedCalls()
        }
    }, [missedCallsAmount, resetMissedCalls, pageIsVisible])

    return (
        <TabsRoot
            data-testid="recent"
            value={selectedTabId}
            className="flex h-full flex-col gap-2.5 px-4 pt-2"
        >
            <TabsList data-testid="recent-tabs">
                {tabs.map(tab => (
                    <TabsTrigger
                        key={tab.id}
                        value={tab.id}
                        data-testid={tab.testId}
                        isSelected={selectedTabId === tab.id}
                        onClick={() => onTabChange(tab.id)}
                        className="w-full text-center"
                        triggerClassName={tcn(
                            'py-2',
                            {['text-primary-on-primary']: tab.id === selectedTabId},
                        )}
                    >
                        {tab.content}
                    </TabsTrigger>
                ))}
            </TabsList>

            <ServiceSelect itemId={activeInteractionId} />

            <RecentSearchControls />

            <TabsContent
                value={selectedTabId}
                className="flex min-h-0 !flex-auto flex-col py-1 pr-0.5"
            >
                <RecentList type={selectedTabId} />
            </TabsContent>
        </TabsRoot>
    )
}
