import type {FC} from 'react'
import React, {useState} from 'react'
import {Button} from '@servicepattern/ui'

import {SmLabel} from '@bpinc/lib-external-ui-elements'
import {useBoolean} from '@bpinc/lib-helpers'
import {ButtonDropdown, IconMenu24, TriggerDropdown} from '@bpinc/lib-jskit-ui'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {useExternalMoreButtonOptions} from '../../../../../hooks/useExternalMoreButtonOptions'


interface ExernalViewMoreButtonProps {
    handleMerge: () => void
    handleCopyLink: () => void
}
export const ExternalViewMoreButton: FC<ExernalViewMoreButtonProps> = ({handleMerge, handleCopyLink}) => {
    const [linkClick, setLinkClicks] = useState(0)
    const {t} = useLocalizationState()

    const {value: show, toggle: toggleShow} = useBoolean()

    const externalMoreButtonOptions = useExternalMoreButtonOptions({setLinkClicks, toggleShow, handleMerge, handleCopyLink})

    return (
        <>
            <TriggerDropdown
                modal={false}
                open={show}
                onOpenChange={toggleShow}
                className="group z-10"
                align="start"
                options={
                    <>
                        {externalMoreButtonOptions.map((message, i) => {
                            return (
                                <ButtonDropdown.Item
                                    key={i}
                                    onClick={message.onClick}
                                >
                                    {message.children}
                                </ButtonDropdown.Item>
                            )
                        })}
                    </>
                }
            >
                <Button
                    variant="ghost"
                    icon={<IconMenu24 />}
                />
            </TriggerDropdown>

            {!!linkClick && (
                <SmLabel key={linkClick} pointing="left" className="animate-copied-label select-none">
                    {t.CONTACT.copied_confirmation_label_text()}
                </SmLabel>
            )}
        </>
    )
}
