import React from 'react'

import {useFormatting} from '@bpinc/ad-formatting-context'
import {formatPhone} from '@bpinc/ad-formatting-helpers'
import type {PhoneData} from '@bpinc/adapters-public-api-types'


export const PhoneOption: React.FC<PhoneData> = ({
    number,
    type,
    displayType,
}) => {
    const {formatPhoneType} = useFormatting()

    return (
        <div className="flex items-center">
            <div className="mr-2 inline-block w-1/3">
                <span data-key="phone-type">
                    {displayType || formatPhoneType(type.toUpperCase())}
                </span>
                <span>:</span>
            </div>
            <span data-key="phone">{formatPhone(number)}</span>
        </div>
    )
}
