import {useEffect} from 'react'

import {useInteractions} from '@bpinc/ad-interaction-state-context'
import {getGlobalInteractionId} from '@bpinc/ad-interaction-state-methods'
import {useRemoteAssistStateConnector} from '@bpinc/ad-remote-assist-state-context'
import {useAsRef} from '@bpinc/lib-helpers'
import {useInteractionUIItemStateSetter} from '@bpinc/state-interaction-ui'


export function useRemoteAssistStateSubscription() {
    const connector = useRemoteAssistStateConnector()
    const items = useInteractions()
    const itemsRef = useAsRef(items)
    const setItemState = useInteractionUIItemStateSetter()

    useEffect(
        function remoteAssistStateSubscription() {
            const subscription = connector.dispenser.subscribe({
                onSessionStarted: ({globalInteractionId, clientUrl}) => {
                    const realItem = itemsRef.current.find(item => {
                        return getGlobalInteractionId(item) === globalInteractionId
                    })
                    const internalChatItem = itemsRef.current.find(item => {
                        return item.id === globalInteractionId
                    })
                    const item = realItem ?? internalChatItem
                    if (!item) {
                        return
                    }
                    setItemState(item.id, itemState => ({
                        ...itemState,
                        chatMessage: itemState.chatMessage + ' ' + clientUrl,
                    }))
                },
            })
            return () => subscription.unsubscribe()
        },
        [connector, setItemState, itemsRef],
    )
}
