import React from 'react'

import {useLocalizationState} from '@bpinc/state-localization-context'

import styles from '../../styles/InteractionsPage.module.less'


export const Connecting = () => {
    const {t} = useLocalizationState()
    return (
        <span data-testid="connecting" className={styles.prolongedConnectionItemLabel}>
            {t.GLOBAL.dial_request_status_long_connection()}
        </span>
    )
}
