import {useCallback, useMemo} from 'react'
import {mapValues, pickBy} from 'lodash-es'

import {useDirectorySearchResultsState, useDirectoryState} from '@bpinc/ad-directory-state-context'
import type {
    DirectoryItem,
    EmiUsers,
    MsTeamsChannel,
    MsTeamsUser,
    SearchResult,
} from '@bpinc/ad-directory-state-types'
import {MS_TEAMS_CHANNELS_CATEGORY_ID, MS_TEAMS_CHATS_CATEGORY_ID} from '@bpinc/ad-directory-state-types'
import {getMsTeamsUserPrimaryPhoneNumber} from '@bpinc/ad-emi-helpers'
import {useSessionState} from '@bpinc/ad-session-context'


type UseFilterDataParams = {
    transferMode?: 'messages' | 'calls'
    filter: ((user: DirectoryItem) => boolean) | undefined
}

export function useFilteredDirectoryState({
    transferMode,
    filter = () => true,
}: UseFilterDataParams) {
    const {categories, categoryEntries, emiUsers} = useDirectoryState(
        ['categories', 'categoryEntries', 'emiUsers'],
    )
    const searchResults = useDirectorySearchResultsState()
    const {privileges} = useSessionState()

    const privilegesLoaded = !!Object.keys(privileges).length

    const msTeamsUserFilter = useCallback((user: MsTeamsUser) => {
        if (transferMode === 'messages') {
            return user.hasChat
        } else if (transferMode === 'calls') {
            return getMsTeamsUserPrimaryPhoneNumber(user.phones) !== undefined
        } else {
            return true
        }
    }, [transferMode])

    const msTeamsChannelFilter = useCallback((_channel: MsTeamsChannel) => {
        return !transferMode
    }, [transferMode])


    const filteredSearchResults = useMemo((): SearchResult => {
        return {
            ...searchResults,
            count: searchResults.count ? Number(searchResults.count) : undefined,
            data: {
                ...searchResults.data,
                directoryEntries: searchResults.data.directoryEntries,
                emiUsers: {
                    msteamsUsers: searchResults.data.emiUsers.msteamsUsers?.filter(
                        u => msTeamsUserFilter(u) && filter(u),
                    ),
                    msteamsChannels: searchResults.data.emiUsers.msteamsChannels?.filter(u =>
                        msTeamsChannelFilter(u) && filter(u),
                    ),
                },
                users:
                    privilegesLoaded && privileges.CAN_VIEW_DIRECTORY
                        ? searchResults.data.users.filter(filter)
                        : [],
                usersWithoutCurrentUser:
                    privilegesLoaded && privileges.CAN_VIEW_DIRECTORY
                        ? searchResults.data.usersWithoutCurrentUser.filter(filter)
                        : [],
                teams: privilegesLoaded && privileges.CAN_VIEW_DIRECTORY
                    ? searchResults.data.teams
                    : [],
            },
        }
    }, [
        filter,
        searchResults,
        msTeamsUserFilter,
        msTeamsChannelFilter,
        privilegesLoaded,
        privileges.CAN_VIEW_DIRECTORY,
    ])

    const filteredDirectoryEntries = useMemo(() => {
        return pickBy(mapValues(categoryEntries, entry => entry.filter(filter)), items => items.length)
    }, [categoryEntries, filter])

    const filteredCategories = useMemo(
        function memoizeFilteredCategories() {
            return categories.filter(
                category => category.id in filteredDirectoryEntries,
            )
        },
        [categories, filteredDirectoryEntries],
    )

    const filteredEmiUsers = useMemo<EmiUsers>(() => ({
        [MS_TEAMS_CHATS_CATEGORY_ID]: emiUsers[MS_TEAMS_CHATS_CATEGORY_ID]?.filter(msTeamsUserFilter),
        [MS_TEAMS_CHANNELS_CATEGORY_ID]: emiUsers[MS_TEAMS_CHANNELS_CATEGORY_ID]?.filter(msTeamsChannelFilter),
    }), [emiUsers, msTeamsChannelFilter, msTeamsUserFilter])

    return {
        filteredSearchResults,
        filteredDirectoryEntries,
        filteredCategories,
        filteredEmiUsers,
    }
}
