import {useCallback} from 'react'

import {phoneNumberFormatter} from '@bpinc/lib-phonenumber-formatter'
import {ALL_COUNTRIES_ID, useCountrySelectorState} from '@bpinc/lib-ui-elements'


function usePhoneNumberFormatter() {
    const countrySelectorValue = useCountrySelectorState('countrySelectorValue')

    return useCallback(
        (phone: string) => {
            if (countrySelectorValue === ALL_COUNTRIES_ID) {
                return {formattedNumber: phone}
            }

            const result = phoneNumberFormatter.format(phone, countrySelectorValue)
            return {
                formattedNumber: result.success
                    ? removeLeadingPlus(result.formatted.E164)
                    : phone,
                meta: result,
            }
        },
        [countrySelectorValue],
    )
}

function removeLeadingPlus(phone?: string) {
    if (!phone) {
        return
    }
    return phone[0] === '+' ? phone.substring(1) : phone
}

export {removeLeadingPlus, usePhoneNumberFormatter}
