import {useActivitiesState} from '@bpinc/ad-activities-state-context'
import {useContactActivityHistory} from '@bpinc/ad-interaction-state-hooks'


export function useChatIsLoading(contactId: string) {
    const {isLoading: isLoadingExternal, messages} = useContactActivityHistory()
    const isInitLoadingExternal = isLoadingExternal && !messages.length

    const activities = useActivitiesState('activitiesListByContactId')[contactId]
    const activitiesIsLoading = !!activities?.isLoading
    const activitiesInitialLoading = activitiesIsLoading && !activities?.data.length

    return {isLoading: isLoadingExternal || activitiesIsLoading, isInitialLoading: isInitLoadingExternal || activitiesInitialLoading}
}
