import {tw} from '@servicepattern/ui'
import {tv} from 'tailwind-variants'


export const slidingPanelStyles = tv({
    base: [
        tw`bg-primary-background`,
        tw`flex flex-col`,
        tw`rounded-t-md`,
        tw`absolute z-10 w-[calc(100%_-_8px)]`,
        tw`bottom-0 left-1 top-auto`,
        tw`overflow-hidden`,
        tw`data-[state=open]:animate-slide-panel-up`,
        tw`data-[state=closed]:animate-fade-out-100`,
    ],
    variants: {
        modal: {
            false: tw`shadow-5`,
        },
    },
    slots: {
        overlay: [
            tw`absolute inset-0 z-10 bg-inverse-overlay`,
            tw`data-[state=open]:animate-fade-in-100`,
            tw`data-[state=closed]:animate-fade-out-100`,
        ],
    },
})

export const slidingPanelHeaderStyles = tv({
    base: [
        tw`relative truncate px-6 py-4`,
        tw`flex items-center justify-between gap-4`,
        tw`heading-s`,
        tw`border-b border-default`,
    ],
    slots: {
        innerContainer: tw`flex items-center gap-4 truncate`,
        close: tw`size-10 shrink-0`,
    },
})


export const slidingPanelContentStyle = tv({
    base: [
        tw`relative overflow-y-auto p-6`,
        tw`flex flex-auto flex-col`,
    ],
})

export const slidingPanelFooterStyles = tv({
    base: [
        tw`shrink-0`,
        tw`relative truncate px-6 py-4`,
        tw`flex items-center justify-between gap-4`,
        tw`border-t border-default`,
    ],
})
