import React from 'react'
import {Button} from '@servicepattern/ui'

import {useIsCommunicator} from '@bpinc/ad-current-tab-data-context'
import {useFormatting} from '@bpinc/ad-formatting-context'
import {formatPhone} from '@bpinc/ad-formatting-helpers'
import type {HandleCallParams} from '@bpinc/ad-interaction-state-hooks'
import {useSessionState} from '@bpinc/ad-session-context'
import {useEvent} from '@bpinc/lib-helpers'
import {IconEmailEnvelope16, IconPhone16, IconRoundChat16} from '@bpinc/lib-jskit-ui'
import {assertDefined} from '@bpinc/lib-types-helpers'
import type {ChatParams} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'
import type {RecentItem} from '@bpinc/state-recent-types'

import type {OnEmailParams} from '../../../directory/components/user/types'
import {CreateContactOptionsButton} from './CreateContactOptionsButton'


type Props = {
    item: RecentItem
    closeHovercard: () => void
    onCallClick: (callParams: HandleCallParams) => void
    onChatClick: (chatParams: ChatParams) => void
    onEmailClick: (emailParams: OnEmailParams) => void
}

export const UnidentifiedContactContent: React.FC<Props> = ({
    item,
    closeHovercard,
    onCallClick,
    onChatClick,
    onEmailClick,
}) => {

    const t = useLocalizationState('t')
    const phoneDeviceCapabilities = useSessionState('phoneDeviceCapabilities')
    const {formatFullName} = useFormatting()
    const isCommunicator = useIsCommunicator()

    const formattedPhone = item.number && formatPhone(item.number)
    const fullName = formatFullName(item.firstName, item.lastName)
    const displayName = fullName || item.name || formattedPhone || t.GLOBAL.anonymous()

    const showCallButton = !!item.number && item.number !== '-' && phoneDeviceCapabilities.canDial
    const showChatButton = !!item.number
    const showEmailButton = !isCommunicator && !!item.email

    const handleCall = useEvent(() => {
        assertDefined(item.number)
        onCallClick({
            phone: item.number,
            firstName: item.firstName,
            lastName: item.lastName,
        })
    })

    const handleChat = useEvent(() => {
        assertDefined(item.number)
        onChatClick({
            phone: item.number,
            firstName: item.firstName,
            lastName: item.lastName,
        })
    })

    const handleEmail = useEvent(() => {
        assertDefined(item.email)
        onEmailClick({emailAddress: item.email})
    })

    return (
        <>
            <div className="flex items-start gap-1 px-2 py-1.5">
                <div className="truncate font-semibold body text-primary-action">
                    {displayName}
                </div>
            </div>
            <div className="flex flex-auto gap-2 px-2 pb-2">
                {showCallButton && (
                    <Button
                        size="sm"
                        variant="neutral"
                        className="h-7 flex-auto"
                        tooltip={t.CONTACT.button_start_call_text()}
                        iconLeft={<IconPhone16 className="svg-color-current" />}
                        onPressed={handleCall}
                    />
                )}
                {showChatButton && (
                    <Button
                        size="sm"
                        variant="neutral"
                        className="h-7 flex-auto"
                        tooltip={t.CONTACT.button_initiate_chat_text()}
                        iconLeft={<IconRoundChat16 className="svg-color-current" />}
                        onPressed={handleChat}
                    />
                )}
                {showEmailButton && (
                    <Button
                        size="sm"
                        variant="neutral"
                        className="h-7 flex-auto"
                        tooltip={t.CONTACT.button_send_email_text()}
                        iconLeft={<IconEmailEnvelope16 className="svg-color-current" />}
                        onPressed={handleEmail}
                    />
                )}
                <CreateContactOptionsButton item={item} closeHovercard={closeHovercard} />
            </div>
        </>
    )
}
