import React, {useMemo, useState} from 'react'
import {Button} from '@servicepattern/ui'

import {useContactsStateMethods} from '@bpinc/ad-contacts-state-context'
import {PhoneTypes} from '@bpinc/ad-contacts-state-types'
import {useFormatting} from '@bpinc/ad-formatting-context'
import {formatPhone} from '@bpinc/ad-formatting-helpers'
import {Modal, SelectField} from '@bpinc/lib-jskit-ui'
import {isStringInEnum} from '@bpinc/lib-types-helpers'
import {useLocalizationState} from '@bpinc/state-localization-context'
import type {CommonModalProps} from '@bpinc/state-modals-types'


type AddPhoneModalProps = CommonModalProps & {
    phone?: string
    contactId?: string
    firstName?: string
    lastName?: string
    onConfirm?: () => void
}

export const AddPhoneModal: React.FC<AddPhoneModalProps> = ({onClose, phone, contactId, firstName, lastName, onConfirm, ...other}) => {
    const {t} = useLocalizationState()
    const {addPhone} = useContactsStateMethods()
    const {formatPhoneType, formatFullName} = useFormatting()
    const [value, setValue] = useState<PhoneTypes>(PhoneTypes.BUSINESS)

    const name = formatFullName(firstName, lastName)
    const formattedPhone = formatPhone(phone)

    const options = useMemo(() => {
        return Object.keys(PhoneTypes).map(type => ({
            value: type,
            content: formatPhoneType(type),
        }))
    }, [formatPhoneType])

    function handleSelect(innerValue: string | undefined) {
        if (!isStringInEnum(PhoneTypes, innerValue)) {
            throw Error('Invalid phone type')
        }
        setValue(innerValue)
    }

    function handleConfirm() {
        if (contactId && phone) {
            addPhone({contactId, type: value, phone})
        }
        onConfirm?.()
        onClose()
    }

    return (
        <Modal
            onClose={onClose}
            data-testid="contact-add-phone-modal"
            {...other}
        >
            <Modal.Header>
                {t.CONTACT.add_phone_heading()}
            </Modal.Header>
            <Modal.Content className="body-m">
                {!!formattedPhone && (
                    <div className="flex flex-col gap-4">
                        <p>
                            {t.CONTACT.add_phone_description_1()}
                            {' '}
                            <b>{formattedPhone}</b>
                            {' '}
                            {t.CONTACT.add_phone_description_2()}
                            {' '}
                            <b>{name}</b>
                            {' '}
                            {t.CONTACT.add_phone_description_3()}
                        </p>
                        <p>
                            {t.CONTACT.add_phone_description_4()}
                            <SelectField
                                layoutClassName="inline-block min-w-[187px] mx-4"
                                placeholder={t.CONTACT.add_phone_phone_type_placeholder()}
                                value={value}
                                onValueChange={handleSelect}
                                options={options}
                                data-testid="phone-type-dropdown"
                            />
                            {t.CONTACT.add_phone_description_5()}
                        </p>
                    </div>
                )}
            </Modal.Content>
            <Modal.Actions>
                <Button onClick={() => onClose()} data-testid="cancel-button" variant="secondary" >
                    {t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_buttonCancel()}
                </Button>
                <Button onClick={handleConfirm} data-testid="add-button" variant="primary">
                    {t.CONTACT.add_phone_confirmation_button()}
                </Button>
            </Modal.Actions>
        </Modal>
    )
}
