import React, {useEffect, useMemo} from 'react'

import {CHAT_VOICE_RECORDING_PLAYER_GROUP} from '@bpinc/ad-constants'
import {useContactsState} from '@bpinc/ad-contacts-state-context'
import type {ChatType} from '@bpinc/ad-interaction-chat-state-types'
import {useInteractionProp} from '@bpinc/ad-interaction-state-context'
import {getContactId} from '@bpinc/ad-interaction-state-methods'
import {unmountPlayers} from '@bpinc/lib-audio-player'
import {Loader} from '@bpinc/lib-jskit-ui'
import {useInteractionUIChatType} from '@bpinc/state-interaction-ui'

import {useBotSuggestionsLinksAutoOpener} from '../../hooks/useBotSuggestionsLinksAutoOpener'
import {useChatFormVisible} from '../../hooks/useChatFormVisible'
import type {ChatProps} from '../../types'
import {ExternalMessages} from './ExternalMessages/ExternalMessages'
import {useBotSuggestions} from './VoiceTranscript/selectors'
import {VoiceTranscriptMessages} from './VoiceTranscript/VoiceTranscriptMessages'
import {ChatForm} from './ChatForm'
import {useChatIsLoading} from './useChatIsLoading'
import {useMessagesLoader} from './useMessagesLoader'


function typeToMediaFilter(type: ChatType) {
    switch (type) {
        case 'messages':
            return 'chat'
        case 'calls':
            return 'voice'
        default:
            return 'all'
    }
}

export const ExternalChat: React.FC<ChatProps> = React.memo(function ExternalChat(props) {
    const {itemId} = props
    const isChatFormVisible = useChatFormVisible(itemId)
    const botSuggestions = useBotSuggestions(itemId)
    const type = useInteractionUIChatType(itemId)
    const contactId = useInteractionProp(itemId, getContactId)
    const activityHistoryExists = useContactsState(s => s.activityHistory[contactId]?.activityCreatedTimeByMedia?.[typeToMediaFilter(type)])
    const loadMessages = useMessagesLoader(itemId, contactId, type)
    useEffect(() => {
        if (!activityHistoryExists) {
            loadMessages()
        }
    }, [activityHistoryExists, loadMessages])

    const {isLoading, isInitialLoading} = useChatIsLoading(contactId)

    const ChatMessages = useMemo(() => type === 'transcript' ? VoiceTranscriptMessages : ExternalMessages, [type])

    useEffect(() => {
        return () => {
            unmountPlayers({playerGroup: CHAT_VOICE_RECORDING_PLAYER_GROUP})
        }
    }, [])

    //Auto open bot suggestions links only in specified tabs
    useBotSuggestionsLinksAutoOpener(itemId, botSuggestions, type === 'all' || type === 'transcript' || type === 'messages')

    return (
        <div className="relative flex flex-1 flex-col justify-between overflow-hidden">
            <div className="relative flex flex-auto flex-col overflow-hidden bg-secondary-background" data-testid="chat-content">
                {!isInitialLoading && isLoading && <Loader size="32" className="top-[30px] z-10" color="primary" />}
                {isInitialLoading
                    ? <Loader size="32" color="primary" className="z-10" />
                    : <ChatMessages {...props} />
                }
            </div>
            {isChatFormVisible && (
                <ChatForm {...props} />
            )}
        </div>
    )
})
