import React from 'react'


interface ActiveItemDisplayNameProps {
    displayName: string
}

export const ActiveItemDisplayName: React.FC<ActiveItemDisplayNameProps> = ({displayName}) => {
    return (
        <div className="truncate font-semibold body text-primary-on-primary" data-testid="display-name">
            {displayName}
        </div>
    )
}
