import React from 'react'

import {formatPhone} from '@bpinc/ad-formatting-helpers'
import {useInteractionStateMethods} from '@bpinc/ad-interaction-state-context'
import {useTimerById} from '@bpinc/ad-timers-context'
import {ContactListItem} from '@bpinc/lib-contact-list-item-ui'
import {cutMilliseconds, useEvent, useTimer} from '@bpinc/lib-helpers'
import {useInteractionId} from '@bpinc/lib-interaction-context'
import {assertDefined} from '@bpinc/lib-types-helpers'
import {SelectionItem} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'


export type PhoneNumberItemProps = {
    number: string
    label: string
    suggested: boolean
    disabled: boolean
}

export const PhoneNumberItem: React.FC<PhoneNumberItemProps> = ({
    number,
    label,
    suggested,
    disabled,
}) => {

    const {CALL_DETAILS_FORM: i18n} = useLocalizationState('t')
    const activeItemId = useInteractionId()
    assertDefined(activeItemId)
    const {dialWorkitem} = useInteractionStateMethods()

    const formattedNumber = formatPhone(number)
    const autoDialTimer = useTimerById(`workitemAutoDial/${activeItemId}`)

    const uiTimerValue = useTimer(autoDialTimer?.endTime, {countdown: true, format: cutMilliseconds})

    const title = (
        <div>
            {suggested && <span className="absolute left-[5px] top-4.5 size-1.5 rounded-full bg-warning" />}
            <span className="font-semibold body text-primary-on-primary">{label}</span>
        </div>
    )
    const body = (
        <span data-testid="phone" className="body text-neutral-on-secondary">
            {formattedNumber}
        </span>
    )
    const autoDialIndicator = suggested && uiTimerValue
        ? (
            <div className="h-5 rounded bg-teal-on-white-1 px-2">
                <span className="font-semibold caption-s text-primary-on-primary">
                    {i18n.workitem_auto_dial_message_text(String(uiTimerValue ?? 0))}
                </span>
            </div>
        )
        : null

    const clickHandler = useEvent(() => {
        dialWorkitem({
            workitemItemId: activeItemId,
            phoneNumber: number,
        })
    })

    return (
        <ContactListItem
            as={SelectionItem}
            title={title}
            titleRight={autoDialIndicator}
            body={body}
            onClick={clickHandler}
            disabled={disabled}
            tooltip={disabled ? undefined : i18n.workitem_phone_number_call_tooltip()}
            containerProps={{
                focusOnSelection: true,
                touchedOnly: true,
                'data-testid': 'phone-number-item',
                'data-disabled': disabled || undefined,
                'data-suggested': suggested || undefined,
            }}
        />
    )
}
