import type {FC} from 'react'
import React from 'react'
import * as RCollapsible from '@radix-ui/react-collapsible'

import {useBoolean} from '@bpinc/lib-helpers'
import {IconChevronDown16} from '@bpinc/lib-jskit-ui'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {ActiveInteractionTopButtons} from '../active-interaction/ActiveInteractionTopButtons'
import type {ParticipantItem} from './helpers'
import {MAX_PARTICIPANTS_COUNT, useDisplayNamesParticipants} from './helpers'
import {InteractionParticipantsListItem} from './InteractionParticipantsListItem'
import {MultiAvatar} from './MultiAvatar'
import {SelfItem} from './SelfItem'


export type ParticipantsListData = {
    participantsItems?: ParticipantItem[]
    onRemoveItem: (id: string) => void
    canRemove?: boolean
    isChat?: boolean
}

export const InteractionParticipantsList: FC<ParticipantsListData> = ({
    participantsItems,
    onRemoveItem,
    canRemove,
    isChat,
}) => {
    const {value: open, toggle} = useBoolean()
    const {t: {INTERACTIONS}} = useLocalizationState()
    const displayNames = useDisplayNamesParticipants(participantsItems, isChat)
    const countOfParticipants = participantsItems?.length
    if (!countOfParticipants) {
        return null
    }

    const renderTrigger = () => {
        return (
            <div className="flex items-center gap-x-2">
                <MultiAvatar avatarsItems={participantsItems} />
                <div className="flex flex-col">
                    <span className="truncate font-semibold body">{displayNames}</span>
                    <div className="flex gap-0.5">
                        <div className="caption-s text-neutral-fade">
                            {INTERACTIONS.count_members(countOfParticipants + 1)}
                        </div>
                        <IconChevronDown16 className={tcn('transition-transform duration-300', {'-rotate-180': open})} />
                    </div>
                </div>
            </div>
        )
    }

    const renderContent = () => {
        return (
            <div className="scrollbar-general flex max-h-[136px] flex-col overflow-y-auto pl-4 pr-2">
                {participantsItems.map((item, i) => (
                    <InteractionParticipantsListItem
                        key={i}
                        item={item}
                        onRemoveItem={onRemoveItem}
                        isChat={isChat}
                        canRemove={canRemove}
                    />
                ))}
                {countOfParticipants < MAX_PARTICIPANTS_COUNT && <SelfItem />}
            </div>
        )
    }

    return (
        <RCollapsible.Root open={open} onOpenChange={toggle}>
            <div data-testid="parties" className="flex w-full items-center justify-between px-4 py-2">
                <RCollapsible.Trigger>
                    {renderTrigger()}
                </RCollapsible.Trigger>
                <ActiveInteractionTopButtons className="mt-2.5" />
            </div>
            <RCollapsible.Content className="pr-1">
                {renderContent()}
            </RCollapsible.Content>
        </RCollapsible.Root>
    )
}
