import {useMemo} from 'react'

import {useInteractionProp} from '@bpinc/ad-interaction-state-context'
import {
    isAfterCallWork,
    isDelivered,
    isExternalChat,
    isExternalContactCall,
    isNeedToShowWorkitemUI,
    isOnHold,
    isOutboundInteraction,
    isPotentialInteraction,
    isQualityManagementItemInMonitorMode,
} from '@bpinc/ad-interaction-state-methods'
import {useSessionState} from '@bpinc/ad-session-context'


export function useChatFormVisible(itemId: string) {
    const {privileges} = useSessionState()
    const isPotentialItem = useInteractionProp(itemId, isPotentialInteraction)
    const iqQmMonitorMode = useInteractionProp(itemId, isQualityManagementItemInMonitorMode)
    const isOngoingCall = useInteractionProp(itemId, item => isExternalContactCall(item) && (isDelivered(item) || isOnHold(item)))
    const isDeliveredChat = useInteractionProp(itemId, item => {
        /**
         * delivered - when item is active,
         * hold - when other interaction is active, even if it's an associated interaction
         */
        return isExternalChat(item) && (isDelivered(item) || isOnHold(item))
    })
    const isOutboundChat = useInteractionProp(itemId, item => isExternalChat(item) && isOutboundInteraction(item))
    const isAfterCallWorkChat = useInteractionProp(itemId, isAfterCallWork)
    const needToShowWorkitemUI = useInteractionProp(itemId, isNeedToShowWorkitemUI)


    return useMemo(() => {
        /** Hide for supervisor in monitor mode */
        if (iqQmMonitorMode) {
            return false
        }
        /** Don't show chat form if it's after call work chat */
        if (isAfterCallWorkChat) {
            return false
        }
        /** Preview contact activity history for workitem and workitem call */
        if (needToShowWorkitemUI) {
            return false
        }

        /** Show chat form if it's outbound chat or delivered chat */
        if (isOutboundChat || isDeliveredChat) {
            return true
        }

        /** If user has CAN_START_EXTERNAL_CHAT privilege, show chat form if it's potential item or delivered call
         * Basically, it's scenario when chat interaction isn't started yet
         */
        if (privileges?.CAN_START_EXTERNAL_CHAT) {
            return isPotentialItem || isOngoingCall
        }
    },
    [
        iqQmMonitorMode,
        isAfterCallWorkChat,
        needToShowWorkitemUI,
        isOngoingCall,
        isDeliveredChat,
        isOutboundChat,
        isPotentialItem,
        privileges?.CAN_START_EXTERNAL_CHAT,
    ])
}
