import type {ReactElement} from 'react'
import React, {useMemo} from 'react'

import {tcn} from '@bpinc/lib-tailwind-classnames'
import {Button} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {KeyPadButtonContent} from './KeyPadButtonContent'
import {LongPressButton} from './LongPressButton'
import {useLetterBasedDialing} from './useLetterBasedDialing'


type ButtonsMatrix = {
    value: string
    additionalValue?: string
    label?: ReactElement
    holdAction?: boolean
}[][]

export interface KeypadProps {
    handleButtonClick: (value: string) => () => void
    allowLetterBasedDialing?: boolean
}

const keyCode = (key: string) => {
    if (key === '*') {
        return 'asterisk'
    }
    if (key === '#') {
        return 'hashtag'
    }
    return key
}


export const KeyPad: React.FC<KeypadProps> = ({
    handleButtonClick,
    allowLetterBasedDialing = true,
}) => {
    useLetterBasedDialing({handleButtonClick, allowLetterBasedDialing})

    const {t} = useLocalizationState()
    const buttons: ButtonsMatrix = useMemo(
        () => [
            [
                {value: '1', additionalValue: ''},
                {value: '2', additionalValue: t.DIAL_PAD.letter2()},
                {value: '3', additionalValue: t.DIAL_PAD.letter3()},
            ],
            [
                {value: '4', additionalValue: t.DIAL_PAD.letter4()},
                {value: '5', additionalValue: t.DIAL_PAD.letter5()},
                {value: '6', additionalValue: t.DIAL_PAD.letter6()},
            ],
            [
                {value: '7', additionalValue: t.DIAL_PAD.letter7()},
                {value: '8', additionalValue: t.DIAL_PAD.letter8()},
                {value: '9', additionalValue: t.DIAL_PAD.letter9()},
            ],
            [
                {
                    value: '*',
                    label: <span className="h-3.5 text-[25px]">*</span>,
                },
                {value: '0', holdAction: true, additionalValue: '+'},
                {
                    value: '#',
                    label: <span className="text-xl">#</span>,
                },
            ],
        ],
        [t],
    )

    const dialpadButtonClassName = tcn(
        'flex h-full w-full flex-col items-center justify-center',
        'rounded-full',
        'font-noto text-neutral-on-secondary',
        'transition-colors state-layer-bg-neutral-50',
        'hover:state-layer-mix-dark-on-white-1',
        'focus:state-layer-mix-dark-on-white-2',
        'active:state-layer-mix-dark-on-white-3',
    )

    return (
        <div className="grid gap-2.5">
            {buttons.map((row, index) => (
                <div key={index} className="flex items-center gap-[11px]">
                    {row.map(item => (
                        <div key={item.value} className={tcn('flex h-[55px] w-[55px] items-center justify-center')}>
                            {item.holdAction && item.additionalValue ? (
                                <LongPressButton
                                    className={dialpadButtonClassName}
                                    onClick={handleButtonClick(item.value)}
                                    onHold={handleButtonClick(item.additionalValue)}
                                    data-testid={`keypad-${keyCode(item.value)}-button`}
                                >
                                    <KeyPadButtonContent
                                        value={item.label || item.value}
                                        additionalValue={item.additionalValue}
                                    />
                                </LongPressButton>
                            ) : (
                                <Button
                                    className={dialpadButtonClassName}
                                    onClick={handleButtonClick(item.value)}
                                    data-testid={`keypad-${keyCode(item.value)}-button`}
                                >
                                    <KeyPadButtonContent
                                        value={item.label || item.value}
                                        additionalValue={item.additionalValue}
                                    />
                                </Button>
                            )}
                        </div>
                    ))}
                </div>
            ))}
        </div>
    )
}
