import {useEffect} from 'react'

import {useSessionState} from '@bpinc/ad-session-context'
import {useEvent} from '@bpinc/lib-helpers'
import {Notifications, useNotificationsState} from '@bpinc/lib-notifications-context'
import {useLocalizationState} from '@bpinc/state-localization-context'


export function usePhoneDeviceNotDetectedNotification() {
    const {showNotification, hideNotificationById} = useNotificationsState('methods')
    const phoneDeviceIn = useSessionState('phoneDeviceIn')
    const {ERROR_MESSAGES: i18n} = useLocalizationState('t')

    const updateNotification = useEvent(() => {
        if (phoneDeviceIn) {
            hideNotificationById(Notifications.PhoneDeviceNotDetectedError)
            showNotification({
                type: 'success',
                id: Notifications.PhoneDeviceDetectedMessage,
                message: i18n.device_detected(phoneDeviceIn),
                duration: 5000,
            })
        } else {
            showNotification({
                type: 'error',
                id: Notifications.PhoneDeviceNotDetectedError,
                message: i18n.device_empty(),
                duration: 5000,
                showDelay: 5000,
            })
        }
    })

    useEffect(() => {
        updateNotification()
        // track device change only
    }, [phoneDeviceIn, updateNotification, hideNotificationById])
}
