import React from 'react'

import {getMomentUTC} from '@bpinc/lib-helpers'
import {IconRemoteAssistSession} from '@bpinc/lib-icons'
import {useLocalizationState} from '@bpinc/state-localization-context'

import styles from './RemoteAssistMessage.module.less'


type RemoteAssistMessageProps = {
    duration: number
    privateIp?: string
    publicIp?: string
    osVersion?: string
}

export const RemoteAssistMessage: React.FC<RemoteAssistMessageProps> = props => {
    const {duration, privateIp, publicIp, osVersion} = props
    const {t} = useLocalizationState()

    const durationValue = getMomentUTC(duration * 1000, 'm:s')
    const privateIpValue = privateIp || t.ACTIVITY_HISTORY.remote_assist_empty_private_ip_display_value()
    const publicIpValue = publicIp || t.ACTIVITY_HISTORY.remote_assist_empty_public_ip_display_value()
    const osVersionValue = osVersion || t.ACTIVITY_HISTORY.remote_assist_empty_os_version_display_value()

    return (
        <div className={styles.remoteAssistMessage}>
            <div className={styles.remoteAssistMessageHeader}>
                <IconRemoteAssistSession
                    className={styles.remoteAssistMessageIcon}
                    aria-hidden
                />
                <span>{t.ACTIVITY_HISTORY.remote_assist_title()}</span>
            </div>
            <div className={styles.remoteAssistMessageContent}>
                <div className={styles.remoteAssistContentRow}>
                    <span>{t.ACTIVITY_HISTORY.remote_assist_duration_label()}</span>
                    <span>{durationValue}</span>
                </div>
                <div className={styles.remoteAssistContentRow}>
                    <span>{t.ACTIVITY_HISTORY.remote_assist_os_version_label()}</span>
                    <span>{osVersionValue}</span>
                </div>
                <div className={styles.remoteAssistContentRow}>
                    <span>{t.ACTIVITY_HISTORY.remote_assist_private_ip_label()}</span>
                    <span>{privateIpValue}</span>
                </div>
                <div className={styles.remoteAssistContentRow}>
                    <span>{t.ACTIVITY_HISTORY.remote_assist_public_ip_label()}</span>
                    <span>{publicIpValue}</span>
                </div>
            </div>
        </div>
    )
}
