import {useMemo} from 'react'

import {useFormatting} from '@bpinc/ad-formatting-context'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {getDispositionTitle} from '../../../../../helpers'
import type {ActivityMessageGroup as ActivityMessageGroupType} from '../../../../../types'


export function useDispositionTitle(message: ActivityMessageGroupType) {
    const {t} = useLocalizationState()
    const {formatFullName} = useFormatting()

    return useMemo(function memoizeDispositionTitle() {
        return getDispositionTitle(message, t, formatFullName)

    }, [formatFullName, message, t])
}
