import {useCallback} from 'react'

import {formatChatDate} from '@bpinc/ad-formatting-helpers'
import {areSameDays} from '@bpinc/lib-helpers'


interface GetTimeStamp<T> {
    (message: T): number
}
interface Conditions<T> {
    (message: T): boolean
}

interface AddKeyDatesCallback {
    <T>(messages: T[], getTimestamp: GetTimeStamp<T>, additionalConditions?: Conditions<T>): (T | string)[]
}

export function useKeyDatesInserter(): AddKeyDatesCallback {
    function addKeyDates<T>(
        messages: T[],
        getTimestamp: (message: T) => number,
        additionalConditions?: (message: T) => boolean,
    ) {
        return messages.reduce(
            (acc: (T | string)[], message: T, index, messagesArray) => {
                if (index === 0) {
                    addTime(acc, message)
                } else {
                    if (
                        (additionalConditions ? additionalConditions(message) : true) &&
                        showTimeBeforeMessage(
                            new Date(getTimestamp(messagesArray[index - 1])),
                            new Date(getTimestamp(message)),
                        )
                    )
                        addTime(acc, message)
                }

                acc.push(message)

                return acc
            },
            [],
        )

        function addTime(acc: (T | string)[], message: T) {
            const time = formatChatDate(getTimestamp(message))
            acc.push(time)
        }
    }

    return useCallback(addKeyDates, [])
}

//****************************
function showTimeBeforeMessage(currentTime: Date, nextTime: Date) {
    return !areSameDays(currentTime, nextTime)
}
