import React from 'react'

import type {DirectoryEntry} from '@bpinc/ad-directory-state-types'
import {useServiceDetailsState} from '@bpinc/ad-service-details-state-context'

import {HuntGroupMetrics} from './HuntGroupMetrics'
import {ServiceMetrics} from './ServiceMetrics'


type Props = {
    item: DirectoryEntry
}

export const DirectoryEntryMetrics: React.FC<Props> = ({item}) => {

    const {huntGroupsDetails, serviceDetails} = useServiceDetailsState()

    const huntGroupMetrics = item.huntGroupId ? huntGroupsDetails[item.huntGroupId] : undefined
    const serviceMetrics = item.serviceId ? serviceDetails[item.serviceId] : undefined

    if (huntGroupMetrics) {
        return <HuntGroupMetrics metrics={huntGroupMetrics} />
    }
    if (serviceMetrics) {
        return <ServiceMetrics metrics={serviceMetrics} />
    }
    return null
}
