import type {FC} from 'react'
import React from 'react'

import {tcn} from '@bpinc/lib-tailwind-classnames'
import {useLocalizationState} from '@bpinc/state-localization-context'

import type {ControlButton} from './types'


export const DisconnectButton: FC<ControlButton> = ({onClick, className}) => {
    const {t} = useLocalizationState()

    return (
        <button
            className={tcn('underline button-sm text-primary-action', className)}
            onClick={onClick}
            data-testid="remove-party-button"
        >
            {t.INTERACTIONS.disconnect()}
        </button>
    )
}
