import React, {useCallback} from 'react'

import {useInteractionChatDataSelector} from '@bpinc/ad-interaction-chat-state-context'
import {useInteractionProp} from '@bpinc/ad-interaction-state-context'
import {
    getInteractionStepId,
    isAfterCallWork,
    isExternalCall,
    isExternalChat,
    isInternalChat,
    isPotentialInteraction,
    isPotentialInternalInteraction,
} from '@bpinc/ad-interaction-state-methods'
import type {Item} from '@bpinc/ad-interaction-state-types'
import {useDisconnectButtonClickHandler} from '@bpinc/lib-helpers-ui'
import {ButtonIcon, IconChatPanelOpen} from '@bpinc/lib-jskit-ui'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {Sentiment} from '@bpinc/lib-ui-elements'
import {useInteractionUIStateItemSelector} from '@bpinc/state-interaction-ui'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {useParallelChats} from '../../hooks/useParallelChats'
import {CompleteButton} from '../buttons/CompleteButton'
import {FlagButton} from '../buttons/FlagButton'
import {InternalChatWindowButton} from '../buttons/InternalChatWindowButton'
import {useUnread} from './hooks/useUnread'
import {Unread} from './Unread'


type ButtonsOnHoverProps = {
    item: Item
}

const common = 'flex items-center'
const showInFocusClassName = tcn(
    common,
    'w-0 group-focus-within:w-auto group-hover:w-auto',
    'opacity-0 group-focus-within:opacity-100 group-hover:opacity-100',
)
const showOutFocusClassName = tcn(
    common,
    'w-auto group-focus-within:w-0 group-hover:w-0',
    'opacity-100 group-focus-within:opacity-0 group-hover:opacity-0',
)

export const ButtonsOnHover: React.FC<ButtonsOnHoverProps> = ({item}) => {
    const {t} = useLocalizationState()
    const totalChatSentiment = useInteractionChatDataSelector(
        getInteractionStepId(item),
        s => s?.sentiment,
    )
    const isEmbeddedChatView = useInteractionUIStateItemSelector(
        item.id,
        s => s?.chatView === 'embedded',
    )
    const handleDisconnectButtonClick = useDisconnectButtonClickHandler()

    const onClose = useCallback(
        (event: React.SyntheticEvent) => handleDisconnectButtonClick(item, {event}),
        [handleDisconnectButtonClick, item],
    )

    const showFlagButton = useInteractionProp(
        item.id,
        activeItem =>
            !isPotentialInteraction(activeItem) && !isInternalChat(activeItem),
    )

    const showExternalChatWindowButton = (isExternalChat(item) || isExternalCall(item))
    const showInternalChatWindowButton = (
        (isInternalChat(item) || isPotentialInternalInteraction(item)) &&
        isEmbeddedChatView
    )
    const isAfterCallWorkState = isAfterCallWork(item)

    const {unreadAmount, customerWaitTimer} = useUnread(item, showExternalChatWindowButton)

    const {showOpenParallelChatButton, openParallelChat} = useParallelChats(item)

    if (isAfterCallWorkState) {
        return (
            <>
                <div className={showOutFocusClassName}>
                    <span className="button-sm text-neutral-on-secondary">
                        {t.INTERACTIONS.acl_after_call_work_label()}
                    </span>
                </div>
                <div className={showInFocusClassName}>
                    <CompleteButton
                        item={item}
                        handleCloseClick={onClose}
                        small
                    />
                </div>
            </>
        )
    }

    return (
        <>
            <div className={showInFocusClassName}>
                {showExternalChatWindowButton && showOpenParallelChatButton && (
                    <ButtonIcon
                        tooltip={t.KNOWLEDGE_BASE.new_tab_button_tooltip()}
                        onClick={openParallelChat}
                    >
                        <IconChatPanelOpen className="size-4" />
                    </ButtonIcon>
                )}
                {showInternalChatWindowButton && (
                    <InternalChatWindowButton itemId={item.id} />
                )}
                <CompleteButton
                    item={item}
                    handleCloseClick={onClose}
                    small
                />
            </div>
            <div className={showOutFocusClassName}>
                {showFlagButton && <FlagButton />}
                <Unread
                    unreadAmount={unreadAmount}
                    customerWaitTimer={customerWaitTimer}
                    showTimerProgressBar={showExternalChatWindowButton}
                />
                {totalChatSentiment && <Sentiment className="ml-2" sentiment={totalChatSentiment} />}
            </div>
        </>
    )
}
