import {useEffect, useMemo} from 'react'

import {useActivitiesState, useActivitiesStateMethods} from '@bpinc/ad-activities-state-context'
import {useSessionState} from '@bpinc/ad-session-context'

import {activityToChatMessage} from '../../../../helpers'


export function useActivitiesAsMessages(contactId: string) {
    const activitiesListByContactId = useActivitiesState('activitiesListByContactId')
    const {getActivitiesList} = useActivitiesStateMethods()
    const {userId: selfUserId} = useSessionState()
    const activitiesList = activitiesListByContactId[contactId]

    useEffect(function initializeActivitiesList() {
        if (contactId && !activitiesList) {
            getActivitiesList({id: contactId})
        }
    }, [activitiesList, getActivitiesList, contactId])


    return useMemo(function memoizeActivitiesMessages() {
        return activitiesList?.data?.flatMap(activityToChatMessage(selfUserId)) ?? []
    }, [activitiesList, selfUserId])
}
