import type {
    FormDefinitionField,
    FormDefinitionFieldContainer,
    FormDefinitionFieldSection,
} from '@bpinc/ad-request-urls'


export function checkIsLayout(field: FormDefinitionField): field is FormDefinitionFieldSection | FormDefinitionFieldContainer {
    return field.type === 'STATIC' &&
    (field.staticType === 'SECTION' || field.staticType === 'CONTAINER')
}
