import React, {useMemo} from 'react'

import type {ActivityEmail} from '@bpinc/ad-case-state-types'
import {useGetAvatarUrl} from '@bpinc/ad-contacts-state-hooks'
import {useFormatting} from '@bpinc/ad-formatting-context'
import {formatDate} from '@bpinc/ad-formatting-helpers'
import {ContactListItem} from '@bpinc/lib-contact-list-item-ui'
import {SmIcon} from '@bpinc/lib-external-ui-elements'

import {getNameFromParty} from '../utils'
import {Disposition} from './Disposition'
import {EmailContent} from './EmailContent'
import {Info} from './Info'


interface Props {
    activity: ActivityEmail
    onOpenClick: (id: string) => void
}

export const EmailPreview: React.FC<Props> = ({activity}) => {
    const {formatFullName} = useFormatting()
    const getAvatarUrl = useGetAvatarUrl()
    const {emailData, direction, parties, services} = activity
    const receivers = (emailData.toAddresses || []).map(a => a.name).join(', ')
    const partyInternal = parties.find(p => p.partyType === 'USER')
    const partyExternal = parties.find(p => p.partyType !== 'USER')
    const isOutbound = direction !== 'INBOUND'
    const [firstName, lastName] = getNameFromParty(isOutbound ? partyInternal : partyExternal)
    const contactId = isOutbound ? undefined : partyExternal?.contactId
    const userId = isOutbound ? partyInternal?.userId : undefined

    const avatar = useMemo(() => {
        return {
            firstName,
            lastName,
            url: getAvatarUrl({
                userId,
                contactId,
            }),
            isAgent: isOutbound,
        }
    }, [contactId, firstName, getAvatarUrl, isOutbound, lastName, userId])

    const title = formatFullName(firstName, lastName)
    const body = (
        <span className="relative -top-1 text-[0.8em]">
            <SmIcon name="envelope" />
            {formatDate(emailData.date)}
        </span>
    )

    const emailTitle = emailData.subject
    const emailInfo = isOutbound && receivers
        ? <Info title="To" size="small">{receivers}</Info>
        : undefined

    return (
        <div>
            <ContactListItem
                as="div"
                avatarProps={avatar}
                contactId={contactId}
                userId={userId}
                title={title}
                body={body}
                isAlignedToRight={isOutbound}
                isFitted
            />
            <EmailContent
                info={emailInfo}
                title={emailTitle}
                html={emailData.content}
                attachments={emailData.attachments}
                services={services}
                partyInternal={partyInternal}
            />
            {partyInternal && partyInternal.disposition && (
                <Disposition data={partyInternal} type="email" services={services} />
            )}
        </div>
    )
}
