import {useEffect, useRef} from 'react'

import {bpLogger} from '@bpinc/ad-persistent-logger'
import {useEvent} from '@bpinc/lib-helpers'
import {Notifications, useNotificationsState} from '@bpinc/lib-notifications-context'
import {useLocalizationState} from '@bpinc/state-localization-context'


const CHECK_INTERVAL = 5000
const SLOW_THRESHOLD = 800

/**
 * This hook periodically checks the user's connection speed by fetching root of the current host.
 * and shows a notification if the connection is slow.
 * Connection is considered slow based on the predefined threshold (SLOW_THRESHOLD),
 * When the connection speed improves, it hides the notification.
 */
export function useSlowNetworkNotification() {
    const {showNotification, hideNotificationById} = useNotificationsState('methods')
    const {GLOBAL: i18n} = useLocalizationState('t')

    const startTime = useRef(0)

    const checkConnectionSpeed = useEvent(() => {
        const isRequestAlreadyInProgress = startTime.current !== 0

        if (isRequestAlreadyInProgress || process.env.NODE_ENV === 'development') {
            return
        }

        startTime.current = new Date().getTime()

        fetch('/', {
            method: 'HEAD',
            cache: 'no-store',
        })
            .catch(e => console.error(e))
            .finally(() => {
                const endTime = new Date().getTime()
                const duration = endTime - startTime.current

                startTime.current = 0

                if (duration > SLOW_THRESHOLD) {
                    const message = i18n.slow_connection()
                    showNotification({
                        type: 'error',
                        id: Notifications.SlowNetworkConnectionError,
                        message,
                    })
                    bpLogger.warn(`Slow connection detected`)
                } else {
                    hideNotificationById(Notifications.SlowNetworkConnectionError)
                }
            })
    })

    useEffect(() => {
        const interval = setInterval(checkConnectionSpeed, CHECK_INTERVAL)
        return () => clearInterval(interval)
    }, [checkConnectionSpeed])
}
