import {atomWithReducer} from 'jotai/utils'

import type {RecentPartyType} from '@bpinc/state-recent-types'


type RecentConferencePanelState = {
    selected: RecentPartyType | null
    searchValue: string
}


type SelectPartyAction = {
    type: 'selectParty'
    payload: {
        party: RecentPartyType
    }
}
type SetSearchValueAction = {
    type: 'setSearchValue'
    payload: {
        value: string
    }
}
type ClearStateAction = {type: 'clearState'}
type Action = SelectPartyAction | SetSearchValueAction | ClearStateAction


function reducer(_prev: RecentConferencePanelState, action: Action) {
    switch (action.type) {
        case 'selectParty':
            return {
                selected: action.payload.party,
                searchValue: action.payload.party.phone ?? '',
            }
        case 'setSearchValue':
            return {
                selected: null,
                searchValue: action.payload.value,
            }
        case 'clearState':
            return {
                selected: null,
                searchValue: '',
            }
    }
}

export const stateAtom = atomWithReducer<RecentConferencePanelState, Action>(
    {selected: null, searchValue: ''},
    reducer,
)
