import React, {useCallback, useState} from 'react'

import {platformInfo} from '@bpinc/ad-build-and-platform-info'
import {useIntegrationApiState, useIntegrationApiStateMethods} from '@bpinc/ad-integration-api-state-context'
import {useInteractionStateSelector} from '@bpinc/ad-interaction-state-context'
import type {DirectChatInvitationRequest} from '@bpinc/ad-request-urls'
import {apiRequests} from '@bpinc/ad-request-urls'
import {useSessionState} from '@bpinc/ad-session-context'
import {ActiveStatusControlCommunicator} from '@bpinc/lib-agent-state-ui'
import {AppRoutes, useNavigationState} from '@bpinc/lib-communicator-navigation-state'
import {useCommunicatorUIState} from '@bpinc/lib-communicator-ui-context'
import {SmHeader, SmIcon, SmLabel} from '@bpinc/lib-external-ui-elements'
import {clipboard} from '@bpinc/lib-helpers'
import {IconDirectChatInvitation} from '@bpinc/lib-icons'
import {Notifications, useNotificationsState} from '@bpinc/lib-notifications-context'
import {OfflineBar} from '@bpinc/lib-offline-state-ui'
import {MissedCallsBadge, SemanticButton} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {useNavigationTitle} from '../../hooks'
import {InteractionBar} from '../../interactions'
import {DirectChatLinkSafariNotification} from './DirectChatLinkSafariNotification'

import styles from '../styles/MainPage.module.less'


export const MainPageHeader: React.FC = () => {
    const {
        toggleRightPanel,
        toggleLeftPanel,
    } = useCommunicatorUIState(['toggleLeftPanel', 'toggleRightPanel'])
    const {t} = useLocalizationState()
    const missedCallsAmount = useInteractionStateSelector(s => s.missedCallsAmount)
    const {activePage, setActivePage} = useNavigationState()
    const title = useNavigationTitle()
    const {serverOrigin, userId, privileges} = useSessionState()
    const {showNotification} = useNotificationsState('methods')
    const {integrationKey} = useIntegrationApiState()
    const {requestRecordOnScreen} = useIntegrationApiStateMethods()

    const [chatLinkRequestInProcess, setChatLinkRequestInProcess] = useState(false)
    const [copyLinkBadgeTrigger, setCopyLinkBadgeTrigger] = useState(0)

    const handleMenuItemClick = useCallback(() => {
        setActivePage({name: AppRoutes.Interactions})
    }, [setActivePage])

    const requestDirectChatLink = useCallback(async () => {
        if (!userId) {
            return
        }
        setChatLinkRequestInProcess(true)

        const requestBody: DirectChatInvitationRequest = {userId}
        if (integrationKey) {
            const crmRecord = await requestRecordOnScreen()
            requestBody.params = {
                crmType: integrationKey,
                crmObjectType: crmRecord?.type,
                crmObjectId: crmRecord?.id,
            }
        }

        apiRequests(serverOrigin)
            .requestDirectChatInvitationLink({requestBody})
            .then(({URL, expiresIn}) => {
                if (platformInfo.browser === 'safari') {
                    showNotification({
                        type: 'success',
                        id: Notifications.DirectChatSafariConfirmation,
                        duration: expiresIn * 1000,
                        closable: true,
                        content: <DirectChatLinkSafariNotification url={URL} />,
                    })
                    return
                }
                clipboard.copy(URL)
                setCopyLinkBadgeTrigger(c => ++c)
            })
            .catch(e => {
                console.error('Unable to get direct chat invitation link:', e)
            })
            .finally(() => setChatLinkRequestInProcess(false))
    }, [userId, serverOrigin, integrationKey, requestRecordOnScreen, showNotification])

    return (
        <>
            {activePage.name !== AppRoutes.Interactions && <InteractionBar action={handleMenuItemClick} />}

            <header className={styles.header} role="none">
                <div className={styles.headerTop}>
                    <div className={styles.agentState}>
                        <ActiveStatusControlCommunicator toggleLeftPanel={toggleLeftPanel} />
                    </div>

                    <SmHeader as="h3" className={styles.pageTitle} role="heading" aria-level="1">
                        {title}
                    </SmHeader>

                    <div className={styles.rightButtons}>
                        {privileges.DIRECT_CHAT_INVITATION && (
                            <>
                                {!!copyLinkBadgeTrigger && (
                                    <SmLabel
                                        key={copyLinkBadgeTrigger}
                                        pointing="right"
                                        className={styles.copiedLabel}
                                    >
                                        {t.HOME_SCREEN.direct_chat_invitation_link_copied_label()}
                                    </SmLabel>
                                )}
                                <SemanticButton
                                    className={styles.directChat}
                                    onClick={requestDirectChatLink}
                                    tooltip={t.HOME_SCREEN.request_direct_chat_invitation_link_button_tooltip()}
                                    data-key="direct-chat-invitation-button"
                                    enabledInOffline={false}
                                    disabled={chatLinkRequestInProcess}
                                    basic
                                    icon
                                    compact
                                >
                                    <IconDirectChatInvitation />
                                </SemanticButton>
                            </>
                        )}
                        <SemanticButton
                            className={styles.hamburger}
                            onClick={toggleRightPanel}
                            tooltip={t.HOME_SCREEN.menu_icon_tooltip()}
                            data-testid="menu-button"
                            basic
                            enabledInOffline
                            icon
                            compact
                        >
                            <SmIcon name="bars" />
                            <MissedCallsBadge
                                className="absolute bottom-0 right-0 translate-x-1/3 translate-y-1/3"
                                amount={missedCallsAmount}
                            />
                        </SemanticButton>
                    </div>
                </div>
                <OfflineBar className={styles.offlineBar} />
            </header>
        </>
    )
}
