import {useEffect} from 'react'

import {useNavigationState} from '@bpinc/lib-communicator-navigation-state'
import {useInteractionUIItemStateSetter} from '@bpinc/state-interaction-ui'


export function useInitialTabFromPageParams(itemId: string | undefined) {
    const {activePage, removePageParam} = useNavigationState()
    const setItemState = useInteractionUIItemStateSetter()

    useEffect(() => {
        const currentTab = activePage?.params?.currentTab
        if (currentTab && itemId) {
            setItemState(itemId, state => ({...state, activeTab: currentTab}))
            removePageParam('currentTab')
        }
    }, [itemId, activePage?.params?.currentTab, setItemState, removePageParam])
}
