import type {FC} from 'react'
import React from 'react'
import type {ButtonProps} from '@servicepattern/ui'
import {Button} from '@servicepattern/ui'

import {tcn} from '@bpinc/lib-tailwind-classnames'
import {useLocalizationState} from '@bpinc/state-localization-context'


type Props = ButtonProps & {
    withStateLayer?: boolean
}

export const EndButton: FC<Props> = ({withStateLayer, ...props}) => {
    const {t} = useLocalizationState()

    return (
        <div
            className={tcn(
                'rounded p-2.5',
                'transition-colors hover:state-layer-opacity-4',
                'state-layer-bg-[--c-main-bars-bg] state-layer-color-black state-layer-opacity-0', {
                    'p-0': !withStateLayer,
                })}
        >
            <Button
                //TODO: Add the click throttle
                variant="destructive"
                size="sm"
                color="danger"
                className="px-3 py-0.5"
                data-testid="complete-button"
                {...props}
            >
                {t.INTERACTIONS.end()}
            </Button>
        </div>
    )
}
