import {useEffect} from 'react'

import {formatDate} from '@bpinc/ad-formatting-helpers'
import {useInteractionStateConnector} from '@bpinc/ad-interaction-state-context'
import {isCallWithoutCallId} from '@bpinc/ad-interaction-state-methods'
import type {Item} from '@bpinc/ad-interaction-state-types'
import {useEvent} from '@bpinc/lib-helpers'
import {Notifications, useNotificationsState} from '@bpinc/lib-notifications-context'
import {useLocalizationState} from '@bpinc/state-localization-context'


/**
 * Show warning when call completed with invalid state
 */
export function useFailedCallNotification() {
    const interactionStateConnector = useInteractionStateConnector()
    const {showNotification} = useNotificationsState('methods')
    const {GLOBAL: i18n} = useLocalizationState('t')

    const onItemCompleted = useEvent(({item}: {item: Item}) => {
        if (isCallWithoutCallId(item)) {
            showNotification({
                type: 'warning',
                id: Notifications.CallFailedDueNetworkIssueWarning,
                message: i18n.notification_callWithNoAnswer_networkProblem(formatDate(new Date().getTime())),
            })
        }
    })

    useEffect(() => {
        const subscription = interactionStateConnector.dispenser.subscribe({onItemCompleted})
        return subscription.unsubscribe
    }, [interactionStateConnector, onItemCompleted])
}
