import type {FC} from 'react'
import React from 'react'

import {CircleButton} from '@bpinc/lib-jskit-ui'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {MicrophoneIcon} from './MicrophoneIcon'
import type {ControlButton} from './types'


export const MuteButton: FC<ControlButton> = ({
    isActive,
    disabled,
    onClick,
}) => {
    const {t} = useLocalizationState()

    return (
        <CircleButton
            onClick={onClick}
            disabled={disabled}
            tooltip={{content: isActive
                ? t.CALL_DETAILS_FORM.unmute_tooltip()
                : t.CALL_DETAILS_FORM.mute_tooltip(),
            }}
            data-testid="mute-button"
            data-muted={isActive || undefined}
        >
            <MicrophoneIcon className="icon-fade" muted={!!isActive} />
        </CircleButton>
    )
}
