import React, {useCallback} from 'react'

import {useAppConfigState} from '@bpinc/ad-app-config-state-context'
import {ButtonIcon, IconWarning16} from '@bpinc/lib-jskit-ui'
import {useLocalizationState} from '@bpinc/state-localization-context'
import {useModalsState} from '@bpinc/state-modals-context'

import {useOpenCallProblemModal} from '../../../../modals'


interface ReportProblemButtonProps {
    className?: string
}

export const ReportProblemButton: React.FC<ReportProblemButtonProps> = ({
    className,
}) => {
    const showModal = useModalsState(({modals}) => modals.some(({id}) => id === 'call_problem'))
    const openCallProblemModal = useOpenCallProblemModal()
    const {t} = useLocalizationState()
    const renderReportProblemButton = useAppConfigState('renderReportProblemButton')

    const onClick = useCallback(
        () => openCallProblemModal(undefined, {id: 'call_problem'}),
        [openCallProblemModal],
    )


    return (
        <>
            {renderReportProblemButton && (
                <ButtonIcon
                    active={showModal}
                    className={className}
                    onClick={onClick}
                    tooltip={t.CALL_DETAILS_FORM.report_a_call_problem()}
                    data-testid="report-problem-button"
                >
                    <IconWarning16 />
                </ButtonIcon>
            )}
        </>
    )
}
