import React from 'react'

import {useDirectoryUIState} from '@bpinc/lib-communicator-ui-context'

import {CreateContactForm} from './CreateContactForm'
import type {ExternalContactProps} from './ExternalUsers'
import {ExternalUsers} from './ExternalUsers'


export const External: React.FC<ExternalContactProps> = props => {
    const createContactMode = useDirectoryUIState(s => s.createContactMode)

    return createContactMode ? <CreateContactForm /> : <ExternalUsers {...props} />
}
