import {useMemo} from 'react'
import {useAtomValue, useSetAtom} from 'jotai'

import {useIsCommunicator} from '@bpinc/ad-current-tab-data-context'
import type {Item} from '@bpinc/ad-interaction-state-types'
import {useCommunicatorUIState} from '@bpinc/ad-main-page-ui-context'
import {AppRoutes, useNavigationState} from '@bpinc/ad-navigation-state-context'
import {useEvent} from '@bpinc/lib-helpers'
import {useIsParallelInteractionContext} from '@bpinc/lib-interaction-context'
import {parallelItemsAtom, parallelItemsHaveEnoughSpaceAtom} from '@bpinc/state-interaction-ui'


export const useParallelChats = (item?: Item) => {
    const getLocation = useNavigationState('getLocationState')
    const isCommunicator = useIsCommunicator()
    const isParalleled = useIsParallelInteractionContext()
    const showParallelControls = !isCommunicator && getLocation() === AppRoutes.interactionWorkspace
    const hasEnoughSpaceForParallelItem = useAtomValue(parallelItemsHaveEnoughSpaceAtom)


    const parallelItems = useAtomValue(parallelItemsAtom)
    const parallelItemsDispatch = useSetAtom(parallelItemsAtom)
    const setParallelInteractionsWidth = useCommunicatorUIState(s => s.setParallelInteractionsWidth)

    const openParallelChat = useEvent((e: React.SyntheticEvent) => {
        e.stopPropagation()
        item?.id && parallelItemsDispatch({
            type: 'add',
            payload: {activeId: item.id, ids: [item.id, ...item?.associatedItemIds ?? []]},
        })

        /**  If all parallel items have ids,
         * we should increase the width as we add one more item visually
        */
        if (parallelItems.every(p => p.activeItemId)) {
            setParallelInteractionsWidth(w => w + w / (parallelItems.length + 1))
        }
    })

    const closeParallelChat = useEvent((e: React.SyntheticEvent) => {
        e.stopPropagation()
        item?.id && parallelItemsDispatch({
            type: 'empty',
            payload: {ids: [item.id, ...item?.associatedItemIds ?? []]},
        })
    })

    return useMemo(() => ({
        showOpenParallelChatButton: showParallelControls && hasEnoughSpaceForParallelItem && !isParalleled,
        showCloseParallelChatButton: showParallelControls && isParalleled,
        openParallelChat,
        closeParallelChat,
    }), [
        showParallelControls,
        hasEnoughSpaceForParallelItem,
        isParalleled,
        openParallelChat,
        closeParallelChat,
    ])
}
