import type {ReactElement} from 'react'
import React from 'react'
import type {FieldValues} from 'react-hook-form'

import {SmRef} from '@bpinc/lib-external-ui-elements'
import type {
    DateTimeBaseProps,
    FieldImplementationProps,
    FieldProps,
    FieldRenderImplementationProps,
} from '@bpinc/lib-forms-base'
import {DateTimeBase, FieldConstructor} from '@bpinc/lib-forms-base'


type DatePickerFieldProps = FieldImplementationProps<DateTimeBaseProps>

function renderElement<TFormValues extends FieldValues>({
    ref,
    error,
    ...props
}: FieldRenderImplementationProps<DatePickerFieldProps, TFormValues>): ReactElement {
    return (
        <div>
            <SmRef innerRef={ref}>
                <DateTimeBase {...props} />
            </SmRef>
            {error && <p className="self-end text-red-500" role="alert aria-atomic">{error}</p>}
        </div>

    )
}

export function DatePicker<TFormValues extends FieldValues>(props: FieldProps<DatePickerFieldProps, TFormValues>): ReactElement {
    return <FieldConstructor {...props} renderElement={renderElement} />
}
