import {useCallback} from 'react'

import {useContactsStateMethods} from '@bpinc/ad-contacts-state-context'
import type {ContactShortType} from '@bpinc/ad-contacts-state-types'
import {useActiveInteraction, useInteractionStateMethods} from '@bpinc/ad-interaction-state-context'
import {
    getChannel,
    getChatParties,
    isCall,
    isExternalChat,
    isWorkItem,
} from '@bpinc/ad-interaction-state-methods'
import {isMessengerChannel} from '@bpinc/ad-transport-types'
import {assertDefined} from '@bpinc/lib-types-helpers'

import {useOpenAddEmailModal, useOpenAddPhoneModal} from '../components/modals'


export function useHandleUseContact() {
    const activeInteraction = useActiveInteraction()
    const interactionStateMethods = useInteractionStateMethods()
    const openAddEmailModal = useOpenAddEmailModal()
    const openAddPhoneModal = useOpenAddPhoneModal()
    const channel = getChannel(activeInteraction)
    const {getContactDetails} = useContactsStateMethods()

    return useCallback((contact: ContactShortType) => {
        assertDefined(activeInteraction)

        const contactId = contact.id
        const {firstName: contactFirstName, lastName: contactLastName} = contact

        if (isCall(activeInteraction)) {
            const parties = activeInteraction.allParties || []

            if (parties.length) {
                const {id: partyId, theirItemId, phone: partyPhone} = parties[0]

                interactionStateMethods.useContact({
                    partyId,
                    itemId: theirItemId || '',
                    contactId: contactId,
                    firstName: contactFirstName || '',
                    lastName: contactLastName || '',
                })

                if (partyPhone && phoneIsAbsent(partyPhone)) {
                    openAddPhoneModal({
                        phone: partyPhone,
                        contactId,
                        firstName: contactFirstName,
                        lastName: contactLastName,
                    })
                }
                return Promise.resolve()
            }

        } else if (isExternalChat(activeInteraction)) {
            const parties = getChatParties(activeInteraction)

            if (parties?.length) {
                const {
                    email,
                    item_id: itemId,
                    party_id: partyId,
                    first_name: partyFirstName,
                    last_name: partyLastName,
                    phone: partyPhone,
                } = parties[0]

                const firstNameComputed = contactFirstName || partyFirstName || ''
                const lastNameComputed = contactLastName || partyLastName || ''

                interactionStateMethods.useContact({
                    itemId,
                    partyId,
                    contactId: contactId,
                    firstName: firstNameComputed,
                    lastName: lastNameComputed,
                })

                if (partyPhone && phoneIsAbsent(partyPhone)) {
                    openAddPhoneModal({
                        phone: partyPhone,
                        contactId,
                        firstName: firstNameComputed,
                        lastName: lastNameComputed,
                    })
                }

                if (email) {
                    openAddEmailModal({
                        email,
                        contactId,
                        firstName: contactFirstName,
                        lastName: contactLastName,
                    })
                }

                if (isMessengerChannel(channel)) {
                    setTimeout(() => {
                        getContactDetails({id: contactId, force: true})
                    }, 500)
                }
                return Promise.resolve()
            }

        } else if (isWorkItem(activeInteraction)) {
            interactionStateMethods.useContact({
                itemId: activeInteraction.id,
                contactId: contactId,
                firstName: contactFirstName ?? '',
                lastName: contactLastName ?? '',
            })
            return Promise.resolve()
        }
        return Promise.reject()

        function phoneIsAbsent(checkingPhone: string) {
            return contact.phones?.every(p => p !== checkingPhone)
        }
    }, [activeInteraction, interactionStateMethods, openAddPhoneModal, channel, openAddEmailModal, getContactDetails])

}
