import type {ReactElement} from 'react'
import React from 'react'


interface KeyyPadButtonProps {
    value: string | ReactElement
    additionalValue?: string
}

export const KeyPadButtonContent: React.FC<KeyyPadButtonProps> = ({
    value,
    additionalValue,
}) => {
    return (
        <>
            <p className="flex flex-col items-center justify-center text-[22px] leading-[26px]">{value}</p>
            {additionalValue !== undefined && (
                <span className="h-3 text-[9px] uppercase tracking-[1.44px]">
                    {additionalValue}
                </span>
            )}
        </>
    )
}
