import {isEqual} from 'lodash-es'

import type {AgentStateData} from '@bpinc/ad-agent-state-types'
import type {BPClientStateData} from '@bpinc/ad-bpclient-state-types'
import {getInitialBPClientState} from '@bpinc/ad-bpclient-state-types'
import type {AuthorizedConnectors, BaseConnectors} from '@bpinc/ad-connected-state-types'
import {getInitialDidState} from '@bpinc/ad-did-types'
import {getInitialDirectoryState} from '@bpinc/ad-directory-state-types'
import type {ErrorType} from '@bpinc/ad-error-types'
import {
    getDIDNumberErrorRaw,
    getMessageErrorsRaw,
    getNumberErrorsRaw,
    getServiceErrorsRaw,
} from '@bpinc/ad-error-validation-logic'
import {formatFullName as formatFullNameBase} from '@bpinc/ad-formatting-helpers'
import type {IntegrationApiStateData} from '@bpinc/ad-integration-api-state-types'
import {ApiError, getInitialIntegrationApiState, resultError} from '@bpinc/ad-integration-api-state-types'
import {getGlobalInteractionId, getInteractionStepId, getMainItem} from '@bpinc/ad-interaction-state-methods'
import type {InteractionStateData, Item} from '@bpinc/ad-interaction-state-types'
import {getInitialInteractionState} from '@bpinc/ad-interaction-state-types'
import {bpLogger} from '@bpinc/ad-persistent-logger'
import {getInitialRASharedState} from '@bpinc/ad-remote-assist-state-types'
import {getInitialServicesDispositionsState} from '@bpinc/ad-services-dispositions-types'
import type {AgentPrivilegeType, SessionData} from '@bpinc/ad-session-types'
import {getInitialSessionState} from '@bpinc/ad-session-types'
import {getInitialTimezonesState} from '@bpinc/ad-timezones-state-types'
import type {ItemState} from '@bpinc/ad-transport-types'
import type {
    AgentState as ApiAgentState,
    ChatChannelType,
    CustomTransferData,
    DispositionTarget,
    InitiateResult as ApiInitiateResult,
    InteractionAssociatedObjectData,
    InteractionScreen,
    LoginData,
    PhoneDeviceData,
    RescheduleData,
    ResultError,
} from '@bpinc/adapters-public-api-types'
import {getErrorMessage, isBefore} from '@bpinc/lib-helpers'
import type {EventSubscription} from '@bpinc/lib-typed-events'
import {typedKeys} from '@bpinc/lib-types-helpers'

import {
    convertActivity,
    convertDIDNumber,
    convertDisposition,
    convertItem,
    convertPhoneCapabilities,
    convertScreenRecordingState,
    convertServerError,
    convertService,
    convertTeam,
    convertTeamMember,
} from './converters'
import {createApiRequestTracker} from './pending-requests'


const API_REQUEST_TIMEOUT = process.env.NODE_ENV === 'production' ? 5000 : 50_000

export type UISpecificRefValue = {
    getDIDNumber: () => string | undefined
    setDIDNumber: (didNumber?: string) => void
    getService: () => string | undefined
    setService: (service?: string) => void
    setChatMessageSuggestion: (message: string, allowOverwrite: boolean, interactionId: string) => void
    showInteractionPage: () => void
    deferredDial?: (number: string) => void
}

const adErrorToApiError: Record<ErrorType, ResultError> = {
    noChatInService: resultError('no_chat_in_service', 'Selected service has not associated chat entry'),
    noChannelInService: resultError('no_channel_in_service', 'No service selected or service is not suitable for this action'),
    noNumber: resultError('empty_number', 'Number is empty'),
    noService: resultError('no_service', 'Service is required, but not selected'),
    selfCall: resultError('self_call', 'Recursive call to yourself'),
    noDIDPermissionsSMS: resultError('no_permissions_did_sms', 'No permissions to start SMS chat with a selected DID number'),
    noDIDPermissionsCall: resultError('no_permissions_did_call', 'No permissions to start SMS chat with a selected DID number'),
    messageTooBig: resultError('invalid_args', 'A single message size exceeded its limit'),
}

const bpLog = bpLogger.withPrefix('@API_con')

export const connectIntegrationApiAuthorized = (
    {currentTabDataProvider, integrationApiConnector, sessionConnector}: BaseConnectors,
    {sharedConnectors}: AuthorizedConnectors,
    ref: {current: UISpecificRefValue},
): EventSubscription[] => {

    const interactionStateMethods = sharedConnectors.interactionStateConnector.dispatch
    const agentStateMethods = sharedConnectors.agentStateConnector.dispatch
    const directoryStateMethods = sharedConnectors.directoryStateConnector.dispatch
    const servicesDispositionsStateMethods = sharedConnectors.servicesDispositionsStateConnector.dispatch
    const didNumbersStateMethods = sharedConnectors.didStateConnector.dispatch
    const bpClientStateMethods = sharedConnectors.bpClientStateConnector.dispatch
    const interactionChatStateMethods = sharedConnectors.interactionChatStateConnector.dispatch

    const apiRequestTracker = createApiRequestTracker(API_REQUEST_TIMEOUT)

    let newUnknownItems: Record<string, boolean> = {}
    let apiDispatchedItems: Record<string, boolean> = {}
    let transferredDataItems: Record<string, boolean> = {}
    let agentState: AgentStateData = {}
    let interactionState = getInitialInteractionState()
    let sessionState = getInitialSessionState()
    let servicesDispositionsState = getInitialServicesDispositionsState()
    let didNumbersState = getInitialDidState()
    let timezonesState = getInitialTimezonesState()
    let directoryState = getInitialDirectoryState()
    let bpClientState = getInitialBPClientState()
    let remoteAssistState = getInitialRASharedState()
    let apiState = getInitialIntegrationApiState()

    const formatFullName = (firstName: string, lastName: string) => formatFullNameBase(sessionState.displayNameFormat, firstName, lastName)

    const validate = (type: 'call' | 'smsChat', phoneNumber: string): ResultError[] => {
        const options = type === 'call' ? {skip: ['noChatInService' as const]} : {}
        const interactionType: 'call' | 'sms' = type === 'call' ? 'call' : 'sms'
        const selectedService = ref.current.getService()
        const selectedDIDNumber = ref.current.getDIDNumber()
        const serviceDIDErrors = selectedDIDNumber
            ? getDIDNumberErrorRaw({
                options: {...options, interactionType},
                selectedDIDNumber,
                didNumbers: didNumbersState.didNumbers,
            })
            : getServiceErrorsRaw({
                options,
                selectedService,
                services: servicesDispositionsState.services,
                serviceRequiredForOutboundCall: servicesDispositionsState.serviceRequiredForOutboundCall,
            })
        const errors = [
            ...serviceDIDErrors,
            ...getNumberErrorsRaw({
                options: {number: phoneNumber},
                selfPhoneNumber: sessionState.phoneNumber,
            }),
        ]
        return errors.map(err => adErrorToApiError[err])
    }

    const getItem = (interactionId?: string) => {
        const activeItem = interactionState.items.find(item => item.id === interactionState.activeItemId)
        const item = interactionId
            ? interactionState.items.find(currItem => currItem.id === interactionId)
            : activeItem
        if (!item) {
            throw new ApiError(
                'no_interaction',
                interactionId
                    ? `No interaction with id "${interactionId}"`
                    : 'No active interaction',
            )
        }
        return item
    }

    const getRemoteAssistSessionPin = (item: Item): string | undefined => {
        const globalInteractionId = getGlobalInteractionId(item)
        return globalInteractionId
            ? remoteAssistState.items[globalInteractionId]?.pin
            : undefined
    }

    const getRemoteAssistSessionId = (item: Item): string | undefined => {
        const globalInteractionId = getGlobalInteractionId(item)
        return globalInteractionId
            ? remoteAssistState.items[globalInteractionId]?.sessionId
            : undefined
    }

    const requireLogin = () => {
        if (!sessionState.isLoggedIn) {
            throw new ApiError('not_logged_in', 'You are not logged in')
        }
    }

    const requirePrivileges = (...privileges: AgentPrivilegeType[]) => {
        if (privileges.some(privilege => !sessionState.privileges[privilege])) {
            throw new ApiError('not_enough_privileges', 'Not allowed')
        }
    }

    const onAgentStateChange = (newAgentState: AgentStateData) => {
        if (
            agentState.state !== newAgentState.state ||
            agentState.notReadyReason !== newAgentState.notReadyReason ||
            !!agentState.reservedForIncomingInteraction !== !!newAgentState.reservedForIncomingInteraction
        ) {
            integrationApiConnector.dispatch.agentStateChange({
                state: !newAgentState.state ? 'supervising' : newAgentState.state,
                reservedForIncomingInteraction: !!newAgentState.reservedForIncomingInteraction,
                notReadyReason: newAgentState.notReadyReason,
            })
        }
    }
    const agentStateSubscription = sharedConnectors.agentStateConnector.dispenser.subscribe({
        onInitialState: newState => {
            onAgentStateChange(newState)
            agentState = {...newState}
        },
        onChange: stateUpdate => {
            const newState = {...agentState, ...stateUpdate}
            onAgentStateChange(newState)
            agentState = newState
        },
    })

    const onInteractionStateChange = (newInteractionState: Partial<InteractionStateData>) => {
        if (interactionState.activeItemId && !newInteractionState.activeItemId) {
            integrationApiConnector.dispatch.activeInteractionSwitched(null)
        }
    }
    const onInteractionItemNewOrChange = (item: Item) => {
        try {
            if (item.state === 'queued') {
                return
            }
            if (apiDispatchedItems[item.id]) {
                const oldItem = interactionState.items.find(i => i.id === item.id)
                const oldInteractionData = oldItem && convertItem(sessionState.serverOrigin, oldItem, formatFullName)
                const newInteractionData = convertItem(sessionState.serverOrigin, item, formatFullName)
                if (!isEqual(oldInteractionData, newInteractionData)) {
                    integrationApiConnector.dispatch.interactionStateChange(newInteractionData)
                }
            } else {
                apiDispatchedItems[item.id] = true
                newUnknownItems[item.id] = true

                integrationApiConnector.dispatch.newInteraction(convertItem(sessionState.serverOrigin, item, formatFullName))
            }
            if (item.mediaType && item.mediaType !== 'unknown' && newUnknownItems[item.id]) {
                const associatedCrmRecords = apiState.pendingAssociatedObjects[item.mediaType as string]?.objects
                if (associatedCrmRecords) {
                    integrationApiConnector.dispatch.removePendingAssociatedObjects(item.mediaType as string)
                    associatedCrmRecords.forEach(crmRecord => {
                        interactionStateMethods.addAssociatedObject(item.id, crmRecord, {force: true, primary: false})
                    })
                }
                delete newUnknownItems[item.id]
            }
        } catch (err) {
            bpLog.error('onInteractionItemChange error:', getErrorMessage(err))
        }
    }
    const interactionStateSubscription = sharedConnectors.interactionStateConnector.dispenser.subscribe({
        onInitialState: newState => {
            newUnknownItems = {}
            apiDispatchedItems = {}
            transferredDataItems = {}
            newState.items.forEach(item => onInteractionItemNewOrChange(item))
            onInteractionStateChange(newState)
            interactionState = {...newState}
        },
        onChange: stateUpdate => {
            onInteractionStateChange(stateUpdate)
            interactionState = {...interactionState, ...stateUpdate}
        },
        onNewItem: item => {
            onInteractionItemNewOrChange(item)
        },
        onItemChanged: item => {
            onInteractionItemNewOrChange(item)
        },
        onItemRemoved: item => {
            try {
                delete newUnknownItems[item.id]
                delete apiDispatchedItems[item.id]
                delete transferredDataItems[item.id]

                const interactionData = convertItem(sessionState.serverOrigin, item, formatFullName)
                integrationApiConnector.dispatch.interactionRemoved(interactionData)

                const activityData = convertActivity(apiState.integrationKey ?? '', item)
                integrationApiConnector.dispatch.saveActivityRecord(activityData)
            } catch (err) {
                bpLog.error('onItemRemoved error:', getErrorMessage(err))
            }
        },
        onActivateItemAttempt: activeItem => {
            const {integrationKey} = apiState
            if (integrationKey) {
                const mainItem = getMainItem(interactionState.items, activeItem)
                const prevMainItem = getMainItem(
                    interactionState.items,
                    interactionState.items.find(item => {
                        return item.id === interactionState.activeItemId
                    }),
                )
                if (
                    mainItem && mainItem.activeCrmScreen &&
                        mainItem.activeCrmScreen[integrationKey] &&
                        mainItem.id !== prevMainItem?.id
                ) {
                    integrationApiConnector.dispatch.showScreen(mainItem.activeCrmScreen[integrationKey], activeItem.id)
                }
            }
        },
        onActiveItemChanged: activeItem => {
            integrationApiConnector.dispatch.activeInteractionSwitched(activeItem.id)
        },
    })

    const servicesDispositionsStateSubscription = sharedConnectors.servicesDispositionsStateConnector.dispenser.subscribe({
        onInitialState: newState => {
            servicesDispositionsState = {...newState}
        },
        onChange: stateUpdate => {
            servicesDispositionsState = {...servicesDispositionsState, ...stateUpdate}
        },
    })

    const didNumbersStateSubscription = sharedConnectors.didStateConnector.dispenser.subscribe({
        onInitialState: newState => {
            didNumbersState = {...newState}
        },
        onChange: stateUpdate => {
            didNumbersState = {...didNumbersState, ...stateUpdate}
        },
    })

    const onSessionStateChange = (prevSessionState: SessionData, newSessionState: SessionData) => {
        if (newSessionState.phoneDeviceCapabilities) {
            const oldCaps = convertPhoneCapabilities(prevSessionState.phoneDeviceCapabilities)
            const newCaps = convertPhoneCapabilities(newSessionState.phoneDeviceCapabilities)
            if (
                oldCaps.canRetrieve !== newCaps.canRetrieve ||
                oldCaps.canHold !== newCaps.canHold ||
                oldCaps.canDial !== newCaps.canDial ||
                oldCaps.canAnswer !== newCaps.canAnswer
            ) {
                integrationApiConnector.dispatch.phoneCapabilitiesChange(newCaps)
            }
        }
        if (!prevSessionState.loginError && newSessionState.loginError) {
            apiRequestTracker.rejectAllPendingRequests('onLogin', newSessionState.loginError)
        }
    }
    const sessionStateSubscription = sessionConnector.dispenser.subscribe({
        onInitialState: newState => {
            if (newState.isLoggedIn) {
                apiRequestTracker.resolveAllPendingRequests('onLogin', undefined)
                integrationApiConnector.dispatch.login({
                    isLoggedIn: true,
                    agentId: newState.userId!,
                    username: newState.loginId!,
                    sessionId: newState.sessionId!,
                })
            }
            const prevState = sessionState
            onSessionStateChange(prevState, newState)
            sessionState = {...newState}
        },
        onChange: stateUpdate => {
            const prevState = sessionState
            sessionState = {...sessionState, ...stateUpdate}
            onSessionStateChange(prevState, sessionState)
        },
        onLogin: session => {
            apiRequestTracker.resolveAllPendingRequests('onLogin', undefined)
            integrationApiConnector.dispatch.login({
                isLoggedIn: true,
                agentId: session.userId!,
                username: session.loginId!,
                sessionId: session.sessionId!,
            })
        },
        onLogout: () => {
            integrationApiConnector.dispatch.logout()
        },
        onServerError: (source, code, message) => {
            integrationApiConnector.dispatch.serverError(
                convertServerError(source, code, message),
            )
        },
        onBadCallQualityAlert: (callId: string, alert: boolean) => {
            integrationApiConnector.dispatch.callAudioQualityAlert(callId, alert)
        },
    })

    const timezonesStateSubscription = sharedConnectors.timezonesStateConnector.dispenser.subscribe({
        onInitialState: newState => {
            timezonesState = {...newState}
        },
        onChange: stateUpdate => {
            timezonesState = {...timezonesState, ...stateUpdate}
        },
    })

    const directoryStateSubscription = sharedConnectors.directoryStateConnector.dispenser.subscribe({
        onInitialState: newState => {
            directoryState = {...newState}
        },
        onChange: stateUpdate => {
            directoryState = {...directoryState, ...stateUpdate}
        },
    })

    const onBpClientStateChange = (newBpClientState: BPClientStateData) => {
        const oldSRState = convertScreenRecordingState(bpClientState.screenRecording)
        const newSRState = convertScreenRecordingState(newBpClientState.screenRecording)
        if (
            oldSRState.allowed !== newSRState.allowed ||
            oldSRState.active !== newSRState.active ||
            oldSRState.muted !== newSRState.muted
        ) {
            integrationApiConnector.dispatch.screenRecordingStateChange(newSRState)
        }
        if (
            bpClientState.softphone.ready !== newBpClientState.softphone.ready ||
            bpClientState.softphone.error !== newBpClientState.softphone.error
        ) {
            integrationApiConnector.dispatch.softphoneStatusChange(
                newBpClientState.softphone.ready,
                newBpClientState.softphone.error,
            )
        }
        if (
            bpClientState.audioDevice.input !== newBpClientState.audioDevice.input ||
            bpClientState.audioDevice.output !== newBpClientState.audioDevice.output
        ) {
            integrationApiConnector.dispatch.audioDeviceChange(
                newBpClientState.audioDevice.input,
                newBpClientState.audioDevice.output,
            )
        }
    }
    const bpClientStateSubscription = sharedConnectors.bpClientStateConnector.dispenser.subscribe({
        onInitialState: newState => {
            onBpClientStateChange(newState)
            bpClientState = {...newState}
        },
        onChange: stateUpdate => {
            const newState = {...bpClientState, ...stateUpdate}
            onBpClientStateChange(newState)
            bpClientState = newState
        },
    })

    const remoteAssistStateSubscription = sharedConnectors.remoteAssistStateConnector.dispenser.subscribe({
        onInitialState: newState => {
            remoteAssistState = {...newState}
        },
        onChange: stateUpdate => {
            remoteAssistState = {...remoteAssistState, ...stateUpdate}
        },
    })

    const onIntegrationApiStateChange = (newApiState: IntegrationApiStateData) => {
        if (apiState.isAvailable !== newApiState.isAvailable) {
            currentTabDataProvider.setIntegrationApiActive(newApiState.isAvailable)
        }
    }
    const integrationApiOriginalSubscription = integrationApiConnector.dispenser.subscribe({
        onInitialState: newState => {
            onIntegrationApiStateChange(newState)
            apiState = {...newState}
        },
        onChange: stateUpdate => {
            const newState = {...apiState, ...stateUpdate}
            onIntegrationApiStateChange(newState)
            apiState = newState
        },
    })

    const onGetConfig = () => {
        switch (apiState.integrationKey) {
            case 'salesforce':
                return Promise.resolve({clickAction: 'call'})
            default:
                return Promise.resolve({})
        }
    }

    const onStartCall = (phonenumber: string, initialObjects: InteractionAssociatedObjectData[]): Promise<ApiInitiateResult> => {
        requireLogin()

        const adapterParams = apiState.adapterUrlParams
        const deferredDial = adapterParams && adapterParams['deferredDial']

        const associatedObjects = initialObjects.map(crmObject => ({
            ...crmObject,
            source: apiState.integrationKey || '',
        }))

        if (deferredDial && deferredDial !== '0' && ref.current.deferredDial && associatedObjects.length) {
            integrationApiConnector.dispatch.setDeferredDialData({
                number: phonenumber,
                objects: associatedObjects,
            })
            ref.current.deferredDial(phonenumber)

            return Promise.resolve({id: null})

        } else {

            const errors = validate('call', phonenumber)
            if (errors.length) {
                throw new ApiError(errors[0].name, errors[0].message)
            }
            ref.current.showInteractionPage()
            return interactionStateMethods.initiateCall({
                phone: phonenumber,
                accessNumberId: ref.current.getDIDNumber(),
                serviceId: ref.current.getService(),
                associatedRecords: associatedObjects,
                origination: 'integration-api',
            }).then(result => ({
                id: result.id,
                error: result.error?.message,
            }))
        }
    }

    const onStartChat = (
        channel: ChatChannelType,
        address: string,
        initialObjects: InteractionAssociatedObjectData[],
    ): Promise<ApiInitiateResult> => {
        requireLogin()

        const adapterParams = apiState.adapterUrlParams
        const deferredDial = adapterParams && adapterParams['deferredDial']

        const associatedObjects = initialObjects.map(crmObject => ({
            ...crmObject,
            source: apiState.integrationKey || '',
        }))

        if (deferredDial && deferredDial !== '0' && ref.current.deferredDial && associatedObjects.length) {
            integrationApiConnector.dispatch.setDeferredDialData({
                number: address,
                objects: associatedObjects,
            })
            ref.current.deferredDial(address)

            return Promise.resolve({id: null})

        } else {
            if (channel === 'sms') {
                const errors = validate('smsChat', address)
                if (errors.length) {
                    throw new ApiError(errors[0].name, errors[0].message)
                }
            }
            const promise = interactionStateMethods.initiateChat({
                address,
                channel,
                serviceId: ref.current.getService()!, // service is checked by validation procedure
                accessNumberId: ref.current.getDIDNumber(),
                origination: 'integration-api',
            })
            if (associatedObjects) {
                integrationApiConnector.dispatch.addPendingAssociatedObjects('chat', associatedObjects)
            }
            ref.current.showInteractionPage()
            return promise.then(result => ({
                id: result.id,
                error: result.error?.message,
            }))
        }
    }

    const onStartEmail = (_email: string, _initialObjects: InteractionAssociatedObjectData[]): Promise<ApiInitiateResult> => {
        requireLogin()
        throw new ApiError('api_method_not_implemented', 'Email interactions is not implemented')
        // if (initialObject) {
        //     jsApi.addPendingAssociatedObjects('email', initialObject)
        // }
    }

    const onGetAgentState = () => {
        requireLogin()
        return Promise.resolve({
            state: !agentState.state ? 'supervising' as const : agentState.state,
            reservedForIncomingInteraction: !!agentState.reservedForIncomingInteraction,
            notReadyReason: agentState.notReadyReason,
        })
    }

    const onSetAgentState = (state: ApiAgentState, notReadyReason?: string) => {
        requireLogin()
        requirePrivileges('SYS_RECEIVE_AUTOMATICALLY_DISTRIBUTED_CALLS')
        if (state === 'not_ready' && !notReadyReason) {
            throw new ApiError('invalid_args', 'not_ready state requires notReadyReason to be present')
        }
        if (state === 'not_ready' && notReadyReason && !agentState.notReadyReasons?.includes(notReadyReason)) {
            throw new ApiError('invalid_args', `Unknown not ready reason: "${notReadyReason}"`)
        }
        agentStateMethods.setState({
            state: state === 'supervising' ? '' : state,
            reason: notReadyReason,
        })
        return Promise.resolve()
    }

    const onSwitchActiveInteraction = (interactionId: string) => {
        requireLogin()
        getItem(interactionId)
        interactionStateMethods.activateItem(interactionId)
        return Promise.resolve()
    }

    const onSetVariable = (key: string, value: string, interactionId?: string) => {
        requireLogin()
        const item = getItem(interactionId)
        interactionStateMethods.setScenarioVariables(
            {[key]: value},
            getInteractionStepId(item),
        )
        return Promise.resolve()
    }

    const onGetVariables = (keys: string[], interactionId?: string): Promise<Record<string, string>> => {
        requireLogin()
        const item = getItem(interactionId)

        return interactionStateMethods.getScenarioVariables({
            itemId: item.id,
            variableNames: keys,
        })
    }

    const onLeaveInteraction = (interactionId?: string) => {
        requireLogin()
        const item = getItem(interactionId)
        const state = item ? item.state : ''
        if (state === 'delivered' || state === 'hold' || state === 'delivery_pending' || state === 'IVR') {
            interactionStateMethods.disconnectItem(item.id)
        } else {
            throw new ApiError(
                'not_suitable_state',
                `The interaction is in the '${state}' state and cannot be terminated.`,
            )
        }
        return Promise.resolve()
    }

    const onCompleteInteraction = (interactionId?: string) => {
        requireLogin()
        const item = getItem(interactionId)
        const state = item?.state || ''
        const internalChatData = item?.internalChatData || ''
        const serviceId = item.scenarioData?.serviceId || item.workitemData?.serviceId
        const currentService = serviceId ? servicesDispositionsState?.services[serviceId] : undefined
        const requireDisposition = currentService?.requireDispositions
        const selectedDisposition = item.dispositionData?.disposition
        const rescheduleDate = item.dispositionData?.disposition?.rescheduleFrom

        if (requireDisposition && !selectedDisposition) {
            throw new ApiError(
                'no_disposition',
                'Cannot complete interaction without a set disposition. It is required.',
            )
        }

        if (rescheduleDate && isBefore(rescheduleDate)) {
            throw new ApiError(
                'invalid_request',
                'Reschedule date is in the past.',
            )
        }

        if (internalChatData) {
            interactionStateMethods.agentChatClose(item.id)
        }
        if (
            state === 'wrap_up' ||
            state === 'wrap_up_hold' ||
            (item.callData && item.callData.callState === 'failed') ||
            (item.mediaType === 'workitem' && state === 'delivered')
        ) {
            interactionStateMethods.completeItem(item.id)
        } else {
            throw new ApiError(
                'not_suitable_state',
                `The interaction is in the "${state}" state and cannot be completed.`,
            )
        }
        return Promise.resolve()
    }

    const onLeaveAndCompleteInteraction = (disposition?: string, note?: string, interactionId?: string) => {
        requireLogin()
        const item = getItem(interactionId)
        if (!(['delivered', 'hold', 'delivery_pending', 'IVR'] as ItemState[]).includes(item.state!)) {
            throw new ApiError(
                'not_suitable_state',
                `The interaction is in the '${item.state}' state and cannot be terminated.`,
            )
        }
        const serviceId = item.scenarioData?.serviceId || item.workitemData?.serviceId
        const serviceData = Object.values(servicesDispositionsState.services).find(service => service.id === serviceId)
        if (!serviceData) {
            throw new ApiError(
                'object_not_found',
                `Unable to get service (id: "${serviceId}") info for requested interaction (id: "${item.id}")`,
            )
        }
        if (serviceData.requireDispositions && !disposition) {
            throw new ApiError(
                'no_disposition',
                `Interaction with service "${serviceData.name}" requires disposition`,
            )
        }
        if (disposition) {
            const dispositions = Object.values(servicesDispositionsState?.dispositions[serviceData.id] ?? {})

            const requestedDisposition = dispositions.find(disp => {
                return [disp.id, disp.code, disp.name].includes(disposition)
            })

            if (!requestedDisposition) {
                throw new ApiError(
                    'object_not_found',
                    `There is no disposition with id/code/name "${disposition}" for service "${serviceData.name}"`,
                )
            }
            interactionStateMethods.setDisposition({
                itemId: item.id,
                disposition: {
                    id: requestedDisposition.id,
                    name: requestedDisposition.name,
                },
            })
        }
        if (note) {
            interactionStateMethods.addNote({itemId: item.id, note})
        }
        setTimeout(() => {
            interactionStateMethods.disconnectItem(item.id)
            setTimeout(() => {
                interactionStateMethods.completeItem(item.id)
            })
        })
        return Promise.resolve()
    }

    const onGetServicesList = () => {
        requireLogin()
        const storedServices = Object.values(servicesDispositionsState.services)
        if (storedServices.length) {
            servicesDispositionsStateMethods.requestServices()
            return Promise.resolve(storedServices.map(service => convertService(service)))
        }
        return servicesDispositionsStateMethods.requestServices().then(services => {
            return services?.map(service => convertService(service)) ?? []
        })
    }

    const onGetService = () => {
        requireLogin()
        const serviceId = ref.current.getService()
        if (!serviceId) {
            return Promise.resolve(null)
        }
        const serviceData = servicesDispositionsState.services[serviceId]
        if (!serviceData) {
            throw new ApiError(
                'object_not_found',
                'Unable to resolve selected service. Try to refresh services list data.',
            )
        }
        return Promise.resolve(convertService(serviceData))
    }

    const onSetService = (service: string | null) => {
        requireLogin()
        if (service === null) {
            ref.current.setService()
            return Promise.resolve()
        }
        const services = Object.values(servicesDispositionsState.services)
        const serviceById = services.find(srv => srv.id === service)
        if (serviceById) {
            ref.current.setService(serviceById.id)
        } else {
            const serviceByName = services.find(srv => srv.name === service)
            if (!serviceByName) {
                throw new ApiError(
                    'object_not_found',
                    `There is no service with id/name "${service}"`,
                )
            }
            ref.current.setService(serviceByName.id)
        }
        return Promise.resolve()
    }

    const onGetDIDNumbersList = () => {
        requireLogin()
        const didNumbers = Object.values(didNumbersState.didNumbers)
        if (didNumbers.length) {
            didNumbersStateMethods.requestDIDNumbers(undefined)
            return Promise.resolve(didNumbers.map(didNumber => convertDIDNumber(didNumber)))
        }
        return didNumbersStateMethods.requestDIDNumbers(sessionState.phoneNumber)
            .then(numbers => {
                return numbers?.map(didNumber => convertDIDNumber(didNumber)) ?? []
            })
    }

    const onGetDIDNumber = () => {
        requireLogin()
        const didNumberId = ref.current.getDIDNumber()
        if (!didNumberId) {
            return Promise.resolve(null)
        }
        const didNumberData = didNumbersState.didNumbers[didNumberId]
        if (!didNumberData) {
            throw new ApiError(
                'object_not_found',
                'Unable to resolve selected did number. Try to refresh did numbers list data.',
            )
        }
        return Promise.resolve(convertDIDNumber(didNumberData))
    }

    const onSetDIDNumber = (did: string | null) => {
        requireLogin()
        if (did === null) {
            ref.current.setDIDNumber()
            return Promise.resolve()
        }
        const didNumbers = Object.values(didNumbersState.didNumbers)
        const didNumberById = didNumbers.find(didNumber => didNumber.id === did)
        if (didNumberById) {
            ref.current.setDIDNumber(didNumberById.id)
        } else {
            const didNumberByNumber = didNumbers.find(didNumber => {
                return didNumber.externalNumber.toString() === did
            })
            if (!didNumberByNumber) {
                throw new ApiError(
                    'object_not_found',
                    `There is no DID number with id/number "${did}"`,
                )
            }
            ref.current.setDIDNumber(didNumberByNumber.id)
        }
        return Promise.resolve()
    }

    const onGetDispositionsList = ({service, interactionId}: DispositionTarget) => {
        requireLogin()
        let serviceId: string | undefined = service
        if (!service) {
            const item = getItem(interactionId)
            serviceId = item.scenarioData?.serviceId || item.workitemData?.serviceId
        }
        if (!serviceId) {
            throw new ApiError(
                'object_not_found',
                'Service associated with active interaction not found',
            )
        }
        const services = Object.values(servicesDispositionsState.services)
        if (services.length) {
            const serviceData = services.find(srv => {
                return srv.id === serviceId || srv.name === serviceId
            })
            if (!serviceData) {
                throw new ApiError(
                    'object_not_found',
                    `There is no service with id/name "${service}"`,
                )
            }
            serviceId = serviceData.id
        } else {
            servicesDispositionsStateMethods.requestServices()
        }
        const dispositions = Object.values(servicesDispositionsState.dispositions[serviceId] || {})
        if (dispositions.length) {
            servicesDispositionsStateMethods.requestServiceDispositions(serviceId)
            return Promise.resolve(dispositions.map(disposition => convertDisposition(disposition)))
        }
        return servicesDispositionsStateMethods.requestServiceDispositions(serviceId).then(disps => {
            return disps?.map(disposition => convertDisposition(disposition)) ?? []
        })
    }

    const onSetDisposition = (disposition: string, interactionId?: string) => {
        requireLogin()
        const item = getItem(interactionId)
        const serviceId = item.scenarioData?.serviceId || item.workitemData?.serviceId
        const serviceData = Object.values(servicesDispositionsState?.services ?? {}).find(srv => {
            return srv.id === serviceId
        })
        if (!serviceData) {
            throw new ApiError(
                'object_not_found',
                `Unable to get service (id: "${serviceId}") info for requested interaction (id: "${item.id}")`,
            )
        }
        const dispositions = Object.values(servicesDispositionsState?.dispositions[serviceData.id] ?? {})

        const requestedDisposition = dispositions.find(disp => {
            return [disp.id, disp.code, disp.name].includes(disposition)
        })

        if (!requestedDisposition) {
            throw new ApiError(
                'object_not_found',
                `There is no disposition with id/code/name "${disposition}" for service "${serviceData.name}"`,
            )
        }
        interactionStateMethods.setDisposition({
            itemId: item.id,
            disposition: {
                id: requestedDisposition.id,
                name: requestedDisposition.name,
            },
        })
        return Promise.resolve()
    }

    const onAddInteractionAssociatedObject = (object: InteractionAssociatedObjectData, interactionId?: string) => {
        requireLogin()
        const item = getItem(interactionId)
        const dataWithIntegrationKey = {
            ...object,
            source: apiState.integrationKey || '',
        }
        interactionStateMethods.addAssociatedObject(item.id, dataWithIntegrationKey)
        return Promise.resolve()
    }

    const onSetInteractionActiveScreen = (screen: InteractionScreen, interactionId?: string) => {
        requireLogin()
        const integrationKey = apiState.integrationKey
        if (integrationKey === null) {
            return Promise.resolve()
        }
        const item = getItem(interactionId)
        interactionStateMethods.setActiveCrmScreen(item.id, integrationKey, screen)
        return Promise.resolve()
    }

    const onConsultCall = (phone: string) => {
        requireLogin()
        const adapterParams = apiState.adapterUrlParams
        const deferredDial = adapterParams && adapterParams['deferredDial']
        if (deferredDial && deferredDial !== '0' && ref.current.deferredDial) {
            ref.current.deferredDial(phone)
        } else {
            const errors = validate('call', phone)
            if (errors.length) {
                throw new ApiError(errors[0].name, errors[0].message)
            }
            interactionStateMethods.initiateCall({
                phone,
                serviceId: ref.current.getService(),
                accessNumberId: ref.current.getDIDNumber(),
                origination: 'integration-api',
            })
        }
        return Promise.resolve()
    }

    const onBlindTransfer = (phone: string, customTransferData?: CustomTransferData, interactionId?: string) => {
        requireLogin()
        requirePrivileges('CAN_TRANSFER')

        const item = getItem(interactionId)
        const raSessionPin = getRemoteAssistSessionPin(item)
        const raSessionId = getRemoteAssistSessionId(item)

        transferredDataItems[item.id] = true
        interactionStateMethods.updateScreenPopData({
            itemId: item.id,
            customData: customTransferData,
            raSessionPin,
            raSessionId,
        })
        interactionStateMethods.blindTransfer({item, phone})
        return Promise.resolve()
    }

    const onTransfer = (customTransferData?: CustomTransferData) => {
        requireLogin()
        requirePrivileges('CAN_TRANSFER')

        const activeItemId = interactionState.activeItemId
        const activeItem = getItem(activeItemId)
        const consultMainItemId = activeItem.displayData.isConsultCall
        const mainItem = consultMainItemId ? getItem(consultMainItemId) : activeItem

        const raSessionPin = getRemoteAssistSessionPin(mainItem)
        const raSessionId = getRemoteAssistSessionId(mainItem)

        transferredDataItems[mainItem.id] = true
        interactionStateMethods.updateScreenPopData({
            itemId: mainItem.id,
            customData: customTransferData,
            raSessionPin,
            raSessionId,
        })
        interactionStateMethods.completeTransfer()

        return Promise.resolve()
    }

    const onAddNote = (note: string, interactionId?: string) => {
        requireLogin()
        const item = getItem(interactionId)
        interactionStateMethods.addNote({itemId: item.id, note})
        return Promise.resolve()
    }

    const onUpdateNote = (note: string, interactionId?: string) => {
        requireLogin()
        const item = getItem(interactionId)
        interactionStateMethods.updateNote({itemId: item.id, note})
        return Promise.resolve()
    }

    const onReplaceNote = (note: string, interactionId?: string) => {
        requireLogin()
        const item = getItem(interactionId)
        interactionStateMethods.replaceNote({itemId: item.id, note})
        return Promise.resolve()
    }

    const onLogin = ({username, password, tenant}: LoginData, force = false) => {
        if (sessionState.isLoggedIn) {
            throw new ApiError('not_suitable_state', 'You are already in logged in state')
        }
        if (sessionState.isAuthorizing) {
            throw new ApiError('not_suitable_state', 'Login procedure in process now')
        }
        if (force) {
            sessionConnector.dispatch.forceLogin()
        } else {
            sessionConnector.dispatch.login({
                loginId: username,
                password,
                domain: tenant || location.host,
            })
        }
        return apiRequestTracker.newPendingRequest(
            'onLogin',
            [{username, password, tenant}, force],
        )
    }

    const onLogout = () => {
        requireLogin()
        sessionConnector.dispatch.logout()
        return Promise.resolve()
    }

    const onSetRescheduleWindow = (rescheduleData: RescheduleData, interactionId?: string) => {
        requireLogin()
        const item = getItem(interactionId)
        const timezoneId = rescheduleData.timezoneCode
        if (timezoneId) {
            const timezone = timezonesState.allTimezones.find(tz => tz.id === timezoneId)
            if (!timezone) {
                throw new ApiError(
                    'object_not_found',
                    `Unknown timezone with code "${timezoneId}"`,
                )
            }
        }
        interactionStateMethods.setDispositionReschedule(item.id, {
            rescheduleTimezone: timezoneId,
            reschedulePhone: rescheduleData.numberToDial,
            rescheduleFrom: rescheduleData.fromTime,
            rescheduleTo: rescheduleData.untilTime,
        })
        return Promise.resolve()
    }

    const onGetPhoneDevicesList = () => {
        requireLogin()
        return Promise.resolve(typedKeys(sessionState.availablePhoneTypes))
    }

    const onGetPhoneDevice = () => {
        requireLogin()
        const selectedPhoneDevice = sessionState.phoneType
        if (!selectedPhoneDevice) {
            throw new ApiError(
                'logged_without_phone',
                'Selected phone is undefined',
            )
        }
        return Promise.resolve({
            type: selectedPhoneDevice,
            phone: ['phone_type_external', 'phone_type_hard_custom'].includes(selectedPhoneDevice)
                ? sessionState.phoneNumber
                : undefined,
        })
    }

    const onSetPhoneDevice = ({type, phone}: PhoneDeviceData) => {
        requireLogin()
        const availablePhoneDevices = Object.keys(sessionState.availablePhoneTypes)
        if (!availablePhoneDevices.includes(type)) {
            throw new ApiError(
                'banned',
                `Phone device "${type}" is not available`,
            )
        }
        sessionConnector.dispatch.submitPhoneTypeSelection({
            phoneType: type,
            phoneNumber: phone,
        })
        return Promise.resolve()
    }

    const onGetLoginState = () => {
        return Promise.resolve({
            isLoggedIn: sessionState.isLoggedIn || false,
            agentId: sessionState.userId,
            username: sessionState.loginId,
            sessionId: sessionState.sessionId,
        })
    }

    const onSendDtmf = (dtmf: string, interactionId?: string) => {
        requireLogin()
        const item = getItem(interactionId)

        if (item.mediaType !== 'voice' || !item.callData?.callId) {
            throw new ApiError(
                'not_suitable_state',
                'Dtmf is applicable only for voice calls, ' +
                    `but the interaction "${item.id}" is of a type "${item.mediaType}"`,
            )
        }
        if (item.state !== 'delivered') {
            throw new ApiError(
                'not_suitable_state',
                'Dtmf is applicable only for active calls, ' +
                    `but the interaction "${item.id}" is in the state "${item.state}"`,
            )
        }
        interactionStateMethods.sendDtmf({
            dtmf,
            callId: item.callData.callId,
        })
        return Promise.resolve()
    }

    const onAcceptInteraction = (interactionId?: string) => {
        requireLogin()
        const item = getItem(interactionId)
        if (item.state !== 'delivery_pending') {
            throw new ApiError(
                'not_suitable_state',
                'To be accepted interaction must be in a "delivery_pending" state, ' +
                    `but the interaction "${item.id}" is in the "${item.state}" state`,
            )
        }
        interactionStateMethods.acceptItem(item.id)
        return Promise.resolve()
    }

    const onRejectInteraction = (interactionId?: string) => {
        requireLogin()
        const item = getItem(interactionId)
        if (item.state !== 'delivery_pending') {
            throw new ApiError(
                'not_suitable_state',
                'To be rejected interaction must be in a "delivery_pending" state, ' +
                    `but the interaction "${item.id}" is in the "${item.state}" state`,
            )
        }
        interactionStateMethods.rejectItem(item.id)
        return Promise.resolve()
    }

    const onInviteToCallConference = (phone: string, customTransferData?: CustomTransferData, interactionId?: string) => {
        requireLogin()
        requirePrivileges('CAN_TRANSFER')

        const item = getItem(interactionId)
        const raSessionPin = getRemoteAssistSessionPin(item)

        transferredDataItems[item.id] = true
        interactionStateMethods.updateScreenPopData({
            itemId: item.id,
            customData: customTransferData,
            raSessionPin,
        })
        interactionStateMethods.singleStepConference({
            phone,
            serviceId: ref.current.getService(),
            accessNumberId: ref.current.getDIDNumber(),
        })
        return Promise.resolve()
    }

    const onRemoveFromCallConference = (partyId: string, interactionId?: string) => {
        requireLogin()
        const item = getItem(interactionId)
        const partyForRemoval = (item.parties || []).find(party => party.id === partyId)
        if (!partyForRemoval) {
            throw new ApiError(
                'object_not_found',
                `Could not find party "${partyId}" for the interaction "${item.id}"`,
            )
        }
        interactionStateMethods.removeCallParty(partyForRemoval)
        return Promise.resolve()
    }

    const onDestroyCallConference = (interactionId?: string) => {
        requireLogin()
        const item = getItem(interactionId)
        interactionStateMethods.destroyCallConference(item.id)
        return Promise.resolve()
    }

    const onInviteToChatConference = (partyId: string, interactionId?: string) => {
        requireLogin()
        const item = getItem(interactionId)
        if (!item.scenarioData?.interactionStepId) {
            throw new ApiError(
                'object_not_found',
                `Could not find interaction step id in the interaction "${item.id}"`,
            )
        }
        interactionChatStateMethods.inviteToChat({itemId: item.scenarioData.interactionStepId, partyId})
    }

    const onRemoveFromChatConference = (partyId: string, interactionId?: string) => {
        requireLogin()
        const item = getItem(interactionId)
        if (!item.scenarioData?.interactionStepId) {
            throw new ApiError(
                'object_not_found',
                `Could not find interaction step id in the interaction "${item.id}"`,
            )
        }

        const partyForRemoval = (item.chatParties || []).find(party => party.party_id === partyId)
        if (!partyForRemoval) {
            throw new ApiError(
                'object_not_found',
                `Could not find chat party "${partyId}" for the interaction "${item.id}"`,
            )
        }
        interactionChatStateMethods.removePartyFromChat({itemId: item.scenarioData.interactionStepId, partyId: partyForRemoval.party_id})
    }

    const onMergeAllCallsIntoConference = (customTransferData?: CustomTransferData) => {
        requireLogin()
        getItem()
        const mainItem = interactionState.items.find(item => !!item.displayData.hasConsultCalls?.length)

        if (mainItem) {
            const raSessionPin = getRemoteAssistSessionPin(mainItem)

            transferredDataItems[mainItem.id] = true
            interactionStateMethods.updateScreenPopData({
                itemId: mainItem.id,
                customData: customTransferData,
                raSessionPin,
            })
        }
        interactionStateMethods.mergeAllCallsIntoConference()
        return Promise.resolve()
    }

    const onGetTeams = () => {
        requireLogin()
        if (directoryState.teams.length) {
            directoryStateMethods.getAgentTeams()
            return Promise.resolve(directoryState.teams.map(team => convertTeam(team)))
        }
        return directoryStateMethods.getAgentTeams().then(teams => {
            return teams.map(team => convertTeam(team))
        })
    }

    const onGetTeamMembers = (teamId: string) => {
        requireLogin()
        if (directoryState.teams.length) {
            const team = directoryState.teams.find(currTeam => currTeam.id === teamId)
            if (!team) {
                throw new ApiError(
                    'object_not_found',
                    `Team with id "${teamId}" is not found`,
                )
            }
        } else {
            directoryStateMethods.getAgentTeams()
        }
        return directoryStateMethods.requestTeamUsers(teamId).then(users => {
            return users.map(user => convertTeamMember(user))
        })
    }

    const onSetCallHold = (state: boolean, interactionId?: string) => {
        requireLogin()
        const item = getItem(interactionId)
        if (state) {
            interactionStateMethods.holdItem(item.id)
        } else {
            interactionStateMethods.resumeItem(item.id)
        }
        return Promise.resolve()
    }

    const onSetCallRecording = (state: boolean, interactionId?: string) => {
        requireLogin()
        const item = getItem(interactionId)
        if (state) {
            requirePrivileges('CAN_START_RECORDING')
            interactionStateMethods.recordCall(item.id)
        } else {
            requirePrivileges('CAN_STOP_RECORDING')
            interactionStateMethods.stopRecordingCall(item.id)
        }
        return Promise.resolve()
    }

    const onSetCallMute = (state: boolean, interactionId?: string) => {
        requireLogin()
        const item = getItem(interactionId)
        if (state) {
            interactionStateMethods.muteItem(item.id)
        } else {
            interactionStateMethods.unmuteItem(item.id)
        }
        return Promise.resolve()
    }

    const onSetScreenRecordingMute = (state: boolean) => {
        requireLogin()
        if (!bpClientState.screenRecording.vncInit) {
            throw new ApiError(
                'banned',
                'Screen Recording is not allowed for the current user',
            )
        }
        bpClientStateMethods.setScreenRecordingMute(state)
        return Promise.resolve()
    }

    const onGetScreenRecordingState = () => {
        requireLogin()
        return Promise.resolve(convertScreenRecordingState(bpClientState.screenRecording))
    }

    const onSendChatMessage = (message: string, interactionId?: string) => {
        requireLogin()
        const item = getItem(interactionId)
        if (item.itemKind === 'scenario' && item.mediaType === 'chat') {

            const channel = item.externalChatData?.channel || 'sms'
            const errors = getMessageErrorsRaw({options: {message, channel}})
                .map(err => adErrorToApiError[err])

            if (errors.length) {
                throw new ApiError(errors[0].name, errors[0].message)
            }

            const itemId = item.scenarioData?.interactionStepId
            if (itemId) {
                interactionChatStateMethods.sendMessage({
                    text: message,
                    type: 'message',
                    itemId,
                    channel: item.externalChatData?.channel,
                })
            }
            return Promise.resolve()
        } else {
            throw new ApiError(
                'invalid_args',
                `Invalid interaction type  for sending chat service. id=${interactionId} kind=${item.itemKind} mediaType=${item.mediaType}`,
            )
        }
    }

    const onSuggestChatMessage = (message: string, allowOverwrite?: boolean, interactionId?: string) => {
        requireLogin()
        const item = getItem(interactionId)
        if (item.mediaType !== 'chat') {
            throw new ApiError('invalid_args', `Interaction isn't a chat. id=${interactionId} mediaType=${item.mediaType}`)
        }
        ref.current.setChatMessageSuggestion(message, !!allowOverwrite, item.id)
        return Promise.resolve()
    }

    const onGetAgentNotReadyReasons = () => {
        requireLogin()
        return Promise.resolve(agentState.notReadyReasons ?? [])
    }

    const onActivatePage = () => {
        currentTabDataProvider.activateTab()
    }

    const onGetInteractionsState = () => {
        requireLogin()
        return {
            activeInteractionId: interactionState.activeItemId ?? null,
            interactions: interactionState.items.map(item => {
                return convertItem(sessionState.serverOrigin, item, formatFullName)
            }),
        }
    }

    const onSetCallRecordingMute = (mute: boolean, interactionId?: string | 'all') => {
        requireLogin()

        if (interactionId === 'all') {
            interactionState.items.forEach(setRecordingMuteForItem)
        } else {
            const item = getItem(interactionId)
            setRecordingMuteForItem(item)
        }

        function setRecordingMuteForItem(item: Item) {
            if (item.displayData.isRecording) {
                if (mute && !item.displayData.isRecordingMuted) {
                    interactionStateMethods.muteCallRecording(getInteractionStepId(item))
                }
                if (!mute && item.displayData.isRecordingMuted) {
                    interactionStateMethods.unmuteCallRecording(getInteractionStepId(item))
                }
            }
        }
    }

    const integrationApiSubscription = integrationApiConnector.dispenser.subscribe({
        onGetConfig,
        onStartCall,
        onStartChat,
        onStartEmail,
        onGetAgentState,
        onSetAgentState,
        onSwitchActiveInteraction,
        onSetVariable,
        onGetVariables,
        onLeaveInteraction,
        onCompleteInteraction,
        onLeaveAndCompleteInteraction,
        onGetServicesList,
        onGetService,
        onSetService,
        onGetDIDNumbersList,
        onGetDIDNumber,
        onSetDIDNumber,
        onGetDispositionsList,
        onSetDisposition,
        onAddInteractionAssociatedObject,
        onSetInteractionActiveScreen,
        onConsultCall,
        onBlindTransfer,
        onTransfer,
        onAddNote,
        onUpdateNote,
        onReplaceNote,
        onLogin,
        onLogout,
        onSetRescheduleWindow,
        onGetPhoneDevicesList,
        onGetPhoneDevice,
        onSetPhoneDevice,
        onGetLoginState,
        onSendDtmf,
        onAcceptInteraction,
        onRejectInteraction,
        onInviteToCallConference,
        onRemoveFromCallConference,
        onDestroyCallConference,
        onInviteToChatConference,
        onRemoveFromChatConference,
        onMergeAllCallsIntoConference,
        onGetTeams,
        onGetTeamMembers,
        onSetCallHold,
        onSetCallRecording,
        onSetCallMute,
        onSetScreenRecordingMute,
        onGetScreenRecordingState,
        onSendChatMessage,
        onSuggestChatMessage,
        onGetAgentNotReadyReasons,
        onActivatePage,
        onGetInteractionsState,
        onSetCallRecordingMute,
        onSetDialCandidates: () => {},
    })

    return [
        agentStateSubscription,
        interactionStateSubscription,
        servicesDispositionsStateSubscription,
        didNumbersStateSubscription,
        sessionStateSubscription,
        timezonesStateSubscription,
        directoryStateSubscription,
        bpClientStateSubscription,
        remoteAssistStateSubscription,
        integrationApiOriginalSubscription,
        integrationApiSubscription,
    ]
}
