import React, {useMemo} from 'react'

import {useDirectoryState} from '@bpinc/ad-directory-state-context'
import {MS_TEAMS_CHATS_CATEGORY_ID} from '@bpinc/ad-directory-state-types'
import type {EmiItemStatus} from '@bpinc/ad-emi-helpers'
import type {EmiUserType} from '@bpinc/ad-transport-types'
import {ContactListItem} from '@bpinc/lib-contact-list-item-ui'
import {FavoriteChannelButton} from '@bpinc/lib-favorites-button'
import {useEvent} from '@bpinc/lib-helpers'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {assertDefined} from '@bpinc/lib-types-helpers'
import {SelectionItem} from '@bpinc/lib-ui-elements'
import type {FavoritesItem} from '@bpinc/state-favorites-types'

import {useSubscribeForFavoriteItemDetails} from '../../hooks/useSubscribeForFavoriteItemDetails'
import type {FavoritesListChannelItem} from '../../types'
import {FavoriteItemBody} from './FavoriteItemBody'
import {FavoriteControls} from './FavoriteItemControls'
import {FavoriteItemStats} from './FavoriteItemStats'
import {getUniqueChannelId} from './getUniqueChannelId'
import {useAvatar} from './useAvatar'
import type {OnClickParams} from './useItemClickHandler'


type FavoritesItemProps = FavoritesListChannelItem & {
    onClick?: (params: OnClickParams) => void
    isHighlighted?: boolean
    onDetailsClick: (item: FavoritesItem) => void
    useEmiItemStatus: (emiUserType?: EmiUserType, ownerId?: string) => EmiItemStatus
    transferMode?: 'messages' | 'calls'
    transferControlsStyle?: 'add' | 'replace'
}

export const FavoritesListItem: React.FC<FavoritesItemProps> = ({
    item,
    channel,
    media,
    transferMode,
    transferControlsStyle,
    onClick,
    onDetailsClick,
    useEmiItemStatus,
    isHighlighted,
}) => {
    const emiUsers = useDirectoryState('emiUsers')

    const {active, disableReasonMessage} = useEmiItemStatus(item.emiUserType, item.emiOwnerId)

    const relatedEMIUser = emiUsers[MS_TEAMS_CHATS_CATEGORY_ID]?.find(emiUser => emiUser.userId === item.userId)
    const anyUserId = item.userId || item.emiUserId || item.entryId

    useSubscribeForFavoriteItemDetails(item)

    const avatar = useAvatar(item)

    const title = useMemo(() => (
        <span className="flex gap-1">
            <b data-testid="name" className="inline-block truncate font-semibold body text-primary-on-primary">
                {item.displayName}
            </b>
            <FavoriteChannelButton
                item={item}
                channel={channel}
                media={media}
                className={tcn(
                    'hidden group-[[data-pseudo-hover]]/contact-item:block',
                    'group-focus-within/contact-item:block group-hover/contact-item:block',
                )}
            />
        </span>
    ), [item, channel, media])

    const body = <FavoriteItemBody channel={channel} media={media} />
    const stats = <FavoriteItemStats item={item} />
    const controls = <FavoriteControls item={item} onDetailsClick={onDetailsClick} />

    const handleClick = useEvent((event: React.MouseEvent) => {
        assertDefined(onClick)
        onClick({item, channel, media})
        event.stopPropagation()
    })

    return (
        <ContactListItem
            as={SelectionItem}
            isSelected={isHighlighted}
            transferMode={transferMode}
            transferControlsStyle={transferControlsStyle}
            userId={anyUserId}
            contactId={item.contactId}
            number={channel.number}
            avatarProps={avatar}
            title={title}
            body={body}
            stats={stats}
            controls={controls}
            userType={item.emiUserType}
            disabled={!active}
            tooltip={active ? undefined : disableReasonMessage}
            hasChat={relatedEMIUser?.hasChat}
            onClick={handleClick}
            containerProps={{
                itemId: getUniqueChannelId({item, channel, media}),
                focusOnSelection: true,
                tabIndex: 0,
                'data-testid': 'favorites-item',
            }}
        />
    )
}
