import type {FC, MouseEvent} from 'react'
import React from 'react'
import {Button} from '@servicepattern/ui'

import {tcn} from '@bpinc/lib-tailwind-classnames'


interface CompleteButtonProps {
    onClick: (e: MouseEvent) => void
    className?: string
    tooltip?: string
    children: string
}

export const CompleteInteractionButton: FC<CompleteButtonProps> = ({className, onClick, children, tooltip}) => {
    return (
        //TO DO: add clickThrottle=500 in button after refactoring button
        <Button
            variant="primary"
            onClick={onClick}
            className={tcn('h-7 rounded-xs px-3 py-1.5 !font-semibold button-sm', className)}
            data-testid="complete-button"
            tooltip={tooltip}
        >
            {children}
        </Button>
    )
}
