import React from 'react'

import {useIntegrationApiState} from '@bpinc/ad-integration-api-state-context'

import type {DialpadHandlersType} from '../../hooks/useHandlers'
import {CrmDialCandidate} from './CrmDialCandidate'


type Props = {
    dialpadNumber?: string
    callNumber: DialpadHandlersType['callNumber']
    initiateChatWith: DialpadHandlersType['initiateChatWith']
}

export const CrmDialCandidates: React.FC<Props> = ({
    dialpadNumber,
    callNumber,
    initiateChatWith,
}) => {

    const {isAvailable, dialCandidates} = useIntegrationApiState()

    if (!isAvailable || dialpadNumber || !dialCandidates.length) {
        return null
    }

    return (
        <>
            {dialCandidates.map(candidate => (
                <CrmDialCandidate
                    key={candidate.id}
                    candidate={candidate}
                    callNumber={callNumber}
                    initiateChatWith={initiateChatWith}
                />
            ))}
        </>
    )
}
