import type {FC} from 'react'
import React from 'react'

import {IconInteractionCall, IconMissedCall16} from '@bpinc/lib-jskit-ui'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {Sentiment} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import type {CallMessageProps} from './CallMessageContent'


type Props = CallMessageProps & {
    hasVoiceTranscript?: boolean
    title: string
    createdTime?: string
    ariaLabel?: string
}

export const EmptyCallMessage: FC<Props> = React.memo(({
    isMissedCall,
    duration,
    title,
    sentiment,
    ariaLabel,
    createdTime,
}) => {
    const {t} = useLocalizationState()

    return (
        <div className="flex items-center justify-between gap-4" aria-label={ariaLabel}>
            <div className="flex items-center gap-1">
                {isMissedCall
                    ? <IconMissedCall16 className="size-4" aria-hidden />
                    : <IconInteractionCall className="size-4 icon-default" aria-hidden />
                }
                <div
                    className={tcn('flex items-center gap-1 caption-s text-neutral-on-secondary', {
                        'text-on-error': isMissedCall,
                    })}
                >
                    <span
                        aria-hidden
                        data-testid="call-title"
                    >
                        {title}
                    </span>
                    {!!duration && (<span data-testid="call-duration">{`(${duration})`}</span>)}
                </div>
            </div>
            <div className="flex items-center gap-1">
                {sentiment && <Sentiment sentiment={sentiment} className="mr-1 w-5" />}
                <span className="flex items-center">
                    <span className="aria-description">
                        {t.ACTIVITY_HISTORY.call_message_created_time_aria()}
                    </span>
                    <span data-testid="call-start-time" className="caption-s text-neutral-fade">{createdTime}</span>
                </span>
            </div>
        </div>
    )
})
