import type {KeyboardEvent} from 'react'
import React, {useCallback, useEffect, useMemo, useRef} from 'react'
import {useAtomValue, useSetAtom} from 'jotai'

import {useSessionState} from '@bpinc/ad-session-context'
import {useUserProfileState} from '@bpinc/ad-user-profile-state-context'
import {useCommunicatorUIState} from '@bpinc/lib-communicator-ui-context'
import type {CountryCode, CountryCodeWithAll, CountryPhoneValue} from '@bpinc/lib-jskit-ui'
import {CountryPhoneField} from '@bpinc/lib-jskit-ui'
import {phoneNumberFormatter} from '@bpinc/lib-phonenumber-formatter'
import {ALL_COUNTRIES_ID, DEFAULT_COUNTRY_RECENT_ID, useCountrySelectorState} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {contactsListMethodsAtom, phoneInputAtom} from '../atoms'
import {getLetter, sanitizePhone} from '../helpers'
import {useFormatedPhone} from '../hooks/useFormatedPhone'
import {useHandlers} from '../hooks/useHandlers'
import {useQueryContacts} from '../hooks/useQueryContacts/useQueryContacts'


export const PhoneInput = () => {
    const {t} = useLocalizationState()
    const {searchValue} = useCommunicatorUIState('dialpad')
    const contactsListMethods = useAtomValue(contactsListMethodsAtom)
    const {
        onCallClick,
        updateCaretPosition,
        onInputValueChange,
        closeDialpad,
    } = useHandlers()

    const setInputRef = useSetAtom(phoneInputAtom)
    const contactsFound = !!useQueryContacts().contacts.length

    const {countrySelectorValue, setCountrySelectorValue} = useCountrySelectorState(['countrySelectorValue', 'setCountrySelectorValue'])
    const {countrySelectorRecent, addCountryToRecent} = useCountrySelectorState(['countrySelectorRecent', 'addCountryToRecent'])

    const {isValidPhoneNumber} = useFormatedPhone()

    const hasCountryPrefix = countrySelectorValue !== ALL_COUNTRIES_ID
    const showCountryPrefixError = !!hasCountryPrefix && !!searchValue && !isValidPhoneNumber && !contactsFound

    const {country: callCenterCountryCode} = useSessionState()
    const {userProfile} = useUserProfileState()

    const onCountryCodeChange = useCallback((value: CountryCodeWithAll) => {
        const newCountryCode = value === 'ALL' ? ALL_COUNTRIES_ID : value

        if (newCountryCode === countrySelectorValue) {
            return
        }
        addCountryToRecent(DEFAULT_COUNTRY_RECENT_ID, newCountryCode)
        setCountrySelectorValue(newCountryCode)
    }, [setCountrySelectorValue, countrySelectorValue, addCountryToRecent])

    const countryCode = countrySelectorValue === ALL_COUNTRIES_ID
        ? 'ALL'
        : countrySelectorValue

    const countryPhoneValue = useMemo<CountryPhoneValue>(() => ({
        phone: searchValue || '',
        countryCode,
    }), [searchValue, countryCode])


    const onChangeDialNumber = useCallback((value: string) => {
        const newValue = sanitizePhone(value)
        if (newValue !== searchValue) {
            onInputValueChange(newValue)
        }
    }, [searchValue, onInputValueChange])

    const onChange = useCallback((value: CountryPhoneValue) => {
        onChangeDialNumber(value.phone)
        onCountryCodeChange(value.countryCode)
    }, [onChangeDialNumber, onCountryCodeChange])

    const countries = useMemo(() => {
        const countriesInner = phoneNumberFormatter.getSupportedRegions()
            .map(region => ({
                code: region.toLowerCase(),
                name: region,
                prefix: phoneNumberFormatter.getCountryCodeForRegion(region),
            }))

        const profileCountryCode = countriesInner.find(country => country.name.toUpperCase() === userProfile?.country.toUpperCase())?.code

        popCountryOnTop(profileCountryCode)
        popCountryOnTop(callCenterCountryCode)

        return countriesInner

        /** Puts the country with the given code on top of the list. */
        function popCountryOnTop(code?: CountryCode) {
            const foundIndex = countriesInner.findIndex(country => {
                return country.code.toUpperCase() === code?.toUpperCase()
            })
            if (foundIndex === -1) {
                return countriesInner
            }
            return [
                countriesInner[foundIndex],
                ...countriesInner.slice(0, foundIndex),
                ...countriesInner.slice(foundIndex + 1),
            ]
        }

    }, [callCenterCountryCode, userProfile?.country])

    const recentCountryCodes = useMemo(
        () => (countrySelectorRecent[DEFAULT_COUNTRY_RECENT_ID] || []),
        [countrySelectorRecent],
    )

    const handleInputKeyDown = useCallback((evt: KeyboardEvent<HTMLInputElement>) => {
        if (evt.altKey) {
            return
        }
        if (evt.key === 'Enter') {
            onCallClick()
            evt.preventDefault()
        } else if (evt.key === 'ArrowDown' || evt.key === 'ArrowUp') {
            if (evt.key === 'ArrowDown') {
                contactsListMethods?.scrollToFirst()
            } else {
                contactsListMethods?.scrollToLast()
            }
            closeDialpad()
            evt.preventDefault()
        } else {
            const letter = getLetter(evt.key, evt.code)
            updateCaretPosition(letter)
        }
    }, [closeDialpad, onCallClick, updateCaretPosition, contactsListMethods])

    const inputRef = useRef<HTMLInputElement | null>(null)

    useEffect(() => {
        setInputRef(inputRef.current)
        return () => setInputRef(null)
    }, [inputRef, setInputRef])

    return (
        <CountryPhoneField
            value={countryPhoneValue}
            onChange={onChange}
            countries={countries}
            recentCountryCodes={recentCountryCodes}
            inputRef={inputRef}
            onKeyDown={handleInputKeyDown}
            error={showCountryPrefixError && t.DIAL_PAD.invalid_number_error_text()}

            searchFieldPlaceholder={t.GLOBAL.country_selector_search_input_placeholder()}
            recentTitle={t.GLOBAL.country_selector_recent()}
            countriesTitle={t.GLOBAL.country_selector_countries()}
            allCountriesTitle={t.GLOBAL.country_selector_all_countries_title()}

            data-testid="dialpad-input"
        />
    )
}
