import React from 'react'

import {useInteraction} from '@bpinc/ad-interaction-state-context'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {assertDefined} from '@bpinc/lib-types-helpers'

import {useHandleKeyPress} from './hooks/useHandleKeyPress'
import {ButtonsOnHover} from './ButtonsOnHover'
import {Connecting} from './Connecting'
import {DisplayText} from './DisplayText'
import {Failed} from './Failed'
import {IconAndTimer} from './IconAndTimer'
import {MonitorIcon} from './MonitorIcon'


interface InteractionsListItemProps {
    itemId: string
    onClick: () => void
    isActive: boolean
    isSelected: boolean
}

export const InteractionsListItem: React.FC<InteractionsListItemProps> = React.memo(({
    itemId,
    onClick,
    isActive,
    isSelected,
}) => {
    const item = useInteraction(itemId)
    assertDefined(item)
    const {failed, prolongedConnection} = item.displayData
    const handleKeyPress = useHandleKeyPress(onClick)

    return (
        <div
            className={tcn(
                'flex items-center',
                'h-9.5 cursor-pointer border-none px-2 py-1.5',
                'transition ease-in-out',
                'group state-layer-bg-neutral-0',
                {'state-layer-mix-teal-on-white-2': isSelected},
            )}
            data-testid="interaction-item"
            data-active={isActive || undefined}
            onClick={onClick}
            onKeyPress={handleKeyPress}
            role="button"
            tabIndex={0}
        >
            <div className="flex flex-none items-center overflow-hidden">
                <IconAndTimer item={item} />
                <MonitorIcon item={item} />
            </div>
            <div className="ml-2 flex flex-auto items-center overflow-hidden">
                <DisplayText item={item} />
                {failed && <Failed />}
                {!failed && prolongedConnection && <Connecting />}
            </div>
            <div className="ml-4 flex flex-none shrink-0 items-center">
                <ButtonsOnHover item={item} />
            </div>
        </div>
    )
})
