import {useEffect} from 'react'

import {useEvent} from '@bpinc/lib-helpers'
import {Notifications, useNotificationsState} from '@bpinc/lib-notifications-context'
import {useFavoritesStateConnector} from '@bpinc/state-favorites-context'
import {useLocalizationState} from '@bpinc/state-localization-context'


/**
 * Show error when user tried to add new favorite item, but the limit is reached
 */
export function useFavoritesLimitReachedError() {
    const favoritesStateConnector = useFavoritesStateConnector()
    const {showNotification} = useNotificationsState('methods')
    const {FAVORITES: i18n} = useLocalizationState('t')

    const onLimitReachedError = useEvent(() => {
        showNotification({
            type: 'error',
            id: Notifications.CallFailedDueNetworkIssueWarning,
            closable: true,
            tags: ['current-view'],
            message: i18n.favorites_limit_reached_error(),
        })
    })

    useEffect(() => {
        const subscription = favoritesStateConnector.dispenser.subscribe({onLimitReachedError})
        return subscription.unsubscribe
    }, [favoritesStateConnector, onLimitReachedError])
}
