import React, {useCallback} from 'react'

import {SmMenu} from '@bpinc/lib-external-ui-elements'
import {isHTMLButtonElement} from '@bpinc/lib-html-utils'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {useEnabledInOffline} from '@bpinc/lib-ui-elements'

import type {MenuItemParams} from '../hooks/useSettingsMenu'

import styles from '../../../styles/MainPage.module.less'


interface SettingsMenuItemProps extends MenuItemParams {
    activePage: string
    id: string
}

export const MenuItem: React.FC<SettingsMenuItemProps> = ({
    allowRender = true,
    id,
    onClick,
    enabledOffline,
    children,
    isLink,
    page,
    activePage,
    disabled,
    disabledTitle,
}) => {
    const handleItemKeyPress = useCallback((e: React.KeyboardEvent) => {
        if (['Enter', ' '].includes(e.key)) {
            const button = e.target
            if (!isHTMLButtonElement(button)) {
                return
            }
            e.preventDefault()
            button.click()
        }
    }, [])

    const isActive = page === activePage

    const propsWithOffline = useEnabledInOffline(enabledOffline, {
        className: tcn(styles.item, {[styles.itemLink]: isLink, [styles[`itemActive_${id}`]]: isActive}),
        onClick,
    })

    if (!allowRender) return null

    return (
        <SmMenu.Item
            onKeyPress={handleItemKeyPress}
            as="button"
            role="menuitem"
            disabled={disabled}
            title={disabled ? disabledTitle : undefined}
            data-testid="menu-item-button"
            data-item={id}
            data-element="menu-item"
            data-active={isActive || undefined}
            {...propsWithOffline}
        >
            {children}
        </SmMenu.Item>
    )
}
