import React, {useMemo} from 'react'
import {useContext} from 'use-context-selector'

import {checkIsLayout} from '@bpinc/lib-form-builder'

import {FormBuilderContext} from './FormBuilder'
import {FormElementResolver} from './FormElementResolver'

/**
 * Renders all form fields in a single column.
 * It is used in Communicator.
 */
export function CompactForm() {
    const {formDefinition, pageIndex} = useContext(FormBuilderContext)

    const pageFields = useMemo(() => {
        return formDefinition?.layout?.pages?.[pageIndex]?.fields || []
    }, [formDefinition?.layout?.pages, pageIndex])

    const flatFields = pageFields.filter(item => !checkIsLayout(item.field))

    return (
        <>
            {flatFields.map(pageItem => (
                <FormElementResolver
                    key={pageItem.field.id}
                    pageItem={pageItem}
                />
            ))}
        </>
    )
}
