import type {FormDefinition} from '@bpinc/ad-request-urls'

import {checkIsLayout} from './checkIsLayout'
import {getAllFormFields} from './getAllFormFields'
import {getFormElementMappingString} from './getFormElementMappingString'

/**
 * Form values are stored as a map of field names to values.
 * This function creates a map of lead mapping strings to form values.
 */
export function createLeadMappingStringToFormValueMap(
    formValues: Record<string, unknown>,
    formDefinition: FormDefinition,
): Record<string, unknown> {
    const nonLayoutFields = getAllFormFields(formDefinition)
        .filter(({field}) => !checkIsLayout(field))

    return Object.entries(formValues)
        .reduce((acc, [fieldName, value]) => {
            const field = nonLayoutFields.find(it => it.field?.name === fieldName)
            const mappingString = getFormElementMappingString(field)

            if (!mappingString) {
                return acc
            }

            return {
                ...acc,
                [mappingString]: value,
            }
        }, {})
}
