import React, {useMemo} from 'react'
import {createColumnHelper} from '@tanstack/react-table'

import {formatDate} from '@bpinc/ad-formatting-helpers'
import type {QueueItem} from '@bpinc/ad-request-urls'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {Sla} from './Sla'


const columnHelper = createColumnHelper<QueueItem>()


export function useBasicColumns() {
    const {t} = useLocalizationState()

    return useMemo(() => [
        columnHelper.accessor(row => row.from || t.GLOBAL.heading_unknown(), {
            id: 'from',
            header: () => null,
            /** Setting size and minSize to 0 is a hack to use 'auto' for the width */
            size: 0,
            minSize: 0,

            cell: info => (
                <>
                    <p className="m-0 truncate">{info.getValue()}</p>
                    <p className="m-0 truncate">{info.row.original.subject}</p>
                </>
            ),
        }),
        columnHelper.accessor('serviceName', {
            id: 'serviceName',
            header: () => null,
            cell: info => <p className="m-0">{info.getValue()}</p>,
            size: 90,
        }),
        columnHelper.accessor(row => row.date && formatDate(row.date), {
            id: 'date',
            header: () => null,
            cell: info => <p className="m-0">{info.getValue()}</p>,
            size: 120,
        }),
        columnHelper.accessor('sla', {
            id: 'sla',
            header: () => null,
            cell: info => <Sla value={info.getValue()} />,
            size: 70,
        }),
    ], [t.GLOBAL])
}
