import type {FC} from 'react'
import React from 'react'
import {AnimatePresence, motion} from 'framer-motion'

import {CircleButton, IconHold16, IconPause16} from '@bpinc/lib-jskit-ui'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {InteractionTimer} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import type {ControlButton} from './types'


interface HoldButtonProps extends ControlButton {
    holdStartTime?: number
}

export const HoldButton: FC<HoldButtonProps> = ({
    isActive,
    onClick,
    holdStartTime,
    disabled,
    iconClassName,
}) => {
    const {t} = useLocalizationState()

    return (
        <CircleButton
            isActive={isActive}
            onClick={onClick}
            className="group/hold w-auto"
            disabled={disabled}
            tooltip={{
                content: isActive
                    ? t.CALL_DETAILS_FORM.interaction_retrieve()
                    : t.CALL_DETAILS_FORM.interaction_hold(),
            }}
            data-testid="hold-button"
            data-on-hold={isActive || undefined}
        >
            <motion.div
                animate={isActive ? {paddingRight: 9} : {paddingRight: 5}}
                className="flex items-center py-[3px] pl-[5px]"
            >
                <div className={tcn('flex gap-0.5 ')}>
                    <IconHold16
                        className={tcn(iconClassName, 'hidden size-4.5', {
                            'group-hover/hold:block': isActive,
                        })}
                    />
                    <IconPause16
                        className={tcn(iconClassName, 'visible size-4.5', {
                            'group-hover/hold:hidden': isActive,
                        })}
                    />
                </div>
                <AnimatePresence>
                    {isActive && (
                        <motion.div
                            initial={{width: 0}}
                            animate={{width: 'auto'}}
                            exit={{width: 0}}
                        >
                            <InteractionTimer startTime={holdStartTime} />
                        </motion.div>
                    )}
                </AnimatePresence>
            </motion.div>
        </CircleButton>
    )
}
