import type {FC} from 'react'
import React, {useCallback} from 'react'

import {useInteractionId} from '@bpinc/lib-interaction-context'
import {Button} from '@bpinc/lib-ui-elements'
import {useInteractionUIItemStateSetter} from '@bpinc/state-interaction-ui'
import {useLocalizationState} from '@bpinc/state-localization-context'

import styles from '../styles/InteractionsPage.module.less'


export const ChatInWindowPlaceholder: FC = () => {
    const {t} = useLocalizationState()
    const itemId = useInteractionId()
    const setState = useInteractionUIItemStateSetter()

    const handleUnpin = useCallback(
        function handleUnpinCallback() {
            setState(itemId, s => ({...s, chatView: 'embedded'}))
        },
        [itemId, setState],
    )
    return (
        <>
            <div className={styles.chatInWindowPlaceholder}>
                <p className={styles.chatInWindowPlaceholderText}>{t.INTERNAL_CHAT.chat_in_popup_message()}</p>
                <Button linkLike onClick={handleUnpin}>
                    {t.INTERNAL_CHAT.unpin()}
                </Button>
            </div>
        </>
    )
}
