import {useCallback} from 'react'
import {useAtomValue} from 'jotai'

import {useTransferHandlers} from '@bpinc/ad-interaction-state-hooks'
import {useEventListener} from '@bpinc/lib-helpers'

import {searchAtom} from '../../atoms'


export function useKeyboardShortcuts() {
    const search = useAtomValue(searchAtom)

    const transferHandlers = useTransferHandlers()

    const consultCallHandler = useCallback(
        () => transferHandlers.consultCallHandler(search),
        [search, transferHandlers],
    )
    const blindTransferHandler = useCallback(
        () => transferHandlers.blindTransferHandler(search),
        [search, transferHandlers],
    )
    const singleStepConferenceHandler = useCallback(
        () => transferHandlers.singleStepConferenceHandler(search),
        [search, transferHandlers],
    )

    const handleKeypadKeyEnterKeyDown = useCallback(
        function handleKeypadKeyEnterKeyDownCallback(keyboardEvent: KeyboardEvent) {
            if (keyboardEvent.shiftKey && keyboardEvent.altKey) {
                switch (keyboardEvent.code) {
                    case 'KeyB':
                        return blindTransferHandler()
                    case 'KeyC':
                        return singleStepConferenceHandler()
                    case 'KeyD':
                        return consultCallHandler()

                }
            }
        },
        [blindTransferHandler, consultCallHandler, singleStepConferenceHandler],
    )

    useEventListener('keydown', handleKeypadKeyEnterKeyDown)
}
