import {useEffect} from 'react'

import {useSessionState, useSessionStateMethods} from '@bpinc/ad-session-context'
import {useEvent} from '@bpinc/lib-helpers'
import {Notifications, useNotificationsState} from '@bpinc/lib-notifications-context'
import {useLocalizationState} from '@bpinc/state-localization-context'


export function usePhoneTypeErrorNotification() {
    const {showNotification, hideNotificationById} = useNotificationsState('methods')
    const {startPhoneTypeSelection} = useSessionStateMethods()
    const phoneTypeErrorCode = useSessionState('phoneTypeErrorCode')
    const {AUTHORIZATION_DIALOG_LABELS: i18n} = useLocalizationState('t')

    const updateNotification = useEvent(() => {
        if (phoneTypeErrorCode === 2) {
            showNotification({
                type: 'error',
                id: Notifications.BadPhoneExtensionError,
                message: i18n.auth_message_undefinedExtension(),
            })
            startPhoneTypeSelection()
        }

        if (!phoneTypeErrorCode || phoneTypeErrorCode === 0) {
            hideNotificationById(Notifications.BadPhoneExtensionError)
        }
    })

    useEffect(() => {
        updateNotification()
        // track only phoneTypeErrorCode
    }, [phoneTypeErrorCode, updateNotification])
}
