import React, {useEffect} from 'react'
import {useDropzone} from 'react-dropzone'
import {partial} from 'lodash-es'

import {useAsRef} from '@bpinc/lib-helpers'
import {useLocalizationState} from '@bpinc/state-localization-context'
import {useModalComponentOpener} from '@bpinc/state-modals-context'

import {getFileErrors} from '../../../../helpers'
import type {ChatProps} from '../../../../types'
import {UploadErrorModal} from '../../ChatForm/UploadErrorModal'
import {useOnDrop} from './useOnDrop'
import {useShowChecker} from './useShowChecker'

import styles from './Dropzone.module.less'


export const Dropzone: React.FC<ChatProps> = ({itemId}) => {
    const {t} = useLocalizationState()
    const show = useShowChecker()
    const onDrop = useOnDrop(itemId)

    const {getRootProps, getInputProps, fileRejections, acceptedFiles} = useDropzone({
        onDrop,
        validator: partial(getFileErrors, t),
    })

    const openUploadErrorModal = useModalComponentOpener(UploadErrorModal)

    const acceptedFilesLengthRef = useAsRef(acceptedFiles.length)
    useEffect(function addErrorMessageOnReject() {
        if (fileRejections.length) {
            openUploadErrorModal({
                rejection: fileRejections,
                totalFilesAmount: acceptedFilesLengthRef.current + fileRejections.length,
            })
        }
    }, [acceptedFilesLengthRef, fileRejections, openUploadErrorModal])

    return (
        <div
            {...getRootProps()}
            className={show ? styles.dropzone : styles.dropzoneDisabled}
            data-testid="attachments-dropzone"
            data-visible={show}
        >
            <input {...getInputProps()} />
            <div className={styles.dropzoneContent}>
                <p className={styles.dropzoneTitle}>{t.GLOBAL.drop_files_here()}</p>
                <p className={styles.dropzoneSubtitle}>{t.GLOBAL.drag_and_drop_subtitle()}</p>
            </div>
        </div>
    )
}
