import {useEffect} from 'react'

import {useSessionState} from '@bpinc/ad-session-context'
import {incomingEMIErrorMessageCodes} from '@bpinc/ad-transport-types'
import {useEvent} from '@bpinc/lib-helpers'
import {Notifications, useNotificationsState} from '@bpinc/lib-notifications-context'
import {useLocalizationState} from '@bpinc/state-localization-context'


const MSTEAMS_ERROR_NOTIFICATION_ID = 'msteams_error_notification_id'

/**
 * Shows EMI (Enterprise Messengers Integration) errors
 */
export function useEmiErrorsNotifications() {
    const emiData = useSessionState('emi')
    const {ERROR_MESSAGES: i18n} = useLocalizationState('t')
    const {showNotification, hideNotificationById} = useNotificationsState('methods')

    const getMsTeamsError = useEvent((errorCode: string, errorText = '') => {
        switch (errorCode) {
            case incomingEMIErrorMessageCodes.unable_to_get_access_token:
                return i18n.msteams_access_token_error_message() + errorText
            case incomingEMIErrorMessageCodes.refresh_token_expired:
                return i18n.communicator_msteams_refresh_token_expired_error_message()
            case incomingEMIErrorMessageCodes.user_deauthorized:
                return undefined
            case incomingEMIErrorMessageCodes.no_integration_account:
            case incomingEMIErrorMessageCodes.feature_disabled:
            case incomingEMIErrorMessageCodes.no_emi_servers:
                return i18n.msteams_disabled()
            case incomingEMIErrorMessageCodes.integration_account_changed:
            case incomingEMIErrorMessageCodes.mismatched_directory_id:
            case incomingEMIErrorMessageCodes.mismatched_client_id:
                return i18n.communicator_msteams_reset_by_admin_message()
            case incomingEMIErrorMessageCodes.azure_user_deleted:
                return i18n.msteams_user_deleted()
            case incomingEMIErrorMessageCodes.unsufficient_privileges:
                return i18n.msteams_unsufficient_privileges()
            default:
                return errorText
        }
    })

    useEffect(() => {
        if (emiData?.msteams?.errorCode) {
            const message = getMsTeamsError(emiData.msteams.errorCode, emiData.msteams.errorMessage)
            if (!message) {
                return
            }
            showNotification({
                type: 'error',
                id: Notifications.MSTeamsIntegrationError,
                message,
            })
        } else {
            hideNotificationById(MSTEAMS_ERROR_NOTIFICATION_ID)
        }
    }, [showNotification, hideNotificationById, getMsTeamsError, emiData?.msteams])
}
