import type React from 'react'
import {useCallback} from 'react'


export function useHandleKeyPress(handler: () => void) {
    return useCallback(
        function handleKeyPressCallback(e: React.KeyboardEvent) {
            if (e.key === 'Enter' || e.key === ' ') {
                e.preventDefault()
                handler()
            }
        },
        [handler],
    )
}
