import {useCallback} from 'react'

import {useContactsStateMethods} from '@bpinc/ad-contacts-state-context'
import {maskActivityHistoryMessages} from '@bpinc/ad-contacts-state-methods'
import {useInteractionChatStateMethods} from '@bpinc/ad-interaction-chat-state-context'
import {bpLogger} from '@bpinc/ad-persistent-logger'
import {useSessionState} from '@bpinc/ad-session-context'
import {getErrorMessage} from '@bpinc/lib-helpers'
import {Notifications, useNotificationsState} from '@bpinc/lib-notifications-context'
import {useLocalizationState} from '@bpinc/state-localization-context'


interface UseFileMaskerParams {
    globalInteractionId?: string
    itemContactId: string
    fileId: string
    interactionStepId: string
    isActiveChat: boolean
}

export function useFileMasker({
    globalInteractionId,
    interactionStepId,
    itemContactId,
    isActiveChat,
    fileId,
}: UseFileMaskerParams) {
    const {loginId, sessionId, serverOrigin} = useSessionState()
    const {maskFile} = useContactsStateMethods()
    const {ERROR_MESSAGES: i18n} = useLocalizationState('t')

    const {toggleFileMask} = useInteractionChatStateMethods()
    const {showNotification} = useNotificationsState('methods')

    return useCallback(() => {
        if (isFileInCurrentChatSession()) {
            toggleFileMask({
                itemId: interactionStepId,
                fileId,
            })
        } else if (isFileInAH(globalInteractionId)) {
            maskActivityHistoryMessages({
                loginId,
                sessionId,
                serverOrigin,
                globalInteractionId,
                fileIds: [fileId],
            })
                .then(() => {
                    if (itemContactId) {
                        maskFile({fileId, contactId: itemContactId})
                    }
                })
                .catch(err => {
                    //TODO: Ask for real error message text
                    showNotification({
                        type: 'error',
                        id: Notifications.ChatFileMaskError,
                        tags: ['current-view'],
                        message: i18n.activity_history_file_masking_error(),
                        duration: 5000,
                    })
                    bpLogger.error(getErrorMessage(err))
                })
        }
        function isFileInCurrentChatSession() {
            return isActiveChat && !globalInteractionId
        }

        function isFileInAH(checkingGlobalInteractionId?: string): checkingGlobalInteractionId is string {
            return !!checkingGlobalInteractionId
        }
    }, [
        i18n,
        itemContactId,
        fileId,
        globalInteractionId,
        interactionStepId,
        isActiveChat,
        loginId,
        maskFile,
        serverOrigin,
        sessionId,
        showNotification,
        toggleFileMask,
    ])
}
