import React from 'react'

import type {CommonSlidingModalItemProps} from '@bpinc/lib-sliding-modal-context'
import {SlidingModal} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {KeypadModalContent} from './KeypadModalContent'


export const KeypadModal: React.FC<CommonSlidingModalItemProps> = React.memo(function KeypadModal(props) {
    const t = useLocalizationState('t')

    return (
        <SlidingModal name="keypad" header={t.DIAL_PAD.keypad()} {...props}>
            <KeypadModalContent />
        </SlidingModal>
    )
})
