import {useEffect} from 'react'

import {useCommunicatorUIState} from '@bpinc/lib-communicator-ui-context'
import {useModalsState} from '@bpinc/state-modals-context'


export function useClosePanelsOnNotifications(): void {
    const {toggleRightPanel, toggleLeftPanel} = useCommunicatorUIState(['toggleLeftPanel', 'toggleRightPanel'])
    const hasNotification = useModalsState(
        ({modals}) => modals.some(modal => modal.id === 'notification'),
    )

    useEffect(() => {
        if (hasNotification) {
            toggleRightPanel(false)
            toggleLeftPanel(false)
        }
    }, [hasNotification, toggleRightPanel, toggleLeftPanel])
}
