import type {ChangeEvent, FC} from 'react'
import React, {useCallback, useMemo} from 'react'

import {useContactsState} from '@bpinc/ad-contacts-state-context'
import {useDIDNumberState} from '@bpinc/ad-did-number-context'
import {useDirectoryState} from '@bpinc/ad-directory-state-context'
import {isPhone} from '@bpinc/ad-formatting-helpers'
import {useActivityHistoryOpener, useCallHandler} from '@bpinc/ad-interaction-state-hooks'
import {useServiceState} from '@bpinc/ad-service-context'
import {useSessionState} from '@bpinc/ad-session-context'
import {useDirectoryUIState} from '@bpinc/lib-communicator-ui-context'
import {DIRECTORY_TABS} from '@bpinc/lib-communicator-ui-types'
import {
    getDirectoryItemContactId,
    getDirectoryItemPhone,
    getDirectoryItemPhoneBySearch,
    getDirectoryItemUniqueId,
    getDirectoryItemUserId,
    isMsTeamsUser,
} from '@bpinc/lib-helpers-ui'
import {isHTMLInputElement} from '@bpinc/lib-html-utils'
import {SearchField} from '@bpinc/lib-jskit-ui'
import {useNotificationsState} from '@bpinc/lib-notifications-context'
import {SearchForInteractionControls} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'


export const DirectorySearchControls: FC = () => {
    const {phoneDeviceCapabilities} = useSessionState()

    const users = useDirectoryState('users')
    const {contactList} = useContactsState(['contactList', 'contactListLoading'])
    const {t} = useLocalizationState()
    const {hideNotificationsByTag} = useNotificationsState('methods')

    const {
        tabIndex,
        selected,
        searchValue,
        setSearchValue,
        setSelected,
        firstFoundItemId,
        setFirstFoundItemId,
        searchInputRef,
    } = useDirectoryUIState()

    const handleSearch = useCallback(
        (e: ChangeEvent) => {
            const input = e.target
            if (!isHTMLInputElement(input)) {
                return
            }
            const newValue = input.value
            setSearchValue(newValue)
            setSelected(undefined)
            setFirstFoundItemId(undefined)
        },
        [setFirstFoundItemId, setSearchValue, setSelected],
    )

    const {selectedService} = useServiceState()
    const {selectedDIDNumber} = useDIDNumberState()

    const handleCall = useCallHandler()
    const openActivityHistory = useActivityHistoryOpener()

    const directoryTab = DIRECTORY_TABS[+tabIndex]

    const targetItem = useMemo(
        function memoizeSelectedItem() {
            if (directoryTab === 'internal') {
                return users.find(u => getDirectoryItemUniqueId(u) === (selected || firstFoundItemId))
            }
            return contactList.find(u => getDirectoryItemUniqueId(u) === (selected || firstFoundItemId))
        },
        [contactList, directoryTab, firstFoundItemId, selected, users],
    )

    const handleCallClick = useCallback(
        function handleCallClickCallback() {
            if (!targetItem) {
                if (!searchValue) {
                    return
                }
                handleCall({
                    serviceId: targetItem,
                    accessNumberId: selectedDIDNumber,
                    phone: searchValue,
                    origination: 'directory',
                    navigateToInteractionsPage: true,
                })
            } else {
                handleCall({
                    phone: getDirectoryItemPhoneBySearch(targetItem, searchValue),
                    firstName: targetItem.firstName,
                    lastName: targetItem.lastName,
                    serviceId: selectedService,
                    contactId: getDirectoryItemContactId(targetItem),
                    userId: getDirectoryItemUserId(targetItem),
                    accessNumberId: selectedDIDNumber,
                    origination: 'directory',
                    navigateToInteractionsPage: true,
                })
            }
        },
        [handleCall, searchValue, selectedDIDNumber, targetItem, selectedService],
    )

    const handleChatClick = useCallback(
        function handleChatClickCallback() {
            if (!targetItem) {
                if (!searchValue) {
                    return
                }

                hideNotificationsByTag('interaction-init')

                return openActivityHistory({
                    phone: searchValue,
                    forceSearchForContact: false,
                })
            }

            hideNotificationsByTag('interaction-init')

            const userId = getDirectoryItemUserId(targetItem)
            const contactId = getDirectoryItemContactId(targetItem)

            openActivityHistory({
                contactId,
                userId,
                phone: getDirectoryItemPhone(targetItem),
                origination: 'directory',
                firstName: targetItem.firstName,
                lastName: targetItem.lastName,
                emiPhotoUrl: isMsTeamsUser(targetItem) ? targetItem.photoUrl : undefined,
                emiUserType: isMsTeamsUser(targetItem) ? targetItem.userType : undefined,
            })
        },
        [hideNotificationsByTag, openActivityHistory, searchValue, targetItem],
    )

    return (
        <div className="flex items-start gap-2.5">
            <SearchField
                value={searchValue}
                onChange={handleSearch}
                ref={searchInputRef}
                layoutClassName="grow"
                data-testid="search-input"
                aria-label={t.DIRECTORY.search_field_placeholder()}
                placeholder={t.DIRECTORY.search_field_placeholder()}
            />
            <SearchForInteractionControls
                onCallClick={handleCallClick}
                showCallButton={!!phoneDeviceCapabilities.canDial}
                callButtonDisabled={!getDirectoryItemPhoneBySearch(targetItem, searchValue) && !isPhone(searchValue)}
                chatButtonDisabled={!targetItem && !isPhone(searchValue)}
                onChatClick={handleChatClick}
                searchQuery={searchValue}
            />
        </div>
    )
}
