import React, {useMemo} from 'react'
import {TabsContent, TabsList, TabsRoot, TabsTrigger} from '@servicepattern/ui'

import {useInteractionProp} from '@bpinc/ad-interaction-state-context'
import {isExternalChat} from '@bpinc/ad-interaction-state-methods'
import {useCommunicatorUIState} from '@bpinc/lib-communicator-ui-context'
import {useInteractionId} from '@bpinc/lib-interaction-context'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {ServiceSelect} from '../../../../../components/ServiceSelect'
import {useKeyboardShortcuts} from '../hooks/useKeyboardShortcuts'
import {TabContent} from './TabContent'


export type TransferTab = 'recent' | 'favorites' | 'directory'

export const InteractionsTransfer: React.FC = () => {
    const {t} = useLocalizationState()
    const itemId = useInteractionId()

    const activeItemIsExternalChat = useInteractionProp(itemId, isExternalChat)
    const transferMode = activeItemIsExternalChat ? 'messages' : 'calls'

    useKeyboardShortcuts()

    const {transferTabIndex, handleTransferTabChange} =
        useCommunicatorUIState(['transferTabIndex', 'handleTransferTabChange'])


    const tabs = useMemo(
        () => [
            {
                id: 'recent',
                key: 'tab-recent',
                content: t.ADDRESS_BOOK_AREA.addressBook_recents(),
                className: 'w-full',
            },
            {
                id: 'favorites',
                key: 'tab-favorites',
                content: t.ADDRESS_BOOK_AREA.addressBook_favorites(),
                className: 'w-full',
            },
            {
                id: 'directory',
                key: 'tab-directory',
                content: t.ADDRESS_BOOK_AREA.addressBook_directory(),
                className: 'w-full',
            },
        ],
        [t],
    )

    const onTabChange = (newTabId: string) => {
        const newTabIndex = tabs.findIndex(tab => tab.id === newTabId)
        handleTransferTabChange(newTabIndex)
    }

    const selectedTab = tabs[Number(transferTabIndex)].id as TransferTab
    return (
        <div className="flex flex-auto flex-col overflow-auto px-4 pt-4">
            {transferMode === 'calls' && (
                <ServiceSelect itemId={itemId} labeled />
            )}
            <TabsRoot
                value={selectedTab}
                className="mt-4 flex flex-col overflow-auto rounded-xs border px-4 pb-4 bg-primary-background border-default"
            >
                <TabsList>
                    {tabs.map(tab => (
                        <TabsTrigger
                            triggerClassName={tcn('p-2 text-center', {
                                'text-primary-on-primary':
                                        selectedTab === tab.id,
                            })}
                            key={tab.key}
                            value={tab.id}
                            className={tab.className}
                            onClick={() => onTabChange(tab.id)}
                            isSelected={selectedTab === tab.id}
                        >
                            {tab.content}
                        </TabsTrigger>
                    ))}
                </TabsList>
                <TabsContent
                    value={selectedTab}
                    className="flex flex-auto flex-col overflow-auto pt-4"
                >
                    <TabContent
                        transferMode={transferMode}
                        selectedTab={selectedTab}
                    />
                </TabsContent>
            </TabsRoot>
        </div>
    )
}
