import type {FC} from 'react'
import React, {useCallback} from 'react'

import {getCaseURL} from '@bpinc/ad-request-urls'
import {useSessionState} from '@bpinc/ad-session-context'
import {clipboard} from '@bpinc/lib-helpers'
import {SemanticButton, Svg} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import iconLink from '../images/link.svg'


const linkIcon = <Svg src={iconLink} className="[&_svg]:!h-[15px] [&_svg]:!w-[15px]" />

interface CopyCaseLinkButtonProps {
    caseId: string
}

export const CopyCaseLinkButton: FC<CopyCaseLinkButtonProps> = ({caseId}) => {
    const {serverOrigin} = useSessionState()
    const {t} = useLocalizationState()

    const handleCopy = useCallback(() => {
        clipboard.copy(getCaseURL(serverOrigin, caseId))
    }, [caseId, serverOrigin])

    return (
        <SemanticButton
            basic
            icon={linkIcon}
            onClick={handleCopy}
            className="!h-9 !w-9 [&_svg]:h-full [&_svg]:w-full"
            tooltip={t.GLOBAL.copy_shareable_link_button_tooltip()}
        />
    )
}
