import type {ContactShortType} from '@bpinc/ad-contacts-state-types'
import type {SearchUserItem} from '@bpinc/ad-directory-state-types'

import type {CategoryItem, ComposedContact} from '../hooks/useQueryContacts/useQueryContacts'
import {isCategoryItem, isInternalContact} from './checkers'


export function getPhone(contact: ComposedContact, pattern?: string) {
    const primaryPhone = isInternalContact(contact)
        ? contact.extension
        : isCategoryItem(contact)
            ? contact.number
            : contact.phone

    if (pattern && primaryPhone && !primaryPhone.includes(pattern)) {
        if (!isInternalContact(contact) && !isCategoryItem(contact)) {
            const phone = (contact.phones || []).find(phoneItem => {
                return phoneItem.includes(pattern)
            })
            if (phone) return phone
        }
    }

    return primaryPhone
}
export function getDisplayName(item: ContactShortType | SearchUserItem | CategoryItem) {
    return isCategoryItem(item) ? item.title : item.firstName + item.lastName
}
