import {useEffect} from 'react'

import {useInteractionProp} from '@bpinc/ad-interaction-state-context'
import {usePrevious} from '@bpinc/lib-helpers'
import {useInteractionId} from '@bpinc/lib-interaction-context'
import {useInteractionUIItemStateSetter} from '@bpinc/state-interaction-ui'


export function useAutoSelectTranscriptTab(itemId: string) {
    const interactionId = useInteractionId()
    const hasVoiceTranscriptOrSuggestions = useInteractionProp(
        interactionId,
        i => !!i?.voiceTranscript || !!i?.botSuggestions,
    )

    const previousHasVoiceTranscriptOrSuggestions = usePrevious(
        hasVoiceTranscriptOrSuggestions,
    )
    const setItemState = useInteractionUIItemStateSetter()

    useEffect(() => {
        if (
            !previousHasVoiceTranscriptOrSuggestions &&
            hasVoiceTranscriptOrSuggestions
        ) {
            setItemState(itemId, oldState => ({
                ...oldState,
                activeTab: 'transcript',
            }))
        }
    }, [
        itemId,
        hasVoiceTranscriptOrSuggestions,
        setItemState,
        previousHasVoiceTranscriptOrSuggestions,
    ])
}
