import React, {useCallback, useMemo} from 'react'
import {useAtom} from 'jotai'
import {noop} from 'lodash-es'

import type {DirectoryItem} from '@bpinc/ad-directory-state-types'
import {useInteractionProp} from '@bpinc/ad-interaction-state-context'
import {isExternalChat, isRecentCallConference, isRecentExtChatConference} from '@bpinc/ad-interaction-state-methods'
import {getDirectoryItemPhone} from '@bpinc/lib-helpers-ui'
import {useInteractionId} from '@bpinc/lib-interaction-context'
import {SearchField} from '@bpinc/lib-jskit-ui'
import {useOpenInteractionSlidingModal} from '@bpinc/lib-sliding-modal-context'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {assertDefined} from '@bpinc/lib-types-helpers'
import {useLocalizationState} from '@bpinc/state-localization-context'
import type {RecentItem} from '@bpinc/state-recent-types'

import {DirectoryCombined, DirectoryInternal} from '../../../../../directory'
import {FavoritesList} from '../../../../../favorites'
import type {FavoritesListChannelItem} from '../../../../../favorites/types'
import {RecentList} from '../../../../../recent'
import {dialNumberAtom, searchAtom} from '../../atoms'
import type {TransferTab} from './InteractionsTransfer'


interface TabContentProps {
    transferMode: 'messages' | 'calls'
    selectedTab: TransferTab
}

export function TabContent({
    transferMode,
    selectedTab,
}: TabContentProps) {
    const [search, setSearch] = useAtom(searchAtom)
    const [dialNumber, setDialNumber] = useAtom(dialNumberAtom)

    const t = useLocalizationState('t')
    const interactionId = useInteractionId()
    const activeItemIsExternalChat = useInteractionProp(
        interactionId,
        isExternalChat,
    )

    const recentsType = activeItemIsExternalChat ? 'all' : 'calls'

    const recentFilter = useCallback(
        (item: RecentItem) => {
            return [item.firstName, item.lastName, item.number, item.name]
                .join(' ')
                .toLowerCase()
                .includes(search.trim().toLowerCase())
        },
        [search],
    )

    const favoritesFilter = useCallback(
        ({item, channel, media}: FavoritesListChannelItem) => {
            function searchFilter() {
                return [item.displayName, channel.number]
                    .join(' ')
                    .toLowerCase()
                    .includes(search.trim().toLowerCase())
            }

            function channelFilter() {
                return transferMode === 'calls' ? media === 'voice' : media === 'chat'
            }
            return searchFilter() && channelFilter()
        },
        [search, transferMode],
    )

    const directoryFilter = useCallback(
        (item: DirectoryItem) => {
            //Search filter is omitted as query prop is passed to Directory (Internal and Combined) components
            return Boolean(getDirectoryItemPhone(item))
        },
        [],
    )

    const paneWrapperClassName = tcn(
        'flex min-h-0 flex-auto flex-col overflow-auto',
    )

    const SearchInput = useMemo(() => {
        return (
            <SearchField
                autoFocus
                value={search}
                data-testid="dialpad-input"
                fieldClassName="mb-4"
                onChange={e => setSearch(e.target.value)}
                placeholder={t.VOICE_CALL.transfer_search_placeholder()}
            />
        )
    }, [search, setSearch, t.VOICE_CALL])

    const openInteractionSlidingModal = useOpenInteractionSlidingModal()

    function openRecentConferenceModal(recentItem: RecentItem) {
        const payload = {
            recentItem,
            onClick: onRecentClick,
            handleCallClick: noop,
            handleChatClick: noop,
            transferMode,
        }
        assertDefined(interactionId)
        openInteractionSlidingModal(interactionId, 'recentConference', payload)
    }


    function onRecentClick(item: RecentItem) {
        if (isRecentCallConference(item) || isRecentExtChatConference(item)) {
            return openRecentConferenceModal(item)
        }

        if (item.number) {
            setDialNumber(item.number)
        }
    }

    return (
        <div className={tcn('flex min-h-0 flex-auto flex-col overflow-auto')}>
            {selectedTab === 'favorites' && (
                <div className={paneWrapperClassName}>
                    {SearchInput}
                    <FavoritesList
                        onClick={({channel}) => {
                            return channel.number && setDialNumber(channel.number)
                        }}
                        transferMode={transferMode}
                        transferControlsStyle="replace"
                        isHighlighted={({channel}) => channel.number === dialNumber}
                        filter={favoritesFilter}
                    />
                </div>
            )}

            {selectedTab === 'recent' && (
                <div className={paneWrapperClassName}>
                    {SearchInput}
                    <RecentList
                        onClick={onRecentClick}
                        isHighlighted={item => item.number === dialNumber}
                        renderContext="transfer"
                        filter={recentFilter}
                        type={recentsType}
                        transferMode={transferMode}
                        transferControlsStyle="replace"
                    />
                </div>
            )}

            {selectedTab === 'directory' && (
                <div className={paneWrapperClassName}>
                    {SearchInput}
                    {activeItemIsExternalChat ? (
                        <DirectoryInternal
                            onClick={(_evt, item) => {
                                const phone = getDirectoryItemPhone(item)
                                phone && setDialNumber(phone)
                            }}
                            query={search}
                            filter={directoryFilter}
                            transferMode={transferMode}
                            isHighlighted={item => getDirectoryItemPhone(item) === dialNumber}
                            transferControlsStyle="replace"
                        />
                    ) : (
                        <DirectoryCombined
                            onClick={(_evt, item) => {
                                const phone = getDirectoryItemPhone(item)
                                phone && setDialNumber(phone)
                            }}
                            query={search}
                            filter={directoryFilter}
                            transferMode={transferMode}
                            isHighlighted={item => getDirectoryItemPhone(item) === dialNumber}
                            transferControlsStyle="replace"
                        />
                    )}
                </div>
            )}
        </div>
    )
}
