import type {AriaAttributes, CSSProperties, ReactNode, RefCallback} from 'react'
import React, {useCallback} from 'react'

import {tcn} from '@bpinc/lib-tailwind-classnames'


interface Option {
    value: string
    label: string
}

export type DropdownProps = AriaAttributes & {
    inputRef?: RefCallback<HTMLSelectElement>
    options?: Option[]
    className?: string
    value?: Option['value']
    name?: string
    label?: ReactNode
    error?: ReactNode
    placeholder?: string
    noValueLabel?: string | null
    labelClassName?: string
    errorClassName?: string
    onChange(value: Option['value'] | undefined, extra: { name?: string }): void
    style?: CSSProperties
}

const DEFAULT_LABEL = 'Please select'

export const Dropdown: React.FC<DropdownProps> = props => {
    const {
        options,
        onChange,
        className,
        name,
        label,
        error,
        value,
        noValueLabel = DEFAULT_LABEL,
        inputRef,
        placeholder,
        labelClassName,
        errorClassName,
        ...selectProps
    } = props

    const handleChange = useCallback((e: React.ChangeEvent<HTMLSelectElement>) => {
        const el = e.target
        if (el) {
            const newValue = el.value || undefined
            onChange(newValue, {name})
        }
    }, [name, onChange])

    return (
        <div
            className={tcn(
                className,
                'relative flex min-w-0 justify-between',
                {
                    'text-red-500': error,
                },
            )}
        >
            {label && <label className={tcn(labelClassName, 'mt-1.5 flex-1')}>{label}</label>}

            <select
                placeholder={placeholder}
                ref={inputRef}
                value={value || ''}
                onChange={handleChange}
                {...selectProps}
                className="max-w-full flex-[2] rounded-md border border-gray-400 bg-white p-1"
            >
                {noValueLabel && <option value="">{noValueLabel}</option>}
                {options?.map((o, i) => (
                    <option key={i} value={o.value}>
                        {o.label || o.value}
                    </option>
                ))}
            </select>
            {error && <p className={errorClassName} role="alert aria-atomic">{error}</p>}
        </div>
    )
}
