import type {FC} from 'react'
import React from 'react'

import {useGetContactPictureUrl} from '@bpinc/ad-contacts-state-hooks'
import type {ContactShortType} from '@bpinc/ad-contacts-state-types'
import {useFormatting} from '@bpinc/ad-formatting-context'
import {formatPhone} from '@bpinc/ad-formatting-helpers'
import {AvatarLegacy} from '@bpinc/lib-avatar-ui'
import {tcn} from '@bpinc/lib-tailwind-classnames'


interface ContactPreview extends ContactShortType {
    className?: string
}

export const ContactPreview: FC<Partial<ContactPreview>> = ({firstName, lastName, id, phone, email, className}) => {
    const {formatFullName} = useFormatting()
    const getContactPictureUrl = useGetContactPictureUrl()
    const fullName = formatFullName(firstName, lastName)
    return (
        <section className={tcn(className, 'flex')}>
            <AvatarLegacy
                size={60}
                firstName={firstName}
                lastName={lastName}
                initialsClassName="text-2xl"
                url={getContactPictureUrl(id)}
                type={id ? 'contact' : 'unknown'}
            />
            <div className="ml-2">
                {fullName && <h4 className="mx-0 mb-0 mt-0.5 text-base">{fullName}</h4>}
                {phone && <p className="mx-0 mb-0 mt-1 text-main-secondary">{formatPhone(phone)}</p>}
                {email && <p className="mx-0 mb-0 mt-1 text-main-secondary">{email}</p>}
            </div>
        </section>
    )
}
