import type {DirectoryItem} from '@bpinc/ad-directory-state-types'
import {useSubscribeForHuntGroupDetails, useSubscribeForServiceDetails} from '@bpinc/ad-service-details-state-context'
import {useSubscribeForUserDetails} from '@bpinc/ad-user-details-state-context'
import {isHuntGroup, isDirectoryEntry, isUserWithDetails} from '@bpinc/lib-helpers-ui'


export function useSubscribeForUserOrServiceDetails(item: DirectoryItem, isVisible?: boolean) {
    useSubscribeForUserDetails(isUserWithDetails(item) ? item.userId : undefined, isVisible)
    useSubscribeForServiceDetails(isDirectoryEntry(item) ? item.serviceId : undefined, isVisible)
    useSubscribeForHuntGroupDetails(isHuntGroup(item) ? item.huntGroupId : undefined, isVisible)
}
