import React, {useCallback} from 'react'

import type {DispositionOption} from '@bpinc/lib-interaction-ui-hooks'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {highlighted} from '@bpinc/lib-ui-elements'


interface DispositionsListItemProps {
    className?: string
    value: string
    label: string
    isGroupLabel?: boolean
    highlight?: string
    onSelect: (option: DispositionOption) => void
}

export const DispositionsListItem: React.FC<DispositionsListItemProps> = props => {
    const {
        value,
        label,
        isGroupLabel,
        highlight,
        onSelect,
    } = props

    const selectDisposition = useCallback(() => {
        onSelect({value, label})
    }, [onSelect, value, label])

    const resolutionsListItemClassName = tcn(
        'state-layer-bg-neutral-0',
        'hover:state-layer-mix-dark-on-white-1',
        'focus:state-layer-mix-dark-on-white-2',
        'data-[selected=true]:state-layer-mix-dark-on-white-2',
        'active:state-layer-mix-dark-on-white-3',
        'flex w-full py-2.5 pl-9 pr-3',
    )

    const className = tcn(resolutionsListItemClassName, {
        'pointer-events-none font-bold': isGroupLabel,
    })


    return (
        <div
            data-selectable="dispositions-accordion"
            onClick={selectDisposition}
            key={value}
            tabIndex={isGroupLabel ? -1 : 0}
            className={className}
            data-testid="disposition-option"
        >
            {isGroupLabel ? label : highlighted({highlight, text: label})}
        </div>
    )
}
