import type {FC} from 'react'
import React, {memo} from 'react'

import {AppRoutes, useNavigationState} from '@bpinc/lib-communicator-navigation-state'
import {SmMessage} from '@bpinc/lib-external-ui-elements'
import {ErrorBoundary} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {Calendar} from '../../calendar'
import {Cases} from '../../cases'
import {ContactDetails} from '../../contact-details'
import {Dialpad} from '../../dialpad'
import {Directory} from '../../directory'
import {Favorites} from '../../favorites'
import {InteractionsPage} from '../../interactions'
import {GlobalKB} from '../../kb'
import {Recent} from '../../recent'
import {GlobalSlidingModalsList} from '../../sliding-modal'
import {HelpPage} from './HelpPage'
import {MainPageNav} from './MainPageNav'
import {MainPageTitle} from './MainPageTitle'

import styles from '../styles/MainPage.module.less'


const ErrorFallback: React.FC<{ errorMessage: string }> = ({
    errorMessage,
}) => {
    const {t} = useLocalizationState()

    return (
        <SmMessage
            className={styles.errorFallback}
            error
            header={t.ERROR_MESSAGES.unhandled_router_error()}
            content={
                process.env.NODE_ENV === 'development' &&
                `Error info: ${errorMessage}`
            }
        />
    )
}

function wrapWithErrorBoundary(Component: React.FC) {
    return (
        <ErrorBoundary fallback={ErrorFallback}>
            <Component />
        </ErrorBoundary>
    )
}

const mainPageContentConfig: Record<AppRoutes, React.ReactElement> = {
    [AppRoutes.Recents]: wrapWithErrorBoundary(Recent),
    [AppRoutes.Dial]: wrapWithErrorBoundary(Dialpad),
    [AppRoutes.Favorites]: wrapWithErrorBoundary(Favorites),
    [AppRoutes.Directory]: wrapWithErrorBoundary(Directory),
    [AppRoutes.Interactions]: wrapWithErrorBoundary(InteractionsPage),
    [AppRoutes.Cases]: wrapWithErrorBoundary(Cases),
    [AppRoutes.ContactDetails]: wrapWithErrorBoundary(ContactDetails),
    [AppRoutes.KnowledgeBase]: wrapWithErrorBoundary(GlobalKB),
    [AppRoutes.Help]: wrapWithErrorBoundary(HelpPage),
    [AppRoutes.Calendar]: wrapWithErrorBoundary(Calendar),
}

export const CommunicatorContent: FC = memo(function CommunicatorContent() {
    const {activePage} = useNavigationState()

    return (
        <>
            <MainPageTitle />
            <div
                className="relative z-0 flex w-full shrink grow flex-col overflow-hidden"
                data-testid="communicator_content"
            >
                {mainPageContentConfig[activePage.name]}
            </div>
            <MainPageNav />
            <GlobalSlidingModalsList />
        </>
    )
})
