import type {ReactNode} from 'react'
import React from 'react'

import {tcn} from '@bpinc/lib-tailwind-classnames'


type FieldArrayItemProps = {
    label?: string
    layoutClassName?: string
    children: ReactNode
}

export const FieldArrayItem: React.FC<FieldArrayItemProps> = ({label, layoutClassName, children}) => {
    return (
        <div className={tcn('flex min-w-0 flex-col', layoutClassName)}>
            {label && <label>{label}</label>}
            {children}
        </div>
    )
}
