import React, {useEffect, useReducer} from 'react'

import {useSessionState} from '@bpinc/ad-session-context'
import {getStateDuration} from '@bpinc/ad-user-details-state-context'
import type {CapacityItemType, UserDetails, UserDetailsObject} from '@bpinc/ad-user-details-state-types'
import {formatDurationShort} from '@bpinc/lib-helpers'
import {useLocalizationState} from '@bpinc/state-localization-context'


type UserOnlineStatusProps = {
    details: UserDetailsObject | undefined
}

export const UserOnlineStatus: React.FC<UserOnlineStatusProps> = ({details}) => {
    const tenantFeatures = useSessionState('tenantFeatures')

    const [_, forceUpdate] = useReducer(s => s + 1, 0)
    const stateDuration = getStateDuration(details)

    useEffect(() => {
        const interval = setInterval(forceUpdate, 1000)
        return () => clearInterval(interval)
    })

    if (!details || !tenantFeatures?.rtDataEnabled) {
        return null
    }

    return (
        <span
            data-testid="online-status"
            className="flex max-w-[138px] gap-1 overflow-hidden caption-s"
        >
            <span className="font-semibold">
                <UserStatusLabel userDetails={details?.data} />
            </span>
            {!!stateDuration && (
                <span className="text-neutral-fade">
                    {`(${formatDurationShort(stateDuration)})`}
                </span>
            )}
        </span>
    )
}

type UserStatusLabelProps = {
    userDetails: UserDetails | undefined
}

const UserStatusLabel: React.FC<UserStatusLabelProps> = ({userDetails}) => {
    const {AGENT_STATE_CONTROL: i18n} = useLocalizationState('t')

    function yellow(text: string) {
        return <span className="line-clamp-2 truncate text-on-warning">{text}</span>
    }
    function green(text: string) {
        return <span className="line-clamp-2 truncate text-on-success">{text}</span>
    }
    function red(text: string) {
        return <span className="line-clamp-2 truncate text-on-error">{text}</span>
    }
    function gray(text: string) {
        return <span className="line-clamp-2 truncate text-neutral-disabled">{text}</span>
    }
    function dark(text: string) {
        return <span className="line-clamp-2 truncate text-primary-on-primary">{text}</span>
    }

    if (!userDetails) {
        return null
    }
    if (!userDetails.isLogged) {
        return gray(i18n.presence_offline())
    }

    if (userDetails.presenceState && (!userDetails.acdState && !userDetails.isSuper && !userDetails.userType)) {
        switch (userDetails.presenceState) {
            case 'available':
                return green(i18n.presence_available())
            case 'busy':
                return dark(getInteractionBusyStatus())
            default:
                return red(i18n.presence_dnd())
        }
    }

    if (userDetails.acdState !== undefined) {
        switch (userDetails.acdState) {
            case '':
                return gray(i18n.acd_supervision())
            case 'ready':
                return green(i18n.acd_ready())
            case 'not_ready':
                return yellow(userDetails.reason ?? i18n.acd_not_ready())
            case 'busy':
                return dark(userDetails.reason ?? i18n.acd_busy())
            case 'after_call_work':
                return dark(userDetails.reason ?? i18n.acd_acw_long())
        }
    }

    if (userDetails.capacity) {
        const userHasActiveCapacityOfType = (type: CapacityItemType) => {
            const capacityItem = userDetails.capacity && userDetails.capacity[type]
            return capacityItem && capacityItem.usedSlots > 0
        }
        if (userHasActiveCapacityOfType('voice') || userHasActiveCapacityOfType('workitem')) {
            return dark(i18n.acd_busy_call())
        }
        if (userHasActiveCapacityOfType('chat')) {
            return dark(i18n.acd_busy_chat())
        }
        if (userHasActiveCapacityOfType('email')) {
            return dark(i18n.acd_busy_email())
        }
    }

    switch (userDetails.presenceState) {
        case 'available':
            return green(i18n.presence_available())
        case 'offline':
            return gray(i18n.presence_offline())
        case 'away':
            return yellow(i18n.presence_away())
        case 'dnd':
            return red(i18n.presence_dnd())
        case 'busy':
            return dark(i18n.acd_busy())
    }

    if (userDetails.isSuper) {
        return gray(i18n.acd_supervision())
    }

    return null


    function getInteractionBusyStatus(): string {
        if (userHasItemOfType('voice') || userHasItemOfType('workitem')) {
            return i18n.acd_busy_call()
        }
        if (userHasItemOfType('chat')) {
            return i18n.acd_busy_chat()
        }
        if (userHasItemOfType('email')) {
            return i18n.acd_busy_email()
        }
        return i18n.acd_busy()

        function userHasItemOfType(type: string) {
            return userDetails?.items?.some(item => item.mediaType === type)
        }
    }
}
