import type {ButtonHTMLAttributes, FC, PropsWithChildren} from 'react'
import React from 'react'

import {useContact, useContactsStateMethods} from '@bpinc/ad-contacts-state-context'
import type {ContactType} from '@bpinc/ad-contacts-state-types'
import {stopPropagation, useEvent} from '@bpinc/lib-helpers'
import {ButtonIcon, IconArrowDown16, IconArrowUp16, TriggerDropdown} from '@bpinc/lib-jskit-ui'
import {assertDefined} from '@bpinc/lib-types-helpers'
import {Loader} from '@bpinc/lib-ui-elements'

import type {DropdownOption} from './InteractionButtonIcon'


type HtmlProps = Omit<ButtonHTMLAttributes<HTMLButtonElement>, 'onClick'>

type OptionsExtractor = (contactData: ContactType) => DropdownOption[]

type Props = PropsWithChildren<HtmlProps & {
    contactId: string
    tooltip: string
    popupOpen: boolean
    optionsExtractor: OptionsExtractor
    OptionComponent: FC<Omit<DropdownOption, 'key'>>
    onPressed: (value: string) => void
    onPopupOpenChange: (value: boolean) => void
}>

export const ExternalContactInteractionButtonIcon: React.FC<Props> = ({
    contactId,
    tooltip,
    popupOpen,
    optionsExtractor,
    OptionComponent,
    onPressed,
    onPopupOpenChange,
    children,
    ...htmlProps
}) => {

    const {getContact} = useContactsStateMethods()
    const {data: fullContactData, isLoading} = useContact(contactId) ?? {}

    const loadFullContactDataAndContinue = useEvent(() => {
        getContact({id: contactId})
            .then(contactData => {
                if (!contactData) {
                    return
                }
                const options = optionsExtractor(contactData)

                if (options.length > 1) {
                    onPopupOpenChange(true)
                } else if (options.length === 1) {
                    onPressed(options[0].value)
                }
            })
    })

    const keyDownHandler = useEvent((e: React.KeyboardEvent) => {
        if (onPopupOpenChange && e.key === 'Escape') {
            e.stopPropagation()
            onPopupOpenChange(!popupOpen)
        }
    })

    if (!fullContactData) {
        if (isLoading) {
            return (
                <ButtonIcon tooltip={tooltip} className="relative [&>span]:h-4 [&>span]:w-4">
                    <Loader active size="mini" />
                </ButtonIcon>
            )
        }
        return (
            <ButtonIcon
                onClick={loadFullContactDataAndContinue}
                tooltip={tooltip}
                {...htmlProps}
            >
                {children}
            </ButtonIcon>
        )
    }

    const options = optionsExtractor(fullContactData)

    return options.length > 1 ? (
        <TriggerDropdown
            modal={false}
            inPortal={false}
            align="start"
            open={popupOpen}
            onOpenChange={onPopupOpenChange}
            onKeyDown={keyDownHandler}
            onClick={stopPropagation}
            onFocus={stopPropagation}
            className="[&>*:not(:last-child)]:border-b [&>*:not(:last-child)]:border-default"
            options={options.map(({key, ...props}) => (
                <button key={key} onClick={() => onPressed(props.value)}>
                    <OptionComponent {...props} />
                </button>
            ))}
        >
            {isOpened => (
                <ButtonIcon
                    tooltip={tooltip}
                    data-focused={isOpened}
                    iconRight={isOpened ? <IconArrowUp16 /> : <IconArrowDown16 />}
                >
                    {children}
                </ButtonIcon>
            )}
        </TriggerDropdown>
    ) : (
        <ButtonIcon
            onClick={() => {
                assertDefined(options[0])
                onPressed(options[0].value)
            }}
            tooltip={tooltip}
            disabled={!options.length}
            {...htmlProps}
        >
            {children}
        </ButtonIcon>
    )
}
