import {useChatUnread, usePersonalMessagesUnreadAmount} from '@bpinc/ad-interaction-chat-state-context'
import {useInternalChatTotalUnread} from '@bpinc/ad-interaction-internal-chat-state-context'


export function useUnreadAmount() {
    const unreadExternal = useChatUnread()
    const unreadPersonalMessagesAmount = usePersonalMessagesUnreadAmount()

    const unreadInternal = useInternalChatTotalUnread()

    return unreadPersonalMessagesAmount + unreadExternal + unreadInternal
}
