import {useCallback} from 'react'

import {useContact} from '@bpinc/ad-contacts-state-context'
import {useDIDNumberState} from '@bpinc/ad-did-number-context'
import {useErrorValidation} from '@bpinc/ad-error-hooks'
import {useInteractionChatStateMethods} from '@bpinc/ad-interaction-chat-state-context'
import {useInteractions, useInteractionStateMethods} from '@bpinc/ad-interaction-state-context'
import {getAssociatedChatServiceId, isWorkItem} from '@bpinc/ad-interaction-state-methods'
import type {ActionCase, FormButtonAction} from '@bpinc/ad-request-urls'
import {useServiceState} from '@bpinc/ad-service-context'
import {useServicesDispositionsState} from '@bpinc/ad-services-dispositions-context'
import {useSessionState} from '@bpinc/ad-session-context'
import {AppRoutes, useNavigationState} from '@bpinc/lib-communicator-navigation-state'
import {checkConditions} from '@bpinc/lib-form-builder'
import {useDisconnectButtonClickHandler} from '@bpinc/lib-helpers-ui'
import {useNotificationsState} from '@bpinc/lib-notifications-context'

import type {ChangeFormPageId} from './FormBuilderButton'


 type UseButtonHandlersParams = {
     contactId?: string
     actionCases?: ActionCase[]
     action: FormButtonAction
     values?: Record<string, unknown>
     changeFormPageId?: ChangeFormPageId
 }

export function useButtonHandlers({
    contactId,
    actionCases,
    values,
    action,
    changeFormPageId,
}: UseButtonHandlersParams) {
    const {initiateCall, initiateChat} = useInteractionStateMethods()
    const contact = useContact(contactId)
    const {addMessageToPending} = useInteractionChatStateMethods()
    const setActivePage = useNavigationState('setActivePage')
    const {privileges} = useSessionState()
    const privilegesLoaded = !!Object.keys(privileges).length
    const canStartExternalChatPrivilege = privilegesLoaded && privileges.CAN_START_EXTERNAL_CHAT

    const interactionStateMethods = useInteractionStateMethods()
    const {services} = useServicesDispositionsState()
    const {selectedService} = useServiceState()
    const {selectedDIDNumber} = useDIDNumberState()
    const {showNotification, hideNotificationsByTag} = useNotificationsState('methods')
    const {getServiceAndDIDNumbersErrors} = useErrorValidation()

    const {dispositions} = useServicesDispositionsState()

    const associatedChatServiceId = getAssociatedChatServiceId(services, selectedService)

    const startAction = useCallback(
        function startActionCallback(actionCallback: (actionCase: ActionCase) => void) {
            actionCases?.forEach(actionCase => {
                const validConditions = checkConditions({
                    conditions: actionCase?.conditions,
                    values,
                })

                if (!validConditions) {
                    return
                }

                actionCallback(actionCase)
            })
        },
        [values, actionCases],
    )

    const startCallAction = useCallback(
        function startCallCallback(actionCase: ActionCase) {
            if (actionCase?.numberToCall) {
                const errors = getServiceAndDIDNumbersErrors({
                    skip: ['noChatInService'],
                    interactionType: 'call',
                })

                if (errors.length) {
                    showNotification(errors[0])
                    return
                }
                hideNotificationsByTag('interaction-init')

                initiateCall({
                    phone: actionCase.numberToCall,
                    serviceId: selectedService,
                    accessNumberId: selectedDIDNumber,
                    contactId,
                    firstName: contact?.data?.firstName,
                    lastName: contact?.data?.firstName,
                    origination: 'contact-profile',
                })
                setActivePage({name: AppRoutes.Interactions})
            }
        },
        [
            contact?.data?.firstName,
            contactId,
            getServiceAndDIDNumbersErrors,
            hideNotificationsByTag,
            initiateCall,
            selectedDIDNumber,
            selectedService,
            setActivePage,
            showNotification,
        ],
    )

    const startSMSChatAction = useCallback(
        function startSMSChatCallback(actionCase: ActionCase) {
            if (!canStartExternalChatPrivilege) {
                return
            }

            if (actionCase?.numberToSMS) {
                if (actionCase?.messageToSMS) {
                    addMessageToPending(
                        {
                            content: actionCase.messageToSMS,
                            channel: 'sms',
                            type: 'message',
                        },
                        actionCase.numberToSMS,
                    )
                }

                const errors = getServiceAndDIDNumbersErrors({interactionType: 'sms'})

                if (errors.length) {
                    showNotification(errors[0])
                    return
                }
                hideNotificationsByTag('interaction-init')

                initiateChat({
                    channel: 'sms',
                    address: actionCase.numberToSMS,
                    serviceId: associatedChatServiceId,
                    displayData: {
                        firstName: contact?.data?.firstName,
                        lastName: contact?.data?.lastName,
                    },
                    accessNumberId: selectedDIDNumber,
                    contactId,
                    origination: 'contact-profile',
                })
                setActivePage({name: AppRoutes.Interactions})
            }
        },
        [
            contact?.data?.firstName,
            contact?.data?.lastName,
            addMessageToPending,
            associatedChatServiceId,
            canStartExternalChatPrivilege,
            contactId,
            getServiceAndDIDNumbersErrors,
            initiateChat,
            hideNotificationsByTag,
            selectedDIDNumber,
            setActivePage,
            showNotification,
        ],
    )

    const searchKBAction = useCallback(
        function searchKBCallback(actionCase: ActionCase) {
            if (actionCase?.searchText) {
                setActivePage({
                    name: AppRoutes.KnowledgeBase,
                    params: {
                        searchText: actionCase.searchText,
                    },
                })
            }
        },
        [setActivePage],
    )

    const changeFormPageAction = useCallback(
        function changeFormPageActionCallback(actionCase: ActionCase) {
            if (!actionCase.page) {
                return
            }

            changeFormPageId?.(actionCase.page)
        },
        [changeFormPageId],
    )

    const items = useInteractions()
    const handleDisconnectButtonClick = useDisconnectButtonClickHandler()

    const completeItemAction = useCallback(
        function completeItemActionCallback() {
            const itemWithTheSameContactId = items.find(item => item.contactId === contactId)
            if (itemWithTheSameContactId) {
                handleDisconnectButtonClick(itemWithTheSameContactId, {
                    completeAfterDisconnect: true,
                    openInteractionsPageOnError: true,
                })
            }
        },
        [contactId, handleDisconnectButtonClick, items],
    )

    const sendDispositionAction = useCallback(
        function sendDispositionActionCallback(actionCase: ActionCase) {
            const itemWithTheSameContactId = items.find(item => item.contactId === contactId)

            if (!itemWithTheSameContactId) {
                return
            }

            const dispositionName = actionCase.disposition

            const serviceId = isWorkItem(itemWithTheSameContactId)
                ? itemWithTheSameContactId?.workitemData?.serviceId
                : itemWithTheSameContactId?.scenarioData?.serviceId

            const dispositionsObject = serviceId ? dispositions[serviceId] : {}

            const dispositionToSelect = Object.values(dispositionsObject).find(
                disposition => disposition.name === dispositionName,
            )

            if (dispositionToSelect) {
                interactionStateMethods.setDisposition({
                    itemId: itemWithTheSameContactId.id,
                    disposition: {
                        id: dispositionToSelect.id,
                        name: dispositionToSelect.name,
                    },
                })
            }

            handleDisconnectButtonClick(itemWithTheSameContactId, {
                completeAfterDisconnect: true,
                openInteractionsPageOnError: true,
                disposition: dispositionToSelect,
            })
        },
        [contactId, dispositions, handleDisconnectButtonClick, interactionStateMethods, items],
    )

    const completeItem = useCallback(
        function completeItemCallback() {
            startAction(completeItemAction)
        },
        [completeItemAction, startAction],
    )

    const sendDisposition = useCallback(
        function sendDispositionCallback() {
            startAction(sendDispositionAction)
        },
        [startAction, sendDispositionAction],
    )

    const startCall = useCallback(
        function startCallCallback() {
            startAction(startCallAction)
        },
        [startAction, startCallAction],
    )

    const startSMSChat = useCallback(
        function startSMSChatCallback() {
            startAction(startSMSChatAction)
        },
        [startAction, startSMSChatAction],
    )

    const searchKB = useCallback(
        function startSMSChatCallback() {
            startAction(searchKBAction)
        },
        [searchKBAction, startAction],
    )

    const changeFormPage = useCallback(
        function changeFormPageCallback() {
            startAction(changeFormPageAction)
        },
        [changeFormPageAction, startAction],
    )

    return useCallback(
        function handleClickCallback() {
            if (action === 'Start SMS') {
                startSMSChat()
            }

            if (action === 'Call number') {
                startCall()
            }

            if (action === 'Search Knowledge Base') {
                searchKB()
            }

            if (action === 'Complete') {
                completeItem()
            }

            if (action === 'Disposition') {
                sendDisposition()
            }

            if (action === 'Go To Page') {
                changeFormPage()
            }
        },
        [
            action,
            startSMSChat,
            startCall,
            searchKB,
            completeItem,
            sendDisposition,
            changeFormPage,
        ],
    )
}
