import {useMemo, useState} from 'react'
import type {UseFormReturn} from 'react-hook-form'

import {useContactsStateMethods} from '@bpinc/ad-contacts-state-context'
import type {ContactDetailsType, ContactDetailsUnflatten, WithId} from '@bpinc/ad-contacts-state-types'
import {useFormatting} from '@bpinc/ad-formatting-context'
import {flatten, unflatten} from '@bpinc/lib-flat'
import {Notifications, useNotificationsState} from '@bpinc/lib-notifications-context'
import {useOpenGlobalSlidingModal} from '@bpinc/lib-sliding-modal-context'
import {useLocalizationState} from '@bpinc/state-localization-context'

import type {NonForkableFields} from '../../hooks/useFormElementsToViews'
import {NON_FORKABLE_FIELDS} from '../../hooks/useFormElementsToViews'
import type {MergeContactsProps} from './'
import type {MergeContactsFormProps} from './MergeContactsForm'


interface UseOnSubmitParams extends MergeContactsProps {
    distContactDetails: MergeContactsFormProps['distContactDetails']
    handleSubmit: UseFormReturn<Partial<WithId<ContactDetailsType>>>['handleSubmit']
    contactId: string
}

export function useOnSubmit({
    handleSubmit,
    contactId,
    distContactDetails,
    onMerge,
    srcContactDetails,
}: UseOnSubmitParams) {
    const [submitting, setSubmitting] = useState(false)

    const {t} = useLocalizationState()
    const {showNotification} = useNotificationsState('methods')

    const openGlobalSlidingModal = useOpenGlobalSlidingModal()
    const {getNamesFromDisplayName} = useFormatting()
    const contactsStateMethods = useContactsStateMethods()
    const onSubmit = useMemo(
        function memoizeSubmit() {
            return handleSubmit(function onSuccess(data) {
                if (!contactId) {
                    return
                }

                setSubmitting(true)

                const submitValues = unflatten<ContactDetailsType, ContactDetailsUnflatten>(
                    splitNames(getMergedDetails()),
                )

                contactsStateMethods.saveContactDetails(contactId, submitValues, {
                    onSuccess() {
                        onMerge?.({...submitValues, id: contactId}, srcContactDetails)
                        openGlobalSlidingModal('mergeContacts')
                        setSubmitting(false)
                    },
                    onError() {
                        setSubmitting(false)
                        showNotification({
                            type: 'error',
                            id: Notifications.ContactMergeError,
                            tags: ['current-view'],
                            message: t.VALIDATION.CONTACT_MERGE_ERROR(),
                            duration: 10_000,
                        })
                    },
                })

                function getMergedDetails() {
                    return {
                        ...distContactDetails,
                        ...mergedNonForkableFields(),
                        ...flatten<ContactDetailsUnflatten, ContactDetailsType>(data),
                    }
                }
                function splitNames(details: ContactDetailsType) {
                    const updatedDetails = {...details}
                    const fullName = updatedDetails['Contact.name']
                    if (typeof fullName === 'string') {
                        const [firstName, lastName] = getNamesFromDisplayName(fullName)
                        delete updatedDetails['Contact.name']

                        updatedDetails['Contact.first_name'] = firstName
                        updatedDetails['Contact.last_name'] = lastName
                    }

                    return updatedDetails
                }

                function mergedNonForkableFields() {
                    return NON_FORKABLE_FIELDS.reduce((fields, key) => {
                        return {...fields, ...bothContactDetails(key)}
                    }, {})
                }

                function bothContactDetails(key: NonForkableFields) {
                    return {[key]: [...(srcContactDetails?.[key] ?? []), ...(distContactDetails?.[key] ?? [])]}
                }
            })
        },
        [
            contactId,
            contactsStateMethods,
            distContactDetails,
            getNamesFromDisplayName,
            handleSubmit,
            onMerge,
            showNotification,
            srcContactDetails,
            t.VALIDATION,
            openGlobalSlidingModal,
        ],
    )

    return {submitting, onSubmit}
}
