import {useMemo} from 'react'
import {createSelector} from 'reselect'

import {useInteractionStateSelector} from '@bpinc/ad-interaction-state-context'
import {getAssociatedItems, isCall, isChat} from '@bpinc/ad-interaction-state-methods'
import type {InteractionStateDataWithComputeds, VoiceTranscriptSuggestion} from '@bpinc/ad-interaction-state-types'
import {truthy} from '@bpinc/lib-types-helpers'


export function useBotSuggestions(itemId: string) {
    const botSuggestionSelector = useMemo(() => selectBotSuggestions(itemId), [itemId])
    return useInteractionStateSelector(botSuggestionSelector)
}

export function useBotSuggestionsByType(itemId: string) {
    const botSuggestionSelector = useMemo(() => selectBotSuggestionsByType(itemId), [itemId])
    return useInteractionStateSelector(botSuggestionSelector)
}

function selectBotSuggestionsByType(itemId: string) {
    return createSelector(
        (state: InteractionStateDataWithComputeds) => state.items.find(item => item.id === itemId),
        (state: InteractionStateDataWithComputeds) => state.items,
        (item, items) => {
            const associatedItems = getAssociatedItems({item, items})
            const allItems = truthy([...associatedItems, item])

            return allItems.reduce<Record<'voice' | 'chat', VoiceTranscriptSuggestion[]>>((acc, innerItem) => {
                if (isCall(innerItem)) {
                    acc.voice = innerItem?.botSuggestions ?? []
                } else if (isChat(innerItem)) {
                    acc.chat = innerItem?.botSuggestions ?? []
                }
                return acc
            }, {voice: [], chat: []})
        })
}

function selectBotSuggestions(itemId: string) {
    return createSelector(
        (state: InteractionStateDataWithComputeds) => state.items.find(item => item.id === itemId),
        (state: InteractionStateDataWithComputeds) => state.items,
        (item, items) => {
            const associatedItems = getAssociatedItems({item, items})
            const allItems = truthy([...associatedItems, item])

            return allItems.flatMap(innerItem => innerItem?.botSuggestions ?? [])
        })
}
