import React from 'react'

import {formatPhone} from '@bpinc/ad-formatting-helpers'
import type {FavoritesChannelMedia, MessengerChannelType} from '@bpinc/ad-transport-types'
import {IconEmail16, IconPhone16Filled} from '@bpinc/lib-jskit-ui'
import type {FCWithChildren} from '@bpinc/lib-types-helpers'
import {assertDefined} from '@bpinc/lib-types-helpers'
import {getChannelImageIcon, useGetMessengerName} from '@bpinc/lib-ui-elements'
import type {FavoritesChannel} from '@bpinc/state-favorites-types'
import {
    isFavoritesEmailChannel,
    isFavoritesInternalChannel,
    isFavoritesMessengerChannel,
    isFavoritesPhoneChannel,
} from '@bpinc/state-favorites-types'
import {useLocalizationState} from '@bpinc/state-localization-context'


type Props = {
    channel: FavoritesChannel
    media?: FavoritesChannelMedia
}

export const FavoriteItemBody: React.FC<Props> = ({channel, media}) => {

    const {CONTACT: i18n} = useLocalizationState('t')
    const getMessengerName = useGetMessengerName()

    if (isFavoritesInternalChannel(channel) || isFavoritesPhoneChannel(channel)) {
        assertDefined(media)
        if (media === 'voice') {
            return <Phone>{formatPhone(channel.number)}</Phone>
        }
        if (channel.number) {
            return <Chat>{formatPhone(channel.number)}</Chat>
        }
        return <Chat>{i18n.internal_messages_field_value()}</Chat>
    }

    if (isFavoritesEmailChannel(channel)) {
        return <Email>{channel.email}</Email>
    }

    if (isFavoritesMessengerChannel(channel)) {
        return (
            <Chat type={channel.messengerType}>
                {`${getMessengerName(channel.messengerType)}: ${channel.userId}`}
            </Chat>
        )
    }
    return null
}

const Email: FCWithChildren = ({children}) => {
    return (
        <span className="flex items-center gap-1">
            <IconEmail16 className="my-px mr-1 inline-block shrink-0 icon-neutral" />
            <span data-testid="email" className="truncate caption-s text-neutral-on-secondary">
                {children}
            </span>
        </span>
    )
}

const Phone: FCWithChildren = ({children}) => {
    return (
        <span className="flex items-center gap-1">
            <IconPhone16Filled className="my-px mr-1 inline-block shrink-0 icon-neutral" />
            <span data-testid="phone" className="truncate caption-s text-neutral-on-secondary">
                {children}
            </span>
        </span>
    )
}

const Chat: React.FC<{ type?: MessengerChannelType; children?: string }> = ({children, type}) => {
    const IconComponent = getChannelImageIcon(type)
    return (
        <span className="flex items-center gap-1">
            {IconComponent && (
                <IconComponent className="my-px mr-1 inline-block shrink-0 icon-neutral" />
            )}
            <span data-testid="chat" className="truncate caption-s text-neutral-on-secondary">
                {children}
            </span>
        </span>
    )
}
