import {useCallback, useMemo} from 'react'

import {useInteractionChatData} from '@bpinc/ad-interaction-chat-state-context'
import type {ChatMessage, MessageDisplayType} from '@bpinc/ad-interaction-message-types'
import {useInteraction} from '@bpinc/ad-interaction-state-context'
import {getChatParties, getInteractionStepId, isExternalChat} from '@bpinc/ad-interaction-state-methods'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {useChannelState} from '../channel-context'


export function useMessagesList(itemId: string) {
    const chatActiveItem = useInteraction(itemId)
    const {channelOptions} = useChannelState()
    const {t} = useLocalizationState()
    const chatData = useInteractionChatData(getInteractionStepId(chatActiveItem))

    const masked = chatData?.masked

    const messagesList = useMemo(
        function memoizeList() {
            return (isExternalChat(chatActiveItem) && chatData?.messages) || []
        },
        [chatActiveItem, chatData],
    )

    const addUserId = useCallback(
        (message: ChatMessage) => {
            const partiesList = getChatParties(chatActiveItem)
            const currentParty = partiesList?.find(party => party.party_id === message.partyId)
            const userId = message.userId || (currentParty && currentParty.user_id)
            return {...message, userId}
        },
        [chatActiveItem],
    )

    const maskMessage = useCallback(
        function maskMessageCallback(message: ChatMessage): ChatMessage {
            const maskedMessage = message.msgId ? masked?.messages?.[message.msgId] : undefined
            const fileIsMasked = message.fileId ? masked?.files.includes(message.fileId) : undefined

            if (maskedMessage || fileIsMasked) {
                const updatedMessage = {...message}

                updatedMessage.maskedText = maskedMessage?.maskedText
                updatedMessage.fileIsMasked = fileIsMasked

                return updatedMessage
            }

            return message

        }, [masked],
    )

    const localizeSystemMessage = useCallback(
        (message: ChatMessage): ChatMessage => {
            message.eventType = message.msg
            if (message.needLocalization && message.msg) {
                let args: string[] = []
                const partyName = message.partyName || 'unknown party'
                if (message.msg === 'external_chat_party_joined_new' || message.msg === 'external_chat_party_left_new') {
                    return {
                        ...message,
                        msg: t.EXTERNAL_CHAT[message.msg](),
                    }
                }
                // @DEPRECATED (remove after tests)
                if (message.msg === 'external_chat_party_joined' || message.msg === 'external_chat_party_left') {
                    args = [partyName]
                }
                // @DEPRECATED (remove after tests)
                // special case - special message for supervisor in Coaching mode
                if (message.msg === 'external_chat_party_joined_new' && message.qmMode === 'whisper') {
                    return {
                        ...message,
                        msg: t.EXTERNAL_CHAT.external_chat_party_coaching(partyName),
                    }
                }

                if (message.msg === 'message_connection_accepted') {
                    if (message.channel === 'sms') {
                        args = [t.GLOBAL.chat_type_sms()]
                    } else {
                        const selectedChannel = channelOptions.find(channel => channel.value === message.channel)

                        args = selectedChannel ? [selectedChannel?.text] : []
                    }
                }

                if (message.msg in t.EXTERNAL_CHAT) {
                    const msg = message.msg as Exclude<keyof typeof t.EXTERNAL_CHAT, 'sms_messages_number'>
                    return {
                        ...message,
                        msg: t.EXTERNAL_CHAT[msg](args[0], args[1], args[2]),
                    }
                }
            }
            return message
        },
        [channelOptions, t],
    )

    const msgHasDisplayTypes = useCallback((displayTypes: MessageDisplayType[]) => {
        return function skipMsgWithDisplayTypesInner(message: ChatMessage) {
            return displayTypes.some(type => type === message.displayType)
        }
    }, [])

    return useMemo(
        () => ({
            messagesList,
            addUserId,
            localizeSystemMessage,
            msgHasDisplayTypes: msgHasDisplayTypes,
            maskMessage,
        }),
        [messagesList, addUserId, localizeSystemMessage, msgHasDisplayTypes, maskMessage],
    )
}
