import React, {useMemo} from 'react'
import type {CellContext} from '@tanstack/react-table'
import {createColumnHelper} from '@tanstack/react-table'

import type {CasesListItem} from '@bpinc/ad-case-state-types'
import {useFormatting} from '@bpinc/ad-formatting-context'
import {formatDate} from '@bpinc/ad-formatting-helpers'
import {Checkbox} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'


const columnHelper = createColumnHelper<CasesListItem>()

export const useCasesTableColumns = () => {
    const {t} = useLocalizationState()
    const {formatFullName} = useFormatting()

    return useMemo(() => [
        columnHelper.display({
            id: 'select',
            header: ({table}) => (
                <Checkbox
                    checked={table.getIsAllRowsSelected()}
                    indeterminate={table.getIsSomeRowsSelected()}
                    onChange={checked => table.toggleAllPageRowsSelected(checked)}
                />
            ),
            cell: ({row}) => (
                <Checkbox
                    checked={row.getIsSelected()}
                    disabled={!row.getCanSelect()}
                    onChange={checked => row.toggleSelected(checked)}
                />
            ),
            enableHiding: false,
            size: 41,
        }),
        columnHelper.accessor(item => formatFullName(item.reporterFirstName, item.reporterLastName) || t.GLOBAL.unidentified(), {
            id: 'title',
            header: t.CASE_SEARCH.caseSearch_case_column(),
            cell: ({getValue, row}) => {
                const title = row.original.title
                return (
                    <>
                        <p className="truncate" title={getValue()}>{getValue()}</p>
                        {title && <p className="truncate" title={title}>{title}</p>}
                    </>
                )
            },
            enableHiding: false,
            size: 0,
        }),
        columnHelper.accessor('queuedCase.ownerName', {
            id: 'ownerName',
            header: t.EMAIL.search_queue_case_content_location(),
            size: 100,
        }),
        columnHelper.accessor('categoryName', {
            id: 'categoryName',
            header: t.EMAIL.search_queue_case_category(),
            size: 100,
        }),
        columnHelper.accessor('status', {
            id: 'status',
            header: t.EMAIL.case_view_status_label(),
            cell: renderTruncatedValue,
            size: 80,
        }),
        columnHelper.accessor(item => formatDate(item.createdTime), {
            id: 'createdTime',
            header: t.EMAIL.search_queue_case_content_date(),
            size: 110,
        }),
        columnHelper.accessor('queuedCase.sla', {
            id: 'sla',
            header: t.EMAIL.search_queue_case_content_sla(),
            size: 70,
        }),
        columnHelper.accessor('lastService', {
            id: 'lastService',
            header: t.EMAIL.search_queue_case_content_last_service(),
            cell: renderTruncatedValue,
            size: 0,
        }),
        columnHelper.accessor('lastDisposition', {
            id: 'lastDisposition',
            header: t.EMAIL.search_queue_case_content_disposition(),
            cell: renderTruncatedValue,
            size: 80,
        }),
    ], [formatFullName, t.CASE_SEARCH, t.EMAIL])
}

const renderTruncatedValue = (context: CellContext<CasesListItem, string>) => {
    return <div className="truncate" title={context.getValue()}>{context.getValue()}</div>
}
