import React, {useMemo} from 'react'

import {useLocalizationState} from '@bpinc/state-localization-context'


type VoiceTranscriptConfidenceProps = {
    confidence: number
    className?: string
}

function hsv2rgb(h: number, s: number, v: number) {
    // adapted from http://schinckel.net/2012/01/10/hsv-to-rgb-in-javascript/
    let rgb, i, data = []
    if (s === 0) {
        rgb = [v, v, v]
    } else {
        h = h / 60
        i = Math.floor(h)
        data = [v * (1 - s), v * (1 - s * (h - i)), v * (1 - s * (1 - (h - i)))]
        switch (i) {
            case 0:
                rgb = [v, data[2], data[0]]
                break
            case 1:
                rgb = [data[1], v, data[0]]
                break
            case 2:
                rgb = [data[0], v, data[2]]
                break
            case 3:
                rgb = [data[0], data[1], v]
                break
            case 4:
                rgb = [data[2], data[0], v]
                break
            default:
                rgb = [v, data[0], data[1]]
                break
        }
    }
    return '#' + rgb.map(code => {
        return ('0' + Math.round(code * 255).toString(16)).slice(-2)
    }).join('')
}

type ConfidenceGrade = 'high' | 'medium' | 'low'

export const VoiceTranscriptConfidence: React.FC<VoiceTranscriptConfidenceProps> = ({className, confidence}) => {

    const {t} = useLocalizationState()
    const color = hsv2rgb(Math.floor(confidence * 120), .66, 1)
    const style = useMemo(() => ({backgroundColor: color}), [color])

    const confidenceGrade: ConfidenceGrade = confidence > .7 ? 'high' : confidence > .4 ? 'medium' : 'low'
    const confidencePercentage = Math.floor(confidence * 100)

    const messages = useMemo<Record<ConfidenceGrade, string>>(() => ({
        'high': t.ACTIVITY_HISTORY.call_message_transcript_confidence_high(),
        'medium': t.ACTIVITY_HISTORY.call_message_transcript_confidence_medium(),
        'low': t.ACTIVITY_HISTORY.call_message_transcript_confidence_low(),
    }), [t])
    const title = messages[confidenceGrade] + ` (${confidencePercentage}%)`

    return (
        <span
            className={className}
            style={style}
            title={title}
            data-testid="message-confidence"
            data-value={confidencePercentage}
            data-confidence-low={confidenceGrade === 'low'}
            data-confidence-medium={confidenceGrade === 'medium'}
            data-confidence-high={confidenceGrade === 'high'}
        />
    )
}
