import React from 'react'
import {Button} from '@servicepattern/ui'

import {useFormatting} from '@bpinc/ad-formatting-context'
import {IconClose24} from '@bpinc/lib-jskit-ui'

import type {User} from '../../helpers'
import {isDirectoryEntry, isMsTeamsChannel} from '../../helpers'


interface HeaderProps {
    user: User
    onGoBack: () => void
}

export const Header: React.FC<HeaderProps> = ({
    user,
    onGoBack,
}) => {
    const {formatFullName} = useFormatting()

    const title = (isDirectoryEntry(user) || isMsTeamsChannel(user))
        ? user.title
        : formatFullName(user.firstName ?? '', user.lastName)

    return (
        <div className="relative border-b p-6">
            <h1 className="truncate heading-s text-primary-on-primary">
                {title}
            </h1>

            <Button
                icon={<IconClose24 />}
                variant="ghost"
                onClick={onGoBack}
                className="absolute right-6 top-1/2 -translate-y-1/2"
            />
        </div>
    )
}
