import React from 'react'
import classNames from 'classnames'
import {noop} from 'lodash-es'
import {useContext} from 'use-context-selector'

import type {FormDefinitionFieldCheckbox, FormDefinitionPageItem} from '@bpinc/ad-request-urls'
import {getFormElementStyle, getPageItemCSSProperties} from '@bpinc/lib-form-builder'
import {Checkbox} from '@bpinc/lib-ui-elements'

import {FormBuilderContext} from '../../FormBuilder'
import {ViewItem} from '../common/ViewItem'
import {CheckboxFieldBase} from './CheckboxFieldBase'


export const CheckboxField: React.FC<{
    pageItem: FormDefinitionPageItem
    field: FormDefinitionFieldCheckbox
}> = ({
    pageItem,
    field,
}) => {
    const {
        form,
        view,
    } = useContext(FormBuilderContext)

    if (view) {
        const viewValue = form.getValues()[field.name || '']
        const viewStyle = getPageItemCSSProperties(pageItem.style)

        if (!viewValue) {
            return null
        }

        return (
            <ViewItem
                viewStyle={viewStyle}
                label={field.label || ''}
                value={(
                    <Checkbox
                        checked={viewValue}
                        onChange={noop}
                    />
                )}
            />
        )
    }

    const style = getFormElementStyle(pageItem)

    const isChecked = form.getValues(field.name || '')

    return (
        <CheckboxFieldBase
            name={field.name || ''}
            control={form.control}
            className={classNames(`flex items-center gap-x-[--form-cellPadding] gap-y-[--form-rowHeight]`, {
                'line-through': field.crossOutWhenChecked && isChecked,
            })}
            style={style}
            label={field.label}
            required={field.required}
            disabled={field.intentCheckOnly}
            type="checkbox"
        />
    )
}
