import React, {useCallback, useEffect} from 'react'
import {TabsContent, TabsList, TabsRoot, TabsTrigger} from '@servicepattern/ui'

import {useAssociatedChatInteractionId} from '@bpinc/ad-interaction-state-context'
import {useInteractionId} from '@bpinc/lib-interaction-context'
import {assertDefined} from '@bpinc/lib-types-helpers'
import type {InteractionTab} from '@bpinc/state-interaction-ui'
import {useInteractionUIActiveTab, useInteractionUIItemStateSetter} from '@bpinc/state-interaction-ui'

import {useAutoSelectResolutionTab} from '../hooks/useAutoSelectResolutionTab'
import {useAutoSelectTranscriptTab} from '../hooks/useAutoSelectTranscriptTab'
import {useInitialTabFromPageParams} from '../hooks/useInitialTabFromPageParams'
import {useTabs} from '../hooks/useTabs'
import {InteractionTabContent} from './InteractionTabContent'


export const InteractionTabs: React.FC = React.memo(function InteractionTabs() {
    const tabs = useTabs()
    const setItemState = useInteractionUIItemStateSetter()
    const interactionId = useInteractionId()

    const activeChatId = useAssociatedChatInteractionId(interactionId)
    assertDefined(activeChatId)

    const activeTab = useInteractionUIActiveTab(activeChatId)

    useInitialTabFromPageParams(activeChatId)
    useAutoSelectTranscriptTab(activeChatId)
    useAutoSelectResolutionTab(setItemState)

    const handleTabChange = useCallback((newTabId: InteractionTab) => {
        setItemState(activeChatId, oldState => ({...oldState, activeTab: newTabId}))
    }, [activeChatId, setItemState])

    // When activeTab is not in tabs, set first tab as active
    useEffect(() => {
        const hasActiveTab = tabs.some(it => it.id === activeTab)

        if (!hasActiveTab) {
            const firstTabId = tabs[0]?.id
            setItemState(activeChatId, oldState => ({...oldState, activeTab: firstTabId}))
        }
    }, [activeChatId, activeTab, tabs, setItemState])

    return (
        <TabsRoot
            value={activeTab}
            data-test-id="interaction-tabs"
            className="flex h-full grow flex-col overflow-hidden"
        >
            <TabsList className="w-full shrink-0 px-4">
                {tabs.map(tab => (
                    <TabsTrigger
                        key={tab.id}
                        value={tab.id}
                        className={tab.className}
                        triggerClassName="py-2"
                        onClick={() => handleTabChange(tab.id)}
                        isSelected={tab.id === activeTab}
                        data-test-id={`tab-${tab.id}`}
                    >
                        {tab.content}
                    </TabsTrigger>
                ))}
            </TabsList>

            <TabsContent value={activeTab} className="flex grow flex-col overflow-auto" asChild>
                <InteractionTabContent activeChatId={activeChatId} activeTab={activeTab} />
            </TabsContent>
        </TabsRoot>
    )
})
