import React from 'react'

import type {DirectoryEntry} from '@bpinc/ad-directory-state-types'
import {isStaticEmailEntry, isStaticVoiceEntry} from '@bpinc/ad-directory-state-types'
import {useFormatting} from '@bpinc/ad-formatting-context'
import type {OpenContactDetailsParams} from '@bpinc/ad-interaction-state-hooks'
import type {InitiateCall} from '@bpinc/ad-interaction-state-types'
import {useSessionState} from '@bpinc/ad-session-context'
import {useEvent} from '@bpinc/lib-helpers'
import {
    ButtonIcon,
    IconEmail16,
    IconInformation,
    IconPhone16Filled,
    IconRoundChat16Filled,
} from '@bpinc/lib-jskit-ui'
import {assertDefined} from '@bpinc/lib-types-helpers'
import {useLocalizationState} from '@bpinc/state-localization-context'

import type {OnChatParams, OnEmailParams} from '../types'


type Props = {
    item: DirectoryEntry
    transferMode?: 'messages' | 'calls'
    onCall?: (callParams: InitiateCall) => void
    onChat?: (chatParams: OnChatParams) => void
    onEmail?: (chatParams: OnEmailParams) => void
    onDetails?: (params: OpenContactDetailsParams) => void
}

export const DirectoryEntryControls: React.FC<Props> = ({
    item,
    transferMode,
    onCall,
    onChat,
    onEmail,
    onDetails,
}) => {

    const {CONTACT: i18n} = useLocalizationState('t')
    const {phoneDeviceCapabilities} = useSessionState()
    const {getNamesFromDisplayName} = useFormatting()

    const [firstName, lastName] = getNamesFromDisplayName(item.title)

    const showCallButton =
        transferMode !== 'calls' &&
        phoneDeviceCapabilities.canDial &&
        item.mediaType === 'voice' &&
        !!onCall &&
        !!item.number

    const showChatButton =
        transferMode !== 'messages' &&
        isStaticVoiceEntry(item) &&
        !!onChat &&
        !!item.number

    const showEmailButton = isStaticEmailEntry(item) && !!onEmail && !!item.email

    const handleCall = useEvent(() => {
        assertDefined(onCall)
        assertDefined(item.number)

        onCall({
            phone: item.number,
            userId: item.id,
            firstName,
            lastName,
            serviceId: item.serviceId,
            origination: 'directory',
        })
    })

    const handleChat = useEvent(() => {
        assertDefined(onChat)
        assertDefined(item.number)

        onChat({
            phone: item.number,
            userId: item.id,
            firstName,
            lastName,
        })
    })

    const handleEmail = useEvent(() => {
        assertDefined(onEmail)
        assertDefined(item.email)
        onEmail({emailAddress: item.email})
    })

    const handleDetails = useEvent(() => {
        onDetails?.({entryId: item.id})
    })

    return (
        <div className="flex gap-0.5">
            {showCallButton && (
                <ButtonIcon
                    onClick={handleCall}
                    tooltip={i18n.button_start_call_text()}
                    data-testid="call-button"
                >
                    <IconPhone16Filled />
                </ButtonIcon>
            )}
            {showChatButton && (
                <ButtonIcon
                    onClick={handleChat}
                    tooltip={i18n.button_initiate_chat_text()}
                    data-testid="chat-button"
                >
                    <IconRoundChat16Filled />
                </ButtonIcon>
            )}
            {showEmailButton && (
                <ButtonIcon
                    onClick={handleEmail}
                    tooltip={i18n.button_send_email_text()}
                    data-testid="email-button"
                >
                    <IconEmail16 />
                </ButtonIcon>
            )}
            <ButtonIcon
                tooltip={i18n.button_show_contact_details_text()}
                data-testid="user-info-button"
                onPressed={handleDetails}
            >
                <IconInformation />
            </ButtonIcon>
        </div>
    )
}
