import React from 'react'

import {useGetAvatarUrl} from '@bpinc/ad-contacts-state-hooks'
import {isHuntGroupEntry, isStaticEmailEntry, isStaticVoiceEntry} from '@bpinc/ad-directory-state-types'
import {useFormatting} from '@bpinc/ad-formatting-context'
import {getInitials} from '@bpinc/ad-formatting-helpers'
import {MS_TEAMS_CHANNEL_AVATAR_FIRST_NAME, MS_TEAMS_CHANNEL_AVATAR_LAST_NAME} from '@bpinc/lib-avatar-ui'
import type {AvatarType} from '@bpinc/lib-jskit-ui'
import {Avatar as LibAvatar} from '@bpinc/lib-jskit-ui'

import type {User} from '../helpers'
import {isDirectoryEntry, isExternalUser, isMsTeamsChannel, isMsTeamsUser} from '../helpers'


const getAvatarType = (user: User): AvatarType => {
    if (isMsTeamsChannel(user)) {
        return 'msteams-channel'
    }
    if (isMsTeamsUser(user)) {
        return 'msteams-user'
    }
    if (isExternalUser(user)) {
        return 'contact'
    }
    if (isDirectoryEntry(user)) {
        if (isHuntGroupEntry(user)) {
            return 'hunt-group'
        } else if (isStaticVoiceEntry(user) || isStaticEmailEntry(user)) {
            return 'static-entry'
        } else {
            return 'scenario-entry'
        }
    } else {
        return 'user'
    }
}


export const Avatar: React.FC<{
    user: User
}> = ({
    user,
}) => {
    const getAvatarUrl = useGetAvatarUrl()

    const avatarPhotoUrl = isMsTeamsChannel(user) || isDirectoryEntry(user)
        ? undefined
        : getAvatarUrl({
            contactId: isExternalUser(user) ? user.Id.oid : undefined,
            userId: isExternalUser(user) ? undefined : user.userId,
            userType: isMsTeamsUser(user) ? user.userType : undefined,
            photoUrl: isMsTeamsUser(user) ? user.photoUrl : undefined,
        })

    const {getNamesFromDisplayName} = useFormatting()

    const firstName = isMsTeamsChannel(user)
        ? MS_TEAMS_CHANNEL_AVATAR_FIRST_NAME
        : isDirectoryEntry(user)
            ? getNamesFromDisplayName(user.title)?.[0]
            : user.firstName

    const lastName = isMsTeamsChannel(user)
        ? MS_TEAMS_CHANNEL_AVATAR_LAST_NAME
        : isDirectoryEntry(user)
            ? getNamesFromDisplayName(user.title)?.[1]
            : user.lastName

    const avatarInitials = getInitials(firstName, lastName)

    const type = getAvatarType(user)

    return (
        <LibAvatar
            src={avatarPhotoUrl}
            size="56"
            type={type}
            fallback={avatarInitials}
        />
    )
}
