import {useCallback, useDeferredValue, useMemo} from 'react'

import {itemTypeToTabType} from '@bpinc/ad-formatting-helpers'
import {
    isRecentCall as isCall,
    isRecentCallConference as isCallConference,
    isRecentChat as isChat,
} from '@bpinc/ad-interaction-state-methods'
import {useSessionState} from '@bpinc/ad-session-context'
import {useRecentUIState} from '@bpinc/lib-communicator-ui-context'
import type {RecentItem} from '@bpinc/state-recent-types'

import {useCallPartyUsersFilter, useChatPartyUsersFilter} from '../../directory'
import type {ItemType, RecentListRenderContext} from '../'


export interface UniqueRecentItem extends RecentItem {
    repeatCount?: number // if present, is > 1
}

interface RecentChatUniqueData {
    mediaType: 'chat'
    type: string // direction or 'missed'
    unique: boolean
}
interface RecentVoiceUniqueData {
    mediaType: 'voice'
    phone: string
    type: string // direction or 'missed'
    isConference: boolean
}
interface RecentCaseUniqueData {
    mediaType: 'email'
    caseId: string
}

type RecentItemUniqueData = RecentChatUniqueData | RecentVoiceUniqueData | RecentCaseUniqueData

type SameContactChats = {
    [type in 'contactIds' | 'emails' | 'numbers' | 'names']: {
        [key: string]: true
    }
}

interface UseFilteredListParams {
    recentList: RecentItem[]
    renderContext: RecentListRenderContext | undefined
    transferMode?: 'messages' | 'calls'
    filter?: (item: RecentItem) => boolean | undefined
    type: ItemType
}

const defaultExternalFilter = () => true

export function useFilteredList({
    type,
    transferMode,
    renderContext,
    recentList,
    filter: externalFilter = defaultExternalFilter,
}: UseFilteredListParams) {
    const {userId: selfUserId} = useSessionState()

    const searchValue = useRecentUIState(s => s.searchValue)
    const deferredSearchValue = useDeferredValue(searchValue)

    const searchValueFilter = useCallback(
        function searchValueFilterCallback(item: RecentItem) {
            return (
                //Ignore search filter in transfer modals
                renderContext === 'transfer' ||
                //Ignore filter when no search
                !deferredSearchValue ||
                //Search by number
                item.number?.includes(deferredSearchValue) ||
                //Search by name
                item.name?.toLowerCase().includes(deferredSearchValue.toLowerCase()) ||
                item.firstName?.toLowerCase().includes(deferredSearchValue.toLowerCase()) ||
                item.lastName?.toLowerCase().includes(deferredSearchValue.toLowerCase())
            )
        },
        [renderContext, deferredSearchValue],
    )

    const callUsersFilter = useCallPartyUsersFilter()
    const chatUsersFilter = useChatPartyUsersFilter()
    const combinedFilter = useCallback(
        (item: RecentItem) => {
            const isAgent = !!item.userId
            const isEMIUser = item?.userType === 'msteams'

            const hasAgentsInChatConferenceMode = !(transferMode === 'messages') || isAgent || isEMIUser
            const isCallWithSelf = isCall(item) && item.userId === selfUserId
            const isNotAPartyOfActiveInteraction =
                transferMode === 'calls'
                    ? callUsersFilter(item)
                    : transferMode === 'messages'
                        ? chatUsersFilter(item)
                        : true
            return (
                searchValueFilter(item) &&
                externalFilter(item) &&
                hasAgentsInChatConferenceMode &&
                filterByType(item, type) &&
                !isCallWithSelf &&
                isNotAPartyOfActiveInteraction
            )
        },
        [transferMode, selfUserId, callUsersFilter, chatUsersFilter, searchValueFilter, externalFilter, type],
    )

    return useMemo(
        function memoizeRecentList() {
            return keepLastOfKindFilter(recentList).filter(combinedFilter)
        },
        [combinedFilter, recentList],
    )
}

// ----------- Helpers--------------//

const filterByType = (item: RecentItem, type: ItemType) => {
    return type === 'all' || itemTypeToTabType(item.type, item.media) === type
}

function incrementRecentItemRepeatCount(items: UniqueRecentItem[]): void {
    const item = items[items.length - 1]
    if (typeof item.repeatCount === 'number') {
        item.repeatCount += 1
    } else {
        items[items.length - 1] = {...item, repeatCount: 2}
    }
}

function keepLastOfKindFilter(list: RecentItem[]): UniqueRecentItem[] {
    let lastUniqueData: RecentItemUniqueData | undefined = undefined
    const result: UniqueRecentItem[] = []
    const sameContactChats: SameContactChats = {
        contactIds: {},
        emails: {},
        numbers: {},
        names: {},
    }
    list.filter(item => isChatUnique(item, sameContactChats)) // first, keep only the latest chat from the same contact
        .forEach(item => {
            // then group repeated calls and emails from the same contact
            const uniqueData = extractRecentItemUniqueData(item) // returns undefined for chat, and undefined is always unique (not equal)
            if (result.length > 0 && equalRecentItemUniqueData(uniqueData, lastUniqueData)) {
                // list is sorted so that the most recent item is the first - so keep it there, just increment the repeat count
                incrementRecentItemRepeatCount(result)
            } else {
                result.push(item)
            }
            lastUniqueData = uniqueData
        })
    return result
}

const extractRecentItemUniqueData = (item: RecentItem): RecentItemUniqueData | undefined => {
    if (item.media === 'VOICE') {
        if (item.number) {
            const type = item.answered ? item.direction : 'missed'
            return {
                mediaType: 'voice',
                phone: item.number,
                type,
                isConference: isCallConference(item),
            }
        } else {
            return undefined
        }
    } else if (item.media === 'EMAIL') {
        if (item.caseId) {
            return {mediaType: 'email', caseId: item.caseId}
        } else {
            return undefined
        }
    } else {
        return undefined
    }
}

function equalRecentItemUniqueData(
    r1: RecentItemUniqueData | undefined,
    r2: RecentItemUniqueData | undefined,
): boolean {
    if (!r1 || !r2) {
        return false
    } else if (r1.mediaType === 'chat') {
        return false // chats are special-handled before this
    } else if (r1.mediaType === 'voice') {
        if (r2.mediaType === 'voice') {
            if (r1.isConference || r2.isConference) {
                return false
            } else {
                return r1.phone === r2.phone && r1.type === r2.type
            }
        } else {
            return false
        }
    } else if (r1.mediaType === 'email') {
        if (r2.mediaType === 'email') {
            return r1.caseId === r2.caseId
        } else {
            return false
        }
    } else {
        return false
    }
}

function isChatUnique(item: RecentItem, sameContactChats: SameContactChats): boolean {
    let unique = true
    if (isChat(item)) {
        if (item.contactId) {
            if (sameContactChats.contactIds[item.contactId]) {
                unique = false
            } else {
                sameContactChats.contactIds[item.contactId] = true
            }
        } else if (item.email) {
            if (sameContactChats.emails[item.email]) {
                unique = false
            } else {
                sameContactChats.emails[item.email] = true
            }
        } else if (item.number) {
            if (sameContactChats.numbers[item.number]) {
                unique = false
            } else {
                sameContactChats.numbers[item.number] = true
            }
        } else {
            const name = item.name || [item.firstName, item.lastName].join(' ')
            if (name.trim()) {
                if (sameContactChats.names[name]) {
                    unique = false
                } else {
                    sameContactChats.names[name] = true
                }
            }
        }
    }
    return unique
}
