import type {CSSProperties} from 'react'
import {useMemo} from 'react'

import {useCommunicatorContentHeight} from './useCommunicatorContentHeight'
import {useCommunicatorNavbarHeight} from './useCommunicatorNavbarHeight'


export function useGlobalModalStyles(isOpened: boolean, topOffset = 5): CSSProperties {
    const communicatorContentHeight = useCommunicatorContentHeight(isOpened)
    const navBarHeight = useCommunicatorNavbarHeight()

    return useMemo(() => ({
        height: `${communicatorContentHeight + navBarHeight - topOffset}px`,
    }), [communicatorContentHeight, navBarHeight, topOffset])
}

