import {useMemo} from 'react'

import {useDirectoryState} from '@bpinc/ad-directory-state-context'
import type {DirectoryItem} from '@bpinc/ad-directory-state-types'

import {useCallPartyUsersFilter, useChatPartyUsersFilter} from '../../hooks'


interface UseUsersFilterParams {
    transferMode?: 'messages' | 'calls'
    filter: ((item: DirectoryItem) => boolean) | undefined
}
export function useFilteredUsers({transferMode, filter = () => true}: UseUsersFilterParams) {
    const usersWithoutSelf = useDirectoryState('usersWithoutCurrentUser')
    const chatUsersFilter = useChatPartyUsersFilter()
    const callUsersFilter = useCallPartyUsersFilter()

    return useMemo(() => {
        if (transferMode === 'messages') {
            return usersWithoutSelf.filter(i => chatUsersFilter(i) && filter(i))
        } else if (transferMode === 'calls') {
            return usersWithoutSelf.filter(i => callUsersFilter(i) && filter(i))
        } else {
            return usersWithoutSelf.filter(filter)
        }
    }, [transferMode, usersWithoutSelf, chatUsersFilter, filter, callUsersFilter])
}
