import React from 'react'

import type {ChatMessage} from '@bpinc/ad-interaction-message-types'

import {useGetSystemMessageIcon, useGetTextContentSystemMessage} from './utils'


type SystemMessageProps = {
    message: ChatMessage
}

export const SystemMessage: React.FC<SystemMessageProps> = ({message}) => {
    const getTextContentSystemMessage = useGetTextContentSystemMessage()
    const getSystemMessageIcon = useGetSystemMessageIcon()

    return (
        <div className="flex items-center justify-center pb-2.5">
            <div
                className="flex flex-col"
                data-testid="message-item"
                data-system="true"
            >
                <div className="flex items-center gap-2">
                    {getSystemMessageIcon(message)}
                    <div className="flex items-center gap-1">
                        {getTextContentSystemMessage(message)}
                    </div>
                </div>
            </div>
        </div>
    )
}
