import React from 'react'

import type {ActivityMediaType} from '@bpinc/ad-activities-state-types'
import type {MessageSource} from '@bpinc/ad-interaction-message-types'
import {paramsToQueryString, requestUrls} from '@bpinc/ad-request-urls'
import {FileProgress} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {useMarkAsReadVisibilitySensor} from '../../../../../hooks/useMarkAsReadVisibilitySensor'
import type {ExternalChatMessageFileData} from '../../../../../types'
import {WithAttachmentMaskAction} from '../../../ChatMessagesActions'
import type {MarkAsReadProps} from '../../ExternalChatMessage'
import {AttachmentFileMessage} from './AttachmentFileMessage'
import {AudioFileMessage} from './AudioFileMessage'
import {ImageFileMessage} from './ImageFileMessage'


interface SessionProps {
    id?: string
    domain?: string
    loginId?: string
}

interface FileMessageContentProps {
    canMask: boolean
    session?: SessionProps
    globalInteractionId?: string
    mediaType?: ActivityMediaType | 'EMAIL' | 'VOICE_TRANSCRIPT' | 'REMOTE_ASSIST'
    source?: MessageSource
    isRead?: boolean
    fileData: ExternalChatMessageFileData
    markAsReadData: MarkAsReadProps
    timestamp: number
    senderName: string | undefined
    msg?: string
}

export const FileMessageContent: React.FC<FileMessageContentProps> = ({
    session = {},
    canMask,
    mediaType,
    source,
    isRead,
    fileData,
    markAsReadData,
    globalInteractionId,
    msg,
    senderName,
    timestamp,
}) => {
    const {
        refMsgId,
        fileId = '',
        fileType = 'attachment',
        fileName = '',
        fileProgress = 0,
        fileError,
        fileIsMasked = false,
    } = fileData
    const {id: sessionId, domain, loginId} = session
    const queryString = paramsToQueryString({
        token: sessionId,
        user: loginId,
        tenant: domain,
        id: fileId,
        no_disposition: 0,
    })
    const fileLink = `${requestUrls.getChatAttachmentDownload}?${queryString}`
    const {t} = useLocalizationState()

    const fileIsLoading = !fileId && !fileError

    const ref = useMarkAsReadVisibilitySensor({
        msgId: refMsgId,
        historyMessageGlobalInteractionId: globalInteractionId,
        mediaType,
        source,
        isRead,
        ...markAsReadData,
    })

    if (fileIsLoading && fileProgress) {
        return <FileProgress progress={fileProgress} />
    }

    if (!fileId) {
        const attachmentText = t.INTERNAL_CHAT.chat_removedAttachment_label()
        return (
            <>
                <div>
                    [
                    {attachmentText}
                    {' '}
                    <del data-testid="file-name">{fileName}</del>
                    ]
                </div>
                {msg && (<div data-testid="message-content">{msg}</div>)}
            </>
        )
    }

    if (fileError) {
        return (
            <>
                <div data-testid="file-error">{fileError}</div>
                {msg && (<div data-testid="message-content">{msg}</div>)}
            </>
        )
    }

    if (fileType === 'attachment' || fileType === 'video') {
        return (
            <>
                <WithAttachmentMaskAction
                    fileId={fileId}
                    fileIsMasked={fileIsMasked}
                    canMask={canMask}
                    globalInteractionId={globalInteractionId}
                    interactionStepId={markAsReadData.itemInteractionStepId}
                    contactId={markAsReadData.itemContactId}
                >
                    <AttachmentFileMessage
                        ref={ref}
                        fileName={fileName}
                        fileLink={fileLink}
                    />
                </WithAttachmentMaskAction>
                {msg && (<div data-testid="message-content">{msg}</div>)}
            </>
        )
    }

    if (fileType === 'audio') {
        return (
            <WithAttachmentMaskAction
                fileId={fileId}
                fileIsMasked={fileIsMasked}
                canMask={canMask}
                globalInteractionId={globalInteractionId}
                interactionStepId={markAsReadData.itemInteractionStepId}
                contactId={markAsReadData.itemContactId}
            >
                <AudioFileMessage
                    fileLink={fileLink}
                />
            </WithAttachmentMaskAction>
        )
    }

    return (
        <>
            <WithAttachmentMaskAction
                fileId={fileId}
                fileIsMasked={fileIsMasked}
                canMask={canMask}
                globalInteractionId={globalInteractionId}
                interactionStepId={markAsReadData.itemInteractionStepId}
                contactId={markAsReadData.itemContactId}
            >
                <ImageFileMessage
                    ref={ref as React.RefObject<HTMLImageElement>}
                    fileName={fileName}
                    fileLink={fileLink}
                    senderName={senderName}
                    timestamp={timestamp}
                />
            </WithAttachmentMaskAction>
            <p className="mt-1 caption-xs text-neutral-fade">{fileName}</p>
            {msg && (<div data-testid="message-content">{msg}</div>)}
        </>
    )
}
