import {useMemo} from 'react'

import {useContactsState} from '@bpinc/ad-contacts-state-context'
import {useFormatting} from '@bpinc/ad-formatting-context'

import type {HistoryVoiceTranscriptMessage} from '../../../../../types'


export function useCallMessageVoiceTranscriptName(
    voiceTranscriptMessage: HistoryVoiceTranscriptMessage,
) {
    const contacts = useContactsState('contacts')
    const {formatFullName} = useFormatting()

    return useMemo(
        function memoizeName() {
            if (voiceTranscriptMessage.type === 'suggestion') {
                return
            }

            const contactData = voiceTranscriptMessage.contactId && contacts[voiceTranscriptMessage.contactId]?.data
            const contactName = contactData && formatFullName(contactData.firstName, contactData.lastName)
            const nonContactName = formatFullName(voiceTranscriptMessage.firstName, voiceTranscriptMessage.lastName)

            return contactData ? contactName : nonContactName
        },
        [
            contacts,
            formatFullName,
            voiceTranscriptMessage.contactId,
            voiceTranscriptMessage.firstName,
            voiceTranscriptMessage.lastName,
            voiceTranscriptMessage.type,
        ],
    )
}
