import React from 'react'

import {useInteractionId} from '@bpinc/lib-interaction-context'
import {useSlidingModalState} from '@bpinc/lib-sliding-modal-context'
import {typedKeys} from '@bpinc/lib-types-helpers'

import {useActiveTransferModalOpener} from './hooks/useActiveTransferModalOpener'
import {InteractionsSlidingModalsListItem} from './InteractionsSlidingModalsListItem'


type InteractionsSlidingModalsListProps = {
    className?: string
}

export const InteractionsSlidingModalsList: React.FC<InteractionsSlidingModalsListProps> = React.memo(function SlidingModalsList() {
    const itemId = useInteractionId()

    const visibleInteractionModals = useSlidingModalState(s => {
        const interactionModalsData = s.getInteractionModalData(itemId)
        const allModals = typedKeys(interactionModalsData)
        return allModals.filter(m => interactionModalsData[m].show || interactionModalsData[m].closing)
    })


    useActiveTransferModalOpener()

    return (
        <>
            {visibleInteractionModals.map(name => {
                return <InteractionsSlidingModalsListItem key={name} name={name} />
            })}
        </>
    )
})
