export function combineDirectoryNavigationByArrows(e: React.KeyboardEvent) {
    if (e.key !== 'ArrowDown' && e.key !== 'ArrowUp') {
        return
    }

    const container = e.currentTarget
    const currentElement = e.target
    const internalItems = container.querySelectorAll(
        '[data-combined-directory="internal"] [data-element="directory-item"]',
    )
    const lastInternalItem = internalItems[internalItems.length - 1]
    const externalItems = container.querySelectorAll(
        '[data-combined-directory="external"] [data-element="directory-item"]',
    )
    const externalItemHeader = externalItems[0]
    const firstExternalItem = externalItems[1]

    switch (e.key) {
        case 'ArrowDown':
            if (currentElement === lastInternalItem && externalItemHeader instanceof HTMLElement) {
                externalItemHeader.focus()
                e.stopPropagation()
            }

            if (currentElement === externalItemHeader && firstExternalItem instanceof HTMLElement) {
                firstExternalItem.focus()
                e.stopPropagation()
            }

            break
        case 'ArrowUp':
            if (currentElement === firstExternalItem && externalItemHeader instanceof HTMLElement) {
                externalItemHeader.focus()
                e.stopPropagation()
            }

            if (currentElement === externalItemHeader && lastInternalItem instanceof HTMLElement) {
                lastInternalItem.focus()
                e.stopPropagation()
            }
            break
    }
}
