import {useEffect} from 'react'

import {useInteractionChatStateConnector} from '@bpinc/ad-interaction-chat-state-context'
import type {CobrowsingStartedData, SwitchItemChannelData} from '@bpinc/ad-interaction-chat-state-types'
import {useInteractions} from '@bpinc/ad-interaction-state-context'
import {getInteractionStepId} from '@bpinc/ad-interaction-state-methods'
import {useAsRef} from '@bpinc/lib-helpers'
import {useOpenChannel} from '@bpinc/lib-helpers-ui'


export function useInteractionChatStateSubscription() {
    const connector = useInteractionChatStateConnector()
    const items = useInteractions()
    const itemsRef = useAsRef(items)
    const openChannel = useOpenChannel()

    useEffect(() => {
        const subscription = connector.dispenser.subscribe({
            onSwitchItemChannel: openChannelOnSwitch,
            onCobrowsingStarted: openCobrowsingWindow,
        })

        return () => {
            subscription.unsubscribe()
        }

        function openChannelOnSwitch({interactionStepId, channel}: SwitchItemChannelData) {
            const item = itemsRef.current.find(it => {
                return getInteractionStepId(it) === interactionStepId
            })
            if (!item) {
                return
            }
            openChannel(channel, item.id)
        }

        function openCobrowsingWindow(cobrowsingPayload: CobrowsingStartedData) {
            cobrowsingPayload.cobrowsingLink && window.open(cobrowsingPayload.cobrowsingLink, '_blank', '')
        }

    }, [connector.dispenser, itemsRef, openChannel])
}
