import React, {useCallback, useEffect} from 'react'

import {useContact, useContactsState, useContactsStateMethods} from '@bpinc/ad-contacts-state-context'
import {useActiveInteractionId} from '@bpinc/ad-interaction-state-context'
import {useNavigationState} from '@bpinc/lib-communicator-navigation-state'
import {useBoolean} from '@bpinc/lib-helpers'
import {Notifications, useNotificationsState} from '@bpinc/lib-notifications-context'
import {Loader} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'
import {useModalsStateMethods} from '@bpinc/state-modals-context'

import {ServiceSelect} from '../../../components/ServiceSelect'
import {ContactDetailsControls} from '../ContactDetailsControls'
import {Header} from '../Header'
import {getGoBackWarningModal} from '../modals/getGoBackWarningModal'
import {ExternalEdit} from './ExternalEdit'
import {ExternalView} from './ExternalView'


interface Props {
    contactId: string
}

export const External: React.FC<Props> = ({contactId}) => {
    const goBack = useNavigationState('goBack')
    const {t} = useLocalizationState()
    const {openModal} = useModalsStateMethods()
    const activeInteractionId = useActiveInteractionId()

    const contact = useContact(contactId)
    const formDefinitionsLoading = useContactsState('formDefinitionsLoading')

    const {getContact, getContactDetails, getContactFormsDefinitions} = useContactsStateMethods()

    const {data, contactDetailsUpdating, contactDetailsLoading, isLoading} = contact || {}
    const {value: editMode, toggle: toggleEditMode} = useBoolean()

    const handleGoBack = useCallback(() => {
        const onConfirm = () => {
            toggleEditMode(false)
            goBack()
        }
        if (editMode) {
            openModal(getGoBackWarningModal(onConfirm))
        } else {
            onConfirm()
        }
    }, [editMode, goBack, openModal, toggleEditMode])

    const {showNotification} = useNotificationsState('methods')

    useEffect(
        () => {
            Promise.all([
                getContact({id: contactId}),
                getContactFormsDefinitions({}),
                getContactDetails({id: contactId}),
            ],
            )
                .then(result => {
                    if (!result.every(Boolean)) {
                        throw Error()
                    }
                })
                .catch(() => {
                    goBack()
                    showNotification({
                        type: 'error',
                        id: Notifications.ContactOpenError,
                        tags: ['current-view'],
                        message: t.CONTACT.open_error(),
                        duration: 7000,
                    })
                })
        },
        // onComponentDidMount
        // eslint-disable-next-line
        [],
    )

    const isLoaderShown = !data || formDefinitionsLoading || contactDetailsLoading || isLoading || contactDetailsUpdating

    return (
        <div
            className="flex min-h-0 flex-auto flex-col overflow-y-auto overflow-x-hidden"
            data-testid="contact-details"
        >
            {isLoaderShown ? (
                <Loader
                    size="large"
                    color="primary"
                    active
                />
            ) : (
                <>
                    <Header
                        user={data}
                        onGoBack={handleGoBack}
                    />

                    {editMode ? (
                        <ExternalEdit
                            toggleEditMode={toggleEditMode}
                            contactId={contactId}
                        />
                    ) : (
                        <>
                            <div className="flex items-center gap-2 px-6 pb-2 pt-4">
                                <ServiceSelect itemId={activeInteractionId} />
                                <ContactDetailsControls user={data} />
                            </div>

                            <ExternalView
                                toggleEditMode={toggleEditMode}
                                contactId={contactId}
                            />
                        </>
                    )}
                </>
            )}
        </div>
    )
}
