import React from 'react'

import type {FormDefinitionPageItem} from '@bpinc/ad-request-urls'
import {type RecursiveFormLayout, calculateContainerStyle, isRecuriveFormLayout} from '@bpinc/lib-form-builder'

import {FormElementResolver} from './FormElementResolver'


export const FormLayout: React.FC<{
    contentItem: RecursiveFormLayout | FormDefinitionPageItem
}> = ({
    contentItem,
}) => {
    if (!isRecuriveFormLayout(contentItem)) {
        return <FormElementResolver pageItem={contentItem} />
    }

    return (
        <div
            data-testid={contentItem.key}
            className="gap-6"
            style={calculateContainerStyle(contentItem)}
        >
            {contentItem.content.map(it => {
                const key = isRecuriveFormLayout(it) ? it.key : it.field.id

                return (
                    <FormLayout
                        key={key}
                        contentItem={it}
                    />
                )
            })}
        </div>
    )
}
