import {useCallback} from 'react'

import {useCommunicatorUIState} from '@bpinc/lib-communicator-ui-context'
import {useEventListener} from '@bpinc/lib-helpers'
import {useFavoritesState} from '@bpinc/state-favorites-context'

import {useFilteredAndSortedFavoritesList} from '../../favorites'
import {useHandlers} from '../hooks/useHandlers'


export function useAccessibleShortcuts() {
    const {onChatClick} = useHandlers()
    const favoritesList = useFavoritesState('favoritesList')
    const setSearchValue = useCommunicatorUIState(s => s.dialpad.setSearchValue)
    const sortedAndFilterFavoritesList = useFilteredAndSortedFavoritesList({favoritesList})

    const onKeyPress = useCallback(
        function onKeyPressCallback(keyboardEvent: KeyboardEvent) {
            const numPressed = Number(keyboardEvent.code?.match(/^(Numpad|Digit)([1-9])$/)?.[2])

            if (numPressed && keyboardEvent.altKey && keyboardEvent.shiftKey) {
                searchFavoriteNumber()
            }

            switch (keyboardEvent.code) {
                case 'KeyS':
                    if (keyboardEvent.altKey && keyboardEvent.shiftKey) {
                        return onChatClick()
                    }
                    break
            }

            function searchFavoriteNumber() {
                const favoritesIndex = numPressed - 1
                const favoritesItem = sortedAndFilterFavoritesList[favoritesIndex].item
                const phoneChannel = favoritesItem.channels.find(({channelType}) => channelType === 'phone')

                if (phoneChannel?.number) {
                    const dialpadInput = document.querySelector('[data-testid="dialpad-input"] input')
                    if (dialpadInput) {
                        setSearchValue(phoneChannel.number)
                        ;(dialpadInput as HTMLInputElement).focus()
                    }
                }
            }
        },
        [onChatClick, setSearchValue, sortedAndFilterFavoritesList],
    )

    useEventListener('keydown', onKeyPress)
}
