import {useEffect, useState} from 'react'
import {get} from 'lodash-es'

import {AppRoutes, useNavigationState} from '@bpinc/lib-communicator-navigation-state'


interface useContactsLoaderParams {
    searchQuery?: string
    skipSearch?: boolean

    getExternalContactsByQuery(query: string): void
    getInternalContactsByQuery(query: string): void
}

const TYPING_TIMEOUT = 500

export function useContactsLoader({
    searchQuery,
    skipSearch,
    getExternalContactsByQuery,
    getInternalContactsByQuery,
}: useContactsLoaderParams) {
    const [isDebouncing, setIsDebouncing] = useState(false)
    const {
        activePage: {params: pageParams},
    } = useNavigationState()

    const prevPage = get(pageParams, 'prev')

    useEffect(() => {
        let typingTimeout: number
        const getContacts = () => {
            if (searchQuery) {
                getInternalContactsByQuery(searchQuery)
                getExternalContactsByQuery(searchQuery)
            }

            setIsDebouncing(false)
        }

        if (!skipSearch) {
            if (prevPage === AppRoutes.ContactDetails) {
                getContacts()
            } else {
                setIsDebouncing(true)
                typingTimeout = setTimeout(getContacts, TYPING_TIMEOUT)
            }
        }

        return () => {
            setIsDebouncing(false)
            typingTimeout && clearTimeout(typingTimeout)
        }
    }, [
        getExternalContactsByQuery,
        getInternalContactsByQuery,
        prevPage,
        skipSearch,
        searchQuery,
        setIsDebouncing,
    ])

    return isDebouncing
}
