import React, {useId} from 'react'
import {useContext} from 'use-context-selector'

import {useFormatting} from '@bpinc/ad-formatting-context'
import type {ContactEmail, FormDefinitionFieldEmailList, FormDefinitionPageItem} from '@bpinc/ad-request-urls'
import {getPageItemCSSProperties} from '@bpinc/lib-form-builder'

import {FormBuilderContext} from '../../FormBuilder'
import {EmailFieldArray} from '../common/FieldArray/EmailFieldArray'
import {ViewItem} from '../common/ViewItem'


export const EmailListField: React.FC<{
    pageItem: FormDefinitionPageItem
    field: FormDefinitionFieldEmailList
}> = ({
    pageItem,
    field,
}) => {
    const {
        form,
        view,
    } = useContext(FormBuilderContext)

    const {formatEmailType} = useFormatting()
    const ariaId1 = useId()
    const ariaId2 = useId()

    if (view) {
        const emails = form.getValues()[field.name || ''] as ContactEmail[]
        const viewStyle = getPageItemCSSProperties(pageItem.style)

        return (
            <>
                {emails?.map((email, index) => (
                    <ViewItem
                        key={index}
                        viewStyle={viewStyle}
                        label={formatEmailType(email.type)}
                        value={(
                            <a
                                tabIndex={0}
                                aria-labelledby={`${ariaId1} ${ariaId2}`}
                                id={ariaId2}
                            >
                                {email.email_address}
                            </a>
                        )}
                    />
                ))}
            </>
        )
    }

    const editStyle = getPageItemCSSProperties(pageItem.editStyle)

    return (
        <EmailFieldArray
            name={field.name || ''}
            control={form.control}
            style={editStyle}
            layoutClassName={'gap-x-[--form-cellPadding] gap-y-[--form-rowHeight]'}
            typeClassName={'w-[--form-fieldLabelWidth] min-w-[--form-fieldLabelWidth] max-w-[--form-fieldLabelWidth]'}
        />
    )
}
