/** This is a transformed to TS copy of one we have in the old AD.
 * However, here we use our own implementation of the Email regex.
 * the old AD uses this regex:
 * '[a-zA-Z0-9.!#$%&\'*+/=?^_`{|}~-]+@[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?' +
 * '(?:\\.[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?)+'
 */

import {EMAIL_REGEX} from '@bpinc/ad-regexp-constants'


export class EmailAddressValidator {
    private static readonly emailPattern: RegExp = new RegExp(EMAIL_REGEX)

    static validate(input: string | null | undefined): boolean {
        return input !== null && input !== undefined && this.emailPattern.test(input)
    }
}

