import React from 'react'

import {useEvent} from '@bpinc/lib-helpers'
import {ButtonIcon, IconInformation} from '@bpinc/lib-jskit-ui'
import type {FavoritesItem} from '@bpinc/state-favorites-types'
import {useLocalizationState} from '@bpinc/state-localization-context'


type Props = {
    item: FavoritesItem
    onDetailsClick: (item: FavoritesItem) => void
}

export const FavoriteControls: React.FC<Props> = ({item, onDetailsClick}) => {

    const {CONTACT: i18n} = useLocalizationState('t')

    const handleDetails = useEvent(() => {
        onDetailsClick?.(item)
    })

    return (
        <ButtonIcon
            tooltip={i18n.button_show_contact_details_text()}
            data-testid="user-info-button"
            onPressed={handleDetails}
        >
            <IconInformation />
        </ButtonIcon>
    )
}
