import type {CSSProperties} from 'react'
import React, {useEffect, useMemo} from 'react'
import type {FieldPathValue, FieldValues, Path} from 'react-hook-form'
import {useController, useWatch} from 'react-hook-form'

import type {MessengerData} from '@bpinc/ad-interaction-chat-state-types'
import type {ContactMessenger} from '@bpinc/ad-request-urls'
import type {FieldControlProps} from '@bpinc/lib-forms-base'
import {useGetMessengerName} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {SelectFieldRaw} from '../SelectField'
import {TextFieldRaw} from '../TextField'
import {HiddenFieldRaw} from './HiddenField'


type MessengerFieldItemProps<TValues> = {
    typeFiledName: Path<TValues>
    userIdFieldName: Path<TValues>
    userNameFieldName: Path<TValues>
    accountIdFieldName: Path<TValues>
    availableMessengers: MessengerData[]
    userNameDefault?: string
    userIdDefault?: string
    typeClassName?: string
    accountIdDefault?: string
    style?: CSSProperties
}


const MESSENGERS_WITH_USER_NAME: ContactMessenger['type'][] = ['telegram', 'twitter']

export const MessengerFieldItem = <TValues extends FieldValues>({
    control,
    userNameFieldName,
    typeFiledName,
    userIdFieldName,
    accountIdFieldName,
    availableMessengers,
    userNameDefault,
    accountIdDefault,
    userIdDefault,
    typeClassName,
    style,
}: FieldControlProps<TValues, FieldValues> & MessengerFieldItemProps<TValues>) => {
    const {t} = useLocalizationState()

    const {
        field: accountIdField,
    } = useController({
        name: accountIdFieldName,
        defaultValue: (accountIdDefault || '') as FieldPathValue<TValues, Path<TValues>>,
        control,
    })

    const {field: typeField} = useController({
        name: typeFiledName,
        control,
    })

    const {
        field: userIdField,
    } = useController({
        name: userIdFieldName,
        control,
        defaultValue: (userIdDefault || '') as FieldPathValue<TValues, Path<TValues>>,
    })

    const {
        field: userNameField,
    } = useController({
        name: userNameFieldName,
        control,
        defaultValue: (userNameDefault || '') as FieldPathValue<TValues, Path<TValues>>,
    })

    const accountField = useWatch({control, name: accountIdFieldName})
    const currentType = useMemo(() => availableMessengers.find(messenger => messenger.id === accountField)?.type, [accountField, availableMessengers])

    const showUserName = currentType ? MESSENGERS_WITH_USER_NAME.includes(currentType) : false
    const getMessengerName = useGetMessengerName()

    const options = useMemo<{label: string; value: string}[]>(() => {
        return availableMessengers.map(messenger => ({value: messenger.id, label: getMessengerName(messenger.type)}))
    }, [availableMessengers, getMessengerName])


    useEffect(function synchronizeTypeField() {
        typeField.onChange(currentType)
    }, [currentType, typeField])

    useEffect(function synchronizeUserNameField() {
        if (!showUserName) {
            userNameField.onChange('')
        }
    }, [showUserName, userNameField])

    return (
        <>
            <SelectFieldRaw
                className={typeClassName}
                style={style}
                field={accountIdField}
                placeholder={t.UNDEFINED.contact_messenger()}
                options={options}
            />
            <HiddenFieldRaw field={typeField} />
            <TextFieldRaw
                style={style}
                placeholder={t.UNDEFINED.contact_messenger_user_id_placeholder()}
                field={userIdField}
            />
            {
                showUserName && (
                    <TextFieldRaw
                        style={style}
                        placeholder={t.UNDEFINED.contact_messenger_username_placeholder()}
                        field={userNameField}
                    />
                )
            }
        </>
    )
}
