import React from 'react'

import type {DirectoryEntry} from '@bpinc/ad-directory-state-types'
import {
    isHuntGroupEntry,
    isScenarioVoiceEntry,
    isStaticEmailEntry,
    isStaticVoiceEntry,
} from '@bpinc/ad-directory-state-types'
import {formatPhone} from '@bpinc/ad-formatting-helpers'
import {IconEmail16, IconPhone16Filled} from '@bpinc/lib-jskit-ui'
import {highlighted} from '@bpinc/lib-ui-elements'


type Props = {
    item: DirectoryEntry
    highlight?: string
}

export const DirectoryEntryBody: React.FC<Props> = ({item, highlight}) => {

    if (isScenarioVoiceEntry(item) || isStaticVoiceEntry(item) || isHuntGroupEntry(item)) {
        const phone = formatPhone(item.number)

        return (
            <span className="flex items-center gap-1">
                <IconPhone16Filled className="my-px mr-1 inline-block shrink-0 icon-neutral" />
                <span data-testid="phone" className="truncate caption-s text-neutral-on-secondary">
                    {highlighted({highlight, text: phone})}
                </span>
            </span>
        )
    }

    if (isStaticEmailEntry(item)) {
        return (
            <span className="flex items-center gap-1">
                <IconEmail16 className="my-px mr-1 inline-block shrink-0 icon-neutral" />
                <span data-testid="email" className="truncate caption-s text-neutral-on-secondary">
                    {highlighted({highlight, text: item.email})}
                </span>
            </span>
        )
    }

    return null
}
