import type {KeyboardEvent} from 'react'
import React, {
    useCallback,
    useEffect,
    useMemo,
    useRef,
    useState,
} from 'react'
import type {VirtuosoHandle} from 'react-virtuoso'
import {Virtuoso} from 'react-virtuoso'
import {useAtomValue, useSetAtom} from 'jotai'
import {throttle} from 'lodash-es'

import type {DirectoryItem as DirectoryItemType} from '@bpinc/ad-directory-state-types'
import {useDefaultTransferMode} from '@bpinc/ad-interaction-state-context'
import {useContactDetailsOpener} from '@bpinc/ad-interaction-state-hooks'
import type {InitiateCall} from '@bpinc/ad-interaction-state-types'
import {useCommunicatorUIState} from '@bpinc/lib-communicator-ui-context'
import {ContactListPlaceholder} from '@bpinc/lib-contact-list-item-ui'
import {isMsTeamsChannel, isMsTeamsUser, isUserWithDetails} from '@bpinc/lib-helpers-ui'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {ErrorBoundary, SelectionContainer, SelectionItem} from '@bpinc/lib-ui-elements'

import {DirectoryItem} from '../../directory'
import {contactsListMethodsAtom, phoneInputAtom, selectedItemIdAtom} from '../atoms'
import {getPhone, isInternalContact} from '../helpers'
import {type InitiateChatAndCallOptionalParams, useHandlers} from '../hooks/useHandlers'
import {type ComposedContact, useQueryContacts} from '../hooks/useQueryContacts/useQueryContacts'
import {CrmDialCandidates} from './dial-candidates/CrmDialCandidates'

import styles from '@bpinc/lib-chat-ui/src/components/external/VoiceTranscript/VoiceTranscript.module.less'


export type ContactsListMethods = {
    scrollToFirst: () => void
    scrollToLast: () => void
}

function getContactKey(_idx: number, contact: ComposedContact) {
    return getContactId(contact)
}

function getContactId(contact: ComposedContact) {
    return isInternalContact(contact) ? contact.userId : contact.id
}


export const DialpadContacts = () => {
    const {
        callNumber,
        initiateChatWith,
        onContactSelect,
        closeDialpad,
    } = useHandlers()
    const {searchValue: dialNumber} = useCommunicatorUIState('dialpad')
    const {contacts, isLoading} = useQueryContacts()
    const selectedItemId = useAtomValue(selectedItemIdAtom)
    const phoneInput = useAtomValue(phoneInputAtom)
    const defaultTransferMode = useDefaultTransferMode()
    const openContactDetails = useContactDetailsOpener()
    const virtuosoRef = useRef<VirtuosoHandle>(null)
    const contactsIds = useMemo(() => contacts.map((contact, i) => getContactId(contact)), [contacts])

    const [scrollNode, setScrollNode] = useState<HTMLDivElement | undefined>(undefined)
    const setScrollNodeRef = useCallback((node: HTMLDivElement) => {
        setScrollNode(node)
    }, [])

    const setContactsListMethods = useSetAtom(contactsListMethodsAtom)

    const contactsListMethods = useMemo<ContactsListMethods>(() => ({
        scrollToFirst: () => {
            virtuosoRef.current?.scrollIntoView({index: 0,
                done: () => {
                    focusContactElement(scrollNode, contactsIds[0])
                }})
        },
        scrollToLast: () => {
            const lastIndex = contactsIds?.length - 1
            virtuosoRef.current?.scrollIntoView({
                index: lastIndex,
                done: () => {
                    focusContactElement(scrollNode, contactsIds[lastIndex])
                },
            })
        },
    }), [contactsIds, scrollNode])

    useEffect(() => {
        setContactsListMethods(contactsListMethods)
    }, [contactsListMethods, setContactsListMethods])

    const handleContactSelect = useCallback(
        (_evt: React.MouseEvent, contact: DirectoryItemType) => {
            if (isMsTeamsUser(contact) || isMsTeamsChannel(contact) || isUserWithDetails(contact)) {
                throw Error('We don\'t support this type of contact in dialpad search.')
            }

            const phone = getPhone(contact, dialNumber) || ''
            const id = getContactId(contact)
            onContactSelect({id, phone})
        },
        [dialNumber, onContactSelect],
    )

    const renderContact = useCallback((_index: number, contact: ComposedContact) => {
        const handleCall = ({phone, ...params}: InitiateCall) => {
            callNumber(phone, 'dialpad-search', params)
        }

        const handleChat = ({phone, ...params}: InitiateChatAndCallOptionalParams & {phone: string}) => {
            initiateChatWith(phone, 'dialpad-search', params)
        }

        const contactId = getContactId(contact)
        const isSelected = selectedItemId === contactId

        return (
            <ErrorBoundary fallback={null}>
                <DirectoryItem
                    showFavoriteButton={false}
                    className="h-14 w-full"
                    listNode={scrollNode}
                    item={contact}
                    isSelected={isSelected}
                    transferMode={defaultTransferMode}
                    onCall={handleCall}
                    onChat={handleChat}
                    onClick={handleContactSelect}
                    onDetails={openContactDetails}
                    as={SelectionItem}
                    containerProps={{
                        itemId: contactId,
                        focusOnSelection: true,
                        tabIndex: 0,
                    }}
                />
            </ErrorBoundary>
        )
    }, [callNumber, defaultTransferMode, handleContactSelect, initiateChatWith, openContactDetails, scrollNode, selectedItemId])


    function onItemSelect(id: string) {
        const index = contactsIds?.findIndex(contactId => id === contactId)
        virtuosoRef.current?.scrollIntoView({index})
        closeDialpad()
    }

    function handleFocusInput(e: KeyboardEvent<HTMLDivElement>) {
        e.stopPropagation()
        if (e.key === 'Escape') {
            phoneInput?.focus()
        }
    }

    const handleCloseDialpad = useMemo(() => throttle(() => closeDialpad(), 300, {leading: true}), [closeDialpad])

    return (
        <div className="flex grow flex-col overflow-hidden pt-2">
            <div
                ref={setScrollNodeRef}
                className={tcn('scrollbar-on-hover relative !m-0 flex min-h-2.5 flex-auto grow flex-col overflow-y-auto overflow-x-hidden', {
                    'relative overflow-y-hidden': isLoading,
                })}
                role="list"
                onScroll={handleCloseDialpad}
            >
                {dialNumber
                    ? isLoading
                        ? (
                            <div className="absolute inset-x-0">
                                <ContactListPlaceholder amount={15} />
                            </div>
                        )
                        : (
                            <SelectionContainer
                                className={styles.selectionContainer}
                                itemsListProvider={() => contactsIds}
                                onItemSelect={onItemSelect}
                                circular
                                onKeyDown={handleFocusInput}
                            >
                                <Virtuoso
                                    ref={virtuosoRef}
                                    tabIndex={undefined}
                                    data={contacts}
                                    itemContent={renderContact}
                                    computeItemKey={getContactKey}
                                    customScrollParent={scrollNode}
                                />
                            </SelectionContainer>
                        )
                    : null
                }
                {!dialNumber && !contacts.length && (
                    <CrmDialCandidates
                        callNumber={callNumber}
                        dialpadNumber={dialNumber}
                        initiateChatWith={initiateChatWith}
                    />
                )}
            </div>
        </div>
    )
}

function focusContactElement(container: HTMLDivElement | undefined, id: string) {
    container?.querySelector<HTMLDivElement>(`[data-id="${id}"]`)?.focus()
}
