import React, {useCallback, useMemo} from 'react'

import type {PhoneData} from '@bpinc/adapters-public-api-types'
import type {ArrayElement} from '@bpinc/lib-types-helpers'
import type {DropdownProps, UseDropdownOutput} from '@bpinc/lib-ui-elements'
import {useDropdown} from '@bpinc/lib-ui-elements'

import {PhoneOption} from './PhoneOption'


type Options = {
    phones: PhoneData[]
    handleCall: (phone: string) => void
    handleSinglePhoneCall: () => void
}

interface DropdownState extends UseDropdownOutput {
    handleCallClick(e: React.MouseEvent): void
}

export function useDialCandidatesPhonesDropdown({
    phones,
    handleCall,
    handleSinglePhoneCall,
}: Options): DropdownState {
    const dropdownState = useDropdown()
    const {openerRef, setOptions, show} = dropdownState

    const onPhoneClick = useCallback(
        (phone: string) => {
            show.setFalse()
            handleCall(phone)
        },
        [show, handleCall],
    )

    const getPhoneOption = useCallback(
        ({type, number, displayType}: PhoneData): ArrayElement<DropdownProps['options']> => ({
            onClick: () => onPhoneClick(number),
            text: (
                <PhoneOption
                    number={number}
                    type={type}
                    displayType={displayType}
                />
            ),
            value: `${type}_${number}`,
        }),
        [onPhoneClick],
    )

    const handleCallClick = useCallback(
        (e: React.MouseEvent) => {
            e.persist()
            e.preventDefault()
            e.stopPropagation()

            if (show && e.target === openerRef.current) {
                return show.setFalse()
            }
            if (phones.length > 1) {
                setOptions(phones.map(getPhoneOption))
                openerRef.current = e.target as HTMLButtonElement
                return show.setTrue()
            } else {
                handleSinglePhoneCall()
            }
        },
        [
            handleSinglePhoneCall,
            getPhoneOption,
            openerRef,
            phones,
            setOptions,
            show,
        ],
    )

    return useMemo(
        () => ({
            handleCallClick,
            dataKey: 'phones-dropdown',
            ...dropdownState,
        }),
        [dropdownState, handleCallClick],
    )
}
