import React, {useCallback, useMemo} from 'react'
import {TabsContent, TabsList, TabsRoot, TabsTrigger} from '@servicepattern/ui'

import type {DirectoryItem} from '@bpinc/ad-directory-state-types'
import {useActiveInteractionId} from '@bpinc/ad-interaction-state-context'
import {useActivityHistoryOpener, useCallHandler} from '@bpinc/ad-interaction-state-hooks'
import {useDirectoryUIState} from '@bpinc/lib-communicator-ui-context'
import {
    getDirectoryItemContactId,
    getDirectoryItemFirstName,
    getDirectoryItemLastName,
    getDirectoryItemPhone,
    getDirectoryItemUniqueId,
    getDirectoryItemUserId,
    isMsTeamsUser,
} from '@bpinc/lib-helpers-ui'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {ServiceSelect} from '../../components/ServiceSelect'
import {Internal} from './Internal/'
import type {OnClickCallbacks} from './user/types'
import {DirectorySearchControls} from './DirectorySearchControls'
import {External} from './External'


let singleClickTimeout: number

export const DirectoryTabs: React.FC = () => {
    const {t} = useLocalizationState()

    const {
        selected,
        setSelected,
        tabIndex,
        handleTabChange,
        searchValue,
        setSearchValue,
        searchInputRef,
    } = useDirectoryUIState()
    const activeInteractionId = useActiveInteractionId()

    const handleCall = useCallHandler()
    const openActivityHistory = useActivityHistoryOpener()

    const onClick = useCallback((evt: React.MouseEvent, item: DirectoryItem) => {
        /** Clicked more than 1 times in a row */
        if (evt.nativeEvent.detail > 1) {
            clearTimeout(singleClickTimeout)
            return
        }

        /** wait for potential doubleclick before handling a single click*/
        singleClickTimeout = setTimeout(() => {
            const id = getDirectoryItemUniqueId(item)
            setSelected(id)
            const phoneNumber = getDirectoryItemPhone(item)
            if (phoneNumber) {
                setSearchValue(phoneNumber)
            }
        }, 250)
    }, [setSelected, setSearchValue])

    const onDoubleClick = useCallback((item: DirectoryItem, callbacks?: OnClickCallbacks) => {
        clearTimeout(singleClickTimeout)
        if (callbacks?.onCall) {
            return callbacks.onCall()
        }

        const phone = getDirectoryItemPhone(item)
        const firstName = getDirectoryItemFirstName(item)
        const lastName = getDirectoryItemLastName(item)
        const userId = getDirectoryItemUserId(item)
        const contactId = getDirectoryItemContactId(item)

        if (phone) {
            handleCall({
                phone,
                firstName,
                lastName,
                userId,
                contactId,
                navigateToInteractionsPage: true,
            })
        } else {
            openActivityHistory({
                contactId,
                userId,
                phone,
                firstName,
                lastName,
                origination: 'directory',
                emiPhotoUrl: isMsTeamsUser(item) ? item.photoUrl : undefined,
                emiUserType: isMsTeamsUser(item) ? item.userType : undefined,
            })
        }
    }, [handleCall, openActivityHistory])

    const tabs = useMemo(() => [
        {
            id: 'internal',
            content: t.ADDRESS_BOOK_AREA.tab_directory_internal(),
        },
        {
            id: 'external',
            content: t.ADDRESS_BOOK_AREA.tab_directory_external(),
        },
    ], [t])

    const onTabChange = useCallback((newTabId: string) => {
        const newTabIndex = tabs.findIndex(tab => tab.id === newTabId)
        handleTabChange(newTabIndex)
    }, [handleTabChange, tabs])

    const selectedTabId = tabs[Number(tabIndex)].id

    return (
        <TabsRoot value={selectedTabId} className="flex h-full flex-col gap-2.5 px-4 pt-2">
            <TabsList data-testid="directory-tabs" >
                {tabs.map(tab => (
                    <TabsTrigger
                        key={tab.id}
                        value={tab.id}
                        data-testid={`tab-${tab.id}`}
                        isSelected={tab.id === selectedTabId}
                        onClick={() => onTabChange(tab.id)}
                        className="w-full text-center"
                        triggerClassName={tcn(
                            'py-2',
                            {['text-primary-on-primary']: tab.id === selectedTabId},
                        )}
                    >
                        {tab.content}
                    </TabsTrigger>
                ))}
            </TabsList>

            <ServiceSelect itemId={activeInteractionId} />

            <DirectorySearchControls />

            <TabsContent
                value={selectedTabId}
                className="flex min-h-0 !flex-auto flex-col py-1 pr-0.5"
            >
                {selectedTabId === 'internal' && (
                    <Internal
                        query={searchValue}
                        onClick={onClick}
                        onDoubleClick={onDoubleClick}
                    />
                )}
                {selectedTabId === 'external' && (
                    <External
                        query={searchValue}
                        onClick={onClick}
                        selectedId={selected}
                        onDoubleClick={onDoubleClick}
                        searchInputRef={searchInputRef}
                    />
                )}
            </TabsContent>
        </TabsRoot>
    )
}
