import React from 'react'

import type {CommonSlidingModalItemProps} from '@bpinc/lib-sliding-modal-context'
import {SlidingModal} from '@bpinc/lib-ui-elements'

import {InteractionKB} from '../../kb'


export const KnowledgeBaseSlidingModal: React.FC<CommonSlidingModalItemProps> = React.memo(
    function KnowledgeBaseSlidingModal(props) {
        return (
            <SlidingModal
                className="text-left"
                contentClassName="p-0"
                name="knowledgeBase"
                focusTrap={false}
                {...props}
            >
                <InteractionKB />
            </SlidingModal>
        )
    },
)
