import React from 'react'

import {useDirectoryState} from '@bpinc/ad-directory-state-context'
import {MS_TEAMS_CHATS_CATEGORY_ID} from '@bpinc/ad-directory-state-types'
import type {HandleCallParams} from '@bpinc/ad-interaction-state-hooks'
import {isRecentCallConference, isRecentExtChatConference, isRecentGroupChat} from '@bpinc/ad-interaction-state-methods'
import {Avatar} from '@bpinc/lib-avatar-ui'
import type {ChatParams} from '@bpinc/lib-ui-elements'
import type {RecentItem} from '@bpinc/state-recent-types'

import type {OnEmailParams} from '../../directory/components/user/types'
import {useAvatar} from '../hooks/useAvatar'
import {WithRecentContactCard} from './contact-card/WithRecentContactCard'


type Props = {
    item: RecentItem
    onCallClick: (callParams: HandleCallParams) => void
    onChatClick: (chatParams: ChatParams) => void
    onEmailClick: (emailParams: OnEmailParams) => void
}

export const RecentItemAvatar: React.FC<Props> = ({item, ...restProps}) => {

    const emiUsers = useDirectoryState('emiUsers')
    const relatedEMIUser = emiUsers[MS_TEAMS_CHATS_CATEGORY_ID]?.find(user => user.userId === item.userId)

    const avatarProps = useAvatar(item, relatedEMIUser)

    const avatar = (
        <Avatar
            firstName={avatarProps.firstName}
            lastName={avatarProps.lastName}
            fullName={avatarProps.fullName}
            url={avatarProps.url}
            icon={avatarProps.icon}
            online={avatarProps.isOnline}
            onlineStatus={avatarProps.onlineStatusIcon}
            headPhones={avatarProps.isAgent}
            type={avatarProps.avatarType}
        />
    )

    /** show contact card only for 1) identified external contacts 2) not identified contacts */
    if (
        (item.contactId || !item.userId) &&
        !isRecentCallConference(item) &&
        !isRecentExtChatConference(item) &&
        !isRecentGroupChat(item)
    ) {
        return (
            <WithRecentContactCard item={item} {...restProps}>
                {avatar}
            </WithRecentContactCard>
        )
    }

    return avatar
}
