import {useCallback, useMemo, useRef, useState} from 'react'
import type {FileRejection} from 'react-dropzone'
import type {Canceler} from 'axios'
import produce, {castDraft} from 'immer'

import type {ChatAttachment, EmailAttachment} from '@bpinc/ad-request-urls'
import {useSessionState} from '@bpinc/ad-session-context'
import {getFileFromUrl} from '@bpinc/lib-helpers'
import {uuid} from '@bpinc/lib-uuid'

import type {
    Attachment,
    Attachments,
    BasicAttachmentData,
    PendingRequestsCancelers,
    RequestQueue,
    RequestQueueItem,
    UploadAttachment,
    UploadError,
} from './types'
import {useAttachmentLink} from './useAttachmentLink'

// browser allows this to be no greater than 6
// however even few big files may freeze the browser
const MAX_CONCURRENT_REQUESTS = 1


interface useAttachmentsStateParams<
    AttachmentData extends BasicAttachmentData
> {
    uploadFile: UploadAttachment<AttachmentData>
    handleError?: (attachmentsGroupId: string, error: UploadError) => void
    getFormData: (item: RequestQueueItem) => FormData
}

export function useAttachmentsState<
    AttachmentData extends ChatAttachment | EmailAttachment
>({
    uploadFile,
    handleError,
    getFormData,
}: useAttachmentsStateParams<AttachmentData>) {
    const [attachments, setAttachments] = useState<Attachments<AttachmentData>>({})
    const [fileRejections, setFileRejections] = useState<FileRejection[]>([])

  // requests that are already sent and waiting for response from server
    const pendingRequestsCancelersRef = useRef<PendingRequestsCancelers>({})
  // requests that are not yet sent to the server
    const requestQueueRef = useRef<RequestQueue>({pendingAmount: 0, items: []})

    const getCanceler = useCallback(function getCancelerCallback(
        attachmentsGroupId: string,
        id: string,
    ): Canceler | undefined {
        return pendingRequestsCancelersRef.current[attachmentsGroupId]?.[id]
    },
    [])

    const removeCancelers = useCallback(function removeCancelersCallback(
        attachmentsGroupId: string,
    ) {
        delete pendingRequestsCancelersRef.current[attachmentsGroupId]
    },
    [])

    const removeCanceler = useCallback(function removeCancelerCallback(
        attachmentsGroupId: string,
        id: string,
    ) {
        delete pendingRequestsCancelersRef.current[attachmentsGroupId]?.[id]
    },
    [])

    const cancelCurrentRequest = useCallback(
        function cancelCurrentRequestCallback(
            attachmentsGroupId: string,
            id: string,
        ) {
            const cancel = getCanceler(attachmentsGroupId, id)

            if (cancel) {
                cancel()
                removeCanceler(attachmentsGroupId, id)
            }
        },
        [getCanceler, removeCanceler],
    )

    const attachmentsRef = useRef(attachments)

    const {sessionId = '', domain = '', loginId = ''} = useSessionState(['sessionId', 'domain', 'loginId'])

    const updateAttachments = useCallback(function updateAttachmentsCallback(
        setter: (a: Attachments<AttachmentData>) => Attachments<AttachmentData>,
    ) {
        setAttachments(currentAttachments => {
            const updatedAttachments = setter(currentAttachments)
            attachmentsRef.current = updatedAttachments

            return updatedAttachments
        })
    },
    [])

    const getAttachmentsList = useCallback(
        function getAttachmentsListCallback(attachmentsGroupId?: string) {
            return (attachmentsGroupId && Object.values(attachments[attachmentsGroupId] ?? {})) || []
        },
        [attachments],
    )

    const removeRequestFromQueue = useCallback((attachmentsGroupId: string, id: string) => {
        requestQueueRef.current.items = requestQueueRef.current.items.filter(item => {
            return id !== item.id || attachmentsGroupId !== item.attachmentsGroupId
        })
    }, [])

    const addNewAttachment = useCallback(
        function addNewAttachmentCallback(
            attachmentsGroupId: string,
            newAttachment: Omit<Attachment<AttachmentData>, 'id'>,
        ) {
            const id = uuid()

            updateAttachments(
                produce(draft => {
                    if (!draft[attachmentsGroupId]) {
                        draft[attachmentsGroupId] = {}
                    }
                    draft[attachmentsGroupId][id] = castDraft({id, ...newAttachment})
                }),
            )
            return id
        },
        [updateAttachments],
    )

    const updateAttachment = useCallback(
        function updateAttachmentCallback(
            attachmentsGroupId: string,
            id: string,
            attachment: Attachment<AttachmentData>,
        ) {
            updateAttachments(
                produce(draft => {
                    if (!draft[attachmentsGroupId]) {
                        draft[attachmentsGroupId] = {}
                    }

                    draft[attachmentsGroupId][id] = {
                        ...draft[attachmentsGroupId][id],
                        ...castDraft(attachment),
                    }
                }),
            )
        },
        [updateAttachments],
    )

    const removeAttachment = useCallback(
        function removeAttachmentCallback(attachmentsGroupId: string, id: string) {
            cancelCurrentRequest(attachmentsGroupId, id)
            removeRequestFromQueue(attachmentsGroupId, id)
            updateAttachments(
                produce(draft => {
                    if (draft[attachmentsGroupId]) {
                        delete draft[attachmentsGroupId][id]
                    }
                }),
            )
        },
        [cancelCurrentRequest, removeRequestFromQueue, updateAttachments],
    )

    const processNextRequestQueueItem = useCallback(async () => {
        const queue = requestQueueRef.current

        if (queue.pendingAmount >= MAX_CONCURRENT_REQUESTS) return

        const item = queue.items.shift()

        if (!item) return

        queue.pendingAmount++

        const {id, attachmentsGroupId, file} = item

        const bodyFormData = new FormData()

        bodyFormData.append('file', file)

        const params = {
            data: getFormData(item),
            tenant: domain,
            token: sessionId,
            user: loginId,
            fileName: file.name,
            fileType: 'attachment',
        } as const

        const canceler = await uploadFile(params, {
            onSuccess: fileData => {
                const pendingRequest = getCanceler(attachmentsGroupId, id)
                if (pendingRequest) {
                    updateAttachment(attachmentsGroupId, id, {
                        id,
                        file,
                        isLoading: false,
                        fileData,
                    })
                    removeCanceler(attachmentsGroupId, id)
                }
                queue.pendingAmount--
                processNextRequestQueueItem()
            },
            onError: error => {
                const pendingRequest = getCanceler(attachmentsGroupId, id)
                if (pendingRequest) {
                    removeAttachment(attachmentsGroupId, id)
                    setFileRejections(
                        produce(draft => {
                            draft.push({
                                file,
                                errors: [
                                    error,
                                ],
                            })
                        }),
                    )
                    handleError?.(attachmentsGroupId, error)
                    removeCanceler(attachmentsGroupId, id)
                }
                queue.pendingAmount--
                processNextRequestQueueItem()
            },
        })

        if (!pendingRequestsCancelersRef.current[attachmentsGroupId]) {
            pendingRequestsCancelersRef.current[attachmentsGroupId] = {}
        }
        pendingRequestsCancelersRef.current[attachmentsGroupId][id] = canceler
    }, [domain, getCanceler, getFormData, handleError, loginId, removeAttachment, removeCanceler, sessionId, updateAttachment, uploadFile])


    const addAttachment = useCallback(
        function addAttachmentCallback(attachmentsGroupId: string, file: File) {
            const id = addNewAttachment(attachmentsGroupId, {
                isLoading: true,
                file,
            })
            requestQueueRef.current.items.push({
                id,
                attachmentsGroupId,
                file,
            })
            processNextRequestQueueItem()
        },
        [addNewAttachment, processNextRequestQueueItem],
    )

    const clearAttachmentsList = useCallback(
        function clearAttachmentsListCallback(attachmentsGroupId: string) {
            removeCancelers(attachmentsGroupId)
            updateAttachments(
                produce(draft => {
                    draft[attachmentsGroupId] = {}
                }),
            )
        },
        [removeCancelers, updateAttachments],
    )

    const removeAttachmentsList = useCallback(
        (attachmentsGroupId: string) => {
            removeCancelers(attachmentsGroupId)

            updateAttachments(
                produce(draft => {
                    delete draft[attachmentsGroupId]
                }),
            )
        },
        [removeCancelers, updateAttachments],
    )

    const isAttachmentsListReady = useCallback(
        function attachmentsListReadyCallback(attachmentsGroupId?: string) {
            const attachmentsList = getAttachmentsList(attachmentsGroupId)

            if (!attachmentsList?.length) {
                return true
            }

            return attachmentsList.every(attachmentIsLoaded)

            function attachmentIsLoaded(attachment: Attachment<AttachmentData>) {
                return !attachment.isLoading
            }
        },
        [getAttachmentsList],
    )


    const {generateUrl} = useAttachmentLink()

    const initializeAttachmentsList = useCallback(async (attachmentGroupId: string, attachmentsData: BasicAttachmentData[]) => {
        const getFilesPromises = attachmentsData.map(async a => {
            return await getFileFromUrl(generateUrl(a.id), a.name)
        })

        Promise.all(getFilesPromises).then(files => {
            files.forEach(file => {
                addAttachment(attachmentGroupId, file)
            })
        })
    }, [addAttachment, generateUrl])

    return useMemo(
        () => ({
            fileRejections,
            setFileRejections,
            removeAttachmentsList,
            getAttachmentsList,
            removeAttachment,
            clearAttachmentsList,
            addAttachment,
            initializeAttachmentsList,
            isAttachmentsListReady,
        }),
        [
            fileRejections,
            removeAttachmentsList,
            getAttachmentsList,
            removeAttachment,
            clearAttachmentsList,
            addAttachment,
            initializeAttachmentsList,
            isAttachmentsListReady,
        ],
    )
}
