import React from 'react'

import type {ChatMessageDate} from '../../../../../types'


type DateMessageProps = {
    message: ChatMessageDate
}

export const DateMessage: React.FC<DateMessageProps> = React.memo(function DateMessage({message}) {
    return (
        <div
            className="text-neutral-on-fade words-break flex w-full select-none items-center justify-center gap-1 px-8 py-4 caption-s"
            data-testid="date-separator"
        >
            {message}
        </div>
    )
})
