import {useMemo} from 'react'
import {useAtomValue} from 'jotai'

import type {ContactShortType} from '@bpinc/ad-contacts-state-types'
import type {DirectoryEntry, SearchUserItem} from '@bpinc/ad-directory-state-types'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {selectedItemIdAtom} from '../../atoms'
import {getDisplayName, isInternalContact} from '../../helpers'
import {useFormatedPhone} from '../useFormatedPhone'
import {useCategoryItems} from './useCategoryItems'
import {useContactsLoader} from './useContactsLoader'
import {useExternalContacts} from './useExternalContacts'
import {useInternalContacts} from './useInternalContacts'


type ExternalContact = ContactShortType
export type InternalContact = SearchUserItem
export type CategoryItem = DirectoryEntry

export type ComposedContact = ExternalContact | InternalContact | CategoryItem

type QueryContactsState = {
    contacts: ComposedContact[]
    isLoading: boolean
}

export function useQueryContacts(): QueryContactsState {
    const selectedItemId = useAtomValue(selectedItemIdAtom)
    const skipSearch = !!useAtomValue(selectedItemIdAtom)
    const {phoneNumber: searchQuery} = useFormatedPhone()

    const {
        internalContacts,
        internalContactsLoading,
        getInternalContactsByQuery,
    } = useInternalContacts(searchQuery)
    const {language} = useLocalizationState()

    const {
        externalContacts,
        externalContactsLoading,
        getExternalContactsByQuery,
    } = useExternalContacts()

    const categoryItems = useCategoryItems(searchQuery)

    const isDebouncing = useContactsLoader({
        searchQuery,
        skipSearch,
        getExternalContactsByQuery,
        getInternalContactsByQuery,
    })

    const isLoading = internalContactsLoading || externalContactsLoading || isDebouncing

    const allContacts = useMemo(() => {
        const composedList = [...internalContacts, ...externalContacts, ...categoryItems]
            .sort((item, nextItem) => {
                return getDisplayName(item)
                    .localeCompare(getDisplayName(nextItem), language, {sensitivity: 'base'})
            })

        return searchQuery
            ? composedList
            : []
    }, [internalContacts, externalContacts, categoryItems, searchQuery, language])

    const contacts = useMemo(() => {
        if (!selectedItemId) {
            return allContacts
        }
        return allContacts.filter((item: ComposedContact) => {
            const itemId = isInternalContact(item) ? item.userId : item.id
            return selectedItemId === itemId
        })
    }, [allContacts, selectedItemId])

    return useMemo(() => ({
        contacts,
        isLoading,
    }), [contacts, isLoading])
}
