import React from 'react'

import {useAssociatedChatInteractionId} from '@bpinc/ad-interaction-state-context'
import {useNonAssertedInteractionId} from '@bpinc/lib-interaction-context'

import {InteractionsSlidingModalsList} from '../../../sliding-modal'
import {ActiveInteractionControlPanel} from './active-interaction/ActiveInteractionControlPanel'
import {ActiveInteractionContentArea} from './ActiveInteractionContentArea'


export const InteractionsPage = React.memo(function InteractionsPage() {
    const interactionId = useNonAssertedInteractionId()
    const activeChatId = useAssociatedChatInteractionId(interactionId)


    if (!activeChatId) {
        return null
    }

    return (
        <div className="relative flex grow flex-col" data-interaction-page>
            <div className="flex h-full w-full shrink-0 flex-col bg-primary-background">
                <ActiveInteractionControlPanel />
                <ActiveInteractionContentArea />
            </div>
            <InteractionsSlidingModalsList />
        </div>
    )
})
