import React from 'react'

import {useContact} from '@bpinc/ad-contacts-state-context'
import {useContactDisplayName} from '@bpinc/ad-contacts-state-hooks'
import {contactToShortContact} from '@bpinc/ad-contacts-state-methods'
import {externalContact} from '@bpinc/ad-contacts-state-types'
import type {HandleCallParams} from '@bpinc/ad-interaction-state-hooks'
import {ExternalContactFavoriteButton} from '@bpinc/lib-favorites-button'
import {useEvent} from '@bpinc/lib-helpers'
import {IconEmailEnvelope16, IconPhone16, IconRoundChat16} from '@bpinc/lib-jskit-ui'
import type {ChatParams} from '@bpinc/lib-ui-elements'
import {EmailOption, PhoneOption} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import type {OnEmailParams} from '../../../directory/components/user/types'
import {ExternalContactInteractionButton} from './ExternalContactInteractionButton'


type Props = {
    contactId: string
    onCallClick: (callParams: HandleCallParams) => void
    onChatClick: (chatParams: ChatParams) => void
    onEmailClick: (emailParams: OnEmailParams) => void
}

export const IdentifiedContactContent: React.FC<Props> = ({
    contactId,
    onCallClick,
    onChatClick,
    onEmailClick,
}) => {

    const t = useLocalizationState('t')

    const {data: contactData} = useContact(contactId) ?? {}
    const contactDisplayName = useContactDisplayName(contactId) ?? t.GLOBAL.anonymous()

    const shortContactData = contactData ? contactToShortContact(contactData) : undefined

    const handleCall = useEvent((phone: string) => {
        onCallClick({
            phone,
            contactId: contactId,
            firstName: contactData?.firstName,
            lastName: contactData?.lastName,
        })
    })

    const handleChat = useEvent((phone: string) => {
        onChatClick({
            phone,
            contactId: contactId,
            firstName: contactData?.firstName,
            lastName: contactData?.lastName,
        })
    })

    const handleEmail = useEvent((email: string) => {
        onEmailClick({
            contactId: contactId,
            emailAddress: email,
        })
    })

    return (
        <>
            <div className="flex items-start gap-1 px-2 py-1.5">
                <div className="truncate font-semibold body text-primary-action">
                    {contactDisplayName}
                </div>
                {!!shortContactData && (
                    <ExternalContactFavoriteButton
                        item={shortContactData}
                        className="ml-auto size-6 p-1"
                    />
                )}
            </div>
            <div className="flex flex-auto gap-2 px-2 pb-2">
                <ExternalContactInteractionButton
                    contactId={contactId}
                    className="h-7 flex-auto"
                    icon={<IconPhone16 className="svg-color-current" />}
                    tooltip={t.CONTACT.button_start_call_text()}
                    OptionComponent={PhoneOption}
                    optionsExtractor={externalContact.callOptions}
                    onPressed={handleCall}
                />
                <ExternalContactInteractionButton
                    contactId={contactId}
                    className="h-7 flex-auto"
                    icon={<IconRoundChat16 className="svg-color-current" />}
                    tooltip={t.CONTACT.button_initiate_chat_text()}
                    OptionComponent={PhoneOption}
                    optionsExtractor={externalContact.chatOptions}
                    onPressed={handleChat}
                />
                <ExternalContactInteractionButton
                    contactId={contactId}
                    className="h-7 flex-auto"
                    icon={<IconEmailEnvelope16 className="svg-color-current" />}
                    tooltip={t.CONTACT.button_send_email_text()}
                    OptionComponent={EmailOption}
                    optionsExtractor={externalContact.emailOptions}
                    onPressed={handleEmail}
                />
            </div>
        </>
    )
}
