import {useMemo} from 'react'

import {useActivitiesAsMessages} from './useActivitiesAsMessages'


export function useActivitiesMessagesTillCreatedTime(
    contactId: string,
    latestCreatedTime?: number,
) {
    const activitiesMessages = useActivitiesAsMessages(contactId)

    return useMemo(
        function memoizeNormalizedActivitiesMessages() {
            let finalLatestCreatedTime = latestCreatedTime
            if (!latestCreatedTime) {
                finalLatestCreatedTime = new Date().getTime()
            }

            return latestCreatedTime
                ? activitiesMessages.filter(
                    message =>
                        finalLatestCreatedTime &&
                        message.timestamp &&
                        message.timestamp >= finalLatestCreatedTime,
                )
                : []
        },
        [latestCreatedTime, activitiesMessages],
    )
}
