import React, {useDeferredValue, useEffect, useMemo, useRef} from 'react'
import {partial} from 'lodash-es'

import {useCaseStateMethods} from '@bpinc/ad-case-state-context'
import {useDirectoryState, useDirectoryStateMethods} from '@bpinc/ad-directory-state-context'
import {useEmiItemStatus} from '@bpinc/ad-emi-helpers'
import {useDefaultTransferMode} from '@bpinc/ad-interaction-state-context'
import type {HandleCallParams} from '@bpinc/ad-interaction-state-hooks'
import {useActivityHistoryOpener, useCallHandler, useContactDetailsOpener} from '@bpinc/ad-interaction-state-hooks'
import {
    isRecentCall as isCall,
    isRecentCallConference as isCallConference,
    isRecentChat as isChat,
    isRecentEmail as isEmail,
    isRecentExtChatConference as isChatConference,
} from '@bpinc/ad-interaction-state-methods'
import {useNativeScrollDataManager} from '@bpinc/ad-scroll-state-context-builder'
import {useServiceState} from '@bpinc/ad-service-context'
import {useSessionState} from '@bpinc/ad-session-context'
import {AppRoutes, useNavigationState} from '@bpinc/lib-communicator-navigation-state'
import {useRecentUIState} from '@bpinc/lib-communicator-ui-context'
import {ContactListPlaceholder} from '@bpinc/lib-contact-list-item-ui'
import {useEvent} from '@bpinc/lib-helpers'
import {useNonAssertedInteractionId} from '@bpinc/lib-interaction-context'
import {useNotificationsState} from '@bpinc/lib-notifications-context'
import {
    useOpenGlobalSlidingModal,
    useOpenInteractionSlidingModal,
    useShowSlidingModalChecker,
} from '@bpinc/lib-sliding-modal-context'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {assertDefined} from '@bpinc/lib-types-helpers'
import {type ChatParams, ErrorBoundary, SelectionContainer} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'
import {useRecentState} from '@bpinc/state-recent-context'
import type {RecentItem} from '@bpinc/state-recent-types'
import {isRecentDirectoryEntryItem} from '@bpinc/state-recent-types'

import type {OnEmailParams} from '../../directory/components/user/types'
import {SCROLL_KEYS} from '../../scroll-context'
import {useContactData} from '../hooks/useContactsData'
import {useFilteredList} from '../hooks/useFilteredList'
import {useItemAutoSelector} from '../hooks/useItemAutoSelector'
import {useTransferRecentConferenceScrollManager} from '../hooks/useTransferRecentConferenceScrollManager'
import {RecentListItem} from './RecentListItem'


export type ItemType = 'all' | 'calls' | 'messages'

export type RecentListRenderContext = 'transfer'

type RecentListProps = {
    type: ItemType
    isHighlighted?: (item: RecentItem) => boolean
    /** Custom onClick handler */
    onClick?: (item: RecentItem, e: React.MouseEvent) => void
    renderContext?: RecentListRenderContext
    transferMode?: 'calls' | 'messages'
    transferControlsStyle?: 'add' | 'replace'
    filter?: (recentItem: RecentItem) => boolean
}

let singleClickTimeout: number

export const RecentList: React.FC<RecentListProps> = props => {
    const t = useLocalizationState('t')
    const defaultTransferMode = useDefaultTransferMode()
    const {selected, setSelected, setSearchValue, searchValue} = useRecentUIState()
    const {filter, isHighlighted, transferMode = defaultTransferMode, transferControlsStyle, type, renderContext, onClick} = props
    const openActivityHistory = useActivityHistoryOpener()
    const openContactDetails = useContactDetailsOpener()
    const handleCall = useCallHandler()
    const {selectedService} = useServiceState()
    const listRef = useNativeScrollDataManager(SCROLL_KEYS.recent(type))
    const {recentList, isLoading} = useRecentState()
    const teams = useDirectoryState('teams')
    const directoryStateMethods = useDirectoryStateMethods()
    const {privileges, emi} = useSessionState()
    const {createCaseEmail} = useCaseStateMethods()

    const {hideNotificationsByTag} = useNotificationsState('methods')

    const isUsersRequestedRef = useRef(false)

    const privilegesLoaded = !!Object.keys(privileges).length

    useEffect(() => {
        if (privilegesLoaded && privileges.CAN_VIEW_DIRECTORY) {
            directoryStateMethods.getAgentTeams()
        }
    }, [directoryStateMethods, privileges.CAN_VIEW_DIRECTORY, privilegesLoaded])

    useEffect(() => {
        if (teams.length && !isUsersRequestedRef.current) {
            directoryStateMethods.getLoggedUsers(teams.map(team => team.id))
            isUsersRequestedRef.current = true
        }
    }, [directoryStateMethods, teams, isUsersRequestedRef])

    const useEmiItemStatusCurried = useMemo(() => partial(useEmiItemStatus, emi), [emi])

    const tryInitiateCall = useEvent((callParams: HandleCallParams) => {
        handleCall({
            ...callParams,
            navigateToInteractionsPage: true,
            serviceId: selectedService ?? callParams.serviceId,
        })
    })

    // -------------------------
    // handlers
    // -------------------------

    const handleCallClick = useEvent((callParams: HandleCallParams) => {
        tryInitiateCall({
            ...callParams,
            origination: 'recent',
        })
    })

    const handleChatClick = useEvent((chatParams: ChatParams) => {
        if (chatParams.contactId || chatParams.userId || chatParams.phone || chatParams.groupChatId) {
            openActivityHistory({
                ...chatParams,
                origination: 'recent',
            })
        }
    })

    const handleEmailClick = useEvent((emailParams: OnEmailParams) => {
        createCaseEmail(emailParams)
    })

    const openInteractionSlidingModal = useOpenInteractionSlidingModal()
    const openGlobalSlidingModal = useOpenGlobalSlidingModal()
    const setActivePage = useNavigationState('setActivePage')
    const interactionId = useNonAssertedInteractionId()
    const showRecentConferenceModal = useShowSlidingModalChecker(interactionId, 'recentConference')

    const openRecentConferenceModal = useEvent((recentItem: RecentItem) => {
        const payload = {
            recentItem,
            handleChatClick,
            handleCallClick,
            onClick,
            transferMode,
        }
        if (transferMode) {
            assertDefined(interactionId)
            openInteractionSlidingModal(interactionId, 'recentConference', payload)
        } else {
            openGlobalSlidingModal('recentConference', payload)
        }
    })

    useTransferRecentConferenceScrollManager(listRef)

    const handleClick = useEvent((item: RecentItem, e: React.MouseEvent) => {
        e.preventDefault()
        /** Clicked more than 1 times in a row */
        if (e.nativeEvent.detail > 1) {
            clearTimeout(singleClickTimeout)
            return
        }

        /** wait for potential double click before handling a single click*/
        singleClickTimeout = setTimeout(handleClickCallback, 250)

        function handleClickCallback() {
            /** If custom onClick is passed, use it's handler */
            if (onClick) {
                onClick(item, e)
                return
            }

            if (isCallConference(item) || isChatConference(item)) {
                openRecentConferenceModal(item)
                return
            }

            if (isCall(item)) {
                setSelected(item.itemId)

                if (item.number) {
                    setSearchValue(item.number)
                }
                return
            }

            if (isChat(item)) {
                hideNotificationsByTag('interaction-init')

                openActivityHistory({
                    contactId: item.contactId,
                    userId: item.userId,
                    phone: item.number,
                    firstName: item.firstName,
                    lastName: item.lastName,
                    origination: 'recent',
                    emiPhotoUrl: item.photoUrl,
                    emiUserType: item.userType,
                    previousActivity: item.globalInteractionId
                        ? {
                            globalInteractionId: item.globalInteractionId,
                            mediaType: item.media,
                        }
                        : undefined,
                })
                return
            }

            if (isEmail(item)) {
                if (!item.caseId) {
                    return
                }

                setActivePage({
                    name: AppRoutes.Cases,
                    params: {
                        id: item.caseId,
                    },
                })
            }
        }
    })

    const handleDoubleClick = useEvent((item: RecentItem) => {
        clearTimeout(singleClickTimeout)

        if (item.number && isCall(item)) {
            setSearchValue('')

            return handleCallClick({
                phone: item.number,
                firstName: item.firstName,
                lastName: item.lastName,
                userId: item.userId,
                contactId: item.contactId,
                serviceId: item.serviceId,
            })
        }
    })

    const handleDetailsClick = useEvent((item: RecentItem) => {
        if (isCallConference(item) || isChatConference(item)) {
            openRecentConferenceModal(item)
            return
        }
        openContactDetails({
            userId: isRecentDirectoryEntryItem(item) ? undefined : item.userId,
            contactId: item.contactId,
            entryId: isRecentDirectoryEntryItem(item) ? item.userId : undefined,
        })
    })

    const filteredList = useFilteredList({type, transferMode, filter, recentList, renderContext})
    useItemAutoSelector(filteredList)
    useContactData(filteredList)

    const deferredSearchValue = useDeferredValue(searchValue)

    if (!filteredList.length) {
        if (deferredSearchValue.length) {
            return (
                <p className="mt-[32.5px] text-center">
                    {t.RECENT.no_recent_interactions_found_for(deferredSearchValue)}
                </p>
            )
        }
        return (
            <p className="mt-[32.5px] text-center">
                {t.RECENT.no_recent_interactions_found()}
            </p>
        )
    }

    if (isLoading) {
        return (
            <div className="relative mt-2">
                <div className="absolute inset-x-0">
                    <ContactListPlaceholder amount={15} />
                </div>
            </div>
        )
    }

    return (
        <SelectionContainer
            ref={listRef}
            className={tcn(
                'scrollbar-on-hover flex-auto overflow-auto',
                {'overflow-hidden': showRecentConferenceModal},
            )}
        >
            {/* without this extra wrapper with filled background "scrollbar-on-hover" produces visual artifacts */}
            <div className="bg-primary-background">
                <ErrorBoundary fallback={null} >
                    {filteredList.map((item, index) => (
                        <RecentListItem
                            key={item.itemId || index}
                            onClick={handleClick}
                            onDoubleClick={handleDoubleClick}
                            onCallClick={handleCallClick}
                            onChatClick={handleChatClick}
                            onEmailClick={handleEmailClick}
                            onDetailsClick={handleDetailsClick}
                            isHighlighted={isHighlighted?.(item)}
                            isSelected={!!selected && selected === item.itemId}
                            transferMode={transferMode}
                            transferControlsStyle={transferControlsStyle}
                            useEmiItemStatus={useEmiItemStatusCurried}
                            recentItem={item}
                            repeatCount={item.repeatCount}
                        />
                    ))}
                </ErrorBoundary>
            </div>
        </SelectionContainer>
    )
}
