import React, {useCallback} from 'react'
import {DndProvider} from 'react-dnd'
import {HTML5Backend} from 'react-dnd-html5-backend'

import {useAppDefaultSettings} from '@bpinc/ad-local-settings-context'
import type {ActivePage, AppRoutes} from '@bpinc/lib-communicator-navigation-state'
import {useNavigationState} from '@bpinc/lib-communicator-navigation-state'
import {useCommunicatorUIState} from '@bpinc/lib-communicator-ui-context'
import {SmSegment, SmSidebar} from '@bpinc/lib-external-ui-elements'
import {useIncomingChatMessageAudioNotifications} from '@bpinc/lib-helpers-ui'
import {Notifications} from '@bpinc/lib-notifications-ui'
import {useInteractionNotifications} from '@bpinc/lib-system-notifications'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {createComponentLoader} from '@bpinc/lib-ui-elements'

import {AppMinimized} from '../../app-minimized'
import {defaultModalsConfig} from '../constants'
import {useStateSubscriptions} from '../hooks/state-subscription/useStateSubscriptions'
import {useAppConfigCheckers} from '../hooks/useAppConfigCheckers'
import {useClosePanelsOnNotifications} from '../hooks/useClosePanelsOnNofification'
import {useMinimized} from '../hooks/useMinimized'
import {useModulesImportByPrivileges} from '../hooks/useModulesImportByPrivileges'
import {useOpenSettingsOnPhoneSelectionDialogClosed} from '../hooks/useOpenSettingsOnPhoneSelectionDialogClosed'
import {SidebarRight} from './SideBarRight/SidebarRight'
import {CommunicatorContent} from './CommunicatorContent'
import {CompactNotificationModal} from './CompactNotificationModal'
import {InteractionModalsAutoOpener} from './InteractionModalsAutoOpener'
import {MainPageHeader} from './MainPageHeader'
import {MainPageInteractionsList} from './MainPageInteractionsList'
import {SidebarLeft} from './SidebarLeft'

import styles from '../styles/MainPage.module.less'


const {CalendarModalsAutoOpenerLoader} = createComponentLoader(
    'calendar',
    () => import('calendar'),
    'CalendarModalsAutoOpener',
)

export const MainPage: React.FC = React.memo(function MainPage() {
    const {
        leftPanelOpened,
        rightPanelOpened,
        toggleLeftPanel,
        toggleRightPanel,
        toggleSettings,
    } = useCommunicatorUIState([
        'leftPanelOpened',
        'rightPanelOpened',
        'toggleLeftPanel',
        'toggleRightPanel',
        'toggleSettings',
    ])

    const {activePage, setActivePage} = useNavigationState()

    useAppConfigCheckers()
    useClosePanelsOnNotifications()
    useAppDefaultSettings({
        modals: defaultModalsConfig,
    })
    useOpenSettingsOnPhoneSelectionDialogClosed()
    useInteractionNotifications()
    useIncomingChatMessageAudioNotifications(true)
    useModulesImportByPrivileges()

    const onMenuItemClick = useCallback(
        (name: AppRoutes, params?: Record<string, unknown>) => {
            const page: ActivePage = {name}

            if (params) page.params = params

            setActivePage(page)
            toggleRightPanel(false)
        },
        [toggleRightPanel, setActivePage],
    )

    const {
        isMinimized,
        setIsMinimized,
        onMinimizedAgentStateClick,
        onMinimizedInteractionClick,
        onMinimizedMenuItemClick,
        onMinimizedSettingsClick,
    } = useMinimized({
        toggleLeftPanel,
        toggleRightPanel,
        toggleSettings,
        onMenuItemClick,
    })

    useStateSubscriptions({
        isMinimized,
        toggleRightPanel,
        toggleSettings,
        setIsMinimized,
    })

    return (
        <DndProvider backend={HTML5Backend}>
            {isMinimized && (
                <AppMinimized
                    activePageName={activePage.name}
                    onMenuItemClick={onMinimizedMenuItemClick}
                    onSettingsClick={onMinimizedSettingsClick}
                    onAgentStateClick={onMinimizedAgentStateClick}
                    onInteractionClick={onMinimizedInteractionClick}
                />
            )}
            <SmSegment.Group
                className={tcn(
                    styles.outerWrapper,
                    isMinimized && styles.invisible,
                )}
                data-page-section="main-page"
                id="mainContainer"
            >
                <Notifications className={styles.notifications} />
                <SmSidebar.Pushable
                    as={SmSegment.Group}
                    className={styles.wrapper}
                >
                    <SidebarLeft />
                    <SidebarRight
                        activePageName={activePage.name}
                        onMenuItemClick={onMenuItemClick}
                    />

                    <SmSidebar.Pusher
                        data-page-section="main-content"
                        dimmed={leftPanelOpened || rightPanelOpened}
                        className={styles.pusher}
                    >
                        <MainPageHeader />
                        <MainPageInteractionsList />
                        <CommunicatorContent />
                    </SmSidebar.Pusher>
                    <CompactNotificationModal />
                </SmSidebar.Pushable>
            </SmSegment.Group>
            <InteractionModalsAutoOpener />
            <CalendarModalsAutoOpenerLoader />
        </DndProvider>
    )
})
