import type {MouseEvent} from 'react'
import React, {useCallback, useMemo} from 'react'

import {useInteractionProp} from '@bpinc/ad-interaction-state-context'
import {getInteractionStepId} from '@bpinc/ad-interaction-state-methods'
import {useSessionState} from '@bpinc/ad-session-context'
import {IconOpenDetached} from '@bpinc/lib-icons'
import {useInteractionId} from '@bpinc/lib-interaction-context'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {Button} from '@bpinc/lib-ui-elements'
import type {InteractionTab} from '@bpinc/state-interaction-ui'
import {useAvailableInteractionTabsController, useInteractionUIAvailableTabs} from '@bpinc/state-interaction-ui'
import type {MessagesDeclaration} from '@bpinc/state-localization'
import {useLocalizationState} from '@bpinc/state-localization-context'


const tabClassName = tcn('min-w-0 flex-auto grow-0 px-2.5')

const TABS_ORDER: InteractionTab[] = [
    'transcript',
    'all',
    'calls',
    'messages',
    'workitemContacts',
    'workitemDetails',
    'workitemHistory',
    'crm',
    'resolution',
]

type Tab = {
    id: InteractionTab
    content: React.ReactNode
    title: string
    className?: string
}

export function useTabs(): Tab[] {
    const interactionId = useInteractionId()

    useAvailableInteractionTabsController(interactionId)

    const {t} = useLocalizationState()
    const availableTabs = useInteractionUIAvailableTabs(interactionId)
    const transcriptTab = useTranscriptTab(t)
    const allInteractionsTab = useAllInteractionsTab(t)
    const callInteractionsTab = useCallInteractionsTab(t)
    const messageInteractionsTab = useMessageInteractionsTab(t)
    const crmTab = useCrmTab(t)
    const workitemContactsTab = useWorkitemContactsTab(t)
    const workitemDetailsTab = useWorkitemDetailsTab(t)
    const workitemHistoryTab = useWorkitemHistoryTab(t)
    const resolutionTab = useResolutionTab(t)

    const tabsMap = useMemo<Record<InteractionTab, Tab>>(() => ({
        transcript: transcriptTab,
        all: allInteractionsTab,
        calls: callInteractionsTab,
        messages: messageInteractionsTab,
        crm: crmTab,
        workitemContacts: workitemContactsTab,
        workitemDetails: workitemDetailsTab,
        workitemHistory: workitemHistoryTab,
        resolution: resolutionTab,
    }), [
        transcriptTab,
        allInteractionsTab,
        callInteractionsTab,
        messageInteractionsTab,
        crmTab,
        workitemContactsTab,
        workitemDetailsTab,
        workitemHistoryTab,
        resolutionTab,
    ])

    return useMemo(() => {
        return TABS_ORDER.reduce<Tab[]>((arr, tabId) => {
            if (availableTabs[tabId]) {
                arr.push(tabsMap[tabId])
            }
            return arr
        }, [])
    }, [availableTabs, tabsMap])
}

function useTranscriptTab(t: MessagesDeclaration): Tab {
    const {serverOrigin} = useSessionState()
    const interactionId = useInteractionId()
    const interactionStepId = useInteractionProp(interactionId, getInteractionStepId)
    const label = t.HOME_SCREEN.interaction_tab_label_transcript()

    const openDetachedTranscriptWindow = useCallback((evt: MouseEvent) => {
        evt.stopPropagation()
        const params = new URLSearchParams({
            interactionStepId: interactionStepId,
        })

        window.open(`${serverOrigin}/agent/voice-transcript-app?${params.toString()}`)
    }, [serverOrigin, interactionStepId])

    return useMemo(() => ({
        content: (
            <div className="flex min-w-0 flex-auto items-center">
                <span className="flex-auto truncate">{label}</span>
                <Button
                    transparent
                    className="group ml-4"
                    onClick={openDetachedTranscriptWindow}
                >
                    <IconOpenDetached
                        className="transition-all duration-300 group-hover:[&_g]:stroke-primary-legacy group-hover:[&_path]:fill-primary-legacy"
                    />
                </Button>
            </div>
        ),
        title: label,
        id: 'transcript',
    }), [label, openDetachedTranscriptWindow])
}

function useAllInteractionsTab(t: MessagesDeclaration): Tab {
    const label = t.HOME_SCREEN.interaction_tab_label_all_interactions()
    return useMemo(() => ({
        content: <span className="truncate">{label}</span>,
        id: 'all',
        title: label,
    }), [label])
}

