import {useInteractionProp} from '@bpinc/ad-interaction-state-context'
import {isWorkItem, isWorkitemCall} from '@bpinc/ad-interaction-state-methods'
import {useInteractionId} from '@bpinc/lib-interaction-context'

import {useSelectedDispositionType} from './useSelectedDispositionType'


export function useShowDispositionRescheduleForm(): boolean {
    const interactionId = useInteractionId()
    const activeItemIsWorkItem = useInteractionProp(
        interactionId,
        activeItem => isWorkItem(activeItem) || isWorkitemCall(activeItem),
    )
    const dispositionType = useSelectedDispositionType()

    return activeItemIsWorkItem && dispositionType === 'CONTINUE'
}
