import {useRef} from 'react'

import {useResizeObserver} from '@bpinc/lib-helpers'


export function useCommunicatorNavbarHeight() {
    const bottomNav = useRef<HTMLElement>(document.querySelector('[data-testid="bottom-nav"]'))
    const resizeObserver = useResizeObserver(bottomNav)

    return resizeObserver?.contentRect?.height ?? 0
}
