import type {CSSProperties} from 'react'
import React, {useMemo} from 'react'


interface ImageProps {
    style?: CSSProperties
    value?: string
    className?: string
}

export const Image: React.FC<ImageProps> = ({
    style,
    value,
    className,
}) => {
    const finalStyle = useMemo(() => ({
        ...style,
        minHeight: style?.height,
    }), [style])

    return (
        <img
            className={className}
            src={value ? `data:image;base64,${value}` : undefined}
            style={finalStyle}
            alt="form-image"
        />
    )
}
