import {useMemo} from 'react'
import {orderBy} from 'lodash-es'

import {useContactsState} from '@bpinc/ad-contacts-state-context'
import type {ChatType} from '@bpinc/ad-interaction-chat-state-types'
import type {ChatMessage} from '@bpinc/ad-interaction-message-types'
import type {VoiceTranscriptSuggestion} from '@bpinc/ad-interaction-state-types'

import {getExternalChatMessageDisplayType} from '../../../../helpers'
import {useKeyDatesInserter} from '../../../../hooks/useKeyDatesInserter'
import type {ExternalChatMessage, ExternalChatMessageWithDate} from '../../../../types'
import {
    isCallOrVoiceTranscript,
    isExternalChatMessageDate,
    isHistoryMessage,
    isVoiceTranscriptMessage,
    isVoiceTranscriptSuggestion,
} from '../../../../types'
import {useActivitiesMessagesTillCreatedTime} from './useActivitiesMessagesTillCreatedTime'
import {useNormalizedChatMessages} from './useNormalizedChatMessages'
import {useNormalizedHistoryMessages} from './useNormalizedHistoryMessages'


const defaultBotSuggestions: VoiceTranscriptSuggestion[] = []

export function useNormalizedMessagesWithDates(
    type: ChatType,
    contactId: string,
    itemId: string,
    botSuggestions: VoiceTranscriptSuggestion[] = defaultBotSuggestions,
) {
    const typeToMediaFilter = {
        messages: 'chat',
        calls: 'voice',
        all: 'all',
        transcript: 'all', //useNormalizedMessagesWithDates isn't called for transcript tab
    } as const

    const insertKeyDates = useKeyDatesInserter()
    const normalizedHistoryMessages = useNormalizedHistoryMessages(contactId, itemId)
    const {normalizedChatMessages} = useNormalizedChatMessages(itemId)
    const activityHistory = useContactsState('activityHistory')
    const latestCreatedTime = activityHistory[contactId]?.activityCreatedTimeByMedia?.[typeToMediaFilter[type]]

    const activitiesMessages = useActivitiesMessagesTillCreatedTime(contactId, latestCreatedTime)

    return useMemo(
        function memoizeMessagesWithDates() {
            const allMessages = orderBy(
                [...normalizedHistoryMessages, ...botSuggestions, ...activitiesMessages, ...normalizedChatMessages],
                'timestamp',
            )

            const filteredByTypeMessages = filterMessagesByTypeAndStartTime(allMessages, type, latestCreatedTime)

            return {
                messagesWithDates: insertKeyDates(filteredByTypeMessages, getTimestamp, additionalConditions),
            }
        },
        [
            botSuggestions,
            normalizedHistoryMessages,
            activitiesMessages,
            normalizedChatMessages,
            type,
            latestCreatedTime,
            insertKeyDates,
        ],
    )
}

function filterMessagesByTypeAndStartTime<T extends Exclude<ExternalChatMessage | VoiceTranscriptSuggestion, string>[]>(
    messages: T,
    type: ChatType,
    latestCreatedTime: number | undefined,
) {
    return messages.reduce(filterReducer(type, latestCreatedTime), [])
}

function getTimestamp<T extends {timestamp: number}>(message: T): number {
    return message.timestamp
}

function isChatMessage(value: {timestamp: number}): value is ChatMessage {
    return 'displayName' in value && 'itemId' in value && 'partyId' in value
}

function additionalConditions<T extends {timestamp: number}>(message: T): boolean {
    return isChatMessage(message) ? message.displayType !== 'navigation' : true
}

function filterReducer<T extends ExternalChatMessage | VoiceTranscriptSuggestion>(
    type: ChatType, latestCreatedTime: number | undefined,
) {
    function allowMessage(messages: T[], message: T, predicate?: (msg: T) => boolean) {
        if (
            (!latestCreatedTime || latestCreatedTime <= message.timestamp) &&
            (isVoiceTranscriptSuggestion(message) || !predicate || predicate(message))
        ) {
            messages.push(message)
        }

        return messages
    }

    function cleanKeyDates<N extends ExternalChatMessageWithDate | VoiceTranscriptSuggestion>(messages: N[], message: N) {
        const lastIndex = messages.length - 1
        if (isExternalChatMessageDate(messages[lastIndex])) {
            messages.splice(lastIndex, 1, message)
        } else {
            messages.push(message)
        }

        return messages
    }

    return function checkByTypeReducer(messages: T[], message: T) {
        if (isExternalChatMessageDate(message)) {
            return cleanKeyDates(messages, message)
        }
        if (type === 'messages') {
            return allowMessage(messages, message, isMessage)
        }
        if (type === 'calls') {
            return allowMessage(messages, message, isCallOrVoiceTranscript)
        }

        return allowMessage(messages, message)
    }
}

//**Private Functions***************************************

function isMessage(message: ExternalChatMessageWithDate | VoiceTranscriptSuggestion): boolean {
    if (isVoiceTranscriptMessage(message)) {
        return false
    }

    if (isExternalChatMessageDate(message)) {
        return true
    }


    const mediaType = isHistoryMessage(message) ? message.mediaType : undefined
    if (mediaType === 'VOICE_TRANSCRIPT') {
        return false
    }

    const displayType = getExternalChatMessageDisplayType(message)
    return (
        displayType === 'message' ||
        displayType === 'file' ||
        displayType === 'system' ||
        mediaType === 'CHAT'
    )
}

