import type {ReactElement} from 'react'
import React from 'react'
import type {FieldValues} from 'react-hook-form'

import type {FieldImplementationProps, FieldProps, FieldRenderImplementationProps} from '@bpinc/lib-forms-base'
import {FieldConstructor} from '@bpinc/lib-forms-base'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {uuid} from '@bpinc/lib-uuid'

import type {TextAreaProps} from './TextArea'
import {TextArea as TextAreaUI} from './TextArea'


type TextAreaFieldProps = FieldImplementationProps<TextAreaProps & {
    dataKey?: string
}>

function renderElement<TFormValues extends FieldValues>({
    errorClassName,
    ...props
}: FieldRenderImplementationProps<TextAreaFieldProps, TFormValues>): ReactElement {
    const ariaId1 = uuid()

    return (
        <TextAreaUI
            {...props}
            aria-labelledby={ariaId1}
            errorClassName={tcn('self-end text-red-500', errorClassName)}
        />
    )
}

export function TextAreaField<TFormValues extends FieldValues>(props: FieldProps<TextAreaFieldProps, TFormValues>): ReactElement {
    return <FieldConstructor {...props} renderElement={renderElement} />
}

