import type {SyntheticEvent} from 'react'
import React, {useCallback, useEffect, useMemo, useState} from 'react'
import {partial, uniq} from 'lodash-es'

import {
    useInteraction,
    useInteractionProp,
    useInteractionStateConnector,
    useInteractionStateMethods,
} from '@bpinc/ad-interaction-state-context'
import {getItemDisposition, getPhoneFromItem} from '@bpinc/ad-interaction-state-methods'
import type {DispositionRescheduleData} from '@bpinc/ad-interaction-state-types'
import {useAllTimezones, useTimezonesStateMethods} from '@bpinc/ad-timezones-state-context'
import {convertToDayjs, formatDate, getLongDateFormat, useAsRef} from '@bpinc/lib-helpers'
import {useInteractionId} from '@bpinc/lib-interaction-context'
import {
    FieldLayout,
    IconArrowDown16,
    SearchableSelectField,
    Tooltip,
    TriggerDropdown,
} from '@bpinc/lib-jskit-ui'
import {DateInput, TimeInput} from '@bpinc/lib-semantic-ui-date-time-pickers'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {truthy} from '@bpinc/lib-types-helpers'
import type {DropdownProps} from '@bpinc/lib-ui-elements'
import type {DispositionRescheduleForm as DispositionRescheduleFormType} from '@bpinc/state-interaction-ui'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {useFavoriteTimeZoneInitializer} from '../hooks/useFavoriteTimeZoneInitializer'
import {useRescheduleFormOptions} from '../hooks/useRescheduleFormOptions'
import {TimeZoneDropdown} from './TimeZoneDropdown'


interface DispositionRescheduleForm {
    date: string
    timeFrom: string
    timeTo: string
    selectedTimezoneId: string
    phoneOn: string
}

const initialState: DispositionRescheduleForm = {
    date: '',
    timeFrom: '',
    timeTo: '',
    selectedTimezoneId: '',
    phoneOn: '',
}

const rescheduleDateFormat = 'L'
const rescheduleTimeFormat = 'HH:mm'
const dispositionRescheduleDatetimeFormat = 'YYYY-MM-DD HH:mm:ss'

const convert = {
    serverFormToClient({
        rescheduleFrom,
        reschedulePhone,
        rescheduleTimezone,
        rescheduleTo,
    }: Partial<DispositionRescheduleData> = {}) {
        const formState: Partial<DispositionRescheduleForm> = {}

        if (rescheduleFrom || rescheduleTo) {
            formState.date = formatDate(
                rescheduleFrom || rescheduleTo,
                rescheduleDateFormat,
            )
        }

        if (rescheduleFrom) {
            formState.timeFrom = formatDate(
                rescheduleFrom,
                rescheduleTimeFormat,
            )
        }

        if (rescheduleTo) {
            formState.timeTo = formatDate(rescheduleTo, rescheduleTimeFormat)
        }

        if (reschedulePhone) {
            formState.phoneOn = reschedulePhone
        }

        if (rescheduleTimezone) {
            formState.selectedTimezoneId = rescheduleTimezone
        }

        return formState
    },
    clientFormToServer({
        date,
        timeFrom,
        timeTo,
        selectedTimezoneId,
        phoneOn,
        currentState,
    }: Partial<DispositionRescheduleForm> & {
        currentState: Partial<DispositionRescheduleForm>
    }) {
        const rescheduleData: DispositionRescheduleData = {}

        const dateFrom = convertToDayjs(
            `${date || currentState.date} ${timeFrom || currentState.timeFrom}`,
            `${rescheduleDateFormat} ${rescheduleTimeFormat}`,
        )
        const dateTo = convertToDayjs(
            `${date || currentState.date} ${timeTo || currentState.timeTo}`,
            `${rescheduleDateFormat} ${rescheduleTimeFormat}`,
        )
        dateTo.add(dateFrom.isAfter(dateTo) ? 1 : 0, 'd')

        if (dateFrom.isValid() && (date || timeFrom)) {
            rescheduleData.rescheduleFrom = dateFrom.format(
                dispositionRescheduleDatetimeFormat,
            )
        }

        if (dateTo.isValid() && (date || timeTo)) {
            rescheduleData.rescheduleTo = dateTo.format(
                dispositionRescheduleDatetimeFormat,
            )
        }

        if (phoneOn) {
            rescheduleData.reschedulePhone = phoneOn
        }

        if (selectedTimezoneId) {
            rescheduleData.rescheduleTimezone = selectedTimezoneId
        }

        return rescheduleData
    },
    phoneToOption(phone: string) {
        return {content: phone, value: phone, 'data-testid': 'option'}
    },
}

export const DispositionRescheduleForm = React.memo(function RescheduleForm() {
    const itemId = useInteractionId()

    const syncedFormState = useInteractionProp(itemId, getItemDisposition)

    const [formState, setFormState] = useState(() =>
        syncedFormState
            ? {
                ...initialState,
                ...convert.serverFormToClient(syncedFormState),
            }
            : initialState,
    )

    const {setDispositionReschedule: setSyncedFormState} =
        useInteractionStateMethods()

    const {t} = useLocalizationState()
    const allTimezones = useAllTimezones()
    const timezonesMethods = useTimezonesStateMethods()
    const options = useRescheduleFormOptions()


    const activeInteraction = useInteraction(itemId)
    const activeItemNumber = useInteractionProp(itemId, getPhoneFromItem)

    const suggestedPhones = uniq([
        activeInteraction?.displayData?.number,
        activeItemNumber,
        syncedFormState?.reschedulePhone,
    ]).filter(Boolean)

    const interactionStateConnector = useInteractionStateConnector()

    const [suggestedPhoneOptions, setSuggestedPhoneOptions] = useState(() =>
        truthy(suggestedPhones).map(convert.phoneToOption),
    )

    const suggestedPhoneOptionsRef = useAsRef(suggestedPhoneOptions)

    useEffect(() => {
        const subscription = interactionStateConnector?.dispenser.subscribe({
            onDispositionRescheduleUpdated: (innerItemId, data) => {
                if (itemId === innerItemId) {
                    setFormState(s => ({
                        ...s,
                        ...convert.serverFormToClient(data),
                    }))
                    if (
                        data.reschedulePhone &&
                        !suggestedPhoneOptionsRef.current.some(phone => {
                            return phone.value === data.reschedulePhone
                        })
                    ) {
                        setSuggestedPhoneOptions(s => {
                            if (!data.reschedulePhone) {
                                return s
                            }

                            return [
                                ...s,
                                convert.phoneToOption(data.reschedulePhone),
                            ]
                        })
                    }
                }
            },
        })
        return () => subscription?.unsubscribe()
    }, [itemId, interactionStateConnector?.dispenser, suggestedPhoneOptionsRef])

    const {date, timeFrom, timeTo, selectedTimezoneId, phoneOn} = formState

    const setRescheduleFormState = useCallback(
        function setRescheduleFormStateCallback(
            rescheduleForm: Partial<DispositionRescheduleFormType>,
        ) {
            setFormState(state => ({
                ...state,
                ...rescheduleForm,
            }))
        },
        [],
    )

    const handleInitializeFavoriteTimeZone = useCallback(
        function handleInitializeFavoriteTimeZoneCallback(
            suggestedTimeZoneId: string,
        ) {
            setRescheduleFormState({selectedTimezoneId: suggestedTimeZoneId})
        },
        [setRescheduleFormState],
    )

    const firstSuggestedPhone = suggestedPhones[0] ?? ''

    useEffect(() => {
        if (firstSuggestedPhone && !formState.phoneOn) {
            setRescheduleFormState({phoneOn: firstSuggestedPhone})
        }
    }, [setRescheduleFormState, firstSuggestedPhone, formState.phoneOn])

    useFavoriteTimeZoneInitializer({
        additionalOnInitializeHandler: handleInitializeFavoriteTimeZone,
    })

    const selectTimezone = useCallback(
        (timezoneId: string) => {
            if (timezoneId) {
                const timezoneData = allTimezones.find(
                    tz => tz.id === timezoneId,
                )
                if (timezoneData) {
                    timezonesMethods.addToFavorites(timezoneData)

                    const selectedTimezoneData = allTimezones.find(
                        tz => tz.id === selectedTimezoneId,
                    )
                    if (selectedTimezoneData) {
                        const mOffset = Number(
                            timezoneData.standardOffset -
                                selectedTimezoneData.standardOffset,
                        )
                        const dateFrom = convertToDayjs(
                            `${date} ${timeFrom}`,
                            `${rescheduleDateFormat} ${rescheduleTimeFormat}`,
                        )
                        const dateTo = convertToDayjs(
                            `${date} ${timeTo}`,
                            `${rescheduleDateFormat} ${rescheduleTimeFormat}`,
                        )
                        if (
                            dateFrom.isValid() &&
                            dateTo.isValid() &&
                            !Number.isNaN(mOffset)
                        ) {
                            dateTo.add(dateFrom.isAfter(dateTo) ? 1 : 0, 'd')
                            dateFrom.add(mOffset, 'm')
                            dateTo.add(mOffset, 'm')
                            const updatedState = {
                                date: dateFrom.format(rescheduleDateFormat),
                                timeFrom: dateFrom.format(rescheduleTimeFormat),
                                timeTo: dateTo.format(rescheduleTimeFormat),
                            }

                            setRescheduleFormState(updatedState)
                            setSyncedFormState(
                                itemId,
                                convert.clientFormToServer({
                                    ...updatedState,
                                    currentState: formState,
                                }),
                            )
                        }
                    }
                }
                setRescheduleFormState({selectedTimezoneId: timezoneId})
                setSyncedFormState(itemId, {rescheduleTimezone: timezoneId})
            }
        },
        [
            allTimezones,
            setRescheduleFormState,
            setSyncedFormState,
            itemId,
            timezonesMethods,
            selectedTimezoneId,
            date,
            timeFrom,
            timeTo,
            formState,
        ],
    )

    const handleDateChange = useCallback(
        (_evt: SyntheticEvent, {value}: DropdownProps) => {
            if (typeof value !== 'string') {
                throw new Error('Date value must be a string')
            }

            setRescheduleFormState({date: value})
        },
        [setRescheduleFormState],
    )

    const handleTimeFromChange = useCallback(
        (_evt: SyntheticEvent, {value}: DropdownProps) => {
            if (typeof value !== 'string') {
                throw new Error('Time value must be a string')
            }

            setRescheduleFormState({timeFrom: value})
        },
        [setRescheduleFormState],
    )

    const handleTimeToChange = useCallback(
        (_evt: SyntheticEvent, {value}: DropdownProps) => {
            if (typeof value !== 'string') {
                throw new Error('Time value must be a string')
            }

            setRescheduleFormState({timeTo: value})
        },
        [setRescheduleFormState],
    )

    const handlePhoneOnChange = useCallback(
        (value: string | undefined) => {
            if (typeof value !== 'string') {
                throw new Error('Phone value must be a string')
            }

            setRescheduleFormState({phoneOn: value})
            setSyncedFormState(itemId, {reschedulePhone: value})
        },
        [itemId, setSyncedFormState, setRescheduleFormState],
    )

    const optionsCallbacks = useMemo(() => {
        return options.map(({getDateAndTime}) => {
            return {
                onClick: (e: SyntheticEvent) => {
                    const {newDate, newTime} = getDateAndTime()
                    setRescheduleFormState({
                        date: newDate,
                        timeFrom: newTime,
                    })
                    e.preventDefault()
                },
            }
        })
    }, [options, setRescheduleFormState])

    const handleDateTimeBlur = useCallback(
        function handleDateBlurCallback(
            fieldName: 'timeFrom' | 'timeTo' | 'date',
            _: SyntheticEvent,
            data?: { value: string },
        ) {
            if (typeof data !== 'object' || typeof data.value !== 'string') {
                return
            }

            setSyncedFormState(
                itemId,
                convert.clientFormToServer({
                    [fieldName]: data.value,
                    currentState: formState,
                }),
            )
        },
        [itemId, formState, setSyncedFormState],
    )

    const handleDateBlur = useMemo(
        () => partial(handleDateTimeBlur, 'date'),
        [handleDateTimeBlur],
    )
    const handleTimeFromBlur = useMemo(
        () => partial(handleDateTimeBlur, 'timeFrom'),
        [handleDateTimeBlur],
    )
    const handleTimeToBlur = useMemo(
        () => partial(handleDateTimeBlur, 'timeTo'),
        [handleDateTimeBlur],
    )

    const smallPickerClassName = tcn('w-1/4 [&_input]:w-full')

    return (
        <div className="flex flex-auto flex-col gap-6" data-testid="reschedule">
            <h3 className="mt-6 font-semibold body">
                {t.RESCHEDULE.reschedule_call()}
            </h3>
            <div className="flex items-end justify-between gap-4">
                <FieldLayout
                    className="w-1/2 [&_div]:!w-full"
                    label={
                        <TriggerDropdown
                            options={options.map(({id, name}, i) => {
                                return (
                                    <button
                                        onClick={optionsCallbacks[i].onClick}
                                        key={id}
                                        aria-label={name}
                                        data-testid="option"
                                    >
                                        {name}
                                    </button>
                                )
                            })}
                        >
                            {isOpened => (
                                <button>
                                    <Tooltip
                                        content={t.CALENDAR_EVENT_FORM.show_date_suggestions_label()}
                                    >
                                        <div className="flex items-center gap-1">
                                            <span>
                                                {t.RESCHEDULE.next_call_date()}
                                            </span>
                                            <IconArrowDown16 className={isOpened ? 'rotate-180' : undefined} />
                                        </div>
                                    </Tooltip>
                                </button>
                            )}
                        </TriggerDropdown>
                    }
                >
                    <DateInput
                        value={date}
                        onChange={handleDateChange}
                        onBlur={handleDateBlur}
                        placeholder={getLongDateFormat(rescheduleDateFormat)}
                        dateFormat={rescheduleDateFormat}
                        closable
                        animation="fade right"
                        popupPosition="bottom left"
                        localization={navigator.language}
                        aria-label={t.CALENDAR_EVENT_FORM.label_date()}
                        data-testid="date-picker"
                    />
                </FieldLayout>
                <FieldLayout
                    label={t.RESCHEDULE.from()}
                    required
                    className={smallPickerClassName}
                >
                    <TimeInput
                        value={timeFrom}
                        onChange={handleTimeFromChange}
                        onBlur={handleTimeFromBlur}
                        placeholder={rescheduleTimeFormat.toUpperCase()}
                        timeFormat="24"
                        closable
                        animation="fade right"
                        popupPosition="bottom right"
                        localization={navigator.language}
                        aria-label={t.CALENDAR_EVENT_FORM.time_from_label()}
                        data-testid="time-from-picker"
                    />
                </FieldLayout>
                <FieldLayout
                    label={t.RESCHEDULE.to()}
                    required
                    className={smallPickerClassName}
                >
                    <TimeInput
                        value={timeTo}
                        onChange={handleTimeToChange}
                        onBlur={handleTimeToBlur}
                        placeholder={rescheduleTimeFormat.toUpperCase()}
                        timeFormat="24"
                        closable
                        animation="fade right"
                        popupPosition="bottom right"
                        localization={navigator.language}
                        aria-label={t.CALENDAR_EVENT_FORM.time_to_label()}
                        data-testid="time-to-picker"
                    />
                </FieldLayout>
            </div>
            <TimeZoneDropdown
                layoutClassName="w-full"
                label={t.RESCHEDULE.time_zone()}
                selectedTimeZone={syncedFormState?.rescheduleTimezone ?? ''}
                handleSelect={selectTimezone}
            />
            <SearchableSelectField
                required
                allowCustomValue
                layoutClassName="w-full"
                label={t.RESCHEDULE.phone()}
                value={phoneOn}
                onChange={handlePhoneOnChange}
                aria-label={t.CALENDAR_EVENT_FORM.phone_number_label()}
                options={suggestedPhoneOptions}
                data-testid="phone-dropdown"
            />
        </div>
    )
})
