import {useEffect, useMemo} from 'react'
import {get} from 'lodash-es'

import {useInteraction} from '@bpinc/ad-interaction-state-context'
import {useAllTimezones, useFavoritesTimezones, useTimezonesStateMethods} from '@bpinc/ad-timezones-state-context'
import {useInteractionId} from '@bpinc/lib-interaction-context'


interface UseFavoriteTimeZoneInitializer {
    additionalOnInitializeHandler: (suggestedTimeZoneId: string) => void
}

export function useFavoriteTimeZoneInitializer({additionalOnInitializeHandler}: UseFavoriteTimeZoneInitializer) {
    const allTimezones = useAllTimezones()
    const favoriteTimezones = useFavoritesTimezones()
    const timezonesMethods = useTimezonesStateMethods()
    const interactionId = useInteractionId()
    const activeInteraction = useInteraction(interactionId)

    const suggestedTimezone =
        get(activeInteraction, 'workitemData.suggestedTz') ||
        get(favoriteTimezones[0], 'name') ||
        get(allTimezones[0], 'name')

    const suggestedTimezoneData = useMemo(function memoizeSuggestedTimezoneData() {
        return allTimezones.find(
            tz => tz.id === suggestedTimezone,
        )
    }, [allTimezones, suggestedTimezone])

    useEffect(() => {
        const suggestedTimeZoneId = get(suggestedTimezoneData, 'id')
        if (suggestedTimezoneData && suggestedTimeZoneId) {
            additionalOnInitializeHandler(suggestedTimeZoneId)
            timezonesMethods.addToFavorites(suggestedTimezoneData)
        }
    }, [additionalOnInitializeHandler, suggestedTimezoneData, timezonesMethods])
}
