import React from 'react'

import type {ActivityHistoryType} from '@bpinc/ad-contacts-state-types'
import type {ExternalChatChannelType} from '@bpinc/ad-transport-types'
import {formatDate} from '@bpinc/lib-helpers'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {getChannelImageIcon} from '@bpinc/lib-ui-elements'

import styles from '../../../../styles/Chat.module.less'


interface MessageTopInfoProps {
    isScenario?: boolean
    displayName?: string
    showName?: boolean
    gapBeforeTime?: boolean
    channel?: ExternalChatChannelType
    mediaType?: ActivityHistoryType
    timestamp: number
    showTime?: boolean
    direction: 'incoming' | 'outgoing'
}

export const MessageTopInfo: React.FC<MessageTopInfoProps> = ({
    isScenario,
    displayName,
    showName,
    timestamp,
    gapBeforeTime,
    channel,
    mediaType,
    direction,
    showTime,
}) => {
    const finalGapBeforeTime = gapBeforeTime || (showTime && showName)

    const ChannelIcon = getChannelImageIcon(channel, {omit: ['web'], default: undefined})

    return (
        <p className={tcn(styles.messageTopInfo, styles[direction])}>
            <span data-testid="party-display-name">
                {showName && displayName}
            </span>
            <span className={finalGapBeforeTime ? styles.messageTopInfoGap : ''} />
            {showTime && (
                <span className={tcn(styles.timeChannel, styles[direction])}>
                    {!isScenario && mediaType !== 'EMAIL' && ChannelIcon && (
                        <ChannelIcon
                            data-testid="channel"
                            data-value={channel}
                            className={styles.channelIcon}
                            title={channel}
                        />
                    )}
                    <span data-testid="message-time">
                        {formatDate(timestamp, 'h:mm A')}
                    </span>
                </span>
            )}
        </p>
    )
}
