import React from 'react'

import {bpLogger} from '@bpinc/ad-persistent-logger'
import {useNavigationState} from '@bpinc/lib-communicator-navigation-state'

import {DirectoryEntry} from './Internal/DirectoryEntry'
import {User} from './Internal/User'
import {External} from './External'


export const ContactDetails: React.FC = () => {
    const {activePage} = useNavigationState()
    const {contactId, userId, entryId} = activePage.params ?? {}

    if (contactId) {
        return <External contactId={contactId} />
    }
    if (entryId) {
        return <DirectoryEntry entryId={entryId} />
    }
    if (userId) {
        return <User userId={userId} />
    }

    bpLogger.error('You are trying to open a contact details page without providing a proper ID.')
    return null
}
