import type {SyntheticEvent} from 'react'
import React from 'react'

import {useContactsState} from '@bpinc/ad-contacts-state-context'
import {useGetAvatarUrl} from '@bpinc/ad-contacts-state-hooks'
import {useFormatting} from '@bpinc/ad-formatting-context'
import {AvatarLegacy} from '@bpinc/lib-avatar-ui'
import type {SmInputProps} from '@bpinc/lib-external-ui-elements'
import {SmForm, SmSegment} from '@bpinc/lib-external-ui-elements'
import type {DropdownProps} from '@bpinc/lib-ui-elements'
import {Loader} from '@bpinc/lib-ui-elements'
import type {ContactRecord} from '@bpinc/state-interaction-ui'

import {OTHER_PHONE_OPTION_VALUE} from '../../hooks/useContactPhoneOptions'
import {ContactPhonesDropdown} from '../FormControls/ContactPhonesDropdown'
import {OtherPhoneFormGroup} from './OtherPhoneFormGroup'


export interface ExistingContactRecordProps {
    contact: ContactRecord
    contactHandlers: {
        handleContactNameChange: (
            evt: SyntheticEvent<Element, Event>,
            {value}: SmInputProps
        ) => void
        handleContactPhoneChange: (
            evt: SyntheticEvent<Element, Event>,
            {value}: SmInputProps
        ) => void
        handleContactPhoneTypeChange: (
            evt: SyntheticEvent<Element, Event>,
            {value}: DropdownProps
        ) => void

        handleContactPhoneIdChange: (
            evt: SyntheticEvent<Element, Event>,
            {value}: DropdownProps
        ) => void
        handleContactCreateContactChange: (
            evt: SyntheticEvent<Element, Event>,
            {value}: DropdownProps
        ) => void
    }
}

export const ExistingContactRecord: React.FC<ExistingContactRecordProps> = ({
    contact,
    contactHandlers,
}) => {
    const contacts = useContactsState('contacts')
    const getAvatarUrl = useGetAvatarUrl()
    const {formatFullName} = useFormatting()
    const contactId = contact?.contactId ?? ''
    const avatarUrl = getAvatarUrl({contactId})
    const {contactData} = contact

    return (
        <SmSegment>
            {contacts[contactId]?.isLoading ? (
                <Loader
                    size="large"
                    color="primary"
                    active
                />
            ) : (
                <>
                    <SmForm.Field className="flex items-center">
                        <AvatarLegacy
                            size={36}
                            firstName={contactData?.firstName}
                            lastName={contactData?.lastName}
                            url={avatarUrl}
                            type="contact"
                        />
                        <p className="ml-2 text-xl">
                            {formatFullName(contactData?.firstName, contactData?.lastName)}
                        </p>
                    </SmForm.Field>
                    <ContactPhonesDropdown
                        contact={contact}
                        contactHandlers={contactHandlers}
                    />
                    {contact.phoneId === OTHER_PHONE_OPTION_VALUE && (
                        <OtherPhoneFormGroup
                            contact={contact}
                            contactHandlers={contactHandlers}
                        />
                    )}
                </>
            )}
        </SmSegment>
    )
}
