import React from 'react'

import {formatChatDate} from '@bpinc/ad-formatting-helpers'
import {useInteractionProp} from '@bpinc/ad-interaction-state-context'
import {SystemMessageUI} from '@bpinc/lib-chat-ui'
import {useInteractionId} from '@bpinc/lib-interaction-context'

import styles from '../styles/InteractionsPage.module.less'


export const InternalScenarioMessages = () => {
    const interactionId = useInteractionId()
    const messages = useInteractionProp(interactionId, activeItem => activeItem?.internalMessageData?.messages)
    return (
        <div className={styles.scenarioMessages} data-testid="interaction-body">
            {messages?.filter(m => m.text)?.map((message, i) => (
                <>
                    <p className="pb-1 pl-4 pt-2 text-xs text-chat-name">{formatChatDate(message.timestamp)}</p>
                    <SystemMessageUI className="pt-0" key={i}>
                        {message.text}
                    </SystemMessageUI>
                </>
            ))}
        </div>
    )
}
