import {useEffect} from 'react'

import {useContactsStateMethods} from '@bpinc/ad-contacts-state-context'
import type {ContactType} from '@bpinc/ad-contacts-state-types'
import {PhoneTypes} from '@bpinc/ad-contacts-state-types'
import {useInteraction} from '@bpinc/ad-interaction-state-context'
import {useInteractionId} from '@bpinc/lib-interaction-context'
import type {ContactRecord, RescheduleForm as RescheduleFormType} from '@bpinc/state-interaction-ui'


interface UseContactRecordInitializerParams {
    contacts: ContactRecord[]
    setRescheduleFormState: (rescheduleForm: Partial<RescheduleFormType>) => void
}

export function useContactRecordInitializer({setRescheduleFormState, contacts}: UseContactRecordInitializerParams) {
    const interactionId = useInteractionId()
    const activeItem = useInteraction(interactionId)
    const {getContact} = useContactsStateMethods()

    useEffect(() => {
        if (!contacts?.length) {
            const contactId = activeItem?.contactId
            if (contactId) {
                getContact({id: contactId}).then(contactData => {
                    if (contactData) {
                        saveWorkItemContact(contactData)
                    }
                }).catch(err => {
                    console.error(err)
                    saveWorkItemContact()
                })
            } else {
                saveWorkItemContact()
            }
        }

        function saveWorkItemContact(contactData?: ContactType) {
            setRescheduleFormState({
                contacts: [
                    {
                        name: activeItem?.displayData.displayName ?? '',
                        phone: activeItem?.displayData.number ?? '',
                        createContact: false,
                        phoneType: PhoneTypes.BUSINESS,
                        contactId: activeItem?.contactId,
                        phoneId: contactData?.phones?.find(number => number.phone === activeItem?.displayData.number)?.id,
                        contactData,
                    },
                ],
            })
        }
    }, [activeItem, contacts?.length, getContact, setRescheduleFormState])


}
