import {useMemo} from 'react'

import {useGetAvatarUrl} from '@bpinc/ad-contacts-state-hooks'
import {getUserStatusIcon, useUserDetailsById} from '@bpinc/ad-user-details-state-context'
import type {LegacyAvatarType} from '@bpinc/lib-avatar-ui'
import type {PropsAvatar} from '@bpinc/lib-contact-list-item-ui'
import type {FavoritesItem} from '@bpinc/state-favorites-types'
import {isFavoritesDirectoryEntryItem, isFavoritesEmiItem, isFavoritesUserItem} from '@bpinc/state-favorites-types'

import {useDirectoryEntryOnlineStatus} from '../../../directory/hooks/useDirectoryEntryOnlineStatus'


function getAvatarType(item: FavoritesItem): LegacyAvatarType | undefined {
    if (!isFavoritesDirectoryEntryItem(item)) {
        return
    }
    if (item.huntGroupId) {
        return 'hunt-group'
    }
    // it can be empty string!
    if (item.serviceId !== undefined) {
        return 'scenario-entry'
    }
    return 'static-entry'
}

export function useAvatar(item: FavoritesItem) {
    const {
        userId,
        emiUserId,
        contactId,
        emiUserType,
        emiPhotoUrl,
        firstName,
        lastName,
        displayName,
    } = item
    const anyUserId = userId || emiUserId

    const details = useUserDetailsById(anyUserId)
    const getAvatarUrl = useGetAvatarUrl()
    const directoryEntryOnlineStatus = useDirectoryEntryOnlineStatus(item.serviceId, item.huntGroupId)

    const avatarType = getAvatarType(item)
    const isAgent =
        isFavoritesUserItem(item) ||
        isFavoritesDirectoryEntryItem(item) ||
        (isFavoritesEmiItem(item) && item.emiUserType === 'msteams')

    return useMemo(() => {
        const avatar: PropsAvatar = {
            firstName,
            lastName,
            fullName: displayName,
            isAgent,
            avatarType,
            url: getAvatarUrl({
                userId: anyUserId,
                contactId,
                userType: emiUserType,
                photoUrl: emiPhotoUrl,
            }),
        }
        if (details?.data) {
            avatar.isOnline = details.data.presenceState === 'available'
            avatar.onlineStatusIcon = getUserStatusIcon(details?.data)
        } else if (directoryEntryOnlineStatus) {
            avatar.onlineStatusIcon = directoryEntryOnlineStatus
        }
        return avatar
    }, [
        avatarType,
        firstName,
        lastName,
        displayName,
        directoryEntryOnlineStatus,
        isAgent,
        getAvatarUrl,
        anyUserId,
        contactId,
        emiUserType,
        emiPhotoUrl,
        details,
    ])
}
