import {constructUserPictureUrl} from '@bpinc/ad-contacts-state-methods'
import type {SessionData} from '@bpinc/ad-session-types'
import {getUserStatusIcon} from '@bpinc/ad-user-details-state-context'
import type {UserDetails} from '@bpinc/ad-user-details-state-types'
import type {PropsAvatar} from '@bpinc/lib-contact-list-item-ui'
import type {GroupChatItem} from '@bpinc/state-group-chat-types'


interface Params {
    chatData?: GroupChatItem
    p2pUser?: UserDetails
    session: SessionData
}

export function getGroupChatAvatar({chatData, p2pUser, session}: Params): PropsAvatar {
    if (!chatData || chatData.members.length > 2) {
        return getMultiuserChatAvatar()
    }
    if (chatData.type === 'TEAM') {
        return getTeamChatAvatar(chatData)
    }
    const avatar = getP2PChatAvatar(chatData, session)

    if (p2pUser) {
        avatar.isOnline = p2pUser.presenceState === 'available'
        avatar.onlineStatusIcon = getUserStatusIcon(p2pUser)
    }
    return avatar
}

/** Makes avatar props for a group chat if there is more then two users*/
function getMultiuserChatAvatar(): PropsAvatar {
    return {
        avatarType: 'conference',
        isAgent: true,
    }
}

/** Makes avatar props for a pear-to-pear group chat */
function getP2PChatAvatar(
    chatData: GroupChatItem,
    session: SessionData,
): PropsAvatar {
    const member = chatData.members.find(m => m.id !== session.userId)
    return {
        avatarType: 'user',
        firstName: member?.firstName,
        isAgent: true,
        lastName: member?.lastName,
        url: member?.id && constructUserPictureUrl(session, member.id),
    }
}

/** Makes avatar props for a team group chat */
function getTeamChatAvatar(chatData: GroupChatItem): PropsAvatar {
    return {
        avatarType: 'team-chat',
        fullName: chatData.title,
        isAgent: false,
    }
}
