import type {SyntheticEvent} from 'react'
import React from 'react'

import type {AppRoutes} from '@bpinc/lib-communicator-navigation-state'
import type {SmMenuProps} from '@bpinc/lib-external-ui-elements'
import {SmMenu} from '@bpinc/lib-external-ui-elements'
import {useMenuKeysHandler} from '@bpinc/lib-ui-elements'

import {useMainMenu} from '../hooks/useMainMenu'
import {MenuItem} from './MenuItem'


interface MainMenuProps {
    menuProps: SmMenuProps
    toggleSettings: (forceOpenedOrEvent?: boolean | SyntheticEvent) => void
    onMenuItemClick: (name: AppRoutes, params?: Record<string, unknown>) => void
    activePageName: AppRoutes
}

export const MainMenu: React.FC<MainMenuProps> = ({
    menuProps,
    toggleSettings,
    onMenuItemClick,
    activePageName,
}) => {
    const mainMenu = useMainMenu({onMenuItemClick, toggleSettings})
    const handlerMenuKeys = useMenuKeysHandler('menu-item', 'menu-container', true)
    return (
        <SmMenu data-element="menu-container" {...menuProps} data-testid="menu" onKeyDown={handlerMenuKeys}>
            {mainMenu.map(({children, ...props}) => (
                <MenuItem
                    {...props}
                    key={props.key}
                    id={props.key}
                    activePage={activePageName}
                >
                    {children}
                </MenuItem>
            ))}
        </SmMenu>
    )
}
