import {useFormatting} from '@bpinc/ad-formatting-context'
import type {Item} from '@bpinc/ad-interaction-state-types'


export function useItemNames(item?: Item): string[] {
    const {
        getNamesFromDisplayName,
        formatFullName,
    } = useFormatting()

    if (!item) return ['', '']

    const {
        displayData: {
            displayName,
            firstName: callFirstName,
            lastName: callLastName,
        },
        externalChatData,
    } = item

    const chatFirstName = externalChatData?.firstName
    const chatLastName = externalChatData?.lastName
    const finalDisplayName =
        displayName ||
        formatFullName(
            chatFirstName || callFirstName,
            chatLastName || callLastName,
        )

    return getNamesFromDisplayName(finalDisplayName)
}
