import React from 'react'

import {useContact} from '@bpinc/ad-contacts-state-context'
import {useGetAvatarUrl} from '@bpinc/ad-contacts-state-hooks'
import {useFormatting} from '@bpinc/ad-formatting-context'
import {formatPhone, getInitials} from '@bpinc/ad-formatting-helpers'
import {useInteraction} from '@bpinc/ad-interaction-state-context'
import {useContactDetailsOpener} from '@bpinc/ad-interaction-state-hooks'
import {useEvent} from '@bpinc/lib-helpers'
import {useInteractionId} from '@bpinc/lib-interaction-context'
import {Avatar} from '@bpinc/lib-jskit-ui'
import {assertDefined} from '@bpinc/lib-types-helpers'
import {useLocalizationState} from '@bpinc/state-localization-context'


export const WorkitemDisplayContent: React.FC = () => {
    const t = useLocalizationState('t')
    const {formatFullName} = useFormatting()

    const interactionId = useInteractionId()
    const activeItem = useInteraction(interactionId)
    const getAvatarUrl = useGetAvatarUrl()
    const openContactDetails = useContactDetailsOpener()

    assertDefined(activeItem)
    assertDefined(activeItem.workitemData)

    const {
        firstName: listFirstName,
        lastName: listLastName,
    } = activeItem.workitemData.contactInfo
    const listFullName = formatFullName(listFirstName, listLastName)
    const listDisplayName = listFullName || t.GLOBAL.anonymous()

    const contactId = activeItem.contactId
    const {data: contactData, isLoading: contactLoading} = useContact(contactId) ?? {}
    const {
        firstName: contactFirstName,
        lastName: contactLastName,
        primaryPhone,
        primaryEmail,
    } = contactData ?? {}

    const contactFullName = formatFullName(contactFirstName, contactLastName)
    const contactPrimaryPhoneNumber = formatPhone(primaryPhone?.phone)
    const contactPrimaryEmailAddress = primaryEmail?.emailAddress
    const contactDisplayName =
        contactFullName ||
        contactPrimaryPhoneNumber ||
        contactPrimaryEmailAddress ||
        t.GLOBAL.anonymous()

    const avatarUrl = getAvatarUrl({contactId})
    const avatarInitials = getInitials(contactFirstName, contactLastName)

    const handleAvatarClick = useEvent(() => {
        openContactDetails({contactId})
    })

    return (
        <>
            <div className="my-2 ml-4 mr-6 truncate body text-primary-on-primary">
                <span data-testid="list-record-name">
                    {listDisplayName}
                </span>
            </div>
            {contactId && !contactLoading && (
                <div className="mx-4 my-2 flex items-center gap-2">
                    <Avatar
                        type="contact"
                        src={avatarUrl}
                        fallback={avatarInitials}
                        tooltip={t.CALL_DETAILS_FORM.contact_details_link_tooltip()}
                        onClick={handleAvatarClick}
                    />
                    <div className="truncate font-semibold body text-primary-on-primary">
                        <span data-testid="name">
                            {contactDisplayName}
                        </span>
                    </div>
                </div>
            )}
        </>
    )
}
