import React from 'react'
import {useContext} from 'use-context-selector'

import type {FormDefinitionFieldEmail, FormDefinitionPageItem} from '@bpinc/ad-request-urls'
import {getPageItemCSSProperties} from '@bpinc/lib-form-builder'

import {FormBuilderContext} from '../../FormBuilder'
import {GeneralTextFormField} from '../common/GeneralTextFormField'
import {ViewItem} from '../common/ViewItem'


export const EmailField: React.FC<{
    pageItem: FormDefinitionPageItem
    field: FormDefinitionFieldEmail
}> = ({
    pageItem,
    field,
}) => {
    const {
        form,
        view,
    } = useContext(FormBuilderContext)

    if (view) {
        const viewValue = form.getValues()[field.name || '']
        const viewStyle = getPageItemCSSProperties(pageItem.style)

        if (!viewValue) {
            return null
        }

        return (
            <ViewItem
                viewStyle={viewStyle}
                label={field.label || ''}
                value={(
                    <a
                        data-value={viewValue}
                        tabIndex={0}
                    >
                        {viewValue}
                    </a>
                )}
            />
        )
    }

    return (
        <GeneralTextFormField
            placeholder={field.emptyText || ''}
            label={field.label}
            required={field.required}
            key={field.key}
            type="email"
            name={field.name || ''}
            control={form.control}
            pageItem={pageItem}
            minRows={field.initialLinesNumber}
            multiline={field.multiline}
        />
    )
}
