import type {FC} from 'react'
import React, {useMemo} from 'react'
import {useController, useForm} from 'react-hook-form'
import {DevTool} from '@hookform/devtools'

import type {ActiveDraft} from '@bpinc/ad-interaction-state-types'
import {useEmailAttachmentsUIState} from '@bpinc/lib-attachments-state'
import {useBoolean} from '@bpinc/lib-helpers'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {AttachmentsList, Button} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {useGetEmailQuery} from '../../queries/useGetEmailQuery'
import {EmailControls} from './EmailControls'
import {EmailSelectionInput} from './EmailSelectionInput'
import {emailAddressToEmailSearchItem} from './helpers'
import type {EmailFormFields} from './types'
import {useAttachmentsStateSync} from './useAttachmentsStateSync'
import {useEmailTemplateFetcher} from './useEmailTemplateFetcher'
import {usePasteFromKB} from './usePasteFromKB'
import {useSaveDraftInterval} from './useSaveDraftInterval'
import {WysiwygEditor} from './WysiwygEditor'


const inputSectionStyles = 'flex items-center border-t border-main-border pt-2'

export interface EmailFormProps extends ActiveDraft {
    interactionId: string
}


export const EmailForm: FC<EmailFormProps> = props => {
    const {emailId, referencedEmailId} = props

    const {data: email, isSuccess: emailLoaded} = useGetEmailQuery(emailId)

    const form = useForm<EmailFormFields>({
        values: {
            to: email?.toAddresses.map(emailAddressToEmailSearchItem) ?? [],
            cc: email?.ccAddresses.map(emailAddressToEmailSearchItem) ?? [],
            bcc: email?.bccAddresses.map(emailAddressToEmailSearchItem) ?? [],
            subject: email?.subject ?? '',
            html: email?.content ?? '',
            headerFooterUserId: email?.headerFooterUserId,
            //Attachments are initialized in useAttachmentsStateSync
            attachments: [],
        },
    })

    useEmailTemplateFetcher({draft: props, form})

    useSaveDraftInterval({emailId, form})

    usePasteFromKB(props.interactionId, form)

    const ccEnabled = useBoolean()
    const bccEnabled = useBoolean()
    const {
        t: {EMAIL: i18n},
    } = useLocalizationState()

    const {field} = useController({
        control: form.control,
        name: 'subject',
    })
    const {getAttachmentsList, removeAttachment} = useEmailAttachmentsUIState(['getAttachmentsList', 'removeAttachment'])

    const attachmentsList = useMemo(() => getAttachmentsList(emailId), [emailId, getAttachmentsList])
    useAttachmentsStateSync({email, attachmentsList, control: form.control, emailLoaded})

    return (
        <div className="p-2">
            <div
                className="rounded border border-main-border p-2"
            >
                <div className="group min-h-10">
                    <section className="flex justify-between">
                        <EmailSelectionInput
                            name="to"
                            control={form.control}
                            label={i18n.email_address_to_prefix()}
                        />
                        <div className="hidden shrink-0 group-focus-within:block">
                            <Button
                                className={tcn('block text-sm', {
                                    'text-primary-legacy': ccEnabled.value,
                                })}
                                onClick={ccEnabled.toggle}
                            >
                                {i18n.email_address_cc_prefix()}
                            </Button>
                            <Button
                                className={tcn('block text-sm', {
                                    'text-primary-legacy': bccEnabled.value,
                                })}
                                onClick={bccEnabled.toggle}
                            >
                                {i18n.email_address_bcc_prefix()}
                            </Button>
                        </div>
                    </section>
                    {ccEnabled.value && (
                        <EmailSelectionInput
                            name="cc"
                            control={form.control}
                            label={i18n.email_address_cc_prefix()}
                        />
                    )}
                    {bccEnabled.value && (
                        <EmailSelectionInput
                            name="bcc"
                            control={form.control}
                            label={i18n.email_address_bcc_prefix()}
                        />
                    )}
                </div>
                <section className={tcn(inputSectionStyles, 'h-10 pb-2')}>
                    <label>{i18n.email_subject()}</label>
                    <input {...field} className="w-full pl-2" />
                </section>
                <section
                    className={tcn(
                        inputSectionStyles,
                        'min-h-56 flex-col pb-2',
                        {'min-h-80': attachmentsList.length},
                    )}
                >
                    <WysiwygEditor control={form.control} emailId={emailId} referencedEmailId={referencedEmailId} />
                    {!!attachmentsList.length && (
                        <AttachmentsList
                            list={attachmentsList}
                            className="mb-2"
                            removeAttachment={removeAttachment}
                            attachmentsListGroupId={emailId}
                        />
                    )}
                    <EmailControls {...props} form={form} />
                </section>
            </div>
            {process.env.NODE_ENV === 'development' && <DevTool control={form.control} />}
        </div>
    )
}
