import type {ReactElement} from 'react'
import React from 'react'

import type {BaseConnectors} from '@bpinc/ad-connected-state-types'

import type {BaseConnectorsConsumerComponent} from './AuthorizationModule'


type Props = {
    baseConnectors: BaseConnectors
    children: ReactElement
    BaseConnectorsConsumer?: BaseConnectorsConsumerComponent
}

export const BaseConnectorsConsumers: React.FC<Props> = ({
    baseConnectors,
    children,
    BaseConnectorsConsumer,
}) => {

    if (BaseConnectorsConsumer) {
        return (
            <BaseConnectorsConsumer baseConnectors={baseConnectors}>
                {children}
            </BaseConnectorsConsumer>
        )
    }
    return children
}
