import React from 'react'
import {Button} from '@servicepattern/ui'

import {useSessionState} from '@bpinc/ad-session-context'
import type {Toggle} from '@bpinc/lib-helpers'
import {IconDelete24, IconEdit24} from '@bpinc/lib-jskit-ui'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {useViewModeHandlers} from '../../../../hooks/useViewModeHandlers'
import {ExternalViewMoreButton} from './ExternalViewMoreButton'


export const ExternalViewControls: React.FC<{
    toggleEditMode: Toggle
    contactId: string
}> = ({
    toggleEditMode,
    contactId,
}) => {
    const {privileges} = useSessionState()
    const {t} = useLocalizationState()
    const {handleDelete, handleCopyLink, handleMerge} = useViewModeHandlers(contactId)

    return (
        <div className="flex items-center gap-2 px-6 pb-2 pt-4">
            <h2>{t.CONTACT.external_contact()}</h2>

            {!!privileges.EDIT_CONTACTS && (
                <Button
                    onClick={toggleEditMode}
                    variant="ghost"
                    tooltip={t.UNDEFINED.button_edit_contact_title()}
                    data-testid="edit-contact-button"
                    icon={<IconEdit24 />}
                />
            )}

            {!!privileges.DELETE_CONTACTS && (
                <Button
                    onClick={handleDelete}
                    variant="ghost"
                    tooltip={t.UNDEFINED.button_delete_contact_title()}
                    data-testid="delete-contact-button"
                    icon={<IconDelete24 />}
                >
                    {t.UNDEFINED.button_delete_contact_text()}
                </Button>
            )}

            <ExternalViewMoreButton
                handleCopyLink={handleCopyLink}
                handleMerge={handleMerge}
            />
        </div>
    )
}
