import React from 'react'
import classNames from 'classnames'
import {useContext} from 'use-context-selector'

import {formatDate} from '@bpinc/ad-formatting-helpers'
import type {FormDefinitionFieldDatetime, FormDefinitionPageItem} from '@bpinc/ad-request-urls'
import {getPageItemCSSProperties} from '@bpinc/lib-form-builder'

import {FormBuilderContext} from '../../FormBuilder'
import {ViewItem} from '../common/ViewItem'
import {DatePicker} from './DatePicker'


export const DateTimeField: React.FC<{
    pageItem: FormDefinitionPageItem
    field: FormDefinitionFieldDatetime
}> = ({
    pageItem,
    field,
}) => {
    const {
        form,
        view,
    } = useContext(FormBuilderContext)

    if (view) {
        const fieldValue = form.getValues()[field.name || '']
        const viewStyle = getPageItemCSSProperties(pageItem.style)

        if (field.hideEmpty && !fieldValue) {
            return null
        }

        const viewValue = fieldValue ? formatDate(fieldValue) : ''

        return (
            <ViewItem
                viewStyle={viewStyle}
                label={field.label || ''}
                value={viewValue}
            />
        )
    }

    const editStyle = getPageItemCSSProperties(pageItem.editStyle)

    return (
        <DatePicker
            placeholder={field.emptyText || ''}
            label={field.label || ''}
            required={field.required}
            control={form.control}
            style={editStyle}
            name={field.name || ''}
            className={classNames(
                `w-full`,
                `[&>.field]:w-full [&>.field>*]:w-full`,
                `[&>.field>*>input]:p-[5px] [&>.field>*>input]:flex-auto`,
                `[&>.field>*>input]:border [&>.field>*>input]:border-default [&>.field>*>input]:rounded-none [&>.field>*>input]:leading-6`,
            )}
            labelClassName={'!leading-3 caption-s text-primary-on-primary cursor-pointer'}
            displayFormat={field.displayFormat ?? undefined}
        />
    )
}
