import type {CSSProperties} from 'react'
import React, {useCallback, useMemo} from 'react'
import type {FieldValues, UseFieldArrayRemove} from 'react-hook-form'

import type {ServerPhoneType} from '@bpinc/ad-contacts-state-types'
import {PhoneTypes} from '@bpinc/ad-contacts-state-types'
import {useFormatting} from '@bpinc/ad-formatting-context'
import type {FieldArrayControlProps, FieldArrayPathByValue, FieldSubPathsByValue} from '@bpinc/lib-forms-base'
import {usePhoneValidationRules} from '@bpinc/lib-forms-base'
import {IconAddPhone} from '@bpinc/lib-icons'
import type {OptionalBy} from '@bpinc/lib-types-helpers'

import {FieldArray} from './FieldArray'
import {SelectAndTextFieldRow} from './SelectAndTextFieldRow'


type PhoneFieldType = OptionalBy<ServerPhoneType, 'id'>

const INITIAL_VALUES: PhoneFieldType = {type: PhoneTypes.BUSINESS, phone: ''}

type PhonesFieldArrayProp = {
    className?: string
    typeClassName?: string
    layoutClassName?: string
    style?: CSSProperties
}

export const PhonesFieldArray = <
    TValues extends FieldValues,
    TFieldName extends FieldArrayPathByValue<TValues, PhoneFieldType>,
>({
    name,
    style,
    control,
    className,
    typeClassName,
    layoutClassName,
}: FieldArrayControlProps<PhoneFieldType, TValues, TFieldName> & PhonesFieldArrayProp) => {
    const {formatPhoneType} = useFormatting()
    const phoneValidationRules = usePhoneValidationRules()

    const optionsPhoneTypes = useMemo(() => {
        return Object.keys(PhoneTypes).map(type => ({
            value: type,
            label: formatPhoneType(type),
        }))
    }, [formatPhoneType])

    const renderComponents = useCallback(
        function renderComponentsCallback(
            index: number,
            remove: UseFieldArrayRemove,
        ) {
            return (
                <SelectAndTextFieldRow
                    idFieldName={`${name}.${index}.id` as FieldSubPathsByValue<TValues, ServerPhoneType>}
                    selectFieldName={`${name}.${index}.type` as FieldSubPathsByValue<TValues, ServerPhoneType> }
                    textFieldName={`${name}.${index}.phone` as FieldSubPathsByValue<TValues, ServerPhoneType>}
                    control={control}
                    typeClassName={typeClassName}
                    index={index}
                    options={optionsPhoneTypes}
                    textFiledRules={phoneValidationRules}
                    inputType="tel"
                    remove={remove}
                    layoutClassName={layoutClassName}
                    style={style}
                />
            )
        },
        [typeClassName, name, control, optionsPhoneTypes, phoneValidationRules, layoutClassName, style],
    )

    return (
        <FieldArray
            layoutClassName={layoutClassName}
            className={className}
            control={control}
            name={name}
            initialValues={INITIAL_VALUES}
            renderComponents={renderComponents}
            AddButtonIcon={IconAddPhone}
        />
    )
}
