import {useMemo} from 'react'

import {isPotentialInteraction} from '@bpinc/ad-interaction-state-methods'
import type {Item} from '@bpinc/ad-interaction-state-types'


export function useShowTimerChecker(item?: Item) {

    const {displayData} = item ?? {}
    const {stateStartTime, failed} = displayData ?? {}

    return useMemo(
        () =>
            stateStartTime !== undefined &&
            !failed &&
            !isPotentialInteraction(item),
        [failed, item, stateStartTime],
    )

}
