import type {FC} from 'react'
import React, {useCallback} from 'react'

import type {GlobalModalName} from '@bpinc/lib-sliding-modal-context'
import {useCloseGlobalSlidingModal, useSlidingModalState} from '@bpinc/lib-sliding-modal-context'

import {useGlobalModalStyles} from './hooks/useGlobalModalStyles'
import {CalendarEventSlidingModal} from './modals/CalendarEventSlidingModal'
import {MergeContactsSlidingModal} from './modals/MergeContacts/MergeContactsSlidingModal'
import {RecentConferenceSlidingModal} from './modals/RecentConference'
import {VoiceTranscriptModal} from './modals/VoiceTranscriptModal'


type GlobalSlidingModalsListItemProps = {
    name: GlobalModalName
}

export const GlobalSlidingModalsListItem: FC<GlobalSlidingModalsListItemProps> = ({name}) => {
    const closeGlobalSlidingModal = useCloseGlobalSlidingModal()
    const data = useSlidingModalState('globalModalsData')

    const {show, topOffset} = data[name]
    const style = useGlobalModalStyles(show, topOffset)

    const closeModal = useCallback(
        function closeModalCallback() {
            closeGlobalSlidingModal(name)
        },
        [name, closeGlobalSlidingModal],
    )

    switch (name) {
        case 'recentConference': {
            return <RecentConferenceSlidingModal closeModal={closeModal} style={style} {...data[name]} />
        }
        case 'mergeContacts': {
            return <MergeContactsSlidingModal closeModal={closeModal} style={style} {...data[name]} />
        }
        case 'calendarEvent': {
            return <CalendarEventSlidingModal closeModal={closeModal} style={style} {...data[name]} />
        }
        case 'voiceTranscript': {
            return <VoiceTranscriptModal closeModal={closeModal} style={style} {...data[name]} />
        }
        default:
            return null
    }
}
