import React, {type FC, useCallback} from 'react'

import {ButtonIcon, IconChatDock} from '@bpinc/lib-jskit-ui'
import type {ButtonProps} from '@bpinc/lib-ui-elements'
import {useInteractionPopupItemIdSelector, useInteractionUIItemStateSetter} from '@bpinc/state-interaction-ui'
import {useLocalizationState} from '@bpinc/state-localization-context'


interface ChatWindowButtonProps extends ButtonProps {
    itemId: string
}

export const InternalChatWindowButton: FC<ChatWindowButtonProps> = ({itemId, ...props}) => {
    const {ACTIVITY_HISTORY: i18n} = useLocalizationState('t')
    const setState = useInteractionUIItemStateSetter()
    const popupItemId = useInteractionPopupItemIdSelector()

    const onClick = useCallback(
        function onClickCallback() {
            setState(itemId, s => ({...s, chatView: 'popup'}))
            if (popupItemId) {
                setState(popupItemId, s => ({...s, chatView: 'embedded'}))
            }
        }, [popupItemId, itemId, setState],
    )
    return (
        <ButtonIcon
            tooltip={i18n.button_open_popup_tooltip()}
            onClick={onClick}
            {...props}
        >
            <IconChatDock className="size-4" />
        </ButtonIcon>
    )
}
