import type {FC} from 'react'
import React, {useCallback, useEffect, useMemo} from 'react'
import {useAtom} from 'jotai'
import {sortBy} from 'lodash-es'

import {useInteractionProp} from '@bpinc/ad-interaction-state-context'
import {getInteractionStepId, getServiceId} from '@bpinc/ad-interaction-state-methods'
import {
    usePrerecordedMessagesState,
    usePrerecordedMessagesStateMethods,
} from '@bpinc/ad-prerecorded-messages-state-context'
import type {Message} from '@bpinc/ad-prerecorded-messages-state-types'
import {useBoolean, useEscapeManager} from '@bpinc/lib-helpers'
import {useInteractionId} from '@bpinc/lib-interaction-context'
import {ButtonDropdown, CircleButton, IconPlay16, IconStop16} from '@bpinc/lib-jskit-ui'
import {assertDefined} from '@bpinc/lib-types-helpers'
import {useLocalizationState} from '@bpinc/state-localization-context'
import {useModalComponentOpener} from '@bpinc/state-modals-context'

import {selectedMessageStateAtom} from './atoms'
import {PrerecordedMessagesModal} from './PrerecordedMessagesModal'


const SHOW_MESSAGES_COUNT = 4

function sortMessagesByOrder(messages: Message[], order: string[]): Message[] {
    const orderMap = new Map(order.map((id, index) => [id, index]))
    return sortBy(messages, message => orderMap.get(message.id))
}

export const PrerecordedMessages: FC = () => {
    const {t} = useLocalizationState()
    const interactionId = useInteractionId()
    const interactionStepId = useInteractionProp(
        interactionId,
        getInteractionStepId,
    )
    const serviceId = useInteractionProp(interactionId, getServiceId)

    const {list, serviceMessages, isLoading, playingPrompts} =
        usePrerecordedMessagesState()
    const {playPrompt, stopPlayingPrompt, getServiceMessages} =
        usePrerecordedMessagesStateMethods()

    const [{selectedMessage, messagesOrder}, dispatch] = useAtom(
        selectedMessageStateAtom,
    )

    const isPlayingNow = useCallback(
        (prompt: Message) =>
            prompt.id === playingPrompts[interactionStepId]?.promptId,
        [interactionStepId, playingPrompts],
    )

    const handlePlayButtonClick = useCallback(
        function playCallback() {
            assertDefined(selectedMessage?.id)
            assertDefined(interactionId)

            if (isPlayingNow(selectedMessage)) {
                stopPlayingPrompt(interactionStepId)
            } else {
                playPrompt({
                    itemId: interactionId,
                    interactionStepId,
                    promptId: selectedMessage?.id,
                })
            }
        },
        [
            interactionId,
            interactionStepId,
            isPlayingNow,
            playPrompt,
            selectedMessage,
            stopPlayingPrompt,
        ],
    )

    const finalList = useMemo(
        () => [...list, ...((serviceId && serviceMessages[serviceId]) || [])],
        [list, serviceId, serviceMessages],
    )

    useEffect(
        function selectFirstMessage() {
            if (!isLoading && finalList.length && !selectedMessage) {
                dispatch({
                    type: 'revalidateMessages',
                    payload: {existingMessages: finalList},
                })
            }
        },
        [dispatch, finalList, isLoading, selectedMessage],
    )

    useEffect(
        function initializeServiceMessages() {
            if (!serviceId) {
                return
            }
            getServiceMessages(serviceId)
        },
        [getServiceMessages, serviceId],
    )

    const dropdownOpened = useBoolean()

    useEscapeManager(dropdownOpened.setFalse, {
        enabled: dropdownOpened.value,
    })

    const openPrerecordedMessagesModal = useModalComponentOpener(
        PrerecordedMessagesModal,
    )

    if (!selectedMessage) {
        return null
    }

    const options = sortMessagesByOrder(finalList, messagesOrder).slice(
        0,
        SHOW_MESSAGES_COUNT,
    )

    return (
        <div className="flex items-center gap-2 pb-1 pt-2">
            <CircleButton
                disabled={!selectedMessage.id}
                onClick={handlePlayButtonClick}
            >
                {isPlayingNow(selectedMessage) ? (
                    <IconStop16 className="size-4.5" />
                ) : (
                    <IconPlay16 className="size-4.5" />
                )}
            </CircleButton>
            <ButtonDropdown
                open={dropdownOpened.value}
                onOpenChange={dropdownOpened.toggle}
                align="start"
                contentClassName="min-w-[232px]"
                options={
                    <>
                        <ButtonDropdown.Label className="font-semibold caption-s text-neutral-on-secondary">
                            {t.VOICE_CALL.prerecorded_messages_recents()}
                        </ButtonDropdown.Label>
                        {options.map(message => {
                            const isSelected =
                                selectedMessage.id === message.id
                            return (
                                <ButtonDropdown.Item
                                    data-selected={isSelected}
                                    aria-selected={isSelected}
                                    key={message.id}
                                    onClick={() =>
                                        dispatch({
                                            type: 'selectMessage',
                                            payload: {
                                                selectedMessage: message,
                                            },
                                        })
                                    }
                                >
                                    {message.name}
                                </ButtonDropdown.Item>
                            )
                        })}
                        {finalList.length > options.length && (
                            <ButtonDropdown.Item
                                className="border-t py-4 border-default"
                                onClick={() => openPrerecordedMessagesModal()}
                            >
                                {t.VOICE_CALL.prerecorded_messages_more_messages()}
                            </ButtonDropdown.Item>
                        )}
                    </>
                }
                button={{
                    className: 'h-7 px-2.5',
                    tooltip: t.VOICE_CALL.prerecorded_messages(),
                    variant: 'ghost',
                }}
                chevronClassName="-ml-1"
            >
                {selectedMessage.name}
            </ButtonDropdown>
        </div>
    )
}
