import React from 'react'
import {tw} from '@servicepattern/ui'
import {tv} from 'tailwind-variants'

import {formatPhone} from '@bpinc/ad-formatting-helpers'
import {
    isRecentCall,
    isRecentCallConference,
    isRecentEmail,
    isRecentExtChatConference,
    isRecentExternalChat,
    isRecentGroupChat,
    isRecentInternalChat,
    isRecentWorkitem,
} from '@bpinc/ad-interaction-state-methods'
import {cutMilliseconds, formatDurationShort} from '@bpinc/lib-helpers'
import {formatHTMLAsOneLineString} from '@bpinc/lib-html-utils'
import {
    IconConferenceCall16,
    IconConferenceChat16,
    IconEmail16,
    IconInternalChat16,
    IconOutboundCall16,
    IconOutboundPreview16,
    IconPhone16Filled,
} from '@bpinc/lib-jskit-ui'
import {assertDefined} from '@bpinc/lib-types-helpers'
import {getChannelImageIcon} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'
import {useConfPartiesToString, useGroupChatMembersToString} from '@bpinc/state-recent-helpers'
import type {RecentItem} from '@bpinc/state-recent-types'


type Props = {
    item: RecentItem
}

const rootStyles = 'flex items-center overflow-hidden'
const iconStyles = tv({
    base: tw`shrink-0`,
    variants: {
        isMissed: {
            true: tw`icon-error`,
            false: tw`icon-neutral`,
        },
    },
    defaultVariants: {
        isMissed: false,
    },
})
const textStyles = tv({
    base: tw`ml-1 truncate caption-s`,
    variants: {
        isMissed: {
            true: tw`text-on-error`,
            false: tw`text-neutral-on-secondary`,
        },
    },
    defaultVariants: {
        isMissed: false,
    },
})

export const RecentItemBody: React.FC<Props> = ({item}) => {

    const t = useLocalizationState('t')
    const partiesToString = useConfPartiesToString()
    const groupChatMembersToString = useGroupChatMembersToString()

    const durationBlock = !!Number(item.duration) && (
        <span data-testid="duration">
            {`(${formatDurationShort(cutMilliseconds(item.duration))})`}
        </span>
    )

    if (isRecentCallConference(item)) {
        return (
            <div className={rootStyles}>
                <IconConferenceCall16 className={iconStyles()} />
                <div className={textStyles()}>
                    {durationBlock}
                    {' '}
                    <span data-testid="parties">
                        {partiesToString(item.parties)}
                    </span>
                </div>
            </div>
        )

    } else if (isRecentExtChatConference(item)) {
        return (
            <div className={rootStyles}>
                <IconConferenceChat16 className={iconStyles()} />
                <div className={textStyles()}>
                    {durationBlock}
                    {' '}
                    <span data-testid="parties">
                        {partiesToString(item.parties)}
                    </span>
                </div>
            </div>
        )

    } else if (isRecentGroupChat(item)) {
        assertDefined(item.groupChatData)
        return (
            <div className={rootStyles}>
                <IconConferenceChat16 className={iconStyles()} />
                <div className={textStyles()}>
                    {durationBlock}
                    {' '}
                    <span data-testid="parties">
                        {groupChatMembersToString(item.groupChatData.members)}
                    </span>
                </div>
            </div>
        )

    } else if (isRecentInternalChat(item)) {
        return (
            <div className={rootStyles}>
                <IconInternalChat16 className={iconStyles()} />
                <div className={textStyles()}>
                    {durationBlock}
                    {' '}
                    <span data-testid="last-message-author">
                        {item.lastMessageAuthor}
                    </span>
                    <span>{': '}</span>
                    <span data-testid="last-message">
                        {item.lastMessage}
                    </span>
                </div>
            </div>
        )

    } else if (isRecentExternalChat(item)) {
        const isMissed = !item.userId && item.direction === 'INBOUND' && item.duration === '0'
        const ChannelIcon = getChannelImageIcon(item.channel)

        const text = isMissed
            ? t.ACTIVITY_HISTORY.call_message_missed()
            : item.direction === 'INBOUND'
                ? t.ACTIVITY_HISTORY.call_message_incoming_text()
                : t.ACTIVITY_HISTORY.call_message_outgoing_text()

        const getLastMessage = () => {
            if (item.lastMessage === 'external_chat_party_joined_new') {
                return t.EXTERNAL_CHAT.external_chat_party_joined_new()
            }
            if (item.lastMessage === 'external_chat_party_left_new') {
                return t.EXTERNAL_CHAT.external_chat_party_left_new()
            }
            return formatHTMLAsOneLineString(item.lastMessage)
        }
        const lastMessage = getLastMessage()

        return (
            <div className={rootStyles}>
                {ChannelIcon && <ChannelIcon className={iconStyles({isMissed})} />}
                <div className={textStyles({isMissed})}>
                    <span>{text}</span>
                    {' '}
                    {durationBlock}
                    {!isMissed && lastMessage && (
                        <>
                            {': '}
                            <span data-testid="last-message">
                                {lastMessage}
                            </span>
                        </>
                    )}
                </div>
            </div>
        )

    } else if (isRecentCall(item)) {
        const isMissed = item.direction === 'INBOUND' && item.duration === '0'
        const IconComponent = item.direction === 'INBOUND' ? IconPhone16Filled : IconOutboundCall16

        const text = isMissed
            ? t.ACTIVITY_HISTORY.call_message_missed()
            : item.direction === 'INBOUND'
                ? t.ACTIVITY_HISTORY.call_message_incoming_text()
                : t.ACTIVITY_HISTORY.call_message_outgoing_text()

        return (
            <div className={rootStyles}>
                <IconComponent className={iconStyles({isMissed})} />
                <div className={textStyles({isMissed})}>
                    <span>{text}</span>
                    {' '}
                    {durationBlock}
                </div>
            </div>
        )

    } else if (isRecentEmail(item)) {
        if (item.caseTitle) {
            return (
                <div className={rootStyles}>
                    <IconEmail16 className={iconStyles()} />
                    <div className={textStyles()}>
                        <span data-testid="case-title">
                            {item.caseTitle}
                        </span>
                    </div>
                </div>
            )
        }

    } else if (isRecentWorkitem(item)) {
        const formattedPhone = item.number && formatPhone(item.number)

        return (
            <div className={rootStyles}>
                <IconOutboundPreview16 className={iconStyles()} />
                <div className={textStyles()}>
                    <span data-testid="phone">
                        {formattedPhone}
                    </span>
                </div>
            </div>
        )
    }

    return null
}
