import type {BaseConnectors} from '@bpinc/ad-connected-state-types'
import {ApiError} from '@bpinc/ad-integration-api-state-types'
import {getInitialSessionState} from '@bpinc/ad-session-types'
import type {LoginData} from '@bpinc/adapters-public-api-types'

import {convertServerError} from './converters'
import {createApiRequestTracker} from './pending-requests'


const API_REQUEST_TIMEOUT = 5000

export const connectIntegrationApiBase = ({integrationApiConnector, sessionConnector}: BaseConnectors) => {

    const apiRequestTracker = createApiRequestTracker(API_REQUEST_TIMEOUT)
    let sessionState = getInitialSessionState()

    const sessionStateSubscription = sessionConnector.dispenser.subscribe({
        onInitialState: newState => {
            if (newState.isLoggedIn) {
                apiRequestTracker.resolveAllPendingRequests('onLogin', undefined)
                integrationApiConnector.dispatch.login({
                    isLoggedIn: true,
                    agentId: newState.userId!,
                    username: newState.loginId!,
                    sessionId: newState.sessionId!,
                })
            }
            const prevState = sessionState
            if (!prevState.loginError && newState.loginError) {
                apiRequestTracker.rejectAllPendingRequests('onLogin', newState.loginError)
            }
            sessionState = {...newState}
        },
        onChange: stateUpdate => {
            const prevState = sessionState
            sessionState = {...sessionState, ...stateUpdate}
            if (!prevState.loginError && sessionState.loginError) {
                apiRequestTracker.rejectAllPendingRequests('onLogin', sessionState.loginError)
            }
        },
        onLogin: session => {
            apiRequestTracker.resolveAllPendingRequests('onLogin', undefined)
            integrationApiConnector.dispatch.login({
                isLoggedIn: true,
                agentId: session.userId!,
                username: session.loginId!,
                sessionId: session.sessionId!,
            })
        },
        onServerError: (source, code, message) => {
            integrationApiConnector.dispatch.serverError(
                convertServerError(source, code, message),
            )
        },
    })

    const onLogin = ({username, password, tenant}: LoginData, force = false) => {
        if (sessionState.isLoggedIn) {
            throw new ApiError('not_suitable_state', 'You are already in logged in state')
        }
        if (sessionState.isAuthorizing) {
            throw new ApiError('not_suitable_state', 'Login procedure in process now')
        }
        if (force) {
            sessionConnector.dispatch.forceLogin()
        } else {
            sessionConnector.dispatch.login({
                loginId: username,
                password,
                domain: tenant || location.host,
            })
        }
        return apiRequestTracker.newPendingRequest(
            'onLogin',
            [{username, password, tenant}, force],
        )
    }

    const onGetLoginState = () => {
        return Promise.resolve({
            isLoggedIn: sessionState.isLoggedIn || false,
            agentId: sessionState.userId,
            username: sessionState.loginId,
            sessionId: sessionState.sessionId,
        })
    }

    const throwNotLoggedIn = (): never => {
        throw new ApiError('not_logged_in', 'You are not logged in')
    }

    const integrationApiSubscription = integrationApiConnector.dispenser.subscribe({
        onLogin,
        onGetLoginState,

        onLogout: throwNotLoggedIn,
        onAcceptInteraction: throwNotLoggedIn,
        onAddInteractionAssociatedObject: throwNotLoggedIn,
        onAddNote: throwNotLoggedIn,
        onBlindTransfer: throwNotLoggedIn,
        onCompleteInteraction: throwNotLoggedIn,
        onConsultCall: throwNotLoggedIn,
        onDestroyCallConference: throwNotLoggedIn,
        onGetAgentNotReadyReasons: throwNotLoggedIn,
        onGetAgentState: throwNotLoggedIn,
        onGetConfig: throwNotLoggedIn,
        onGetDIDNumber: throwNotLoggedIn,
        onGetDIDNumbersList: throwNotLoggedIn,
        onGetDispositionsList: throwNotLoggedIn,
        onGetPhoneDevice: throwNotLoggedIn,
        onGetPhoneDevicesList: throwNotLoggedIn,
        onGetScreenRecordingState: throwNotLoggedIn,
        onGetService: throwNotLoggedIn,
        onGetServicesList: throwNotLoggedIn,
        onGetTeamMembers: throwNotLoggedIn,
        onGetTeams: throwNotLoggedIn,
        onGetVariables: throwNotLoggedIn,
        onInviteToCallConference: throwNotLoggedIn,
        onLeaveAndCompleteInteraction: throwNotLoggedIn,
        onLeaveInteraction: throwNotLoggedIn,
        onMergeAllCallsIntoConference: throwNotLoggedIn,
        onRejectInteraction: throwNotLoggedIn,
        onRemoveFromCallConference: throwNotLoggedIn,
        onReplaceNote: throwNotLoggedIn,
        onSendChatMessage: throwNotLoggedIn,
        onSendDtmf: throwNotLoggedIn,
        onSetAgentState: throwNotLoggedIn,
        onSetCallHold: throwNotLoggedIn,
        onSetCallMute: throwNotLoggedIn,
        onSetCallRecording: throwNotLoggedIn,
        onSetDIDNumber: throwNotLoggedIn,
        onSetDisposition: throwNotLoggedIn,
        onSetInteractionActiveScreen: throwNotLoggedIn,
        onSetPhoneDevice: throwNotLoggedIn,
        onSetRescheduleWindow: throwNotLoggedIn,
        onSetScreenRecordingMute: throwNotLoggedIn,
        onSetService: throwNotLoggedIn,
        onSetVariable: throwNotLoggedIn,
        onSetWidgetMinimized: throwNotLoggedIn,
        onStartCall: throwNotLoggedIn,
        onStartChat: throwNotLoggedIn,
        onStartEmail: throwNotLoggedIn,
        onSuggestChatMessage: throwNotLoggedIn,
        onSwitchActiveInteraction: throwNotLoggedIn,
        onTransfer: throwNotLoggedIn,
        onUpdateNote: throwNotLoggedIn,
    })

    return [sessionStateSubscription, integrationApiSubscription]
}
