import React from 'react'

import {tcn} from '@bpinc/lib-tailwind-classnames'
import type {FCWithChildren} from '@bpinc/lib-types-helpers'

import {MessageAvatar} from '../MessageAvatar'
import {MessageContent} from '../MessageContent'
import {MessageTopInfo} from '../MessageTopInfo'

import styles from './ErrorMessage.module.less'


export const ErrorMessage: FCWithChildren<string> = ({children}) => {
    return (
        <div className={tcn(styles.messageBlock, styles.outgoing)}>
            <MessageTopInfo direction="outgoing" isScenario timestamp={0} />
            <section className={tcn(styles.message, styles.outgoing)}>
                <MessageContent className={styles.errorMessage} isError>
                    {children}
                </MessageContent>
            </section>
            <MessageAvatar className={styles.errorMessageAvatar} isScenarioAvatar />
        </div>
    )
}
