import React from 'react'

import {useLocalizationState} from '@bpinc/state-localization-context'


export const EmptyActivityHistoryView: React.FC = () => {
    const {ACTIVITY_HISTORY: i18n} = useLocalizationState('t')

    return (
        <div className="flex h-full flex-col items-center justify-center gap-2.5">
            <div className="heading-m text-neutral-on-secondary">
                {i18n.empty_activity_message_title()}
            </div>
            <div className="body-m text-neutral-fade">
                {i18n.empty_activity_message_description()}
            </div>
        </div>
    )
}
