import React from 'react'

import {useInteractionChatDataSelector} from '@bpinc/ad-interaction-chat-state-context'
import {useAssociatedChatInteractionProp} from '@bpinc/ad-interaction-state-context'
import {getInteractionStepId} from '@bpinc/ad-interaction-state-methods'
import {useInteractionId} from '@bpinc/lib-interaction-context'

import {useRecentActivityHistoryUploader} from '../../hooks/useRecentActivityHistoryUploader'
import {ActiveInteractionTitle} from './ActiveInteractionTitle'
import {ActiveInteractionTop} from './ActiveInteractionTop'
import {InteractionButtons} from './InteractionButtons'
import {InteractionVideo} from './InteractionVideo'


export const ActiveInteractionControlPanel: React.FC = React.memo(() => {
    const interactionId = useInteractionId()
    const chatInteractionStepId = useAssociatedChatInteractionProp(interactionId, getInteractionStepId)
    const chatCallHasVideo = useInteractionChatDataSelector(
        chatInteractionStepId,
        s => !!s?.call?.localStreamId || !!s?.call?.remoteStreamId,
    )

    useRecentActivityHistoryUploader()

    return (
        <div className="flex flex-col" data-testid="interaction-header">
            <ActiveInteractionTitle />
            <ActiveInteractionTop />
            <InteractionButtons />
            {chatCallHasVideo && <InteractionVideo />}
        </div>
    )
})
