import type {MouseEvent} from 'react'
import React from 'react'
import {Button} from '@servicepattern/ui'

import {isAfterCallWork, isChatConference, isWorkItem} from '@bpinc/ad-interaction-state-methods'
import type {Item} from '@bpinc/ad-interaction-state-types'
import {CompleteInteractionButton, EndButton, EndCallButton, EndChatButton} from '@bpinc/lib-interaction-buttons'
import {IconCheck16} from '@bpinc/lib-jskit-ui'
import {tcn} from '@bpinc/lib-tailwind-classnames'

import {useCompleteButtonTexts} from './useCompleteButtonTexts'


interface InteractionsListItemButtonProps {
    item?: Item
    className?: string
    handleCloseClick: (e: MouseEvent) => void
    small?: boolean
    isChat?: boolean
    isDestructive?: boolean
    renderIcon?: () => JSX.Element
}

export const CompleteButton: React.FC<InteractionsListItemButtonProps> = ({
    handleCloseClick,
    item,
    small,
    className,
    isChat,
    isDestructive,
    renderIcon,
}) => {
    const {tooltip, text} = useCompleteButtonTexts(item)

    if (isAfterCallWork(item) || isWorkItem(item)) {
        return small ? (
            <Button
                variant="neutral"
                size="xs"
                className={tcn('ml-2 px-2 py-1 text-on-error', className)}
                data-testid="complete-button"
                onClick={handleCloseClick}
            >
                <span className="flex size-3.5 items-center justify-center rounded-full bg-primary-default">
                    <IconCheck16 className="size-2.5 [&_path]:stroke-neutral-0" />
                </span>
            </Button>
        ) : (
            <CompleteInteractionButton
                onClick={handleCloseClick}
                tooltip={tooltip}
            >
                {text}
            </CompleteInteractionButton>
        )
    }

    if (isChatConference(item) || isChat) {
        return (
            <EndButton
                className="h-7 w-12"
                onClick={handleCloseClick}
                tooltip={tooltip}
            />
        )
    }

    if (small) {
        return (
            <EndChatButton
                onClick={handleCloseClick}
                tooltip={tooltip}
                className={className}
            />
        )
    }

    return (
        <EndCallButton
            renderIcon={renderIcon}
            isDestructive={isDestructive}
            onClick={handleCloseClick}
            tooltip={tooltip}
        />
    )
}
