import React, {useCallback} from 'react'

import {useInteractionChatStateMethods} from '@bpinc/ad-interaction-chat-state-context'
import {useAssociatedChatInteractionProp} from '@bpinc/ad-interaction-state-context'
import {getChatParties} from '@bpinc/ad-interaction-state-methods'
import {useInteractionId} from '@bpinc/lib-interaction-context'

import {getParticipantsListData} from '../InteractionParticipantsList/helpers'
import {InteractionParticipantsList} from '../InteractionParticipantsList/InteractionParticipantsList'


export const ChatConferenceParties = () => {
    const interactionId = useInteractionId()
    const chatParties = useAssociatedChatInteractionProp(interactionId, getChatParties)
    const participantsListData = getParticipantsListData({chatParties})
    const {removePartyFromChat} = useInteractionChatStateMethods()

    const onRemoveItem = useCallback ((id: string) => {
        const party = chatParties?.find(p => p.party_id === id)
        if (party && party.party_id && party.item_id) {
            removePartyFromChat({partyId: party.party_id, itemId: party.item_id})
        }
    }, [chatParties, removePartyFromChat])

    return (
        <InteractionParticipantsList
            isChat
            onRemoveItem={onRemoveItem}
            participantsItems={participantsListData}
        />

    )
}
