import React from 'react'
import produce from 'immer'

import {useContactsState} from '@bpinc/ad-contacts-state-context'
import {useInteractionProp} from '@bpinc/ad-interaction-state-context'
import {getContactId} from '@bpinc/ad-interaction-state-methods'
import {ExternalChat} from '@bpinc/lib-chat-ui'
import {useEvent} from '@bpinc/lib-helpers'
import {useInteractionId} from '@bpinc/lib-interaction-context'
import {Chip} from '@bpinc/lib-jskit-ui'
import {array} from '@bpinc/lib-types-helpers'
import type {ActivityHistoryFilter} from '@bpinc/state-interaction-ui'
import {activityFilters, useInteractionUIChatType, useInteractionUIItemStateSetter} from '@bpinc/state-interaction-ui'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {EmptyActivityHistoryView} from './EmptyActivityHistoryView'


function assertFilterType(value: string): asserts value is ActivityHistoryFilter {
    if (!array(activityFilters).includes(value)) {
        throw Error(`"assertFilterType" failed: ${value} is not filter type`)
    }
}

export const WorkitemHistory: React.FC = () => {

    const {CALL_DETAILS_FORM: i18n} = useLocalizationState('t')
    const activeItemId = useInteractionId()
    const contactId = useInteractionProp(activeItemId, getContactId)
    const selectedFilter = useInteractionUIChatType(activeItemId)
    const setInteractionUIItemState = useInteractionUIItemStateSetter()

    const activityHistory = useContactsState('activityHistory')
    const emptyHistory = !activityHistory[contactId]?.messages.length

    const filterDisplayLabels = {
        'all': i18n.workitem_history_filter_all(),
        'calls': i18n.workitem_history_filter_calls(),
        'messages': i18n.workitem_history_filter_messages(),
    }

    const handleFilterClick = useEvent((e: React.MouseEvent<HTMLButtonElement>) => {
        const {value} = e.currentTarget.dataset
        if (!value) {
            return
        }
        assertFilterType(value)
        setInteractionUIItemState(activeItemId, produce(state => {
            state.activityFilter = value
        }))
    })

    if (emptyHistory) {
        return <EmptyActivityHistoryView />
    }

    return (
        <div className="flex h-full flex-col overflow-hidden">
            <div className="flex gap-2 px-4 py-2 shadow-2 bg-primary-background">
                {activityFilters.map(filterType => (
                    <Chip
                        key={filterType}
                        variant="interactive"
                        data-value={filterType}
                        active={filterType === selectedFilter}
                        onClick={handleFilterClick}
                    >
                        {filterDisplayLabels[filterType]}
                    </Chip>
                ))}
            </div>
            <ExternalChat itemId={activeItemId} />
        </div>
    )
}
