import React, {useMemo, useState} from 'react'
import {Button} from '@servicepattern/ui'

import {useContactsStateMethods} from '@bpinc/ad-contacts-state-context'
import {EmailTypes} from '@bpinc/ad-contacts-state-types'
import {useFormatting} from '@bpinc/ad-formatting-context'
import {Modal, SelectField} from '@bpinc/lib-jskit-ui'
import {isStringInEnum} from '@bpinc/lib-types-helpers'
import {useLocalizationState} from '@bpinc/state-localization-context'
import type {CommonModalProps} from '@bpinc/state-modals-types'


type AddEmailModalProps = CommonModalProps & {
    email?: string
    contactId?: string
    firstName?: string
    lastName?: string
    onConfirm?: () => void
}

export const AddEmailModal: React.FC<AddEmailModalProps> = ({
    onClose,
    email,
    contactId,
    firstName,
    lastName,
    onConfirm,
    ...other
}) => {
    const {t} = useLocalizationState()
    const {addEmail} = useContactsStateMethods()
    const {formatEmailType, formatFullName} = useFormatting()
    const [value, setValue] = useState<EmailTypes>(EmailTypes.PRIMARY)

    const name = formatFullName(firstName, lastName)

    const options = useMemo(() => {
        return Object.keys(EmailTypes).map(type => ({
            value: type,
            content: formatEmailType(type, false),
        }))
    }, [formatEmailType])

    function handleSelect(innerValue: string | undefined) {
        if (!isStringInEnum(EmailTypes, innerValue)) {
            throw Error('Invalid email type')
        }
        setValue(innerValue)
    }

    function handleConfirm() {
        contactId && email && addEmail({contactId, type: value, email})
        onConfirm?.()
        onClose()
    }

    return (
        <Modal
            onClose={onClose}
            data-testid="contact-add-email-modal"
            {...other}
        >
            <Modal.Header>
                {t.CONTACT.add_email_heading()}
            </Modal.Header>

            <Modal.Content className="body-m">
                <div className="flex flex-col gap-4">
                    <p>
                        {t.CONTACT.add_email_description_1()}
                        {' '}
                        <b>{email}</b>
                        {' '}
                        {t.CONTACT.add_email_description_2()}
                        {' '}
                        <b>{name}</b>
                        {' '}
                        {t.CONTACT.add_email_description_3()}
                    </p>
                    <p>
                        {t.CONTACT.add_email_description_4()}
                        <SelectField
                            layoutClassName="inline-block min-w-[187px] mx-4"
                            placeholder={t.CONTACT.add_email_email_type_placeholder()}
                            value={value}
                            onValueChange={handleSelect}
                            options={options}
                            data-testid="phone-type-dropdown"
                        />
                        {t.CONTACT.add_email_description_5()}
                    </p>
                </div>
            </Modal.Content>

            <Modal.Actions>
                <Button variant="secondary" onClick={() => onClose()} data-testid="cancel-button">
                    {t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_buttonCancel()}
                </Button>
                <Button variant="primary" onClick={handleConfirm} data-testid="add-button">
                    {t.CONTACT.add_email_confirmation_button()}
                </Button>
            </Modal.Actions>
        </Modal>
    )
}
