import React, {useMemo} from 'react'

import {MS_TEAMS_CHANNEL_AVATAR_FIRST_NAME, MS_TEAMS_CHANNEL_AVATAR_LAST_NAME} from '@bpinc/lib-avatar-ui'
import type {PropsAvatar} from '@bpinc/lib-contact-list-item-ui'
import {ContactListItem} from '@bpinc/lib-contact-list-item-ui'
import {DirectoryFavoriteButton} from '@bpinc/lib-favorites-button'
import {useEvent} from '@bpinc/lib-helpers'
import {highlighted, SelectionItem} from '@bpinc/lib-ui-elements'

import type {MsTeamsChannelProps} from '../types'
import {MsTeamsChannelControls} from './MsTeamsChannelControls'


export const MsTeamsChannel: React.FC<MsTeamsChannelProps> = ({
    transferMode,
    transferControlsStyle,
    item,
    onDetails,
    onChat,
    highlight,
    onClick,
    onDoubleClick,
    showFavoriteButton = true,
    ...other
}) => {

    const avatar: PropsAvatar = useMemo(() => ({
        firstName: MS_TEAMS_CHANNEL_AVATAR_FIRST_NAME,
        lastName: MS_TEAMS_CHANNEL_AVATAR_LAST_NAME,
    }), [])

    const title = useMemo(() => (
        <span className="flex gap-1">
            <b data-testid="name" className="inline-block truncate font-semibold body text-primary-on-primary">
                {highlighted({
                    highlight,
                    text: item.title,
                })}
            </b>
            {showFavoriteButton && (
                <DirectoryFavoriteButton forContactListItem item={item} />
            )}
        </span>
    ), [highlight, item.title, showFavoriteButton, item])

    const controls = (
        <MsTeamsChannelControls
            item={item}
            transferMode={transferMode}
            onChat={onChat}
            onDetails={onDetails}
        />
    )

    const handleClick = useEvent((e: React.MouseEvent) => {
        if (onClick) {
            onClick(e, item)
        } else if (onChat) {
            onChat({
                userId: item.channelId,
                firstName: item.title,
                lastName: '',
                phone: '',
                userType: item.userType,
                photoUrl: item.photoUrl,
            })
        }
    })

    const handleDoubleClick = useEvent(() => {
        onDoubleClick?.(item)
    })

    return (
        <ContactListItem
            as={SelectionItem}
            keyHandlerElement="directory-item"
            transferMode={transferMode}
            transferControlsStyle={transferControlsStyle}
            isStaticEntry
            userId={item.channelId}
            userType={item.userType}
            avatarProps={avatar}
            title={title}
            controls={controls}
            onClick={handleClick}
            onDoubleClick={handleDoubleClick}
            containerProps={{
                itemId: item.channelId,
                focusOnSelection: true,
                tabIndex: 0,
                'data-testid': 'directory-item',
                'data-ms-teams-channel': 'true',
                'data-element': 'directory-item',
            }}
            {...other}
        />
    )
}
