import type {ReactElement} from 'react'
import React from 'react'

import type {BaseConnectors} from '@bpinc/ad-connected-state-types'
import {BaseProviders} from '@bpinc/ad-local-providers'
import type {PlatformSpecific} from '@bpinc/ad-platform-specific-types'
import {Tooltip} from '@bpinc/lib-jskit-ui'
import {NotificationsStateProvider} from '@bpinc/lib-notifications-context'
import type {AppType} from '@bpinc/state-lookup'

import type {BaseConnectorsConsumerComponent} from './AuthorizationModule'
import {BaseConnectorsConsumers} from './BaseConnectorsConsumers'


type Props = {
    app: AppType
    platformSpecific: PlatformSpecific
    children: (baseConnectors: BaseConnectors) => ReactElement
    BaseConnectorsConsumer?: BaseConnectorsConsumerComponent
}

export const TopLevelProviders: React.FC<Props> = ({
    app,
    platformSpecific,
    BaseConnectorsConsumer,
    children,
}) => {
    return (
        <BaseProviders platformSpecific={platformSpecific} app={app}>
            {baseConnectors => (
                <BaseConnectorsConsumers
                    baseConnectors={baseConnectors}
                    BaseConnectorsConsumer={BaseConnectorsConsumer}
                >
                    <Tooltip.Provider>
                        <NotificationsStateProvider>
                            {children(baseConnectors)}
                        </NotificationsStateProvider>
                    </Tooltip.Provider>
                </BaseConnectorsConsumers>
            )}
        </BaseProviders>
    )
}
