import {useMemo} from 'react'

import {SHOW_MORE_TIMEZONES} from './TimeZoneDropdown'
import {useFavoriteTimeZones} from './useFavoriteTimeZones'


export function useOptions() {
    const favoriteTimeZones = useFavoriteTimeZones()

    return useMemo(function memoizeOutput() {
        return [
            ...favoriteTimeZones.map(timezone => {
                return {
                    content: timezone.fullName,
                    value: timezone.id,
                    key: timezone.id,
                    'data-testid': 'option',
                }
            }),
            {
                content: '. . .',
                key: 'Show more timezones',
                ['aria-label']: 'Show more timezones',
                value: SHOW_MORE_TIMEZONES,
                'data-testid': 'show-more-button',
            },
        ]
    }, [favoriteTimeZones])

}
