import React, {useMemo} from 'react'

import {getMessageId} from '../../../../../../helpers'
import type {HistoryVoiceTranscriptMessage} from '../../../../../../types'
import type {CallMessageProps} from '../CallMessageContent'
import {CallMessageRecording} from '../CallMessageRecording'
import {CallMessageVoiceTranscript} from '../CallMessageVoiceTranscript'


 type Props = CallMessageProps & {
     title: string
     createdTime?: string
     ariaLabel?: string
 }
export const useContentVoiceTranscript = (props: Props) => {
    const {
        direction,
        duration,
        timestamp,
        hasVoiceRecording,
        recordingLink,
        isMissedCall,
        voiceTranscript,
        createdTime,
        ariaLabel,
        title,
    } = props

    return useMemo(() => {
        return (
            <div>
                {recordingLink && (
                    <CallMessageRecording
                        isOpenModal
                        audioPlayerClassName="bg-transparent shadow-none"
                        containerClassName="bg-primary-container-variant1 rounded-xs px-3 py-2"
                        hasVoiceRecording={hasVoiceRecording}
                        recordingLink={recordingLink}
                        voiceTranscript={voiceTranscript}
                        timestamp={timestamp}
                        duration={duration}
                        isMissedCall={isMissedCall}
                        title={title}
                        createdTime={createdTime}
                        hasVoiceTranscript={!!voiceTranscript?.length}
                        ariaLabel={ariaLabel}
                        direction={direction}
                    />
                )}
                <div className="scrollbar-general mt-4 flex select-none flex-col gap-4 overflow-y-auto" data-testid="voice-transcript-messages">
                    {voiceTranscript && voiceTranscript.map((message: HistoryVoiceTranscriptMessage) => (
                        <CallMessageVoiceTranscript message={message} key={getMessageId(message)} />
                    ))}
                </div>
            </div>
        )
    }, [ariaLabel, createdTime, direction, duration, hasVoiceRecording, isMissedCall, recordingLink, timestamp, title, voiceTranscript])
}
