import React, {useCallback, useRef} from 'react'

import {SmList} from '@bpinc/lib-external-ui-elements'
import {useBoolean, useOutsideClick} from '@bpinc/lib-helpers'
import {CircleButton, IconMore} from '@bpinc/lib-jskit-ui'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {useMoreButtons} from './useMoreButtons'


export const ShowMoreButton: React.FC = () => {
    const {t} = useLocalizationState()
    const showMoreList = useRef<HTMLDivElement>(null)
    const {value: show, toggle: toggleShow} = useBoolean(false)

    useOutsideClick({
        ref: showMoreList,
        handler: () => {
            toggleShow(false)
        },
    })

    const openShowMoreList = useCallback(
        (e: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
            if (!show) {
                e.nativeEvent.stopImmediatePropagation()
                toggleShow(true)
            }
        },
        [show, toggleShow],
    )


    const moreButtons = useMoreButtons(toggleShow)
    if (!moreButtons.length) {
        return null
    }

    if (moreButtons.length === 1) {
        const [{Icon, tooltip, ...props}] = moreButtons

        return (
            <CircleButton {...props} tooltip={{content: tooltip}}>
                {Icon && <Icon className="flex size-4 items-center justify-center" />}
            </CircleButton>
        )
    }

    return (
        <div ref={showMoreList}>
            <CircleButton
                isActive={show}
                tooltip={{content: t.GLOBAL.show_more()}}
                onMouseDown={openShowMoreList}
                data-testid="show-more-button"
            >
                <IconMore className="icon-fade" />
            </CircleButton>
            <div>
                {show && (
                    <SmList
                        className="absolute z-[200] min-w-[168px] rounded border border-main-border bg-white"
                        verticalAlign="middle"
                        data-testid="more-buttons-list"
                    >
                        {moreButtons.map(button => {
                            const {Icon} = button
                            return (
                                <SmList.Item
                                    as="button"
                                    key={button['data-testid']}
                                    data-testid={button['data-testid']}
                                    aria-label={button.tooltip}
                                    className={tcn('relative flex h-[37px] cursor-pointer items-center bg-transparent',
                                        'w-full gap-x-2 border-none px-3 py-2.5 text-primary-on-primary hover:bg-menu-bg-hover')}
                                    onClick={button.onClick}
                                    disabled={button.disabled}
                                >
                                    {Icon && <Icon className="size-4" />}
                                    <SmList.Content className="caption-s">
                                        {button.tooltip}
                                    </SmList.Content>
                                </SmList.Item>
                            )
                        })}
                    </SmList>
                )}
            </div>
        </div>
    )
}
