import {useLayoutEffect} from 'react'

import {useNavigationState} from '@bpinc/lib-communicator-navigation-state'
import {useCommunicatorUIState} from '@bpinc/lib-communicator-ui-context'


export function useSearchQueryFromPageParams() {
    const {searchValue, setSearchValue} = useCommunicatorUIState('dialpad')
    const {
        activePage: {params: pageParams},
        cleanPageParams,
    } = useNavigationState()
    const searchQuery = pageParams?.searchQuery

    useLayoutEffect(() => {
        if (!searchValue && searchQuery) {
            setSearchValue(searchQuery)
        }
        cleanPageParams()
    }, [searchValue, searchQuery, setSearchValue, cleanPageParams])
}
