import {useContactDetailsOpener} from '@bpinc/ad-interaction-state-hooks'
import {useEvent} from '@bpinc/lib-helpers'
import type {FavoritesItem} from '@bpinc/state-favorites-types'


export function useDetailsButtonClickHandler() {

    const openContactDetails = useContactDetailsOpener()

    return useEvent((item: FavoritesItem) => {
        openContactDetails({
            contactId: item.contactId,
            userId: item.userId || item.emiUserId,
            entryId: item.entryId,
        })
    })
}
