import {useMemo} from 'react'

import {useActivitiesState} from '@bpinc/ad-activities-state-context'
import type {PartyType} from '@bpinc/ad-activities-state-types'
import {useContactsState} from '@bpinc/ad-contacts-state-context'
import {useInteractionProp} from '@bpinc/ad-interaction-state-context'
import {useSessionState} from '@bpinc/ad-session-context'

import {activityHistoryToHistoryMessageReducer, mapActivityToActivityHistoryItems} from './helpers'


export function useNormalizedHistoryMessages(
    contactId: string,
    itemId: string,
) {
    const activityHistory = useContactsState('activityHistory')

    const {userId: selfUserId} = useSessionState()

    const activities = useActivitiesState('activities')

    const previousActivity = useInteractionProp(itemId, i => i?.previousActivity)
    const previousActivityData = previousActivity ? activities[previousActivity.globalInteractionId] : undefined

    return useMemo(
        function memoizeNormalizedHistoryMessages() {
            const activityHistoryItem = contactId ? activityHistory[contactId] : undefined

            const activityHistoryMessages = activityHistoryItem?.messages
            const previousActivityMessages = mapActivityToActivityHistoryItems(
                previousActivityData?.data?.reduce<PartyType[]>((all, next) => {
                    all.push(...next.messages)
                    return all
                }, []),
            )
            const messages = activityHistoryMessages || previousActivityMessages || []

            return messages.reduce(activityHistoryToHistoryMessageReducer({selfUserId}), [])
        },
        [contactId, activityHistory, previousActivityData?.data, selfUserId],
    )
}
