import React, {useMemo} from 'react'
import {createStore, Provider} from 'jotai'

import {useFormatting} from '@bpinc/ad-formatting-context'
import {useSessionState} from '@bpinc/ad-session-context'
import type {FCWithChildren} from '@bpinc/lib-types-helpers'
import {assertDefined} from '@bpinc/lib-types-helpers'

import {personalTabUserAtom} from './atoms'


const store = createStore()

export const JotaiProvider: FCWithChildren = ({children}) => {
    const {
        userId: selfUserId,
        firstName: selfFirstName,
        lastName: selfLastName,
    } = useSessionState(['userId', 'firstName', 'lastName'])
    const {formatFullName} = useFormatting()

    //Use useMemo instead of useEffect, because we want to initialize the store before rendering the children
    useMemo(function initializeStore() {
        assertDefined(selfUserId)
        store.set(personalTabUserAtom, {
            id: selfUserId,
            name: formatFullName(selfFirstName, selfLastName),
        })
    }, [formatFullName, selfFirstName, selfLastName, selfUserId])

    return <Provider store={store}>{children}</Provider>
}
