import {isDefined} from '@bpinc/lib-types-helpers'

import type {FavoritesListChannelItem} from '../../types'


export function getUniqueChannelId({item, channel, media}: FavoritesListChannelItem): string {
    switch (channel.channelType) {
        case 'internal':
            return [
                item.id,
                channel.channelType,
                media,
            ].filter(isDefined).join('-')
        case 'phone':
            return [
                item.id,
                channel.channelType,
                media,
                channel.numberType,
                channel.numberId,
                channel.number,
            ].filter(isDefined).join('-')
        case 'email':
            return [
                item.id,
                channel.channelType,
                channel.emailId,
                channel.email,
            ].filter(isDefined).join('-')
        case 'messenger':
            return [
                item.id,
                channel.channelType,
                channel.messengerType,
                channel.userId,
            ].filter(isDefined).join('-')
    }
}
