import React from 'react'

import {useFormatting} from '@bpinc/ad-formatting-context'
import {formatDate} from '@bpinc/ad-formatting-helpers'
import {useSessionState, useSessionStateMethods} from '@bpinc/ad-session-context'
import {incomingErrorMessageCodes} from '@bpinc/ad-transport-types'
import {SmButton, SmSegment} from '@bpinc/lib-external-ui-elements'
import {assertDefined} from '@bpinc/lib-types-helpers'
import {Modal} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'


export const ForceLogout: React.FC = () => {
    const forceLogoutInfo = useSessionState('forceLogoutInfo')
    const {closeForceLogout} = useSessionStateMethods()
    const {formatFullName} = useFormatting()
    const {t} = useLocalizationState()

    assertDefined(forceLogoutInfo)
    const loggedOutBy = formatFullName(forceLogoutInfo.firstName, forceLogoutInfo.lastName)
    const loggedOutTime = formatDate(forceLogoutInfo.timestamp)

    const getContent = () => {
        switch (forceLogoutInfo.errorCode) {
            case incomingErrorMessageCodes.forced_logout_by_another_session: {
                return t.ERROR_MESSAGES.sessionWasOverwritten()
            }
            case incomingErrorMessageCodes.forced_logout_by_supervisor: {
                return t.ERROR_MESSAGES.forcedLogout(loggedOutBy, loggedOutTime)
            }
            case incomingErrorMessageCodes.forced_logout_by_disabling_tenant:
            case incomingErrorMessageCodes.forced_logout_by_disabling_user: {
                return t.ERROR_MESSAGES.account_has_been_disabled()
            }
        }
        return forceLogoutInfo.forceLogoutReason
    }

    return (
        <SmSegment
            padded
            className="absolute left-1/2 top-1/2 -translate-x-1/2 -translate-y-1/2
                overflow-hidden rounded-none border-none bg-transparent shadow-none"
            id="login"
        >
            <Modal
                isOpen
                size="small"
                className="max-w-[570px]"
                aria-describedby="forceLogoutDialogDescription"
                data-testid="forced-logout-modal"
            >
                <Modal.Header>
                    {t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_title_information()}
                </Modal.Header>
                <Modal.Content>
                    <Modal.Description id="forceLogoutDialogDescription">
                        <p>{getContent()}</p>
                    </Modal.Description>
                </Modal.Content>
                <Modal.Actions>
                    <SmButton onClick={closeForceLogout} primary data-testid="ok-button">
                        {t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_buttonOk()}
                    </SmButton>
                </Modal.Actions>
            </Modal>
        </SmSegment>
    )
}
