import React, {useMemo} from 'react'

import type {ContactRecord as ContactRecordType} from '@bpinc/state-interaction-ui'

import type {ContactRecordsProps} from './ContactRecords'
import {ExistingContactRecord} from './ExistingContactRecord'
import {NonExistingContactRecord} from './NonExistingContactRecord'


interface ContactRecordProps {
    index: number
    contact: ContactRecordType
    contactHandlersFactories: ContactRecordsProps['contactHandlersFactories']
}

export const ContactRecord: React.FC<ContactRecordProps> = ({contactHandlersFactories, contact, index}) => {

    const contactHandlers = useMemo(
        function memoizeContactHandlers() {
            return {
                handleContactNameChange: contactHandlersFactories.handleContactNameChangeFactory(
                    index,
                ),
                handleContactPhoneChange: contactHandlersFactories.handleContactPhoneChangeFactory(
                    index,
                ),
                handleContactPhoneTypeChange: contactHandlersFactories.handleContactPhoneTypeChangeFactory(
                    index,
                ),
                handleContactPhoneIdChange: contactHandlersFactories.handleContactPhoneIdChangeFactory(
                    index,
                ),
                handleContactCreateContactChange: contactHandlersFactories.handleContactCreateContactChangeFactory(
                    index,
                ),
            }
        },
        [contactHandlersFactories, index],
    )
    return (
        <>
            {contact.contactId ? (
                <ExistingContactRecord contact={contact} contactHandlers={contactHandlers} />
            ) : (
                <NonExistingContactRecord contact={contact} contactHandlers={contactHandlers} />
            )}
        </>
    )
}
