import type {FC} from 'react'
import React, {useMemo} from 'react'

import type {Attachment as AttachmentType} from '@bpinc/ad-request-urls'
import {getAttachmentUrl} from '@bpinc/ad-request-urls'
import {useSessionState} from '@bpinc/ad-session-context'
import {Attachment} from '@bpinc/lib-ui-elements'


export const EmailAttachment: FC<AttachmentType> = ({id, name}) => {
    const {serverOrigin, domain, sessionId, loginId} = useSessionState()

    const attachmentURL = useMemo(() => {
        if (sessionId && domain && loginId) {
            return getAttachmentUrl({serverOrigin, sessionId, domain, loginId, attachmentId: id})
        }
    }, [domain, id, loginId, serverOrigin, sessionId])

    return attachmentURL ? (
        <Attachment name={name} link={attachmentURL} className="!mr-5 !mt-2.5 inline-block w-auto" />
    ) : null
}
