import React, {useCallback} from 'react'

import {SemanticButton} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'
import type {ModalConfig} from '@bpinc/state-modals-types'


interface ActionsProps {
    closeModal: () => void
    onConfirm: () => void
}

interface ContentProps {
    name: string
}

const Title: React.FC = () => {
    const {t} = useLocalizationState()
    const text = t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_title_warning()

    return <>{text}</>
}

const Content: React.FC<ContentProps> = ({name}) => {
    const {t} = useLocalizationState()

    return (
        <div>
            <p>{t.UNDEFINED.contact_deleting_confirmation(name)}</p>
        </div>
    )
}

const Actions: React.FC<ActionsProps> = props => {
    const {t} = useLocalizationState()
    const {closeModal, onConfirm} = props
    const handleConfirm = useCallback(() => {
        closeModal()
        onConfirm()
    }, [closeModal, onConfirm])

    return (
        <>
            <SemanticButton
                enabledInOffline
                onClick={closeModal}
                data-testid="cancel-button"
            >
                {t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_buttonCancel()}
            </SemanticButton>
            <SemanticButton
                primary
                onClick={handleConfirm}
                data-testid="confirm-button"
            >
                {t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_buttonOk()}
            </SemanticButton>
        </>
    )
}

export const getDeleteWarningModal = (
    onConfirm: () => void,
    options: {name: string},
): ModalConfig => {
    const {name} = options
    return {
        dataKey: 'delete-contact-warning-button',
        title: <Title />,
        body: <Content name={name} />,
        actions: ({closeModal}: { closeModal: () => void }) => {
            return (
                <Actions
                    closeModal={closeModal}
                    onConfirm={onConfirm}
                />
            )
        },
    }
}
