import {useEffect, useState} from 'react'

import {useLocalSettings} from '@bpinc/ad-local-settings-context'
import {useValueUpdated} from '@bpinc/lib-helpers'

import type {OpenFolders} from './helpers'
import {getActiveGroupStorageName} from './helpers'


const EMPTY_OPEN_FOLDERS = {
    list: [],
    diff: {added: [], removed: []},
}

export function useOpenFoldersState(query: string | undefined) {
    const localSettings = useLocalSettings()
    const queryChanged = useValueUpdated(query)

    const [openFolders, setOpenFolders] = useState<OpenFolders>(EMPTY_OPEN_FOLDERS)
    const [searchOpenFolders, setSearchOpenFolders] = useState<OpenFolders>(EMPTY_OPEN_FOLDERS)

    useEffect(() => {
        if (queryChanged) {
            setSearchOpenFolders(EMPTY_OPEN_FOLDERS)
            localSettings.set(getActiveGroupStorageName({isSearchMode: true}), [])
        }
    }, [localSettings, queryChanged])

    return query
        ? [searchOpenFolders, setSearchOpenFolders] as const
        : [openFolders, setOpenFolders] as const
}
