import type {FC} from 'react'
import React, {useEffect, useState} from 'react'
import * as Accordion from '@radix-ui/react-accordion'
import produce from 'immer'

import {usePrevious} from '@bpinc/lib-helpers'
import type {DispositionOption} from '@bpinc/lib-interaction-ui-hooks'
import {IconChevronRight16} from '@bpinc/lib-jskit-ui'
import {tcn} from '@bpinc/lib-tailwind-classnames'

import {useAccordionPanels} from '../../hooks/useAccordionPanels'


export interface DispositionsAccordionProps {
    list: DispositionOption[]
    searchValue: string | undefined
    onSelect: (option: DispositionOption) => void
    selectedDisposition: DispositionOption
    className?: string
}

export const DispositionsAccordion: FC<DispositionsAccordionProps> = props => {
    const {
        className,
        searchValue,
    } = props

    const panels = useAccordionPanels(props)

    const [visiblePanels, setVisiblePanels] = useState(
        () => panels.map(p => p.value),
    )

    const prevPanels = usePrevious(panels)
    useEffect(function setInitialVisiblePanels() {
        if (!prevPanels?.length && panels.length && !visiblePanels.length) {
            const initialPanels = panels.map(p => p.value)
            setVisiblePanels(initialPanels)
            setVisiblePanelsDuringSearch(initialPanels)
        }
    }, [panels, prevPanels?.length, visiblePanels.length])

    const [visiblePanelsDuringSearch, setVisiblePanelsDuringSearch] = useState(visiblePanels)


    useEffect(function openClosedFoldersIfSearchMatches() {
        if (!searchValue) {
            setVisiblePanelsDuringSearch(visiblePanels)
            return
        } else {
            const matchingPanels = panels.filter(panel =>
                panel.content.some(item => item.text.toLowerCase().includes(searchValue.toLowerCase())),
            )
            setVisiblePanelsDuringSearch(produce(draft => {
                matchingPanels.forEach(panel => {
                    if (!draft.includes(panel.value)) {
                        draft.push(panel.value)
                    }
                })
            }))
        }
    }, [panels, props, searchValue, visiblePanels])


    return (
        <Accordion.Root
            type="multiple"
            className={className}
            value={searchValue ? visiblePanelsDuringSearch : visiblePanels}
            onValueChange={searchValue ? setVisiblePanelsDuringSearch : setVisiblePanels}
        >
            {panels.map(panel => (
                <Accordion.Item
                    key={panel.value}
                    value={panel.value}
                    className="caption-s"
                >
                    <Accordion.Header>
                        <Accordion.Trigger
                            data-testid="disposition-item-header"
                            data-selectable="dispositions-accordion"
                            className={tcn(
                                'group flex w-full gap-2 px-3 py-2.5',
                                'state-layer-bg-neutral-0',
                                'hover:state-layer-mix-dark-on-white-1',
                                'focus:state-layer-mix-dark-on-white-2',
                                'data-[selected=true]:state-layer-mix-dark-on-white-2',
                                'active:state-layer-mix-dark-on-white-3',
                            )}
                        >
                            <IconChevronRight16 className="group-data-[state=open]:rotate-90" />
                            <span className="font-semibold">{panel.title}</span>
                        </Accordion.Trigger>
                    </Accordion.Header>
                    <Accordion.Content>{panel.content.map(c => c.element)}</Accordion.Content>
                </Accordion.Item>
            ))}
        </Accordion.Root>
    )
}
