import React from 'react'

import type {HandleCallParams} from '@bpinc/ad-interaction-state-hooks'
import type {InitiateCall} from '@bpinc/ad-interaction-state-types'
import {useEvent} from '@bpinc/lib-helpers'
import type {CommonSlidingModalItemProps} from '@bpinc/lib-sliding-modal-context'
import {assertDefined} from '@bpinc/lib-types-helpers'
import type {ChatParams} from '@bpinc/lib-ui-elements'
import {SlidingModal} from '@bpinc/lib-ui-elements'
import type {RecentItem} from '@bpinc/state-recent-types'

import {Footer} from './Footer'
import {Header} from './Header'
import {RecentConferenceModalContent} from './RecentConferenceModalContent'


export type RecentConferenceSlidingModalCommonProps = {
    handleCallClick: (callParams: HandleCallParams) => void
    handleChatClick: (chatParams: ChatParams) => void
    transferMode?: 'messages' | 'calls'
}

interface RecentConferenceSlidingModalProps
    extends RecentConferenceSlidingModalCommonProps, CommonSlidingModalItemProps {
    recentItem?: RecentItem
}

export const RecentConferenceSlidingModal: React.FC<RecentConferenceSlidingModalProps> = ({
    show,
    closeModal,
    closing,
    style,
    handleCallClick,
    handleChatClick,
    recentItem,
    transferMode,
}) => {
    assertDefined(recentItem)

    const onCall = useEvent((callParams: InitiateCall) => {
        handleCallClick({
            ...callParams,
            origination: 'recent',
            navigateToInteractionsPage: true,
            afterCallSideEffectCallback: closeModal,
        })
    })

    const onChat = useEvent((chatParams: ChatParams) => {
        handleChatClick({
            ...chatParams,
            origination: 'recent',
            previousActivity: recentItem.globalInteractionId
                ? {
                    globalInteractionId: recentItem.globalInteractionId,
                    mediaType: recentItem.media,
                }
                : undefined,
        })
        closeModal()
    })

    return (
        <SlidingModal
            show={show}
            closing={closing}
            style={style}
            closeModal={closeModal}
            name="recentConference"
            data-testid="recent-conference-parties-modal"
            header={<Header item={recentItem} />}
            footer={(
                <Footer
                    recentItem={recentItem}
                    onCall={onCall}
                    onChat={onChat}
                />
            )}
        >
            <RecentConferenceModalContent
                recentItem={recentItem}
                transferMode={transferMode}
                onCall={onCall}
                onChat={onChat}
            />
        </SlidingModal>
    )
}
