import type {SyntheticEvent} from 'react'
import React, {useMemo} from 'react'

import type {SmInputProps} from '@bpinc/lib-external-ui-elements'
import {SmForm, SmInput} from '@bpinc/lib-external-ui-elements'
import {Dropdown} from '@bpinc/lib-ui-elements'
import type {DropdownProps} from '@bpinc/lib-ui-elements/'
import {useLocalizationState} from '@bpinc/state-localization-context'


interface ReminderProps {
    amountValue: number
    unitsValue: string
    handleAmountChange: (event: SyntheticEvent, {value}: SmInputProps) => void
    handleUnitsChange: (event: SyntheticEvent, {value}: DropdownProps) => void
}

export const Reminder: React.FC<ReminderProps> = ({
    amountValue,
    unitsValue,
    handleAmountChange,
    handleUnitsChange,
}) => {
    const {t} = useLocalizationState()

    const options = useMemo(
        function memoizeUnitOptions() {
            return [
                {value: 'MINUTES', text: t.GLOBAL.time_units_minutes()},
                {value: 'HOURS', text: t.GLOBAL.time_units_hours()},
                {value: 'DAYS', text: t.GLOBAL.time_units_days()},
            ]
        },
        [t.GLOBAL],
    )

    return (
        <SmForm.Field>
            <label>{t.CALENDAR_EVENT_FORM.label_reminder()}</label>
            <SmForm.Group>
                <SmForm.Field className="w-[76px]">
                    <SmInput value={amountValue} onChange={handleAmountChange} />
                </SmForm.Field>
                <SmForm.Field>
                    <Dropdown
                        selection
                        options={options}
                        value={unitsValue}
                        onChange={handleUnitsChange}
                    />
                </SmForm.Field>
            </SmForm.Group>
        </SmForm.Field>
    )
}
