import type {FC} from 'react'
import React, {useCallback} from 'react'
import {partial} from 'lodash-es'

import {useInteractionStateMethods} from '@bpinc/ad-interaction-state-context'
import {useCallHandler} from '@bpinc/ad-interaction-state-hooks'
import type {ExternalChatChannelType} from '@bpinc/ad-transport-types'
import {AppRoutes, useNavigationState} from '@bpinc/lib-communicator-navigation-state'
import {IconChat, IconPhone} from '@bpinc/lib-icons'
import {CircleButton} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'


export interface EditingContactControlsProps {
    firstName?: string
    lastName?: string
    phone?: string
    messengerId?: string
    channel?: ExternalChatChannelType
}

export const EditingContactControls: FC<EditingContactControlsProps> = ({phone, firstName, lastName}) => {
    const interactionStateMethods = useInteractionStateMethods()
    const setActivePage = useNavigationState('setActivePage')
    const {t} = useLocalizationState()

    const handleChat = useCallback(() => {
        if (phone) {
            interactionStateMethods.createPotentialItem({
                phone,
                firstName,
                lastName,
            })
            setActivePage({name: AppRoutes.Interactions})
        }
    }, [interactionStateMethods, phone, firstName, lastName, setActivePage])

    const onCall = useCallHandler()

    const handleCall = partial(onCall, {firstName, lastName, phone, navigateToInteractionsPage: true})

    return (
        <div className="-mr-1 ml-auto flex flex-none items-center">
            <CircleButton
                noActiveState
                onClick={handleChat}
                tooltip={t.CONTACT.button_initiate_chat_text()}
                className="!h-10 !w-10"
                data-testid="chat-button"
            >
                <IconChat />
            </CircleButton>
            <CircleButton
                noActiveState
                onClick={handleCall}
                tooltip={t.CONTACT.button_start_call_text()}
                className="!h-10 !w-10"
                data-testid="call-button"
            >
                <IconPhone />
            </CircleButton>
        </div>
    )
}
