import React from 'react'

import {useInteractionProp} from '@bpinc/ad-interaction-state-context'
import {useInteractionId} from '@bpinc/lib-interaction-context'
import {assertDefined} from '@bpinc/lib-types-helpers'


export const WorkitemDetails: React.FC = () => {

    const activeItemId = useInteractionId()
    const workitemData = useInteractionProp(activeItemId, item => item?.workitemData)
    assertDefined(workitemData)
    const {fullInfo} = workitemData.contactInfo

    return (
        <div className="scrollbar-on-hover m-4 mt-2 overflow-auto">
            {fullInfo.map(({name, value}) => (
                <div key={name} className="flex gap-6 border-b px-1 py-4 bg-primary-background border-disabled">
                    <div className="line-clamp-[13] w-[120px] shrink-0 font-semibold body text-primary-on-primary">
                        {name}
                    </div>
                    <div className="line-clamp-[13] grow body text-primary-on-primary">
                        {value}
                    </div>
                </div>
            ))}
        </div>
    )
}
