import React from 'react'

import type {ActionCase, FormButtonAction, FormDefinition} from '@bpinc/ad-request-urls'
import {createLeadMappingStringToFormValueMap} from '@bpinc/lib-form-builder/src/utils'
import type {SemanticButtonProps} from '@bpinc/lib-ui-elements'
import {SemanticButton} from '@bpinc/lib-ui-elements'

import {useButtonActionConditionsChecker} from './useButtonActionConditionsChecker'
import {useButtonHandlers} from './useButtonHandlers'
import {useButtonPrivilegesChecker} from './useButtonPrivilegesChecker'


export type ChangeFormPageId = (newPage: string) => void

export type FormBuilderButtonProps = SemanticButtonProps & {
    contactId?: string
    values?: Record<string, unknown>
    changeFormPageId?: ChangeFormPageId
    formDefinition?: FormDefinition
    action: FormButtonAction
    actionCases?: ActionCase[]
    children: React.ReactNode
    dataKey?: string
}

export const FormBuilderButton: React.FC<FormBuilderButtonProps> = ({
    contactId,
    values,
    formDefinition,
    changeFormPageId,
    action,
    actionCases,
    children,
    dataKey,
    ...props
}) => {
    const mappingToFormValues = values && formDefinition
        ? createLeadMappingStringToFormValueMap(values || {}, formDefinition)
        : values

    const handleClick = useButtonHandlers({
        contactId,
        actionCases,
        action,
        values: mappingToFormValues,
        changeFormPageId,
    })

    const hasPrivileges = useButtonPrivilegesChecker(action)
    const isConditionsPassed = useButtonActionConditionsChecker({values: mappingToFormValues, actionCases})

    const disabled = !isConditionsPassed || !hasPrivileges

    return (
        <SemanticButton
            {...props}
            data-testid={dataKey}
            data-form-field="true"
            data-field-type="button"
            data-disabled={disabled || undefined}
            onClick={handleClick}
            basic
            disabled={disabled}
        >
            {children}
        </SemanticButton>
    )
}
