import React, {useState} from 'react'
import type {UseFormReturn} from 'react-hook-form'
import {DevTool} from '@hookform/devtools'

import {useContact, useContactFormDefinition} from '@bpinc/ad-contacts-state-context'
import {FormBuilder} from '@bpinc/ad-form-builder'
import {createFormValues} from '@bpinc/lib-form-builder'
import type {Toggle} from '@bpinc/lib-helpers'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {Avatar} from '../../Avatar'
import {ExternalEditControls} from './ExternalEditControls'


export const ExternalEdit: React.FC<{
    toggleEditMode: Toggle
    contactId: string
}> = ({
    toggleEditMode,
    contactId,
}) => {
    const {t} = useLocalizationState()
    const formDefinition = useContactFormDefinition('CONTACT_DETAIL')
    const contactData = useContact(contactId)
    const variables = contactData?.details

    const formValues = formDefinition && variables
        ? createFormValues(formDefinition, variables)
        : undefined

    const [form, setForm] = useState<UseFormReturn | undefined>()

    if (!formDefinition) {
        return null
    }

    return (
        <>
            <div className="flex flex-auto flex-col overflow-y-auto overflow-x-hidden px-6">
                <div className="flex h-16 shrink-0 items-center pb-2 pt-4">
                    <h2>{t.CONTACT.external_contact()}</h2>
                </div>

                <div className="flex shrink-0 gap-8 px-6 py-4">
                    {contactData?.data && <Avatar user={contactData?.data} />}

                    <FormBuilder
                        formDefinition={formDefinition}
                        onFormAvailable={setForm}
                        defaultValues={formValues}
                        compact
                    />
                </div>
            </div>

            <ExternalEditControls
                form={form}
                toggleEditMode={toggleEditMode}
                contactId={contactId}
            />

            {process.env.NODE_ENV === 'development' && form?.control && <DevTool control={form?.control} />}
        </>
    )
}
