import React from 'react'

import type {MsTeamsUser} from '@bpinc/ad-directory-state-types'
import {getMsTeamsUserPrimaryPhoneNumber} from '@bpinc/ad-emi-helpers'
import type {OpenContactDetailsParams} from '@bpinc/ad-interaction-state-hooks'
import type {InitiateCall} from '@bpinc/ad-interaction-state-types'
import {useSessionState} from '@bpinc/ad-session-context'
import {useEvent} from '@bpinc/lib-helpers'
import {ButtonIcon, IconInformation, IconPhone16Filled, IconRoundChat16Filled} from '@bpinc/lib-jskit-ui'
import {assertDefined} from '@bpinc/lib-types-helpers'
import {PhoneOption} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import type {DropdownOption} from '../../shared/InteractionButtonIcon'
import {InteractionButtonIcon} from '../../shared/InteractionButtonIcon'
import type {OnChatParams} from '../types'


type Props = {
    user: MsTeamsUser
    transferMode?: 'messages' | 'calls'
    onCall?: (initiateCallParams: InitiateCall) => void
    onChat?: (user: OnChatParams) => void
    onDetails?: (user: OpenContactDetailsParams) => void
}

export const MsTeamsUserControls: React.FC<Props> = ({
    user,
    transferMode,
    onCall,
    onChat,
    onDetails,
}) => {

    const {CONTACT: i18n} = useLocalizationState('t')
    const {phoneDeviceCapabilities} = useSessionState()

    const primaryPhoneNumber = getMsTeamsUserPrimaryPhoneNumber(user.phones)
    const callOptions = Object.values(user.phones).map<DropdownOption>(phoneData => ({
        key: phoneData.phone,
        type: phoneData.type.toUpperCase(),
        value: phoneData.phone,
    }))

    const showCallButton = transferMode !== 'calls' && phoneDeviceCapabilities.canDial && !!onCall
    const showChatButton = transferMode !== 'messages' && user.hasChat && !!onChat

    const handleCall = useEvent((phone: string) => {
        assertDefined(onCall)
        onCall({
            phone,
            userId: user.userId,
            firstName: user.firstName,
            lastName: user.lastName,
            origination: 'directory',
        })
    })

    const handleChat = useEvent(() => {
        assertDefined(onChat)
        onChat({
            userId: user.userId,
            firstName: user.firstName,
            lastName: user.lastName,
            userType: user.userType,
            phone: primaryPhoneNumber ?? '',
            photoUrl: user.photoUrl,
        })
    })

    const handleDetails = useEvent(() => {
        onDetails?.({userId: user.userId})
    })

    return (
        <div className="flex gap-0.5">
            {showCallButton && (
                <InteractionButtonIcon
                    data-testid="call-button"
                    items={callOptions}
                    tooltip={i18n.button_start_call_text()}
                    OptionComponent={PhoneOption}
                    onPressed={handleCall}
                >
                    <IconPhone16Filled />
                </InteractionButtonIcon>
            )}
            {showChatButton && (
                <ButtonIcon
                    onClick={handleChat}
                    tooltip={i18n.button_initiate_chat_text()}
                    data-testid="chat-button"
                >
                    <IconRoundChat16Filled />
                </ButtonIcon>
            )}
            <ButtonIcon
                tooltip={i18n.button_show_contact_details_text()}
                data-testid="user-info-button"
                onPressed={handleDetails}
            >
                <IconInformation />
            </ButtonIcon>
        </div>
    )
}
