import React from 'react'

import {useIntegrationApiStateMethods} from '@bpinc/ad-integration-api-state-context'
import type {InteractionAssociatedObject} from '@bpinc/ad-interaction-state-types'
import {useEvent} from '@bpinc/lib-helpers'
import {tcn} from '@bpinc/lib-tailwind-classnames'


type Props = {
    crmObject: InteractionAssociatedObject
}

export const CrmObjectListItem: React.FC<Props> = ({crmObject}) => {

    const {openRecord} = useIntegrationApiStateMethods()
    const {displayName} = crmObject

    const showScreenPopOnClick = useEvent(() => {
        openRecord(
            crmObject,
            undefined,
            {inNewTab: false, source: 'user'},
        )
    })

    return (
        <button
            data-testid="crm-object-item-button"
            onClick={showScreenPopOnClick}
            className={tcn(
                'cursor-pointer whitespace-nowrap caption-s',
                'text-primary-on-primary hover:text-primary-link',
            )}
        >
            {displayName}
        </button>
    )
}
