import {useContact} from '@bpinc/ad-contacts-state-context'
import {useFormatting} from '@bpinc/ad-formatting-context'
import {useInteraction} from '@bpinc/ad-interaction-state-context'
import {
    getContactId,
    getGlobalInteractionId,
    getPhoneFromItem,
    getServiceId,
    getServiceNameFromItem,
} from '@bpinc/ad-interaction-state-methods'
import {AppRoutes, useNavigationState} from '@bpinc/ad-navigation-state-context'
import {useAuthorizedSession} from '@bpinc/ad-session-context'
import {useTimezonesState} from '@bpinc/ad-timezones-state-context'
import type {CalendarContact, CalendarEventDetails} from '@bpinc/cal-request-urls'
import {useCalendarStateMethods} from '@bpinc/cal-state-context'
import {getUserTimezone, useEvent} from '@bpinc/lib-helpers'
import {useInteractionId} from '@bpinc/lib-interaction-context'
import {IconAddEvent} from '@bpinc/lib-jskit-ui'
import {assertDefined, withoutUndefinedKeys} from '@bpinc/lib-types-helpers'
import {uuid} from '@bpinc/lib-uuid'
import {useLocalizationState} from '@bpinc/state-localization-context'

import type {MoreOption} from './useMoreButtons'


export const useScheduleReminderOption = () => {
    const {GLOBAL: i18n} = useLocalizationState('t')
    const {localTimezone} = useTimezonesState()
    const {selectEvent} = useCalendarStateMethods()
    const {userId, firstName, lastName} = useAuthorizedSession()
    const interactionId = useInteractionId()
    const item = useInteraction(interactionId)
    const goTo = useNavigationState('goTo')
    const {formatFullName} = useFormatting()

    const contactId = getContactId(item)
    const contact = useContact(contactId)

    const itemPhoneNumber = getPhoneFromItem(item)

    const itemContactPhone = contact?.data
        ? contact.data.nonEmptyPhones?.find(phone => phone.phone === itemPhoneNumber)
        : undefined
    const itemContactData: CalendarContact | undefined = contact?.data ? {
        id: contactId,
        first_name: contact.data.firstName,
        last_name: contact.data.lastName,
        create: false,
        calendar_phone: itemContactPhone,
    } : undefined

    const userName = formatFullName(firstName, lastName)
    const timezone = localTimezone.length ? localTimezone : getUserTimezone()

    const handleScheduleReminderClick = useEvent(() => {
        assertDefined(item)

        const newCalendarEvent: CalendarEventDetails = withoutUndefinedKeys({
            _temporaryId: uuid(),
            startTime: Date.now(),
            endTime: hourAfter(Date.now()),
            userId,
            userName,

            number: itemPhoneNumber,
            email: item.displayData.email,
            name: item.displayData.displayName,
            serviceId: getServiceId(item),
            serviceName: getServiceNameFromItem(item),
            globalInteractionId: getGlobalInteractionId(item),

            // screenpop: ???,
            screenpopSize: 0,

            listLinkId: item.workitemData?.listLinkId,
            listRecordId: item.workitemData?.listRecordId,

            assignees: [{
                userId,
                displayText: userName,
                type: 'user',
            }],
            contacts: itemContactData ? [itemContactData] : [],

            allDay: false,
            completed: false,
            remindIn: 10,
            remindInUnit: 'MINUTES',
            timezone,
            title: '',
        })

        // TODO: set interaction.disposition.calendarEntryExists = true
        goTo(AppRoutes.calendar)
        selectEvent(newCalendarEvent)
    })


    return {
        onClick: handleScheduleReminderClick,
        tooltip: i18n.button_followUpActivity_tooltip(),
        'data-testid': 'schedule-interaction-event-button',
        Icon: IconAddEvent,
    } satisfies MoreOption
}

function hourAfter(timestamp: number): number {
    return timestamp + 3_600_000
}
