import React, {useCallback} from 'react'

import {SmForm, SmInput, SmTextArea} from '@bpinc/lib-external-ui-elements'
import {IconSuccess} from '@bpinc/lib-icons'
import {useInteractionId} from '@bpinc/lib-interaction-context'
import {Loader, SemanticButton} from '@bpinc/lib-ui-elements'
import type {RescheduleForm as RescheduleFormType} from '@bpinc/state-interaction-ui'
import {useInteractionUIState} from '@bpinc/state-interaction-ui'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {useCurrentDatesInitializer} from '../hooks/useCurrentDatesInitializer'
import {useFavoriteTimeZoneInitializer} from '../hooks/useFavoriteTimeZoneInitializer'
import {useRescheduleFormState} from '../hooks/useRescheduleFormState'
import {ContactRecords} from './ContactRecords/ContactRecords'
import {CompletedCheckBox} from './FormControls/CompletedCheckBox'
import {Reminder} from './FormControls/Reminder'
import {RescheduleDateTimeBlock} from './FormControls/RescheduleDateTimeBlock'
import {TimeZoneDropdown} from './TimeZoneDropdown'


export const RescheduleForm = () => {
    const {t} = useLocalizationState()
    const {
        selectedTimezoneId,
        remindIn,
        remindInUnit,
        completed,
        contacts,
        title,
        notes,
        submittingState,
        setSubmittingState,
        handlers: {
            handleDateChange,
            handleTimeZoneSelect,
            handleRemindAmountChange,
            handleRemindUnitsChange,
            handleCompletedCheckboxChange,
            handleTitleChange,
            handleCancelClick,
            handleSubmitClick,
            handleNotesChange,
        },
        contactHandlersFactories,
        ...dateTimeState
    } = useRescheduleFormState()

    const {setItemState} = useInteractionUIState()
    const itemId = useInteractionId()

    const setRescheduleFormState = useCallback(
        function setRescheduleFormStateCallback(
            rescheduleForm: Partial<RescheduleFormType>,
        ) {
            setItemState(itemId, state => ({
                ...state,
                rescheduleForm: {...state.rescheduleForm, ...rescheduleForm},
            }))
        },
        [itemId, setItemState],
    )

    const handleInitializeFavoriteTimeZone = useCallback(
        function handleInitializeFavoriteTimeZoneCallback(
            suggestedTimeZoneId: string,
        ) {
            setRescheduleFormState({selectedTimezoneId: suggestedTimeZoneId})
        },
        [setRescheduleFormState],
    )

    useCurrentDatesInitializer({setRescheduleFormState, dateTimeState})
    useFavoriteTimeZoneInitializer({
        additionalOnInitializeHandler: handleInitializeFavoriteTimeZone,
    })


    if (submittingState === 'loading') {
        return <Loader active size="huge" color="primary" />
    }

    if (submittingState === 'success') {
        return <IconSuccess className="!absolute inset-0" />
    }

    return (
        <SmForm>
            <SmForm.Field
                control={SmInput}
                label={t.CALENDAR_EVENT_FORM.event_title()}
                placeholder={t.CALENDAR_EVENT_FORM.event_title()}
                value={title}
                onChange={handleTitleChange}
                required
            />
            <div className="flex justify-end">
                <SemanticButton primary onClick={handleSubmitClick}>
                    {t.CALENDAR_EVENT_FORM.button_save_text()}
                </SemanticButton>
                <SemanticButton onClick={handleCancelClick}>
                    {t.CALENDAR_EVENT_FORM.button_cancel_text()}
                </SemanticButton>
            </div>
            <RescheduleDateTimeBlock
                handleDateChange={handleDateChange}
                {...dateTimeState}
            />
            <TimeZoneDropdown
                label={t.CALENDAR_EVENT_FORM.label_timezone()}
                handleSelect={handleTimeZoneSelect}
                selectedTimeZone={selectedTimezoneId}
            />
            <Reminder
                amountValue={remindIn}
                unitsValue={remindInUnit}
                handleUnitsChange={handleRemindUnitsChange}
                handleAmountChange={handleRemindAmountChange}
            />
            <SmForm.Field
                onChange={handleNotesChange}
                value={notes}
                control={SmTextArea}
                label={t.CALENDAR_EVENT_FORM.label_note()}
            />
            <CompletedCheckBox
                onChange={handleCompletedCheckboxChange}
                checked={completed}
            />
            <ContactRecords
                contactHandlersFactories={contactHandlersFactories}
                contacts={contacts}
            />
        </SmForm>
    )
}
