import React from 'react'

import {SmIcon, SmMenu} from '@bpinc/lib-external-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import styles from '../styles/InteractionsPage.module.less'


interface PaginationProps {
    goNextPage: () => void
    goPreviousPage: () => void
    showPrev: boolean
    showNext: boolean
    prevAmount: number
    nextAmount: number
}

export const Pagination: React.FC<PaginationProps> = props => {
    const {t} = useLocalizationState()
    const {goNextPage, goPreviousPage, showNext, showPrev, prevAmount, nextAmount} = props

    return (
        <SmMenu.Item className={styles.pagination}>
            <div className={styles.paginationButton}>
                {showPrev && (
                    <p
                        onClick={goPreviousPage}
                        className="aria-paginate-prev-interaction-items"
                        tabIndex={0}
                        role="button"
                        data-testid="pagination-prev-button"
                    >
                        <SmIcon name="chevron left" />
                        {' '}
                        {t.UNDEFINED.bulk_button_previous()}
                        {' '}
                        {prevAmount}
                    </p>
                )}
            </div>
            <div className={styles.paginationButton}>
                {showNext && (
                    <p
                        onClick={goNextPage}
                        className="aria-paginate-next-interaction-items"
                        tabIndex={0}
                        role="button"
                        data-testid="pagination-next-button"
                    >
                        {t.UNDEFINED.bulk_button_next()}
                        {' '}
                        {nextAmount}
                        {' '}
                        <SmIcon name="chevron right" />
                    </p>
                )}
            </div>
        </SmMenu.Item>
    )
}
