import React, {useEffect} from 'react'

import {useAppConfigState} from '@bpinc/ad-app-config-state-context'
import {useSessionState} from '@bpinc/ad-session-context'
import {useEvent} from '@bpinc/lib-helpers'
import {Notifications, useNotificationsState} from '@bpinc/lib-notifications-context'
import {useLocalizationState} from '@bpinc/state-localization-context'


const reloadPage = () => location.reload()

export function useOutdatedClientNotification() {
    const {showNotification, hideNotificationById} = useNotificationsState('methods')
    const isClientOutdated = useAppConfigState('isClientOutdated')
    const isLoggedIn = useSessionState('isLoggedIn')
    const {GLOBAL: i18n} = useLocalizationState('t')

    const updateNotification = useEvent(() => {
        if (!isLoggedIn) {
            return
        }
        if (isClientOutdated) {
            showNotification({
                type: 'error',
                id: Notifications.OutdatedClientAppError,
                message: (
                    <>
                        <span>{i18n.reload_confirm_new_msg_part1()}</span>
                        <a href="#" onClick={reloadPage}>
                            {i18n.reload_confirm_action_button_text()}
                        </a>
                        <span>{i18n.reload_confirm_new_msg_part2()}</span>
                    </>
                ),
            })
        } else {
            hideNotificationById(Notifications.OutdatedClientAppError)
        }
    })

    useEffect(() => {
        updateNotification()
        // track only isClientOutdated
    }, [isClientOutdated, updateNotification])
}
