import React, {useMemo, useRef, useState} from 'react'

import {useGetAvatarUrl} from '@bpinc/ad-contacts-state-hooks'
import {useFormatting} from '@bpinc/ad-formatting-context'
import {useSessionState} from '@bpinc/ad-session-context'
import {getUserStatusIcon, useUserDetailsById} from '@bpinc/ad-user-details-state-context'
import {ContactListItem} from '@bpinc/lib-contact-list-item-ui'
import {DirectoryFavoriteButton} from '@bpinc/lib-favorites-button'
import {useEvent} from '@bpinc/lib-helpers'
import {IconPhone16Filled} from '@bpinc/lib-jskit-ui'
import {highlighted, SelectionItem} from '@bpinc/lib-ui-elements'

import type {InternalUserProps} from '../types'
import {isSearchUser} from '../types'
import {InternalUserControls} from './InternalUserControls'
import {UserOnlineStatus} from './UserOnlineStatus'


export const InternalUser: React.FC<InternalUserProps> = ({
    transferMode,
    transferNumberInput,
    transferControlsStyle,
    user,
    onCall,
    onChat,
    onEmail,
    onDetails,
    highlight,
    onClick,
    onDoubleClick,
    showFavoriteButton = true,
    ...other
}) => {

    const {tenantFeatures} = useSessionState()
    const {formatFullName} = useFormatting()
    const rtUserDetails = useUserDetailsById(user.userId)

    const mountTimestamp = useRef<number>(Date.now())
    const details = isSearchUser(user)
        ? {data: user, lastUpdated: mountTimestamp.current}
        : rtUserDetails

    const [controlsPopupOpen, setControlsPopupOpen] = useState(false)

    const getAvatarUrl = useGetAvatarUrl()
    const userStatusIcon = getUserStatusIcon(details?.data)
    const presenceState = details?.data?.presenceState
    const isOnline = presenceState === 'available'

    const onlineStatusIcon = useMemo(() => {
        let icon = userStatusIcon

        if (!tenantFeatures?.rtDataEnabled && userStatusIcon && userStatusIcon !== 'loggedOut') {
            icon = 'ready'
        }
        return icon
    }, [userStatusIcon, tenantFeatures?.rtDataEnabled])

    const avatar = useMemo(() => ({
        firstName: user.firstName,
        lastName: user.lastName,
        url: getAvatarUrl({
            userId: user.userId,
            photoUrl: 'photoId' in user ? user.photoId : undefined,
        }),
        isAgent: true,
        isOnline,
        onlineStatusIcon,
    }), [user, isOnline, onlineStatusIcon, getAvatarUrl])

    const title = useMemo(() => (
        <span className="flex gap-1">
            <b data-testid="name" className="inline-block truncate font-semibold body text-primary-on-primary">
                {highlighted({
                    highlight,
                    text: formatFullName(user.firstName, user.lastName),
                })}
            </b>
            {showFavoriteButton && (
                <DirectoryFavoriteButton forContactListItem item={user} />
            )}
        </span>
    ), [formatFullName, highlight, showFavoriteButton, user])

    const body = (
        <span className="flex items-center gap-1">
            <IconPhone16Filled className="my-px mr-1 inline-block shrink-0 icon-neutral" />
            <span data-testid="phone" className="truncate caption-s text-neutral-on-secondary">
                {highlighted({highlight, text: user.extension})}
            </span>
        </span>
    )

    const stats = <UserOnlineStatus details={details} />

    const controls = (
        <InternalUserControls
            user={user}
            transferMode={transferMode}
            onPopupOpenChange={setControlsPopupOpen}
            onChat={onChat}
            onCall={onCall}
            onEmail={onEmail}
            onDetails={onDetails}
        />
    )

    const handleChat = useEvent((e: React.MouseEvent) => {
        e.preventDefault()
        e.stopPropagation()
        onChat?.({
            userId: user.userId,
            firstName: user.firstName,
            lastName: user.lastName,
            phone: user.extension,
        })
    })

    const handleClick = useEvent((e: React.MouseEvent) => {
        onClick?.(e, user)
    })
    const handleDoubleClick = useEvent(() => {
        onDoubleClick?.(user)
    })

    return (
        <ContactListItem
            as={SelectionItem}
            keyHandlerElement="directory-item"
            transferMode={transferMode}
            transferNumberInput={transferNumberInput}
            transferControlsStyle={transferControlsStyle}
            userId={user.userId}
            number={user.extension}
            avatarProps={avatar}
            title={title}
            body={body}
            stats={stats}
            controls={controls}
            onClick={onClick ? handleClick : handleChat}
            onDoubleClick={onDoubleClick && handleDoubleClick}
            containerProps={{
                itemId: user.userId,
                focusOnSelection: true,
                tabIndex: 0,
                'data-testid': 'directory-item',
                'data-internal-user': 'true',
            }}
            forceHover={controlsPopupOpen}
            {...other}
        />
    )
}
