import {useCallback, useMemo} from 'react'

import {useContactsState, useContactsStateMethods} from '@bpinc/ad-contacts-state-context'


export function useExternalContacts() {
    const {getContactList} = useContactsStateMethods()
    const {
        contactList: externalContacts,
        contactListLoading: externalContactsLoading,
    } = useContactsState(['contactListLoading', 'contactList'])

    const getExternalContactsByQuery = useCallback(
        function getExternalContactsByQueryCallback(searchQuery: string) {
            return getContactList({query: `phone: ${searchQuery}*`})
        },
        [getContactList],
    )

    return useMemo(
        function memoizeOutput() {
            return {
                externalContacts,
                externalContactsLoading,
                getExternalContactsByQuery,
            }
        },
        [externalContacts, externalContactsLoading, getExternalContactsByQuery],
    )
}
