import {useContactsState} from '@bpinc/ad-contacts-state-context'
import {useFormatting} from '@bpinc/ad-formatting-context'
import type {CallParty} from '@bpinc/ad-interaction-state-types'
import type {ExternalChatParty} from '@bpinc/ad-transport-types'

import type {ParticipantItem} from './types'
import {useDisplayNameParticipant} from './useDisplayNameParticipant'


export const MAX_PARTICIPANTS_COUNT = 3

type Parties = {
    callParties?: CallParty[]
    chatParties?: ExternalChatParty[]
}

export function getParticipantsListData({callParties, chatParties}: Parties) {
    if (callParties?.length) {
        return callParties?.map(party => ({
            id: party.id,
            name: party.name,
            phone: party.phone,
            firstName: party.firstName,
            lastName: party.lastName,
            userId: party.userId,
            contactId: party.contactId,
            party: party,
        }))
    }
    return chatParties?.map(party => ({
        id: party.party_id,
        phone: party.phone,
        itemId: party.item_id,
        firstName: party.first_name,
        lastName: party.last_name,
        userId: party.user_id,
        contactId: party.contact_id,
        email: party.email,
        party: party,
    }))
}

export const useContacts = (ids: string[] | undefined) => {
    const {formatFullName} = useFormatting()
    const {contacts} = useContactsState()
    return ids?.map(id => formatFullName(contacts[id]?.data?.firstName, contacts[id]?.data?.lastName))
}

export const useDisplayNamesParticipants = (participantItems?: ParticipantItem[], isChat?: boolean) => {
    const data = participantItems?.slice(0, MAX_PARTICIPANTS_COUNT)
    const contactIds = data?.map(({contactId}) => contactId).filter(id => id !== undefined)
    const contacts = useContacts(contactIds as string[])
    const getDisplayName = useDisplayNameParticipant({isChat})
    return data?.map((item, i) => getDisplayName(item, contacts?.[i]).displayName).join(', ')
}
