import React, {type FC} from 'react'
import {type ButtonProps, Button} from '@servicepattern/ui'

import {tcn} from '@bpinc/lib-tailwind-classnames'
import {useLocalizationState} from '@bpinc/state-localization-context'


export const EndChatButton: FC<ButtonProps> = ({className, ...props}) => {
    const {t} = useLocalizationState()

    return (
        <Button
            variant="neutral"
            size="xs"
            className={tcn(
                'rounded-sm border-action',
                'text-on-error',
                'ml-2 px-2 py-1',
                'h-6',
                className,
            )}
            data-testid="complete-button"
            {...props}
        >
            {t.INTERACTIONS.end()}
        </Button>
    )
}
