import type {RefObject} from 'react'
import React, {useMemo} from 'react'
import cn from 'classnames'

import type {ActivityVoice, Recordings} from '@bpinc/ad-case-state-types'
import {useGetAvatarUrl} from '@bpinc/ad-contacts-state-hooks'
import {useFormatting} from '@bpinc/ad-formatting-context'
import {formatDate} from '@bpinc/ad-formatting-helpers'
import {ContactListItem} from '@bpinc/lib-contact-list-item-ui'
import {SmImage} from '@bpinc/lib-external-ui-elements'
import {AudioPlayer} from '@bpinc/lib-ui-elements'

import iconPhone from '../images/phone.svg'
import {getNameFromParty} from '../utils'
import {Disposition} from './Disposition'
import {Info} from './Info'


interface Props {
    activity: ActivityVoice
    recordings?: Recordings
    scrollRef?: RefObject<HTMLDivElement>
}

export const VoicePreview: React.FC<Props> = ({activity, recordings, scrollRef}) => {
    const {formatFullName} = useFormatting()
    const getAvatarUrl = useGetAvatarUrl()
    const {direction, createdTime, parties, services} = activity

    const partyContact = parties.find(p => p.partyType === 'CONTACT')
    const partyInternal = parties.find(p => p.partyType === 'USER')
    const [contactFirstName, contactLastName] = getNameFromParty(partyContact)
    const [agentFirstName, agentLastName] = getNameFromParty(partyInternal)
    const contactName = formatFullName(contactFirstName, contactLastName)
    const agentName = formatFullName(agentFirstName, agentLastName)
    const isOutbound = direction === 'OUTBOUND'

    const avatar = useMemo(() => {
        return {
            firstName: isOutbound ? agentFirstName : contactFirstName,
            lastName: isOutbound ? agentLastName : contactLastName,
            url: getAvatarUrl({
                userId: isOutbound ? partyInternal?.userId : undefined,
                contactId: isOutbound ? undefined : partyContact?.contactId,
            }),
            isAgent: isOutbound,
        }
    }, [
        agentFirstName,
        agentLastName,
        contactFirstName,
        contactLastName,
        getAvatarUrl,
        isOutbound,
        partyContact,
        partyInternal,
    ])
    const title = isOutbound ? agentName : contactName
    const noteContentClassName = cn('relative inline-block bg-[--c-note-content-bg] py-2 pl-2 pr-6',
        'after:absolute after:bottom-0 after:right-0 after:h-0 after:w-0 after:border-[8px]',
        'after:border-b-white after:border-l-[--c-note-border] after:border-r-white after:border-t-[--c-note-border]')
    const body = (
        <span className="relative -top-1 text-[0.8em]">
            <SmImage src={iconPhone} />
            {formatDate(createdTime)}
        </span>
    )

    return (
        <div>
            <ContactListItem
                as="div"
                avatarProps={avatar}
                title={title}
                body={body}
                isAlignedToRight={isOutbound}
                isFitted
            />
            <div className="my-2 rounded bg-note-bg p-2">
                {isOutbound && (
                    <Info title="To" size="small">
                        {contactName}
                    </Info>
                )}
                {recordings && scrollRef?.current ? (
                    recordings.map((recording: string) => {
                        return (
                            <AudioPlayer
                                key={recording}
                                src={recording}
                                agentName={agentName}
                            />
                        )
                    })
                ) : (
                    <Info title="Handled by" size="small">
                        {agentName}
                    </Info>
                )}
            </div>
            {partyInternal?.notes && <div className={cn(noteContentClassName, 'mb-2')}>{partyInternal.notes}</div>}
            {partyInternal?.disposition && <Disposition data={partyInternal} type="call" services={services} />}
        </div>
    )
}
