import React from 'react'

import type {FormDefinitionPageItem} from '@bpinc/ad-request-urls'
import {getFormElementStyle} from '@bpinc/lib-form-builder'
import {Spacer} from '@bpinc/lib-form-builder-ui'


export const SpacerField: React.FC<{
    pageItem: FormDefinitionPageItem
}> = ({
    pageItem,
}) => {
    const {height} = getFormElementStyle(pageItem)

    return <Spacer height={height} />
}
