import {useEffect} from 'react'

import {
    useIsOffline,
    useOfflineErrorMessages,
    useOfflineStateDataShowOfflineAlert,
    useOfflineStateMethods,
} from '@bpinc/ad-offline-state-context'
import {Notifications, useNotificationsState} from '@bpinc/lib-notifications-context'


export function useOfflineNotification() {
    const {showNotification} = useNotificationsState('methods')
    const {setShowOfflineAlert} = useOfflineStateMethods()
    const showOfflineAlert = useOfflineStateDataShowOfflineAlert()
    const offlineErrorMessages = useOfflineErrorMessages()
    const isOffline = useIsOffline()

    useEffect(() => {
        if (showOfflineAlert) {
            showNotification({
                type: 'error',
                id: Notifications.OfflineStateError,
                content: offlineErrorMessages.isOffline,
                closable: true,
                duration: 5000,
                onHide: () => setShowOfflineAlert(false),
            })
        }
    }, [
        offlineErrorMessages.isOffline,
        setShowOfflineAlert,
        showNotification,
        showOfflineAlert,
    ])

    useEffect(() => {
        if (!isOffline) {
            setShowOfflineAlert(false)
        }
    }, [isOffline, setShowOfflineAlert])
}
