import React from 'react'

import {SelectField} from '@bpinc/lib-jskit-ui'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {useHandleChange} from './useHandleChange'
import {useOptions} from './useOptions'


interface TimeZoneProps {
    selectedTimeZone: string
    handleSelect: (timezoneId: string) => void
    label?: string
    layoutClassName?: string
}

export const SHOW_MORE_TIMEZONES = 'show_more'

export const TimeZoneDropdown: React.FC<TimeZoneProps> = ({
    selectedTimeZone,
    handleSelect,
    layoutClassName,
    label,
}) => {
    const {t} = useLocalizationState()
    const options = useOptions()
    const handleChange = useHandleChange({handleSelect})

    return (
        <SelectField
            required
            layoutClassName={layoutClassName}
            value={selectedTimeZone}
            label={label}
            onChange={handleChange}
            aria-label={t.CALENDAR_EVENT_FORM.show_timezones_label()}
            options={options}
            data-testid="timezone-dropdown"
        />
    )
}
