import {useEffect} from 'react'
import {identity} from 'lodash-es'

import {addTime, formatDate} from '@bpinc/lib-helpers'
import type {RescheduleForm as RescheduleFormType} from '@bpinc/state-interaction-ui'

import {dateFormat, timeFormat} from './useRescheduleFormState'


interface UseCurrentDatesInitializerParams {
    setRescheduleFormState: (rescheduleForm: Partial<RescheduleFormType>) => void
    dateTimeState: { dateFrom: string; dateTo: string; timeFrom: string; timeTo: string }
}

export function useCurrentDatesInitializer({setRescheduleFormState, dateTimeState}: UseCurrentDatesInitializerParams) {
    useEffect(() => {
        const datesInitialized = Object.values(dateTimeState).every(identity)
        if (!datesInitialized) {
            initializeDates()
        }

        function initializeDates() {
            const dateFrom = formatDate(new Date(), dateFormat)
            const timeFrom = formatDate(new Date(), timeFormat)
            const dateTo = formatDate(addTime(new Date(), 'hours', 1), dateFormat)
            const timeTo = formatDate(addTime(new Date(), 'hours', 1), timeFormat)

            setRescheduleFormState({dateFrom, timeFrom, dateTo, timeTo})
        }
    }, [dateTimeState, setRescheduleFormState])

}
