import {useMemo} from 'react'

import {useFormatting} from '@bpinc/ad-formatting-context'
import type {Item} from '@bpinc/ad-interaction-state-types'


export function useInteractionBarDisplayName(activeItem: Item | null) {
    const {formatFullName} = useFormatting()

    const {
        displayData: {displayName, firstName = '', lastName = ''},
    } = activeItem ?? {displayData: {}}

    return useMemo(
        function memoizeOutput() {
            if (!activeItem) {
                return null
            }

            return firstName && lastName
                ? formatFullName(firstName, lastName)
                : displayName

        },
        [activeItem, displayName, firstName, formatFullName, lastName],
    )
}
