import React, {useCallback, useId, useRef, useState} from 'react'
import {tw} from '@servicepattern/ui'

import {useAsRef} from '@bpinc/lib-helpers'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import type {DnDParams} from '@bpinc/lib-ui-elements'
import {Draggable, DragItemTypes} from '@bpinc/lib-ui-elements'


export const InteractionsListResizer = React.memo<{
    height: number
    onResize: (height: number) => void
    min?: number
    onReset: () => void
}>(
        function InteractionsListResizer({
            height,
            min = 0,
            onResize,
            onReset,
        }) {
            const id = useId()
            const startHeightRef = useRef<number | undefined>(height)
            const minRef = useAsRef(min)
            const [isDrag, setIsDrag] = useState(false)

            const onDragStart = useCallback(
                function onDragStartCallback() {
                    startHeightRef.current = height
                    setIsDrag(true)
                },
                [setIsDrag, height],
            )

            const onDrag = useCallback(
                function onDragCallback({offset, initialOffset}: DnDParams) {
                    if (startHeightRef.current) {
                        const diff = (offset?.y ?? 0) - (initialOffset?.y ?? 0)
                        const newHeight = Math.max(minRef.current, startHeightRef.current + diff)

                        onResize(newHeight)
                    }
                },
                [minRef, onResize],
            )

            const onDrop = useCallback(
                function onDropCallback({offset, initialOffset}: DnDParams) {
                    if (startHeightRef.current) {
                        const diff = (offset?.y ?? 0) - (initialOffset?.y ?? 0)
                        const newHeight = Math.max(minRef.current, startHeightRef.current + diff)

                        onResize(newHeight)
                        startHeightRef.current = newHeight
                    }
                    setIsDrag(false)
                },
                [minRef, onResize],
            )

            return (
                <Draggable
                    className={tcn(
                        tw`-mx-1 my-2 h-2 cursor-row-resize`,
                        'bg-neutral-200 hover:bg-neutral-300',
                        {'bg-neutral-300': isDrag},
                    )}
                    previewClassName="absolute w-0 h-0 invisible opacity-0 p-1"
                    type={DragItemTypes.RESIZE_TABLE_HEIGHT}
                    onDragStart={onDragStart}
                    onDrag={onDrag}
                    onDrop={onDrop}
                    onDoubleClick={onReset}
                    id={id}
                />
            )
        },
        )
