import React from 'react'
import {useContext} from 'use-context-selector'

import type {FormDefinitionFieldImage, FormDefinitionPageItem} from '@bpinc/ad-request-urls'
import {getFormElementStyle, getPageItemCSSProperties} from '@bpinc/lib-form-builder'
import {Image} from '@bpinc/lib-form-builder-ui'

import {FormBuilderContext} from '../../FormBuilder'


export const ImageField: React.FC<{
    pageItem: FormDefinitionPageItem
    field: FormDefinitionFieldImage
}> = ({
    pageItem,
    field,
}) => {
    const {
        form,
        view,
    } = useContext(FormBuilderContext)

    if (view) {
        const viewValue = form.getValues()[field.name || '']
        const viewStyle = getPageItemCSSProperties(pageItem.style)
        const style = getFormElementStyle(pageItem)

        if (!viewValue) {
            return null
        }

        return (
            <Image
                style={{...viewStyle, ...style}}
                value={viewValue}
            />
        )
    }

    // TODO: Implement ImageField
    return null
}
