import type {SyntheticEvent} from 'react'
import React from 'react'

import type {SmInputProps} from '@bpinc/lib-external-ui-elements'
import {SmForm} from '@bpinc/lib-external-ui-elements'
import {DateInput, TimeInput} from '@bpinc/lib-semantic-ui-date-time-pickers'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {useLocalizationState} from '@bpinc/state-localization-context'


interface RescheduleDateTimeBlockProps {
    dateFrom: string
    dateTo: string
    timeFrom: string
    timeTo: string
    handleDateChange: (e: SyntheticEvent, {value, name}: SmInputProps) => void
}

export const RescheduleDateTimeBlock: React.FC<RescheduleDateTimeBlockProps> = ({dateFrom, dateTo, timeFrom, timeTo, handleDateChange}) => {
    const {t} = useLocalizationState()
    const smallPickerClassName = tcn('w-1/4 [&_input]:w-full')
    return (
        <>
            <SmForm.Field>
                <label>{t.GLOBAL.from()}</label>
                <SmForm.Group>
                    <SmForm.Field>
                        <label>{t.CALENDAR_EVENT_FORM.label_date()}</label>
                        <DateInput
                            required
                            value={dateFrom}
                            onChange={handleDateChange}
                            placeholder="DD/MM/YYYY"
                            closable
                            animation="fade right"
                            popupPosition="bottom left"
                            localization={navigator.language}
                            name="dateFrom"
                            aria-label={t.CALENDAR_EVENT_FORM.date_from_label()}
                        />
                    </SmForm.Field>
                    <SmForm.Field>
                        <label>{t.CALENDAR_EVENT_FORM.label_time()}</label>
                        <TimeInput
                            required
                            value={timeFrom}
                            name="timeFrom"
                            onChange={handleDateChange}
                            className={smallPickerClassName}
                            placeholder="HH:MM"
                            closable
                            animation="fade right"
                            popupPosition="bottom right"
                            localization={navigator.language}
                            aria-label={t.CALENDAR_EVENT_FORM.time_from_label()}
                        />
                    </SmForm.Field>
                </SmForm.Group>
            </SmForm.Field>
            <SmForm.Field>
                <label>{t.GLOBAL.to()}</label>
                <SmForm.Group>
                    <SmForm.Field>
                        <label>{t.CALENDAR_EVENT_FORM.label_date()}</label>
                        <DateInput
                            required
                            value={dateTo}
                            name="dateTo"
                            onChange={handleDateChange}
                            placeholder="DD/MM/YYYY"
                            closable
                            animation="fade right"
                            popupPosition="bottom left"
                            localization={navigator.language}
                            aria-label={t.CALENDAR_EVENT_FORM.date_to_label()}
                        />
                    </SmForm.Field>
                    <SmForm.Field>
                        <label>{t.CALENDAR_EVENT_FORM.label_time()}</label>
                        <TimeInput
                            required
                            value={timeTo}
                            name="timeTo"
                            onChange={handleDateChange}
                            className={smallPickerClassName}
                            placeholder="HH:MM"
                            closable
                            animation="fade right"
                            popupPosition="bottom right"
                            localization={navigator.language}
                            aria-label={t.CALENDAR_EVENT_FORM.time_to_label()}
                        />
                    </SmForm.Field>
                </SmForm.Group>
            </SmForm.Field>
        </>
    )
}
