import type {Context} from 'react'
import React, {forwardRef, useMemo} from 'react'
import * as RDialog from '@radix-ui/react-dialog'

import {slidingPanelStyles} from './styles'
import type {SlidingPanelContextType, SlidingPanelProps} from './types'


export let SlidingPanelContext: Context<SlidingPanelContextType>

export const SlidingPanel = forwardRef<HTMLDivElement, SlidingPanelProps>(function SlidingPanel({
    children,
    onClose,
    isOpen = true,
    closeOnClickOutside = true,
    closeOnEscape = true,
    autoFocus = true,
    modal,
    className,
    ...props
}, ref) {
    const {base, overlay} = slidingPanelStyles()

    if (!SlidingPanelContext) {
        SlidingPanelContext = React.createContext({onClose})
    }

    const contextValue = useMemo(() => ({onClose}), [onClose])

    return (
        <SlidingPanelContext.Provider value={contextValue}>
            <RDialog.Root
                open={isOpen}
                modal={modal}
                onOpenChange={open => {
                    if (!open) {
                        onClose()
                    }
                }}
            >
                <RDialog.Overlay className={overlay()} />
                <RDialog.Content
                    className={base({className, modal})}
                    {...props}
                    ref={ref}
                    onOpenAutoFocus={e => {
                        !autoFocus && e.preventDefault()
                    }}
                    onEscapeKeyDown={e => {
                        if (closeOnEscape) {
                            onClose()
                            e.stopPropagation()
                        }
                    }}
                    onPointerDownOutside={e => {
                        !closeOnClickOutside && e.preventDefault()
                    }}
                >
                    {children}
                </RDialog.Content>
            </RDialog.Root>
        </SlidingPanelContext.Provider>
    )
})
