import type {FC} from 'react'
import React, {useCallback} from 'react'
import {Button} from '@servicepattern/ui'

import type {MsTeamsChannel} from '@bpinc/ad-directory-state-types'
import {useActivityHistoryOpener} from '@bpinc/ad-interaction-state-hooks'
import {useSessionState} from '@bpinc/ad-session-context'
import {AppRoutes, useNavigationState} from '@bpinc/lib-communicator-navigation-state'
import {IconChat} from '@bpinc/lib-jskit-ui'
import {assertDefined} from '@bpinc/lib-types-helpers'
import {useLocalizationState} from '@bpinc/state-localization-context'


interface Props {
    user: MsTeamsChannel
}
export const MsTeamsChannelControls: FC<Props> = ({user}) => {
    const {t} = useLocalizationState()
    const setActivePage = useNavigationState('setActivePage')
    const {userId: selfUserId} = useSessionState()
    const openActivityHistory = useActivityHistoryOpener()

    const handleChat = useCallback(() => {
        assertDefined(selfUserId)
        const {channelId, title} = user
        openActivityHistory({
            userId: channelId,
            firstName: title,
            origination: 'contact-profile',
        })
        setActivePage({name: AppRoutes.Interactions})
    }, [selfUserId, user, openActivityHistory, setActivePage])

    return (
        <div className="-mt-1 ml-auto flex flex-none items-center">
            <Button
                onClick={handleChat}
                tooltip={t.CONTACT.button_initiate_chat_text()}
                data-testid="chat-button"
                icon={<IconChat />}
                variant="ghost"
            />
        </div>
    )
}
