import {useMemo} from 'react'
import {omit, remove} from 'lodash-es'

import type {
    ContactDetailsType,
    ServerAddressType,
    ServerEmailType,
    ServerPhoneType,
} from '@bpinc/ad-contacts-state-types'
import {useFormatting} from '@bpinc/ad-formatting-context'
import {formatDate} from '@bpinc/ad-formatting-helpers'
import {STATES} from '@bpinc/ad-location-data'
import type {ExternalChatChannelType} from '@bpinc/ad-transport-types'
import type {ButtonElement, FormElement, ImageElement, MessengersElement} from '@bpinc/lib-form-builder'
import {parseMessengers} from '@bpinc/lib-forms-base'
import {assertDefined} from '@bpinc/lib-types-helpers'
import {useGetMessengerName} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import type {FormElementViewProps} from '../components/FormElementView'


export type Field = Pick<
    FormElementViewProps,
    'label' | 'value' | 'type' | 'image' | 'height' | 'button' | 'name' | 'options' | 'messenger'
>
type STATES_CODE = keyof typeof STATES

export const NON_FORKABLE_FIELDS = [
    'Contact.phones',
    'Contact.emails',
    'Contact.addresses',
    'Contact.messengers',
] as const

export type NonForkableFields = typeof NON_FORKABLE_FIELDS[number]

/*TODO: Refactor it:
    Separate merge functionality
*/
interface Params {
    elements: FormElement[]
    values: ContactDetailsType | undefined
    srcValues?: ContactDetailsType
}
export function useFormElementsToViews({elements, values, srcValues}: Params): Field[] {
    const {formatPhoneType, formatEmailType, formatAddressType, formatFullName} = useFormatting()
    const {t} = useLocalizationState()
    const getMessengerName = useGetMessengerName()

    const isMergeMode = !!srcValues && !!values
    return useMemo(
        function memoizeOutput() {
            let updatedElements = [...elements]
            const updatedValues = {...values}
            const updatedSrcValues = {...srcValues}

            if (isMergeMode) {
                mergeNamesInSingleField()
                createForkFields()
            }

            function mergeNamesInSingleField() {
                remove(updatedElements, el => {
                    return el.mapping === 'Contact.first_name' || el.mapping === 'Contact.last_name'
                })

                const fullName = formatFullName(values?.['Contact.first_name'], values?.['Contact.last_name'])

                updatedElements.unshift({
                    name: 'Contact.name',
                    mapping: 'Contact.name',
                    value: fullName,
                    label: t.CONTACT.full_name(),
                    type: 'text',
                })
                updatedValues['Contact.name'] = fullName

                if (srcValues) {
                    const newFullName = formatFullName(
                        updatedSrcValues?.['Contact.first_name'],
                        updatedSrcValues?.['Contact.last_name'],
                    )
                    updatedSrcValues['Contact.name'] = newFullName
                }
            }

            function createForkFields() {
                updatedElements = [...updatedElements].reduce<FormElement[]>((acc, element) => {
                    if (haveTwoElements()) {
                        const mapping = element.mapping
                        assertDefined(mapping)
                        addMergeField(updatedSrcValues[mapping], updatedValues[mapping])
                    } else {
                        acc.push(element)
                    }

                    return acc

                    function haveTwoElements() {
                        return (
                            element.mapping &&
                            updatedSrcValues?.[element.mapping] &&
                            !NON_FORKABLE_FIELDS.includes(element.mapping as NonForkableFields)
                        )
                    }

                    function addMergeField(srcValue: unknown, distValue: unknown) {
                        if (typeof srcValue !== 'string' || typeof distValue !== 'string') {
                            return
                        }

                        acc.push({
                            name: element.mapping,
                            mapping: element.mapping,
                            label: element.label,
                            type: 'radio',
                            options: [
                                {id: srcValue, name: srcValue},
                                {id: distValue, name: distValue},
                            ],
                        })
                    }
                }, [])
            }

            return updatedElements.reduce<Field[]>((fields, element) => {
                if (element.mapping) {
                    const value = updatedValues?.[element.mapping]
                    const srcValue = srcValues?.[element.mapping]
                    if (element.type === 'radio') {
                        fields.push({
                            type: 'radio',
                            name: element.mapping,
                            options: element.options,
                            label: element.label,
                        })
                    } else if (element.type === 'spacer') {
                        fields.push({
                            type: 'spacer',
                            height: element?.style?.height,
                            name: element.mapping,
                        })
                    } else if (element.type === 'button') {
                        const button = omit(element, ['viewStyle', 'editStyle', 'containerId'])
                        fields.push({
                            type: 'button',
                            button: button as ButtonElement,
                            name: element.mapping,
                        })
                    } else if (element.type === 'image') {
                        fields.push({
                            type: 'image',
                            image: element as ImageElement,
                            value: value as string | undefined,
                            name: element.mapping,
                        })
                    } else if (element.type === 'messengers') {
                        const messengersElem = element as MessengersElement
                        const messengers = parseMessengers(values?.[messengersElem.mapping])
                        messengers?.filter(m => m.type).forEach(messenger => {
                            fields.push({
                                type: 'messenger',
                                label: element.label || getMessengerName(messenger.type as ExternalChatChannelType),
                                messenger,
                            })
                        })
                    } else if (element.type === 'phoneList') {
                        const phones = (value || []) as ServerPhoneType[]
                        const srcPhones = (srcValue || []) as ServerPhoneType[]

                        ;[...srcPhones, ...phones]?.forEach(phoneItem => {
                            if (phoneItem.phone) {
                                fields.push({
                                    label: formatPhoneType(phoneItem.type),
                                    value: phoneItem.phone,
                                    type: 'number',
                                    name: element.mapping,
                                })
                            }
                        })
                    } else if (element.type === 'emailList') {
                        const emails = (value || []) as ServerEmailType[]
                        const srcEmails = (srcValue || []) as ServerEmailType[]

                        ;[...srcEmails, ...emails]?.forEach(emailItem => {
                            if (emailItem.email_address) {
                                fields.push({
                                    label: formatEmailType(emailItem.type),
                                    value: emailItem.email_address,
                                    type: 'email',
                                    name: element.mapping,
                                })
                            }
                        })
                    } else if (element.type === 'addressList') {
                        const addresses = (value || []) as ServerAddressType[]
                        const srcAddresses = (srcValue || []) as ServerAddressType[]

                        ;[...srcAddresses, ...addresses]?.forEach(item => {
                            fields.push({
                                label: formatAddressType(item.type),
                                value: [
                                    item.address_line1,
                                    item.address_line2,
                                    item.city,
                                    STATES[item.state as STATES_CODE],
                                    item.postcode,
                                ]
                                    .filter(Boolean)
                                    .join(', '),
                                name: element.mapping,
                            })
                        })
                    } else if (element.type === 'label' && element.value) {
                        fields.push({
                            label: element.value,
                            name: element.mapping,
                        })
                    } else if (!element.hideEmpty || (value && typeof value === 'string')) {
                        const typedValue = value as string | undefined

                        if (element.type === 'select') {
                            const optionValue = element?.options?.find(option => option.id === typedValue)?.name
                            element.label &&
                                fields.push({
                                    value: optionValue,
                                    label: element.label,
                                    name: element.mapping,
                                })
                        } else if (element.type === 'email') {
                            element.label &&
                                fields.push({
                                    type: 'email',
                                    value: typedValue,
                                    label: element.label,
                                    name: element.mapping,
                                })
                        } else if (element.type === 'link') {
                            fields.push({
                                type: 'link',
                                value: element.linkUrl,
                                label: element.value || element.linkUrl,
                                name: element.mapping,
                            })
                        } else if (element.type === 'dateTime') {
                            element.label &&
                                fields.push({
                                    label: element.label,
                                    value: typedValue && formatDate(typedValue, {birthday: true}),
                                    name: element.mapping,
                                })
                        } else {
                            element.label &&
                                fields.push({
                                    label: element.label,
                                    value: typedValue,
                                    name: element.mapping,
                                })
                        }
                    }
                }
                return fields
            }, [])
        },
        [
            elements,
            formatAddressType,
            formatEmailType,
            formatFullName,
            formatPhoneType,
            getMessengerName,
            isMergeMode,
            srcValues,
            t.CONTACT,
            values,
        ],
    )
}
