import {useMemo} from 'react'

import {useDirectoryState} from '@bpinc/ad-directory-state-context'
import type {DirectoryObject, SearchDirectoryCategory, SearchResult} from '@bpinc/ad-directory-state-types'
import {useSessionState} from '@bpinc/ad-session-context'

import {omitSelfInTeams} from '../../utils'
import type {DirectoryFolderType} from './helpers'
import {directoryFolderTypes} from './helpers'


interface UseDirectoryListParams {
    isSearchMode: boolean
    filteredCategories: DirectoryObject[]
    filteredSearchResults: SearchResult
}

export interface DirectoryListItem {
    type: DirectoryFolderType
    item: SearchDirectoryCategory
}

export function useDirectoryFoldersList({
    filteredCategories,
    isSearchMode,
    filteredSearchResults,
}: UseDirectoryListParams): DirectoryListItem[] {

    const {teams, emiCategories} = useDirectoryState(['teams', 'emiCategories'])
    const {teamId: selfTeamId} = useSessionState()

    return useMemo(
        function memoizeDirectoryList() {
            return isSearchMode
                ? getSearchResultsAsOneList(filteredSearchResults)
                : getItemsAsOneList(
                    omitSelfInTeams(teams, selfTeamId),
                    filteredCategories,
                    emiCategories,
                )
        },
        [
            filteredCategories,
            emiCategories,
            filteredSearchResults,
            isSearchMode,
            selfTeamId,
            teams,
        ],
    )
}

function sortByNameAsc(a: DirectoryListItem, b: DirectoryListItem): number {
    if (a.item.name.toLowerCase() > b.item.name.toLowerCase()) return 1
    if (a.item.name.toLowerCase() < b.item.name.toLowerCase()) return -1
    return 0
}

const getItemsAsOneList = (
    teams: DirectoryObject[],
    categories: DirectoryObject[],
    emiCategories: DirectoryObject[],
) => {
    return [
        ...teams.map(item => ({
            type: directoryFolderTypes.TEAM,
            item,
        })),
        ...categories.map(item => ({
            type: directoryFolderTypes.CATEGORY,
            item,
        })),
        ...emiCategories.map(item => ({
            type: directoryFolderTypes.EMI_CATEGORY,
            item,
        })),
    ].sort(sortByNameAsc)
}

function getSearchResultsAsOneList(searchResults: SearchResult) {
    if (!searchResults) return []

    const {directoryCategories, teams} = searchResults.data

    return [
        ...teams.map(item => ({
            type: directoryFolderTypes.SEARCH_TEAM,
            item,
        })),
        ...directoryCategories.map(item => ({
            type: directoryFolderTypes.SEARCH_CATEGORY,
            item,
        })),
    ].sort(sortByNameAsc)
}
