import React from 'react'

import type {CommonSlidingModalItemProps} from '@bpinc/lib-sliding-modal-context'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {SlidingModal} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'


type Props = CommonSlidingModalItemProps & {
    contentVoiceTranscript: JSX.Element | null
}

export const VoiceTranscriptModal: React.FC<Props> = React.memo(function VoiceTranscriptModal({contentVoiceTranscript, ...props}) {
    const {t} = useLocalizationState()

    return (
        <SlidingModal
            name="call-transcript"
            headerClassName={tcn('flex min-h-[72px] justify-center heading-xs text-primary-on-primary')}
            showIconClose={false}
            header={t.VOICE_TRANSCRIPT.title()}
            {...props}
        >
            {contentVoiceTranscript}
        </SlidingModal>
    )
})
