import type {FC} from 'react'
import React from 'react'

import {createProviderWrapper} from '@bpinc/lib-helpers'
import {InteractionContextProvider} from '@bpinc/lib-interaction-context'
import {useNonParalleledDeliveredItems, useSelectedItems} from '@bpinc/lib-interaction-ui-hooks'

import {InteractionsList} from '../../../interactions'
import {CommunicatorContent} from '../../../main-page/'
import {providers} from '../../constants'
import type {CommunicatorProps} from '../../types'


const CommunicatorProviders = createProviderWrapper(providers)


/** Communicator's component built into AgentDesktop */
export const CommunicatorWidget: FC<CommunicatorProps> = () => {
    const deliveredNonParalleledItemIds = useNonParalleledDeliveredItems()
    const {selectedItemId, setSelectedItemId} = useSelectedItems(deliveredNonParalleledItemIds)

    return (
        <CommunicatorProviders>
            <InteractionContextProvider interactionId={selectedItemId}>
                <InteractionsList
                    itemIds={deliveredNonParalleledItemIds}
                    selectedItemId={selectedItemId}
                    setSelectedItemId={setSelectedItemId}
                />
                <CommunicatorContent />
            </InteractionContextProvider>
        </CommunicatorProviders>
    )
}
