import React, {useCallback, useMemo, useState} from 'react'
import {Button} from '@servicepattern/ui'

import {useTimezonesState} from '@bpinc/ad-timezones-state-context'
import {IconCheck24, InputField, Modal} from '@bpinc/lib-jskit-ui'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {useLocalizationState} from '@bpinc/state-localization-context'
import type {CommonModalProps} from '@bpinc/state-modals-types'


type TimezonesModalProps = CommonModalProps & {
    onSelect: (timezoneId: string) => void
}

export const TimezonesModal: React.FC<TimezonesModalProps> = ({
    onSelect,
    onClose,
    ...other
}) => {
    const [searchString, setSearchString] = useState('')
    const [selectedTimezoneId, setSelectedTimezoneId] = useState('')
    const {t} = useLocalizationState()
    const {allTimezones} = useTimezonesState()

    const filteredTimezones = allTimezones.filter(tz => {
        return (
            tz.fullName.toLowerCase().indexOf(searchString.toLowerCase()) !== -1
        )
    })

    const selectAndClose = useCallback(() => {
        onSelect(selectedTimezoneId)
        onClose()
    }, [onClose, onSelect, selectedTimezoneId])

    const timezoneSearchChangeHandler = useCallback((e: React.ChangeEvent) => {
        if (e.target instanceof HTMLInputElement) {
            setSearchString(e.target.value)
        }
    }, [])

    const filteredTimezonesCallbacks = useMemo(() => {
        return filteredTimezones.map(({id}) => {
            return {
                onClick: () => setSelectedTimezoneId(id),
                onDoubleClick: () => {
                    setSelectedTimezoneId(id)
                    selectAndClose()
                },
            }
        })
    }, [filteredTimezones, selectAndClose])

    return (
        <Modal
            onClose={onClose}
            aria-labelledby="timezonesDialogHeader"
            data-testid="timezones-modal"
            className="max-w-[540px]"
            {...other}
        >
            <Modal.Header id="timezonesDialogHeader" role="heading">
                {t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_timezones_heading()}
            </Modal.Header>

            <Modal.Content className="flex flex-col">
                <InputField
                    label={t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_timezones_filter_label()}
                    placeholder={t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_timezones_filter_placeholder()}
                    value={searchString}
                    onChange={timezoneSearchChangeHandler}
                    data-testid="search-input"
                />
                <ul className="flex-auto overflow-auto p-0">
                    {filteredTimezones.map(({fullName, id}, i) => {
                        return (
                            <li
                                onClick={filteredTimezonesCallbacks[i].onClick}
                                onDoubleClick={
                                    filteredTimezonesCallbacks[i].onDoubleClick
                                }
                                key={id}
                                tabIndex={0}
                                className={tcn(
                                    'flex items-center justify-between',
                                    'list-none px-3 py-4 state-layer-bg-neutral-0',
                                    'hover:state-layer-mix-dark-on-white-1',
                                    'focus:state-layer-mix-dark-on-white-2',
                                    {
                                        'state-layer-mix-teal-on-white-2 hover:state-layer-mix-teal-on-white-3 focus:state-layer-mix-teal-on-white-3':
                                            id === selectedTimezoneId,
                                    },
                                )}
                                role="button"
                                data-testid="option"
                            >
                                {fullName}
                                {id === selectedTimezoneId && <IconCheck24 className="ml-2 size-5 icon-fade" />}
                            </li>
                        )
                    })}
                </ul>
            </Modal.Content>
            <Modal.Actions>
                <Button
                    variant="secondary"
                    onClick={() => onClose()}
                    data-testid="cancel-button"
                >
                    {t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_buttonCancel()}
                </Button>
                <Button
                    variant="primary"
                    onClick={selectAndClose}
                    disabled={!selectedTimezoneId}
                    data-testid="submit-button"
                >
                    {t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_buttonSelect()}
                </Button>
            </Modal.Actions>
        </Modal>
    )
}
