import React, {useMemo} from 'react'
import {useContext} from 'use-context-selector'

import {transformFormDefinitionsToLayout} from '@bpinc/lib-form-builder'

import {FormBuilderContext} from './FormBuilder'
import {FormLayout} from './FormLayout'

/**
 * Renders a form with multiple columns as it is defined in the form definition.
 * It is used in fullscreen Agent Desktop.
 */
export function WideForm() {
    const {formDefinition, pageIndex} = useContext(FormBuilderContext)

    const pageFields = useMemo(() => {
        return formDefinition?.layout?.pages?.[pageIndex]?.fields || []
    }, [formDefinition?.layout?.pages, pageIndex])

    const pageLayouts = useMemo(() => {
        return transformFormDefinitionsToLayout(pageFields)
    }, [pageFields])

    return (
        <>
            {pageLayouts.map(pageLayout => (
                <FormLayout
                    key={pageLayout.key}
                    contentItem={pageLayout}
                />
            ),
            )}
        </>
    )
}
