import type {ReactElement} from 'react'
import React from 'react'
import type {FieldValues} from 'react-hook-form'

import type {
    FieldImplementationProps,
    FieldProps,
    FieldRawProps,
    FieldRenderImplementationProps,
} from '@bpinc/lib-forms-base'
import {FieldConstructor, FieldRawConstructor} from '@bpinc/lib-forms-base'
import type {InputFieldProps} from '@bpinc/lib-jskit-ui'
import {InputField} from '@bpinc/lib-jskit-ui'


type TextFieldProps = FieldImplementationProps<InputFieldProps>

function renderElement<TFormValues extends FieldValues>(props: FieldRenderImplementationProps<TextFieldProps, TFormValues>): ReactElement {
    return (
        <InputField {...props} />
    )
}

export function TextFieldRaw<TFormValues extends FieldValues>(props: FieldRawProps<TextFieldProps, TFormValues>): ReactElement {
    return <FieldRawConstructor {...props} renderElement={renderElement} />
}

export function TextField<TFormValues extends FieldValues>(props: FieldProps<TextFieldProps, TFormValues>): ReactElement {
    return <FieldConstructor {...props} renderElement={renderElement} />
}

