import React, {useCallback} from 'react'

import {SemanticButton} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'
import type {ModalConfig} from '@bpinc/state-modals-types'


interface ActionsProps {
    closeModal: () => void
    onConfirm: () => void
}

const Title: React.FC = () => {
    const {t} = useLocalizationState()
    const text = t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_title_warning()

    return <>{text}</>
}

const Content: React.FC = () => {
    const {t} = useLocalizationState()

    return (
        <p>{t.CONTACT.unsaved_warning_message()}</p>
    )
}

const Actions: React.FC<ActionsProps> = props => {
    const {t} = useLocalizationState()
    const {closeModal, onConfirm} = props

    const handleContinue = useCallback(() => {
        closeModal()
        onConfirm()
    }, [closeModal, onConfirm])

    return (
        <>
            <SemanticButton
                enabledInOffline
                primary
                onClick={closeModal}
                data-testid="cancel-button"
            >
                {t.UNDEFINED.button_cancel_contact_text()}
            </SemanticButton>
            <SemanticButton
                enabledInOffline
                onClick={handleContinue}
                data-testid="continue-button"
            >
                {t.GLOBAL.continue()}
            </SemanticButton>
        </>
    )
}

export const getGoBackWarningModal = (onConfirm: () => void): ModalConfig => {
    return {
        dataKey: 'contact-details-go-back-modal',
        title: <Title />,
        body: <Content />,
        actions: ({closeModal}: { closeModal: () => void }) => {
            return (
                <Actions
                    closeModal={closeModal}
                    onConfirm={onConfirm}
                />
            )
        },
    }
}
