import type {FC} from 'react'
import React from 'react'

import type {ContactDetailsType, ContactDetailsUnflatten, WithId} from '@bpinc/ad-contacts-state-types'
import type {CommonSlidingModalItemProps} from '@bpinc/lib-sliding-modal-context'
import {SlidingModal} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {MergeContacts} from '../../../contact-details'


interface MergeContactsModalProps extends CommonSlidingModalItemProps {
    srcContactDetails: Partial<WithId<ContactDetailsType>>
    onMerge?: (mergedContactDetails: WithId<ContactDetailsUnflatten>, srcContactDetails: Partial<WithId<ContactDetailsType>>) => void
}

export const MergeContactsSlidingModal: FC<MergeContactsModalProps> = ({show, closing, closeModal, style, ...props}) => {
    const {t} = useLocalizationState()

    return (
        <SlidingModal
            show={show}
            closing={closing}
            style={style}
            closeModal={closeModal}
            name="mergeContacts"
            header={t.CONTACT.merge_contacts()}
            data-testid="merge-contacts-modal"
        >
            <MergeContacts {...props} />
        </SlidingModal>
    )
}

