import React from 'react'

import {useFormatting} from '@bpinc/ad-formatting-context'
import {useInteraction} from '@bpinc/ad-interaction-state-context'
import {useCallHandler} from '@bpinc/ad-interaction-state-hooks'
import {IconPhone} from '@bpinc/lib-icons'
import {useInteractionId} from '@bpinc/lib-interaction-context'
import {CircleButton, InteractionDropdown} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'


export const CallButton = () => {
    const {t} = useLocalizationState()
    const interactionId = useInteractionId()
    const activeItem = useInteraction(interactionId)
    const handleCall = useCallHandler()
    const {getNamesFromDisplayName} = useFormatting()

    const displayName = activeItem?.displayData.displayName || ''
    const [
        firstName = activeItem?.displayData.firstName,
        lastName = activeItem?.displayData.lastName,
    ] = getNamesFromDisplayName(displayName)

    const number = activeItem?.displayData.number || ''

    return (
        <div className="relative flex w-full justify-end">
            <InteractionDropdown
                searchMode="single_contact"
                callParams={{
                    phone: number,
                    contactId: activeItem?.contactId,
                    userId: activeItem?.userId,
                    firstName,
                    lastName,
                    origination: 'interaction',
                }}
                onCall={handleCall}
                trigger={({handleCallClick, isLoading, openerRef}) => (
                    <CircleButton
                        ref={openerRef}
                        noActiveState
                        isLoading={isLoading}
                        onClick={handleCallClick}
                        tooltip={t.CALL_DETAILS_FORM.button_initiate_call_tooltip()}
                        data-testid="call-button"
                    >
                        <IconPhone />
                    </CircleButton>
                )}

            />
        </div>
    )
}
