import React, {useEffect, useState} from 'react'
import {noop} from 'lodash-es'

import {useIsCommunicator} from '@bpinc/ad-current-tab-data-context'
import {internalUser} from '@bpinc/ad-directory-state-types'
import type {OpenContactDetailsParams} from '@bpinc/ad-interaction-state-hooks'
import type {InitiateCall} from '@bpinc/ad-interaction-state-types'
import {useSessionState} from '@bpinc/ad-session-context'
import {useEvent} from '@bpinc/lib-helpers'
import {
    ButtonIcon,
    IconEmail16,
    IconInformation,
    IconPhone16Filled,
    IconRoundChat16Filled,
} from '@bpinc/lib-jskit-ui'
import {assertDefined} from '@bpinc/lib-types-helpers'
import {EmailOption, PhoneOption} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {InteractionButtonIcon} from '../../shared/InteractionButtonIcon'
import type {InternalUser, OnChatParams, OnEmailParams} from '../types'


type Props = {
    user: InternalUser
    transferMode?: 'messages' | 'calls'
    onCall?: (callParams: InitiateCall) => void
    onChat?: (chatParams: OnChatParams) => void
    onEmail?: (emailParams: OnEmailParams) => void
    onDetails?: (user: OpenContactDetailsParams) => void
    /**
     * subscription for change in total popup state in this component
     * `true` - when some popup is opened, `false` - when none popup is opened
     * */
    onPopupOpenChange?: (value: boolean) => void
}

export const InternalUserControls: React.FC<Props> = ({
    user,
    transferMode,
    onCall,
    onChat,
    onEmail,
    onDetails,
    onPopupOpenChange = noop,
}) => {

    const {CONTACT: i18n} = useLocalizationState('t')
    const {userId: selfUserId, phoneDeviceCapabilities} = useSessionState()
    const isCommunicator = useIsCommunicator()

    const [callPopupOpen, setCallPopupOpen] = useState(false)
    const [chatPopupOpen, setChatPopupOpen] = useState(false)
    const [emailPopupOpen, setEmailPopupOpen] = useState(false)

    const isSelf = user.userId === selfUserId
    const showCallButton = !isSelf && phoneDeviceCapabilities.canDial && transferMode !== 'calls' && !!onCall
    const showChatButton = transferMode !== 'messages' && !!onChat
    const showEmailButton = !isCommunicator && !!onEmail

    const callOptions = internalUser.callOptions(user)
    const chatOptions = internalUser.chatOptions(user)
    const emailOptions = internalUser.emailOptions(user)

    useEffect(() => {
        onPopupOpenChange(callPopupOpen || chatPopupOpen || emailPopupOpen)
    }, [onPopupOpenChange, callPopupOpen, chatPopupOpen, emailPopupOpen])

    const handleCall = useEvent((phone: string) => {
        assertDefined(onCall)
        onCall({
            phone,
            userId: user.userId,
            firstName: user.firstName,
            lastName: user.lastName,
            origination: 'directory',
        })
    })

    const handleChat = useEvent((phone: string) => {
        assertDefined(onChat)
        onChat({
            phone,
            userId: user.userId,
            firstName: user.firstName,
            lastName: user.lastName,
        })
    })

    const handleEmail = useEvent((email: string) => {
        assertDefined(onEmail)
        onEmail({emailAddress: email})
    })

    const handleDetails = useEvent(() => {
        onDetails?.({userId: user.userId})
    })

    return (
        <div className="flex gap-0.5">
            {showCallButton && (
                <InteractionButtonIcon
                    data-testid="call-button"
                    items={callOptions}
                    tooltip={i18n.button_start_call_text()}
                    popupOpen={callPopupOpen}
                    OptionComponent={PhoneOption}
                    onPopupOpenChange={setCallPopupOpen}
                    onPressed={handleCall}
                >
                    <IconPhone16Filled />
                </InteractionButtonIcon>
            )}
            {showChatButton && (
                <InteractionButtonIcon
                    data-testid="chat-button"
                    items={chatOptions}
                    tooltip={i18n.button_initiate_chat_text()}
                    popupOpen={chatPopupOpen}
                    OptionComponent={PhoneOption}
                    onPopupOpenChange={setChatPopupOpen}
                    onPressed={handleChat}
                >
                    <IconRoundChat16Filled />
                </InteractionButtonIcon>
            )}
            {showEmailButton && (
                <InteractionButtonIcon
                    data-testid="email-button"
                    items={emailOptions}
                    tooltip={i18n.button_send_email_text()}
                    popupOpen={emailPopupOpen}
                    OptionComponent={EmailOption}
                    onPopupOpenChange={setEmailPopupOpen}
                    onPressed={handleEmail}
                >
                    <IconEmail16 />
                </InteractionButtonIcon>
            )}
            <ButtonIcon
                tooltip={i18n.button_show_contact_details_text()}
                data-testid="user-info-button"
                onPressed={handleDetails}
            >
                <IconInformation />
            </ButtonIcon>
        </div>
    )
}
