import {useCallback} from 'react'

import {useOpenTimezonesModal} from '../../../modals'


interface UseTimeZonesModalOpener {
    handleSelect: (timezoneId: string) => void
}
export function useTimeZonesModalOpener({handleSelect}: UseTimeZonesModalOpener) {
    const openTimezonesModal = useOpenTimezonesModal()

    return useCallback(() => {
        openTimezonesModal({
            onSelect: handleSelect,
        })
    }, [handleSelect, openTimezonesModal])

}
