import React, {useMemo} from 'react'
import cn from 'classnames'

import type {ActivityNotes} from '@bpinc/ad-case-state-types'
import {useGetAvatarUrl} from '@bpinc/ad-contacts-state-hooks'
import {useFormatting} from '@bpinc/ad-formatting-context'
import {formatDate} from '@bpinc/ad-formatting-helpers'
import {ContactListItem} from '@bpinc/lib-contact-list-item-ui'

import {Disposition} from './Disposition'


interface Props {
    activity: ActivityNotes
}

export const NotesPreview: React.FC<Props> = ({activity}) => {
    const {formatFullName} = useFormatting()
    const getAvatarUrl = useGetAvatarUrl()
    const {createdTime, parties} = activity

    const user = parties.find(p => p.partyType === 'USER')
    const {userId, firstName = '', lastName = '', notes = '', disposition = ''} = user || {}
    const noteContentClassName = cn('relative inline-block bg-[--c-note-content-bg] py-2 pl-2 pr-6',
        'after:absolute after:bottom-0 after:right-0 after:h-0 after:w-0 after:border-[8px]',
        'after:border-b-white after:border-l-[--c-note-border] after:border-r-white after:border-t-[--c-note-border]')
    const name = formatFullName(firstName, lastName)
    const avatar = useMemo(() => {
        return {
            firstName,
            lastName,
            url: getAvatarUrl({userId}),
            isAgent: true,
        }
    }, [firstName, getAvatarUrl, lastName, userId])

    const title = name
    const body = <span className="relative -top-1 text-[0.8em]">{formatDate(createdTime)}</span>

    if (user && disposition) {
        return (
            <>
                <div className={cn(noteContentClassName, 'mb-2')}>{notes}</div>
                <Disposition data={user} />
            </>
        )
    }

    return (
        <div>
            <ContactListItem
                as="div"
                avatarProps={avatar}
                title={title}
                body={body}
                userId={userId}
                isFitted
                isAlignedToRight
            />
            <div className={noteContentClassName}>{notes}</div>
        </div>
    )
}
