import React from 'react'

import {useGetAvatarUrl} from '@bpinc/ad-contacts-state-hooks'
import {useInteraction} from '@bpinc/ad-interaction-state-context'
import {useContactDetailsOpener} from '@bpinc/ad-interaction-state-hooks'
import {isEmail, isExternalChat, isInternalChat, isPotentialInteraction} from '@bpinc/ad-interaction-state-methods'
import {Avatar, getAvatarType} from '@bpinc/lib-avatar-ui'
import {useEvent} from '@bpinc/lib-helpers'
import {useInteractionId} from '@bpinc/lib-interaction-context'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {useItemNames} from '../../hooks/useItemNames'


export const ActiveItemAvatar: React.FC = () => {
    const interactionId = useInteractionId()
    const activeItem = useInteraction(interactionId)
    const openContactDetails = useContactDetailsOpener()
    const {CALL_DETAILS_FORM: i18n} = useLocalizationState('t')

    const contactId = activeItem?.contactId
    const userId = activeItem?.userId
    const emiUserType = activeItem?.displayData?.emiUserType
    const isClickable = !!contactId || !!userId

    const getAvatarUrl = useGetAvatarUrl()

    const [firstName, lastName] = useItemNames(activeItem)
    const avatarUrl = getAvatarUrl({
        userId,
        contactId,
        userType: emiUserType,
        photoUrl: activeItem?.displayData?.emiPhotoUrl,
    })

    const interactionIcon =
        isPotentialInteraction(activeItem)
            ? undefined
            : isExternalChat(activeItem) || isInternalChat(activeItem)
                ? 'chat'
                : isEmail(activeItem)
                    ? 'email'
                    : 'phone'

    const type = getAvatarType(userId, contactId, emiUserType)

    const handleAvatarClick = useEvent(() => {
        openContactDetails({contactId, userId})
    })

    const className = tcn({
        'cursor-pointer': isClickable,
    })

    return (
        <Avatar
            interactionIcon={interactionIcon}
            className={className}
            firstName={firstName}
            lastName={lastName}
            url={avatarUrl}
            type={type}
            onClick={handleAvatarClick}
            title={isClickable ? i18n.contact_details_link_tooltip() : undefined}
        />
    )
}
