import {useChatUnread, useInteractionChatStateSelector} from '@bpinc/ad-interaction-chat-state-context'
import {useInternalChatTotalUnread} from '@bpinc/ad-interaction-internal-chat-state-context'
import {useInteractionStateSelector} from '@bpinc/ad-interaction-state-context'


export function useUnreadAmount() {
    const unreadPersonalMessagesAmountByContactId = useInteractionChatStateSelector(
        s => s.unreadPersonalMessagesAmountByContactId,
    )

    const sumUnreadReducerPersonalChats = (totalUnread: number, {contactId = ''}) =>
        totalUnread + (contactId ? unreadPersonalMessagesAmountByContactId[contactId] ?? 0 : 0)

    const unreadActivePersonalChats = useInteractionStateSelector(s =>
        s.items.reduce(sumUnreadReducerPersonalChats, 0),
    )
    const unreadExternal = useChatUnread()
    const unreadInternal = useInternalChatTotalUnread()

    return unreadActivePersonalChats + unreadExternal + unreadInternal
}
