import type {ReactElement, ReactFragment} from 'react'
import React from 'react'

import type {AuthorizedConnectors, BaseConnectors} from '@bpinc/ad-connected-state-types'
import {useSystemNotifications} from '@bpinc/lib-system-notifications'


type Props = {
    baseConnectors: BaseConnectors
    authorizedConnectors: AuthorizedConnectors
    children: (
        baseConnectors: BaseConnectors,
        authorizedConnectors: AuthorizedConnectors,
    ) => ReactElement | ReactFragment
}

export const AuthorizedConnectorsConsumers: React.FC<Props> = ({baseConnectors, authorizedConnectors, children}) => {
    useSystemNotifications()
    return <>{children(baseConnectors, authorizedConnectors)}</>
}
