import {useEffect, useRef} from 'react'

import {useBoolean} from '@bpinc/lib-helpers'


export function useShowChecker() {
    const {value: show, toggle: toggleShow} = useBoolean()
    const dragCounterRef = useRef(0)

    useEffect(() => {
        document.addEventListener('dragenter', onDragEnter)
        document.addEventListener('dragleave', onDragLeave)
        document.addEventListener('drop', onDragLeave)

        return () => {
            document.removeEventListener('dragenter', onDragEnter)
            document.removeEventListener('dragleave', onDragLeave)
            document.removeEventListener('drop', onDragLeave)
        }

        function onDragEnter() {
            dragCounterRef.current++
            toggleShow(true)
        }

        function onDragLeave() {
            dragCounterRef.current--
            if (dragCounterRef.current === 0) {
                toggleShow(false)
            }
        }
    }, [toggleShow])

    return show
}
