import {useCallback} from 'react'

import {useContactsState, useContactsStateMethods} from '@bpinc/ad-contacts-state-context'
import {useGetDirectLink} from '@bpinc/ad-contacts-state-hooks'
import {useFormatting} from '@bpinc/ad-formatting-context'
import {bpLogger} from '@bpinc/ad-persistent-logger'
import {useNavigationState} from '@bpinc/lib-communicator-navigation-state'
import {clipboard} from '@bpinc/lib-helpers'
import {Notifications, useNotificationsState} from '@bpinc/lib-notifications-context'
import {useOpenGlobalSlidingModal} from '@bpinc/lib-sliding-modal-context'
import {useLocalizationState} from '@bpinc/state-localization-context'
import {useModalsStateMethods} from '@bpinc/state-modals-context'

import {getDeleteWarningModal} from '../components/modals/getDeleteWarningModal'


export function useViewModeHandlers(id: string) {
    const goBack = useNavigationState('goBack')
    const {CONTACT: i18n} = useLocalizationState('t')
    const {formatFullName} = useFormatting()
    const {showNotification} = useNotificationsState('methods')

    const contacts = useContactsState('contacts')
    const {deleteContact} = useContactsStateMethods()
    const getDirectLink = useGetDirectLink()

    const {data, details} = contacts[id] || {}
    const {openModal} = useModalsStateMethods()

    const handleCopyLink = useCallback(() => {
        const link = getDirectLink(undefined, id)
        clipboard.copy(link)
    }, [id, getDirectLink])

    const handleDelete = useCallback(() => {
        const onError = () => showNotification({
            type: 'error',
            id: Notifications.ContactDeleteError,
            tags: ['current-view'],
            message: i18n.delete_error(),
        })
        const onConfirm = () => {
            if (data) {
                deleteContact(data.Id.oid).then(goBack).catch(onError)
            } else {
                onError()
            }
        }
        const name = formatFullName(data?.firstName, data?.lastName) || 'No name'
        openModal(getDeleteWarningModal(onConfirm, {name}))

    }, [data, deleteContact, formatFullName, goBack, openModal, showNotification, i18n])

    const openGlobalSlidingModal = useOpenGlobalSlidingModal()
    const handleMerge = useCallback(
        function handleMergeCallback() {
            openGlobalSlidingModal('mergeContacts', {
                srcContactDetails: {...details, id},
                onMerge() {
                    bpLogger.warn(
                        'TODO: Some server method is needed in order to replace',
                        'outdated contact in entries like recent or favorites',
                    )
                },
            })
        },
        [details, id, openGlobalSlidingModal],
    )

    return {
        handleDelete,
        handleCopyLink,
        handleMerge,
    }
}
