import React from 'react'

import {CHAT_MESSAGE_ALLOWED_ATTRIBUTES, CHAT_MESSAGE_ALLOWED_TAGS} from '@bpinc/ad-constants'
import {useChatMessageSanitizer} from '@bpinc/ad-interaction-state-hooks'
import {useInnerHTML} from '@bpinc/lib-helpers'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {Sentiment} from '@bpinc/lib-ui-elements'
import {useChatSentimentsOn} from '@bpinc/state-interaction-ui'

import {useMarkAsReadVisibilitySensor} from '../../../../../hooks/useMarkAsReadVisibilitySensor'
import type {ExternalChatMessage} from '../../../../../types'
import {isChatMessage, isHistoryMessage, isHistoryVoiceTranscriptMessage} from '../../../../../types'
import type {MarkAsReadProps} from '../../ExternalChatMessage'
import {VoiceTranscriptConfidence} from '../../VoiceTranscriptConfidence'

import styles from './PlainMessage.module.less'


type PlainMessageProps = {
    message: ExternalChatMessage
    markAsReadData: MarkAsReadProps
}

export const PlainMessage: React.FC<PlainMessageProps> = ({
    markAsReadData,
    message,
}) => {
    const {
        scrollNode,
        itemGlobalInteractionId,
        itemInteractionStepId,
        isDIDChat,
        itemContactId,
        interactionItemId,
    } = markAsReadData

    const {msgId, userId} = message

    const isHistory = isHistoryMessage(message)
    const mediaType = isHistory ? message.mediaType : undefined
    const globalInteractionId = isHistory ? message.globalInteractionId : undefined
    const confidence = isHistoryVoiceTranscriptMessage(message) ? message.confidence : undefined

    const isChat = isChatMessage(message)
    const source = isChat ? message.source : undefined
    const sentiment = isChat ? message.sentiment : undefined
    const isRead = isChat ? message.isRead : undefined
    const maskedText = isChat ? message.maskedText : undefined

    const msg = isChat || isHistory ? message.msg : undefined

    const sanitizedText = useChatMessageSanitizer({
        message: msg,
        allowedAttributes: CHAT_MESSAGE_ALLOWED_ATTRIBUTES,
        allowedTags: CHAT_MESSAGE_ALLOWED_TAGS,
    })
    const messageInnerHTML = useInnerHTML(maskedText || sanitizedText)
    const chatSentimentsOn = useChatSentimentsOn(interactionItemId)
    const ref = useMarkAsReadVisibilitySensor({
        msgId,
        historyMessageGlobalInteractionId: globalInteractionId,
        scrollNode,
        mediaType,
        source,
        isRead,
        itemContactId,
        isDIDChat,
        itemInteractionStepId,
        itemGlobalInteractionId,
    })

    return (
        <div data-message-id={msgId} data-global-interaction-id={globalInteractionId ?? 'current'} ref={ref}>
            {userId && sentiment && (
                <Sentiment
                    sentiment={sentiment}
                    className={tcn({[styles.visible]: chatSentimentsOn}, styles.sentimentLeft)}
                />
            )}
            {userId && confidence && (
                <VoiceTranscriptConfidence confidence={confidence} />
            )}

            <span
                dangerouslySetInnerHTML={messageInnerHTML}
                className={styles.plainMessage}
                data-testid="message-content"
            />

            {!userId && sentiment && (
                <Sentiment
                    sentiment={sentiment}
                    className={tcn({[styles.visible]: chatSentimentsOn}, styles.sentimentRight)}
                />
            )}
            {!userId && confidence && (
                <VoiceTranscriptConfidence confidence={confidence} />
            )}
        </div>
    )
}
