import React, {useMemo, useState} from 'react'

import {useGetContactPictureUrl} from '@bpinc/ad-contacts-state-hooks'
import type {ContactShortType} from '@bpinc/ad-contacts-state-types'
import {useFormatting} from '@bpinc/ad-formatting-context'
import {ContactListItem} from '@bpinc/lib-contact-list-item-ui'
import {ExternalContactFavoriteButton} from '@bpinc/lib-favorites-button'
import {useEvent} from '@bpinc/lib-helpers'
import {highlighted, SelectionItem} from '@bpinc/lib-ui-elements'

import type {ExternalUserProps} from '../types'
import {ExternalContactBody} from './ExternalContactBody'
import {ExternalContactControls} from './ExternalContactControls'


function getContactId(contact: ContactShortType) {
    return contact.id
}

export const ExternalContact: React.FC<ExternalUserProps> = ({
    transferMode,
    transferNumberInput,
    transferControlsStyle,
    contact,
    onCall,
    onChat,
    onEmail,
    onDetails,
    onClick,
    noControls,
    onDoubleClick,
    highlight,
    showFavoriteButton = true,
    ...other
}) => {

    const getContactPictureUrl = useGetContactPictureUrl()
    const {formatFullName} = useFormatting()

    const [controlsPopupOpen, setControlsPopupOpen] = useState(false)

    const contactId = getContactId(contact)

    const avatar = useMemo(
        () => ({
            firstName: contact.firstName,
            lastName: contact.lastName,
            url: getContactPictureUrl(contactId),
        }),
        [getContactPictureUrl, contact.firstName, contactId, contact.lastName],
    )

    const title = useMemo(() => (
        <span className="flex gap-1">
            <b data-testid="name" className="inline-block truncate font-semibold body text-primary-on-primary">
                {highlighted({
                    highlight,
                    text: formatFullName(contact.firstName, contact.lastName),
                })}
            </b>
            {showFavoriteButton && (
                <ExternalContactFavoriteButton forContactListItem item={contact} />
            )}
        </span>
    ), [formatFullName, highlight, showFavoriteButton, contact])

    const body = <ExternalContactBody contact={contact} highlight={highlight} />

    const controls = (
        <ExternalContactControls
            contact={contact}
            transferMode={transferMode}
            onPopupOpenChange={setControlsPopupOpen}
            onChat={onChat}
            onCall={onCall}
            onEmail={onEmail}
            onDetails={onDetails}
        />
    )

    const handleChat = useEvent((e: React.MouseEvent) => {
        e.preventDefault()
        e.stopPropagation()
        onChat?.({
            contactId,
            firstName: contact.firstName,
            lastName: contact.lastName,
            phone: contact.phone,
        })
    })

    const callButtonRef = React.useRef<HTMLButtonElement | null>(null)

    const handleClick = useEvent((e: React.MouseEvent) => {
        onClick?.(e, contact)
    })

    const handleDoubleClick = useEvent(() => {
        onDoubleClick?.(contact, {
            onCall: () => {
                callButtonRef.current?.click()
            },
        })
    })

    return (
        <ContactListItem
            as={SelectionItem}
            keyHandlerElement="directory-item"
            transferMode={transferMode}
            transferNumberInput={transferNumberInput}
            transferControlsStyle={transferControlsStyle}
            contactId={contactId}
            number={contact.phone}
            avatarProps={avatar}
            title={title}
            body={body}
            controls={controls}
            onClick={onClick ? handleClick : handleChat}
            onDoubleClick={onDoubleClick && handleDoubleClick}
            containerProps={{
                itemId: contact.id,
                focusOnSelection: true,
                tabIndex: 0,
                'data-testid': 'directory-item',
                'data-external-contact': 'true',
            }}
            forceHover={controlsPopupOpen}
            {...other}
        />
    )
}
