import type {KeyboardEvent, SyntheticEvent} from 'react'
import React, {useCallback, useEffect, useState} from 'react'

import type {ButtonProps} from '@bpinc/lib-ui-elements'
import {Button} from '@bpinc/lib-ui-elements'


interface LongPressButtonProps extends ButtonProps {
    className: string
    onClick?: (e: SyntheticEvent) => void
    onHold?: (e: SyntheticEvent) => void
}

const LongPressButton: React.FC<LongPressButtonProps> = props => {
    const {
        onClick,
        onHold,
        children,
        ...rest
    } = props
    const [timer, setTimer] = useState<number>()
    const [keyPressed, setKeyPressed] = useState<boolean>(false)

    useEffect(() => () => {
        timer && clearTimeout(timer)
    })

    const onTimeIsUp = useCallback((e: SyntheticEvent) => {
        setTimer(undefined)
        onHold && onHold(e)
    }, [onHold])

    const start = useCallback((e: SyntheticEvent) => {
        const timerId = setTimeout(() => onTimeIsUp(e), 1000)
        setTimer(timerId)
    }, [onTimeIsUp])

    const stop = useCallback((e: SyntheticEvent) => {
        if (timer) {
            clearTimeout(timer)
            setTimer(undefined)
            onClick && onClick(e)
        }
    }, [onClick, timer])


    const onEnterOrSpacePress = useCallback(
        (e: KeyboardEvent, handler: (e: SyntheticEvent) => void, isKeyDown?: boolean) => {
            //-- Prevent auto repeating event -- start --//
            if (isKeyDown) {
                if (keyPressed) return
                setKeyPressed(true)
            } else {
                setKeyPressed(false)
            }
            //-- Prevent auto repeating event -- end --//

            if (e.key === 'Enter' || e.key === ' ') {
                e.preventDefault()
                handler(e)
            }
        },
        [keyPressed],
    )

    const startByKey = useCallback(
        (e: KeyboardEvent) => onEnterOrSpacePress(e, start, true),
        [onEnterOrSpacePress, start],
    )
    const stopByKey = useCallback(
        (e: KeyboardEvent) => onEnterOrSpacePress(e, stop),
        [onEnterOrSpacePress, stop],
    )

    return (
        <Button
            onMouseDown={start}
            onMouseUp={stop}
            onTouchStart={start}
            onTouchEnd={stop}
            onKeyDownCapture={startByKey}
            onKeyUp={stopByKey}
            {...rest}
        >
            {children}
        </Button>
    )
}

export {LongPressButton}
