import React, {useEffect} from 'react'

import {useBpClientStateConnector} from '@bpinc/ad-bpclient-state-context'
import {platformInfo} from '@bpinc/ad-build-and-platform-info'
import {useSessionState} from '@bpinc/ad-session-context'
import {SmButton} from '@bpinc/lib-external-ui-elements'
import {useEvent} from '@bpinc/lib-helpers'
import {URL_EXTENSION_INSTALLER_CHROME, URL_EXTENSION_INSTALLER_FIREFOX, URL_MAC_PLUGIN} from '@bpinc/lib-helpers-ui'
import {useLocalizationState} from '@bpinc/state-localization-context'
import {useModalsStateMethods} from '@bpinc/state-modals-context'


type Props = {
    browserName: string
}

const ModalContent: React.FC<Props> = ({browserName}) => {
    const {INSTALLATION_PROCESS: i18n} = useLocalizationState('t')
    const serverOrigin = useSessionState('serverOrigin')

    const isChrome = platformInfo.browser === 'chrome'
    const isFirefox = platformInfo.browser === 'firefox'
    const isSafari = platformInfo.browser === 'safari'
    let linkHref = ''

    if (isChrome) {
        linkHref = URL_EXTENSION_INSTALLER_CHROME
    } else if (isFirefox) {
        linkHref = serverOrigin + URL_EXTENSION_INSTALLER_FIREFOX
    } else if (isSafari) {
        linkHref = serverOrigin + URL_MAC_PLUGIN
    }
    return (
        <>
            <span>
                {i18n.bp_client_old_extension_version(browserName)}
            </span>
            {(isChrome || isFirefox || isSafari) && (
                <>
                    <br />
                    <br />
                    <span>
                        {i18n.bp_client_update_extension_part1()}
                    </span>
                    <a href={linkHref} rel="noopener noreferrer" target="_blank">
                        {i18n.bp_client_update_extension_text()}
                    </a>
                    <span>
                        {i18n.bp_client_update_extension_part2()}
                    </span>
                </>
            )}
        </>
    )
}

export const useOutdatedBrowserExtensionPopup = () => {
    const bpClientConnector = useBpClientStateConnector()
    const {WINDOW_POPUP_TITLES_AND_MESSAGES: i18n} = useLocalizationState('t')
    const {openModal} = useModalsStateMethods()

    const onShowExtensionIsOldDialog = useEvent((browserName: string) => {
        openModal({
            title: i18n.window_title_warning(),
            body: (
                <ModalContent browserName={browserName} />
            ),
            actions: ({closeModal}) => (
                <SmButton onClick={closeModal}>
                    {i18n.window_buttonOk()}
                </SmButton>
            ),
        })
    })

    useEffect(() => {
        const subscription = bpClientConnector.dispenser.subscribe({onShowExtensionIsOldDialog})
        return subscription.unsubscribe
    }, [bpClientConnector, onShowExtensionIsOldDialog])
}
