import type {ScreenRecordingState} from '@bpinc/ad-bpclient-state-types'
import type {DIDNumber} from '@bpinc/ad-did-types'
import type {DirectoryObject, TeamUserInfo} from '@bpinc/ad-directory-state-types'
import {getItemNotes, getPhoneParties} from '@bpinc/ad-interaction-state-methods'
import type {Item} from '@bpinc/ad-interaction-state-types'
import {getPlaybackUrl, getRecordingUrl} from '@bpinc/ad-request-urls'
import type {Disposition, Service} from '@bpinc/ad-services-dispositions-types'
import type {PhoneCapabilities, ServerErrorSource} from '@bpinc/ad-session-types'
import type {
    CallDirection,
    CallParty,
    ChatParty,
    ChatPartyType,
    DIDNumber as ApiDIDNumber,
    DispositionData,
    InteractionActivityData,
    InteractionData,
    InteractionState,
    InteractionType,
    PhoneCapabilities as ApiPhoneCapabilities,
    ScreenRecordingState as ApiScreenRecordingState,
    ServerErrorCodes,
    ServerErrorData,
    ServiceData,
    Team,
    User,
} from '@bpinc/adapters-public-api-types'
import {chatPartyTypes, serverErrorCodes} from '@bpinc/adapters-public-api-types'
import {array, withoutUndefinedKeys} from '@bpinc/lib-types-helpers'


const convertItem = (
    serverOrigin: string,
    item: Item,
    formatFullName: (firstName: string, lastName: string) => string,
): InteractionData => {
    const data: InteractionData = {
        interactionId: item.id,
        globalInteractionId: item.scenarioData?.globalInteractionId,
        state: item.state as InteractionState,
        type: item.mediaType as InteractionType,
        phoneNumber: item.displayData.number,
        email: item.displayData.email,
        callDirection: item.direction as CallDirection,
        callMuted: item.callData?.muted ?? false,
        callRecording: item.displayData.isRecording ?? false,
        startTime: item.displayData.deliveredStartTime,
        endTime: item.displayData.acwStartTime,
        duration: item.displayData.deliveredStartTime &&
            item.displayData.acwStartTime &&
            Math.floor(
                (item.displayData.acwStartTime - item.displayData.deliveredStartTime) / 1000,
            ) || undefined,
        description: getItemNotes(item).join('\n'),
        disposition: item.dispositionData?.disposition?.name,
        service: item.scenarioData?.serviceName,
        playbackUrl: item.playbackUrl ? getPlaybackUrl(serverOrigin, item.playbackUrl) : undefined,
        recordingUrl: item.scenarioData?.interactionStepId
            ? getRecordingUrl(serverOrigin, item.scenarioData.interactionStepId)
            : undefined,
        DNIS: item.callData?.destination,
        ANI: item.callData?.fromNumber,
        callParties: (getPhoneParties(item) || []).map<CallParty>(party => ({
            id: party.id,
            name: party.name,
            phone: party.phone,
            userId: party.userId,
        })),
        chatParties: (item.chatParties || [])
            .filter(cp => array(chatPartyTypes).includes(cp.type))
            .map<ChatParty>(cp => ({
                id: cp.party_id,
                type: cp.type as ChatPartyType,
                name: formatFullName(cp.first_name || '', cp.last_name || ''),
                userId: cp.user_id,
                contactId: cp.contact_id,
                email: cp.email,
                phone: cp.phone,
            })),
        attachedData: item.scenarioData?.attachedData || {},
        origination: item.origination,

        subject: '',
        associatedObjects: {
            list: item.associatedObjects?.list ?? [],
            selected: item.associatedObjects?.selected ?? [],
        },
    }
    return withoutUndefinedKeys(data)
}

const convertActivity = (serverOrigin: string, item: Item): InteractionActivityData => {
    const data: InteractionActivityData = {
        interactionId: item.id,
        globalInteractionId: item.scenarioData?.globalInteractionId,
        state: item.state as InteractionState,
        type: item.mediaType as InteractionType,
        associatedObjects: {
            list: item.associatedObjects?.list ?? [],
            selected: item.associatedObjects?.selected ?? [],
        },
        phoneNumber: item.displayData.number,
        email: item.displayData.email,
        callDirection: item.direction as CallDirection,
        startTime: item.displayData.deliveredStartTime,
        endTime: item.displayData.acwStartTime,
        duration: item.displayData.deliveredStartTime &&
            item.displayData.acwStartTime &&
            Math.floor(
                (item.displayData.acwStartTime - item.displayData.deliveredStartTime) / 1000,
            ) || undefined,
        description: (getItemNotes(item) ?? []).join('\n'),
        disposition: item.dispositionData?.disposition?.name,
        service: item.scenarioData?.serviceName,
        playbackUrl: item.playbackUrl ? getPlaybackUrl(serverOrigin, item.playbackUrl) : undefined,
        recordingUrl: item.scenarioData?.interactionStepId
            ? getRecordingUrl(serverOrigin, item.scenarioData.interactionStepId)
            : undefined,
        DNIS: item.callData?.destination,
        ANI: item.callData?.fromNumber,
        attachedData: item.scenarioData?.attachedData || {},
        origination: item.origination,

        subject: '',
        callParties: [],
        chatParties: [],
        callMuted: false,
        callRecording: false,
    }
    return withoutUndefinedKeys(data)
}

function convertTeam(directoryObject: DirectoryObject): Team {
    return {
        id: directoryObject.id,
        displayName: directoryObject.name,
        isSupervised: !!directoryObject.supervised,
        usersCount: directoryObject.usersCount,
    }
}

function convertTeamMember(user: TeamUserInfo): User {
    return {
        id: user.userId,
        teamId: user.teamId,
        phone: user.extension,
        firstName: user.firstName,
        lastName: user.lastName,
        state: user.isLogged
            ? user.agentState === '' ? 'supervising' : user.agentState
            : 'offline',
    }
}

function convertService(service: Service): ServiceData {
    return {
        id: service.id,
        name: service.name,
        useDispositions: service.useDispositions,
        requireDisposition: service.requireDispositions,
    }
}

function convertDIDNumber(didNumber: DIDNumber): ApiDIDNumber {
    return {
        id: didNumber.id,
        number: didNumber.externalNumber.toString(),
        isSMS: didNumber.isSMS,
        isVoice: didNumber.isVoice,
    }
}

function convertDisposition(disposition: Disposition): DispositionData {
    return {
        id: disposition.id,
        code: disposition.code,
        name: disposition.name,
        type: disposition.type,
    }
}

function convertScreenRecordingState(state: ScreenRecordingState): ApiScreenRecordingState {
    return {
        allowed: state.vncInit,
        active: state.started,
        muted: state.muted,
    }
}

function err(
    name: keyof ServerErrorCodes,
    message?: string,
): ServerErrorData {
    return {
        code: serverErrorCodes[name],
        name,
        timestamp: Date.now(),
        message: message || 'Message undefined',
    }
}
function convertServerError(source: ServerErrorSource, code: number, message?: string): ServerErrorData {
    switch (source) {
        case 'auth': {
            switch (code) {
                case 1:
                    return err('invalid_credentials', message)
                case 2:
                    return err('other_user_logged_with_phone', message)
                case 3:
                    return err('invalid_token', message)
                case 4:
                    return err('user_already_logged_in', message)
                case 5:
                    return err('logged_users_limit_reached', message)
                case 6:
                    return err('tenant_disabled', message)
                case 12:
                    return err('already_logged_in_with_the_same_session', message)
            }
            break
        }
        case 'agentServer': {
            switch (code) {
                case 1:
                    return err('forced_logout_by_other_client', message)
                case 2:
                    return err('not_enough_privileges', message)
                case 3:
                    return err('invalid_request', message)
                case 4:
                    return err('phone_not_registered', message)
                case 5:
                    return err('phone_not_in_dial_plan', message)
                case 6:
                    return err('not_enough_privileges', message)
                case 7:
                    return err('forced_logout_by_supervisor', message)
                case 8:
                    return err('tenant_disabled', message)
                case 9:
                    return err('phone_not_registered', message)
                case 10:
                    return err('destination_user_not_logged', message)
                case 11:
                    return err('user_disabled_or_deleted', message)
            }
            break
        }
        case '3pcc': {
            return err('sip_server_error', message)
        }
    }
    return err('unknown_error', message)
}

function convertPhoneCapabilities(caps: PhoneCapabilities): ApiPhoneCapabilities {
    return {
        canAnswer: !!caps.canAnswer,
        canDial: !!caps.canDial,
        canHold: !!caps.canHold,
        canRetrieve: !!caps.canRetrieve,
    }
}

export {
    convertActivity,
    convertDIDNumber,
    convertDisposition,
    convertItem,
    convertPhoneCapabilities,
    convertScreenRecordingState,
    convertServerError,
    convertService,
    convertTeam,
    convertTeamMember,
}
