import React from 'react'

import type {Message} from '@bpinc/ad-prerecorded-messages-state-types'
import {IconCheck24} from '@bpinc/lib-jskit-ui'
import {tcn} from '@bpinc/lib-tailwind-classnames'


interface PrerecordedItemProps {
    prompt: Message
    selected: boolean
    onClick: (message: Message) => void
}

export const PrerecordedItem: React.FC<PrerecordedItemProps> = ({
    prompt,
    selected,
    onClick,
}) => {
    return (
        <li
            data-selected={selected}
            className={tcn(
                'group',
                'text-primary-on-primary',
                'truncate',
                'hover:state-layer-mix-dark-on-white-1',
                'focus:state-layer-mix-dark-on-white-1',
                'transition-colors',
                'state-layer-bg-main-bg',
                'data-[selected=true]:state-layer-mix-teal-on-white-1',
                'data-[selected=true]:hover:state-layer-mix-teal-on-white-2',
                'data-[selected=true]:focus:state-layer-mix-teal-on-white-2',
            )}
        >
            <button
                className="flex w-full items-center justify-between gap-2 px-4 py-3 leading-[24px]"
                onClick={() => onClick(prompt)}
            >
                {prompt.name}
                <IconCheck24 className="hidden group-data-[selected=true]:block" />
            </button>
        </li>
    )
}
