import React from 'react'

import {IconACWDisposition24} from '@bpinc/lib-jskit-ui'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {SelectionItem} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {WithHoverOverActions} from '../../../ChatMessagesActions'


interface DispositionMessageProps {
    title: string
    disposition?: string
    serviceName?: string
    messageId: string
}

export const DispositionMessage: React.FC<DispositionMessageProps> = ({title, disposition, serviceName, messageId}) => {
    const {t} = useLocalizationState()
    const messageContentClassName = tcn('break-words font-normal caption-s text-neutral-on-secondary')
    const rowMessageClassName = tcn('flex w-full items-center gap-1')

    return (
        <SelectionItem
            touchedOnly
            focusOnSelection
            tabIndex={0}
            itemId={messageId}
            className="w-full rounded-xs border py-1.5 pl-2 pr-3 bg-primary-container-variant1 border-action"
        >
            <WithHoverOverActions messageId={messageId}>
                <div className="flex w-full flex-col gap-2">
                    <div className={rowMessageClassName}>
                        <IconACWDisposition24 className="size-4 icon-neutral" />
                        <div className="font-semibold caption-xs text-primary-on-primary">
                            {t.ACTIVITY_HISTORY.disposition_title()}
                        </div>
                    </div>
                    <div
                        className={tcn('flex w-full gap-1', messageContentClassName)}
                        data-testid="disposition-title"
                    >
                        {title}
                    </div>
                    <div className={rowMessageClassName}>
                        <span className={messageContentClassName}>
                            {t.EXTERNAL_CHAT.disposition_message_service_name()}
                        </span>
                        <span
                            className={tcn(messageContentClassName, 'font-semibold')}
                            data-testid="disposition-service"
                        >
                            {serviceName}
                        </span>
                    </div>
                    <div
                        className={rowMessageClassName}
                        data-testid="disposition"
                    >
                        <span className={messageContentClassName}>
                            {t.EXTERNAL_CHAT.disposition_message_name()}
                        </span>
                        <span className={tcn(messageContentClassName, 'font-semibold')}>
                            {disposition}
                        </span>
                    </div>
                </div>
            </WithHoverOverActions>
        </SelectionItem>
    )
}
