import React, {useCallback} from 'react'
import type {FieldArray, FieldValues, UseFieldArrayAppend} from 'react-hook-form'

import type {FieldArrayPathByValue} from '@bpinc/lib-forms-base'
import type {SVGRComponent} from '@bpinc/lib-types-helpers'
import {useLocalizationState} from '@bpinc/state-localization-context'


type AddRowProps<
    TFieldType extends FieldArray,
    TValues extends FieldValues,
    TFieldName extends FieldArrayPathByValue<TValues, TFieldType>
> = {
    initialValues: TFieldType
    append: UseFieldArrayAppend<TValues, TFieldName>
    AddButtonIcon: SVGRComponent
    addButtonText?: string
}


export function AddRow<
    TFieldType extends FieldArray,
    TValues extends FieldValues,
    TFieldName extends FieldArrayPathByValue<TValues, TFieldType>
>({
    append,
    AddButtonIcon,
    initialValues,
    addButtonText,
}: AddRowProps<TFieldType, TValues, TFieldName>) {
    const {t} = useLocalizationState()

    const handleAdd = useCallback(
        function handleAddCallback() {
            append(initialValues as FieldArray<TValues, TFieldName>)
        },
        [append, initialValues],
    )

    return (
        <div>
            <button
                onClick={handleAdd}
                className="group/add-btn flex cursor-pointer border-[none] bg-[none] pl-0"
                title={t.CONTACT.add_entry_button_tooltip()}
            >
                <AddButtonIcon aria-hidden />
                {addButtonText && (
                    <span className="ml-1 align-middle text-main-secondary group-hover/add-btn:text-black">
                        {addButtonText}
                    </span>
                )}
            </button>
        </div>
    )
}
