import {useMemo} from 'react'
import {flow} from 'lodash-es'

import type {ChatMessage as ChatMessageType} from '@bpinc/ad-interaction-message-types'

import {useMessagesList} from '../../../../hooks/useMessagesList'


export function useNormalizedChatMessages(itemId: string) {
    const {messagesList, addUserId, localizeSystemMessage, maskMessage} = useMessagesList(itemId)

    return useMemo(
        function memoizeNormalizedMessages() {
            return {
                normalizedChatMessages: messagesList.reduce<ChatMessageType[]>((messages, message) => {
                    const normalizedMessage = flow(addUserId, localizeSystemMessage, maskMessage)(message)

                    messages.push(normalizedMessage)

                    return messages
                }, []),
            }
        },
        [addUserId, localizeSystemMessage, maskMessage, messagesList],
    )
}
