import React, {useCallback} from 'react'

import {useIntegrationApiStateMethods} from '@bpinc/ad-integration-api-state-context'
import type {InteractionAssociatedObject} from '@bpinc/ad-interaction-state-types'
import {SmCheckbox} from '@bpinc/lib-external-ui-elements'
import {useInteractionId} from '@bpinc/lib-interaction-context'
import {useLocalizationState} from '@bpinc/state-localization-context'


type AssociatedCRMObjectCheckboxProps = {
    record: InteractionAssociatedObject
    checked: boolean
    handleChange: (recordId: string) => void
}

export const CrmRecordItem: React.FC<AssociatedCRMObjectCheckboxProps> = ({
    record,
    checked,
    handleChange,
}) => {

    const {t} = useLocalizationState()
    const {openRecord} = useIntegrationApiStateMethods()
    const interactionId = useInteractionId()

    const handleChangeCallback = useCallback(() => {
        handleChange(record.id)
    }, [handleChange, record.id])

    const handleRecordClick = useCallback(() => {
        openRecord(record, interactionId, {source: 'user', inNewTab: false})
    }, [openRecord, record, interactionId])

    return (
        <li className="flex list-none items-center overflow-hidden">
            <SmCheckbox
                id={record.id}
                checked={checked}
                onChange={handleChangeCallback}
                data-testid="crm-object-item"
                {...(checked ? {'data-selected': 'true'} : undefined)}
            />
            <div
                onClick={handleRecordClick}
                className="ml-3 grow cursor-pointer truncate px-1.5 py-2.5 hover:bg-menu-bg-hover"
                title={t.DISPOSITION.resolution_crm_object_click_tooltip(record.displayName)}
                data-testid="crm-object-name"
            >
                {record.displayName}
            </div>
        </li>
    )
}
