import type {ActionCase} from '@bpinc/ad-request-urls'
import {checkConditions} from '@bpinc/lib-form-builder'


type UseButtonConditionsParams = {
    values?: Record<string, unknown>
    actionCases?: ActionCase[]
}

export function useButtonActionConditionsChecker({values, actionCases}: UseButtonConditionsParams) {
    return actionCases?.some(actionCase => {
        return checkConditions({
            values,
            conditions: actionCase?.conditions,
        })
    })
}
