import {type FC, useEffect, useRef} from 'react'
import {useAtomValue, useSetAtom} from 'jotai'

import {useInteraction} from '@bpinc/ad-interaction-state-context'
import {useCommunicatorUIState} from '@bpinc/ad-main-page-ui-context'
import {createProviderWrapper} from '@bpinc/lib-helpers'
import {InteractionContextProvider} from '@bpinc/lib-interaction-context'
import {ButtonIcon, IconClose24} from '@bpinc/lib-jskit-ui'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {parallelItemsAtom, parallelItemsCountAtom} from '@bpinc/state-interaction-ui'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {InteractionsPage} from '../../../interactions'
import {parallelChatProviders} from '../../constants'
import type {ParallelChatInteractionProps} from '../../types'


const ParallelChatInteractionProviders = createProviderWrapper(parallelChatProviders)

export const ParallelChatInteraction: FC<ParallelChatInteractionProps> = ({
    parallelItem,
}) => {
    const {INTERACTIONS, KNOWLEDGE_BASE} = useLocalizationState('t')
    const item = useInteraction(parallelItem.activeItemId)
    const parallelItemsDispatch = useSetAtom(parallelItemsAtom)
    const parallelItemsCount = useAtomValue(parallelItemsCountAtom)
    const setParallelInteractionsWidth = useCommunicatorUIState(
        s => s.setParallelInteractionsWidth,
    )

    const ref = useRef<HTMLDivElement>(null)
    useEffect(() => {
        const element = ref.current
        if (!element) return

        element.style.opacity = '1'

        return () => {
            ref.current?.style.setProperty('opacity', '0')
        }
    }, [])

    if (!item || !parallelItem.activeItemId) {
        return (
            <div
                ref={ref}
                className="ml-2 transition-all bg-secondary-container-variant1"
            >
                <div
                    className={tcn(
                        'flex w-full items-center justify-between px-4 py-2',
                        'border-b border-neutral-100 bg-primary-background',
                    )}
                >
                    <div className="uppercase caption-xs text-neutral-disabled">
                        {INTERACTIONS.interaction_details()}
                    </div>
                    <ButtonIcon
                        tooltip={KNOWLEDGE_BASE.close_tab_text()}
                        onClick={() => {
                            parallelItemsDispatch({
                                type: 'remove',
                                payload: {index: parallelItem.index},
                            })
                            const decreaseWidthFactor = parallelItemsCount + 1
                            setParallelInteractionsWidth(
                                w => w - w / decreaseWidthFactor,
                            )
                        }}
                        className="p-0"
                    >
                        <IconClose24 className="!size-6" />
                    </ButtonIcon>
                </div>
                <div className="flex h-full flex-col items-center justify-center">
                    <h1 className="heading-s text-neutral-on-secondary">{INTERACTIONS.no_new_interactions()}</h1>
                    <p className="text-neutral-fade">{INTERACTIONS.next_message_interaction_show()}</p>
                </div>
            </div>
        )
    }

    return (
        <div
            className={tcn(
                'flex flex-col',
                'ml-2 flex-auto shrink-0 overflow-hidden',
                'border-2 border-t-[6px] transition-all duration-300 border-on-primary',
                'has-[:focus-within]:border-2 has-[:focus-within]:border-t-[6px] has-[:focus-within]:border-primary',
            )}
        >
            <InteractionContextProvider interactionId={parallelItem.activeItemId} isParallelInteractionContext>
                <ParallelChatInteractionProviders>
                    <InteractionsPage />
                </ParallelChatInteractionProviders>
            </InteractionContextProvider>
        </div>
    )
}
