import type {FC} from 'react'
import React from 'react'

import {CircleButton, IconRecording16, IconRecordingActive16} from '@bpinc/lib-jskit-ui'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {useLocalizationState} from '@bpinc/state-localization-context'

import type {ControlButton} from './types'


export const RecordButton: FC<ControlButton> = ({
    isActive,
    onClick,
    className,
    disabled,
}) => {
    const {t} = useLocalizationState()
    return (
        <CircleButton
            onClick={onClick}
            isActive={isActive}
            disabled={disabled}
            className={tcn(disabled ? 'icon-disabled' : 'icon-error', className)}
            tooltip={{
                content: isActive
                    ? t.CALL_DETAILS_FORM.interaction_recordStop_tooltip()
                    : t.CALL_DETAILS_FORM.interaction_recordStart_tooltip(),
            }}
            data-testid="call-recording-button"
            data-recording-active={isActive || undefined}
        >
            {isActive ? (
                <IconRecordingActive16 className="size-4.5" />
            ) : (
                <IconRecording16 className="size-4.5" />
            )}
        </CircleButton>
    )
}
