import {useCallback} from 'react'

import type {ContactDetailsType} from '@bpinc/ad-contacts-state-types'
import {useFormatting} from '@bpinc/ad-formatting-context'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {useMergeContactsActions} from './useMergeContactsActions'
import {useSearchPageTabsActions} from './useSearchPageTabsActions'
import type {ContactTab, CreateContactTab} from './utils'


interface OpenContactItem {
    id: string
    firstName: string | undefined
    lastName: string | undefined
}

export function useContactsActions() {
    const {t} = useLocalizationState()
    const {handleTabOpen, handleTabClose, handleTabUpdate} = useSearchPageTabsActions()
    const {formatFullName} = useFormatting()
    const {addContactForMerge} = useMergeContactsActions()

    const handleContactOpen = useCallback((item: OpenContactItem, editMode?: boolean) => {
        const newTab: ContactTab = {
            id: item.id,
            type: 'contact',
            title: formatFullName(item.firstName, item.lastName),
            props: {
                editMode,
                onMerge: addContactForMerge ? () => addContactForMerge(item.id) : undefined,
                onEditMode: edit => handleTabUpdate({...newTab, props: {...newTab.props, editMode: edit}}),
            },
            closeable: true,
        }

        handleTabOpen(newTab)
    }, [addContactForMerge, formatFullName, handleTabOpen, handleTabUpdate])

    const handleContactCreate = useCallback((
        title?: string,
        initialData?: ContactDetailsType,
        onContactCreated?: (contactId: string) => void,
    ) => {
        const newTab: CreateContactTab = {
            id: 'create',
            type: 'create_contact',
            title: title || t.GLOBAL.anonymous(),
            closeable: true,
            props: {
                initialData,
                onContactCreated,
            },
        }
        handleTabOpen(newTab)
    }, [t, handleTabOpen])

    const handleContactCreateCancel = useCallback(() => {
        handleTabClose('create')
    }, [handleTabClose])

    return {handleContactCreate, handleContactOpen, handleContactCreateCancel}
}
