import React from 'react'

import {createComponentLoader} from '@bpinc/lib-ui-elements'

import {CalendarHeader} from './CalendarHeader'


const {CalendarBodyLoader} = createComponentLoader('calendar', () => import('calendar'), 'CalendarBody')

export const Calendar = () => {
    return (
        <div
            data-testid="calendar-page"
            className="flex h-full flex-col"
        >
            <CalendarHeader />
            <CalendarBodyLoader />
        </div>
    )
}
