import {useMemo} from 'react'

import {useAgentState} from '@bpinc/ad-agent-state-context'
import {useInteraction, useInteractions} from '@bpinc/ad-interaction-state-context'
import {isInternalChat, isPotentialInteraction} from '@bpinc/ad-interaction-state-methods'
import {useNonAssertedInteractionId} from '@bpinc/lib-interaction-context'


//TODO: Do we really need this hook?
export function useActiveOrLastInteraction() {
    const interactions = useInteractions()
    const interactionId = useNonAssertedInteractionId()
    const activeItem = useInteraction(interactionId)

    const {
        reservedForIncomingInteraction,
        reservedForIncomingInteractionTimeout,
    } = useAgentState()

    return useMemo(
        function memoizeActiveOrLastInteraction() {
            if (!(interactions && interactions.length)) {
                return null
            }

            if (!activeItem) {
                if (
                    reservedForIncomingInteraction ||
                    reservedForIncomingInteractionTimeout
                ) {
                    return (
                        interactions
                            .reverse()
                            .find(
                                interaction =>
                                    isInternalChat(interaction) ||
                                        isPotentialInteraction(interaction),
                            ) ?? null
                    )
                }
                return interactions[interactions.length - 1]
            }

            return activeItem
        },
        [
            activeItem,
            interactions,
            reservedForIncomingInteraction,
            reservedForIncomingInteractionTimeout,
        ],
    )
}
