import React from 'react'

import {useInteractionProp, useInteractionStateMethods} from '@bpinc/ad-interaction-state-context'
import {useOptimisticActivation} from '@bpinc/ad-interaction-state-hooks/src/hooks/useOptimisticActivation'
import {getInteractionStepId} from '@bpinc/ad-interaction-state-methods'
import {FlagMode} from '@bpinc/ad-interaction-state-types'
import {useNonAssertedInteractionId} from '@bpinc/lib-interaction-context'
import {ButtonIcon, IconFlag16} from '@bpinc/lib-jskit-ui'
import {useLocalizationState} from '@bpinc/state-localization-context'


export const FlagButton: React.FC<{className?: string}> = ({className}) => {
    const {t} = useLocalizationState()
    const interactionId = useNonAssertedInteractionId()
    const {flagItem} = useInteractionStateMethods()
    const isFlagged = useInteractionProp(interactionId, activeItem => !!activeItem?.displayData?.isFlagged)
    const interactionStepId = useInteractionProp(interactionId, getInteractionStepId)

    const [optimisticIsFlagged, toggleOptimistigIsFlagged] = useOptimisticActivation(isFlagged)

    function handleClick() {
        toggleOptimistigIsFlagged()
        flagItem(interactionStepId, isFlagged ? FlagMode.unflag : FlagMode.flag)
    }

    return (
        <ButtonIcon
            active={optimisticIsFlagged}
            onClick={handleClick}
            className={className}
            tooltip={isFlagged ? t.SHORTCUTS_DIALOG_BOX.shortcuts_alt_shift_m() : t.SHORTCUTS_DIALOG_BOX.shortcuts_alt_m()}
            data-testid="flag-button"
            data-flagged={isFlagged || undefined}
        >
            <IconFlag16 />
        </ButtonIcon>
    )
}
