import React, {useMemo} from 'react'
import {createColumnHelper} from '@tanstack/react-table'

import type {QueueItem} from '@bpinc/ad-request-urls'
import {Checkbox} from '@bpinc/lib-ui-elements'

import {useBasicColumns} from '../useBasicColumns'


const columnHelper = createColumnHelper<QueueItem>()


export function useColumns() {
    const basicColumns = useBasicColumns()

    return useMemo(() => [
        columnHelper.display({
            id: 'select',
            header: ({table}) => (
                <div className="w-9.5 px-2.5 pt-1">
                    <Checkbox
                        {...{
                            checked: table.getIsAllRowsSelected(),
                            indeterminate: table.getIsSomeRowsSelected(),
                            onChange: checked =>
                                table.toggleAllPageRowsSelected(checked),
                        }}
                    />
                </div>
            ),
            cell: ({row}) => (
                <div className="w-9.5 px-2.5 pt-1">
                    <Checkbox
                        {...{
                            checked: row.getIsSelected(),
                            disabled: !row.getCanSelect(),
                            indeterminate: row.getIsSomeSelected(),
                            onChange: checked =>
                                row.toggleSelected(checked),
                        }}
                    />
                </div>
            ),
            size: 38,
        }),
        ...basicColumns,
    ], [basicColumns])
}
