import {useCaseStateMethods} from '@bpinc/ad-case-state-context'
import {useActivityHistoryOpener, useCallHandler} from '@bpinc/ad-interaction-state-hooks'
import type {FavoritesChannelMedia} from '@bpinc/ad-transport-types'
import {AppRoutes, useNavigationState} from '@bpinc/lib-communicator-navigation-state'
import {useEvent} from '@bpinc/lib-helpers'
import {Notifications, useNotificationsState} from '@bpinc/lib-notifications-context'
import {assertDefined} from '@bpinc/lib-types-helpers'
import type {FavoritesChannel, FavoritesItem} from '@bpinc/state-favorites-types'
import {
    isFavoritesEmailChannel,
    isFavoritesInternalChannel,
    isFavoritesMessengerChannel,
    isFavoritesPhoneChannel,
} from '@bpinc/state-favorites-types'
import {useLocalizationState} from '@bpinc/state-localization-context'


export type OnClickParams = {
    item: FavoritesItem
    channel: FavoritesChannel
    media?: FavoritesChannelMedia
}

export function useItemClickHandler(onClick?: (params: OnClickParams) => void) {

    const {CONTACT_POPUP: i18n} = useLocalizationState('t')
    const {createCaseEmail} = useCaseStateMethods()
    const {showNotification} = useNotificationsState('methods')
    const setActivePage = useNavigationState('setActivePage')

    const openActivityHistory = useActivityHistoryOpener()
    const handleCall = useCallHandler()

    return useEvent(({item, channel, media}: OnClickParams) => {
        if (onClick) {
            return onClick({item, channel, media})
        }

        const {
            contactId,
            userId,
            emiUserId,
            entryId,
            userDetails,
            firstName,
            lastName,
            emiUserType,
            emiPhotoUrl,
        } = item

        const anyUserId = userId || entryId || emiUserId

        if (isFavoritesPhoneChannel(channel) || isFavoritesInternalChannel(channel)) {
            assertDefined(media)
            if (media === 'voice') {
                handleCall({
                    userId: anyUserId,
                    contactId,
                    firstName,
                    lastName,
                    phone: channel.number ?? '',
                    origination: 'favorites',
                    afterCallSideEffectCallback: () => {
                        setActivePage({
                            name: AppRoutes.Interactions,
                            params: {contactId},
                        })
                    },
                })
            } else {
                if (emiUserType === 'msteams' && !userDetails?.hasChat) {
                    return showNotification({
                        type: 'error',
                        id: Notifications.MSTeamsNoChatWithUserError,
                        tags: ['current-view'],
                        message: i18n.contact_msteams_no_chat(),
                        duration: 7000,
                    })
                }
                openActivityHistory({
                    contactId,
                    phone: channel.number,
                    firstName,
                    lastName,
                    userId: anyUserId,
                    origination: 'favorites',
                    emiUserType,
                    emiPhotoUrl,
                })
            }
        }

        if (isFavoritesEmailChannel(channel)) {
            createCaseEmail({
                contactId,
                emailAddress: channel.email,
            })
        }

        if (isFavoritesMessengerChannel(channel)) {
            openActivityHistory({
                contactId,
                firstName,
                lastName,
                userId: anyUserId,
                origination: 'favorites',
            })
        }
    })
}
