import React from 'react'

import {useUnreadAmount} from '../hooks/useUnreadAmount'

import styles from '../styles/InteractionBar.module.less'


export const InteractionBarUnread = () => {
    const unread = useUnreadAmount()
    return (
        <>
            {!!unread && (
                <div className={styles.unread} data-testid="unread-counter">
                    {unread}
                </div>
            )}
        </>
    )
}
