import React from 'react'

import {useSessionState} from '@bpinc/ad-session-context'
import {useCommunicatorUIState} from '@bpinc/lib-communicator-ui-context'
import {SearchForInteractionControls} from '@bpinc/lib-ui-elements'

import {useHandlers} from '../hooks/useHandlers'
import {PhoneInput} from './PhoneInput'


export const DialpadForm: React.FC = () => {
    const {searchValue} = useCommunicatorUIState('dialpad')
    const {onCallClick, onChatClick} = useHandlers()
    const {phoneDeviceCapabilities} = useSessionState()

    return (
        <div className="flex items-start gap-2 pr-3.5">
            <PhoneInput />
            <SearchForInteractionControls
                searchQuery={searchValue}
                onCallClick={onCallClick}
                onChatClick={onChatClick}
                callButtonDisabled={!phoneDeviceCapabilities.canDial}
            />
        </div>
    )
}
