import React from 'react'

import type {User} from '../../helpers'
import {isDirectoryEntry, isExternalUser, isMsTeamsChannel, isMsTeamsUser} from '../../helpers'
import {AgentControls} from './AgentControls'
import {DirectoryEntryControls} from './DirectoryEntryControls'
import {ExternalUserControls} from './ExternalUserControls'
import {MsTeamsChannelControls} from './MsTeamsChannelControls'
import {MsTeamsChatControls} from './MsTeamsChatControls'


export const ContactDetailsControls: React.FC<{
    user: User
}> = ({user}) => {
    return isExternalUser(user) ? (
        <ExternalUserControls user={user} />
    ) : isMsTeamsChannel(user) ? (
        <MsTeamsChannelControls user={user} />
    ) : isMsTeamsUser(user) ? (
        <MsTeamsChatControls user={user} />
    ) : isDirectoryEntry(user) ? (
        <DirectoryEntryControls item={user} />
    ) : (
        <AgentControls user={user} />
    )
}
