import {useCallback, useMemo} from 'react'

import {useIsCommunicator} from '@bpinc/ad-current-tab-data-context'
import {useIntegrationApiStateMethods, useIntegrationApiStateRef} from '@bpinc/ad-integration-api-state-context'
import {useInteraction, useInteractions, useInteractionStateMethods} from '@bpinc/ad-interaction-state-context'
import {
    getGlobalInteractionId,
    getMainItem,
    getServiceId,
    hasConsultCalls,
    isCallConference,
    isChatConference,
    isConsultCall,
} from '@bpinc/ad-interaction-state-methods'
import type {CallParty, CustomTransferData} from '@bpinc/ad-interaction-state-types'
import {usePrerecordedMessagesList} from '@bpinc/ad-prerecorded-messages-state-context'
import {useRemoteAssistState, useRemoteAssistStateMethods} from '@bpinc/ad-remote-assist-state-context'
import {useRescueState} from '@bpinc/ad-rescue-state-context'
import {useSessionState} from '@bpinc/ad-session-context'
import type {SmICONS} from '@bpinc/lib-external-ui-elements'
import type {Toggle} from '@bpinc/lib-helpers'
import {useAsRef} from '@bpinc/lib-helpers'
import {useInteractionId} from '@bpinc/lib-interaction-context'
import {IconRemoteAssist, IconScreenshare} from '@bpinc/lib-jskit-ui'
import type {SVGRComponent} from '@bpinc/lib-types-helpers'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {useOpenRescueModal} from '../../../../modals'
import {useScheduleReminderOption} from './useScheduleReminderOption'

import styles from '../../styles/InteractionsPage.module.less'


type SendScreenReceiver = {
    userItemId: string
    userId: string
}

export type MoreOption = {
    onClick: () => void
    tooltip: string
    className?: string
    activeClassName?: string
    'data-testid': string
    iconName?: SmICONS //semantic icon name
    Icon?: SVGRComponent
    active?: boolean
    disabled?: boolean
}

export function useMoreButtons(toggleList: Toggle) {
    const {t} = useLocalizationState()
    const integrationApiStateRef = useIntegrationApiStateRef()
    const {requestTransferData} = useIntegrationApiStateMethods()
    const {sendScreenPopData} = useInteractionStateMethods()
    const customRecordings = usePrerecordedMessagesList()
    const interactionId = useInteractionId()
    const items = useInteractions()
    const activeItem = useInteraction(interactionId)
    const {isEnabled: isRescueEnabled} = useRescueState()
    const {tenantFeatures, privileges} = useSessionState()
    const {openRemoteAssist} = useRemoteAssistStateMethods()
    const {items: raItems} = useRemoteAssistState()
    const openRescueModal = useOpenRescueModal()
    const isCommunicator = useIsCommunicator()

    const serviceId = getServiceId(activeItem)
    const globalInteractionId = getGlobalInteractionId(activeItem)
    const remoteAssistEnabled = !!tenantFeatures?.nativeRemoteAssistEnabled && !!privileges.SYS_USE_REMOTE_ASSIST
    const itemRaSession = globalInteractionId ? raItems[globalInteractionId] : undefined
    const raSessionEstablished = !!itemRaSession?.sessionId
    const raSessionPin = itemRaSession?.pin
    const raSessionId = itemRaSession?.sessionId

    const giidRef = useAsRef(globalInteractionId)
    const activeItemRef = useAsRef(activeItem)
    const raSessionPinRef = useAsRef(raSessionPin)
    const raSessionIdRef = useAsRef(raSessionId)
    const callInDeliveredState = activeItem?.state === 'delivered' || activeItem?.state === 'hold'
    const callInWrapUpState = activeItem?.state === 'wrap_up' || activeItem?.state === 'wrap_up_hold'
    const showScheduleReminderButton =
        (callInDeliveredState || callInWrapUpState) &&
        privileges.CAN_USE_CALENDAR &&
        !isCommunicator
    const scheduleReminderOption = useScheduleReminderOption()

    const handleRescueClick = useCallback(() => {
        openRescueModal({type: 'voice'})
        toggleList(false)
    }, [openRescueModal, toggleList])

    const handleRemoteAssistClick = useCallback(() => {
        if (!giidRef.current) {
            return
        }
        openRemoteAssist(
            {
                globalInteractionId: giidRef.current,
                popup: true,
            },
            {
                action: 'create',
                giid: giidRef.current,
                mediaType: activeItemRef.current?.mediaType || 'unknown',
                caseId: activeItemRef.current?.emailData?.caseId,
                contactId: activeItemRef.current?.contactId,
                clientFirstName: activeItemRef.current?.displayData.firstName,
                clientLastName: activeItemRef.current?.displayData.lastName,
                clientDisplayName: activeItemRef.current?.displayData.displayName,
            },
        )
    }, [giidRef, activeItemRef, openRemoteAssist])

    const mainItemId = getMainItem(items, activeItem)?.id
    const activeItemHasConsultCalls = hasConsultCalls(activeItem)
    const activeItemIsConsultCall = isConsultCall(activeItem)
    const activeItemParties = activeItem?.parties
    const activeItemIsCallConference = isCallConference(activeItem)
    const activeItemIsChatConference = isChatConference(activeItem)
    const activeItemHaveAgents = !!activeItemParties?.some(party => !!party.userId)

    const canSendScreen = useMemo(() => {
        return (
            activeItemHasConsultCalls ||
            activeItemIsConsultCall ||
            activeItemIsCallConference ||
            activeItemIsChatConference ||
            activeItemHaveAgents
        )
    }, [
        activeItemHasConsultCalls,
        activeItemIsConsultCall,
        activeItemIsCallConference,
        activeItemIsChatConference,
        activeItemHaveAgents,
    ])

    const sendScreen = useCallback(
        async (receivers: SendScreenReceiver[]) => {
            if (mainItemId && receivers.length) {
                let customData: CustomTransferData = {}
                if (integrationApiStateRef.current.isAvailable) {
                    try {
                        customData = await requestTransferData(mainItemId)
                    } catch {
                        /**/
                    }
                }
                receivers.forEach(({userItemId, userId}) =>
                    sendScreenPopData({
                        targetItemId: mainItemId,
                        userId,
                        userItemId,
                        customData,
                        raSessionPin: raSessionPinRef.current,
                        raSessionId: raSessionIdRef.current,
                    }),
                )
            }
        },
        [mainItemId, integrationApiStateRef, sendScreenPopData, requestTransferData, raSessionPinRef, raSessionIdRef],
    )

    const prepareSendScreen = useCallback(async () => {
        toggleList(false)

        if (activeItem) {
            let receivers: SendScreenReceiver[] | null = null
            if (
                activeItemIsCallConference ||
                activeItemIsChatConference ||
                activeItemIsConsultCall ||
                activeItemHaveAgents
            ) {
                if (activeItem.parties) {
                    receivers = activeItem.parties
                        .filter(({theirItemId, userId}) => theirItemId && userId)
                        .map(({theirItemId, userId}) => ({
                            userItemId: theirItemId,
                            userId,
                        }))
                        .filter(party => !!party.userId) as SendScreenReceiver[]
                }
            } else if (activeItemHasConsultCalls) {
                receivers = (
                    activeItem.displayData
                        .hasConsultCalls!.flatMap(itemId => items.find(item => item.id === itemId)?.parties)
                        .filter(party => party?.theirItemId && party?.userId) as CallParty[]
                ).map(({theirItemId, userId}) => ({
                    userItemId: theirItemId!,
                    userId: userId!,
                }))
            }
            if (receivers) {
                await sendScreen(receivers)
            }
        }
    }, [
        toggleList,
        activeItem,
        activeItemIsCallConference,
        activeItemIsChatConference,
        activeItemIsConsultCall,
        activeItemHaveAgents,
        activeItemHasConsultCalls,
        items,
        sendScreen,
    ])

    return useMemo(
        function memoizeMoreButtons() {
            const moreButtons: MoreOption[] = []

            if (remoteAssistEnabled) {
                moreButtons.push({
                    className: styles.rescue,
                    onClick: handleRemoteAssistClick,
                    tooltip: t.INTERNAL_CHAT.chat_remote_assist(),
                    active: false,
                    Icon: IconRemoteAssist,
                    'data-testid': 'remote-assist-button',
                    disabled: raSessionEstablished,
                })
            } else if (isRescueEnabled) {
                moreButtons.push({
                    className: styles.rescue,
                    onClick: handleRescueClick,
                    tooltip: t.CALL_DETAILS_FORM.button_remote_access_tooltip(),
                    active: false,
                    Icon: IconRemoteAssist,
                    'data-testid': 'remote-assist-button',
                })
            }

            if (canSendScreen) {
                moreButtons.push({
                    tooltip: t.CALL_DETAILS_FORM.interaction_sendScreen_tooltip(),
                    active: false,
                    onClick: prepareSendScreen,
                    Icon: IconScreenshare,
                    'data-testid': 'send-screen-button',
                })
            }

            if (showScheduleReminderButton) {
                moreButtons.push(scheduleReminderOption)
            }

            return moreButtons
        },
        [
            scheduleReminderOption,
            showScheduleReminderButton,
            serviceId,
            remoteAssistEnabled,
            isRescueEnabled,
            customRecordings.length,
            canSendScreen,
            handleRemoteAssistClick,
            t.INTERNAL_CHAT,
            t.CALL_DETAILS_FORM,
            t.PLAY_CANNED_PROMPTS_DIALOG_BOX,
            raSessionEstablished,
            handleRescueClick,
            prepareSendScreen,
        ],
    )
}
