import React, {useEffect, useRef} from 'react'
import * as Popover from '@radix-ui/react-popover'

import {useBoolean, useEvent} from '@bpinc/lib-helpers'
import type {DispositionOption} from '@bpinc/lib-interaction-ui-hooks'
import {useDispositionSelect} from '@bpinc/lib-interaction-ui-hooks'
import {FieldFrame, FieldLayout, IconArrowDown16, Input} from '@bpinc/lib-jskit-ui'
import {Notifications, useNotificationsState} from '@bpinc/lib-notifications-context'
import {useLocalizationState} from '@bpinc/state-localization-context'

//TODO: Move DispositionRescheduleForm closer to components that use it?
import {useSearchDisposition} from '../../hooks/useSearchDisposition'
import type {DispositionSelectProps} from '../../types'
import {DispositionsAccordion} from './DispositionsAccordion'
import {DispositionsList} from './DispositionsList'


export const DispositionSelect: React.FC<DispositionSelectProps> = React.memo(
    function DispositionSelect({dispositionsListClassName, accordion}) {
        const {t} = useLocalizationState()
        const {
            options,
            value: selectedDisposition,
            onSelect,
            dispositionRequired,
            defaultOption,
        } = useDispositionSelect()
        const {searchValue, setSearch, optionsFilteredBySearch} =
            useSearchDisposition(options)

        const searchOpened = useBoolean()
        const notifications = useNotificationsState('notifications')
        const dispositionNotSelectedError = notifications[Notifications.DispositionNotSelectedError]


        const handleSelect = useEvent((option: DispositionOption) => {
            onSelect(option)
            setSearch(option.label)
            searchOpened.setFalse()
        })

        const inputRef = useRef<HTMLInputElement>(null)
        const popoverContentRef = useRef<HTMLDivElement>(null)

        const focusInput = useEvent(() => {
            inputRef.current?.focus()
            searchOpened.setTrue()
        })

        useEffect(function focusInputOnError() {
            if (dispositionNotSelectedError) {
                focusInput()
            }
        }, [dispositionNotSelectedError, focusInput])

        return (
            <Popover.Root
                open={searchOpened.value}
                onOpenChange={searchOpened.toggle}
            >
                <Popover.Trigger
                    asChild
                    className="group"
                    onClick={e => {
                        e.preventDefault()
                        inputRef.current?.focus()
                        searchOpened.setTrue()
                    }}
                    onKeyDown={e => {
                        switch (e.key) {
                            case 'ArrowDown':
                                if (e.currentTarget.dataset.state === 'closed') {
                                    e.preventDefault()
                                    openWithSelection()
                                    return
                                }
                                selectNextItem('down')
                                break
                            case 'ArrowUp':
                                e.preventDefault()
                                selectNextItem('up')
                                break
                            case 'Enter':
                                e.preventDefault()
                                if (e.currentTarget.dataset.state === 'open') {
                                    const validOptions = optionsFilteredBySearch.filter(o => !o.isGroupLabel)
                                    const onlyOneOption = validOptions.length === 1

                                    if (onlyOneOption) {
                                        handleSelect(validOptions[0])
                                        searchOpened.setFalse()
                                        return
                                    }
                                    clickSelectedItem()
                                } else {
                                    openWithSelection()
                                }
                                break
                            case 'Escape':
                                e.preventDefault()
                                e.stopPropagation()
                                searchOpened.setFalse()
                                break
                            default: {
                                const isPrintableKey = e.key.length === 1 && (e.key.match(/\S/))

                                if (isPrintableKey || e.key === 'Backspace') {
                                    searchOpened.setTrue()
                                    onSelect(defaultOption)
                                }
                            }
                        }

                        function selectNextItem(direction: 'up' | 'down') {
                            const selectableItems = Array.from(
                                popoverContentRef.current?.querySelectorAll('[data-selectable="dispositions-accordion"]') ?? [],
                            )
                            const selectedIndex = selectableItems.findIndex(i => i.hasAttribute('data-selected'))

                            if (selectedIndex === -1) {
                                selectableItems[0]?.setAttribute('data-selected', 'true')
                            } else {
                                selectableItems[selectedIndex].removeAttribute('data-selected')
                                const nextIndex = direction === 'down'
                                    ? (selectedIndex + 1) % selectableItems.length
                                    : (selectedIndex - 1 + selectableItems.length) % selectableItems.length
                                selectableItems[nextIndex].setAttribute('data-selected', 'true')
                                selectableItems[nextIndex].scrollIntoView()
                            }
                        }

                        function clickSelectedItem() {
                            const selectedElement = popoverContentRef.current?.querySelector('[data-selected="true"]')
                            if (selectedElement instanceof HTMLElement) {
                                selectedElement.click()
                            }
                        }

                        function openWithSelection() {
                            searchOpened.setTrue()
                            setTimeout(() => {
                                selectNextItem('down')
                            })
                        }

                    }}
                >
                    <FieldLayout
                        error={dispositionNotSelectedError?.message}
                        label={t.GLOBAL.disposition()}
                        className="w-full"
                        required={dispositionRequired}
                    >
                        <FieldFrame
                            focused={searchOpened.value}
                            error={Boolean(dispositionNotSelectedError)}
                            rightPrefix={
                                <IconArrowDown16 className="cursor-pointer group-data-[state=open]:rotate-180" />
                            }
                        >
                            <Input
                                ref={inputRef}
                                placeholder={t.DISPOSITION.select_disposition()}
                                autoFocus
                                value={searchValue}
                                onChange={e => setSearch(e.target.value)}
                                onBlur={() => {
                                    if (
                                        searchValue !==
                                            selectedDisposition.label
                                    ) {
                                        setSearch('')
                                    }
                                }}
                                aria-label={t.DISPOSITION.disposition_search_input_hint()}
                                data-testid="disposition-search-input"
                            />
                        </FieldFrame>
                    </FieldLayout>
                </Popover.Trigger>
                <Popover.Portal>
                    <Popover.Content
                        ref={popoverContentRef}
                        sideOffset={2}
                        collisionPadding={20}
                        className="w-[--radix-popover-trigger-width] border shadow-2  border-default"
                        onOpenAutoFocus={e => {
                            e.preventDefault()
                        }}
                    >
                        {!!optionsFilteredBySearch.length && (
                            <>
                                {accordion ? (
                                    <DispositionsAccordion
                                        className={
                                            dispositionsListClassName
                                        }
                                        selectedDisposition={
                                            selectedDisposition
                                        }
                                        onSelect={handleSelect}
                                        list={optionsFilteredBySearch}
                                        searchValue={searchValue}
                                    />
                                ) : (
                                    <DispositionsList
                                        className={
                                            dispositionsListClassName
                                        }
                                        selectedDisposition={
                                            selectedDisposition
                                        }
                                        onSelect={handleSelect}
                                        list={optionsFilteredBySearch}
                                        highlight={searchValue}
                                    />
                                )}
                            </>
                        )}
                    </Popover.Content>
                </Popover.Portal>
            </Popover.Root>
        )
    },
)
