import {useCallback} from 'react'

import {useSessionState} from '@bpinc/ad-session-context'


export const useAttachmentLink = () => {

    const {serverOrigin, sessionId, loginId, domain} = useSessionState()

    const generateUrl = useCallback((attachmentId: string) => {
        const params = {
            token: sessionId,
            user: loginId,
            tenant: domain,
            id: attachmentId,
            no_disposition: 0,
        }
        const query = Object.keys(params).map(key => `${key}=${params[key as keyof typeof params]}`).join('&')
        const fileLink = `${serverOrigin}/agentdesktop/agentdesktop/attachmentDownload?${query}`
        return fileLink
    }, [serverOrigin, sessionId, loginId, domain])

    return {generateUrl}
}
