import React, {useEffect, useMemo, useState} from 'react'

import {useDIDNumberLocalization, useDidState} from '@bpinc/ad-did-context'
import {useDIDNumberState} from '@bpinc/ad-did-number-context'
import type {DIDNumber} from '@bpinc/ad-did-types'
import {useInteraction} from '@bpinc/ad-interaction-state-context'
import {getServiceId, getServiceName, isExternalChat} from '@bpinc/ad-interaction-state-methods'
import {useServiceState} from '@bpinc/ad-service-context'
import {useServicesDispositionsState} from '@bpinc/ad-services-dispositions-context'
import type {Service} from '@bpinc/ad-services-dispositions-types'
import {useCommunicatorUIState} from '@bpinc/lib-communicator-ui-context'
import {useEvent} from '@bpinc/lib-helpers'
import type {SelectItemType} from '@bpinc/lib-jskit-ui'
import {SelectField} from '@bpinc/lib-jskit-ui'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {useLocalizationState} from '@bpinc/state-localization-context'


function serviceFilter(service: Pick<Service, 'id' | 'name' | 'type' | 'notUserSelectable'>): boolean {
    return ['INBOUND', 'OUTBOUND', 'BLENDED'].includes(service.type) && !service.notUserSelectable
}

function serviceToOption({id, name}: Pick<Service, 'id' | 'name'>): SelectItemType {
    return {
        value: id,
        content: name,
    }
}

function didNumberToOption(
    didNumber: DIDNumber,
    normalizer = (didNumberInner: DIDNumber) => didNumberInner.externalNumber.toString(),
): SelectItemType {
    return {
        value: didNumber.id,
        content: normalizer(didNumber),
    }
}

type ServiceSelectProps = {
    itemId: string | undefined
    trackVisibility?: boolean
    labeled?: boolean
    className?: string
}

export const ServiceSelect: React.FC<ServiceSelectProps> = React.memo(
    ({className, itemId, trackVisibility = true, labeled}) => {
        const [selectedOption, setSelectedOption] = useState<string | undefined>(() =>
            (selectedService && services[selectedService])
                ? serviceToOption(services[selectedService]).value
                : undefined,
        )

        const t = useLocalizationState('t')
        const didNumbers = useDidState('didNumbers')
        const {services} = useServicesDispositionsState()
        const setServiceSelectorVisibility = useCommunicatorUIState('setServiceSelectorVisibility')
        const {selectedDIDNumber, setDIDNumber} = useDIDNumberState()

        const {selectedService, setService} = useServiceState()
        const localizeDIDNumber = useDIDNumberLocalization()
        const activeItem = useInteraction(itemId)

        const itemServiceId = getServiceId(activeItem)
        const itemServiceName = useMemo(() => {
            return getServiceName(services, activeItem, isExternalChat(activeItem))
        }, [services, activeItem])

        useEffect(() => {
            if (trackVisibility) {
                setServiceSelectorVisibility(true)
            }
            return () => {
                if (trackVisibility) {
                    setServiceSelectorVisibility(false)
                }
            }
        }, [setServiceSelectorVisibility, trackVisibility])

        const serviceOptions = useMemo<SelectItemType[]>(() => {

            const availableServices = {...services}
            if (itemServiceId) {
                availableServices[itemServiceId] = {
                    ...availableServices[itemServiceId],
                    id: itemServiceId,
                    name: itemServiceName,
                    type: 'BLENDED' as const, //used to pass serviceFilter
                    notUserSelectable: false,
                }
            }

            return [
                ...Object.values(availableServices)
                    .filter(serviceFilter)
                    .map(serviceToOption),

                ...Object.values(didNumbers)
                    .map(didNumber => didNumberToOption(didNumber, localizeDIDNumber)),
            ]
        }, [didNumbers, localizeDIDNumber, itemServiceId, itemServiceName, services])

        const handleSelect = useEvent(async (value?: string) => {
            updateSelectValue()
            async function updateSelectValue() {

                if (!value) {
                    await setService(value, itemId)
                    await setDIDNumber(value)

                } else if (value in didNumbers) {
                    await setDIDNumber(value)
                    await setService(undefined, itemId)

                } else if (value in services) {
                    await setDIDNumber(undefined)
                    await setService(value, itemId)
                }
            }
        })

        useEffect(() => {
            if (selectedService && services[selectedService]) {
                setSelectedOption(serviceToOption(services[selectedService]).value)
            } else if (selectedDIDNumber && didNumbers[selectedDIDNumber]) {
                setSelectedOption(didNumberToOption(didNumbers[selectedDIDNumber]).value)
            } else {
                setSelectedOption(undefined)
            }
        }, [didNumbers, selectedDIDNumber, selectedService, services])

        return (
            <SelectField
                clearable
                inPortal={false}
                value={selectedOption}
                options={serviceOptions}
                onChange={handleSelect}
                placeholder={labeled ? t.GLOBAL.select() : t.GLOBAL.service_selector_no_value()}
                label={labeled ? t.GLOBAL.service_selector_label() : undefined}
                data-testid="service-selector"
                className="w-full px-2 py-[7.5px]"
                layoutClassName={tcn('w-full', className)}
            />
        )
    },
)
