import React, {useEffect} from 'react'
import {useAtom} from 'jotai'

import {useInteractionProp} from '@bpinc/ad-interaction-state-context'
import {getServiceId} from '@bpinc/ad-interaction-state-methods'
import {
    usePrerecordedMessagesState,
    usePrerecordedMessagesStateMethods,
} from '@bpinc/ad-prerecorded-messages-state-context'
import {useInteractionId} from '@bpinc/lib-interaction-context'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {modalSelectedMessageAtom} from './atoms'
import {PrerecordedItem} from './PrerecordedItem'


export const PrerecordedList: React.FC = () => {
    const {t} = useLocalizationState()
    const interactionId = useInteractionId()
    const serviceId = useInteractionProp(interactionId, getServiceId)
    const {list, serviceMessages} = usePrerecordedMessagesState()
    const {getServiceMessages} = usePrerecordedMessagesStateMethods()

    useEffect(() => {
        if (!serviceId) {
            return
        }
        getServiceMessages(serviceId)
    }, [getServiceMessages, serviceId])


    const currentServiceMessages = serviceId ? serviceMessages[serviceId] : undefined
    const [selected, setSelected] = useAtom(modalSelectedMessageAtom)

    const promptsTitleClassName = tcn(
        'p-4 uppercase caption-s text-neutral-disabled',
    )

    useEffect(() => {
        if (!selected) {
            setSelected(currentServiceMessages?.[0] || list[0])
        }
    }, [currentServiceMessages, list, selected, serviceId, serviceMessages, setSelected])

    if (currentServiceMessages?.length && list.length) {
        return (
            <>
                <h2 className={promptsTitleClassName}>
                    {t.PLAY_CANNED_PROMPTS_DIALOG_BOX.canned_prompts()}
                </h2>
                <ul>
                    {currentServiceMessages.map(prompt => (
                        <PrerecordedItem
                            selected={selected?.id === prompt.id}
                            onClick={setSelected}
                            prompt={prompt}
                            key={prompt.id}
                        />
                    ))}
                </ul>

                <h2 className={promptsTitleClassName}>
                    {t.PLAY_CANNED_PROMPTS_DIALOG_BOX.custom_prompts()}
                </h2>
                <ul>
                    {list.map(prompt => (
                        <PrerecordedItem
                            selected={selected?.id === prompt.id}
                            onClick={setSelected}
                            prompt={prompt}
                            key={prompt.id}
                        />
                    ))}
                </ul>
            </>
        )
    }

    if (currentServiceMessages?.length) {
        return (
            <ul>
                {currentServiceMessages.map(prompt => (
                    <PrerecordedItem
                        selected={selected?.id === prompt.id}
                        onClick={setSelected}
                        prompt={prompt}
                        key={prompt.id}
                    />
                ))}
            </ul>
        )
    }

    return (
        <ul>
            {list.map(prompt => (
                <PrerecordedItem
                    selected={selected?.id === prompt.id}
                    onClick={setSelected}
                    prompt={prompt}
                    key={prompt.id}
                />
            ))}
        </ul>
    )
}
