import type {ReactElement} from 'react'
import React, {useCallback, useEffect, useRef} from 'react'

import type {AuthorizedConnectors, BaseConnectors} from '@bpinc/ad-connected-state-types'
import {useDIDNumberState} from '@bpinc/ad-did-number-context'
import {ApiError} from '@bpinc/ad-integration-api-state-types'
import {useInteractionStateRef} from '@bpinc/ad-interaction-state-context'
import {useServiceState} from '@bpinc/ad-service-context'
import type {UISpecificRefValue} from '@bpinc/lib-communicator-integration-api-connection'
import {connectIntegrationApiAuthorized} from '@bpinc/lib-communicator-integration-api-connection'
import {AppRoutes, useNavigationState} from '@bpinc/lib-communicator-navigation-state'
import {useCommunicatorUIState} from '@bpinc/lib-communicator-ui-context'
import {useInteractionUIState} from '@bpinc/state-interaction-ui'


type IntegrationApiInitializerProps = {
    baseConnectors: BaseConnectors
    authorizedConnectors: AuthorizedConnectors
    children: ReactElement
}

export const IntegrationApiAuthorizedInitializer: React.FC<IntegrationApiInitializerProps> = ({
    baseConnectors,
    authorizedConnectors,
    children,
}) => {
    const {selectedService, setService} = useServiceState()
    const {selectedDIDNumber, setDIDNumber} = useDIDNumberState()
    const {items, setItemState} = useInteractionUIState()
    const interactionStateRef = useInteractionStateRef()
    const setChatMessageSuggestion = useCallback((message: string, allowOverwrite: boolean, interactionId: string) => {
        const item = items[interactionId]
        if (!item) {
            throw new ApiError('no_interaction', `No interaction with id "${interactionId}"`)
        }
        if (item.chatMessage && !allowOverwrite) {
            throw new ApiError('banned', 'Not allowed to overwrite the existing message')
        }
        setItemState(interactionId, oldState => ({
            ...oldState,
            chatMessage: message,
        }))
    }, [items, setItemState])

    const setDialPadValue = useCommunicatorUIState(s => s.dialpad.setSearchValue)
    const setActivePage = useNavigationState('setActivePage')

    const deferredDial = useCallback((number: string) => {
        setDialPadValue(number)
        setActivePage({name: AppRoutes.Dial})
    }, [setDialPadValue, setActivePage])

    const showInteractionPage = useCallback(() => {
        setActivePage({name: AppRoutes.Interactions})
    }, [setActivePage])

    const uiSpecificRef = useRef<UISpecificRefValue>({
        getService: () => selectedService,
        setService: s => setService(s),
        getDIDNumber: () => selectedDIDNumber,
        setDIDNumber: (number?: string) => setDIDNumber(number),
        setChatMessageSuggestion,
        deferredDial,
        showInteractionPage,
    })

    useEffect(() => {
        uiSpecificRef.current = {
            getDIDNumber: () => selectedDIDNumber,
            setDIDNumber: (number?: string) => setDIDNumber(number),
            getService: () => selectedService,
            setService: (service?: string) => setService(service, interactionStateRef.current?.activeItem?.id),
            setChatMessageSuggestion,
            deferredDial,
            showInteractionPage,
        }
    }, [
        interactionStateRef,
        selectedService,
        setService,
        deferredDial,
        selectedDIDNumber,
        setDIDNumber,
        setChatMessageSuggestion,
        showInteractionPage,
    ])

    useEffect(() => {
        const subscriptions = connectIntegrationApiAuthorized(baseConnectors, authorizedConnectors, uiSpecificRef)
        return () => {
            subscriptions.forEach(s => s.unsubscribe())
        }
    }, [baseConnectors, authorizedConnectors])

    return <>{children}</>
}
