import React, {useMemo} from 'react'
import {tw} from '@servicepattern/ui'
import {tv} from 'tailwind-variants'

import {useChatUnread, usePersonalMessagesUnreadAmount} from '@bpinc/ad-interaction-chat-state-context'
import {useInternalChatUnread} from '@bpinc/ad-interaction-internal-chat-state-context'
import {useInteractionStateSelector} from '@bpinc/ad-interaction-state-context'
import {
    getInteractionId,
    getInteractionStepId,
    isChatConference as isExternalChatConference,
    isExternalChat,
    isInternalChat,
    isRecentChat,
    isRecentExtChatConference,
} from '@bpinc/ad-interaction-state-methods'
import {useGroupChatTotalUnread} from '@bpinc/state-group-chat-context'
import type {RecentItem} from '@bpinc/state-recent-types'


type Props = {
    item: RecentItem
}

// TODO: review unread indicator logic
export const RecentItemUnreadIndicator: React.FC<Props> = ({item}) => {

    const internalInteractionId = useInteractionStateSelector(s => {
        const currentItem = s.items.find(i => isInternalChat(i) && i.userId === item.userId)
        return getInteractionId(currentItem)
    })

    const associatedActiveChatInteractionStepId = useInteractionStateSelector(s => {
        const currentItem = s.items.find(i =>
            (isExternalChat(i) || isExternalChatConference(i)) && i.contactId === item.contactId,
        )
        return getInteractionStepId(currentItem)
    })
    const activeChatUnreadAmount = useChatUnread(associatedActiveChatInteractionStepId)
    const internalChatUnreadAmount = useInternalChatUnread(internalInteractionId)

    const chatId = item.groupChatData?.chatId
    const groupChatUnreadAmount = useGroupChatTotalUnread(i => i.chatId === chatId)

    const personalMessagesUnreadAmount = usePersonalMessagesUnreadAmount(item.contactId ?? '')

    const unreadAmount = useMemo(function memoizeUnreadAmount() {
        if (internalChatUnreadAmount) {
            return internalChatUnreadAmount
        }

        if (groupChatUnreadAmount) {
            return groupChatUnreadAmount
        }

        if (!item.contactId || (!isRecentExtChatConference(item) && !isRecentChat(item))) {
            return 0
        }
        return personalMessagesUnreadAmount + activeChatUnreadAmount
    }, [
        activeChatUnreadAmount,
        internalChatUnreadAmount,
        item,
        personalMessagesUnreadAmount,
        groupChatUnreadAmount,
    ])

    if (!unreadAmount) {
        return null
    }

    const digitsNumber = String(unreadAmount).length

    const styles = tv({
        base: tw`flex items-center justify-center rounded-full bg-primary-700 caption-xs text-inverse-primary`,
        variants: {
            digitsNumber: {
                1: tw`size-3.5`,
                2: tw`size-4`,
                3: tw`size-5`,
            } as Record<number, string>,
        },
    })

    return (
        <div className={styles({digitsNumber})}>
            {unreadAmount}
        </div>
    )
}
