import {useEffect} from 'react'

import {useIsCommunicator} from '@bpinc/ad-current-tab-data-context'
import {useInteractionProp, useInteractionStateMethods} from '@bpinc/ad-interaction-state-context'
import {isAfterCallWork} from '@bpinc/ad-interaction-state-methods'
import {useCommunicatorUIState} from '@bpinc/ad-main-page-ui-context'
import {useAsRef} from '@bpinc/lib-helpers'
import {useInteractionId} from '@bpinc/lib-interaction-context'
import {Notifications, useNotificationsState} from '@bpinc/lib-notifications-context'
import type {InteractionUIItemStateUpdater} from '@bpinc/state-interaction-ui'


export function useAutoSelectResolutionTab(setItemState: InteractionUIItemStateUpdater): void {
    const interactionId = useInteractionId()
    const {notifications} = useNotificationsState()
    const itemInWrapUpState = useInteractionProp(interactionId, isAfterCallWork)
    const {activateItem} = useInteractionStateMethods()
    useEffect(() => {
        if (itemInWrapUpState) {
            setItemState(interactionId, oldState => ({
                ...oldState,
                activeTab: 'resolution',
            }))
        }
    }, [interactionId, itemInWrapUpState, setItemState])

    const errorAboutMissingDisposition = notifications[Notifications.DispositionNotSelectedError]
    const isCommunicator = useIsCommunicator()
    const interactionIdRef = useAsRef(interactionId)
    const setCommunicatorFloatingState = useCommunicatorUIState(s => s.setFloatingState)

    useEffect(() => {
        if (errorAboutMissingDisposition) {
            if (!isCommunicator) {
                setCommunicatorFloatingState(s => s === 'closed' ? 'floating' : s)
            }

            if (errorAboutMissingDisposition.interactionId && interactionIdRef.current !== errorAboutMissingDisposition.interactionId) {
                activateItem(errorAboutMissingDisposition.interactionId)
            }

            setItemState(interactionIdRef.current, oldState => ({
                ...oldState,
                activeTab: 'resolution',
            }))
        }
    }, [isCommunicator, setCommunicatorFloatingState, activateItem, errorAboutMissingDisposition, interactionIdRef, setItemState])
}
