import type {MutableRefObject, ReactNode, RefObject} from 'react'
import {invert} from 'lodash-es'

import type {ContactMessenger} from '@bpinc/ad-request-urls'
import type {Toggle} from '@bpinc/lib-helpers'
import type {Dictionary, StateSetter} from '@bpinc/lib-types-helpers'
import type {FavoritesItem} from '@bpinc/state-favorites-types'


export const RECENT_TABS = ['all', 'calls', 'messages'] as const
export const RECENT_TAB_INDEXES: Dictionary<string> = invert(RECENT_TABS)

export const TRANSFER_TABS = ['recent', 'favorites', 'directory'] as const
export const TRANSFER_TAB_INDEXES: Dictionary<string> = invert(TRANSFER_TABS)

export const DIRECTORY_TABS = ['internal', 'external'] as const
export const DIRECTORY_TABS_INDEXES: Dictionary<string> = invert(DIRECTORY_TABS)

export interface CommunicatorUIState {
    rightPanelOpened: boolean
    leftPanelOpened: boolean
    settingsOpened: boolean
    toggleLeftPanel: Toggle
    toggleRightPanel: Toggle
    toggleSettings: Toggle

    transferTabIndex: typeof TRANSFER_TAB_INDEXES[number]
    handleTransferTabChange: HandleTabChange

    interactionChatFormRef: MutableRefObject<HTMLDivElement | null>

    recent: RecentUIState
    directory: DirectoryUIState
    dialpad: DialpadUIState

    serviceSelectorVisible: boolean
    setServiceSelectorVisibility: StateSetter<boolean>
}

type HandleTabChange = (newTabIndex: number) => void


export interface FavoritesUIStateData {
    editMode: boolean
    contactMode: FavoriteContactMode | null
    formSubmitError: ReactNode
}

export interface FavoriteContactMode {
    view: FavoriteContactModeView
    contact?: Partial<FavoritesItem>
    goBack: VoidFunction[]
}

export type FavoriteContactModeView = 'create' | 'modify' | 'preview'

export type DialpadUIState = {
    searchValue: string
    setSearchValue: StateSetter<string>
}

export type RecentUIStateData = TabUIStateData<typeof RECENT_TAB_INDEXES[number]>

export interface RecentUIState extends RecentUIStateData, TabUIStateMethods {}

export type DirectoryUIStateData = TabUIStateData<typeof DIRECTORY_TABS_INDEXES[number]> & {
    createContactMode: boolean
    firstFoundItemId: string | undefined
    createContactFormValues: CreateContactFormFields
    searchInputRef: RefObject<HTMLInputElement>
}


export interface DirectoryUIState extends DirectoryUIStateData, TabUIStateMethods {
    toggleCreateContactMode: Toggle
    setFirstFoundItemId: StateSetter<string | undefined>
    setCreateContactFormValues: StateSetter<CreateContactFormFields>
}


type TabUIStateMethods = {
    handleTabChange: HandleTabChange
    setSearchValue: StateSetter<string>
    setSelected: StateSetter<string | undefined>
}

type TabUIStateData<T> = {
    tabIndex: T
    searchValue: string
    selected: string | undefined
}

export type CreateContactFormFields = {
    firstName: string
    lastName: string
    phone: string
    email: string
    messenger: ContactMessenger | undefined
}
