import type {ReactElement, ReactFragment} from 'react'
import React, {useEffect, useMemo} from 'react'
import {throttle} from 'lodash-es'

import {useUserActivityStateMethods} from '@bpinc/ad-user-activity-state-context'
import type {FCWithChildren} from '@bpinc/lib-types-helpers'


const eventsToTrack = [
    'mousemove',
    'click',
    'keydown',
    'scroll',
]

export const WebUserActivityTracker: FCWithChildren<ReactElement | ReactFragment> = ({children}) => {
    const {registerActivity} = useUserActivityStateMethods()

    const throttledRegisterActivity = useMemo(
        () => throttle(registerActivity, 5000),
        [registerActivity],
    )

    useEffect(() => {
        eventsToTrack.forEach(event => {
            window.addEventListener(event, throttledRegisterActivity)
        })

        return () => {
            eventsToTrack.forEach(event => {
                window.removeEventListener(event, throttledRegisterActivity)
            })
        }
    }, [throttledRegisterActivity])

    return <>{children}</>
}
