import React, {useCallback, useMemo} from 'react'
import {noop} from 'lodash-es'

import {useFormatting} from '@bpinc/ad-formatting-context'
import {formatPhone} from '@bpinc/ad-formatting-helpers'
import type {DialCandidateWithSource} from '@bpinc/ad-integration-api-state-types'
import {useSessionState} from '@bpinc/ad-session-context'
import type {PhoneData} from '@bpinc/adapters-public-api-types'
import {ContactListItem} from '@bpinc/lib-contact-list-item-ui'
import {IconChat, IconPhone} from '@bpinc/lib-icons'
import {assertDefined} from '@bpinc/lib-types-helpers'
import {CircleButton, Dropdown} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import type {useHandlers} from '../../hooks/useHandlers'
import {removeLeadingPlus} from '../../hooks/usePhoneNumberFormatter'
import {useDialCandidatesPhonesDropdown} from './useDialCandidatesPhonesDropdown'


type Props = {
    candidate: DialCandidateWithSource
    callNumber: ReturnType<typeof useHandlers>['callNumber']
    initiateChatWith: ReturnType<typeof useHandlers>['initiateChatWith']
}

function getFirstPhone(phones: PhoneData[]): string | undefined {
    if (!phones.length) {
        return undefined
    }
    const businessPhone = phones.find(phone => phone.type === 'business')
    if (businessPhone) {
        return businessPhone.number
    }
    return phones[0].number
}

export const CrmDialCandidate: React.FC<Props> = ({candidate, callNumber, initiateChatWith}) => {

    const {CONTACT: i18n} = useLocalizationState('t')
    const {privileges, phoneDeviceCapabilities} = useSessionState()
    const {getNamesFromDisplayName} = useFormatting()

    const {email, phones, displayName, associatedObjects} = candidate
    const displayNameParts = displayName.split(': ')
    const displayNameWithoutPrefix = displayNameParts.length > 1 ? displayNameParts[1] : displayName

    const [firstName, lastName] = getNamesFromDisplayName(displayNameWithoutPrefix)

    const firstPhone = getFirstPhone(phones)
    const formattedPhone = formatPhone(removeLeadingPlus(firstPhone))

    const title = (
        <b data-key="name">{candidate.displayName}</b>
    )

    const body = (
        <>
            {formattedPhone && (
                <span data-key="phone">{formattedPhone}</span>
            )}
            {email && !formattedPhone && (
                <span data-key="email">{email}</span>
            )}
        </>
    )

    const avatar = useMemo(() => ({
        firstName,
        lastName,
    }), [firstName, lastName])

    const handleCall = useCallback((phone: string) => {
        callNumber(phone, 'integration-api', {
            firstName,
            lastName,
            associatedRecords: associatedObjects,
        })
    }, [callNumber, firstName, lastName, associatedObjects])

    const handleSinglePhoneCall = useCallback(() => {
        assertDefined(formattedPhone)
        handleCall(formattedPhone)
    }, [handleCall, formattedPhone])

    const handleChat = useCallback(() => {
        assertDefined(formattedPhone)
        initiateChatWith(formattedPhone, 'integration-api', {firstName, lastName})
    }, [initiateChatWith, formattedPhone, firstName, lastName])

    const dropdownState = useDialCandidatesPhonesDropdown({
        phones,
        handleCall,
        handleSinglePhoneCall,
    })

    const controls = useMemo(
        () => (
            <>
                {formattedPhone && phoneDeviceCapabilities.canDial && (
                    <Dropdown
                        //onOpen is intentionally omitted as it is controlled by trigger
                        open={dropdownState.show.value}
                        onClose={dropdownState.show.setFalse}
                        options={dropdownState.options}
                        selectOnBlur={false}
                        tabIndex={-1}
                        trigger={
                            <CircleButton
                                className="mx-1.5 h-4.5 w-4.5"
                                onClick={dropdownState.handleCallClick}
                                tooltip={i18n.button_start_call_text()}
                                noActiveState
                                ref={dropdownState.openerRef}
                                transparent
                                clickThrottle={500}
                                data-testid="call-button"
                            >
                                <IconPhone />
                            </CircleButton>
                        }
                    />
                )}
                {formattedPhone && privileges?.CAN_START_EXTERNAL_CHAT && (
                    <CircleButton
                        className="mx-1.5 h-4.5 w-4.5"
                        onClick={handleChat}
                        tooltip={i18n.button_initiate_chat_text()}
                        noActiveState
                        transparent
                        clickThrottle={500}
                        data-testid="chat-button"
                    >
                        <IconChat />
                    </CircleButton>
                )}
            </>
        ),
        [
            formattedPhone,
            phoneDeviceCapabilities.canDial,
            dropdownState.show,
            dropdownState.options,
            dropdownState.handleCallClick,
            dropdownState.openerRef,
            i18n,
            privileges?.CAN_START_EXTERNAL_CHAT,
            handleChat,
        ],
    )

    return (
        <>
            <ContactListItem
                number={formattedPhone}
                title={title}
                body={body}
                avatarProps={avatar}
                onClick={noop}
                controls={controls}
                className="relative"
            />
        </>
    )
}
