import React from 'react'

import {isAfterCallWork, isCall, isOnHold} from '@bpinc/ad-interaction-state-methods'
import type {Item} from '@bpinc/ad-interaction-state-types'
import {InteractionTimer} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {useShowTimerChecker} from './hooks/useShowTimerChecker'
import {InteractionTypeImage} from './InteractionTypeImage'


export const IconAndTimer: React.FC<{item: Item}> = ({item}) => {
    const {DISPOSITION: i18n} = useLocalizationState('t')

    const showTimer = useShowTimerChecker(item)
    const isTimerShown = showTimer && item.displayData.deliveredStartTime
    const isCallOnHold = Boolean(isCall(item) && isOnHold(item) && item.displayData.holdStartTime)
    const isAfterCallWorkItem = isAfterCallWork(item)

    const renderTimer = () => {
        if (isTimerShown) {
            return (
                <InteractionTimer
                    startTime={item.displayData.deliveredStartTime}
                    className="ml-1 py-0 caption-xs"
                    data-testid="duration-timer"
                />
            )
        }
    }

    return (
        <>
            {isCallOnHold && (
                <>
                    <div className="flex h-6 items-center justify-center rounded-full pl-1 pr-2 bg-container-disabled">
                        <InteractionTypeImage item={item} />
                        <InteractionTimer
                            startTime={item.displayData.holdStartTime}
                            className="m-0 text-primary-fade"
                            data-testid="hold-timer"
                        />
                    </div>
                    {renderTimer()}
                </>
            )}
            {isAfterCallWorkItem && (
                <>
                    <InteractionTypeImage item={item} />
                    <span className="mx-1 font-semibold caption-xs text-neutral-fade">
                        {i18n.ended()}
                    </span>
                </>
            )}
            {!isCallOnHold && !isAfterCallWorkItem && (
                <>
                    <InteractionTypeImage item={item} />
                    {renderTimer()}
                </>
            )}
        </>
    )
}
