import React from 'react'

import type {SmCheckboxProps} from '@bpinc/lib-external-ui-elements'
import {SmCheckbox} from '@bpinc/lib-external-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'


export const CompletedCheckBox: React.FC<SmCheckboxProps> = props => {
    const {t} = useLocalizationState()

    return <SmCheckbox label={t.GLOBAL.interaction_state_completed()} {...props} />
}
