import React from 'react'
import {omit, pickBy} from 'lodash-es'

import type {PartyType} from '@bpinc/ad-activities-state-types'
import type {
    ActivityHistoryItem,
    Email,
    FileMessage,
    RemoteAssistSession,
    TextMessageEvent,
    TextMessageFromContact,
    TextMessageType,
    VoiceCallFromContact,
    VoiceTranscriptItem,
} from '@bpinc/ad-contacts-state-types'
import {
    isCall,
    isEmail,
    isFile,
    isMessage,
    isRemoteAssistSession,
    isTranscriptMessage,
} from '@bpinc/ad-contacts-state-types'
import {formatCallDuration, getSentimentFrom} from '@bpinc/ad-formatting-helpers'
import type {ExternalChatChannelType} from '@bpinc/ad-interaction-chat-state-types'
import {uuid} from '@bpinc/lib-uuid'

import type {
    HistoryCallMessage,
    HistoryChatMessage,
    HistoryEmailMessage,
    HistoryFileMessage,
    HistoryMessage,
    HistoryRemoteAssistMessage,
    HistoryVoiceTranscriptMessage,
} from '../../../../types'
import {CallMessageContent} from './CallMessageContent'
import {EmailMessage} from './EmailMessage'
import {RemoteAssistMessage} from './RemoteAssistMessage'


const fulfillMessages = (messages: PartyType[] = []): PartyType[] => {
    const parties: Record<string, Partial<PartyType>> = {}
    const notUndefined = (v: unknown) => v !== undefined

    messages.forEach(m => {
        const id = m.partyId
        const partyData: Partial<PartyType> = {
            partyId: id,
            contactId: m.contactId,
            firstName: m.firstName,
            lastName: m.lastName,
            phone: m.phone,
            userId: m.userId,
        }
        parties[id] = {
            ...parties[id],
            ...pickBy(partyData, notUndefined),
        }
    })

    return messages
        .filter(({event}) => !['party_changed', 'party_navigation'].includes(event ?? ''))
        .map(m => ({...m, ...parties[m.partyId]}))
}


interface ReducerParams {
    selfUserId: string | undefined
}

export function activityHistoryToHistoryMessageReducer({
    selfUserId,
}: ReducerParams) {
    return function historyMessagesReducer(messages: HistoryMessage[], message: ActivityHistoryItem): HistoryMessage[] {
        const isSelf = message.userId === selfUserId

        if (isCall(message)) {
            messages.push(getHistoryCallMessage(message, selfUserId))
        }

        if (isMessage(message)) {
            messages.push(getHistoryChatMessage(message, isSelf))
        }

        if (isFile(message)) {
            messages.push(getHistoryFileMessage(message, isSelf))
        }

        if (isEmail(message)) {
            messages.push(getHistoryEmailMessage(message, isSelf))
        }

        if (isRemoteAssistSession(message)) {
            messages.push(getHistoryRemoteAssistMessage(message, selfUserId))
        }

        return messages
    }
}

export function mapActivityToActivityHistoryItems(data?: PartyType[]): ActivityHistoryItem[] | undefined {
    if (!data) {
        return undefined
    }
    return fulfillMessages(data).reduce<ActivityHistoryItem[]>((messages, activityItem) => {
        if (activityItem.type && activityItem.event) {
            messages.push({
                ...activityItem,
                mediaType: 'CHAT',
                nickName: '',
                createdTime: activityItem.timestamp,
                phone: activityItem.phone ?? '',
                type: activityItem.type as TextMessageType,
                event: activityItem.event as TextMessageEvent,
                msg: activityItem.msg ?? '',
                channel: activityItem.channel as ExternalChatChannelType | undefined,
                firstName: activityItem.firstName ?? '',
                lastName: activityItem.lastName ?? '',
            })
        }
        return messages
    }, [])
}

function getHistoryCallMessage(call: VoiceCallFromContact, selfUserId?: string): HistoryCallMessage {
    const direction = call.direction === 'INBOUND' ? 'incoming' : 'outgoing'
    const duration = formatCallDuration(call.counterpartDuration)

    const selfUser = call.users?.find(user => user.userId === selfUserId)
    const isSelf = selfUser && call.users && call.users.length === 1
    const wasMissedByParty = !!selfUser?.partyFailureCause && selfUser?.partyFailureCause !== 'OTHER'

    // call can not be missed if there were no self party (that is, other agent's calls are never shown as missed)
    const isMissedCall = !!selfUser && (call.duration === 0 || wasMissedByParty) && call.direction === 'INBOUND'

    const voiceTranscript = call.transcript?.reduce<HistoryVoiceTranscriptMessage[]>((total, msg) => {
        if (isTranscriptMessage(msg)) {
            total.push(getHistoryVoiceTranscriptMessage(msg, selfUserId))
        }
        return total
    }, [])

    return {
        direction,
        users: call.users,
        timestamp: call.createdTime,
        mediaType: 'VOICE',
        globalInteractionId: call.globalInteractionId,
        msg: (
            <CallMessageContent
                direction={direction}
                timestamp={call.createdTime}
                sentiment={call.sentiment}
                duration={duration}
                isMissedCall={isMissedCall}
                hasVoiceRecording={call.hasVoiceRecording}
                recordingLink={call.recordingLink}
                voiceTranscript={voiceTranscript}
            />
        ),
        msgId: `${call.createdTime}`,
        isSelf,
        isMissedCall,
        hasVoiceRecording: call.hasVoiceRecording,
        duration,
        firstName: undefined,
        lastName: undefined,
    }
}

function getHistoryChatMessage(msg: TextMessageFromContact, isSelf?: boolean): HistoryChatMessage {
    return {
        mediaType: 'CHAT',
        msg: msg.msg,
        msgId: msg.msgId || String(msg.timestamp),
        timestamp: msg.timestamp,
        userId: msg.userId,
        contactId: msg.contactId,
        direction: msg.type === 'external' ? 'incoming' : 'outgoing',
        isSelf,
        globalInteractionId: msg.globalInteractionId,
        firstName: msg.firstName,
        lastName: msg.lastName,
        channel: msg.channel,
        displayType: msg.type === 'scenario' ? 'scenario' : 'message',
    }
}

function getHistoryEmailMessage(msg: Email, isSelf?: boolean): HistoryEmailMessage {
    const direction = msg.direction === 'INBOUND' ? 'incoming' : 'outgoing'

    return {
        msgId: msg.caseId,
        direction,
        globalInteractionId: msg.globalInteractionId,
        timestamp: msg.createdTime,
        mediaType: 'EMAIL',
        isSelf,
        firstName: msg.firstName,
        lastName: msg.lastName,
        msg: <EmailMessage caseId={msg.caseId} caseNumber={msg.caseNumber} caseTitle={msg.caseTitle} />,
    }
}

function getHistoryRemoteAssistMessage(msg: RemoteAssistSession, selfUserId?: string): HistoryRemoteAssistMessage {
    const selfAgent = msg.users?.find(user => !!selfUserId && user.userId === selfUserId)
    const lastAgent = msg.users?.toSorted((user1, user2) => user2.startTime - user1.startTime)?.[0]
    const agent = selfAgent || lastAgent

    return {
        mediaType: 'REMOTE_ASSIST',
        direction: 'outgoing',
        msgId: `${msg.createdTime}`,
        globalInteractionId: msg.globalInteractionId,
        timestamp: msg.createdTime,
        isSelf: !!selfUserId && agent?.userId === selfUserId,
        firstName: agent?.firstName,
        lastName: agent?.lastName,
        userId: agent?.userId,
        msg: (
            <RemoteAssistMessage
                duration={msg.counterpartDuration}
                privateIp={msg.privateIp}
                publicIp={msg.publicIp}
                osVersion={msg.osVersion}
            />
        ),
    }
}

function getHistoryFileMessage(msg: FileMessage, isSelf?: boolean): HistoryFileMessage {
    return {
        globalInteractionId: msg.globalInteractionId,
        direction: msg.userId ? 'outgoing' : 'incoming',
        msgId: msg.fileId || uuid(),
        displayType: 'file',
        fileId: msg.fileId,
        fileType: msg.fileType,
        fileName: msg.fileName,
        timestamp: msg && msg.timestamp ? Number(msg.timestamp) : Date.now(),
        mediaType: 'CHAT',
        isSelf,
        msg: msg.text,
        refMsgId: msg.refMsgId,
        firstName: msg.firstName,
        lastName: msg.lastName,
    }
}

function getHistoryVoiceTranscriptMessage(
    msg: VoiceTranscriptItem,
    selfUserId?: string,
): HistoryVoiceTranscriptMessage {
    const isSelf = msg?.userId === selfUserId

    return {
        ...omit(getHistoryChatMessage(msg as unknown as TextMessageFromContact, isSelf), 'channel'),
        confidence: msg.confidence,
        endTimestamp: msg.endTimestamp,
        mediaType: 'VOICE_TRANSCRIPT',
        sentiment: msg.sentimentScore !== undefined ? getSentimentFrom(msg.sentimentScore) : undefined,
        type: undefined,
    }
}
