import type {Dispatch, SetStateAction} from 'react'
import {useEffect, useRef} from 'react'


interface UseExternalContactFolderOpenerForSearchModeParams {
    isSearchMode: boolean
    setIsExternalVisible: Dispatch<SetStateAction<boolean>>
}
export function useExternalContactFolderOpenerForSearchMode({isSearchMode, setIsExternalVisible}: UseExternalContactFolderOpenerForSearchModeParams) {
    const openedExternalFolderBecauseOfSearchRef = useRef(false)

    useEffect(function openExternalFolderWhenSearchResultMatches() {
        if (isSearchMode) {
            setIsExternalVisible(isExternalVisible => {
                if (!isExternalVisible) {
                    openedExternalFolderBecauseOfSearchRef.current = true
                }

                return true
            })
        }

        if (!isSearchMode && openedExternalFolderBecauseOfSearchRef.current) {
            openedExternalFolderBecauseOfSearchRef.current = false
            setIsExternalVisible(false)
        }

    }, [isSearchMode, setIsExternalVisible])

}
