import {useActiveInteractionProp} from '@bpinc/ad-interaction-state-context'
import {isCall, isEmail, isExternalChat} from '@bpinc/ad-interaction-state-methods'


export function useIsUseContactButtonVisible() {
    return useActiveInteractionProp(i => isCall(i) || isExternalChat(i) || isEmail(i))
}
