import React, {useCallback, useMemo, useState} from 'react'

import {useInteraction, useInteractionProp} from '@bpinc/ad-interaction-state-context'
import {getContactId, getInteractionStepId, isAfterCallWork} from '@bpinc/ad-interaction-state-methods'
import {useAsRef} from '@bpinc/lib-helpers'
import type {SelectionOrigin} from '@bpinc/lib-ui-elements'
import {SelectionContainer} from '@bpinc/lib-ui-elements'
import {useInteractionUIChatScrollerApi, useInteractionUIChatType} from '@bpinc/state-interaction-ui'

import {getMessageId} from '../../../helpers'
import type {ChatCommonMessagesType, ChatProps, ChatScrollNodeType} from '../../../types'
import {isExternalChatMessageDate, isHistoryMessage, isVoiceTranscriptMessage} from '../../../types'
import {ChatMessagesSelectionActions} from '../ChatMessagesActions'
import {useBotSuggestionsByType} from '../VoiceTranscript/selectors'
import {Dropzone} from './Dropzone'
import {ScrollToFirstUnreadBadge} from './ScrollToFirstUnreadBadge'
import {useNormalizedMessagesWithDates} from './useMessages'
import {VirtualizedExternalMessages} from './VirtualizedExternalMessages'

import styles from './ExternalMessages.module.less'


export const ExternalMessages: React.FC<ChatProps> = React.memo(function ExternalMessages({
    itemId,
}) {
    const scrollerApi = useInteractionUIChatScrollerApi(itemId)
    const [scrollNode, setScrollNode] = useState<ChatScrollNodeType>(null)

    const type = useInteractionUIChatType(itemId)
    const interaction = useInteraction(itemId)
    const contactId = getContactId(interaction)
    const interactionStepId = getInteractionStepId(interaction)

    const botSuggestionsByType = useBotSuggestionsByType(itemId)
    const botSuggestions = useMemo(() => {
        switch (type) {
            case 'calls':
                return botSuggestionsByType.voice
            case 'messages':
                return botSuggestionsByType.chat
            default:
                return [...botSuggestionsByType.voice, ...botSuggestionsByType.chat]
        }

    }, [botSuggestionsByType.chat, botSuggestionsByType.voice, type])

    const {messagesWithDates} = useNormalizedMessagesWithDates(
        type,
        contactId,
        itemId,
        botSuggestions,
    )

    const itemIsACW = useInteractionProp(itemId, isAfterCallWork)

    const itemsIds = useMemo(() => {
        return messagesWithDates
            .filter(isSelectableMessage)
            .map(getMessageId)
    }, [messagesWithDates])
    const itemsIdsRef = useAsRef(itemsIds)
    const itemsProvider = useCallback(() => itemsIdsRef.current, [itemsIdsRef])
    const onItemSelect = useCallback((id: string, selectionOrigin: SelectionOrigin | undefined) => {
        if (selectionOrigin === 'keys') {
            scrollerApi?.scrollTo(id, 'center')
        }
    }, [scrollerApi])

    return (
        <SelectionContainer
            startFrom="bottom"
            className={styles.messagesContainer}
            itemsListProvider={itemsProvider}
            onItemSelect={onItemSelect}
        >
            {!itemIsACW && <Dropzone itemId={itemId} />}
            <div ref={setScrollNode} className={styles.messages}>
                <ChatMessagesSelectionActions messages={messagesWithDates} interactionId={itemId} scrollContainer={scrollNode} withMaskAction>
                    <VirtualizedExternalMessages
                        messages={messagesWithDates}
                        scrollNode={scrollNode}
                        itemId={itemId}
                        contactId={contactId}
                        type={type}
                        interactionStepId={interactionStepId}
                    />
                </ChatMessagesSelectionActions>
            </div>
            <ScrollToFirstUnreadBadge itemId={itemId} scrollNode={scrollNode} />
        </SelectionContainer>
    )
})

function isSelectableMessage(message: ChatCommonMessagesType) {
    if (isVoiceTranscriptMessage(message)) {
        return true
    }

    return !(isExternalChatMessageDate(message) || (isHistoryMessage(message) && message.mediaType === 'REMOTE_ASSIST'))


}

